import { AngularConstants } from "../common/angular-constants";

import { Component } from "./component";

interface IComponentCommonScope extends ng.IScope {
	component: Component;
}

class ComponentCommonCtrl {
	static $inject = [AngularConstants.SCOPE];
}

export class ComponentCommonDirective {
	static add(m: ng.IModule): void {
		m.directive("eirComponentCommon", () => {
			return {
				controller: ComponentCommonCtrl,
				template: require("./component-common.html"),
				replace: true,
				scope: true
			};
		});
	}
}
