import { ComponentClassNames, SettingKeys } from "ng2/common/constants/common.constant";

import type { IJsonTimepicker } from "common/model/component/timepicker.model";
import type { ITimepickerApi } from "common/model/sohoxi/sohoxi-timepicker.model";
import type { IComponentFieldSetting } from "ng2/common/model/components/settings/component-field-setting.model";

import type { IEditorComponent } from "common/interface/ieditor-component";
import { Timepicker } from "components/timepicker/timepicker";
import { EditorComponentCtrl, IEditorComponentScope } from "editor/components/editor-component";
import type { IEditorFlexbox } from "editor/components/editor-flexbox";

export interface IEditorTimepicker extends IEditorComponent, Timepicker {
	parent: IEditorFlexbox;
	// Overrides to make dual extend work
	getScope(): IEditorComponentScope;
	getDatabearer(): IJsonTimepicker;
	getSohoXiApi(): ITimepickerApi;
}

export class EditorTimepickerCtrl extends EditorComponentCtrl {
	static add(m: ng.IModule): void {
		m.controller(
			ComponentClassNames.EDITOR_COMPONENT_PREFIX + Timepicker.name,
			EditorTimepickerCtrl
		);
	}

	protected component: IEditorTimepicker;

	setSetting(settingMeta: IComponentFieldSetting, newSettingValue: any): void {
		super.setSetting(settingMeta, newSettingValue);
		switch (settingMeta.ref) {
			case SettingKeys.IS_READONLY:
			case SettingKeys.TIME_FORMAT:
			case SettingKeys.ENABLE_SECONDS:
				this.component.refreshTimepicker();
				break;
		}
	}
}
