import {
	AfterViewInit,
	Component,
	ComponentRef,
	ElementRef,
	EventEmitter,
	HostBinding,
	Input,
	OnChanges,
	Output,
	Renderer2,
	SimpleChanges,
	ViewChild,
	ViewContainerRef,
} from "@angular/core";
import { SohoBusyIndicatorDirective } from "ids-enterprise-ng";
import isUndefined from "lodash-es/isUndefined";

import type { IComponent } from "ng2/common/model/components/base/component.model";
import type { IContext } from "ng2/common/model/components/base/context.model";
import type { ILayoutComponent } from "ng2/common/model/components/base/layout-component.model";
import type { IJsonComponent } from "ng2/common/model/databearer/components/json-component.model";

import { LayoutComponent } from "ng2/components/base/layout-component";
import { ButtonComponent } from "ng2/components/button/button.component";
import { DropdownComponent } from "ng2/components/dropdown/dropdown.component";
import { FlexboxComponent } from "ng2/components/layout-components/flexbox/flexbox.component";
import { ListComponent } from "ng2/components/list/list.component";
import { LookupComponent } from "ng2/components/lookup/lookup.component";
import { TextboxComponent } from "ng2/components/textbox/textbox.component";

import { DynamicComponentService } from "ng2/components/services/dynamic-component.service";

@Component({
	selector: "component-container",
	template: `
		<div [hidden]="false"
			soho-busyindicator [activated]="true" [blockUI]="true"
		></div>
		<ng-template #dynamic></ng-template>
	`,
	styles: [`
		.eir2-component {
			width: 100%;
			height: 100%;
		}
	`],
})
export class ComponentContainer implements
	AfterViewInit,
	OnChanges {

	@Input() component: IJsonComponent;
	@Input() context: IContext;
	@Input() parent: ILayoutComponent;
	@Output() componentReady = new EventEmitter<IComponent>();

	@ViewChild("dynamic", {
		read: ViewContainerRef,
	}) viewContainerRef: ViewContainerRef;

	@ViewChild(SohoBusyIndicatorDirective, {
		static: true
	}) busyIndicator: SohoBusyIndicatorDirective;

	@HostBinding("id") id: string;
	@HostBinding("class") class: string;

	componentInstance: IComponent | ILayoutComponent;
	totalChildren = 0;

	constructor(
		private renderer: Renderer2,
		private elementRef: ElementRef,
		private dynamicComponentService: DynamicComponentService,
	) { }

	ngAfterViewInit(): void {

		this.dynamicComponentService
			.setRootViewContainerRef(this.viewContainerRef);

		this.setComponentContainerProperties();
		this.attachComponent();
		this.onComponentReady();
		this.onBoundaryChange();
	}

	ngOnChanges(changes: SimpleChanges): void {

		const newComponentChange = changes.component;
		if (!isUndefined(newComponentChange.previousValue)) {

			this.component = newComponentChange.currentValue;

			this.dynamicComponentService.destroyComponent();
			$("#child-cage-AppPanel").remove();
			this.ngAfterViewInit();
		}
	}

	private setComponentContainerProperties(): void {

		const component = this.component;

		this.id = component.id;
		// this.isVisible = true;

		if (component instanceof LayoutComponent || // TODO: verify why it isn't enough to check instance of LayoutComponent
			component.children
		) {
			this.totalChildren = component.children.length;
		}
	}

	private attachComponent(): void {

		const component = this.component;
		const cssClass = ["eir2-component"];

		let componentRef: ComponentRef<IComponent|ILayoutComponent>;
		switch (component.typedef) {
			case "Button":
				componentRef = this.dynamicComponentService
					.addComponent(ButtonComponent);
				cssClass.push("eir2-button-component");
				break;

			case "Dropdown":
				componentRef = this.dynamicComponentService
					.addComponent(DropdownComponent);
				cssClass.push("eir2-dropdown-component");
				break;

			case "Flexbox":
				componentRef = this.dynamicComponentService
					.addComponent(FlexboxComponent);
				cssClass.push("eir2-flexbox-component");
				break;
			case "List":
				componentRef = this.dynamicComponentService
					.addComponent(ListComponent);
				cssClass.push("eir2-list-component");
				break;
			case "Lookup":
				componentRef = this.dynamicComponentService
					.addComponent(LookupComponent);
				cssClass.push("eir2-lookup-component");
				break;
			case "Textbox":
			case "TextBox":
				componentRef = this.dynamicComponentService
					.addComponent(TextboxComponent);
				cssClass.push("eir2-textbox-component");
				break;
			default:
				throw Error("Invalid component");
		}

		this.class = cssClass.join(" ");

		this.componentInstance = componentRef.instance;

		this.setAttachedComponentProperties();

		this.componentInstance
			.initializeBinding(this.component);
	}

	private setAttachedComponentProperties(): void {

		// Assign more properties?
		this.componentInstance.id = this.component.id;
		this.componentInstance.displayName = this.component.displayName;
		this.componentInstance.typedef = this.component.typedef;
		this.componentInstance.isReadonly = this.component.isReadonly;

		// TODO: Shouldn't be specific properties. Make IComponent more generic
		// Assign specific properties
		const component: any = this.component;
		const componentInstance: any = this.componentInstance;
		if (component.style) {
			componentInstance.style = component.style;
		}

		// End assign properties

		this.componentInstance.databearer = this.component;
		this.componentInstance.parent = this.parent;
		this.componentInstance.context = this.context;
	}

	private onComponentReady(): void {
		this.componentInstance
			.componentReady$
			.subscribe({
				next: (value) => {

					if (value instanceof LayoutComponent) {
						this.totalChildren--;
						console.info(`${value.id} is ready! Number of children left to render ${this.totalChildren}`);
					}

					this.busyIndicator.activated = value.isLoading;
					this.componentReady.emit(value);
				}
			});
	}

	private onBoundaryChange(): void {

		this.componentInstance
			.boundaryChange$
			.subscribe({
				next: (value: IComponent) => {

					if (value instanceof LayoutComponent) {

						const componentElement = this.elementRef.nativeElement;

						// TODO: Add more flexbox related boundary cases here
						if (!value.isVisible) {
							this.renderer.addClass(componentElement, "is-hidden");
						} else {
							this.renderer.removeClass(componentElement, "is-hidden");
						}

						console.info(`${value.id} has boundary change! isVisible: ${value.isVisible}`);
					}
				}
			});
	}
}
