import angular from "angular";

import { Datagrid } from "../../../components/datagrid/datagrid";
import { TranslationManagementService } from "../../../components/services/translation-management-service";
import type { IFinderModeOptions } from "../../defines";
import { DesignerHelper } from "../../helper";
import { ProjectService } from "../../services/project-service";
import type { IExpressionData } from "../components/editor-component";
import type { IEditorDatagrid } from "../components/editor-datagrid";
import { ISidebarView, SidebarViewEnum } from "../defines";
import { EditorService } from "../services/editor-service";
import { HotkeyService } from "../services/hotkey-service";
import { InsertComponentService } from "../services/insert-component-service";
import { SaveService } from "../services/save-service";
import { UndoService } from "../services/undo-service";

import { CanvasModeEnum, ConditionType, Config, IChanged, InjectNames, ProjectTypeEnum, SaveFlagsEnum, SettingKeys } from "ng2/common/constants/common.constant";

import type { IJsonComponent } from "ng2/common/model/databearer/components/json-component.model";
import type { IMingleLanguage } from "ng2/common/model/language.model";

import { AngularConstants } from "common/angular-constants";
import { Debugger } from "common/debugger";
import type { IErrorResponse } from "common/error-response";
import type { IEditorComponent } from "common/interface/ieditor-component";
import { DesignerManifest } from "ng2/common/project/manifest/designer-manifest";

import { CommonService } from "common/services/common-service";
import {
	DialogButtonType, DialogService, IDialogOptions, IDialogResult
} from "common/services/dialog-service";
import { LanguageService } from "common/services/language-service";

export enum OverlayStateEnum {
	HIDDEN = "HIDDEN",
	OVERLAY = "OVERLAY",
	FINDER_OVERLAY = "FINDER_OVERLAY",
}

export class TopbarCtrl {
	sidebarView: SidebarViewEnum;
	isSidebarVisible = true;
	overlayState: OverlayStateEnum = OverlayStateEnum.HIDDEN;
	overlayStateEnum = OverlayStateEnum;
	finderOptions: IFinderModeOptions;
	projectSaveState: string;
	canCut: boolean;
	canCopy: boolean;
	canDelete: boolean;
	hasClipboardComponent: boolean;
	isRuntime: boolean;
	supportedLanguages: IMingleLanguage[];
	currentLanguage: string;
	private saveError: string;

	static $inject = [
		AngularConstants.SCOPE,
		AngularConstants.WINDOW,
		AngularConstants.STATE_SERVICE,
		InjectNames.HOTKEY_SERVICE,
		InjectNames.EDITOR_SERVICE,
		InjectNames.SAVE_SERVICE,
		InjectNames.UNDO_SERVICE,
		InjectNames.DIALOG_SERVICE,
		InjectNames.INSERT_COMPONENT_SERVICE,
		InjectNames.LANGUAGE_SERVICE,
		InjectNames.TRANSLATION_MANAGEMENT_SERVICE,
		InjectNames.PROJECT_SERVICE,
		InjectNames.COMMON_SERVICE,
	];

	get isDeveloper(): boolean {
		return Debugger.isEnabled() && Config.IS_DEV;
	}

	get isBaseBuilder(): boolean {
		return Config.IS_BASE_BUILDER;
	}

	constructor(
		private $scope: ng.IScope,
		private $window: ng.IWindowService,
		private $state: ng.ui.IStateService,
		private hotKeyService: HotkeyService,
		private editorService: EditorService,
		private saveService: SaveService,
		private undoService: UndoService,
		private dialogService: DialogService,
		private insertComponentService: InsertComponentService,
		private languageService: LanguageService,
		private translationManagementService: TranslationManagementService,
		private projectService: ProjectService,
		private commonService: CommonService

	) {
		const sidebarView = editorService.getSidebarView();
		this.sidebarView = sidebarView.view;
		this.isSidebarVisible = sidebarView.isSidebarVisible;
		this.isRuntime = this.editorService.isRuntimeMode();
		this.supportedLanguages = this.languageService.getSupportedLanguages();
		this.currentLanguage = this.languageService.getLanguage();
		this.initiateSubscribers($scope);
	}

	openExpressions(): void {
		const options: IDialogOptions<IExpressionData> = {
			title: "Project Expressions",
			parameter: {
				component: null,
				option: ConditionType.ALL
			},
			template: require("../component-settings/expression-dialog/expression-dialog.html")
		};
		this.dialogService.show(options);
	}

	openTranslations(): void {
		this.translationManagementService.openTranslations().then((result) => {
			const data = result.value as IChanged;
			if (result.button === DialogButtonType.Ok && data.savedComponentStrings.length > 0) {
				data.savedComponentStrings.forEach((value) => {
					const component = this.editorService.getComponentInstanceById(value.component);
					component.forEachBindable((binding) => {
						if (binding.ref === value.reference) {
							const field = component.getBindableField(value.reference);
							field.value = value.newValue;
						}
					});
				});

				// check if titlebar is changed
				const titlebarChanged = data.savedComponentStrings.find((translation) => {
					return translation.reference === SettingKeys.APPLICATION_HEADER;
				});
				if (titlebarChanged) {
					this.updateStringHeader(titlebarChanged.newValue);
				}

				//Check if selected component is changed
				const selectedComponent = this.editorService.getSelectedComponent();
				if (selectedComponent) {
					const selectedIsChanged = data.savedComponentStrings.filter((translation) => {
						return translation.component === selectedComponent.getId();
					});
					if (selectedIsChanged) {
						selectedIsChanged.forEach((updatedTranslation) => {
							this.editorService.translationChangedEvent.raise(updatedTranslation);
						});
					}
				}
				this.editorService.updateTranslatables();
				this.editorService.saveCurrent(SaveFlagsEnum.TRANSLATIONS);
			}
			//If global translations is saved
			if (result.button === DialogButtonType.Ok && !angular.equals({}, data.savedGlobalStrings)) {
				this.translationManagementService.saveGlobalTranslations(result.value.savedGlobalStrings).then(() => {
					this.editorService.updateTranslatables();
				});
			}
			//If global translations is deleted
			if (result.button === DialogButtonType.Ok && !angular.equals({}, data.deletedGlobalStrings)) {
				this.translationManagementService.deleteGlobalTranslations(result.value.deletedGlobalStrings)
					.then(() => {
						this.editorService.updateTranslatables();
					});
			}
		});
	}

	private updateStringHeader(newString: string): void {
		const manifest = this.editorService.getCurrentProject().getManifest();
		manifest.title = newString;
		this.editorService.setManifest(manifest);
		this.editorService.projectSettingsChangedEvent.raise(
			this.editorService.getCurrentProject().getManifest()
		);
		this.editorService.saveCurrent(SaveFlagsEnum.MANIFEST);
	}

	/**
	 * Initiate the subscribers.
	 *	Also unsubscribe when scope is destroyed.
		*
		* @private
		* @param {ng.IScope} scope
		*
		* @memberof TopbarCtrl
		*/
	private initiateSubscribers(scope: ng.IScope): void {
		const subscribers = [
			// Update the sidebarview according to the editorservice
			this.editorService.sidebarViewChangedEvent.on((view: ISidebarView) => {
				if (view) {
					this.sidebarView = view.view;
					this.isSidebarVisible = view.isSidebarVisible;
				}
			}),
			// Check if the new selected component and be cut, copied or deleted
			this.editorService.selectedComponentChangedEvent.on((component: IEditorComponent) => {
				if (component && this.editorService.isMovable(component)) {
					this.canCut = this.editorService.canCut(component);
					this.canCopy = this.editorService.canCopy(component);
					this.canDelete = component.canDelete();
				} else {
					this.canCut = false;
					this.canCopy = false;
					this.canDelete = false;
				}
			}),
			// Set the overlayState value based on the canvas current mode
			this.editorService.canvasModeChangedEvent.on((mode: CanvasModeEnum) => {
				if (mode === CanvasModeEnum.RUNTIME) {
					this.isRuntime = true;
				} else {
					this.isRuntime = false;
				}
				if (mode === CanvasModeEnum.FINDER) {
					this.overlayState = OverlayStateEnum.FINDER_OVERLAY;
					this.finderOptions = this.editorService.getFinderModeOptions();
				} else if (mode === CanvasModeEnum.WIZARD) {
					this.overlayState = OverlayStateEnum.OVERLAY;
				} else {
					this.overlayState = OverlayStateEnum.HIDDEN;
				}
				scope.$applyAsync();
			}),
			// On clipboard component changes
			this.insertComponentService.clipboardComponentChangedEvent.on((component: IJsonComponent) => {
				if (component) {
					this.hasClipboardComponent = true;
				} else {
					this.hasClipboardComponent = false;
				}
			}),
			// Update the save state on saveStateEvent
			this.saveService.saveStateEvent.on((value: string | IErrorResponse) => {
				this.updateSaveState(value);
			}),
		];

		scope.$on(AngularConstants.DESTROY, () => {
			subscribers.forEach((unsubscribe) => {
				unsubscribe();
			});
		});
	}

	saveBase(): void {
		const options: IDialogOptions<string> = {
			title: "Define Stratus Base Template",
			template: require("./base-modal/base.html")
		};

		this.dialogService.show(options).then((result: IDialogResult<string>) => {
			if (result.button === DialogButtonType.Ok) {
				const manifest = this.editorService.getCurrentProject().getManifest();
				manifest.typeDef = ProjectTypeEnum.BASE;
				this.editorService.saveCurrent(SaveFlagsEnum.QUESTIONS);
				this.editorService.saveCurrent(SaveFlagsEnum.MANIFEST);
			}
		});

	}

	changeLanguage(language: IMingleLanguage): void {
		if (language.key !== this.currentLanguage) {
			this.languageService.changeCurrentLanguage(language.key).then(() => {
				this.currentLanguage = this.languageService.getLanguage();
				this.languageService.loadSohoLocale(this.currentLanguage)
					.done(() => {
						this.editorService.getAllComponents()
							.forEach(component => {
								// switch case on components which can have locale specific data
								switch (component.getTypedef()) {
									case Datagrid.name:
										(component as IEditorDatagrid).updateValuesWithTranslations();
										break;
									default:
										break;
								}
							});
					});
				this.languageService.loadGlobalStrings(this.currentLanguage).then(() => {
					this.editorService.updateTranslatables();
					this.updateStringHeader(this.translationManagementService.getTranslatableHeaderString());
				});
			});
		}
	}

	toggleRuntimeMode(): void {
		let mode = CanvasModeEnum.SELECTION;
		if (this.editorService.getCanvasMode() === mode) {
			mode = CanvasModeEnum.RUNTIME;
		} else {
			mode = CanvasModeEnum.SELECTION;
		}
		this.editorService.setCanvasMode(mode);
		this.isRuntime = this.editorService.isRuntimeMode();
	}

	copyToForm(): void {
		const options: IDialogOptions<string> = {
			title: "Copy To Mongoose Form",
			parameter: this.editorService.getCurrentProject().getManifest().displayName,
			template: require("./copy-form/copy-form.html")
		};

		this.dialogService.show(options).then((result: IDialogResult<string>) => {
			if (result.button === DialogButtonType.Ok) {
				const projectId = this.editorService.getCurrentProject().getId();
				this.projectService.copyToForm(projectId, result.value);
			}
		});
	}

	refresh(): void {
		this.$state.reload();
	}

	showProjectSettings(): void {
		const manifest = this.editorService.getCurrentProject().getManifest();
		const options: IDialogOptions<DesignerManifest> = {
			title: "Project Settings",
			parameter: manifest,
			style: "max-width: 350px;",
			template: require("../project-settings/project-settings-dialog.html")
		};

		this.dialogService.show<DesignerManifest, void>(options);
	}

	showKeyboardShortcuts(): void {
		const keydialog: IDialogOptions<any> = {
			title: "Keyboard Shortcuts",
			template: require("./key-dialog/key-dialog.html"),
			buttons: [{
				text: "OK",
				value: "Cancel",
				isDefault: false
			}]
		};
		this.dialogService.show(keydialog);
	}

	saveProject(): void {
		this.saveError = null;
		this.editorService.saveCurrent(SaveFlagsEnum.PROJECT)
			.catch((errResponse: IErrorResponse) => {
				this.saveError = errResponse.message;
			});
	}

	cutComponent(): void {
		this.hotKeyService.cutComponent();
	}

	copyComponent(): void {
		this.hotKeyService.copyComponent();
	}

	pasteComponent(): void {
		this.hotKeyService.pasteComponent();
	}

	deleteComponent(): void {
		this.hotKeyService.deleteComponent();
	}

	screenshotCanvas(): void {
		this.editorService.saveCurrent(SaveFlagsEnum.SCREENSHOT);
	}

	displayClipboardName(): string {
		let displayName = "";
		if (this.insertComponentService.getClipboardComponent()) {
			displayName = this.insertComponentService.getClipboardComponent().displayName;
		}
		return displayName;
	}

	typeDefClipboard(): string {
		let typedef = "";
		if (this.insertComponentService.getClipboardComponent()) {
			typedef = this.insertComponentService.getClipboardComponent().typedef;
		}
		return typedef;
	}

	showClipboard(): IJsonComponent {
		return this.insertComponentService.getClipboardComponent();
	}

	openNewComponent(): void {
		this.editorService.setSidebarView(SidebarViewEnum.NEW_COMPONENT);
		this.showSidebar();
	}

	openSettings(): void {
		this.editorService.setSidebarView(SidebarViewEnum.SETTINGS);
		this.showSidebar();
	}

	private showSidebar(): void {
		this.editorService.setSidebarVisibility(true);
	}

	openRuntime(): void {
		DesignerHelper.openRuntime(
			this.editorService.getCurrentProject().getId(),
			this.$window,
			this.editorService.getActiveUrlBindings(),
			this.editorService.getUrlMetaData());
	}

	openHelp(): void {
		this.commonService.openHelp();
	}

	openUrlSettings(): void {
		this.editorService.openURLDialog();
	}

	undoAction(): void {
		this.hotKeyService.removeClipboardComponentByUndoStack();
		this.undoService.undoAction();
	}

	redoAction(): void {
		this.undoService.redoAction();
	}

	isUndoActionAvailable(): boolean {
		return this.undoService.isUndoAvailable();
	}

	isRedoActionAvailable(): boolean {
		return this.undoService.isRedoAvailable();
	}

	updateSaveState(value: string | IErrorResponse): void {
		if (typeof value === "string") {
			this.projectSaveState = value;
			this.saveError = null;
		} else {
			this.projectSaveState = this.saveService.saveStates.failed;
			this.saveError = value.message;
		}
	}
}

export class TopbarDirective {
	static add(m: ng.IModule): void {
		m.directive("eirTopbar", () => {
			return {
				template: require("./topbar.html"),
				scope: true,
				transclude: true,
				controller: TopbarCtrl,
				controllerAs: "eirTopbarCtrl"
			};
		});
	}
}
