import { DEFAULT_BOUNDARY, Numbers, RowHeight } from "ng2/common/constants/common.constant";
import { IJsonDatagrid } from "ng2/common/model/databearer/components/json-datagrid.model";

import { Datagrid } from "../../../../components/datagrid/datagrid";
import type { IEditorComponent } from "common/interface/ieditor-component";
import { ISettingsClassInstance } from "../../defines";
import { ComponentSettingsCtrl } from "../component-settings";
import { DatagridContentSettingsDirective } from "./datagrid-content-settings";

export class DatagridSettings implements ISettingsClassInstance {
	private static instance: DatagridSettings = new DatagridSettings();
	protected selectedComponent: IEditorComponent;

	public constructor() {
		if (DatagridSettings.instance) {
			throw new Error("Error: Instantiation failed: Use DataGridSettings.getInstance() instead of new.");
		}
	}

	public static getInstance(): DatagridSettings {
		return DatagridSettings.instance;
	}

	public contentSettingsDirective: ng.IDirective = new DatagridContentSettingsDirective();

	public create(id: string): IJsonDatagrid {
		const newDatagrid = ComponentSettingsCtrl.create(id, Datagrid) as IJsonDatagrid;
		newDatagrid.boundarySettings[DEFAULT_BOUNDARY.id].rowHeight = RowHeight.MEDIUM;
		newDatagrid.columns = [];
		newDatagrid.usePaging = false;

		const divideBy = 3.0;
		const numberOfColumns = 3;
		const width = (Numbers.HUNDRED / divideBy).toString() + "%";
		for (let i = 1; i <= numberOfColumns; i++) {
			const newId: string = id + "Column" + i;
			const newName: string = "Column " + i;
			newDatagrid.columns.push({
				id: newId,
				name: newName,
				sortable: true,
				resizable: true,
				width: width,
				textOverflow: "ellipsis",
				editable: false,
				summarize: false,
				align: "left"
			});
		}

		return newDatagrid;
	}
}
