/* eslint-disable @typescript-eslint/explicit-module-boundary-types */
// TODO: Fix the so that documentMode is in document global
import angular from "angular";
import filter from "lodash-es/filter";
import find from "lodash-es/find";
import union from "lodash-es/union";

import type { ILayoutComponent } from "./interface/ilayout-component";

import type { IListComponent } from "./interface/ilist-component";

import {
	ComponentConstants,
	CSSClasses,
	ElementDataKeys,
	Numbers,
	RegexStrings,
} from "ng2/common/constants/common.constant";

import type { IMap } from "ng2/common/model/imap.model";

import type { IProjectInfo } from "ng2/common/project/project-info";

export enum RegExpMatchCandidate { BASE_NAME = 1, IS_COPY = 2, NUMBER = 3 }

export class Helper {

	/**
	 * Gets the date based on the format param
	 * Supported formats:
	 *
	 *	* yyyy-mm-dd
		*
		* Defaults to yyyy-mm-ddThh:mm
		*
		* @static
		* @param {("yyyy-mm-dd" | undefined)} format
		* @returns {string}
		* @memberof Helper
		*/
	static getFormattedDate(format = "yyyy-mm-dd"): string {
		//tslint:disable:no-magic-numbers
		const date = new Date();
		const year = date.getFullYear();

		const month = (date.getMonth() + 1).toString();
		const formattedMonth = month.padStart(2, "0");

		const day = date.getDate().toString();
		const formattedDay = day.padStart(2, "0");

		const hour = date.getHours().toString();
		const formattedHour = hour.padStart(2, "0");

		const minute = date.getMinutes().toString();
		const formattedMinute = minute.padStart(2, "0");

		const second = date.getSeconds().toString();
		const formattedSecond = second.padStart(2, "0");

		const millisecond = date.getMilliseconds().toString();
		const formattedMilleSecond = millisecond.padStart(3, "0");
		//tslint:enable:no-magic-numbers

		//tslint:disable:max-line-length
		switch (format) {
			case "yyyy-mm-dd":
				return `${year}-${formattedMonth}-${formattedDay}`;
			case "yyyymmdd HH:mm:ss.fff":
				return `${year}${formattedMonth}${formattedDay} ${formattedHour}:${formattedMinute}:${formattedSecond}.${formattedMilleSecond}`;
			default: //YYYY-MM-DDTHH:mm
				return `${year}-${formattedMonth}-${formattedDay}T${formattedHour}:${formattedMinute}`;
		}
		//tslint:enable:max-line-length
	}

	static become(obj: any, newClass: any): void {
		obj.__proto__ = ((new newClass())).__proto__;
	}

	static getDeepValue(obj: any, paths: string): any | null {
		for (let i = 0, path = paths.split("."), len = path.length; i < len; i++) {
			if (!this.isUndefinedOrNull(obj[path[i]])) {
				obj = obj[path[i]];
			} else {
				return null;
			}
		}

		return obj;
	}

	static setDeepValue(obj: any, paths: string, newValue: any): void {
		for (var i = 0, path = paths.split("."), secondToLast = path.length - 1; i < secondToLast; i++) {
			if (!obj[path[i]]) {
				obj[path[i]] = {};
			}
			obj = obj[path[i]];
		}
		obj[path[i]] = newValue;
	}

	static stringReplace(str: string, context: IMap<string>): string {
		const reg = /({\w+})/g;
		return str.replace(reg, (match: string, $1: any, $2: any) => {
			const trimmed = /{(\w+)}/.exec(match)[1];

			return context[trimmed];
		});
	}

	static getStringParameters(str: string): string[] {
		const reg = /({\w+})/g;
		const match = str.match(reg);
		if (match) {
			return match.map((hit) => {
				return hit.match(/\w+/g)[0];
			});
		}
		return [];
	}

	static isUndefined(anyObject: any): boolean {
		return typeof anyObject === "undefined";
	}

	/**
	 * Returns true if object is undefined or null
	 * @param anyObject
	 */
	static isUndefinedOrNull(anyObject: any): boolean {
		return anyObject === undefined || anyObject === null;
	}

	static isFloat(value: string | number): boolean {
		return !this.isInt(value);
	}

	static isInt(value: string | number): boolean {
		return Number(value) % 1 === 0;
	}

	private static chars = "0123456789ABCDEFGHIJKLMNOPQRSTUVWXYZ";

	/**
	 * Creates a string with random uppercase letters and numbers.
	 * The default length is 16 if the stringLength parameter is omitted.
	 */
	static random(stringLength = 16): string {
		const chars = this.chars;
		let randomstring = "";
		for (let i = 0; i < stringLength; i++) {
			const rnum = Math.floor(Math.random() * chars.length);
			randomstring += chars.substring(rnum, rnum + 1);
		}

		return randomstring;
	}

	/**
	 * Returns true if string is undefined, null or empty
	 *
	 * @static
	 * @param {string} item
	 * @returns {string}
	 *
	 * @memberOf Helper
	 */
	static isUndefinedEmpty(item: string): boolean {
		return (item === undefined || item === null || item === "") ? true : false;
	}

	/**
	 * Returns a list of strings by regex
	 *
	 * @static
	 * @param {string} regex
	 * @param {string} myString
	 * @returns {string[]}
	 *
	 * @memberOf Helper
	 */
	static getListByRegex(regex: string, myString: string): string[] {
		const group = 2;
		const cleanedString = myString.match(regex)[group];
		const readyList = cleanedString.substring(1, cleanedString.length - 1).split(",");
		return readyList;
	}

	/**
	 * Creates a query parameter string with same key for all values
	 *
	 * @param {string[]} values
	 * @param {string} key
	 */
	static createQueryParameters(values: string[], key: string): string {
		return values.map((value) => {
			return key + "=" + value;
		}).join("&");
	}

	/**
	 * Deep trim trailing whitespace in object
	 *
	 * @param {string[]} array
	 * @returns {string[]}
	 *
	 * @memberOf Helper
	 */
	static deepObjectTrim(objectToTrim: Object): Object {
		const deepTrim = (object: any) => {
			const properties = Object.keys(object);
			properties.forEach((property: string) => {
				if (typeof object[property] === "string") {
					object[property] = object[property].trim();
				} else if (typeof object[property] === "object") {
					deepTrim(object[property]);
				}
			});
		};

		deepTrim(objectToTrim);
		return objectToTrim;
	}

	static getListFromMap<T>(list: IMap<T[]>): T[] {
		let arrayList: T[] = [];
		for (const key in list) {
			arrayList = arrayList.concat(list[key]);
		}
		return arrayList;
	}

	/**
	 * Sorts an object based on selected property
	 *
	 * @static
	 * @template T
	 * @param {any[]} object
	 * @param {string} property
	 * @returns {any[]}
	 *
	 * @memberOf Helper
	 */
	static sortObjectByProperty(object: any[], property: string, reverse = false): any[] {
		const output = reverse ? [1, -1] : [-1, 1];
		return object.sort((a, b): number => {
			if (a[property] < b[property]) {
				return output[0];
			}
			if (a[property] > b[property]) {
				return output[1];
			}
			return 0;
		});
	}

	static sortObjectKeys<T>(unordered: IMap<T>, doNotSortKey?: string): IMap<T> {
		const ordered: IMap<T> = {};
		const sortedKeys = Object.keys(unordered).sort();

		if (doNotSortKey) {
			sortedKeys.splice(sortedKeys.indexOf(doNotSortKey), 1);
			sortedKeys.unshift(doNotSortKey);
		}

		sortedKeys.forEach(key => {
			ordered[key] = unordered[key];
		});

		return ordered;
	}

	/**
	 * Returns true if object has property with value
	 *
	 * @static
	 * @template T
	 * @param {*} object
	 * @param {T} value
	 * @returns {boolean}
	 *
	 * @memberOf Helper
	 */
	static objectHasValue<T>(object: any, value: T): boolean {
		const keys = Object.keys(object);
		return keys.some((key: string) => {
			return object[key] === value;
		});
	}

	static objectHasKey<T>(object: any, value: string): boolean {
		const keys = Object.keys(object);
		return keys.some((key: string) => {
			return key === value;
		});
	}

	/**
	 * Compares one or two lists if its total exceeds limit
	 *
	 * @static
	 * @param {number} limit
	 * @param {any[]} listA
	 * @param {any[]} [listB]
	 * @returns {boolean}
	 *
	 * @memberOf Helper
	 */
	static exceedAllowed(limit: number, listA: any[], listB?: any[]): boolean {
		const numberOfNewDatasource = listB ? listB.length : 1;
		const totalDatasources = listA.length + numberOfNewDatasource;

		return totalDatasources > limit;
	}

	/**
	 * Wrap key(s) in brackets. Good to use if property contains spaces
	 *
	 * @static
	 * @param {string} key
	 * @returns {string}
	 *
	 * @memberOf Helper
	 */
	static wrapKeyInBrackets(key: string): string {
		const wrapInBrackets = (item: string) => { return "['" + item + "']"; };
		let sanitizedSourceKey = [];
		if (key.indexOf(" ") && key.indexOf(".")) {
			sanitizedSourceKey = key.split(".").map(wrapInBrackets);
		} else {
			sanitizedSourceKey.push(wrapInBrackets(key));
		}
		return sanitizedSourceKey.join("");
	}

	static cleanNulls(obj: IMap<any>): void {
		for (const propName in obj) {
			if (obj[propName] === null || obj[propName] === undefined) {
				delete obj[propName];
			} else if (typeof obj[propName] === "object") {
				Helper.cleanNulls(obj[propName]);
			}
		}
	}

	/**
	 * Returns a string clean from new lines and tabs caused by using template literal
	 * @param templateLiteral
	 */
	static cleanTemplateLiteral(templateLiteral: string): string {
		return templateLiteral
			.replace(/(\r\n\t|\n|\r\t)/gm, "")
			.replace(/[\t]+/gm, "");
	}

	/**
	 * Method to HTML encode any string so it can safely be used in UI
	 * @param value
	 * @returns HTML encoded value
	 */
	static htmlEncode(value: string): string {
		return $("<div/>").text(value).html();
	}

	/**
	 * Method to HTML decode any string, reverse method of htmlEncode
	 * @param value
	 * @returns HTML value
	 */
	static htmlDecode(value: string): string {
		return $("<div/>").html(value).text();
	}

	static isMobile(): boolean {
		return /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent);
	}

	/** Generate unique id. The ID is calculated by current time + a random number
	*
	* @return A unique ID
	*/
	static generateUniqueId(): string {
		return (new Date()).getTime() + "" + Math.floor((Math.random() * Numbers.MILLION) + 1);
	}

	/**
	 * Add the suffix "_copy" and a number, depending on the conflicting candidates
	 * As the items in conflictingCandidates can be anything now, this function also checks
	 * that this is a proper string
	 *
	 * @param {string} oldDisplayName
	 * @param {string[]} conflictingCandidates
	 *
	 */
	static generateNewDisplayName(oldDisplayName: string, conflictingCandidates: Array<any> = []): string {
		if (!oldDisplayName) {
			return "Lasagna al forno";
		}
		const splitCopyNumber = /(.+?)(_[Cc]opy(\d*))?$/;
		let newDisplayName: string;
		const splitOldDisplayName = oldDisplayName.match(splitCopyNumber);
		const oldDisplayNameBase = splitOldDisplayName[1];
		const numbers: number[] = [];
		conflictingCandidates.filter((maybeString: any) => {
			return (typeof maybeString === "string") ? true : false;
		}).forEach((conflictCandidate: string) => {
			const matches = conflictCandidate.match(splitCopyNumber);
			const candidateBaseName = matches[RegExpMatchCandidate.BASE_NAME];
			const candidateIsCopy = Boolean(matches[RegExpMatchCandidate.IS_COPY]);
			const candidateNumber = parseInt(matches[RegExpMatchCandidate.NUMBER]);

			if (oldDisplayNameBase === candidateBaseName && candidateIsCopy) {
				if (candidateNumber) {
					numbers.push(parseInt(matches[RegExpMatchCandidate.NUMBER]));
				} else {
					numbers.push(1);
				}
			}
		});
		newDisplayName = oldDisplayNameBase + ComponentConstants.COPY_SUFFIX;
		if (numbers.length > 0) {
			const highestNum = numbers.sort((a: number, b: number) => {
				return a - b;
			}).pop();
			newDisplayName += (highestNum + 1);
		}
		return newDisplayName;
	}

	/**
	* Can be used for any array with a name-property in it to
	* populate a list with only the names.
	*
	* @param {any[]} items
	* @returns {string[]}
	* @memberof Helper
	*
	*/
	static getListNames(items: any[]): string[] {
		const itemNames: string[] = [];
		items.forEach((item) => {
			itemNames.push((item as any).name);
		});
		return itemNames;
	}

	static getProposedNewDisplayName(projectName: string, projects: IProjectInfo[], exceptID?: string): string {
		let proposedName = projectName;
		const recursiveInnerFunction = (counter: number): string => {

			for (const index in projects) {
				if (exceptID && exceptID === projects[index].manifest.id) {
					continue;
				}
				if (projects[index].manifest.displayName === proposedName) {
					proposedName = projectName + counter;
					return recursiveInnerFunction(counter + 1);
				}
			}

			return proposedName;
		};

		return recursiveInnerFunction(1);
	}

	static joinWithDefauldEmptyString(stringArray: string[], joinChar: string): string {
		if (this.isUndefined(joinChar)) {
			joinChar = "";
		}
		return stringArray.join(joinChar);
	}

	static appIsReadyForSelenium(): void {
		angular.element("." + CSSClasses.mashupRootContainer).data(ElementDataKeys.isMashupReady, true);
	}

	/**
	 *	Parse query string key-value pairs into a dictionary.
		* @param query a query-string, e.g '?a=1&b=2' or 'x=y'
		*/
	static parseQueryString(query: string): IMap<string> {
		const rPlus = /\+/g;
		const rQueryParameter = /([^&=]+)=?([^&]*)/g;
		const decode = (uriEncodedText: string) => decodeURIComponent(uriEncodedText.replace(rPlus, " "));
		const urlParams: IMap<string> = {};

		let match = rQueryParameter.exec(query);
		while (match) {
			const nameIndex = 1;
			const valueIndex = 2;
			const name = decode(match[nameIndex]);
			const value = decode(match[valueIndex]);
			urlParams[name] = value;
			match = rQueryParameter.exec(query);
		}
		return urlParams;
	}

	/**
	 * Will trim value if it is string and return it
	 * @param value
	 */
	static trimIfPossible(value: any): string {
		if (typeof value === "string") {
			value = value.trim(); // Trim used to fix MI problem
		}
		return value;
	}

	/**
	 * Returns true if Safari
	 *
	 * @static
	 * @returns {boolean}
	 *
	 * @memberOf Helper
	 */
	static isSafari(): boolean {
		const myWindow: any = window;
		const isChrome = !!myWindow.chrome && !!myWindow.chrome.webstore;
		return (Object.prototype.toString.call(myWindow.HTMLElement).indexOf("Constructor") > 0 ||
			!isChrome && myWindow.webkitAudioContext !== undefined);
	}

	/**
	 * Returns true if Edge
	 *
	 * @static
	 * @returns {boolean}
	 *
	 * @memberOf Helper
	 */
	static isEdge(): boolean {
		return window.navigator.userAgent.indexOf("Edge") > -1;
	}

	/**
	 * Returns true if IE
	 *
	 * @static
	 * @returns {boolean}
	 *
	 * @memberOf Helper
	 */
	static isIE(): boolean {
		return /*@cc_on!@*/false || !!(document as any).documentMode;
	}

	static convertBase64ToSaveableBlob(base64String: string, type: { type: string }): SmartBlob {
		const byteString = atob(base64String);
		const arrayBuffer = new ArrayBuffer(byteString.length);
		const ia = new Uint8Array(arrayBuffer);

		for (let i = 0; i < byteString.length; i++) {
			ia[i] = byteString.charCodeAt(i);
		}

		return new SmartBlob([arrayBuffer], type);
	}

	static getBase64FileExtension(value: string): string {
		//tslint:disable-next-line:no-magic-numbers
		const data = value.substring(0, 5);

		switch (data.toUpperCase()) {
			case "IVBOR":
				return "png";
			case "/9J/4":
				return "jpeg";
			case "R0lGO":
				return "gif";
			case "UklGR":
				return "webp";
			case "AAAAF":
				return "mp4";
			case "JVBER":
				return "pdf";
			case "AAABA":
				return "ico";
			case "UMFYI":
				return "rar";
			case "E1XYD":
				return "rtf";
			case "U1PKC":
				return "txt";
			case "MQOWM":
			case "77U/M":
				return "srt";
			default:
				return "";
		}
	}

	static parseBase64(value: string): string {
		if (value.indexOf("data") === -1) {
			const extension = this.getBase64FileExtension(value);

			return `data:image/${extension};base64,${value}`
				.replace(RegexStrings.NEW_LINES, "");
		}

		return value;
	}

	/**
	 * Checks if two arrays of objects are different.
	 *
	 * @static
	 * @param array1 First Array
	 * @param array2 Second Array
	 * @return True if the arrays are different, otherwise false.
	 */
	static isDifferent(array1: any[], array2: any[]): boolean {
		function compare(x: any[], y: any[]): any[] {
			return filter(x, (object) => {
				return !find(y, object);
			});
		}
		return union(compare(array1, array2), compare(array2, array1)).length > 0;
	}

	/**
	 * Remove an item from list by index number.
	 * This is a mutable change
	 */
	static removeItemFromListByIndex(list: any[], index: number): void {
		list.splice(index, 1);
	}

	static instanceOfLayoutComponent(object: any): object is ILayoutComponent {
		return "children" in object;
	}

	static instanceOfListComponent(object: any): object is IListComponent {
		return "children" in object;
	}

	static isFirstCharLetter(string: string): boolean {
		return RegexStrings.FIRST_CHAR_LETTER.test(string);
	}
}

export class SmartBlob {
	private smartBlob: Blob;

	constructor(blobParts?: any[], type?: BlobPropertyBag) {
		this.smartBlob = new Blob(blobParts, type);
	}

	/**
	 * Save Blob to disk
	 * @param fileName
	 */
	saveAs(fileName: string): void {
		if (Helper.isIE() || window.navigator.msSaveOrOpenBlob) {
			window.navigator.msSaveOrOpenBlob(this.smartBlob, fileName);
		} else {
			const url = URL.createObjectURL(this.smartBlob);
			const a = document.createElement("a");

			a.href = url;
			a.download = fileName;
			document.body.appendChild(a);
			a.click();
			setTimeout(() => {
				document.body.removeChild(a);
				window.URL.revokeObjectURL(url);
			}, 0);

		}
	}
}
