import { ComponentClassNames } from "ng2/common/constants/common.constant";

import type { IEditorComponent } from "common/interface/ieditor-component";
import type { IJsonRadioButtons } from "common/model/component/radiobutton.model";
import type { IEditorFlexbox } from "editor/components/editor-flexbox";

import { RadioButton } from "components/radiobutton/radiobutton";
import { EditorComponentCtrl, IEditorComponentScope } from "editor/components/editor-component";

export interface IEditorRadioButtons extends IEditorComponent, RadioButton {
	parent: IEditorFlexbox;
	// Overrides to make dual extend work
	getScope(): IEditorComponentScope;
	getDatabearer(): IJsonRadioButtons;
}

export class EditorRadioButtonCtrl extends EditorComponentCtrl {
	public static add(m: ng.IModule): void {
		m.controller(
			ComponentClassNames.EDITOR_COMPONENT_PREFIX + RadioButton.name,
			EditorRadioButtonCtrl
		);
	}

	protected component: IEditorRadioButtons;

}
