import angular from "angular";

import {
	ABFormScriptMethod,
	Config,
	InjectNames,
	MGPlatform,
	ReplaceEnum,
	SaveFlagsEnum,
	ServerPaths
} from "ng2/common/constants/common.constant";

import type { IJsonManifest } from "ng2/common/model/databearer/json-manifest.model";
import type { IJsonDesignerMashup } from "ng2/common/model/databearer/json-mashup.model";

import { AngularConstants } from "common/angular-constants";
import type { IExportDialogParams } from "common/dialogs/export/export-dialog";
import type { IErrorResponse } from "common/error-response";
import { WSForm } from "etc-assets/user-control-es";
import { DesignerMashup } from "ng2/common/project/designer-mashup";

import { Helper } from "common/helper";
import { DesignerHelper } from "designer/helper";

import { CommonService } from "common/services/common-service";
import {
	DialogButtonType, DialogService, IDialogOptions, IDialogResult,
	IMessageDialogOptions, IToastOptions, StandardDialogButtons
} from "common/services/dialog-service";
import { HttpService } from "common/services/http-service";

export class ProjectService {

	static $inject = [
		AngularConstants.WINDOW,
		InjectNames.HTTP_SERVICE,
		InjectNames.COMMON_SERVICE,
		InjectNames.DIALOG_SERVICE
	];

	constructor(
		private $window: ng.IWindowService,
		private httpService: HttpService,
		private commonService: CommonService,
		private dialogService: DialogService
	) { }

	createProject(project: IJsonDesignerMashup): ng.IPromise<DesignerMashup> {

		let saveData;
		if (DEV_SERVER) {
			const id = Helper.generateUniqueId();

			project.manifest.id = id;
			saveData = { path: id, data: project };

		} else {
			saveData = project;
		}
		return this.httpService
			.executePost<IJsonDesignerMashup>(ServerPaths.PROJECT, saveData)
			.then((project) => {
				this.commonService.unloadProject();
				const designerMashup = DesignerMashup.fromObj(project);
				this.commonService.setCurrentProject(designerMashup, false);
				return designerMashup;
			});
	}

	duplicate(manifest: IJsonManifest): void {
		this.commonService.loadProjects().then((projects) => {
			const originProject: IJsonManifest = angular.copy(manifest);
			const options: IDialogOptions<string> = {
				title: "Duplicate Project",
				template: require("../dashboard/duplicate-dialog/duplicate-dialog.html"),
				// TODO improve name proposal
				parameter: DesignerHelper.getProposedNewDisplayName(originProject.displayName, projects),
			};

			this.dialogService.show(options).then((result: IDialogResult<string>) => {
				if (result.button === DialogButtonType.Ok) {
					this.duplicateProject(originProject.id, result.value).then((newId: string) => {
						this.commonService.onDuplicateProjectChangedEvent.raise();
					}).catch((errorResponse: IErrorResponse) => {
						const message =
							`Failed to duplicate project: ${options.parameter}. Reason: ${errorResponse.message}`;
						this.dialogService.showError("Operation Failed", message);
					});
				}
			});
		}).catch((error: IErrorResponse) => {
			this.dialogService.showError("Did not load projects", error.message);
		});
	}

	publish(projectId: string, displayName: string): void {
		const options: IMessageDialogOptions = {
			title: "Publish Project",
			message: "Make App available for selected users.",
			standardButtons: StandardDialogButtons.YesNo,
		};

		this.dialogService.showMessage(options).then((result: IDialogResult<string>) => {
			if (result && result.button === DialogButtonType.Yes) {
				this.addToRuntime(projectId)
					.then(() => {
						const options: IToastOptions = {
							title: "Published",
							message: "Successfully published " + displayName,
							position: "top right",
							progressBar: true,
							timeout: 3000
						};
						this.dialogService.showToast(options);
					})
					.catch((errorResponse: IErrorResponse) => {
						this.dialogService.showError("Unsuccessful Publish", errorResponse.message);
					});
			}
		});
	}

	launchProject(projectId: string): void {
		DesignerHelper.openRuntime(projectId, this.$window);
	}

	renameProject(id: string, newDisplayName: string): ng.IPromise<string> {
		return this.httpService.executePost(ServerPaths.PROJECT + "rename/" + id, newDisplayName);
	}

	deleteByIds(projectIds: string[]): ng.IPromise<void> {
		return this.httpService.executeDelete(ServerPaths.PROJECT + "dashboard/", projectIds).then(() => {
			const currentProject = this.commonService.getCurrentProject();
			if (currentProject) {
				const unloadCurrentProject = projectIds.find((projectId) => {
					return projectId === currentProject.getId();
				});
				if (unloadCurrentProject) {
					this.commonService.unloadProject();
				}
			}
		});
	}

	deleteById(projectId: string): ng.IPromise<void> {
		return this.httpService.executeDelete(ServerPaths.PROJECT + projectId).then(() => {
			const currentProject = this.commonService.getCurrentProject();
			if (currentProject && currentProject.getId() === projectId) {
				this.commonService.unloadProject();
			}
		});
	}

	export(projectId: string, displayName: string): void {
		if (Config.MG_PLATFORM === MGPlatform.WEB) {
			const options: IDialogOptions<IExportDialogParams> = {
				title: "Export Project",
				template: require("common/dialogs/export/export-dialog.html"),
				parameter: {
					type: "project",
					filename: displayName,
				}
			};
			this.dialogService.show(options).then((result: IDialogResult<string>) => {
				if (result.button === DialogButtonType.Ok) {
					this.exportProject(projectId, result.value);
				}
			});
		} else { // MG_PLATFORM === WinStudio
			WSForm.call({
				name: ABFormScriptMethod.SHOW_SAVE_FILE_DIALOG,
				parameters: [
					`${displayName}.iap`, // Filename
					"Export Filename",  // Caption
					"AppBuilder Project (*.iap)|*.iap" // Filter
				],
				onCompleted: filePath => {
					this.exportProject(projectId, filePath);
				}
			});
		}
	}

	private exportProject(exportProjectId: string, exportFileName: string): void {
		const resourceName = ServerPaths.PROJECT + exportProjectId + "/export/";
		this.httpService.executeGet<{ base64: string, filename: string }>(
			resourceName, exportFileName)
			.then((exportResult: { base64: string, filename: string }) => {
				if (Config.MG_PLATFORM === MGPlatform.WEB) {
					const blob = Helper.convertBase64ToSaveableBlob(
						exportResult.base64, { type: "application/zip" });

					blob.saveAs(exportResult.filename);
				}
			});
	}

	importProject(
		projectFilePath: string,
		replace: ReplaceEnum,
		newName: string = null
	): ng.IPromise<IJsonDesignerMashup> {
		const url = ServerPaths.PROJECT + "import/" + replace;
		const data = { newDisplayName: newName, filePath: projectFilePath };
		return this.httpService.executePost<IJsonDesignerMashup>(url, data);
	}

	exportProjectContent(
		projectId: string,
		contentIds: string[],
		filePath: string,
		saveFlagEnum: SaveFlagsEnum
	): void {
		this.commonService.exportProjectContent(
			contentIds, filePath, saveFlagEnum, projectId);
	}

	copyToForm(projectId: string, formName: string): ng.IPromise<void> {
		const url = ServerPaths.PROJECT + projectId + "/form/";
		return this.httpService.executePost<void>(url, formName);
	}

	private duplicateProject(originProjectId: string, newDisplayName: string): ng.IPromise<string> {
		return this.httpService.executePost(ServerPaths.PROJECT + "duplicate/" + originProjectId, newDisplayName);
	}

	private addToRuntime(projectId: string): ng.IPromise<string> {
		return this.httpService.executeGet(ServerPaths.PROJECT + projectId + "/publish/" + 0);
	}

	static add(ngModule: ng.IModule): void {
		ngModule.service(InjectNames.PROJECT_SERVICE, ProjectService);
	}
}
