import {
	ChangeDetectorRef,
	Component,
	ElementRef,
	Renderer2,
	ViewChild,
} from "@angular/core";
import { SohoDropDownComponent, SohoMessageService } from "ids-enterprise-ng";
import { Observable, of } from "rxjs";

import { InteractionTriggerEnum } from "ng2/common/constants/common.constant";

import { ValueTypeEnum } from "ng2/common/enums/value-type.enum";

import type { IJsonDropdown } from "ng2/common/model/databearer/components/json-dropdown.model";
import type { IMap } from "ng2/common/model/imap.model";

import { DropdownMetadata } from "ng2/components/dropdown/dropdown.metadata";

import { BackwardCompatible } from "ng2/common/project/backward-compatible";
import { BaseComponent } from "ng2/components/base/component";

import { DynamicComponentService } from "ng2/components/services/dynamic-component.service";
import { FrameworkService } from "ng2/components/services/framework.service";

export interface ICurrentItem extends IMap<any> { }

@Component({
	selector: "ng2-button",
	template: `
		<div [id]="id" class="dropdown-container">
			<label
				*ngIf="showLabel"
				class="label"
				[for]="id"
			>
				{{ label }}
			</label>
		</div>
		<select soho-dropdown soho-busyindicator
			[name]="id"
			[(ngModel)]="currentItem"
			(change)="onChange($event)"
		>
			<option
				*ngFor="let item of (contextData$ | async)"
				[ngValue]="item"
			>
				{{ item[databearer.displayValue.sourceKey] }}
			</option>
		</select>
	`
})
export class DropdownComponent extends BaseComponent {

	label: string;
	showLabel: boolean;
	displayValue: string;
	currentItem: ICurrentItem;
	currentItemIndex: number;

	databearer: IJsonDropdown;

	contextData$: Observable<any[]>;

	@ViewChild(SohoDropDownComponent, {
		static: true
	}) dropDownComponent: SohoDropDownComponent;

	constructor(
		elementRef: ElementRef,
		renderer: Renderer2,
		changeDetectorRef: ChangeDetectorRef,
		frameworkService: FrameworkService,
		dynamicComponentService: DynamicComponentService,
		sohoMessageService: SohoMessageService,
	) {
		super(
			elementRef,
			renderer,
			changeDetectorRef,
			frameworkService,
			dynamicComponentService,
			sohoMessageService
		);
	}

	onInitComponent(): void {
		super.onInitComponent();

		this.showLabel = BackwardCompatible.getShowLabel(this.databearer);
	}

	initializeBinding(componentDatabearer: IJsonDropdown): void {
		DropdownMetadata.BINDABLE_FIELDS_2.forEach(field => {
			switch (field.valueType) {
				case ValueTypeEnum.OBJECT: // 4
					break;
				case ValueTypeEnum.STRING: // 1
				case ValueTypeEnum.LOCALIZED_STRING: // 7
				default: {
					const self: any = this;
					self[field.ref] = componentDatabearer[field.ref]?.value;
				}
			}
		});
	}

	dataServiceCompleted(component: DropdownComponent): void {
		super.dataServiceCompleted(component);

		component.currentItem = component.context.ListResult[0];
		component.contextData$ = of(component.context.ListResult);

		this.refreshComponent();
		this.onChange();
	}

	/**
	 * This is needed because. IDS dropdown does not have item in it until first action.
	 */
	private refreshComponent(): void {
		this.dropDownComponent.setFocus();
		$(":focus").blur();
	}

	onChange(event?: SohoDropDownEvent): void {

		console.info(event);

		this.trigger(InteractionTriggerEnum.CURRENT_ITEM_CHANGED);
		this.updateBindings();
	}

}
