import jsonpath from "jsonpath";

import { InjectNames } from "ng2/common/constants/common.constant";

import { ParameterTypeEnum } from "ng2/common/enums/parameter-type.enum";
import { ValueTypeEnum } from "ng2/common/enums/value-type.enum";

import type { IDataPair } from "ng2/common/model/data-pair.model";
import type { IJsonGenericDatasource2 } from "ng2/common/model/databearer/dataservice/json-datasource.model";
import type { IParameterObject } from "ng2/common/model/dataservice/parameter-object.model";
import type { IMap } from "ng2/common/model/imap.model";
import type { IDataGridColumn, IDataGridOptions } from "ng2/common/model/sohoxi/sohoxi-datagrid.model";
import type * as sw from "ng2/common/model/swagger.model";

import type { IFieldValidationResult, IValidatorError } from "common/shared-components/base/field-validation";
import { IOnComponentChangeNotification, NotifiableComponent } from "common/shared-components/base/notifiable-component";
import { CustomFormatters } from "common/sohoxi/custom-formatters";
import { DatasourceCreateModeEnum } from "designer/repository/datasource/create-wizard/datasource-wizard-ctrl";

import { Helper } from "common/helper";
import { DatasourceWizardHelper } from "designer/repository/datasource/create-wizard/datasource-wizard-helper";
import { DataServiceHelper } from "ng2/common/helpers/data-service.helper";

import { DialogButtonType, DialogService } from "common/services/dialog-service";

export class DatasourceParameterCtrl extends NotifiableComponent {
	static $inject = [
		InjectNames.DIALOG_SERVICE
	];

	parameterTypes: IDataPair[];
	ParameterTypeEnum = ParameterTypeEnum;
	isPathParameter: boolean;
	invalidParameters: boolean[] = [];
	requiredParameters: boolean[] = [];
	get isInput(): boolean { return this.isInputPriv; }
	restPath: string;
	matrixPath = "";
	queryPath: string;

	private bindingDatasourceParameters: IParameterObject[];
	private isInputPriv: boolean;
	parameterDatagridOptions: IDataGridOptions<IParameterObject>;

	// Component Bindings
	headTitle: string;
	swaggerOperation: sw.IOperationObject;
	get datasourceParameters(): IParameterObject[] { return this.bindingDatasourceParameters; }
	set datasourceParameters(value: IParameterObject[]) {
		this.bindingDatasourceParameters = value;
	}
	get datasourceResourceUrl(): string {
		return this.restPath + this.matrixPath + this.queryPath;
	}
	set datasourceResourceUrl(value: string) {
		[this.restPath, this.matrixPath, this.queryPath] = DataServiceHelper.deconstructPath(value);
	}
	datasourceCreateMode: DatasourceCreateModeEnum;
	wizardNextDisabled: ($item: any) => void;
	outputRootElement: string;
	showHelp: () => void;
	private generateOutputParameters: (datasource: any) => void;
	private generateInputParameters: (datasource: any) => void;
	private datasource: IJsonGenericDatasource2;
	readonly: boolean;

	privateErrorList: IValidatorError[] = [];
	set errorList(value: IValidatorError[]) {
		this.privateErrorList = value;
	}
	get errorList(): IValidatorError[] {
		const item = this.privateErrorList.length !== 0;

		if (this.wizardNextDisabled) {
			this.wizardNextDisabled({
				$item: {
					id: DatasourceParameterComponent.componentName,
					status: item
				}
			});
		}

		return this.privateErrorList;
	}

	get isInvalid(): boolean {
		const json = this.isInput ? this.datasource.bodyTemplate : this.datasource.outputJson;
		try {
			if (this.datasource) {
				jsonpath.query(JSON.parse(json), "$");
				return false;
			}
			return true;
		} catch (e) {
			return true;
		}
	}

	constructor(
		private dialogService: DialogService
	) {
		super();
	}

	$onInit(): void {
		if (this.headTitle.indexOf("Input") !== -1) {
			if (this.swaggerOperation && this.swaggerOperation.parameters) {
				this.swaggerOperation.parameters.forEach((swaggerParameter) => {
					if (swaggerParameter.required) {
						this.bindingDatasourceParameters.forEach((datasourceParameter: IParameterObject, index) => {
							if (swaggerParameter.in === "query" && swaggerParameter.name === datasourceParameter.key) {
								this.requiredParameters[index] = true;
							}
						});
					}
				});
			}
			this.isInputPriv = true;
			this.parameterTypes = [
				{ name: "Path", value: ParameterTypeEnum.PATH },
				{ name: "Matrix", value: ParameterTypeEnum.MATRIX },
				{ name: "Query", value: ParameterTypeEnum.QUERY },
				{ name: "Body(jsonpath)", value: ParameterTypeEnum.BODY_PART }
			];
		} else {
			this.parameterTypes = [];
		}
		this.initParameterDatagrid();
	}

	private initParameterDatagrid(): void {
		const columns: IDataGridColumn[] = [
			{
				width: "30%",
				id: "key",
				name: "Name",
				field: "key",
				sortable: false
			},
			{
				width: "30%",
				id: "parameterType",
				name: "Type",
				field: "parameterType",
				sortable: false,
				formatter: CustomFormatters.DataSourceParameterType()
			},
			{
				width: "30%",
				id: "alias",
				name: "Alias",
				field: "alias",
				sortable: false
			},
		];

		this.parameterDatagridOptions = {
			columns: columns,
			isList: true,
			selectable: false,
			dataset: this.datasourceParameters,
			rowHeight: "short"
		};
	}

	generateParameters(): void {
		if (this.isInput) {
			this.generateInputParameters({ $datasource: this.datasource });
		} else {
			this.generateOutputParameters({ $datasource: this.datasource });
		}
	}

	isUrlComponent(type: ParameterTypeEnum): boolean {
		switch (type) {
			case ParameterTypeEnum.MATRIX:
			case ParameterTypeEnum.PATH:
			case ParameterTypeEnum.QUERY:
				return true;
			default:
				return false;
		}
	}

	getParameterTypeName(type: ParameterTypeEnum): string {
		let name: string;
		this.parameterTypes.some((dataPair) => {
			name = dataPair.name;
			return dataPair.value === type;
		});
		return name;
	}

	onDeleteDatasourceParameter(item: IParameterObject): void {
		const deleteParameter = () => {
			switch (item.parameterType) {
				case ParameterTypeEnum.PATH:
					this.updateRestPath(item, true);
					break;
				case ParameterTypeEnum.MATRIX:
					this.updateRestMatrixPath(item, true);
					break;
				case ParameterTypeEnum.QUERY:
					this.updateRestQueryPath(item, true);
					break;
				default:
					console.info("No change in REST path");
			}
			const index = this.bindingDatasourceParameters.indexOf(item);
			if (index !== -1) {
				this.bindingDatasourceParameters.splice(index, 1);
			}

			// clear nextDisabled if needed
			this.privateErrorList = this.privateErrorList.filter(error => error.field !== item.id);
			this.wizardNextDisabled({
				$item: {
					id: DatasourceParameterComponent.componentName,
					status: this.privateErrorList.length !== 0
				} as IFieldValidationResult
			});
		};

		if (item.required) {
			switch (this.datasourceCreateMode) {
				case DatasourceCreateModeEnum.EDIT:
				case DatasourceCreateModeEnum.FROM_SWAGGER:
					this.dialogService.showYesNo(
						"Warning",
						"Deleting mandatory parameter may cause data service to stop working. Do you want to continue?",
						"warning-text")
						.then(result => {
							if (result.button === DialogButtonType.Yes) {
								deleteParameter();
							}
						});
					break;
				case DatasourceCreateModeEnum.FROM_SCRATCH:
				default:
					deleteParameter();
			}
		} else {
			deleteParameter();
		}

	}

	onAddDatasourceParameter(type: ParameterTypeEnum): void {
		const newItem: IParameterObject = {
			id: Helper.generateUniqueId(),
			key: this.getNewName(type),
			valueType: ValueTypeEnum.STRING,
			parameterType: type,
			required: type === ParameterTypeEnum.PATH,
			alias: ""
		};

		switch (type) {
			case ParameterTypeEnum.PATH: {
				const index = this.bindingDatasourceParameters.findIndex((item) => {
					return item.parameterType === ParameterTypeEnum.QUERY;
				});
				if (index !== -1) {
					this.bindingDatasourceParameters.splice(index, 0, newItem);
				} else {
					this.bindingDatasourceParameters.push(newItem);
				}
				this.updateRestPath(newItem);
				break;
			}
			case ParameterTypeEnum.MATRIX:
				this.bindingDatasourceParameters.push(newItem);
				this.updateRestMatrixPath(newItem);
				break;
			case ParameterTypeEnum.QUERY:
				this.bindingDatasourceParameters.push(newItem);
				this.updateRestQueryPath(newItem);
				break;
			case ParameterTypeEnum.BODY_PART:
				this.bindingDatasourceParameters.push(newItem);
				break;
			case ParameterTypeEnum.OUTPUT:
				this.bindingDatasourceParameters.push(newItem);
				break;
			default:
				console.warn("unknown");

				this.isPathParameter = newItem.parameterType === ParameterTypeEnum.PATH;
		}
	}

	private getNextParamName(baseName: string): string {
		const elementMatch = this.bindingDatasourceParameters.filter((item) => item.key.match(baseName + "[0-9]+$"));
		if (elementMatch.length === 0) {
			return baseName + 0;
		}
		const lastElement = elementMatch[elementMatch.length - 1];
		const next = parseInt(lastElement.key.match(/[0-9]+$/g).join()) + 1;
		return baseName + next;
	}

	private getNewName(type: ParameterTypeEnum): string {
		switch (type) {
			case ParameterTypeEnum.PATH:
				return this.getNextParamName("path");
			case ParameterTypeEnum.MATRIX:
				return this.getNextParamName("matrix");
			case ParameterTypeEnum.QUERY:
				return this.getNextParamName("query");
			case ParameterTypeEnum.BODY_PART:
				return "$";
			case ParameterTypeEnum.OUTPUT:
			default:
				return "";
		}
	}

	private updateRestPath(newItem: IParameterObject, remove?: boolean): void {
		if (remove) {
			this.restPath = this.restPath
				.replace("/" + DataServiceHelper.getURLVariable(newItem.key, ParameterTypeEnum.PATH), "");
		} else {
			this.restPath += (this.restPath.endsWith("/") ? "" : "/") +
				DataServiceHelper.getURLVariable(newItem.key, ParameterTypeEnum.PATH);
		}
	}

	private updateRestMatrixPath(newItem: IParameterObject, remove?: boolean): void {
		const deletedItem = (item: string) => { return item.split("=")[0] !== encodeURIComponent(newItem.key); };
		if (remove) {
			this.matrixPath = this.matrixPath.split(";")
				.filter(DataServiceHelper.emptyItem).filter(deletedItem).join(";");
		} else {
			this.matrixPath += ";" + DataServiceHelper.getURLVariable(newItem.key, ParameterTypeEnum.MATRIX);
		}
	}

	private updateRestQueryPath(newItem: IParameterObject, remove?: boolean): void {
		const removeAnyFirstQuestionmarks = (item: string) => {
			return item.charAt(0) === "?" ? item.slice(1) : item;
		};
		const deletedItem = (item: string) => {
			const key = item.split("=")[0];
			return key !== encodeURIComponent(newItem.key);
		};
		if (remove) {
			// removes any first questionmarks and filters out the item to be deleted
			const newQueryPath = this.queryPath
				.split("&")
				.map(removeAnyFirstQuestionmarks)
				.filter(deletedItem)
				.join("&");

			// Adds the questionmark to the beginning only of there is any querypath left.
			this.queryPath = newQueryPath === ""
				? newQueryPath
				: "?" + newQueryPath;

		} else {
			this.queryPath +=
				(this.queryPath === "" ? "?" : "&")
				+ DataServiceHelper.getURLVariable(newItem.key, ParameterTypeEnum.QUERY);
		}
	}

	onUpdateResourceUrl(newItem: IParameterObject, oldItem: IParameterObject, index: number): void {
		if (this.isInput && newItem.key.length === 0) { // Default value and fallback when user erases all chars
			newItem.key = this.getNewName(newItem.parameterType);
		}
		switch (newItem.parameterType) {
			case ParameterTypeEnum.PATH:
			case ParameterTypeEnum.MATRIX:
			case ParameterTypeEnum.QUERY:
				this.datasourceResourceUrl =
					this.datasourceResourceUrl.replace(
						DataServiceHelper.getURLVariable(oldItem.key, oldItem.parameterType),
						DataServiceHelper.getURLVariable(newItem.key, newItem.parameterType)
					);
				break;
			case ParameterTypeEnum.OUTPUT:
				try {
					jsonpath.parse(this.outputRootElement + newItem.key);
					const foundMatch = jsonpath.query(
						JSON.parse(this.datasource.outputJson), this.outputRootElement + newItem.key);
					if (foundMatch.length === 1) {
						newItem.dataType = DatasourceWizardHelper
							.getParameterType(foundMatch[0], this.datasource.suite);
						this.invalidParameters[index] = false;
					} else {
						throw new Error("No match found in Schema");
					}
				} catch (error) {
					//console.warn(error);
					this.invalidParameters[index] = true;
				}
				break;
			case ParameterTypeEnum.BODY_PART:
				try {
					jsonpath.parse(newItem.key);
					this.invalidParameters[index] = false;
				} catch (error) {
					this.invalidParameters[index] = true;
				}
				break;
			default:
				break;
		}
		this.bindingDatasourceParameters[index] = newItem;
	}

	isAliasUnique(alias: string, id: string): boolean {
		if (Helper.isUndefinedEmpty(alias)) { // alias is empty string skip checking
			return true;
		}

		const parameters = this.bindingDatasourceParameters;
		// alias check comes from same id skip checking
		const checkAlias = parameters.filter(parameter => parameter.alias.toLowerCase() === alias.toLowerCase());
		if (checkAlias.length > 1) {
			return false;
		} else if (checkAlias.length === 1 && checkAlias[0].id === id) {
			return true;
		} else if (checkAlias.length === 1 && checkAlias[0].id !== id) {
			return false;
		}

		return true;
	}

	getValidation($data: IOnComponentChangeNotification): boolean {
		let result: boolean;
		if ($data.validator === "isAliasUnique") {
			result = this.isAliasUnique($data.value, $data.id);
		} else if ($data.validator === "isFirstCharLetter") {
			result = $data.value ? Helper.isFirstCharLetter($data.value) : true;
		}

		return result;
	}
}

export class DatasourceParameterComponent implements ng.IComponentOptions {

	static readonly componentName: string = "eirDatasourceParameter";
	controller: any;
	controllerAs: string;
	template: string;
	bindings: IMap<string>;

	constructor() {
		this.bindings = {
			headTitle: "@",
			swaggerOperation: "<",
			datasourceParameters: "=",
			datasourceResourceUrl: "=?",
			datasourceCreateMode: "=?",
			wizardNextDisabled: "&?",
			outputRootElement: "=",
			showHelp: "<",
			generateOutputParameters: "&",
			generateInputParameters: "&",
			datasource: "<",
			readonly: "<"
		};

		this.controller = DatasourceParameterCtrl;
		this.controllerAs = "ctrl";
		this.template = require("./datasource-parameter.html");
	}

	static add(m: ng.IModule): void {
		m.component(
			DatasourceParameterComponent.componentName,
			new DatasourceParameterComponent());
	}
}
