import { IComponentRendered } from "ng2/common/constants/sohoxi.constant";
import { IMap } from "ng2/common/model/imap.model";
import { IDatagridApi, IDataGridOptions } from "ng2/common/model/sohoxi/sohoxi-datagrid.model";

import { DATAGRID_RENDERED } from "../../sohoxi/component/datagrid-directive";
import { IBaseDatagridComponentSettings, IBaseDatagridCtrlSettings } from "./base-datagrid-settings";

export abstract class BaseDatagridCtrl<T> implements IBaseDatagridCtrlSettings<T> {
	public abstract datagridOptions: IDataGridOptions<T>;
	public api: IDatagridApi;

	// bindings
	public dataset: T[];
	public headTitle?: string;

	public $onInit(): void {
		this.initDatagridOptions();
	}

	public $onChanges(onChangesObject: ng.IOnChangesObject): void {}

	public abstract initDatagridOptions(): void;

	public constructor($element: ng.IAugmentedJQuery) {
		$element.on(DATAGRID_RENDERED,
			(event, result: IComponentRendered) => {
				this.api = result.api as IDatagridApi;
			});
	}

	public getDatagridApi(): IDatagridApi {
		return this.api;
	}
}

export abstract class BaseDatagridComponent implements IBaseDatagridComponentSettings {
	public abstract controller: string | ng.Injectable<ng.IControllerConstructor>;
	public template: string = require("./base-datagrid-template.html");
	public bindings: IMap<string> = {
		dataset: "<",
		headTitle: "@?",
	};
}
