import { IDataGridOptions } from "ng2/common/model/sohoxi/sohoxi-datagrid.model";

export class InlineListDataserviceSummaryDatagridCtrl {
	// component bindings
	public headTitle: string;
	public gridOptions: IDataGridOptions<any>;
	public summaryDatagridOptions: IDataGridOptions<any>;

	public $onInit(): void {
		this.initDatagridOptions();
	}

	private initDatagridOptions(): void {
		this.summaryDatagridOptions = {
			columns: this.gridOptions.columns,
			isList: true,
			selectable: false,
			dataset: this.gridOptions.dataset,
			rowHeight: "short"
		};
	}
}
