import { Utils } from "../utils";

/**
 * File Upload Directive.
 *
 * Used for binding a scope variable to selected file(s)
 * The name of the scope variable is specified as xi-fileupload="scopeVariableName"
 *
 * Example:
 * scope["files"]
 *
 * <label class="fileupload">
 * 	<span class="audible">Choose File</span>
 * 	<input type="file" name="file-input" xi-fileupload="chosenFile" />
 * </label>
 */
export class FileuploadDirective implements ng.IDirective {
	private static readonly DIRECTIVE_NAME = "xiFileupload";

	public restrict = "A";
	public scope = false;

	constructor(
		private timeout: ng.ITimeoutService,
		private parse: ng.IParseService
	) {}

	public link(
		scope: ng.IScope,
		element: ng.IAugmentedJQuery,
		attributes: ng.IAttributes
	): void {
		const expression = attributes[FileuploadDirective.DIRECTIVE_NAME];
		const parent = element.parent(".fileupload");
		if (!expression || parent.length === 0) {
			// Incorrect setup
			return;
		}
		element.bind("change.fileupload", () => {
			const files = (element[0] as any)["files"];

			this.parse(expression).assign(scope, files); // tslint:disable-line:no-string-literal
			scope.$apply();
		});
		// Timeout to let the label render
		this.timeout(() => {
			parent.fileupload();
		}, 1);
		parent.on("$destroy", () => {
			const fileUploadData = Utils.getData(parent, "fileupload");
			if (fileUploadData) {
				fileUploadData.destroy();
			}
		});
	}

	public static add(m: ng.IModule): void {
		m.directive(FileuploadDirective.DIRECTIVE_NAME, [
			"$timeout",
			"$parse",
			(timeout, parse) => {
				return new FileuploadDirective(timeout, parse);
			}
		]);
	}
}
