import { IMap } from "ng2/common/model/imap.model";
import { TextareaFieldCtrl } from "./textarea-field-ctrl";

export class TextareaFieldComponent implements ng.IComponentOptions {
	public controller: any;
	public controllerAs: string;
	public template: string;
	public bindings: IMap<string>;

	constructor() {
		this.bindings = {
			labelName: "@?",
			fieldValue: "=",
			validation: "<?",
			readonly: "<?",	// readonly field
		};

		this.controller = TextareaFieldCtrl;
		this.controllerAs = "ctrl";
		this.template = require("./textarea-field-template.html");
	}

	static add(m: ng.IModule): void {
		m.component("eirTextareaFieldComponent", new TextareaFieldComponent());
	}
}
