import { Debugger } from "../../debugger";
import { FeatureSwitchService } from "../../services/feature-switch-service";
import { FeatureSwitchCtrl } from "./feature-switch-ctrl";
import { IValidationResult } from "./field-validation";

/**
 * Extend this class if parent has components which needs to notify each other on changes
 */
export abstract class NotifiableComponent extends FeatureSwitchCtrl {
	public parentAnnounce: IParentAnnounce;

	public constructor(featureSwitchService?: FeatureSwitchService) {
		super(featureSwitchService);
	}

	protected notify($data: IOnComponentChangeNotification): boolean | IValidationResult {
		const result: boolean | IValidationResult = this.getValidation($data);

		if (!$data.callback) {
			Debugger.log(this, `$data.id="${$data.id}" - Notify Children To Update`);

			// tell children it is from this sibling which announced.
			// so that sibling knows it doesn't have to tell its parent about it.
			this.parentAnnounce = {
				id: $data.id
			};
		}

		return result;
	}

	protected getValidation(
		$data: IOnComponentChangeNotification
	): boolean | IValidationResult {
		return null;
	}
}

export interface IOnComponentChangeNotification {
	validator: string;
	id: string;
	value: string;
	callback: boolean;
}

export interface IParentAnnounce {
	id: string;
}
