import { AppState } from "../../../app.state";
import { AngularConstants } from "../../../common/angular-constants";
import { ViewEnum } from "../../../common/defines";
import type { IErrorResponse } from "../../../common/error-response";
import { CommonService } from "../../../common/services/common-service";
import {
	DialogButtonType, DialogService, IDialogResult,
	IMessageDialogOptions, StandardDialogButtons
} from "../../../common/services/dialog-service";
import type { IValidatorError } from "../../../common/shared-components/base/field-validation";
import type { IExpandedRowItem, IRowItem } from "../../../common/sohoxi/datagrid-helper";
import { DesignerService } from "../../services/designer-service";
import { ProjectService } from "../../services/project-service";

import {
	Config,
	DashboardStates,
	DashboardViewStates,
	INewProject,
	InjectNames,
	Numbers,
	SortEnum,
} from "ng2/common/constants/common.constant";
import { FormatterEnum, SelectableEnum } from "ng2/common/constants/sohoxi.constant";

import type { IDataPair } from "ng2/common/model/data-pair.model";
import type { IDesignerManifest } from "ng2/common/model/project/mashup/designer-manifest.model";
import type {
	IDatagridApi,
	IDataGridColumn,
	IDataGridOptions,
	IDataGridRow,
} from "ng2/common/model/sohoxi/sohoxi-datagrid.model";

import type { DesignerMashup } from "ng2/common/project/designer-mashup";
import { IProjectInfo, ProjectInfo } from "ng2/common/project/project-info";
import "../../images/createPage/02-thumb.png";

export class DashboardCtrl {
	get isLoading(): boolean {
		return Config.IS_FIRST_LOAD;
	}

	private errorList: IValidatorError[] = [];
	state = DashboardStates.LIST;
	viewState = DashboardViewStates.TILE;
	projects: IProjectInfo[];
	currentPage: IDesignerManifest[];
	currentProject: DesignerMashup;
	dashboardLoading: boolean;
	searchString = "";
	newProject: INewProject;
	isProjectChanged: boolean;

	filterTypeOptions: IDataPair[] = [
		{ name: "All Projects", value: undefined },
		{
			name: "Created by me", value: {
				manifest: {
					createdBy: Config.USER.name
				}
			}
		}
	];

	sortPropertiesOptions: IDataPair[] = [
		{ name: "Changed Date", value: "manifest.lastChangedTime" },
		{ name: "Created Date", value: "manifest.createdTime" },
		{ name: "Name", value: "manifest.displayName" }
	];

	filterEnum = SortEnum;
	filterContainer: string[] = [];
	datagridOptions: IDataGridOptions<IProjectInfo>;
	selectedProjects: IProjectInfo[] = [];

	static $inject = [
		AngularConstants.SCOPE,
		AngularConstants.ELEMENT,
		AngularConstants.STATE_SERVICE,
		InjectNames.COMMON_SERVICE,
		InjectNames.DESIGNER_SERVICE,
		InjectNames.DIALOG_SERVICE,
		InjectNames.PROJECT_SERVICE
	];

	constructor(
		private $scope: ng.IScope,
		private $element: ng.IAugmentedJQuery,
		private $stateService: ng.ui.IStateService,
		private commonService: CommonService,
		private designerService: DesignerService,
		private dialogService: DialogService,
		private projectService: ProjectService
	) {
		this.designerService.setView(ViewEnum.DASHBOARD);
		this.loadProjects().then(() => {
			this.currentProject = commonService.getCurrentProject() as DesignerMashup;
		});

		this.commonService.onDuplicateProjectChangedEvent.on(() => {
			this.loadProjects();
		});
	}

	initDatagrid(): void {
		this.initGridOptions();
	}

	finishedLoadingProjects(): void {
		this.commonService.$timeout(_ =>
			Config.IS_FIRST_LOAD = false, Numbers.ONE_THOUSAND);
	}

	private loadProjects(): ng.IPromise<void> {
		this.dashboardLoading = true;
		return this.commonService.loadProjects().then((projectInfo: IProjectInfo[]) => {
			this.dashboardLoading = false;
			// Convert JSON to Class and put to projects
			this.projects = projectInfo.map((projectInfo: IProjectInfo) => {
				return new ProjectInfo(projectInfo);
			});
			this.datagridOptions.dataset = this.projects;

			if (this.projects.length === 0) {
				this.finishedLoadingProjects();
			}
		}).catch((errorResponse: IErrorResponse) => {
			this.dashboardLoading = false;
			this.dialogService.showError("List projects", errorResponse.message);
		});
	}

	initGridOptions(): void {
		const columns: IDataGridColumn[] = [];
		columns.push({
			id: "selectionCheckbox",
			sortable: false,
			resizable: false,
			width: 50,
			formatter: Soho.Formatters.SelectionCheckbox,
			align: "center"
		});
		columns.push({
			id: "displayName",
			name: "Name",
			field: "manifest.displayName",
			width: 250,
			sortable: true,
			textOverflow: FormatterEnum.Ellipsis,
			formatter: Soho.Formatters.Expander
		});
		columns.push({
			id: "createdBy",
			name: "Created By",
			field: "manifest.createdByDisplayName",
			width: 150,
			sortable: true,
			formatter: Soho.Formatters.Ellipsis
		});
		columns.push({
			id: "createdTime",
			name: "Created",
			field: "manifest.createdTime",
			width: 100,
			sortable: true,
			formatter: Soho.Formatters.Date
		});
		columns.push({
			id: "lastChangedBy",
			name: "Last Changed By",
			field: "manifest.lastChangedByDisplayName",
			width: 150,
			sortable: true,
			formatter: Soho.Formatters.Ellipsis
		});
		columns.push({
			id: "lastChangedTime",
			name: "Last Changed",
			field: "manifest.lastChangedTime",
			width: 100,
			sortable: true,
			formatter: Soho.Formatters.Date
		});
		columns.push({
			id: "open",
			name: "Action",
			width: 50,
			formatter: Soho.Formatters.Button,
			icon: "launch",
			tooltip: "Open Project",
			resizable: false,
			align: "center",
			click: (_: JQueryEventObject, rows: IDataGridRow<any>[]) => {
				const project = rows[0].item as IProjectInfo;
				this.$stateService.go(AppState.EDITOR.name, {
					id: project.manifest.id,
					reloadProject: false,
				});
			}
		});

		this.datagridOptions = {
			columns: columns,
			dataset: [],
			selectable: SelectableEnum.Multiple,
			paging: true,
			pagesize: 10,
			expandableRow: true,
			onExpandRow: ( data: IExpandedRowItem<IProjectInfo>, response: Function) => {
				setTimeout(() => {
					const manifest = data.item.manifest;
					const image = manifest.previewImg ? manifest.previewImg : "designer/images/02-thumb.png";
					let rowTemplate = `
					<div class="datagrid-cell-layout">
						<div class="img-container">
							<img src="${ image }" alt="No image" />
						</div>
					</div>
					<div class="datagrid-cell-layout">
						<p class="datagrid-row-heading">${ manifest.displayName }</p>
						<p class="datagrid-row-micro-text">${ manifest.title }</p>
						<span class="datagrid-wrapped-text">${ manifest.description }</span>
						<p class="datagrid-row-micro-text">Version: ${ manifest.version }</p>
						<div class="tag-container tag-list">`;

					data.item.tags.forEach((tag) => {
						const tagElement = `<span class="tag">${ tag.name }</span>`;
						rowTemplate += tagElement;
					});

					rowTemplate += "</div>";

					response(rowTemplate);
				});
			},
			toolbar: {
				title: "Projects",
				results: true,
				dateFilter: false,
				keywordFilter: true,
				advancedFilter: true,
				actions: true,
				views: true,
				rowHeight: true
			},
			enableTooltips: true
		};

		// Any cast needed since jQuery only expects one argument on handler
		this.getGridElement().on("selected", (e: any, args: IRowItem<IProjectInfo>[]) => {
			// Using evalAsync due to method called multiple times from grid component
			this.$scope.$evalAsync(() => {
				if (args.length > 0) {
					this.selectedProjects = args.map((rowObject) => { return rowObject.data; });
				} else {
					this.selectedProjects = [];
				}
			});
		});
	}

	exportProject(): void {
		const projectId = this.selectedProjects[0].manifest.id;
		const displayName = this.selectedProjects[0].manifest.displayName;
		this.projectService.export(projectId, displayName);
	}

	launchProject(): void {
		const projectId = this.selectedProjects[0].manifest.id;
		this.projectService.launchProject(projectId);
	}

	duplicateProject(): void {
		const manifest = this.selectedProjects[0].manifest;
		this.projectService.duplicate(manifest);
	}

	publishProject(): void {
		const projectId = this.selectedProjects[0].manifest.id;
		const displayName = this.selectedProjects[0].manifest.displayName;
		this.projectService.publish(projectId, displayName);
	}

	deleteProjects(): void {
		const options: IMessageDialogOptions = {
			title: "Delete Project(s)",
			message: "Please confirm that you want to delete the selected project(s).",
			standardButtons: StandardDialogButtons.YesNo,
		};
		this.dialogService.showMessage(options).then((result: IDialogResult<string>) => {
			if (result && result.button === DialogButtonType.Yes) {
				const projectIds: string[] = [];
				this.selectedProjects.forEach((project) => {
					projectIds.push(project.manifest.id);
				});
				this.projectService.deleteByIds(projectIds).then(() => {
					this.getSohoXiApi().unSelectAllRows();
					this.loadProjects();
				}).catch((errorResponse: IErrorResponse) => {
					const options: IMessageDialogOptions = {
						title: "Delete Project(s)",
						message: errorResponse.message,
						standardButtons: StandardDialogButtons.Ok,
						isError: true,
					};

					this.dialogService.showMessage(options);
				});
			}
		});
	}

	private getSohoXiApi(): IDatagridApi {
		return this.getGridElement().data("datagrid");
	}

	private getGridElement(): ng.IAugmentedJQuery {
		return this.$element.find("#projects-grid");
	}

	setView(view: DashboardViewStates): void {
		this.viewState = view;
	}
}

export class DashboardDirective {
	static add(m: ng.IModule): void {
		m.directive("eirDashboard", () => {
			return {
				scope: true,
				template: require("./dashboard.html"),
				controller: DashboardCtrl,
				controllerAs: "eirDashboardCtrl",
			};
		});
	}
}
