import { ISliderOptions } from "../../model/sohoxi/sohoxi-slider.model";
import { Utils } from "../utils";

/**
 * Slider directive.
 *
 * Example:
 * scope["options"] = {
 * 	value: [1],
 * 	min: 0,
 * 	max: 2,
 * 	range: false,
 * 	step: 1,
 * 	ticks: [
 * 		{ value: 0, description: "Hidden", color: "#ed1c24" },
 * 		{ value: 1, description: "Read only", color: "#faa719" },
 * 		{ value: 2, description: "Enabled", color: "#157a13" }
 * 	]
 * }
 * <input id="slider-example" xi-slider="options"/>
 */
export class SliderDirective implements ng.IDirective {
	private static readonly DIRECTIVE_NAME = "xiSlider";
	public restrict = "A";
	public scope = false;

	public link(
		scope: ng.IScope,
		element: ng.IAugmentedJQuery,
		attributes: ng.IAttributes
	): void {
		const options = attributes[SliderDirective.DIRECTIVE_NAME];
		const unbindWatcher = scope.$watch(
			options,
			(newOptions: ISliderOptions) => {
				if (newOptions) {
					element.slider(newOptions as any);
				} else {
					element.slider();
				}
			}
		);
		element.on("$destroy", () => {
			const sliderData = Utils.getData(element, "slider");
			if (sliderData) {
				sliderData.destroy();
			}
			unbindWatcher();
		});
	}

	public static add(m: ng.IModule): void {
		m.directive(SliderDirective.DIRECTIVE_NAME, () => {
			return new SliderDirective();
		});
	}
}
