import type { IPromise } from "angular";
import isArray from "lodash-es/isArray";

import { IdoProperties } from "ng2/common/constants/common.constant";

import type { IJsonGenericDatasource2, IJsonLocalIdoDatasource } from "ng2/common/model/databearer/dataservice/json-datasource.model";
import type { IListDataItem } from "ng2/common/model/dataservice/list-data-item.model";
import type { IMetaDataField } from "ng2/common/model/dataservice/meta-data-field.model";
import type { IMetaData } from "ng2/common/model/dataservice/meta-data.model";
import type { IMap } from "ng2/common/model/imap.model";

import { ErrorResponse } from "common/error-response";
import { Datasource } from "common/project/datasource/datasource";
import { DatasourceService } from "components/services/datasource-service";

export class LocalIdo extends Datasource {

	protected databearer: IJsonLocalIdoDatasource;

	constructor(
		obj: IJsonGenericDatasource2,
		private datasourceService: DatasourceService
	) {
		super(obj);
	}

	public getDetailData(inputParameterValues: IMap<any>): Promise<any> {
		return this.loadData(inputParameterValues)
			.then(result => Array.isArray(result[0]) ? result[0] : [result[0]])
			.then(result => this.parseListData(result)[0]);
	}

	public getListData(inputParameterValues: IMap<any>): Promise<any> {
		return this.loadData(inputParameterValues)
			.then(result => {
				return this.parseListData(result[0]);
			});
	}

	private loadData(inputParameterValues?: IMap<any>): Promise<any> {
		const defer = this.datasourceService.$q.defer<any>();
		const parameters: IMap<any>[] = [{}];
		const extraParameters: IMap<any> = {};
		try {
			this.databearer.inputParameters.forEach((idoParameter) => {
				const inputparameter = inputParameterValues[idoParameter.id];
				if (idoParameter.required && !inputparameter) {
					throw new Error(("Parameter is required"));
				} else if (inputparameter && isArray(inputparameter)) { // if multiple parameters
					inputparameter.forEach((parameter, index) => {
						if (!parameters[index]) {
							parameters.push({});
						}
						parameters[index][idoParameter.id] = parameter;
					});
				} else if (inputparameter) {
					extraParameters[idoParameter.id] = inputparameter;
					if (parameters.length === 1) {
						parameters[0] = inputParameterValues;
					}
				}
			});
		} catch (e) {
			const error: ErrorResponse = new ErrorResponse(e, e);
			defer.reject(error);
			console.error(e);
			return defer.promise as Promise<any>;
		}

		this.fillInParameters(extraParameters, this.databearer.inputParameters, parameters);
		const promises: IPromise<any>[] = [];
		parameters.forEach((parameter) => {
			promises.push(this.datasourceService.localIdoRequest(this.databearer, parameter));
		});
		return this.datasourceService.$q.all(promises) as Promise<any>;
	}

	public getNonParsedData(inputParameterValues: IMap<any>): Promise<any[]> {
		return this.datasourceService
			.localIdoRequest(this.databearer, inputParameterValues) as Promise<any>;
	}

	private parseListData(result: IListDataItem[]): IListDataItem[] {
		const output = result.map(idoEntry => {
			const newEntry: IListDataItem = {};
			this.databearer.outputParameters.forEach(parameter => {
				newEntry[parameter.id] = idoEntry[parameter.key];
			});
			newEntry.id = idoEntry[IdoProperties.ROW_POINTER]; // setting the ID on the item
			return newEntry;
		});

		return output;
	}

	public getMetaData(): Promise<IMetaData> {
		const deferredMetaData: ng.IDeferred<any> = this.datasourceService.getDeferred();
		const inputFields: IMetaDataField[] = [];
		const outputFields: IMetaDataField[] = [];

		this.databearer.inputParameters.forEach((param) => {
			inputFields.push({
				FLNM: param.id,
				FLDS: param.alias ? param.alias : param.key,
				MAND: param.required ? "1" : "0",
			});
		});

		this.databearer.outputParameters.forEach((param) => {
			outputFields.push({
				FLNM: param.id,
				FLDS: param.alias ? param.alias : param.key,
				MAND: "0",
				type: param.dataType
			});
		});

		deferredMetaData.resolve({
			inputFields: inputFields,
			outputFields: outputFields
		});

		return deferredMetaData.promise as Promise<any>;
	}
}
