import { IMap } from "ng2/common/model/imap.model";
import { DeveloperToolCtrl } from "./developer-tool-ctrl";

export class DeveloperToolComponent implements ng.IComponentOptions {
	public controller: any;
	public controllerAs: string;
	public template: string;
	public bindings: IMap<string>;

	constructor() {
		this.bindings = {};

		this.controller = DeveloperToolCtrl;
		this.controllerAs = "ctrl";
		this.template = require("./developer-tool-template.html");
	}

	static add(m: ng.IModule): void {
		m.component("eirDeveloperTool", new DeveloperToolComponent());
	}
}
