import type { IMap } from "ng2/common/model/imap.model";

import { Generic2 } from "common/project/datasource/generic2";

export interface IMongooseCollectionItem {
	Name: string;
	Value: string;
}
export interface IMongooseIonApiResponse {
	Items?: IMongooseCollectionItem[][]|string[][];
}

export interface IMongooseIonApiParsedResponse {
	Items?: {}[];
}

export class Mongoose extends Generic2 {
	public loadData(
		parameters: IMap<any>
	): Promise<IMongooseIonApiResponse | IMongooseIonApiParsedResponse> {
		return super.loadData(parameters)
			.then((response: IMongooseIonApiResponse[]) => {
				const items = response[0].Items;
				if (items?.length > 0 && typeof items[0][0] === "object") {
					const parsedResponse =
						this.remapItemProperties(response[0]);
					return parsedResponse;
				}
				return response[0];
			})
			.catch(error => {
				console.error(error);
				return error;
			});
	}

	/**
	 * Remaps Mongoose ION response { Items: []:{ Name: string, Value: string} }
	 * into { Items: []:{ Name: Value } } which is compliance with how data mapping is in App Builder
	 *
	 * @param IMongooseIonApiResponse
	 */
	private remapItemProperties(response: IMongooseIonApiResponse): IMongooseIonApiParsedResponse {
		const parsedResponse = response as IMongooseIonApiParsedResponse;
		const items = response.Items as IMongooseCollectionItem[][];

		const parsedItems = items.map(items => {
			const obj: IMap<string> = {};
			items.forEach(item => {
				obj[item.Name] = item.Value !== null ? item.Value : "";
			});
			return obj;
		});
		parsedResponse.Items = parsedItems;
		return parsedResponse;
	}
}
