import { ComponentClassNames } from "ng2/common/constants/common.constant";

import type { IJsonChart } from "ng2/common/model/databearer/components/json-chart.model";
import type { IMetaDataField } from "ng2/common/model/dataservice/meta-data-field.model";
import type { IMap } from "ng2/common/model/imap.model";
import type { IDatasource } from "common/model/datasource/datasource.model";
import type { IAvailableSourceField } from "common/defines";
import type { IEditorComponent } from "common/interface/ieditor-component";
import type { IEditorFlexbox } from "editor/components/editor-flexbox";

import { Chart } from "components/chart/chart";
import { EditorComponentCtrl, IEditorComponentScope } from "editor/components/editor-component";

export interface IEditorChart extends IEditorComponent, Chart {
	parent: IEditorFlexbox;
	// Overrides to make dual extend work
	getScope(): IEditorComponentScope;
	getDatabearer(): IJsonChart;
}

export class EditorChartCtrl extends EditorComponentCtrl {
	public static add(m: ng.IModule): void {
		m.controller(
			ComponentClassNames.EDITOR_COMPONENT_PREFIX + Chart.name,
			EditorChartCtrl
		);
	}

	protected component: IEditorChart;

	/**
	 * Called to fill in content/style dialogs
	 *
	 * @returns {ng.IPromise<IAvailableSourceField[]>}
	 *
	 * @memberOf Chart
	 */
	public getLocalContextMetaData(): ng.IPromise<IAvailableSourceField[]> {
		if (!this.component.datasource) {
			return super.getLocalContextMetaData();
		}

		return this.component.datasource.getMetaData().then(metadata => {
			const availableSourceField: IAvailableSourceField[] = [];
			const listResult = this.component.getContext().ListResult;
			let listRow: IMap<any> = undefined;

			if (listResult.length > 0) {
				listRow = listResult[0];
			}

			metadata.outputFields.map((outputField: IMetaDataField) => {
				const previewText = listRow ? `(${listRow[outputField.FLNM]})` : "";

				availableSourceField.push({
					visibleName: outputField.FLDS.trim() + previewText,
					sourceKey: outputField.FLNM,
					sourceKeyShort: outputField.FLNM
				});
			});
			return availableSourceField;
		});
	}

	public setDatasource(newDatasource: IDatasource): ng.IPromise<void> {
		return super.setDatasource(newDatasource).then(() => {
			this.component.datasourceChanged(newDatasource);
		});
	}

}
