import { ButtonStyle, ComponentClassNames, SettingKeys } from "ng2/common/constants/common.constant";

import type { IJsonButton } from "ng2/common/model/databearer/components/json-button.model";

import type { IEditorComponent } from "common/interface/ieditor-component";

import { Button } from "components/button/button";
import { EditorComponentCtrl, IEditorComponentScope } from "editor/components/editor-component";
import type { IEditorFlexbox } from "editor/components/editor-flexbox";
import { IComponentFieldSetting } from "ng2/common/model/components/settings/component-field-setting.model";
import { SizeTypeEnum } from "ng2/common/enums/size-type.enum";
import { IMessageDialogOptions } from "common/services/dialog-service";

export interface IEditorButton extends IEditorComponent, Button {
	parent: IEditorFlexbox;
	// Overrides to make dual extend work
	getScope(): IEditorComponentScope;
	getDatabearer(): IJsonButton;
}

export class EditorButtonCtrl extends EditorComponentCtrl {

	setSetting(settingMeta: IComponentFieldSetting, newSettingValue: any): void {
		let saveSetting = true;
		if (settingMeta.ref === SettingKeys.STYLE) {
			if (newSettingValue === ButtonStyle.FILL) {
				saveSetting = this.component.parent.children.length === 1;
				if (!saveSetting) {
					this.component.parent.getTypedef();
					const options: IMessageDialogOptions = {
						title: "Warning",
						cssClass: "warning-text",
						message: "The Button cannot fill due to too many components in the " + this.component.parent.getTypedef()
					};
					this.dialogService.showMessage(options);
				} else {
					this.setSize(SizeTypeEnum.PERCENT);
				}
			} else {
				this.setSize(SizeTypeEnum.AUTO);
			}
		}

		if (saveSetting) {
			super.setSetting(settingMeta, newSettingValue);
		}
	}

	private setSize(sizeType: SizeTypeEnum): void {
		this.setBoundarySetting(SettingKeys.WIDTH, { sizeType });
		this.setBoundarySetting(SettingKeys.HEIGHT, { sizeType });
	}

	static add(m: ng.IModule): void {
		m.controller(
			ComponentClassNames.EDITOR_COMPONENT_PREFIX + Button.name,
			EditorButtonCtrl
		);
	}

	protected component: IEditorButton;

}
