
import { AngularConstants } from "../../../../common/angular-constants";
import type { IDialogBaseScope } from "../../../../common/defines";
import type { ISourceItem } from "../../../../common/model/component/hyperlink.model";
import { BindingManagementService } from "../../services/binding-management-service";
import { EditorService } from "../../services/editor-service";

import { BindingTypes, InjectNames, ValueObject } from "ng2/common/constants/common.constant";

import { BindingTypeEnum } from "ng2/common/enums/binding-type.enum";

import type { IDataPair } from "ng2/common/model/data-pair.model";
import type { IJsonValueSource } from "ng2/common/model/databearer/json-binding.model";

import type { IEditorComponent } from "common/interface/ieditor-component";

import { DialogButtonType, IDialog, IDialogResult } from "common/services/dialog-service";

export interface IAlias {
	new?: string;
	old?: string;
	index: string;
}

export interface IBuildLinkResponse {
	link: string,
	sources: ISourceItem[]
}

export interface IBuildLinkParameter {
	link: string,
	sources: ISourceItem[],
	component: IEditorComponent
}

export class BuildLinkCtrl {
	myLink: string;
	availableSources: IDataPair[] = [];
	newTypeValue: string;
	private dialog: IDialog<IBuildLinkParameter>;
	link: string;
	sources: ISourceItem[];
	component: IEditorComponent;

	static $inject = [
		AngularConstants.SCOPE,
		InjectNames.EDITOR_SERVICE,
		InjectNames.BINDING_MANAGEMENT_SERVICE
	];

	constructor(
		scope: IDialogBaseScope<IBuildLinkParameter>,
		private editorService: EditorService,
		private bindingManagementService: BindingManagementService
	) {
		const dialog: IDialog<IBuildLinkParameter> = scope.eirDialog;
		this.dialog = dialog;
		this.link = dialog.parameter.link;
		this.sources = dialog.parameter.sources;
		this.component = dialog.parameter.component;

		this.buildLink();
		this.availableSources = this.getAvailableSources();
	}

	updateAlias(oldAlias: string, newAlias: string, index: string): void {
		let link = this.link;
		const oldSourceAlias = `{${oldAlias}}`;
		const oldAliasExists = link.indexOf(oldSourceAlias) !== -1;

		if (newAlias) { //New alias can not be empty
			if (oldAliasExists) { //Old alias should exist in link
				const newSourceAlias = `{${newAlias}}`;
				const newAliasExists = link.indexOf(newSourceAlias) !== -1;

				if (!newAliasExists) {
					link = link.replace(oldSourceAlias, newSourceAlias);
					this.editorService.hyperlinkAliasChangedEvent.raise({ index: index });
				} else {
					this.editorService.hyperlinkAliasChangedEvent.raise({
						old: oldAlias,
						index: index
					});
				}

				this.link = link;
			} else {
				this.editorService.hyperlinkAliasChangedEvent.raise({ index: index });
			}
		} else {
			this.editorService.hyperlinkAliasChangedEvent.raise({
				new: newAlias,
				index: index
			});
		}
	}

	//components using build-link-dialog need to have a buildLink function
	buildLink(): void {
		const component = this.component as any;
		this.myLink = component.buildLink(this.link, this.sources);
	}

	addSource(): void {
		const sources = this.sources;
		//Default to first
		let newSourceName = "source1";
		if (sources.length > 0) {
			//Get next number
			const newNumber = parseInt(sources[sources.length - 1].alias.replace("source", "")) + 1;
			if (newNumber) {
				newSourceName = `source${newNumber}`;
			}
		}
		sources.push({
			alias: newSourceName,
			value: "",
			type: this.newTypeValue
		});
		this.link = `${this.link}{${newSourceName}}`;
		this.availableSources = this.getAvailableSources();
	}

	removeSource(source: ISourceItem): void {
		const indexToRemove = this.sources.findIndex(item => item.alias === source.alias);
		this.sources.splice(indexToRemove, 1);
		this.link = this.link.replace(`{${source.alias}}`, "");
		this.buildLink();
		this.availableSources = this.getAvailableSources();
		this.unregisterBinding(source);
	}

	save(): void {
		const result: IDialogResult<IBuildLinkResponse> = {
			button: DialogButtonType.Ok,
			value: {
				link: this.link,
				sources: this.sources
			}
		};
		this.dialog.close(result);
	}

	close(): void {
		this.dialog.close();
	}

	private unregisterBinding(source: ISourceItem): void {
		if (source.type === BindingTypes.CONTEXT) {
			this.bindingManagementService.unregisterContextBinding(this.component, source.value, "linkvalue");
		} else {
			const valueSourceObject: IJsonValueSource = {
				Type: BindingTypeEnum.BINDING,
				sourceKey: source.value,
				sourceName: source.type
			};
			this.bindingManagementService.unregisterBinding(this.component, source.alias, valueSourceObject);
		}
	}

	private getAvailableSources(): IDataPair[] {
		const availableSources: IDataPair[] = [{
			value: BindingTypes.CONTEXT,
			name: ValueObject.TARGET_CONTEXT
		}];
		const components = this.editorService.getAllBindableComponents();
		components.forEach((component) => {
			const sourceExists = this.sources.find((source) => {
				return source.type === component.getId();
			});
			if (!sourceExists) {
				availableSources.push({
					value: component.getId(),
					name: component.getDisplayName()
				});
			}
		});
		if (availableSources.length > 0) {
			this.newTypeValue = availableSources[0].value;
		} else {
			this.newTypeValue = null;
		}
		return availableSources;
	}

	static add(m: ng.IModule): void {
		m.controller("eirBuildLinkCtrl", BuildLinkCtrl);
	}
}
