import { IMap } from "ng2/common/model/imap.model";
import { LocalIdoDataserviceParametersCtrl } from "./dataservice-parameters-ctrl";

export class LocalIdoDataserviceParametersComponent implements ng.IComponentOptions {

	public controller: any;
	public controllerAs: string;
	public template: string;
	public bindings: IMap<string>;

	constructor() {
		this.bindings = {
			headTitle: "@",
			idoProperties: "<",
			dataserviceParameters: "=",
			wizardNextDisabled: "=",
			errorList: "=",
			setRequiredParameters: "<?",
			setPrimaryParameters: "<?",
			createParameterObject: "&",
		};

		this.controller = LocalIdoDataserviceParametersCtrl;
		this.controllerAs = "ctrl";
		this.template = require("./dataservice-parameters.html");
	}

	static add(m: ng.IModule): void {
		m.component(
			"eirLocalIdoDataserviceParameters",
			new LocalIdoDataserviceParametersComponent()
		);
	}
}
