import { INewProject } from "ng2/common/constants/common.constant";

import { IValidatorError } from "common/shared-components/base/field-validation";
import { IApplicationTemplate, IPageTemplate, PAGE_TEMPLATES } from "designer/defines";

export class CreateProjectComponent {
	newProjectBinding: INewProject;
	private errorList: IValidatorError[];

	get newProject(): INewProject {
		return this.newProjectBinding;
	}

	set newProject(value: INewProject) {
		this.newProjectBinding = {
			projectName: "",
			projectDescription: "",
			templateNumber: undefined,
			tags: []
		};
	}

	get templates(): IPageTemplate[] {
		return PAGE_TEMPLATES;
	}

	selectTemplate(template: IApplicationTemplate | IPageTemplate): void {
		this.newProjectBinding.templateNumber = template.idx;
	}

	static add(m: ng.IModule): void {
		m.component("eirCreateProject", {
			bindings: {
				newProject: "=",
				errorList: "="
			},
			template: require("./create-project.html"),
			controller: CreateProjectComponent,
			controllerAs: "ctrl",
		});
	}
}
