import { CanvasModeEnum, InjectNames, InteractionTriggerEnum, SaveFlagsEnum } from "ng2/common/constants/common.constant";
import { BindingTypeEnum } from "ng2/common/enums/binding-type.enum";
import { IJsonInteractionParameter } from "ng2/common/model/databearer/json-interaction.model";
import { IAvailableTargetField } from "ng2/common/model/interaction/available-target-field.model";
import { Interaction } from "ng2/components/interaction/interaction";
import { InteractionParameter } from "ng2/components/interaction/interaction-parameter";
import { AngularConstants } from "../../../../common/angular-constants";
import { IComponent } from "../../../../common/IComponent";
import { InstanceEvent } from "../../../../common/instance-event";
import { FrameworkService } from "../../../../components/services/framework-service";
import { ISourceComponent, SourceComponent } from "../../defines";
import { EditorService } from "../../services/editor-service";

export interface IParameterSettingsScope extends ng.IScope {
	interaction: Interaction;
	interactionChangedEvent: InstanceEvent<Interaction>;
	targetActionChangedEvent: InstanceEvent<string>;
	sourceTriggerChangedEvent: InstanceEvent<string>;
}

export class ParameterCtrl {

	public availableSources: ISourceComponent[];
	public availableTargetFields: IAvailableTargetField[];
	public interactionParameters: InteractionParameter[];
	public isAddAvailable: boolean = true;
	public metadataLoading: boolean = false;
	public targetComponent: IComponent;
	public userNote: string;
	public interaction: Interaction;

	private interactionChangedEvent: InstanceEvent<Interaction>;
	private targetActionChangedEvent: InstanceEvent<string>;
	private sourceTriggerChangedEvent: InstanceEvent<string>;
	private unsubscribers: Function[] = [];

	public static $inject = [
		AngularConstants.SCOPE,
		InjectNames.EDITOR_SERVICE,
		InjectNames.FRAMEWORK_SERVICE
	];

	public constructor(
		private $scope: IParameterSettingsScope,
		private editorService: EditorService,
		private frameworkService: FrameworkService
	) {
		this.interactionChangedEvent = $scope.interactionChangedEvent;
		this.targetActionChangedEvent = $scope.targetActionChangedEvent;
		this.sourceTriggerChangedEvent = $scope.sourceTriggerChangedEvent;

		this.unsubscribers.push(this.sourceTriggerChangedEvent.on(() => {
			this.onInteractionTargetActionChanged(this.interaction.targetActionName);
		}));
		this.unsubscribers.push(this.targetActionChangedEvent.on(() => {
			this.onInteractionTargetActionChanged(this.interaction.targetActionName);
		}));
		this.unsubscribers.push(this.interactionChangedEvent.on(this.onNewInteractionSelected.bind(this)));

		this.onNewInteractionSelected($scope.interaction);

		$scope.$on(AngularConstants.DESTROY, () => {
			this.unsubscribers.forEach((unsubscriber) => {
				unsubscriber();
			});
		});
	}

	private getUnusedParameters(): IAvailableTargetField[] {
		return this.availableTargetFields.filter((availableTargetField) => {
			return !this.interaction.getParameters().some((parameter) => {
				return parameter.targetKey === availableTargetField.targetKey;
			});
		});
	}

	private updateView(): void {
		this.isAddAvailable = (this.getUnusedParameters().length > 0);
		// Empty interactionParameters and evasAsync for ng-repeat to update
		this.interactionParameters = [];
		this.$scope.$applyAsync(() => {
			this.interactionParameters = this.interaction.getParameters();
		});
	}

	public save(): void {
		this.editorService.saveCurrent(SaveFlagsEnum.INTERACTIONS);
	}

	private onNewInteractionSelected(newInteraction: Interaction): void {
		this.interaction = newInteraction;
		if (newInteraction) {
			const availableSources = [
				SourceComponent.STATIC_VALUE_OBJECT,
				SourceComponent.TARGET_CONTEXT_OBJECT,
				SourceComponent.URL_VALUE_OBJECT
			];
			// Supplied for valueSourceSettings
			const edgeComponents = this.editorService.getAllBindableComponents();
			edgeComponents.forEach((component) => {
				availableSources.push({ id: component.getId(), name: component.getDisplayName(), isComponent: true });
			});
			this.availableSources = availableSources;

			this.onInteractionTargetActionChanged(newInteraction.targetActionName);
		}
	}

	private onInteractionTargetActionChanged(newTargetAction: string): void {
		this.availableTargetFields = [];
		const targetComponent = this.editorService.getComponentInstanceById(this.interaction.targetName);

		this.targetComponent = targetComponent;
		this.updateView();

		if (targetComponent) {
			this.metadataLoading = true;
			targetComponent.getParameterMetadata(this.interaction.targetActionName).then((availableTargetFields) => {
				this.availableTargetFields = availableTargetFields;
				availableTargetFields.forEach((targetField) => {
					const foundParameter: InteractionParameter = this.interaction.getParameter(targetField.targetKey);
					if (foundParameter) {
						// If parameter have been added to interaction, map availableTargetField with InteractionParameter
						foundParameter.targetField = targetField;
					} else if (targetField.isMandatory) {
						this.createParameter(targetField);
					}
				});
			}).catch(() => {
				// uneccessary error to console
				//console.error("[ParameterCtrl] No metadata available");
				this.userNote = "No metadata available";
			}).finally(() => {
				this.updateView();
				this.metadataLoading = false;
			});
		} else {
			console.warn("[ParameterCtrl] No targetComponent");
			this.userNote = "No target component set";
		}

	}

	public onChangeTargetField(parameter: InteractionParameter): void {
		if (parameter.targetField) {
			parameter.targetKey = parameter.targetField.targetKey;
		}
	}

	private checkForMandatoryParameter(): IAvailableTargetField {
		const parametersList = this.getUnusedParameters();
		let newTarget = parametersList[0];
		for (let i = 0; i < parametersList.length; i++) {
			if (parametersList[i].isMandatory) {
				newTarget = parametersList[i];
				break;
			}
		}
		return newTarget;
	}

	public createParameter(newTarget?: IAvailableTargetField): void {
		if (!newTarget) {
			newTarget = this.checkForMandatoryParameter();
		}
		const newParameter: IJsonInteractionParameter = {
			targetKey: newTarget.targetKey
		} as any;

		const sourceComponentInAvailableSources = this.availableSources.some((availableSource) => {
			return availableSource.id === this.interaction.sourceName;
		});
		if (sourceComponentInAvailableSources) {
			// Setting sourceKey to same key as targetKey, value-source-settings will handle if it is available or not
			newParameter.valueSource = {
				Type: BindingTypeEnum.BINDING,
				sourceName: this.interaction.sourceName,
				sourceKey: this.interaction.sourceTriggerName === InteractionTriggerEnum.CURRENT_ITEM_CHANGED ?
					"currentItem." + newTarget.targetKey : newTarget.targetKey
			};
		} else {
			newParameter.valueSource = { Type: BindingTypeEnum.STATIC_VALUE };
		}

		const newParameterInstance = new InteractionParameter(newParameter);
		newParameterInstance.targetField = newTarget;
		this.interaction.addParameter(newParameterInstance);

		this.editorService.saveCurrent(SaveFlagsEnum.INTERACTIONS);
		this.updateView();
	}

	public removeParameter(index: number): void {
		this.interaction.removeParameter(index);
		this.editorService.saveCurrent(SaveFlagsEnum.INTERACTIONS);
		this.editorService.setCanvasMode(CanvasModeEnum.SELECTION);
		this.updateView();
	}

	private removeAllParameters(): void {
		this.interaction.removeAllParameters();
	}
}

export class ParameterHandlerDirective {
	public static add(m: ng.IModule): void {
		m.directive("eirParameterHandler", () => {
			return {
				template: require("./parameter-handler.html"),
				scope: {
					interaction: "=",
					interactionChangedEvent: "=",
					targetActionChangedEvent: "=",
					sourceTriggerChangedEvent: "="
				},
				controller: ParameterCtrl,
				controllerAs: "eirParameterCtrl"
			};
		});
	}
}
