	public projectBoundariesChangedEvent = new InstanceEvent<IBoundary[]>();
	public finderComponentSelectedEvent = new InstanceEvent<IEditorComponent>();
	public canvasModeChangedEvent = new InstanceEvent<CanvasModeEnum>();

	public sidebarViewChangedEvent = new InstanceEvent<ISidebarView>();
	public sidebarChangedEvent = new InstanceEvent<boolean>();
