	public updateDatasourceOnComponents(datasource: IJsonDatasource): void {
		const allComponents: IEditorComponent[] = this.getAllComponents();
		allComponents.forEach((component) => {
			if (component.datasource && component.datasource.id === datasource.id) {
				const datasourceObject = this.getDatasourceObject(datasource.id);
				component.setDatasource(datasourceObject);
				component.refreshMetaSettings();
			}
		});
	}

	public getTranslationsFromComponentTree(component: IJsonComponent): IMap<IJsonLanguageComponent[]> {
		const languageComponents: IMap<IJsonLanguageComponent[]> = {};
		this.forEachComponent(component, (componentChild: IJsonComponent) => {
			const componentTranslations = this.getCurrentProject().getTranslation(componentChild.id);
			if (componentTranslations) {
				languageComponents[componentChild.id] = angular.copy(componentTranslations);
			}
		});
		return languageComponents;
	}

	/**
	 *	Utility function to find ONE component in tree according to predicate
		*
		* @param parent Top of tree
		* @param predicate Function that
		*/
	public findInComponentTree(
		parent: IJsonComponent,
		predicate: (child: IJsonComponent) => boolean
	): IJsonComponent {

		let foundChild: IJsonComponent;
		this.forEachComponent(parent, (child) => {
			if (predicate(child)) {
				foundChild = child;
				// TODO: Add way to break this recursive loop
			}
		});

		return foundChild;
	}

	public copyTranslationsToCurrentProject(
		languageComponents: IMap<IJsonLanguageComponent[]>,
		oldToNewIdMap: IMap<string>,
		newComponentTree: IJsonComponent
	): void {
		Helper.getListFromMap(angular.copy(languageComponents)).forEach((componentTranslation: IJsonLanguageComponent) => {
			const isCopy = oldToNewIdMap ? true : false;
			if (isCopy) {
				this.copyTranslationByMapList(componentTranslation, oldToNewIdMap, newComponentTree);
			} // else cut
			this.getCurrentProject().addTranslation(componentTranslation);
		});
	}

	private copyTranslationByMapList(
		componentTranslation: IJsonLanguageComponent,
		idMap: IMap<string>,
		newComponentTree: IJsonComponent
	): IJsonLanguageComponent {
		componentTranslation.id = Helper.generateUniqueId();
		componentTranslation.componentId = idMap[componentTranslation.componentId];
		const foundChild: IJsonComponent = this.findInComponentTree(newComponentTree, (child) => {
			return child.id === componentTranslation.componentId;
		});
		// If translation.property is not a bindable reference
		const bindableRefs: string[] = [];
		this.forEachBindable(foundChild, (bindable) => {
			bindableRefs.push(bindable.ref);
		});
		if (bindableRefs.indexOf(componentTranslation.property) === -1) {
			componentTranslation.property = idMap[componentTranslation.property];
		}
		return componentTranslation;
	}

