	public syncTranslations(newTranslations: IMap<IJsonLanguageComponent[]>): void {
		const currentProject = this.getCurrentProject();
		const newTranslationList = Helper.getListFromMap(newTranslations);
		const addTranslations = newTranslationList.length > currentProject.getTranslationsList().length;
		const removeTranslations = newTranslationList.length < currentProject.getTranslationsList().length;

		if (addTranslations) {
			newTranslationList.forEach((translation) => {
				if (!currentProject.getTranslation(translation.componentId)) {
					currentProject.addTranslation(translation);
				}
			});
		} else if (removeTranslations) {
			currentProject.getTranslationsList().forEach((translation) => {
				if (!newTranslations[translation.componentId]) {
					currentProject.removeTranslation(translation.componentId);
				}
			});
		} else { //same length, value is the only change (DO NOT change the languages used)
			// TODO: MUPS-1693, too many forEach(), fix this when translations is Map-structure
			currentProject.getTranslationsList().forEach((projectLanguageComponent, index) => {
				newTranslationList[index].translations.forEach((undoTranslation) => {
					const projectTranslations = projectLanguageComponent.translations;
					for (let i = 0; i < projectTranslations.length; i++) {
						if (projectTranslations[i].locale === undoTranslation.locale) {
							projectTranslations[i].value = undoTranslation.value;
							break;
						}
					}
				});
			});
		}
	}

