	public getActiveUrlBindings(): IJsonValueSource[] {
		const urlBindings: IJsonValueSource [] = [];
		const components = this.getAllComponents(component => {
			return component.getTypedef() !== Flexbox.name;
		});

		components.forEach(component => {
			component.forEachBindable(bindable => {
				const valueSource = component.getBindableField(bindable.ref);
				if (valueSource && valueSource.Type === BindingTypeEnum.URL_BINDING) {
					urlBindings.push(valueSource);
				}
			});
		});
		return urlBindings;
	}

