import { InjectNames } from "ng2/common/constants/common.constant";
import { BindingTypeEnum } from "ng2/common/enums/binding-type.enum";
import { ValueTypeEnum } from "ng2/common/enums/value-type.enum";
import { IJsonComponent } from "ng2/common/model/databearer/components/json-component.model";
import { IJsonValueSource } from "ng2/common/model/databearer/json-binding.model";
import { IMetaDataField } from "ng2/common/model/dataservice/meta-data-field.model";
import { IMap } from "ng2/common/model/imap.model";
import { IComponent } from "../../../common/IComponent";
import { FrameworkService } from "../../../components/services/framework-service";

export class BindingManagementService {

	static $inject = [
		InjectNames.FRAMEWORK_SERVICE,
	];

	constructor(
		private frameworkService: FrameworkService,
	) { }

	public unregisterBinding(
		targetComponent: IComponent,
		targetKey: string,
		valuesourceObject: IJsonValueSource
	): void {
		const bindingID = this.frameworkService.getBindingId(targetComponent.getId(), targetKey, valuesourceObject);
		const activeBindings: IMap<Function> = this.frameworkService.getActiveBinding();

		if (activeBindings[bindingID]) {
			activeBindings[bindingID]();
			delete activeBindings[bindingID];
		}
	}

	public unregisterContextBinding(
		targetComponent: IComponent,
		sourceKey: string,
		targetKey: string = ""
	): void {
		const bindingId = this.frameworkService.getContextBindingId(targetComponent.getId(), targetKey, sourceKey);
		const activeBindings: IMap<Function> = this.frameworkService.getActiveBinding();

		if (activeBindings[bindingId]) {
			activeBindings[bindingId]();
			delete activeBindings[bindingId];
		}
	}

	public registerBinding(
		targetComponent: IComponent,
		targetKey: string,
		valuesourceObject: IJsonValueSource
	): void {
		this.frameworkService.registerBinding(targetComponent, targetKey, valuesourceObject, valuesourceObject.value);
	}

	public registerContextBinding(
		targetComponent: IComponent,
		targetKey: string,
		valuesourceObject: IJsonValueSource
	): void {
		this.frameworkService.registerContextBinding(
			targetComponent,
			targetKey,
			valuesourceObject.sourceKey,
			valuesourceObject.value
		);
	}

	public setDefaultBindings(component: IJsonComponent, availableOutput: IMetaDataField[]): void {
		const newAvailableOutput = $.extend([], availableOutput, true).reverse();
		this.frameworkService.forEachComponent(component, (innerComponent) => {
			if (component === innerComponent || !innerComponent.datasource) {
				this.frameworkService.forEachBindable(innerComponent, (bindableField, currentvalueSource) => {
					// Make sure only writeable and nonbooleans is changed
					if (bindableField.write && bindableField.valueType !== ValueTypeEnum.BOOLEAN) {
						const outputParam = newAvailableOutput.pop();
						if (outputParam) {
							currentvalueSource.Type = BindingTypeEnum.CONTEXT_BINDING;
							currentvalueSource.sourceKey = outputParam.FLNM;
						}
					}
				});
			}
		});
	}

	static add(ngModule: ng.IModule): void {
		ngModule.service(InjectNames.BINDING_MANAGEMENT_SERVICE, BindingManagementService);
	}
}
