import { IMap } from "ng2/common/model/imap.model";
import { ResourceUrlEditorCtrl } from "./resource-url-editor-ctrl";

export class ResourceUrlEditorComponent implements ng.IComponentOptions {

	public controller: any;
	public controllerAs: string;
	public template: string;
	public bindings: IMap<string>;

	constructor() {
		this.bindings = {
			datasourceParameters: "=",
			datasourceResourceUrl: "=",
			datasourceRestUrl: "<",
		};

		this.controller = ResourceUrlEditorCtrl;
		this.controllerAs = "ctrl";
		this.template = require("./resource-url-editor.html");
	}

	static add(m: ng.IModule): void {
		m.component("eirResourceUrlEditor", new ResourceUrlEditorComponent());
	}
}
