import { AngularConstants } from "../../angular-constants";

import { InjectNames } from "ng2/common/constants/common.constant";
import { IMap } from "ng2/common/model/imap.model";
import { FilterTagCtrl } from "./filter-tag-ctrl";

export class FilterTagComponent implements ng.IComponentOptions {
	public controller: any;
	public controllerAs: string;
	public template: string;
	public bindings: IMap<string>;

	constructor() {
		this.bindings = {
			filterValues: "=",
			localPrefix: "<"
		};

		this.controller = [
			AngularConstants.FILTER,
			InjectNames.TAG_SERVICE,
			FilterTagCtrl
		];
		this.controllerAs = "ctrl";
		this.template = require("./filter-tag-template.html");
	}

	static add(m: ng.IModule): void {
		m.component("eirFilterTagComponent", new FilterTagComponent());
	}
}
