import { Component } from "../component";

import {
	DateFormat,
	InteractionPairOperation,
	InteractionTriggerEnum,
	Numbers,
	SettingInputKeys,
	SettingKeys,
	SidebarTabTypes,
} from "ng2/common/constants/common.constant";
import { InteractionDefines } from "ng2/common/constants/interaction-defines.constant";
import { BindingTypeEnum } from "ng2/common/enums/binding-type.enum";
import { SizeTypeEnum } from "ng2/common/enums/size-type.enum";
import { ValueTypeEnum } from "ng2/common/enums/value-type.enum";
import { IComponentSettingCategory } from "ng2/common/model/components/settings/component-setting-category.model";
import { IDataPair } from "ng2/common/model/data-pair.model";
import { IJsonSize } from "ng2/common/model/databearer/components/json-component.model";
import { IJsonBindableField } from "ng2/common/model/databearer/json-binding.model";

import { IComponentScope } from "../../common/interface/icomponent-scope";
import { ILayoutComponent } from "../../common/interface/ilayout-component";
import { IJsonDatepicker } from "../../common/model/component/datepicker.model";
import { IDatepickerApi, IDatePickerOptions } from "../../common/model/sohoxi/sohoxi-datepicker.model";

import { DateHelper } from "ng2/common/helpers/date.helper";
import { BackwardCompatible } from "ng2/common/project/backward-compatible";
import { FrameworkService } from "../services/framework-service";

export class Datepicker extends Component {
	static availableDateFormats: IDataPair[] = [
		{ name: `${DateFormat.FORMAT_1}`, value: DateFormat.FORMAT_1 },
		{ name: `${DateFormat.FORMAT_2}`, value: DateFormat.FORMAT_2 },
		{ name: `${DateFormat.FORMAT_3}`, value: DateFormat.FORMAT_3 },
		{ name: `ISO8601: ${DateFormat.ISO8601}`, value: DateFormat.ISO8601 },
		{ name: `M3: ${DateFormat.M3}`, value: DateFormat.M3 }
	];
	static DEFAULT_WIDTH: IJsonSize = { sizeType: SizeTypeEnum.AUTO };
	static DEFAULT_HEIGHT: IJsonSize = { sizeType: SizeTypeEnum.AUTO };
	static SOURCE_TRIGGERS =
		InteractionDefines.SOURCE_TRIGGERS.concat([InteractionPairOperation.ON_CHANGED]);
	static TARGET_ACTIONS: IDataPair[] = [
		InteractionPairOperation.GET,
		InteractionPairOperation.CLEAR
	];
	static DEFAULT_TRIGGER = InteractionPairOperation.ON_CHANGED;
	static DEFAULT_ACTION = InteractionPairOperation.GET;
	static SETTING_FIELDS: IComponentSettingCategory[] =
		[
			{
				title: "Style",
				tab: SidebarTabTypes.STYLE,
				settings: [{
					title: "Show Label",
					boundarySetting: true,
					ref: SettingKeys.SHOW_LABEL,
					inputType: SettingInputKeys.CHECKBOX,
					defaultValue: true,
				}, {
					title: "Read Only",
					ref: SettingKeys.IS_READONLY,
					inputType: SettingInputKeys.CHECKBOX,
					defaultValue: false,
					button: {
						icon: "#icon-unlocked",
						tooltip: "Disable Component When...",
						dialog: true
					}
				}]
			},
			{
				title: "Date Formats",
				tab: SidebarTabTypes.STYLE,
				settings: [{
					title: "To display",
					ref: SettingKeys.DATE_FORMAT_SHOW,
					dataset: Datepicker.availableDateFormats,
					inputType: SettingInputKeys.DROPDOWN,
					defaultValue: DateFormat.FORMAT_1
				}, {
					title: "For bindings (outbound)",
					ref: SettingKeys.DATE_FORMAT_BINDING,
					dataset: Datepicker.availableDateFormats.concat([
						{ name: "Raw timestamp", value: DateFormat.RAW }
					]),
					inputType: SettingInputKeys.DROPDOWN,
					defaultValue: DateFormat.FORMAT_1
				}, {
					title: "For bindings (inbound)",
					ref: SettingKeys.DATE_FORMAT_INPUT,
					dataset: [{ name: "Automatic", value: DateFormat.AUTOMATIC }].concat(
						Datepicker.availableDateFormats
					),
					inputType: SettingInputKeys.DROPDOWN,
					defaultValue: DateFormat.AUTOMATIC
				}]
			},
			{
				title: "Default Date",
				tab: SidebarTabTypes.STYLE,
				settings: [{
					title: "Today's date",
					ref: SettingKeys.DATE_TODAY,
					inputType: SettingInputKeys.CHECKBOX
				}]
			},
			...Component.SETTING_FIELDS
		];

	static BINDABLE_FIELDS_2: IJsonBindableField[] = [{
		name: "Label",
		ref: SettingKeys.LABEL,
		valueType: ValueTypeEnum.LOCALIZED_STRING,
		write: true,
		read: false,
		defaultValue: { Type: BindingTypeEnum.STATIC_VALUE, value: "" }
	}, {
		name: "Date",
		ref: SettingKeys.DATE_VALUE,
		valueType: ValueTypeEnum.STRING, // Ideally this would be type DATE
		write: true,
		read: false,
		defaultValue: { Type: BindingTypeEnum.STATIC_VALUE, value: "" }
	}, {
		name: "Formatted date",
		ref: SettingKeys.DATE_FORMATTED,
		valueType: ValueTypeEnum.STRING,
		write: false,
		read: true
	}];

	static MAX_TIMESTAMP = 8640000000000000;
	static MIN_TIMESTAMP = -8640000000000000;

	// Variables set from binding framework
	label: string;
	showLabel: boolean;
	dateValue: Date | null;
	dateFormatted: string = "";

	// Variables set from settings framework
	dateFormatShow: string;
	dateFormatBinding: string;
	dateFormatInput: string;
	isDefaultTodayDate: boolean;

	// Variables used in template
	datepickerOptions: IDatePickerOptions;

	datePickerApi: IDatepickerApi;
	isStaticSet: boolean;

	constructor(
		obj: IJsonDatepicker,
		$element: ng.IAugmentedJQuery,
		parent: ILayoutComponent,
		frameworkService: FrameworkService
	) {
		super(obj, $element, parent, frameworkService);

		this.dateFormatShow = obj.dateFormatShow;
		this.dateFormatBinding = obj.dateFormatBinding;
		this.dateFormatInput = obj.dateFormatInput;
		this.isDefaultTodayDate = obj.isDefaultTodayDate;
		this.showLabel = BackwardCompatible.getShowLabel(obj);

		this.setDatepickerOptions();
	}

	newBoundaryEntered(boundaryId: string): void {
		super.newBoundaryEntered(boundaryId);
		this.setBoundarySettingIfChanged<boolean>(SettingKeys.SHOW_LABEL);
	}

	setDatepickerOptions(): void {
		this.datepickerOptions = {
			dateFormat: this.dateFormatShow,
			placeholder: true
		};
	}

	clear(): void {
		this.getSohoXiApi().setValue(undefined, true);
	}

	setBindableValue(bindableReference: string, value: any): void {
		if (bindableReference === SettingKeys.DATE_VALUE) {
			this.setErrorTooltip();
			if (value === "") {
				this.isStaticSet = false;
				this.updateDatepickerValue();
				return;
			}
			const newDate = this.parseDate(value);
			if (DateHelper.isValidDate(newDate)) {
				this.isStaticSet = true;
				this.updateDatepickerValue(newDate);
			} else {
				this.setErrorTooltip(`Invalid date: '${value}'`);
			}
		} else {
			super.setBindableValue(bindableReference, value);
		}
	}

	/**
	 * Set dateValue to newDate, or clear dateValue if newDate is falsy
	 * @param newDate The new date to set, should already be valid.
	 */
	setDateValue(newDate?: Date): void {
		if (newDate) {
			this.dateValue = newDate;
			if (this.dateFormatBinding === DateFormat.RAW) {
				// Keeping string format, change when we specially handle different value types in binding framework
				this.dateFormatted = this.dateValue.getTime().toString();
			} else {
				this.dateFormatted = Soho.Locale.formatDate(this.dateValue, { pattern: this.dateFormatBinding });
			}
		} else {
			this.dateValue = undefined;
			this.dateFormatted = "";
		}
		this.onUpdateValue();
	}

	updateDatepickerValue(newDate?: Date): void {
		const datepickerApi = this.getSohoXiApi();
		datepickerApi.settings.dateFormat = this.datepickerOptions.dateFormat;
		datepickerApi.pattern = this.datepickerOptions.dateFormat;
		if (this.isStaticSet) {
			const value = newDate ? newDate : this.dateValue;
			datepickerApi.setValue(value, true);
		} else if (this.isDefaultTodayDate) {
			datepickerApi.setToday();
		} else {
			this.clear();
		}
	}

	initializeDatepicker(): void {
		const datepickerApi = this.getSohoXiApi();
		this.updateDatepickerReadonlyValue();
		datepickerApi.element.on("change", () => {
			// Parsing date because soho is not updating the currentDate when editing the field
			const newDate = Soho.Locale.parseDate(datepickerApi.element.val().toString(), this.dateFormatShow);
			if (newDate) { // If not valid date soho will show invalid date error
				this.setDateValue(newDate);
				this.frameworkService.trigger(this, InteractionTriggerEnum.CHANGED);
			} else {
				this.setDateValue();
			}
		});

		datepickerApi.element.on("listopened", () => {
			this.addPreventDefault();
		});
	}

	/**
	 * Really nasty fix for interupting href in anchor tags.
	 * Remove this when IDS has an update on
	 * Solved in IDS 4.21
	 */
	private addPreventDefault(): void {
		this.frameworkService.getTimeout()(() => {
			const ulPath = ".picklist-section.is-year ul ";
			$(ulPath + "li a").click((e: any) => {
				e.preventDefault();
			});
			$(ulPath + ".up," + ulPath + ".down").click((e: any) => {
				this.addPreventDefault();
			});
		}, Numbers.THREE_HUNDRED);
	}

	updateDatepickerReadonlyValue(): void {
		const datepickerApi = this.getSohoXiApi();
		if (this.isReadonly) {
			datepickerApi.readonly();
		} else {
			datepickerApi.enable();
		}
	}

	getSohoXiApi(): IDatepickerApi {
		if (!this.datePickerApi) {
			this.datePickerApi = this.element.find("> div > input").data("datepicker");
		}
		return this.datePickerApi;
	}

	private parseDate(value: any): Date | null {
		let newDate: Date = null;
		if (typeof value === "string" && this.dateFormatInput !== DateFormat.AUTOMATIC) {
			newDate = Soho.Locale.parseDate(value, this.dateFormatInput);
		} else { // DateFormat.AUTOMATIC
			newDate = this.parseDateFormatAutomatic(value);
		}
		return newDate;
	}

	private parseDateFormatAutomatic(value: any): Date {
		const timestamp = new Date(value);

		if (!DateHelper.isValidDate(timestamp)) {
			const milliseconds = DateHelper
				.parse(value)
				.getTime();

			if (!isNaN(milliseconds) &&
				DateHelper.isValidMillisecondTimestamp(milliseconds)
			) {
				return new Date(milliseconds);
			}
		}
		return timestamp;
	}

	static directive = [() => {
		return {
			template: require("./datepicker.html"),
			replace: true,
			link: ($scope: IComponentScope, $element: ng.IAugmentedJQuery, $attrs: ng.IAttributes) => {
				const componentInstance = $scope.componentInstance as Datepicker;

				$($element[0]).initialize("en-US");
				componentInstance.initializeDatepicker();

				$scope.componentReady({ componentInstance: $scope.componentInstance });
			}
		};
	}];
}
