import {
	InteractionPairOperation,
	Numbers,
	SidebarTabTypes,
	SettingKeys,
	SettingInputKeys
} from "ng2/common/constants/common.constant";
import { InteractionDefines } from "ng2/common/constants/interaction-defines.constant";

import { OrientationEnum } from "ng2/common/enums/orientation.enum";
import { SizeTypeEnum } from "ng2/common/enums/size-type.enum";

import type { IJsonTabComponent, IJsonTabPanel, IJsonTabs } from "common/model/component/tabs.model";
import type { ITab } from "common/model/sohoxi/sohoxi-tabs.model";
import type { IComponentSettingCategory } from "ng2/common/model/components/settings/component-setting-category.model";
import type { IDataPair } from "ng2/common/model/data-pair.model";
import type { IJsonSize } from "ng2/common/model/databearer/components/json-component.model";
import type { IJsonBindableField } from "ng2/common/model/databearer/json-binding.model";

import type { ITabStyle } from "common/defines";
import type { IComponent } from "common/IComponent";
import type { ILayoutComponent } from "common/interface/ilayout-component";
import type { ITabsComponent } from "common/interface/itabs-component";

import { Component } from "components/component";
import { LayoutComponent } from "components/layout-component";

import { FrameworkService } from "components/services/framework-service";

export class Tabs extends LayoutComponent implements ITabsComponent {
	static availableOrientations = [
		{ name: "Horizontal", value: OrientationEnum.HORIZONTAL },
		{ name: "Vertical", value: OrientationEnum.VERTICAL }
	];
	static DEFAULT_ORIENTAION = OrientationEnum.HORIZONTAL;
	static DEFAULT_WIDTH: IJsonSize = { sizeType: SizeTypeEnum.PERCENT };
	static DEFAULT_HEIGHT: IJsonSize = { sizeType: SizeTypeEnum.PERCENT };
	static SOURCE_TRIGGERS: IDataPair[] = InteractionDefines.SOURCE_TRIGGERS;
	static TARGET_ACTIONS: IDataPair[] = InteractionDefines.TARGET_ACTIONS;
	static DEFAULT_ACTION: IDataPair = InteractionPairOperation.GET;
	static DEFAULT_TRIGGER: IDataPair = InteractionPairOperation.ON_LOAD;
	static BINDABLE_FIELDS_2: IJsonBindableField[] = [];
	static SETTING_FIELDS: IComponentSettingCategory[] =
	(<IComponentSettingCategory[]>[
		{
			title: "Orientation",
			tab: SidebarTabTypes.STYLE,
			settings: [{
				ref: SettingKeys.ORIENTATION,
				inputType: SettingInputKeys.RADIO,
				dataset: Tabs.availableOrientations,
				defaultValue: Tabs.DEFAULT_ORIENTAION
			}]
		},
	]).concat(Component.SETTING_FIELDS);

	componentTabs: IJsonTabComponent[];
	currentTab: IJsonTabComponent;
	childrenDatabearers: IJsonTabPanel[];
	orientation: OrientationEnum;

	protected databearer: IJsonTabs;

	private readyChildrenCount = 0;

	constructor(
		obj: IJsonTabs,
		$element: ng.IAugmentedJQuery,
		parent: ILayoutComponent,
		frameworkService: FrameworkService
	) {
		super(obj, $element, parent, frameworkService);

		this.childrenDatabearers = this.databearer.children;
		this.componentTabs = this.databearer.componentTabs;
		this.currentTab = this.componentTabs[0];
		this.orientation = obj.orientation || Tabs.DEFAULT_ORIENTAION;
	}

	// Wait for the tab element to exist before rendering
	private updateWhenExist(): void {
		const checkInterval = 100; // check every 100ms
		const checkExist = setInterval(() => {
			if (this.element.length > 0) {
				clearInterval(checkExist);
				this.element.children().children(".tab-container").tabs({
					changeTabOnHashChange: true,
					hashChangeCallback: this.onChangeTab.bind(this),
				} as any);
				this.initializeBindings();
				this.updatePanels();

				this.forEachChild((child => {
					child.initializeBindings();
				}), true);
			}
		}, checkInterval);
	}

	private initializeTabs(): void {
		this.getScope().$applyAsync(() => {
			this.updateWhenExist();
		});
	}

	onComponentReady(component: IComponent): void {
		this.addChildInstance(component);
		this.readyChildrenCount++;
		if (this.readyChildrenCount === this.componentTabs.length) {
			this.getScope().onChildrenReady();
		}
	}

	initializeBindings(): void {
		super.initializeBindings();
		this.updateValuesWithTranslations();
	}

	onChildrenReady(): void {
		this.initializeTabs();
	}

	isLayoutComponent(): boolean {
		return false;
	}

	// eslint-disable-next-line @typescript-eslint/no-unused-vars
	resizableChildren(direction: OrientationEnum): boolean {
		return false;
	}

	// eslint-disable-next-line @typescript-eslint/no-unused-vars
	getPxPerPercentComponent(ori: OrientationEnum, percent: number): number {
		return 0;
	}

	// eslint-disable-next-line @typescript-eslint/no-unused-vars
	hasIndividualChildSizes(ori: OrientationEnum): boolean {
		return false;
	}

	onChangeTab(selectedId: string): void {
		const selectedTab = this.componentTabs.find((tab) => {
			return "#"+tab.id === selectedId;
		});
		this.currentTab = selectedTab || this.componentTabs[0];
		this.updatePanels();
	}

	onOrientationChanged(): void {
		const tabAPI: ITab = this.element.find(".tab-container").data("tabs");
		if (tabAPI) {
			tabAPI.destroy();
			this.currentTab = this.componentTabs[0];
			this.updateWhenExist();
		}
	}

	updatePanels(): void {
		this.children.forEach((tabPanel: IComponent) => {
			tabPanel.updateVisibility();

			if (tabPanel.isVisible) {
				tabPanel.forEachChild((innerChild) => {
					innerChild.updateVisibility();
				}, false);

				this.frameworkService.getTimeout()(() => {
					this.frameworkService.triggerExpressions();
				});
			}
		});
	}

	protected getInlineStyle(): ITabStyle {
		const style: ITabStyle = super.getInlineStyle();
		// Shifting back padding to margin
		style.margin = style.padding;
		style.padding = null;
		return style;
	}

	protected calcNewSize(sizeObject: IJsonSize, orientation: OrientationEnum, margin = 0): string {
		// Overloading because of margin calculation
		let retSize = "";
		if (this.parent.hasIndividualChildSizes(orientation)) {
			if (sizeObject.sizeType === SizeTypeEnum.PERCENT) {
				const pxPerPercentComponent = this.parent.getPxPerPercentComponent(orientation, sizeObject.value);
				retSize = "calc(" + (sizeObject.value || Numbers.HUNDRED) + "% - " + (pxPerPercentComponent + margin) + "px)";
			} else if (sizeObject.sizeType === SizeTypeEnum.PIXELS) {
				retSize = "calc(" + sizeObject.value + "px)";
			}
		}
		return retSize;
	}

	updateValuesWithTranslations(): void {
		this.componentTabs.forEach((tab: IJsonTabComponent) => {
			const translatedTitle =
				this.frameworkService.getTranslatableStringByProperty(this.getId(), tab.id);
			if (translatedTitle) {
				const globalString = this.frameworkService.getGlobalValue(translatedTitle, this);

				if (globalString) {
					tab.value = globalString;
				}

				tab.name = translatedTitle;
			}
		});
	}

	// eslint-disable-next-line @typescript-eslint/explicit-module-boundary-types
	static directive = [() => {
		return {
			template: require("./tabs.html"),
			replace: true
		};
	}];
}
