import { IMap } from "ng2/common/model/imap.model";
import { AngularConstants } from "../../angular-constants";
import { SortPopupmenuCtrl } from "./sort-popupmenu-ctrl";

export class SortPopupmenuComponent implements ng.IComponentOptions {
	public controller: any;
	public controllerAs: string;
	public template: string;
	public bindings: IMap<string>;

	constructor() {
		this.bindings = {
			sortOptions: "<",
			sortValues: "=",
			localPrefix: "<"
		};

		this.controller = [
			AngularConstants.FILTER,
			SortPopupmenuCtrl
		];
		this.controllerAs = "ctrl";
		this.template = require("./sort-popupmenu-template.html");
	}

	static add(m: ng.IModule): void {
		m.component("eirSortPopupmenuComponent", new SortPopupmenuComponent());
	}
}
