import angular from "angular";
import { AngularConstants } from "../../../common/angular-constants";

import { IDatasource } from "common/model/datasource/datasource.model";
import { Config, InjectNames, ProjectLimit } from "ng2/common/constants/common.constant";
import { SelectableEnum } from "ng2/common/constants/sohoxi.constant";
import { DatasourceTypeEnum } from "ng2/common/enums/datasource-type.enum";
import { IJsonDatasource, IJsonGenericDatasource, IJsonGenericDatasource2 } from "ng2/common/model/databearer/dataservice/json-datasource.model";
import { IDataGridColumn, IDataGridOptions, IDataGridRow } from "ng2/common/model/sohoxi/sohoxi-datagrid.model";

import { ITestDataserviceParameters, TestDataserviceDialogType } from "../../../common/dialogs/test-dataservice/test-dataservice-dialog";
import { IErrorResponse } from "../../../common/error-response";
import { Helper } from "../../../common/helper";
import { ConflictResolutonService } from "../../../common/services/conflict-resolution-service";
import {
	DialogButtonType, DialogService, IDialogOptions, IDialogResult,
	IMessageDialogOptions, StandardDialogButtons
} from "../../../common/services/dialog-service";
import { CustomFormatters } from "../../../common/sohoxi/custom-formatters";
import { RepositoryService } from "../repository-service";
import { RepositoryTab } from "../repository-tab-ctrl";
import { IDatasourceWizardParameter } from "./create-wizard/datasource-wizard-ctrl";

export interface IUpsertDialogParameter {
	datasources: IJsonDatasource[];
	datasource: IJsonGenericDatasource;
	isEdit: boolean;
}

export class DatasourceTabCtrl extends RepositoryTab {

	public anySelectedDataItem: boolean = false;
	public datagridOptions: IDataGridOptions<IJsonDatasource>;
	public selectedDataItems: IJsonDatasource[] = [];

	protected datagridId: string = "dataservice-grid";

	private dialogTitle: string = "Create";
	private importActionTitle: string = "Import Data Services";
	private dataset: IJsonDatasource[];
	private set Dataset(value: IJsonDatasource[]) {
		this.dataset = value;
		this.saveToCache(this.CACHE_KEY, value);
	}

	private readonly CACHE_KEY = "dataservices";

	public static $inject = [
		AngularConstants.SCOPE,
		AngularConstants.ELEMENT,
		AngularConstants.CACHE_FACTORY,
		InjectNames.REPOSITORY_SERVICE,
		InjectNames.DIALOG_SERVICE,
		InjectNames.CONFLICT_RESOLUTION_SERVICE
	];

	constructor(
		$scope: ng.IScope,
		$element: ng.IAugmentedJQuery,
		$cacheFactory: ng.ICacheFactoryService,
		repositoryService: RepositoryService,
		private dialogService: DialogService,
		private conflictResolutionService: ConflictResolutonService
	) {
		super($scope, $element, $cacheFactory, repositoryService);
	}

	protected initDatagridOptions(): void {
		const columns: IDataGridColumn[] = [];
		this.dataset = [];

		columns.push({
			id: "selectionCheckbox",
			sortable: false,
			resizable: false,
			width: 50,
			formatter: Soho.Formatters.SelectionCheckbox,
			align: "center"
		});
		columns.push({
			id: "displayName",
			name: "Name",
			field: "displayName",
			width: 250,
			sortable: true,
			formatter: Soho.Formatters.Ellipsis
		});
		columns.push({
			id: "author",
			name: "Author",
			field: "authorDisplayName",
			width: 150,
			sortable: true,
			formatter: Soho.Formatters.Ellipsis
		});
		columns.push({
			id: "date",
			name: "Created",
			field: "creationDate",
			width: 150,
			sortable: true,
			formatter: Soho.Formatters.Date
		});
		columns.push({
			id: "Description",
			name: "Description",
			field: "description",
			width: 250,
			sortable: false,
			formatter: CustomFormatters.Multi(CustomFormatters.DataSourceDescription(), Soho.Formatters.Ellipsis)
		});
		columns.push({
			id: "suite",
			name: "Type",
			field: "suite",
			width: 100,
			sortable: true,
			formatter: CustomFormatters.DatasourceSuiteFormatter()
		});
		columns.push({
			id: "action",
			name: "Action",
			field: "",
			width: 210,
			sortable: true,
			align: "center",
			formatter: Soho.Formatters.Button,
			click: (_: JQueryEventObject, rowsSelected: IDataGridRow<any>[]) => {
				const item: IJsonGenericDatasource2 = rowsSelected[0].item;
				this.repositoryService.getDatasources()
					.then((datasources) => this.openTestDataserviceDialog(item))
					.catch((error: Error) => this.dialogService.showError("Could not test data service", error.message));
			},
			text: "Test Data Service",
		});

		this.datagridOptions = {
			columns,
			dataset: [],
			selectable: SelectableEnum.Multiple,
			paging: true,
			pagesize: 25,
			toolbar: {
				title: "Global Data Services",
				results: true,
				dateFilter: false,
				keywordFilter: true,
				advancedFilter: true,
				actions: true,
				views: true,
				rowHeight: true
			},
			enableTooltips: true
		};
		this.loadDatasources();
	}

	private openTestDataserviceDialog(
		dataserviceToTest: IJsonGenericDatasource2
	): ng.IPromise<IDialogResult<string>> {
		const options: IDialogOptions<ITestDataserviceParameters> = {
			title: "Test Data Service",
			template: require("../../../common/dialogs/test-dataservice/test-dataservice-dialog.html"),
			parameter: {
				dataservice: dataserviceToTest as IJsonGenericDatasource2,
				type: TestDataserviceDialogType.UNFORMATTED,
			},
		};
		return this.dialogService.show<ITestDataserviceParameters, string>(options);
	}

	private loadDatasources(): void {
		const cachedDataset =
			this.getFromCache<IJsonDatasource[]>(this.CACHE_KEY);

		if (Helper.isUndefinedOrNull(cachedDataset)) {
			this.loading = true;
			this.repositoryService.getDatasources().then((datasources: IJsonDatasource[]) => {
				this.Dataset = this.datagridOptions.dataset = datasources;
			}).catch((errorResponse) => {
				this.datagridOptions.dataset = [];
				this.dialogService.showError("Load Data Services", errorResponse.message);
			}).finally(() => {
				this.loading = false;
			});
		} else {
			this.datagridOptions.dataset = cachedDataset;
			this.clearSelected();
		}
	}

	public exportDatasource(): void {
		const ids: string[] = this.selectedDataItems.map((datasource) => {
			return datasource.id;
		});
		this.repositoryService.exportDatasources(ids);
	}

	public import(): void {
		const options: IDialogOptions<any> = {
			title: this.importActionTitle,
			template: require("../../../common/dialogs/import/import-dialog.html"),
		};
		this.dialogService.show(options).then((result: IDialogResult<FileList>) => {
			if (result.button === DialogButtonType.Ok) {
				this.loading = true;
				this.repositoryService.readJsonFile<IJsonDatasource[]>(result.value[0]).then((datasourcesToImport) => {
					this.importDatasources(datasourcesToImport);
				}).catch((errorResponse: IErrorResponse) => {
					// Unable to read input file, probable JSON parse error
					console.warn("[DatasourceRepositoryTabCtrl] Could not read file");
					this.dialogService.showError(
						this.importActionTitle,
						"Unable to read file due to: <br \>" + errorResponse.message
					);
				}).finally(() => {
					this.loading = false;
				});
			}
		});
	}

	private importDatasources(datasourcesToImport: IJsonDatasource[]): void {
		this.conflictResolutionService.resolve(
			datasourcesToImport,
			this.datagridOptions.dataset,
			"displayName",
			"datasource",
			Config.MAX_DATASOURCES
		).then((conflictResolutionObject) => {
			const datasourcesToAdd = conflictResolutionObject.toAdd;
			const idsToDelete = conflictResolutionObject.toRemove
				.map(datasourceToDelete => datasourceToDelete.id);

			if (idsToDelete.length > 0) {
				this.deleteDatasources(idsToDelete)
					.then(() => this.addImportedDatasources(datasourcesToAdd))
					.catch((errorResponse: IErrorResponse) => {
						console.warn("[DatasourceRepositoryTabCtrl] Could not replace, unable to remove old data services");
						this.loading = false;
						this.dialogService.showError(
							this.importActionTitle,
							"Unable to replace old data services: <br \>" + errorResponse.message
						);
					});
			} else {
				this.addImportedDatasources(datasourcesToAdd);
			}
		}).catch(() => {
			console.warn("[DatasourceRepositoryTabCtrl] Cancelled");
			this.loading = false;
		});
	}

	private addImportedDatasources(datasources: IJsonDatasource[]): void {
		this.repositoryService.addDatasources(datasources)
			.then((addedDatasources) => {
				this.conflictResolutionService.checkIfInvalidImportedItems<IJsonDatasource>(
					"data service(s)",
					addedDatasources,
				).catch((errorDialogOptions) => {
					this.dialogService.showMessage(errorDialogOptions);
				});
				for (const datasource of addedDatasources) {
					if (datasource) {
						this.datagridOptions.dataset.push(datasource);
					}
				}
				this.Dataset = this.datagridOptions.dataset;
			}).catch((errorResponse: IErrorResponse) => {
				this.dialogService.showError(
					this.importActionTitle,
					"Unable to import data services due to: \n" + errorResponse.message
				);
			}).finally(() => {
				this.loading = false;
			});
	}

	public delete(): void {
		const actionTitle = "Delete Data Services";
		const displayNames: string[] = [];
		const idsToDelete: string[] = this.selectedDataItems.map((datasource) => {
			displayNames.push(datasource.displayName);
			return datasource.id;
		});
		const options: IMessageDialogOptions = {
			title: actionTitle,
			message:
				`Are you sure you want to delete these ${displayNames.length} Data services?<br \> ${displayNames.join(", ")}`,
			standardButtons: StandardDialogButtons.YesNo
		};
		this.dialogService.showMessage(options).then((result) => {
			if (result.button === DialogButtonType.Yes) {
				this.loading = true;
				this.deleteDatasources(idsToDelete).catch((errorResponse: IErrorResponse) => {
					this.dialogService.showError(actionTitle, "Unable to delete due to:<br \>" + errorResponse.message);
				}).finally(() => {
					this.loading = false;
				});
			}
		});
	}

	private deleteDatasources(ids: string[]): ng.IPromise<void> {
		return this.repositoryService.deleteDatasources(ids).then(() => {
			this.Dataset = this.datagridOptions.dataset = this.datagridOptions.dataset.filter((datasource) => {
				return !ids.some((deletedId) => {
					return deletedId === datasource.id;
				});
			});
			this.clearSelected();
		});
	}

	public editDatasource(): void {
		// Only accept generic version 2
		const version: number = 2;
		const selectedDatasource = this.selectedDataItems[0];
		const isCorrectVersion = selectedDatasource.version === version;
		const typedef = selectedDatasource.typedef;
		let promise: ng.IPromise<any>;

		if (isCorrectVersion &&
			(typedef === DatasourceTypeEnum.generic ||
				typedef === DatasourceTypeEnum.IDO ||
				typedef === DatasourceTypeEnum.MONGOOSE ||
				typedef === DatasourceTypeEnum.INLINE_LIST)
		) {
			promise = this.openDataserviceWizard(selectedDatasource as IJsonGenericDatasource2);
		} else {
			/* TODO:
				Remove else clause, since it handles old and most likely unsupported BI
			*/
			const options: IDialogOptions<IUpsertDialogParameter> = {
				title: "Edit Data Service",
				template: require("../datasource/dialog/datasource-upsert-dialog.html"),
				parameter: {
					datasource: angular.copy(selectedDatasource),
					datasources: this.datagridOptions.dataset,
					isEdit: true
				},
				cssClass: "edit-modal"
			};
			promise = this.dialogService.show(options);
		}
		promise.then((result: IDialogResult<any>) => {
			if (result && result.button === DialogButtonType.Ok) {
				const datasource: IJsonDatasource = result.value;
				this.repositoryService.updateDatasource(datasource).then((response: IJsonDatasource) => {
					this.updateDatasource(response);
				}).catch((errorResponse: IErrorResponse) => {
					const options: IMessageDialogOptions = {
						title: "Edit Data Service",
						message: errorResponse.message,
						standardButtons: StandardDialogButtons.Ok,
						isError: true,
					};
					this.dialogService.showMessage(options);
				});
			}
		});
	}

	// TODO: make shared functon, same functionality in datasources-dialog.ts
	public openDataserviceWizard(
		datasource?: IJsonGenericDatasource2
	): ng.IPromise<IDialogResult<IJsonDatasource>> {
		const title = (datasource ? "Edit" : "Create") + " Data Service";

		let template: string = "<eir-wizard ng-controller='eirDatasourceWizardCtrl as wizardCtrl'></eir-wizard>";
		if (datasource) {
			switch (datasource.typedef) {
				case DatasourceTypeEnum.IDO:
					template = "<eir-wizard ng-controller='eirLocalIdoDataserviceWizardCtrl as wizardCtrl'></eir-wizard>";
					break;
				case DatasourceTypeEnum.INLINE_LIST:
					template = "<eir-wizard ng-controller='eirInlineListDataserviceWizardCtrl as wizardCtrl'></eir-wizard>";
					break;
				default:
					break;
			}
		}

		const options: IDialogOptions<IDatasourceWizardParameter> = {
			title: title,
			parameter: {
				datasource: angular.copy(datasource)
			},
			template,
			cssClass: "wizard-dialog large",
			id: "datasource-wizard"
		};

		return this.dialogService.show<IDatasourceWizardParameter, IJsonDatasource>(options).then((result) => {
			if (result.button === DialogButtonType.Ok) {
				if (datasource) {
					this.updateDatasource(result.value);
				} else {
					this.addDatasource(result.value);
				}
			}
			return result;
		});
	}

	private updateDatasource(datasource: IJsonDatasource): void {
		const cachedDatasources = this.repositoryService.cacheDatasources;
		for (let i = 0; i < cachedDatasources.length; i++) {
			if (cachedDatasources[i].id === datasource.id) {
				cachedDatasources[i] = datasource;
				break;
			}
		}
		this.datagridOptions.dataset.some((datasourceFind, index, array) => {
			if (datasourceFind.id === datasource.id) {
				array[index] = datasource;
				return true;
			}
			return false;
		});
		this.clearSelected();
	}

	public create(isSearch: boolean): void {
		if (Helper.exceedAllowed(Config.MAX_DATASOURCES, this.datagridOptions.dataset)) {
			this.dialogService.showError(this.dialogTitle + " Data Service", ProjectLimit.DATASOURCE_ERROR_MESSAGE);
		} else {
			if (isSearch) {
				const options: IDialogOptions<void> = {
					title: "Create from Wizard",
					template: require("../datasource/create-from-wizard/select-source.html"),
					cssClass: "select-source-modal"
				};
				this.dialogService.show(options)
					.then((result: IDialogResult<IJsonGenericDatasource>) => {
						if (result.button === DialogButtonType.Ok) {
							if (result.value) {
								this.addDatasource(result.value);
							}
						}
					});
			} else {
				const options: IDialogOptions<IUpsertDialogParameter> = {
					title: this.dialogTitle + " Data Service",
					template: require("../datasource/dialog/datasource-upsert-dialog.html"),
					parameter: {
						datasource: null,
						datasources: this.datagridOptions.dataset,
						isEdit: false
					},
					cssClass: "create-modal"
				};

				this.dialogService.show(options).then((result: IDialogResult<IDatasource>) => {
					if (result.button === DialogButtonType.Ok && result.value) {
						this.addDatasource(result.value);
					}
				});
				this.dialogTitle = "Create";
			}
		}
	}

	private addDatasource(datasource: IJsonDatasource): void {
		this.repositoryService.addDatasources([datasource]).then((newDatasource) => {
			this.Dataset = this.datagridOptions.dataset = newDatasource.concat(this.datagridOptions.dataset);
			this.clearSelected();
		}).catch((errorResponse: IErrorResponse) => {
			const options: IMessageDialogOptions = {
				title: "Create Data Service",
				message: errorResponse.message,
				standardButtons: StandardDialogButtons.Ok,
				isError: true
			};

			this.dialogService.showMessage(options);
		});
	}
}

export class DatasourceTabComponent implements ng.IComponentOptions {
	public controller = DatasourceTabCtrl;
	public controllerAs = "eirDatasourceTabCtrl";
	public template = require("./datasource-tab.html");

	public static add(m: ng.IModule): void {
		m.component("eirDatasourceTab", new DatasourceTabComponent());
	}
}
