import angular from "angular";

import { colors } from "ng2/common/constants/colors.constant";
import {
	CanvasModeEnum,
	CSSClasses,
	InjectNames,
	SaveFlagsEnum,
	ThemeEnum
} from "ng2/common/constants/common.constant";
import { IContext } from "ng2/common/model/components/base/context.model";
import { IJsonComponent } from "ng2/common/model/databearer/components/json-component.model";
import { IJsonLayoutComponent } from "ng2/common/model/databearer/json-layout-component.model";
import { IMap } from "ng2/common/model/imap.model";
import { IJsonLanguageComponent } from "ng2/common/model/language.model";
import { DesignerMashup } from "ng2/common/project/designer-mashup";
import { Interaction } from "ng2/components/interaction/interaction";
import { AngularConstants } from "../../../common/angular-constants";
import { Helper } from "../../../common/helper";
import { IComponentScope } from "../../../common/interface/icomponent-scope";
import { ILayoutComponent } from "../../../common/interface/ilayout-component";
import { ApplicationParent } from "../../../components/application-parent";
import { FrameworkService } from "../../../components/services/framework-service";
import { EditorApplicationParent } from "../components/editor-application-parent";
import { EditorLayoutComponentCtrl, IEditorLayoutComponent } from "../components/editor-layout-component";
import { EditorService } from "../services/editor-service";
import { InteractionManagementService } from "../services/interaction-management-service";
import { IUndoAction, UndoService } from "../services/undo-service";

export interface ICanvasStyle {
	backgroundColor: String;
	backgroundImage?: String;
}

export class CanvasCtrl {
	public canvasMode: CanvasModeEnum;
	public currentTheme: ThemeEnum;

	public rootComponent: IJsonComponent;
	public parent: ApplicationParent;
	public context: IContext;
	// TODO: When undo redo is smarter, move template to canvas.html
	private originalComponentDirective = `
		<eir-component-directive
			ng-if="eirCanvasCtrl.rootComponent"
			component="eirCanvasCtrl.rootComponent"
			parent="eirCanvasCtrl.parent"
			context="eirCanvasCtrl.context"
			component-ready="eirCanvasCtrl.onComponentReady(componentInstance)"
		></eir-component-directive>`;
	public canvasStyle: ICanvasStyle = {backgroundColor: colors.slate08};

	private mashupRootContainer: ng.IAugmentedJQuery;
	private unsubscribers: Array<Function> = [];
	private renderComponentsTimeout: ng.IPromise<any>;

	public static $inject = [
		AngularConstants.COMPILE,
		AngularConstants.ELEMENT,
		AngularConstants.ROOT_SCOPE,
		AngularConstants.SCOPE,
		AngularConstants.TIMEOUT,
		InjectNames.EDITOR_SERVICE,
		InjectNames.FRAMEWORK_SERVICE,
		InjectNames.INTERACTION_MANAGEMENT_SERVICE,
		InjectNames.UNDO_SERVICE,
	];

	public constructor(
		private $compile: ng.ICompileService,
		private $element: ng.IAugmentedJQuery,
		private $rootScope: ng.IRootScopeService,
		private $scope: ng.IScope,
		private $timeout: ng.ITimeoutService,
		private editorService: EditorService,
		private frameworkService: FrameworkService,
		private interactionManagementService: InteractionManagementService,
		private undoService: UndoService
	) {

		this.mashupRootContainer = $element.find("." + CSSClasses.mashupRootContainer);
		// Init the unsubscribers
		this.initUnsubscribers();

		// Current project will always exist before controller initialization
		const currentProject = this.editorService.getCurrentProject();
		this.openProject(currentProject);

		// Init the canvas
		this.initCanvas();

		$scope.$on(AngularConstants.DESTROY, () => {
			angular.forEach(this.unsubscribers, (unsubscribe) => {
				unsubscribe();
			});
		});

	}

	/**
	 * Initiate the canvas to its correct values
	 *
	 * @private
	 *
	 * @memberof CanvasCtrl
	 */
	private initCanvas(): void {
		this.editorService.setCanvasMode(CanvasModeEnum.SELECTION);
		this.currentTheme = this.editorService.getCurrentCanvasTheme();
		this.canvasStyle = this.getCanvasStyle(this.currentTheme);
	}

	/**
	 * Initiate all the unsubscribers
	 *
	 * @private
	 * @param {UndoService} undoService
	 * @param {EditorService} editorService
	 *
	 * @memberof CanvasCtrl
	 */
	private initUnsubscribers(): void {
		const {undoService, editorService} = this;
		this.unsubscribers.push(undoService.undoActionEvent.on((undoAction: IUndoAction) => {
			this.updateCanvas(undoAction);
		}));

		this.unsubscribers.push(editorService.canvasModeChangedEvent.on((newMode: CanvasModeEnum) => {
			this.canvasMode = newMode;
		}));

		this.unsubscribers.push(editorService.currentCanvasThemeChangedEvent.on((theme: ThemeEnum) => {
			this.currentTheme = theme;
			this.canvasStyle = this.getCanvasStyle(theme);
		}));
	}

	/**
	 * Gets the canvas styling depending on what the theme is
	 *
	 * @private
	 * @param {ThemeEnum} theme
	 * @returns {ICanvasStyle}
	 *
	 * @memberof CanvasCtrl
	 */
	private getCanvasStyle(theme: ThemeEnum): ICanvasStyle {
		const style: ICanvasStyle = {
			backgroundColor: colors.graphite01
		};
		switch (theme) {
			case ThemeEnum.LIGHT:
				style.backgroundColor = colors.graphite01;
				break;
			case ThemeEnum.LIGHT_TRANSPARENT:
				style.backgroundColor = colors.graphite01;
				style.backgroundImage = `url("designer/images/transparent-grid.png")`;
				break;
			case ThemeEnum.DARK:
				style.backgroundColor = colors.slate08;
				break;
			case ThemeEnum.DARK_TRANSPARENT:
				style.backgroundColor = colors.slate08;
				style.backgroundImage = `url("designer/images/transparent-grid-light.png")`;
				break;
			case ThemeEnum.HIGH_CONTRAST:
				style.backgroundColor = colors.graphite03;
				break;
			default:
				style.backgroundColor = colors.graphite01;
				break;
		}
		return style;
	}

	private openProject(project: DesignerMashup): void {
		this.parent = new EditorApplicationParent(
			ApplicationParent.applicationParentDatabearer,
			ApplicationParent.createSpoofedElement(this.$rootScope.$new() as IComponentScope),
			null,
			this.frameworkService);
		this.context = {};
		this.rootComponent = project.rootComponent;
		this.compileComponent();
		this.undoService.clearActions();
		this.undoService.pushUndoAction(project);
	}

	public onComponentReady(componentInstance: ILayoutComponent): void {
		this.$timeout.cancel(this.renderComponentsTimeout);
		this.parent.addChildInstance(componentInstance);
		this.editorService.setCurrentRootComponentInstance(componentInstance);

		//TODO: backwards compatibility, remove in the distant future
		this.editorService.createLanguageComponentFromOldComponents();

		this.interactionManagementService.runStartupInteractions();
		// New boundary entered will recursively update styles and classes
		componentInstance.newBoundaryEntered(this.editorService.getCurrentBoundary().id);
		//Trigger expressions on startup
		this.frameworkService.triggerExpressions();

		Helper.appIsReadyForSelenium();
	}

	private updateCanvas(undoAction: IUndoAction): void {
		const component: IJsonLayoutComponent = undoAction.stateRootComponent;
		const interactions: Interaction[] = undoAction.stateInteractions;
		const translations: IMap<IJsonLanguageComponent[]> = undoAction.stateTranslations;
		const currentProject = this.editorService.getCurrentProject();

		currentProject.setRoot(component);
		this.rootComponent = component;
		//sync translations, do not set
		this.editorService.syncTranslations(translations);
		currentProject.setInteractions(interactions);

		// not saving canvas due to not wanting a new undo stack
		// tslint:disable:no-bitwise
		this.editorService.saveCurrent(
			SaveFlagsEnum.CANVAS_COMPONENTS |
			SaveFlagsEnum.TRANSLATIONS |
			SaveFlagsEnum.INTERACTIONS
		);
		// tslint:enable:no-bitwise
		this.compileComponent();
		this.editorService.deselectComponent();
	}

	private compileComponent(): void {
		const newScope = this.$scope.$new() as IComponentScope;
		newScope.component = this.rootComponent;
		newScope.parent = this.parent;
		newScope.context = this.context;
		const tenSecondsInMillis = 10000;
		this.renderComponentsTimeout = this.$timeout(() => {
			if (this.parent.children.length < 1) {
				// Show error in dialog here for user
				throw new Error("[Canvas] Problem opening project");
			}
		}, tenSecondsInMillis);
		const template = EditorLayoutComponentCtrl.createTemplate(this.rootComponent.typedef);
		template.attr("component-ready", "eirCanvasCtrl.onComponentReady(componentInstance)");
		template.attr("component-destroyed", "eirCanvasCtrl.onComponentDestroyed(componentInstance)");

		this.$compile(template)(newScope, (clone) => {
			this.mashupRootContainer.empty();
			this.mashupRootContainer.append(clone);
		});
	}

	public onComponentDestroyed(component: IEditorLayoutComponent): void {
		// Rootcomponent gets destroyed when navigating to dashboard
	}
}

export class CanvasDirective {
	public static add(m: ng.IModule): void {
		m.directive("eirCanvas", [
			() => {
				return {
					scope: {},
					template: require("./canvas.html"),
					controller: CanvasCtrl,
					controllerAs: "eirCanvasCtrl"
				};
			}]);
	}
}
