import { AngularConstants } from "../../angular-constants";

import { Subcodes } from "ng2/common/constants/common.constant";
import { IDialogBaseScope } from "../../defines";
import { IErrorResponse } from "../../error-response";
import { DialogButtonType, IDialog, IDialogResult } from "../../services/dialog-service";

export interface IConflictDialogState {
	confirmButton: {
		text: string,
		type: DialogButtonType,
	};
	extraInfo: string;
}

export class ImportErrorCtrl {
	public information: string;
	public extraInfo: string;
	public confirmButtonDisplayName: string;
	private dialog: IDialog<IErrorResponse>;

	static $inject = [AngularConstants.SCOPE];

	constructor(private scope: IDialogBaseScope<IErrorResponse>) {
		const dialog = scope.eirDialog;
		const state = this.stateBasedOnSubcode(dialog.parameter.subcode);
		this.dialog = dialog;
		this.information = dialog.parameter.message;
		this.extraInfo = state.extraInfo;
		this.confirmButtonDisplayName = state.confirmButton.text;
	}

	/**
	 * Depending on the given subcode, this import dialog will have different state
	 *
	 * @private
	 * @param {number} subcode
	 * @returns {IConflictDialogState}
	 *
	 * @memberof ImportConflictCtrl
	 */
	private stateBasedOnSubcode(subcode: number): IConflictDialogState {
		switch (subcode) {
			case Subcodes.PROJECT_ID_CONFLICT:
				return {
					confirmButton: {
						text: "Replace",
						type: DialogButtonType.Yes
					},
					extraInfo: "Would you like to replace that version?",
				};
			case Subcodes.PROJECT_DISPLAYNAME_CONFLICT:
				return {
					confirmButton: {
						text: "Replace",
						type: DialogButtonType.Yes
					},
					extraInfo: "Would you like to replace the existing App?",
				};
			default:
				return {
					confirmButton: {
						text: "Confirm",
						type: DialogButtonType.None
					},
					extraInfo: "",
				};
		}
	}

	public close(): void {
		this.dialog.close();
	}

	public confirm(): void {
		const state = this.stateBasedOnSubcode(this.dialog.parameter.subcode);
		const result: IDialogResult<string> = {
			button: state.confirmButton.type
		};
		this.dialog.close(result);
	}

	static add(m: ng.IModule): void {
		m.controller("eirImportErrorCtrl", ImportErrorCtrl);
	}
}
