import { IJsonTag } from "ng2/common/model/databearer/json-tag.model";
import { AngularConstants } from "../../angular-constants";
import * as xi from "../../model/sohoxi/sohoxi-tooltip.model";

export class ProjectTagsComponent {
	public tags: IJsonTag[];
	public tagsOverflowed = false;

	public static $inject = [
		AngularConstants.TIMEOUT,
		AngularConstants.ELEMENT,
	];

	public constructor(
		private $timeout: ng.ITimeoutService,
		private $element: ng.IAugmentedJQuery
	) { }

	public checkOverflow(): void {
		//Timeout needed for getting the correct tag names instead of variable names.
		this.$timeout(() => {
			const tagContainerElement = this.$element.find(".tag-container")[0];
			const overflowed = tagContainerElement.offsetHeight < tagContainerElement.scrollHeight;
			if (overflowed) {
				this.tagsOverflowed = true;
			}
		}, 1);
	}

	public moreTagInit(): void {
		const popoverOptions: xi.ITooltipOptions = {
			popover: true,
			content: this.$element.find("#popover-contents"),
			placement: "bottom",
			title: "Tags",
			extraClass: "alternate"
		};

		this.$element.find("#popover-trigger").popover(popoverOptions);
	}

	public static add(m: ng.IModule): void {
		m.component("eirProjectTags", {
			bindings: {
				tags: "<"
			},
			template: require("./project-tags-component.html"),
			controller: ProjectTagsComponent,
			controllerAs: "ctrl",
		});
	}
}
