import { AngularConstants } from "../../angular-constants";

import { IMap } from "ng2/common/model/imap.model";
import { Helper } from "../../helper";
import { FieldValidation } from "../base/field-validation";

export class DropdownFieldCtrl extends FieldValidation {

	public $static = [
		AngularConstants.ELEMENT,
		AngularConstants.SCOPE,
	];

	// Bindings
	public listValues: string[];
	public onChange: Function;

	constructor(
		$element: ng.IAugmentedJQuery,
		$scope: ng.IScope,
	) {
		super();

		$element
			.on("listclosed", () => {
				if (!Helper.isUndefined(this.validation)) {
					this.validate();

					// needs to trigger Angular digest cycle
					$scope.$applyAsync();
				}
			})
			.on("change", () => {
				if (this.onChange) {
					this.onChange();
				}
			});
	}
}

export class DropdownFieldComponent implements ng.IComponentOptions {
	public controller = DropdownFieldCtrl;
	public template = require("./dropdown-template.html");
	public bindings: IMap<string> = {
		id: "@?",
		labelName: "@?",
		fieldValue: "=",
		validation: "<?",
		readonly: "<?",
		listValues: "<",
		onChange: "&?"
	};

	public static add(m: ng.IModule): void {
		m.component(
			"eirDropdownFieldComponent",
			new DropdownFieldComponent());
	}
}
