import { AngularConstants } from "../../../../../../common/angular-constants";

import { IExtendedDataGridColumn } from "ng2/common/model/databearer/components/json-datagrid.model";

import { IDialogBaseScope } from "../../../../../../common/defines";
import { DialogButtonType, IDialog, IDialogResult } from "../../../../../../common/services/dialog-service";

export class DeleteColumnDialogCtrl {
	public dialog: IDialog<IExtendedDataGridColumn[]>;
	public columns: IExtendedDataGridColumn[];
	public selectedColumn: IExtendedDataGridColumn;
	static $inject = [
		AngularConstants.SCOPE
	];

	constructor(
		scope: IDialogBaseScope<IExtendedDataGridColumn[]>
	) {
		const dialog: IDialog<IExtendedDataGridColumn[]> = scope.eirDialog;
		this.dialog = dialog;
		this.columns = dialog.parameter;
		this.selectedColumn = this.columns[0];
	}

	public close(): void {
		const result: IDialogResult<IExtendedDataGridColumn[]> = {
			button: DialogButtonType.Ok,
			value: this.columns
		};
		this.dialog.close(result);
	}

	public deleteColumn(): void {
		this.columns = this.columns.filter(item => item.id !== this.selectedColumn.id);
		this.selectedColumn = this.columns[0];
	}

	static add(m: ng.IModule): void {
		m.controller("eirDeleteColumnDialogCtrl", DeleteColumnDialogCtrl);
	}
}
