import angular from "angular";
import { AngularConstants } from "../../../common/angular-constants";

import { IDatasource } from "common/model/datasource/datasource.model";
import { InjectNames, Numbers, SettingKeys } from "ng2/common/constants/common.constant";
import { OrientationEnum } from "ng2/common/enums/orientation.enum";
import { IJQueryMouseEventObject, KeyCodeEnum } from "../../../common/defines";
import { IComponentScope } from "../../../common/interface/icomponent-scope";
import { Flexbox } from "../../../components/flexbox/flexbox";
import { TabPanel } from "../../../components/tabs/tab-panel";
import type { IEditorComponent } from "common/interface/ieditor-component";
import { IEditorFlexbox } from "../components/editor-flexbox";
import { InteractionManagementService } from "../services/interaction-management-service";

import { CompositeComponentManagementService } from "../services/composite-component-management-service";
import { EditorService } from "../services/editor-service";
import { HotkeyService } from "../services/hotkey-service";

export interface IContextAction {
	id: string;
	icon?: string;
	orientation?: number;
	name: string;
	actionMethod: Function;
	submenu?: boolean;
	enable: boolean;
	disabled?: boolean;
}

export class SelectionBoxCtrl {
	public componentNameForm: any;
	public isComponentNameEditMode = false;
	public contextActions: IContextAction[];
	public componentInstance: IEditorComponent;
	public flexOrientation: OrientationEnum;
	private unsubscribers: Array<Function> = [];

	private actionKeys = {
		ORIENTATION: "orientation",
		INTERACTION: "interaction",
		COMPOSITE: "composite",
		DELETE: "delete"
	};

	static $inject = [
		AngularConstants.SCOPE,
		AngularConstants.ELEMENT,
		InjectNames.EDITOR_SERVICE,
		InjectNames.COMPOSITE_COMPONENT_MANAGEMENT_SERVICE,
		InjectNames.HOTKEY_SERVICE,
		InjectNames.INTERACTION_MANAGEMENT_SERVICE
	];

	constructor(
		private $scope: IComponentScope,
		private $element: ng.IAugmentedJQuery,
		private editorService: EditorService,
		private compositeComponentManagementService: CompositeComponentManagementService,
		private hotkeyService: HotkeyService,
		private interactionManagementService: InteractionManagementService
	) {
		const componentInstance = this.$scope.componentInstance as IEditorComponent;
		this.componentInstance = componentInstance;

		this.unsubscribers.push(this.editorService.currentFlexOrientationChangedEvent.on((value: OrientationEnum) => {
			this.setOrientationIcon(value);
			this.flexOrientation = value;
		}));

		this.unsubscribers.push(this.editorService.currentFlexIsParentChangedEvent.on((value: boolean) => {
			this.updateOrientationMenuVisibility(value);
		}));

		this.unsubscribers.push(this.editorService.selectedDatasourceChangedEvent
			.on(newDatasource => {
				this.setSelectionBoxOptions(
					this.isLayoutComponentWithNoDataservice(newDatasource));
			}));

		const keyHandler = (event: JQueryEventObject) => {
			// Body focus to solve blur in IE/Edge
			if (event.keyCode === KeyCodeEnum.ESCAPE) {
				this.isComponentNameEditMode = false;
				this.componentNameForm.$rollbackViewValue();
				$("body").focus();
			} else if (event.keyCode === KeyCodeEnum.ENTER) {
				this.isComponentNameEditMode = false;
				$("body").focus();
			}
		};

		$element.on("keydown", keyHandler);

		this.setSelectionBoxOptions(this.isLayoutComponentWithNoDataservice());

		this.flexOrientation = this.componentInstance.getBoundarySetting<OrientationEnum>(SettingKeys.ORIENTATION);
		this.setOrientationIcon(this.flexOrientation);

		$scope.$on(AngularConstants.DESTROY, () => {
			$element.off("keydown", keyHandler);
			angular.forEach(this.unsubscribers, (unsubscribe) => {
				unsubscribe();
			});
		});

		this.updateSize();
	}

	private isLayoutComponentWithNoDataservice(datasource?: IDatasource): boolean {
		const component = this.editorService.getSelectedComponent();
		const newDatasource = datasource ? datasource : component.getDatasource();

		return component.isLayoutComponent() && !newDatasource;
	}

	private setSelectionBoxOptions(isLayoutComponentWithNoDataservice: boolean): void {
		this.contextActions = [
			{
				id: this.actionKeys.ORIENTATION,
				orientation: this.checkOrientationOnFlexbox(),
				name: "Change Orientation",
				actionMethod: () => { this.changeOrientation(); },
				submenu: true,
				enable: this.isFlexboxWithFlexChildren(),
			}, {
				id: this.actionKeys.INTERACTION,
				icon: "action-interaction",
				name: "Create Interaction",
				actionMethod: () => {
					this.interactionManagementService.startInteractionWizard(
						this.componentInstance);
				},
				enable: true,
				disabled: isLayoutComponentWithNoDataservice,
			}, {
				id: this.actionKeys.COMPOSITE,
				icon: "save",
				name: "Save as Composite Component",
				actionMethod: () => {
					this.compositeComponentManagementService.createNewViaWizard(
						this.componentInstance.getDatabearer());
				},
				enable: true
			}, {
				id: this.actionKeys.DELETE,
				name: "Delete",
				actionMethod: () => { this.hotkeyService.deleteComponent(); },
				enable: true,
				disabled: this.getSelectionOptionDisabledStatus()
			}
		];
	}

	/**
	 * Returns true, to 'disable' delete option  if componentInstance is:
	 * 	AppPanel
	 * 	TabPanel
	 * 	Generated component
	 */
	private getSelectionOptionDisabledStatus(): boolean {
		const componentInstance = this.componentInstance;

		return componentInstance.getId() === "AppPanel" ||
			componentInstance.getTypedef() === TabPanel.name ||
			componentInstance.isGenerated;
	}

	private updateSize(): void {
		const extraDisplayNameWidth = 6;
		const extraWrapperWitdh = 33;
		const newWidth = this.componentInstance.getDisplayName().length * extraDisplayNameWidth;
		this.$element.find(".selectionbox-wrapper").css("width", newWidth + extraWrapperWitdh);
		this.$element.find(".selBoxComponentNameInput").css("width", newWidth);
	}

	public checkPopupPosition(): void {
		//If outside of screen move the popup inside of canvas.
		const menuElement = this.$element.find(".icon-selectionbox");
		const popupElement = this.$element.find(".popupmenu-wrapper");
		const left = menuElement.offset().left - Numbers.HUNDRED;
		const canvasWidth = angular.element("#insideRuler").width();

		const isEntirelyVisible = (left <= canvasWidth);

		if (!isEntirelyVisible) {
			popupElement.addClass("edge");
		}
	}

	private updateOrientationMenuVisibility(showOrientationAction: boolean): void {
		const actionItem = this.getContextAction(this.actionKeys.ORIENTATION);
		actionItem.enable = showOrientationAction;
	}

	private setOrientationIcon(orientation: OrientationEnum): void {
		const actionItem = this.getContextAction(this.actionKeys.ORIENTATION);
		actionItem.icon = "action-orientation-" + orientation;
		actionItem.orientation = this.checkOrientationOnFlexbox();
	}

	private checkOrientationOnFlexbox(): number {
		return this.componentInstance.getBoundarySetting<OrientationEnum>(SettingKeys.ORIENTATION);
	}

	private getContextAction(id: string): IContextAction {
		for (let i = 0; i < this.contextActions.length; i++) {
			const actionItem = this.contextActions[i];
			if (actionItem.id === id) {
				return actionItem;
			}
		}
		console.warn("Context action '" + id + "' does not exist");
		return null;
	}

	private changeOrientation(): void {
		if (this.flexOrientation === OrientationEnum.HORIZONTAL) {
			this.flexOrientation = OrientationEnum.VERTICAL;
		} else {
			this.flexOrientation = OrientationEnum.HORIZONTAL;
		}

		this.componentInstance.setBoundarySetting<OrientationEnum>(SettingKeys.ORIENTATION, this.flexOrientation);
		const actionItem = this.getContextAction(this.actionKeys.ORIENTATION);
		actionItem.orientation = this.checkOrientationOnFlexbox();
		this.editorService.currentFlexOrientationChangedEvent.raise(this.flexOrientation);
		this.editorService.refreshSettingEvent.raise(SettingKeys.ORIENTATION);
		this.editorService.saveCanvas();
	}

	private isFlexboxWithFlexChildren(): boolean {
		return (
			this.componentInstance.getTypedef() === Flexbox.name ||
			this.componentInstance.getTypedef() === TabPanel.name) &&
			(this.componentInstance as IEditorFlexbox).isFlexParent;
	}

	public onBlur(): void {
		this.updateSize();
		this.isComponentNameEditMode = false;
		this.editorService.saveCanvas();
	}

	public onDoubleClick(event: IJQueryMouseEventObject): void {
		this.isComponentNameEditMode = true;

		// MUPS-687 Inline editing of component names
		// Strangly. Having a timeout and trigger blur and then trigger focus fix for Edge and IE.
		setTimeout(() => {
			(event.target as HTMLInputElement).blur();
			(event.target as HTMLInputElement).focus();
		}, 1);
	}
}

export class SelectionBoxDirective {
	static add(m: ng.IModule): void {
		m.directive("eirSelectionBox", () => {
			return {
				controller: SelectionBoxCtrl,
				controllerAs: "eirSelectionBoxCtrl",
				template: require("./selection-box.html")
			};
		});
	}
}
