import { AngularConstants } from "../../../../../../common/angular-constants";

import { IMap } from "ng2/common/model/imap.model";

import { IDialogBaseScope } from "../../../../../../common/defines";
import { IValidation } from "../../../../../../common/shared-components/base/field-validation";

import { DataServiceHelper } from "ng2/common/helpers/data-service.helper";
import { DialogButtonType, IDialog, IDialogResult } from "../../../../../../common/services/dialog-service";

export class EditUrlDialogCtrl {

	public get resourceUrl(): string {
		return this.dialog.parameter.resource;
	}
	public set resourceUrl(value: string) {
		this.dialog.parameter.resource = value;
	}

	public errorList: IValidation[] = [];
	public get IsInvalid(): boolean {
		return this.errorList.length !== 0;
	}

	private dialog: IDialog<IMap<string>>;

	public static $inject = [
		AngularConstants.SCOPE
	];

	public constructor(
		$scope: IDialogBaseScope<IMap<string>>
	) {
		this.dialog = $scope.eirDialog;
	}

	public cancel(): void {
		this.dialog.close();
	}

	public ok(): void {
		this.dialog.close({
			value: this.resourceUrl,
			button: DialogButtonType.Ok
		} as IDialogResult<string>);
	}

	public validate(value: string): boolean {
		return DataServiceHelper.validate(value);
	}

	public static add(m: ng.IModule): void {
		m.controller("eirEditUrlDialogCtrl", EditUrlDialogCtrl);
	}
}
