import {
	Component,
	EventEmitter,
	Input,
	OnInit,
	Output,
} from "@angular/core";

import type { IContext } from "ng2/common/model/components/base/context.model";
import type { ILayoutComponent } from "ng2/common/model/components/base/layout-component.model";
import type { IJsonComponent } from "ng2/common/model/databearer/components/json-component.model";
import type { IJsonLayoutComponent } from "ng2/common/model/databearer/json-layout-component.model";

@Component({
	selector: "children-component-container",
	template: `
		<component-container
			*ngFor="let child of parent.getDatabearer().children"
			[component]="child"
			[context]="context"
			[parent]="parent"
			(componentReady)="onComponentReady($event)"
		></component-container>
	`,
})
export class ChildrenComponentContainer implements OnInit {

	@Input() parent: ILayoutComponent;
	@Output() onChildrenReady = new EventEmitter();

	context: IContext;

	private numberOfChildren: number;
	private parentChildren: IJsonComponent[];

	ngOnInit(): void {

		const parentInstance = this.parent;
		this.parentChildren =
			(parentInstance.getDatabearer() as IJsonLayoutComponent).children;

		this.numberOfChildren = this.parentChildren.length;
		if (this.numberOfChildren === 0) {
			this.onChildrenReady.emit();
		}

		this.context = parentInstance.context;
	}

	onComponentReady(component: ILayoutComponent): void {

		this.numberOfChildren--;
		console.info(`ID: ${this.parent.displayName}, TYPEDEF: ${this.parent.typedef}:
	Child Component: '${component.displayName}' in '${this.parent.displayName}' is ready!
	Number of children left ${this.numberOfChildren} to render`);

		const index = this.parentChildren.findIndex((child: IJsonComponent) => child.id === component.id);

		if (index !== -1) {
			this.parent.children[index] = component;
		} else {
			throw Error("Should not happen");
		}

		if (this.numberOfChildren === 0) {
			this.onChildrenReady.emit();
		}
	}

}
