import {
	ComponentClassNames,
	ComponentNames,
	SettingKeys,
} from "ng2/common/constants/common.constant";

import type { IDatasource } from "common/model/datasource/datasource.model";
import type { IMetaDataField } from "ng2/common/model/dataservice/meta-data-field.model";

import type { IEditorComponentWithUpsertDatagridColumnSettings } from "editor/component-settings/upsert-datagrid-columns-content-settings";
import { EditorComponentCtrl } from "editor/components/editor-component";

export interface IEditorDatagrid extends IEditorComponentWithUpsertDatagridColumnSettings { }

export class EditorDatagridCtrl extends EditorComponentCtrl {
	protected component: IEditorDatagrid;

	protected clearData(): void {
		super.clearData();
		this.component.clear();
	}

	getMetaData(fieldRef: string, onSuccess: Function): void {
		const datasource = this.component.getDatasource();
		if ((fieldRef === SettingKeys.SELECTED_ITEM || fieldRef === SettingKeys.DIRTY_ITEMS) && datasource) {
			datasource.getMetaData().then((metaData) => {
				onSuccess(metaData.outputFields);
			});
		} else if (fieldRef === SettingKeys.SUMMARY_OUTPUT) {
			const outputFields: IMetaDataField[] = [];
			const component = this.component as any; // Datagrid
			if (component.summaryOutput) {
				Object.entries(component.summaryOutput).forEach(([key]) => {
					outputFields.push({
						FLNM: key,
						FLDS: key,
						MAND: "0"
					});
				});
				onSuccess(outputFields);
			}
		} else if (fieldRef === SettingKeys.ROW_COUNT_OUTPUT) {
			const rowCount: IMetaDataField[] = [];
			const component = this.component as any; // Datagrid
			if (component.rowCountOutput) {
				Object.entries(component.rowCountOutput).forEach(([key]) => {
					rowCount.push({
						FLNM: key,
						FLDS: "",
						MAND: "0"
					});
				});
				onSuccess(rowCount);
			}
		}
	}

	setDatasource(newDatasource: IDatasource): ng.IPromise<void> {
		return super.setDatasource(newDatasource).then(() => {
			this.component.updateColumns();
		});
	}

	refreshMetaSettings(): void {
		this.component.updateValuesWithTranslations();
	}

	static add(m: ng.IModule): void {
		m.controller(
			ComponentClassNames.EDITOR_COMPONENT_PREFIX + ComponentNames.Datagrid,
			EditorDatagridCtrl
		);
	}
}
