import { Helper } from "./helper";

class ExcludeFromFilter {
	static add(m: ng.IModule): void {
		m.filter("eirExcludeFrom", () => {
			return (inputArray: any[], criteria: any, itemProperty: string, selectedItem: any): any[] => {

				if (criteria instanceof Array) {
					return inputArray ? inputArray.filter((item: any) => {
						let keepItem = true;
						const compareValue1: any = (itemProperty ? item[itemProperty] : item);
						criteria.forEach((item2: any) => {
							const compareValue2: any = (itemProperty ? item2[itemProperty] : item2);
							if (compareValue1 === compareValue2) {
								keepItem = false;
							}
						});
						if (selectedItem && selectedItem[itemProperty] === compareValue1) {
							keepItem = true;
						}

						return keepItem;
					}) : inputArray;
				} else {
					return inputArray ? inputArray.filter((item: any) => {
						const compareValue1: any = (itemProperty ? item[itemProperty] : item);
						if (criteria === compareValue1) {
							return false;
						}

						return true;
					}) : inputArray;
				}
			};
		});
	}
}

class OnlyIncludeFilter {
	static add(m: ng.IModule): void {
		m.filter("eirOnlyInclude", () => {
			return (inputArray: any[], criteria: any, itemProperty1: string, itemProperty2: string) => {
				return inputArray ? inputArray.filter((item: any) => {
					const compareValue1: any = (itemProperty1 ? item[itemProperty1] : item);
					const compareValue2: any = (itemProperty2 ? item[itemProperty2] : item);
					if (criteria === compareValue1) {
						return true;
					}
					if (criteria === compareValue2) {
						return true;
					}
					return false;
				}) : inputArray;
			};
		});
	}
}

interface IFilterHtmlEncode {
	(html: string): string;
}
class HtmlEncode {
	static add(m: ng.IModule): void {
		m.filter("eirHtmlEncode", () => {
			return (input: string) =>
				Helper.htmlEncode(input);
		});
	}
}

export class Filters {
	static add(m: ng.IModule): void {
		ExcludeFromFilter.add(m);
		OnlyIncludeFilter.add(m);
		HtmlEncode.add(m);
	}
}
