import * as xi from "../../model/sohoxi/sohoxi-tabs.model";
import { Utils } from "../../sohoxi/utils";

export class TabsDirective implements ng.IDirective {
	public restrict = "A";
	public scope = false;

	private static readonly DIRECTIVE_NAME = "xiTabs";

	constructor(private $timeout: ng.ITimeoutService) {}

	public link(
		scope: ng.IScope,
		element: ng.IAugmentedJQuery,
		attributes: ng.IAttributes
	): void {
		const options = attributes[TabsDirective.DIRECTIVE_NAME];
		const unbindWatcher = scope.$watch(
			options,
			(newOptions: xi.ITabsOptions) => {
				if (newOptions) {
					if (newOptions.modalId) {
						let tabToFocus;
						const modalElement = $("#" + newOptions.modalId);

						// Force recalculation of modal size
						modalElement.addClass("hidden");

						modalElement.one("open", () => {
							modalElement.removeClass("hidden");
							element.tabs(newOptions as any);
							Utils.getData(modalElement, "modal").resize();

							this.$timeout(() => {
								tabToFocus = modalElement.find(
									":focusable:not(.searchfield):first"
								);
								if (tabToFocus.length) {
									tabToFocus.focus();
								}
								if (newOptions.tabToSelect) {
									const tab = modalElement.find(
										"li" + newOptions.tabToSelect
									);
									if (tab.length) {
										tab.click();
									}
								}
							}, 10); // tslint:disable-line:no-magic-numbers
						});
					} else {
						element.tabs(newOptions as any);
					}
				} else {
					element.tabs();
				}
			}
		);
		// Tear down
		element.on("$destroy", () => {
			const tabData = Utils.getData(element, "tabs");
			if (tabData) {
				tabData.destroy();
			}
			unbindWatcher();
		});
	}

	public static add(m: ng.IModule): void {
		m.directive(TabsDirective.DIRECTIVE_NAME, [
			"$timeout",
			timeout => {
				return new TabsDirective(timeout);
			}
		]);
	}
}
