import { AngularConstants } from "../angular-constants";

import { InjectNames } from "ng2/common/constants/common.constant";
import { ErrorResponse, IErrorResponse } from "../error-response";
import { Helper } from "../helper";
import {
	DialogButtonType, DialogService, IDialogOptions,
	IDialogResult, IMessageDialogOptions, StandardDialogButtons
} from "./dialog-service";

export enum ConflictResolutionEnum { REPLACE = 1, COPY = 2, SKIP = 3 }

export interface IConflictObject {
	objectName: string;
	conflictedString: string;
	occupiedStrings: string[];
	remainingCount: number;
	lastAction: ConflictResolutionEnum;
	maxItems: number;
}

export interface IConflictResult {
	action: ConflictResolutionEnum;
	sameForRemaining: boolean;
	newName: string;
}

export interface IResolutionResult<T> {
	toRemove: T[];
	toAdd: T[];
}

interface IConflictResolution<T> {
	object: T;
	resolution: ConflictResolutionEnum;
	newName: string;
}

interface ISplitConflict<T> {
	conflicting: T[];
	notConflicting: T[];
}

export class ConflictResolutonService {
	public maxItems: number = 0;
	private conflictKey: string;
	private occupiedStrings: string[];
	private objectName: string;
	private conflictingObjects: any[];

	static $inject = [
		AngularConstants.Q,
		InjectNames.DIALOG_SERVICE
	];

	constructor(
		private $q: ng.IQService,
		private dialogService: DialogService
	) {

	}

	public resolve<T>(
		newObjects: T[],
		currentObjects: T[],
		conflictKey: string,
		objectName: string,
		maxItems: number
	): ng.IPromise<IResolutionResult<T>> {
		this.objectName = objectName;
		this.maxItems = maxItems;
		this.conflictKey = conflictKey;
		this.occupiedStrings = currentObjects.map((object) => {
			return Helper.getDeepValue(object, conflictKey);
		});

		const splitConflicting = this.splitConflicting(newObjects, currentObjects);
		this.conflictingObjects = splitConflicting.conflicting;
		this.occupiedStrings = this.occupiedStrings.concat(splitConflicting.notConflicting.map((notConflicting) => {
			return Helper.getDeepValue(notConflicting, conflictKey);
		}));

		const deferredResolutions = this.$q.defer<IResolutionResult<T>>();
		if (this.conflictingObjects.length > 0) {
			this.resolveNextConflict<T>().then((resolutions) => {
				const resolutionResult = this.applyResolutions(resolutions, currentObjects);
				resolutionResult.toAdd = resolutionResult.toAdd.concat(splitConflicting.notConflicting);
				deferredResolutions.resolve(resolutionResult);
			}).catch((e: IErrorResponse) => {
				deferredResolutions.reject(e.message);
			});
		} else {
			deferredResolutions.resolve({
				toRemove: [],
				toAdd: newObjects
			});
		}
		return deferredResolutions.promise;
	}

	public checkIfInvalidImportedItems <T>(errorText: string, importedItems?: T[]): ng.IPromise<IMessageDialogOptions> {
		const deferred = this.$q.defer<IMessageDialogOptions>();

		if (!importedItems) {
			deferred.resolve();
			return deferred.promise;
		}

		const failedItems = importedItems.some((item: T) => {
			return item ? false : true;
		});

		if (failedItems) {
			const options: IMessageDialogOptions = {
				title: "Import Project",
				message: `One or more ${errorText} was not imported due to error in them`,
				standardButtons: StandardDialogButtons.Ok,
				isError: true,
			};
			deferred.reject(options);
			return deferred.promise;
		} else {
			deferred.resolve();
			return deferred.promise;
		}
	}

	private splitConflicting<T>(newObjects: T[], oldObjects: T[]): ISplitConflict<T> {
		const conflicting: T[] = [];
		const notConflicting: T[] = [];
		newObjects.forEach((newObject) => {
			const hasDisplayNameConflict = oldObjects.some((existingObject) => {
				return this.getConflictString(existingObject) === this.getConflictString(newObject);
			});
			hasDisplayNameConflict ? conflicting.push(newObject) : notConflicting.push(newObject);
		});
		return { conflicting: conflicting, notConflicting: notConflicting };
	}

	private getConflictString(object: any): string {
		return Helper.getDeepValue(object, this.conflictKey);
	}

	private resolveConflictDialog(
		conflictedString: string,
		index: number,
		conflictCount: number,
		lastAction: ConflictResolutionEnum
	): ng.IPromise<IDialogResult<IConflictResult>> {
		const options: IDialogOptions<IConflictObject> = {
			title: "Name Conflict (" + (index + 1) + "/" + conflictCount + ")",
			template: require("../dialogs/import-conflict/conflict-decision-dialog.html"),
			parameter: {
				objectName: this.objectName,
				conflictedString: conflictedString,
				occupiedStrings: this.occupiedStrings,
				lastAction: lastAction,
				remainingCount: conflictCount - (index + 1),
				maxItems: this.maxItems
			}
		};
		return this.dialogService.show(options);
	}

	private resolveNextConflict<T>(
		lastAction: ConflictResolutionEnum = ConflictResolutionEnum.REPLACE,
		index: number = 0): ng.IPromise<IConflictResolution<T>[]> {
		const deferredResolutionChoice = this.$q.defer<IConflictResolution<T>[]>();
		const currentObject = this.conflictingObjects[index];

		this.resolveConflictDialog(
			this.getConflictString(currentObject),
			index,
			this.conflictingObjects.length,
			lastAction
		).then((result) => {
			if (result && result.button === DialogButtonType.Ok) {
				const nextIndex = index + 1;
				const dialogValue = result.value;
				const resolutions: IConflictResolution<T>[] = [];
				resolutions.push({ object: currentObject, resolution: dialogValue.action, newName: dialogValue.newName });
				if (dialogValue.action === ConflictResolutionEnum.COPY) {
					this.occupiedStrings.push(dialogValue.newName);
				} else if (dialogValue.action !== ConflictResolutionEnum.SKIP) {
					this.occupiedStrings.push(this.getConflictString(currentObject));
				}
				if (dialogValue.sameForRemaining) {
					for (let i = nextIndex; i < this.conflictingObjects.length; i++) {
						if (dialogValue.action === ConflictResolutionEnum.COPY) {
							dialogValue.newName = Helper.generateNewDisplayName(
								this.getConflictString(this.conflictingObjects[i]),
								this.occupiedStrings
							);
							this.occupiedStrings.push(dialogValue.newName);
						}
						resolutions.push({
							object: this.conflictingObjects[i],
							resolution: dialogValue.action,
							newName: dialogValue.newName
						});
					}
					deferredResolutionChoice.resolve(resolutions);
				} else {
					if (nextIndex < this.conflictingObjects.length) {
						this.resolveNextConflict<T>(dialogValue.action, nextIndex).then((resolvedCompositeComponents) => {
							deferredResolutionChoice.resolve(resolutions.concat(resolvedCompositeComponents));
						}).catch((e: IErrorResponse) => {
							deferredResolutionChoice.reject(e);
						});
					} else {
						deferredResolutionChoice.resolve(resolutions);
					}
				}
			} else {
				deferredResolutionChoice.reject(new ErrorResponse("User cancel"));
			}
		});
		return deferredResolutionChoice.promise;
	}

	private applyResolutions<T>(resolutions: IConflictResolution<T>[], oldObjects: T[]): IResolutionResult<T> {
		const valuesToRemove: string[] = [];
		const resolutionResult: IResolutionResult<T> = {
			toRemove: [],
			toAdd: []
		};
		resolutions.forEach((resolution) => {
			const newItem = resolution.object;
			switch (resolution.resolution) {
				case ConflictResolutionEnum.REPLACE:
					valuesToRemove.push(this.getConflictString(newItem));
					resolutionResult.toAdd.push(newItem);
					break;
				case ConflictResolutionEnum.COPY:
					Helper.setDeepValue(newItem, this.conflictKey, resolution.newName);
					resolutionResult.toAdd.push(newItem);
					break;
				case ConflictResolutionEnum.SKIP:
					break;
				default:
					break;
			}
		});
		if (valuesToRemove.length > 0) {
			const objsToRemove = oldObjects.filter((obj) => {
				return valuesToRemove.some((valueToRemove) => {
					return this.getConflictString(obj) === valueToRemove;
				});
			});
			resolutionResult.toRemove = objsToRemove;
		}
		return resolutionResult;
	}

	static add(m: ng.IModule): void {
		m.service(InjectNames.CONFLICT_RESOLUTION_SERVICE, ConflictResolutonService);
	}
}
