import { ConditionType, InjectNames, Operators, SettingKeys } from "ng2/common/constants/common.constant";
import { IJsonExpression } from "ng2/common/model/databearer/json-expression.model";
import { Helper } from "../helper";
import { IComponent } from "../IComponent";
import { CommonService } from "./common-service";

interface IUsedExpression {
	componentId: string;
	conditionType: string;
}

export class ExpressionService {

	static $inject = [
		InjectNames.COMMON_SERVICE
	];

	private currentProjectComponents: IComponent[];
	private triggeredConditions: IUsedExpression[] = [];

	constructor(
		private commonService: CommonService
	) {}

	public trigger(components: IComponent[]): void {
		this.triggeredConditions = [];
		this.currentProjectComponents = components;
		const currentProject = this.commonService.getCurrentProject();
		const expressions = currentProject.getExpressions();
		expressions.forEach((expression) => {
			const alreadyTriggered = this.isUsed(expression.usedBy, expression.conditionType);

			if (!alreadyTriggered) {
				let sourceValue = expression.sourceProperty;
				if (expression.source !== "<static>") {
					sourceValue = this.getExpressionValue(expression.source, expression.sourceProperty);
				}
				let targetValue = expression.targetProperty;
				if (expression.target !== "<static>") {
					targetValue = this.getExpressionValue(expression.target, expression.targetProperty);
				}

				this.runExpression(sourceValue, targetValue, expression);
			}
		});
	}

	private isUsed(componentId: string, type: string): boolean {
		return this.triggeredConditions.some((triggeredCondition) => {
			return triggeredCondition.componentId === componentId &&
				triggeredCondition.conditionType === type;
		});
	}

	private runExpression(
		sourceValue: string,
		targetValue: string,
		expression: IJsonExpression
	): void {
		let result = false;
		//get the component that uses this expression
		const component = this.findComponent(expression.usedBy);

		//if source is from checkbox
		if (typeof sourceValue === "boolean") {
			sourceValue = sourceValue ? "1" : "0";
		}
		if (typeof targetValue === "boolean") {
			targetValue = targetValue ? "1" : "0";
		}

		//used in lower, greater etc
		let sourceNumber;
		let targetNumber;

		if (component) {
			switch (expression.clauseOperator) {
				case Operators.EQUALS:
					result = sourceValue === targetValue;
					this.triggerCondition(expression, component, result);
					break;
				case Operators.NOT_EQUALS:
					result = sourceValue !== targetValue;
					this.triggerCondition(expression, component, result);
					break;
				case Operators.GREATER:
					sourceNumber = parseInt(sourceValue);
					targetNumber = parseInt(targetValue);
					result = sourceNumber > targetNumber;
					this.triggerCondition(expression, component, result);
					break;
				case Operators.LOWER:
					sourceNumber = parseInt(sourceValue);
					targetNumber = parseInt(targetValue);
					result = sourceNumber < targetNumber;
					this.triggerCondition(expression, component, result);
					break;
				case Operators.SAME_GREATER:
					sourceNumber = parseInt(sourceValue);
					targetNumber = parseInt(targetValue);
					result = sourceNumber >= targetNumber;
					this.triggerCondition(expression, component, result);
					break;
				case Operators.SAME_LOWER:
					sourceNumber = parseInt(sourceValue);
					targetNumber = parseInt(targetValue);
					result = sourceNumber <= targetNumber;
					this.triggerCondition(expression, component, result);
					break;
				case Operators.INCLUDES:
					const targetString = targetValue.trim().toLowerCase();
					const sourceString = sourceValue.trim().toLowerCase();
					if (sourceString && targetString) {
						result = targetString.includes(sourceString);
						this.triggerCondition(expression, component, result);
					}
					break;
				default:
			}
		}
	}

	private triggerCondition(expression: IJsonExpression, component: IComponent, value: boolean): void {
		if (value) {
			this.triggeredConditions.push({
				componentId: component.getId(),
				conditionType: expression.conditionType
			});
		}

		switch (expression.conditionType) {
			case ConditionType.DISABLE:
				component.isReadonly = value;
				break;
			case ConditionType.HIDE:
				component.isVisible = !value;
				break;
			default:
				// type not exists
		}
	}

	private getExpressionComponentValue(component: IComponent, property: string): string {
		let value;
		const componentDataservice = component.getDatasource();
		const componentValue = component.getValue(property);
		const componentBindableValue = component.getBindableValue(property);

		if (componentDataservice && !Helper.isUndefinedOrNull(componentValue)) {
			value = componentValue;
		} else if (!Helper.isUndefinedOrNull(componentBindableValue)) {
			value = componentBindableValue;
		} else if (property.includes(SettingKeys.SELECTED_ITEM)) {
			value = "";
		}

		return value;
	}

	private getExpressionValue(componentName: string, property: string): string {
		const component = this.findComponent(componentName);
		if (component) {
			property = this.getExpressionComponentValue(component, property);
		}
		return property;
	}

	private findComponent(usedByComponent: string): IComponent {
		return this.currentProjectComponents.find((component) => {
			return component.getId() === usedByComponent;
		});
	}

	static add(ngModule: ng.IModule): void {
		ngModule.service(InjectNames.EXPRESSION_SERVICE, ExpressionService);
	}
}
