import isEmpty from "lodash-es/isEmpty";
import isUndefined from "lodash-es/isUndefined";

import { Config, ProjectTypeEnum, ThemeEnum } from "ng2/common/constants/common.constant";

import type { IJsonDesignerManifest, IJsonManifest } from "ng2/common/model/databearer/json-manifest.model";
import type { IJsonUrlVariable } from "ng2/common/model/databearer/json-url-variable.model";
import type { IMap } from "ng2/common/model/imap.model";
import type { IBoundary } from "ng2/common/model/project/boundary.model";
import type { IDesignerManifest } from "ng2/common/model/project/mashup/designer-manifest.model";

import { FileMetaData } from "ng2/common/project/manifest/file-metadata";

export class DesignerManifest implements IDesignerManifest {
	createdBy: string;
	createdByDisplayName: string;
	createdTime: number;
	description: string;
	displayName: string;
	fileMetaData: IMap<FileMetaData> = {};
	id: string;
	lastChangedBy: string;
	lastChangedByDisplayName: string;
	lastChangedTime: number;
	mashupBoundaries: IBoundary[];
	urlVariables: IJsonUrlVariable[];
	title: string;
	version: string;

	previewImg: string;
	showTitleBar: boolean;
	themeSwitchEnabled: boolean;
	defaultTheme: ThemeEnum;
	typeDef: ProjectTypeEnum;

	constructor(arg1?: string | IJsonDesignerManifest, arg2?: string) {
		if (!arg2 && arg1 && typeof arg1 === "object") {
			this.id = arg1.id;
			this.displayName = arg1.displayName;
			this.createdBy = arg1.createdBy;
			this.createdByDisplayName = arg1.createdByDisplayName;
			this.createdTime = arg1.createdTime;
			this.lastChangedBy = arg1.lastChangedBy;
			this.lastChangedByDisplayName = arg1.lastChangedByDisplayName;
			this.lastChangedTime = arg1.lastChangedTime;
			this.mashupBoundaries = arg1.mashupBoundaries;
			this.urlVariables = arg1.urlVariables;
			this.version = arg1.version;
			this.description = arg1.description;
			if (arg1.fileMetaData) {
				this.fileMetaData = arg1.fileMetaData;
			}
			this.previewImg = arg1.previewImg;
			this.showTitleBar = arg1.showTitleBar;
			this.title = arg1.title;
			this.themeSwitchEnabled = arg1.themeSwitchEnabled;
			this.defaultTheme = arg1.defaultTheme;
			this.typeDef = arg1.typeDef;
		} else if (arg1) {
			this.id = "";
			this.displayName = (arg1 as string);
			this.description = arg2;
			this.title = (arg1 as string);
			this.showTitleBar = true;
			this.themeSwitchEnabled = false;
			this.defaultTheme = ThemeEnum.LIGHT;
		}
	}

	setHasFile(fileKey: string, mimeType: string, visibleName: string): void {
		if (!this.fileMetaData[fileKey]) {
			this.fileMetaData[fileKey] = new FileMetaData(visibleName, mimeType);
		}
	}

	updateLastChangedInformation(newManifest: IJsonManifest): void {
		if (newManifest) {
			this.lastChangedBy = newManifest.lastChangedBy;
			this.lastChangedByDisplayName = newManifest.lastChangedByDisplayName;
			this.lastChangedTime = newManifest.lastChangedTime;
		} else {
			this.lastChangedBy = Config.USER.name;
			this.lastChangedByDisplayName =
				isUndefined(Config.USER.displayName) || isEmpty(Config.USER.displayName) ?
					Config.USER.name : Config.USER.displayName;
			this.lastChangedTime = new Date().getTime();
		}
	}
}
