import { BindingTypeEnum } from "ng2/common/enums/binding-type.enum";
import { OrientationEnum } from "ng2/common/enums/orientation.enum";

import type { IJsonComponent } from "ng2/common/model/databearer/components/json-component.model";
import type { IJsonLayoutComponent } from "ng2/common/model/databearer/json-layout-component.model";
import type { ILayoutComponent } from "common/interface/ilayout-component";
import type { IEditorComponent } from "common/interface/ieditor-component";
import type { IInsertPoints } from "editor/defines";

import { ApplicationParent } from "components/application-parent";
import { ChildrenDirective } from "components/children-directive";

import {
	EditorComponentCtrl,
	IEditorComponentScope
} from "editor/components/editor-component";

export interface IEditorLayoutComponent extends IEditorComponent, ILayoutComponent {
	parent: IEditorLayoutComponent;
	// Overrides to make dual extend work
	getScope(): IEditorComponentScope;
	getDatabearer(): IJsonLayoutComponent;
	calcNewSizes(orientation?: OrientationEnum): void;

	// Methods for layout components in Designer
	insert(component: IJsonComponent, index?: number, isFinalInsert?: boolean): Function;
	getInsertPoints(): IInsertPoints;
	removeChild(childId: string): void;
	detachChild(childId: string): IJsonComponent;
	getChildIndex(childId: string): number;
	onComponentInserted(childInstance: IEditorComponent, index: number): void;
	onComponentDeleted(componentInstance: IEditorComponent): void;
}

export class EditorLayoutComponentCtrl extends EditorComponentCtrl {

	protected component: IEditorLayoutComponent;
	protected isFinalInsert: boolean;

	protected init(component: IEditorLayoutComponent): void {
		super.init(component);

		component.onComponentInserted = this.onComponentInserted.bind(this);
		component.onComponentDeleted = this.onComponentDeleted.bind(this);

	}

	public onComponentInserted(childInstance: IEditorComponent, index: number): void {
		const allComponents = this.editorService.getAllBindableComponents();
		this.component.children.splice(index, 0, childInstance);
		childInstance.newBoundaryEntered(this.editorService.getCurrentBoundary().id);
		childInstance.forEachChild((subChild) => {
			if (this.isFinalInsert) {
				if (subChild.getDatasource()) {
					this.interactionManagementService.runStartupOnComponent(subChild.getDatabearer());
				}
				this.initializeSourceBindings(childInstance, allComponents);
			}
			subChild.initializeBindings();
		}, true);
	}

	public onComponentDeleted(componentInstance: IEditorComponent): void {
		// Component deleted
	}

	public static createTemplate(typedef: string): JQuery {
		const componentTemplate = ChildrenDirective.createTemplate(typedef);

		componentTemplate.attr("component-ready", "parent.onComponentInserted(componentInstance, index);");
		componentTemplate.attr("component-destroyed", "parent.onComponentDeleted(componentInstance)");

		return componentTemplate;
	}

	private initializeSourceBindings(childInstance: IEditorComponent, allComponents: IEditorComponent[]): void {
		allComponents.forEach(component => {
			if (component.getTypedef() !== ApplicationParent.name) {
				component.forEachBindable((bindable) => {
					const field = component.getBindableField(bindable.ref);
					if (field && field.sourceName === childInstance.getId() && field.Type !== BindingTypeEnum.STATIC_VALUE) {
						component.initializeBindings();
					}
				});
			}
		});
	}
}
