import { IDatasource } from "common/model/datasource/datasource.model";
import { InjectNames } from "ng2/common/constants/common.constant";
import { IJsonRuntimeMashup } from "ng2/common/model/databearer/json-mashup.model";
import { IBoundary } from "ng2/common/model/project/boundary.model";
import { RuntimeApp } from "ng2/common/project/runtime-app";
import { IAvailableSourceField } from "../common/defines";
import { ILayoutComponent } from "../common/interface/ilayout-component";
import { CommonService } from "../common/services/common-service";
import { FrameworkService } from "../components/services/framework-service";
import { LanguageService } from "common/services/language-service";

export class RuntimeService {
	static $inject = [
		InjectNames.COMMON_SERVICE,
		InjectNames.FRAMEWORK_SERVICE,
		InjectNames.LANGUAGE_SERVICE
	];

	constructor(
		private commonService: CommonService,
		private frameworkService: FrameworkService,
		private languageService: LanguageService
	) {
	}

	loadRuntimeProject(projectId: string, canvasWidth: number): ng.IPromise<RuntimeApp> {
		const promise = this.commonService.loadProject(projectId, canvasWidth) as ng.IPromise<IJsonRuntimeMashup>;
		return this.parseAndSetApp(promise, canvasWidth);
	}

	loadPublishedProject(projectId: string, canvasWidth: number): ng.IPromise<RuntimeApp> {
		const promise = this.commonService.loadPublishedProject(projectId, canvasWidth);
		return this.parseAndSetApp(promise, canvasWidth);
	}

	loadPublishedProjectNoConstraint(projectId: string, canvasWidth: number): ng.IPromise<RuntimeApp> {
		const promise = this.commonService.loadPublishedProjectNoConstraints(projectId, canvasWidth);
		return this.parseAndSetApp(promise, canvasWidth);
	}

	getCurrentProject(): RuntimeApp {
		return this.commonService.getCurrentProject() as RuntimeApp;
	}

	setCurrentRootComponentInstance(component: ILayoutComponent): void {
		this.commonService.setCurrentRootComponentInstance(component);
	}

	getCurrentBoundary(): IBoundary {
		return this.commonService.getCurrentBoundary();
	}

	runStartupInteractions(): void {
		this.frameworkService.runStartupInteractions();
	}

	updateCurrentBoundary(canvasWidth: number): void {
		this.commonService.updateCurrentBoundary(canvasWidth);
	}

	instantiateDataservice(id: string): IDatasource {
		return this.frameworkService.getDatasourceObject(id);
	}

	setCurrentUrlValues(urlDataset: string[]): void {
		const newUrlMetaData: IAvailableSourceField[] = [];
		urlDataset.forEach((urlData) => {
			const name = urlData.substring(0, urlData.indexOf("="));
			const value = urlData.substring(urlData.lastIndexOf("=") + 1);
			const sourceField: IAvailableSourceField = {
				visibleName: name,
				sourceKey: name,
				sourceKeyShort: name,
				currentValue: value,
			};
			newUrlMetaData.push(sourceField);
		});

		this.commonService.setUrlMetaData(newUrlMetaData);
	}

	private parseAndSetApp(
		promise: ng.IPromise<IJsonRuntimeMashup>,
		canvasWidth: number): ng.IPromise<RuntimeApp> {
		return promise.then((jsonMashup: IJsonRuntimeMashup) => {
			const mashup = RuntimeApp.fromObj(jsonMashup);
			//on runtime: language must load before project
			return this.languageService.initializeLanguageSettings().then(() => {
				return this.commonService.buildPrerequisites(mashup, canvasWidth) as ng.IPromise<RuntimeApp>;
			});
		});
	}

	static add(m: ng.IModule): void {
		m.service(InjectNames.RUNTIME_SERVICE, RuntimeService);
	}
}
