import cloneDeep from "lodash-es/cloneDeep";
import { AngularConstants } from "../../../common/angular-constants";

import { InjectNames, IdoDataType } from "ng2/common/constants/common.constant";
import { SelectableEnum } from "ng2/common/constants/sohoxi.constant";
import { IDataGridColumn, IDataGridOptions, IDataGridRow } from "ng2/common/model/sohoxi/sohoxi-datagrid.model";

import { IErrorResponse } from "../../../common/error-response";
import { Helper } from "../../../common/helper";
import { IToolbarOptions } from "../../../common/model/sohoxi/sohoxi-toolbar.model";
import {
	DialogButtonType, DialogService, IDialogOptions, IDialogResult
} from "../../../common/services/dialog-service";
import { CustomFormatters } from "../../../common/sohoxi/custom-formatters";
import { IdoCollection } from "../../../components/services/local-ido-service";
import { RepositoryService } from "../repository-service";
import { RepositoryTab } from "../repository-tab-ctrl";
import { CollectionData } from "./collection-data";
import { RepositoryHelper } from "../repository-helper";

export class IdoTabCtrl extends RepositoryTab {
	protected datagridId: string = "ido-grid";
	protected anySelectedDataItem: boolean = false;
	protected selectedDataItems: IdoCollection[] = [];
	protected datagridOptions: IDataGridOptions<IdoCollection>;

	public dataset: IdoCollection[];
	private readonly CACHE_KEY = "ido-collections";
	private set Dataset(value: IdoCollection[]) {
		this.dataset = value;
		this.saveToCache(this.CACHE_KEY, value);
	}

	public toolbarOptions: IToolbarOptions;

	public static $inject = [
		AngularConstants.SCOPE,
		AngularConstants.ELEMENT,
		AngularConstants.CACHE_FACTORY,
		InjectNames.REPOSITORY_SERVICE,
		InjectNames.DIALOG_SERVICE
	];

	constructor(
		$scope: ng.IScope,
		$element: ng.IAugmentedJQuery,
		$cacheFactory: ng.ICacheFactoryService,
		repositoryService: RepositoryService,
		private dialogService: DialogService
	) {
		super($scope, $element, $cacheFactory, repositoryService);
	}

	public $onInit(): void {
		super.$onInit();
		this.toolbarOptions = {
			rightAligned: true,
			maxVisibleButtons: 5
		};
	}

	protected initDatagridOptions(): void {
		const locale = this.repositoryService.getLanguage();
		this.dataset = [];
		const columns: IDataGridColumn[] = [
			{
				id: "selectionCheckbox",
				sortable: false,
				resizable: false,
				width: "50px",
				formatter: Soho.Formatters.SelectionCheckbox,
				align: "center"
			},
			{
				id: "collectionName",
				name: "Name",
				field: "collectionName",
				width: "450px",
				sortable: true,
				formatter: Soho.Formatters.Ellipsis
			},
			// Uncomment when the CreateBy property in Mongoose does not say 'sa'
			// {
			// 	id: "createdBy",
			// 	name: "Author",
			// 	field: "createdBy",
			// 	width: "150px",
			// 	sortable: true,
			// 	formatter: Soho.Formatters.Ellipsis,
			// },
			{
				id: "createDate",
				name: "Created",
				field: "createDate",
				width: "50px",
				sortable: true,
				formatter: CustomFormatters.Date(locale),
			},
			{
				id: "collectionDesc",
				name: "Description",
				field: "collectionDesc",
				width: "450px",
				sortable: true,
				formatter: Soho.Formatters.Ellipsis
			},
			{
				id: "serverName",
				name: "Project",
				field: "serverName",
				width: "150px",
				sortable: true,
				formatter: Soho.Formatters.Ellipsis
			},
			{
				id: "action",
				name: "Action",
				field: "",
				width: "50px",
				align: "center",
				formatter: Soho.Formatters.Button,
				click: (_, rowsSelected: IDataGridRow<IdoCollection>[]) => {
					const item = rowsSelected[0].item;
					this.repositoryService.getIdoProperties(item.collectionName)
						.then(properties => {
							const collectionProperties = properties.map(item => {
								const name = Helper.isUndefinedEmpty(item.columnName) ?
									item.propertyName :
									item.columnName;

								return {
									name,
									dataType: RepositoryHelper.getAppbuilderDataType(item.dataType),
									propertyLength: item.dataLength,
									primaryKey: item.isRequired,
									required: item.isRequired,
									readonly: item.isReadOnly,
								};
							});
							this.dialogService.show({
								title: "Preview IDO Properties",
								template: require("./create/preview-dialog/table-template.html"),
								parameter: collectionProperties,
								cssClass: "wizard-dialog large"
							});
						});
				},
				text: "Show Table",
			}
		];
		this.datagridOptions = {
			columns,
			dataset: [],
			selectable: SelectableEnum.Multiple,
			paging: true,
			pagesize: 25,
			toolbar: {
				title: "IDO Collections",
				results: true,
				dateFilter: false,
				keywordFilter: true,
				advancedFilter: true,
				actions: true,
				views: true,
				rowHeight: true
			},
			enableTooltips: true
		};
		this.loadIDOs();
	}

	private loadIDOs(): void {
		const cachedDataset = this.getFromCache<IdoCollection[]>(
			this.CACHE_KEY
		);

		if (Helper.isUndefined(cachedDataset)) {
			this.loading = true;
			this.repositoryService.getIDOs().then(idoCollections => {
				this.Dataset = this.datagridOptions.dataset = idoCollections;
			}).catch((errorResponse) => {
				this.datagridOptions.dataset = [];
				this.dialogService.showError("Load IDOs", errorResponse.message);
			}).finally(() => {
				this.loading = false;
			});
		} else {
			this.datagridOptions.dataset = cachedDataset;
			this.clearSelected();
		}
	}

	public create(): void {
		const template =
			`<eir-wizard ng-controller="eirIdoWizardCtrl as wizardCtrl"></eir-wizard>`;

		const options: IDialogOptions<CollectionData> = {
			title: "Table & IDO",
			template,
			cssClass: "wizard-dialog large",
			id: "ido-wizard"
		};

		this.dialogService.show(options)
			.then((result: IDialogResult<CollectionData>) => {
				if (result.button === DialogButtonType.Ok) {
					const collectionData = result.value;
					const today = Helper.getFormattedDate("yyyymmdd HH:mm:ss.fff");

					this.Dataset = this.datagridOptions.dataset = [
						{
							collectionName: collectionData.name,
							collectionDesc: collectionData.idoDescription,
							serverName: collectionData.idoProject,
							createdBy: "sa", // for some reason regardless who created it Author will always be 'sa'
							createDate: today
						},
						...this.datagridOptions.dataset];

					this.$scope.$applyAsync(() => {
						this.datagridApi.updated();
					});
				}
			});
	}

	private removeDeleted(idoNames: string[]): void {
		let newDataset = cloneDeep(this.dataset);
		idoNames.forEach(name => {
			newDataset = newDataset.filter(ido => {
				return ido.collectionName !== name;
			});
		});
		this.Dataset = newDataset;
		this.datagridOptions.dataset = newDataset;
		this.clearSelected();
	}

	public delete(): void {
		const idoNames = this.selectedDataItems.map(ido => ido.collectionName);
		const namesString = idoNames.join(", \n");
		this.dialogService.showYesNo(
			"Warning",
			`Are you sure that you want to delete the following IDOs with its associated SQL Tables? \n ${namesString}`,
			"warning-text")
			.then(result => {
				if (result.button === DialogButtonType.Yes) {
					this.repositoryService.deleteIDOs(idoNames).then((success: boolean) => {
						if (success) {
							this.removeDeleted(idoNames);
						}
					}).catch((response: IErrorResponse) => {
						this.dialogService.showError(
							"Error",
							`Failed to delete ${response.failedItems[0]} \n` +
							response.message
						);

						if (response.successfulItems.length > 0) {
							this.removeDeleted(response.successfulItems);
						}
					});
				}
			});
	}

	public import(): void {
		throw new Error("Method not implemented.");
	}
}

export class IdoTabComponent implements ng.IComponentOptions {
	public controller = IdoTabCtrl;
	public controllerAs = "ctrl";
	public template = require("./ido-tab.html");

	public static add(m: ng.IModule): void {
		m.component("eirIdoTab", new IdoTabComponent());
	}
}
