import { AngularConstants } from "../angular-constants";
import { IJQueryScrollEventObject } from "../defines";

export interface IInfScrollScope extends ng.IScope {
	eirInfiniteScroll: Function;
}

class InfiniteScrollCtrl {

	public static $inject = [
		AngularConstants.SCOPE,
		AngularConstants.ELEMENT,
		AngularConstants.TIMEOUT,
		AngularConstants.INTERVAL
	];

	public constructor(
		private $scope: IInfScrollScope,
		private $element: ng.IAugmentedJQuery,
		private $timeout: ng.ITimeoutService,
		private $interval: ng.IIntervalService
	) {
		this.initialize();
	}

	// Varibles that potentially will be input variables
	private THROTTLE_MILLISECONDS = 50;
	private loadMore: Function;
	private scrollDistanceThreshold: number = 0.25;
	private bounceDistance: number = 10;
	private EDGE_BOUNCE_INTERVAL_MILLIESECONDS = 50;

	// State variables
	private lastScrollTop: number = 0;
	private edgeBounceInterval: {
		up: ng.IPromise<void> | null;
		down: ng.IPromise<void> | null;
	} = { up: null, down: null };

	private initialize(): void {
		const scope = this.$scope;
		const element = this.$element;
		this.loadMore = scope.eirInfiniteScroll;
		scope.$watch("eirInfiniteScrollEnabled", (scrollEnabled) => {
			if (scrollEnabled) {
				element.on("scroll", this.throttle(this.handler, this.THROTTLE_MILLISECONDS));
			} else {
				element.off("scroll");
			}
		});

		scope.$on(AngularConstants.DESTROY, () => {
			element.off("scroll");
		});
	}

	private handler(evt: IJQueryScrollEventObject): void {
		const target = evt.currentTarget;
		const scrollingDown: boolean = (this.lastScrollTop < target.scrollTop);
		const scrollingUp: boolean = (this.lastScrollTop > target.scrollTop);
		this.lastScrollTop = target.scrollTop;

		if (scrollingDown) {
			const distanceFromBottom = target.scrollHeight - (target.offsetHeight + target.scrollTop);
			const closeEnoughToBottom = distanceFromBottom < (target.offsetHeight * this.scrollDistanceThreshold);
			if (closeEnoughToBottom) {
				this.loadMore({ scrollingDown });
				this.$scope.$apply();
			}
			if (distanceFromBottom <= 0) {
				target.scrollTop -= this.bounceDistance;
			}
		} else if (scrollingUp) {
			const distanceFromTop = target.scrollTop;
			const closeEnoughToTop = distanceFromTop < (target.offsetHeight * this.scrollDistanceThreshold);
			if (closeEnoughToTop) {
				this.loadMore({ scrollingDown });
				this.$scope.$apply();
			}
			if (distanceFromTop <= 0) {
				target.scrollTop += this.bounceDistance;
			}
		}
		this.bounceEdges(evt);
	}

	// Make sure that it always bounds when reaching edges.
	// This can happen in the rare case of user manually scrolling in scrollbar
	private bounceEdges(evt: IJQueryScrollEventObject): void {
		const target = evt.currentTarget;
		const distanceFromBottom = target.scrollHeight - (target.offsetHeight + target.scrollTop);
		const distanceFromTop = target.scrollTop;

		if (this.edgeBounceInterval.up) {
			this.$interval.cancel(this.edgeBounceInterval.up);
			this.edgeBounceInterval.up = null;
		}
		if (this.edgeBounceInterval.down) {
			this.$interval.cancel(this.edgeBounceInterval.down);
			this.edgeBounceInterval.down = null;
		}

		if (distanceFromBottom <= 0) {
			this.edgeBounceInterval.up = this.$interval(() => {
				if (distanceFromBottom <= 0) {
					target.scrollTop -= this.bounceDistance;
				}
			}, this.EDGE_BOUNCE_INTERVAL_MILLIESECONDS);
		}
		if (distanceFromTop <= 0) {
			this.edgeBounceInterval.down = this.$interval(() => {
				if (distanceFromTop <= 0) {
					target.scrollTop += this.bounceDistance;
				}
			}, this.EDGE_BOUNCE_INTERVAL_MILLIESECONDS);
		}
	}

	private throttle(
		func: (e: IJQueryScrollEventObject) => void,
		wait: number
	): (e: IJQueryScrollEventObject) => void {
		let timeout: ng.IPromise<void> = null;
		let previous = 0;

		const later = (event: IJQueryScrollEventObject) => {
			previous = new Date().getTime();
			this.$timeout.cancel(timeout);
			timeout = null;
			return func.call(this, event);
		};

		const throttled = (event: IJQueryScrollEventObject) => {
			const now = new Date().getTime();
			const remaining = wait - (now - previous);
			if (remaining <= 0) {
				this.$timeout.cancel(timeout);
				timeout = null;
				previous = now;
				func.call(this, event);
			} else {
				this.$timeout.cancel(timeout);
				timeout = this.$timeout(later, remaining, false, event);
			}
		};
		return throttled;
	}
}

export class InfiniteScrollDirective implements ng.IDirective {
	public restrict = "A";
	public scope = {
		eirInfiniteScroll: "&",
		eirInfiniteScrollEnabled: "="
	};

	public controller = InfiniteScrollCtrl;

	public static add(m: ng.IModule): void {
		const directive: ng.IDirectiveFactory = () => {
			return new InfiniteScrollDirective();
		};
		m.directive("eirInfiniteScroll", directive);
	}
}
