import angular from "angular";

import { Helper } from "../../common/helper";
import type { ILayoutComponent } from "../../common/interface/ilayout-component";
import type { ILookupApi, ILookupOptions } from "../../common/model/sohoxi/sohoxi-lookup.model";
import { Component } from "../component";
import { FrameworkService } from "../services/framework-service";

import {
	InteractionActionEnum, InteractionPairOperation, InteractionTriggerEnum, SettingInputKeys,
	SettingKeys, SidebarTabTypes
} from "ng2/common/constants/common.constant";
import { SelectableEnum } from "ng2/common/constants/sohoxi.constant";

import { BindingTypeEnum } from "ng2/common/enums/binding-type.enum";
import { SizeTypeEnum } from "ng2/common/enums/size-type.enum";
import { ValueTypeEnum } from "ng2/common/enums/value-type.enum";

import type { IComponentSettingCategory } from "ng2/common/model/components/settings/component-setting-category.model";
import type { IDataPair } from "ng2/common/model/data-pair.model";
import type { IJsonSize } from "ng2/common/model/databearer/components/json-component.model";
import type { IJsonLookup } from "ng2/common/model/databearer/components/json-lookup.model";
import type { IJsonBindableField } from "ng2/common/model/databearer/json-binding.model";
import type { IMap } from "ng2/common/model/imap.model";
import type { IInteraction } from "ng2/common/model/interaction/interaction.model";

import { BackwardCompatible } from "ng2/common/project/backward-compatible";

export class Lookup extends Component {
	static AVAILABLE_WIDTHS: IDataPair[] = [
		{ name: "Small", value: "input-sm" },
		{ name: "Medium", value: "input-mm" },
		{ name: "Large", value: "input-default" }
	];
	static OUTPUT_SETTING_TITLE = "Dialog Output Value";
	static DEFAULT_WIDTH: IJsonSize = { sizeType: SizeTypeEnum.AUTO };
	static DEFAULT_HEIGHT: IJsonSize = { sizeType: SizeTypeEnum.AUTO };
	static SOURCE_TRIGGERS: IDataPair[] = [
		InteractionPairOperation.ON_CLICK
	];
	static TARGET_ACTIONS: IDataPair[] = [
		InteractionPairOperation.GET,
		InteractionPairOperation.CLEAR
	];
	static DEFAULT_ACTION: IDataPair = InteractionPairOperation.LIST;
	static DEFAULT_TRIGGER: IDataPair = InteractionPairOperation.ON_CLICK;

	static BINDABLE_FIELDS_2: IJsonBindableField[] =
	[{
		name: "Label",
		ref: SettingKeys.LABEL,
		valueType: ValueTypeEnum.LOCALIZED_STRING,
		write: true,
		read: false,
		defaultValue: { Type: BindingTypeEnum.STATIC_VALUE, value: "" }
	}, {
		name: "Text",
		ref: SettingKeys.TEXT,
		valueType: ValueTypeEnum.STRING,
		write: true,
		read: true,
		defaultValue: { Type: BindingTypeEnum.STATIC_VALUE, value: "" }
	}, {
		name: "Selected Item",
		ref: SettingKeys.SELECTED_ITEM,
		valueType: ValueTypeEnum.OBJECT,
		write: false,
		read: true,
	}];

	static SETTING_FIELDS =
		([
			{
				title: Lookup.OUTPUT_SETTING_TITLE,
				tab: SidebarTabTypes.CONTENT,
				settings: [{
					dataset: [],
					title: "",
					ref: SettingKeys.LOOKUP_OUTPUT_ID,
					inputType: SettingInputKeys.DROPDOWN
				}]
			},
			{
				title: "Style",
				tab: SidebarTabTypes.STYLE,
				settings: [{
					title: "Show Label",
					boundarySetting: true,
					ref: SettingKeys.SHOW_LABEL,
					inputType: SettingInputKeys.CHECKBOX,
					defaultValue: true,
				}, {
					title: "Read Only",
					ref: SettingKeys.IS_READONLY,
					inputType: SettingInputKeys.CHECKBOX,
					defaultValue: false,
					button: {
						icon: "#icon-unlocked",
						tooltip: "Disable Component When...",
						dialog: true
					}
				}]
			},
			{
				title: "Width",
				tab: SidebarTabTypes.STYLE,
				settings: [{
					boundarySetting: true,
					ref: SettingKeys.BOX_WIDTH,
					inputType: SettingInputKeys.RADIO,
					dataset: Lookup.AVAILABLE_WIDTHS,
					defaultValue: "input-default",
				}]
			}
		] as IComponentSettingCategory[])
			.concat(Component.SETTING_FIELDS);

	label: string;
	showLabel: boolean;
	text: string;
	lookupOptions: ILookupOptions;
	boxWidth: string;
	protected databearer: IJsonLookup;
	private lookupCallback: Function;
	private selectedItem: IMap<any>;

	constructor(
		obj: IJsonLookup,
		$element: ng.IAugmentedJQuery,
		parent: ILayoutComponent,
		frameworkService: FrameworkService
	) {
		super(obj, $element, parent, frameworkService);
		this.showLabel = BackwardCompatible.getShowLabel(obj);
		this.initLookupOptions();
	}

	newBoundaryEntered(boundaryId: string): void {
		super.newBoundaryEntered(boundaryId);
		this.setBoundarySettingIfChanged<boolean>(SettingKeys.SHOW_LABEL);
		this.setBoundarySettingIfChanged<string>(SettingKeys.BOX_WIDTH);
	}

	clear(): void {
		this.text = "";
		this.selectedItem = null;
	}

	initializeBindings(): void {
		super.initializeBindings();
		this.updateValuesWithTranslations();
	}

	onChildrenReady(): void {
		this.updateValuesWithTranslations();
	}

	private initLookupOptions(): void {
		this.lookupOptions = {
			title: "Select Item",
			field: (selectedRow: IMap<string>) => {
				this.selectedItem = selectedRow;
				const selectedOutput = selectedRow[this.databearer.lookupOutputId];
				return selectedOutput;
			},
			beforeShow: (lookup: any, callback: Function) => {
				if (Helper.isUndefinedOrNull(this.datasource)) {
					this.setErrorTooltip(`No data service available on component: ${this.getDisplayName()}`);
				} else {
					this.lookupCallback = callback;
					this.trigger(InteractionTriggerEnum.CLICK);
				}
			},
			match: () => {
				// fake function to bypass an issue on soho-code.
			},
			options: {
				paging: true,
				pagesize: 10,
				columns: this.databearer.columns,
				dataset: [],
				selectable: SelectableEnum.Single,
				toolbar: {
					keywordFilter: true,
				}
			},
		};
	}

	updateValuesWithTranslations(): void {
		this.databearer.columns.forEach((column) => {
			const translatedTitle = this.frameworkService.getTranslatableStringByProperty(
				this.getId(),
				column.id
			);

			if (translatedTitle) {
				const globalString = this.frameworkService.getGlobalValue(translatedTitle, this);

				if (globalString) {
					column.value = translatedTitle;
					column.name = Helper.htmlEncode(globalString);
				} else {
					column.name = Helper.htmlEncode(translatedTitle);
				}
			}
		});
	}

	loadInteraction(interaction: IInteraction): ng.IPromise<any | any[]> {
		const interactionPromise = super.loadInteraction(interaction);
		const listData = this.getContext().ListResult;

		if (interaction.targetActionName === InteractionActionEnum.CLEAR) {
			return interactionPromise;
		}

		if (!listData) {
			interactionPromise.then(dataset => this.openLookupDialog(dataset, interaction));
		} else {
			this.openLookupDialog(listData, interaction);
		}

		return interactionPromise;
	}

	getSohoXiApi(): ILookupApi {
		return this.element.find(`#${this.getDatabearer().id}`).data("lookup");
	}

	private openLookupDialog(dataset: any, interaction: IInteraction): void {
		if (interaction.sourceTriggerName === InteractionTriggerEnum.CLICK) {
			this.lookupOptions.options.dataset = angular.copy(dataset);
			this.getSohoXiApi().updated(this.lookupOptions);
			this.getScope().$applyAsync(() => {
				this.lookupCallback();
			});
		}
	}

	static directive = [() => {
		return {
			template: require("./lookup.html"),
			replace: true
		};
	}];
}
