import { Config } from "ng2/common/constants/common.constant";

import { SizeTypeEnum } from "ng2/common/enums/size-type.enum";

import type { IJsonValueSource } from "ng2/common/model/databearer/json-binding.model";

import { Debugger } from "common/debugger";
import { IAvailableSourceField } from "common/defines";
import { WSForm } from "etc-assets/user-control-es";

import { Helper } from "common/helper";

export class DesignerHelper extends Helper {

	static getSizeSuffix(instance: SizeTypeEnum): string {
		return (instance === SizeTypeEnum.PIXELS ? "px" :
			(instance === SizeTypeEnum.PERCENT ? "%" : "auto"));
	}

	static openRuntime(
		projectId: string,
		$window: ng.IWindowService,
		urlBindings?: IJsonValueSource[],
		urlVariables?: IAvailableSourceField[]
	): void {

		if (DEV_SERVER) {
			this.launchAsIntended(
				projectId, $window, urlBindings, urlVariables);
		} else {
			this.launchInMongooseIframes(
				projectId, $window, urlBindings, urlVariables);
		}
	}

	private static launchAsIntended(
		projectId: string,
		$window: ng.IWindowService,
		urlBindings?: IJsonValueSource[],
		urlVariables?: IAvailableSourceField[]
	): void {

		const url = `#!/run/${projectId}`;

		$window.open(url, "_blank");

	}

	private static launchInMongooseIframes(
		projectId: string,
		$window: ng.IWindowService,
		urlBindings?: IJsonValueSource[],
		urlVariables?: IAvailableSourceField[]
	): void {

		const varValue = `run/${projectId}`;

		if (Config.MG_FORM_ONLY) {
			let setVarValues = `SETVARVALUES(InitURL=${varValue}`;
			const mgBaseUrl = "/WSWebClient/PlainForm.aspx?page=FormOnly&notitle=1&";

			if (urlVariables && urlVariables.length > 0) {
				urlVariables.forEach(variable => {
					const binding = urlBindings.find(bind => bind.sourceKey === variable.sourceKey);
					const value = binding ? binding.value : "";
					setVarValues += `,${variable.visibleName}=${value}`;
				});
			}
			setVarValues += ")";

			const app = `Form=${Config.MG_FORM_NAME}(${setVarValues})`;
			const url = `${mgBaseUrl}${app}${ Debugger.isEnabled() ? "&debug=true" : "" }`;

			$window.open(url, "_blank");
		} else {
			if (urlVariables && urlVariables.length > 0) {
				let parameters = `SETVARVALUES(InitURL=${varValue},`;
				urlVariables.forEach(variable => {
					const binding = urlBindings.find(bind => bind.sourceKey === variable.sourceKey);
					const value = binding ? binding.value : "";
					parameters += `${variable.visibleName}=${value},`;
				});
				parameters = parameters.substring(0, parameters.length - 1);
				parameters += ")";
				WSForm.setVarValue("FormRunParameters", parameters, null);
				WSForm.generate("LaunchAppBuilderWithParameters", null, false);
			} else {
				WSForm.setVarValue("InitURL", varValue, null);
				WSForm.generate("LaunchAppBuilder", null, false);
			}

		}
	}
}
