import {
	ComponentClassNames,
	ComponentNames,
	FlexSizeTypeEnum,
	InteractionActionEnum,
	SettingKeys,
} from "ng2/common/constants/common.constant";
import { OrientationEnum } from "ng2/common/enums/orientation.enum";
import { IComponentFieldSetting } from "ng2/common/model/components/settings/component-field-setting.model";
import { IJsonComponent } from "ng2/common/model/databearer/components/json-component.model";
import { IJsonFlexbox } from "ng2/common/model/databearer/components/json-flexbox.model";

import type { IInsertPoints } from "editor/defines";
import type { IEditorComponentScope } from "editor/components/editor-component";
import type { IDatasource } from "common/model/datasource/datasource.model";
import type { IChildScope } from "common/interface/ichild-scope";
import type { IEditorComponent } from "common/interface/ieditor-component";
import type { IFlexboxComponent } from "common/interface/iflexbox-component";
import type { ILayoutComponent } from "common/interface/ilayout-component";

import { EditorLayoutComponentCtrl, IEditorLayoutComponent } from "editor/components/editor-layout-component";

export interface IEditorFlexbox extends IEditorLayoutComponent, IFlexboxComponent {
	children: IEditorComponent[];
	parent: IEditorLayoutComponent;
	// Overrides to make dual extend work
	getScope(): IEditorComponentScope;
	getDatabearer(): IJsonFlexbox;
}

export class EditorFlexboxCtrl extends EditorLayoutComponentCtrl {
	public static add(m: ng.IModule): void {
		m.controller(
			ComponentClassNames.EDITOR_COMPONENT_PREFIX + ComponentNames.Flexbox,
			EditorFlexboxCtrl
		);
	}

	protected component: IEditorFlexbox;

	protected init(component: IEditorFlexbox): void {
		super.init(component);

		component.insert = this.insert.bind(this);
		component.getInsertPoints = this.getInsertPoints.bind(this);
		component.removeChild = this.removeChild.bind(this);
		component.getChildIndex = this.getChildIndex.bind(this);
		component.detachChild = this.detachChild.bind(this);
	}

	public setDatasource(newDatasource: IDatasource | null): ng.IPromise<void> {
		const promise = super.setDatasource(newDatasource);
		if (newDatasource) {
			this.component.loadInteraction(this.interactionManagementService.createNewOnTheFly(InteractionActionEnum.GET));
		}
		return promise;
	}

	public getInsertPoints(): IInsertPoints {
		const flexbox = this.component;
		const thisElement = flexbox.element;
		const defaultPadding = 10;
		const length = thisElement.width() - defaultPadding;
		const margin = 9;
		const isVertical = flexbox.orientation === OrientationEnum.VERTICAL;
		let position = 0;
		let offset = 0;
		let ghostOffset = 0;

		const getPosition = (element: ng.IAugmentedJQuery) => {
			const rootElement = "#insideRuler";
			return isVertical
				? element.offset().left - $(rootElement).offset().left + margin
				: element.offset().top - $(rootElement).offset().top + margin;
		};
		const getOffset = (element: ng.IAugmentedJQuery) => {
			return isVertical
				? element.offset().top - thisElement.offset().top
				: element.offset().left - thisElement.offset().left;
		};

		// Rm any ghostComponents that should not be included in the calculation
		const nonGhostChildren = flexbox.children.filter((childInstance: IEditorComponent, index: number) => {
			const childElement = childInstance.element;
			const isGhost = childElement.hasClass("isInsertChild");

			if (isGhost) {
				ghostOffset = isVertical
					? childElement.outerHeight(true)
					: childElement.outerWidth(true);
			}

			return isGhost === false;
		});

		// When we have no children we do not need to calculate with Alignments
		if (nonGhostChildren.length) {
			// We recalculate and assume the 0-xy point based on where the first child offset are placed
			// Assuming first child are always at top left
			const firstChildElement = nonGhostChildren[0].element;
			position = getPosition(firstChildElement);
			offset = getOffset(firstChildElement);

			// Sometimes, the firstchild is not the actual first child. need to recalc
			// the offset and position then.This happens for example when we move the
			// first component instead of inserting a new component
			nonGhostChildren.forEach((childInstance) => {
				const childElement = childInstance.element;
				const childPosition = getPosition(childElement);
				const childOffset = getOffset(childElement);

				if (Math.abs(position) > Math.abs(childPosition)) {
					position = childPosition;
				}
				if (Math.abs(offset) > Math.abs(childOffset)) {
					offset = childOffset;
				}
			});

			// the ghostoffset is contained inside the current offset. Remove it
			if (offset > ghostOffset) {
				offset -= ghostOffset;
			}
		}

		let leftOffsetToAdd = 0;
		const points = flexbox.children.map((childInstance: IEditorComponent) => {
			// extraOffset here take in to calculation the 1px border on both sides.
			const extraOffset = 2;
			const childElement = childInstance.element;

			// Check if we need to take the width or take the height
			// Depends on orientation
			// only take half the width to start the insertion closer to the true insertpoint
			const half = 0.5;
			const offsetToAdd = isVertical
				? childElement.outerHeight(true) * half + extraOffset
				: childElement.outerWidth(true) * half + extraOffset;

			offset += leftOffsetToAdd + offsetToAdd;
			// give the rest of the half to the next component to add
			leftOffsetToAdd = offsetToAdd;
			return offset;
		});

		return {
			// fallback orientation to vertical
			orientation: flexbox.orientation || OrientationEnum.VERTICAL,
			length: length,
			position: position,
			points: points
		} as IInsertPoints;
	}

	public insert(
		component: IJsonComponent,
		index: number = this.component.getDatabearer().children.length,
		isFinalInsert: boolean
	): Function {
		const flexboxDatabearer = this.component.getDatabearer();

		const eirChildrenNode = this.$element.find("> .childCage > eir-children");
		const template = EditorLayoutComponentCtrl.createTemplate(component.typedef);

		const childScope = this.$scope.$new() as IChildScope;
		childScope.component = component;
		childScope.parent = this.component as any as ILayoutComponent;
		childScope.context = this.$scope.context;
		childScope.index = index;
		let insertedElement: ng.IAugmentedJQuery;
		this.isFinalInsert = isFinalInsert;
		this.$compile(template)(childScope, (clone) => {
			const childAtGivenIndex = eirChildrenNode.children(":nth(" + index + ")");
			if (childAtGivenIndex.length > 0) {
				childAtGivenIndex.before(clone);
			} else { // Insert last
				eirChildrenNode.append(clone);
			}
			insertedElement = clone;
		});

		flexboxDatabearer.children.splice(index, 0, component);
		return () => {
			flexboxDatabearer.children.splice(index, 1);
			insertedElement.remove();
			this.removePossibleChildInstance(index, component.id);
		};
	}

	public removeChild(childId: string): void {
		this.detachChild(childId);
	}

	public detachChild(childId: string): IJsonComponent {
		const indexToDetach = this.getChildIndex(childId);
		if (indexToDetach !== -1) {
			const childDatabearer = this.component.getDatabearer().children.splice(indexToDetach, 1).pop();
			this.removePossibleChildInstance(indexToDetach, childDatabearer.id);

			return childDatabearer;
		} else {
			throw new Error("[EditorFlexbox] Trying to detach nonexistent component");
		}
	}

	public setSetting(settingMeta: IComponentFieldSetting, newSettingValue: any): void {
		const component = this.component;
		const settingReference = settingMeta.ref;
		let previousValue: any;
		if (settingMeta.boundarySetting) {
			previousValue = component.getBoundarySetting(settingReference);
		} else {
			previousValue = (component as any)[settingReference];
		}
		super.setSetting(settingMeta, newSettingValue);
		switch (settingReference) {
			case SettingKeys.ORIENTATION:
				//For orientation menu in canvas
				this.editorService.currentFlexOrientationChangedEvent.raise(newSettingValue);
				break;
			case SettingKeys.SIZE_TYPE:
				let sizeSetting: number;
				if (newSettingValue === FlexSizeTypeEnum.PIXELS) {
					const defaultPixelSize = 100;
					sizeSetting = defaultPixelSize;
				} else {
					sizeSetting = 1;
				}
				//Size needs to update when size type is changed
				this.setBoundarySetting<number>(SettingKeys.SIZE, sizeSetting);
				this.editorService.refreshSettingEvent.raise(SettingKeys.SIZE);
				break;
			case SettingKeys.SIZE:
				const radixBase = 10;
				const parsed = parseInt(newSettingValue, radixBase);
				if (isNaN(parsed)) {
					this.setBoundarySetting<any>(settingReference, previousValue);
				} else {
					this.setBoundarySetting<any>(settingReference, parsed);
				}
				break;
			default:
				// Other specific settings
		}
	}

	public getChildIndex(childId: string): number {
		return this.component.getDatabearer().children.findIndex(
			(component: IJsonComponent) => {
				return component.id === childId;
			}
		);
	}

	private removePossibleChildInstance(index: number, childId: string): void {
		if (this.component.children[index] && this.component.children[index].getId() === childId) {
			const childInstance = this.component.children.splice(index, 1).pop();
			childInstance.getScope().$destroy(); // Will remove DOM element
		} else {
			// Happens sometimes when component is moved within same parent,
			// Keeping it until we have other solution to make sure we're not deleting wrong instance
			console.info("[EditorFlexbox] Did not remove instance");
		}
	}

}
