import { InjectNames } from "ng2/common/constants/common.constant";
import { IJsonManifest } from "ng2/common/model/databearer/json-manifest.model";
import { IJsonTag } from "ng2/common/model/databearer/json-tag.model";
import { KeyCodeEnum } from "../../../../common/defines";
import { IErrorResponse } from "../../../../common/error-response";
import {
	DialogButtonType, DialogService, IDialogResult,
	IMessageDialogOptions, StandardDialogButtons
} from "../../../../common/services/dialog-service";
import { ProjectService } from "../../../services/project-service";

export class ProjectCardComponent {
	public manifest: IJsonManifest;
	public tags: IJsonTag[];
	public isProjectChanged: boolean;
	public onLoadProjects: Function;

	public editingManifest: IJsonManifest;
	public editingManifestOldDisplayName: string;

	public static $inject = [
		InjectNames.PROJECT_SERVICE,
		InjectNames.DIALOG_SERVICE,
	];

	public constructor(
		private projectService: ProjectService,
		private dialogService: DialogService,
	) { }

	public $onInit(): void {
		this.isProjectChanged = false;
	}

	public openRuntime(): void {
		this.projectService.launchProject(this.manifest.id);
	}

	public publishProject(): void {
		this.projectService.publish(this.manifest.id, this.manifest.displayName);
	}

	public exportProject(): void {
		this.projectService.export(this.manifest.id, this.manifest.displayName);
	}

	public duplicateProject(): void {
		this.projectService.duplicate(this.manifest);
	}

	public deleteProject(): void {
		const options: IMessageDialogOptions = {
			title: "Delete Project",
			message: "Please confirm that you want to delete this project.",
			standardButtons: StandardDialogButtons.YesNo,
		};
		this.dialogService.showMessage(options).then((result: IDialogResult<string>) => {
			if (result && result.button === DialogButtonType.Yes) {
				this.projectService.deleteById(this.manifest.id).then(() => {
					this.onLoadProjects();
				}).catch((errorResponse: IErrorResponse) => {
					const options: IMessageDialogOptions = {
						title: "Delete Project",
						message: errorResponse.message,
						standardButtons: StandardDialogButtons.Ok,
						isError: true,
					};

					this.dialogService.showMessage(options);
				});
			}
		});
	}

	public onStartEditProjectName(): void {
		this.editingManifest = this.manifest;
		this.editingManifestOldDisplayName = this.editingManifest.displayName;
	}

	public onDoneEditProjectName(): void {
		const oldDisplayName = this.editingManifestOldDisplayName;
		const newDisplayName = this.editingManifest.displayName;

		// Save only when name changed value
		if (newDisplayName && newDisplayName !== oldDisplayName) {
			this.projectService
				.renameProject(this.editingManifest.id, newDisplayName)
				.then(() => {
					this.isProjectChanged = true;
					this.editingManifestOldDisplayName = null;
					this.onLoadProjects();
				})
				.catch((errorResponse: IErrorResponse) => {
					// Revert back to old displayName when fail
					this.editingManifest.displayName = oldDisplayName;
					const message = `
						Failed to change project name from ${oldDisplayName} to ${newDisplayName}. Reason: ${errorResponse.message}`;

					this.dialogService.showError("Operation Failed", message);
				});
		} else {
			if (!newDisplayName) {
				this.editingManifest.displayName = oldDisplayName;
				const message = `Project name can not be empty`;
				this.dialogService.showError("Operation Failed", message);
			}
			this.editingManifestOldDisplayName = null;
		}
	}

	public isProjectDisplayNameEditMode(): boolean {
		return this.editingManifestOldDisplayName ? true : false;
	}

	public onKeyUpEvent(keyCode: number): void {
		if (keyCode === KeyCodeEnum.ENTER) {
			this.onDoneEditProjectName();
		} else if (keyCode === KeyCodeEnum.ESCAPE) {
			this.onAbortEditProjectName();
		}
	}

	public onAbortEditProjectName(): void {
		this.editingManifest.displayName = this.editingManifestOldDisplayName;
		this.editingManifestOldDisplayName = null;
	}

	public static add(m: ng.IModule): void {
		m.component("eirProjectCard", {
			bindings: {
				manifest: "<",
				tags: "<",
				isProjectChanged: "=",
				onLoadProjects: "&"
			},
			template: require("./project-card.html"),
			controller: ProjectCardComponent,
			controllerAs: "ctrl",
		});
	}
}
