import { IMaskOptions } from "../../model/sohoxi/sohoxi-mask.model";
import { Utils } from "../../sohoxi/utils";

/**
 * Mask Directive.
 *
 * Sets masking properties on an input.
 * Instead of providing av options object, the following attributes can be used:
 * data-mask-mode
 * data-mask (corresponds to 'pattern' in IMaskOptions)
 * data-thousands
 * data-must-complete
 * data-show-currency
 *
 * Example:
 * scope["options"]: IXiMaskOptions = {"pattern": "##:##", }
 * <input xi-mask="options" />
 *
 * <input xi-mask data-mask-mode="number" data-mask="####" data-thousands="true" />
 */
export class MaskDirective implements ng.IDirective {
	private static readonly DIRECTIVE_NAME = "xiMask";
	public restrict = "A";
	public scope = false;

	public link(
		scope: ng.IScope,
		element: ng.IAugmentedJQuery,
		attributes: ng.IAttributes
	): void {
		const options = attributes[MaskDirective.DIRECTIVE_NAME];
		const unbindWatcher = scope.$watch(
			options,
			(newOptions: IMaskOptions) => {
				if (newOptions) {
					// Set attributes and initialize control with options object
					if (newOptions.mode) {
						element.attr("data-mask-mode", newOptions.mode);
					}
					if (newOptions.thousands) {
						element.attr("data-thousands", String(newOptions.thousands));
					}
					if (newOptions.mustComplete) {
						element.attr(
							"data-must-complete",
							String(newOptions.mustComplete)
						);
					}
					if (newOptions.showCurrency) {
						element.attr(
							"data-show-currency",
							String(newOptions.showCurrency)
						);
					}
					element.mask(newOptions);
				} else {
					// Initiate control without options object
					element.mask();
				}
			}
		);
		// Tear down
		element.on("$destroy", () => {
			const maskData = Utils.getData(element, "mask");
			if (maskData) {
				maskData.destroy();
			}
			unbindWatcher();
		});
	}

	public static add(m: ng.IModule): void {
		m.directive(MaskDirective.DIRECTIVE_NAME, () => {
			return new MaskDirective();
		});
	}
}
