import jsonpath from "jsonpath";
import isEmpty from "lodash-es/isEmpty";
import isUndefined from "lodash-es/isUndefined";

import type { IMap } from "ng2/common/model/imap.model";
import type * as sw from "ng2/common/model/swagger.model";

import { DateHelper } from "ng2/common/helpers/date.helper";

export class JsonHelper {
	/**
	 * Generate a sample object from a Swagger (JSON-) Schema.
	 * Useful for displaying minimal information about a schema.
	 *
	 * Returns null if the schema type is invalid.
	 * @param schema
	 */
	static generateSample(schema: sw.ISchemaObject, swaggerDoc?: sw.IDocument, ref?: string): any {
		if (schema && !isEmpty(schema)) {
			let tempSchema = schema;
			let currentRef = ref;
			let path: string;

			if (schema.$ref) {
				switch (schema.$ref[0]) {
					case "#": //local reference
						path = schema.$ref.split("/")[2]; //assume $ref is "#/definitions/someName"
						if (!path) {
							throw new Error(
								"Schema reference error. <br/><br/> Reference not found."
							);
						}
						tempSchema = swaggerDoc.definitions[path];
						currentRef = schema.$ref;
						break;
					default: //TODO: add support for remote and URL references.
						throw new Error(
							"Schema reference error. <br/><br/> Remote and URL references are not supported in App Builder."
						);
				}
				if (!tempSchema) {
					throw new Error(
						"Schema reference error. <br/><br/> Reference not found."
					);
				}
			}

			switch (this.getSchemaType(tempSchema)) {
				case "integer":
					return tempSchema.default || 1;
				case "number":
					return tempSchema.default || 1.0;
				case "string":
					switch (tempSchema.format) {
						case "byte":
							return tempSchema.default || "SGVsbG8gV29ybGQ=";
						case "date":
							return tempSchema.default || "1985-04-12";
						case "date-time":
							return tempSchema.default || "1985-04-12T23:20:50.52Z";
						case "binary":
							return tempSchema.default || "10101010";
						default:
							return tempSchema.default || "text";
					}
				case "boolean":
					return tempSchema.default || true;
				case "array":
					if (Array.isArray(tempSchema.items)) {
						return tempSchema.items.map((item) => JsonHelper.generateSample(item, swaggerDoc, currentRef));
					} else {
						if (currentRef && currentRef === ref) {
							return [];
						}
						return [JsonHelper.generateSample(tempSchema.items, swaggerDoc, currentRef)];
					}
				case "object":
					const sample: any = {};
					for (const key in tempSchema.properties) {
						if (tempSchema.properties[key].$ref && currentRef
							&& tempSchema.properties[key].$ref === currentRef) {
							sample[key] = {};
						} else {
							sample[key] = JsonHelper.generateSample(tempSchema.properties[key], swaggerDoc, currentRef);
						}
					}
					return sample;
				default:
					throw new Error(
						"Unknown schema type. <br/><br/> Review your swagger documentation & try again."
					);
			}
		} else {
			throw new Error(
				"Schema is missing. <br/><br/> Be sure that your ION API Method has a swagger document."
			);
		}
	}

	private static getSchemaType(schema: sw.ISchemaObject): string | string[] {
		if(schema.type) {
			return schema.type;
		}

		//Try to auto-correct missing schema type
		if (schema.properties) {
			return "object";
		} else if (schema.items) {
			return "array";
		} else if (schema.format) {
			switch (schema.format) {
				case "float":
				case "double":
					return "number";
				case "int32":
				case "int64":
					return "integer";
				default:
					return "string";
			}
		} else {
			//default to string to ensure a type
			return "string";
		}
	}

	/**
	 * Generate a very simple Swagger schema for a given object.
	 * Throws a TypeError if the given object cannot be mapped to a valid JSONSchema type.
	 *
	 * If given an array, it will only interpret the type of the first element.
	 * @param {number | boolean} sample asd
	 */

	static generateSchema(
		sample: number | boolean | string | IMap<any> | Array<any>
	): sw.ISchemaObject {
		const schema: sw.ISchemaObject = {};
		if (typeof sample === "number") {
			schema.type = "number";
			schema.default = sample;
		} else if (typeof sample === "string") {
			schema.type = "string";
			schema.default = sample;
			const format = this.getPropertyFormat(sample);
			if (format !== undefined) {
				schema.format = format;
			}
		} else if (typeof sample === "boolean") {
			schema.type = "boolean";
			schema.default = sample;
		} else if (Array.isArray(sample)) {
			schema.type = "array";
			if (sample.length > 1) {
				schema.items = sample.map((item) => JsonHelper.generateSchema(item));
			} else {
				schema.items = JsonHelper.generateSchema(sample[0]);
			}
		} else if (typeof sample === "object" && sample !== null) {
			schema.type = "object";
			schema.properties = {};
			for (const key in sample) {
				schema.properties[key] = JsonHelper.generateSchema(sample[key]);
			}
		} else {
			throw new TypeError("Cannot map " + sample + " to a valid type");
		}
		return schema;
	}

	private static getPropertyFormat(value: string): string {
		const date = DateHelper.parse(value);
		if (date.toString() !== "Invalid Date") {
			if (date.getHours() !== 0) {
				return "date-time";
			}
		}
		return undefined;
	}

	static getPropertyType(
		schema: sw.ISchemaObject,
		propertyName: string
	): string {
		// remove special characters
		const propertyNameMatch = propertyName.match(/[a-zA-Z0-9]+/);

		const jsonQuery =
			propertyNameMatch !== null ? `$..${propertyNameMatch}` : "";

		if (isUndefined(jsonQuery) || isEmpty(jsonQuery)) {
			throw new TypeError("");
		}

		const propertyType = jsonpath.query(schema, jsonQuery);

		return propertyType[0].type;
	}

	/**
	 * Detects if JSON object is cyclic
	 * https://stackoverflow.com/a/14962496
	 * @param obj JSON object
	 */

	static isCyclic(obj: object): boolean {
		const seenObjects: object[] = [];

		const detect = (item: IMap<any>): boolean => {
			if (item && typeof item === "object") {
				if (seenObjects.indexOf(item) !== -1) {
					return true;
				}
				seenObjects.push(item);
				for (const key in item) {
					if (item.hasOwnProperty(key) && detect(item[key])) {
						// Debugger.log(item, "cycle at " + key);
						return true;
					}
				}
			}
			return false;
		};

		return detect(obj);
	}

	/**
	 * Fixes cyclic references if any
	 * https://stackoverflow.com/a/11616993
	 * @param obj JSON object
	 */

	static fixCyclic(obj: object): object {
		let cache: object[] = [];
		const result = JSON.stringify(obj, (key, value) => {
			if (typeof value === "object" && value !== null) {
				if (cache.indexOf(value) !== -1) {
					// Duplicate reference found
					try {
						// If this value does not reference a parent it can be deduped
						return JSON.parse(JSON.stringify(value));
					} catch (error) {
						// discard key if value cannot be deduped
						return;
					}
				}
				// Store value in our collection
				cache.push(value);
			}
			return value;
		});
		cache = null; // Enable garbage collection

		return result ? JSON.parse(result) : null;
	}

	/**
	 * Prettify JSON string
	 * @param jsonString
	 * @param spaces
	 */

	static prettify(jsonString: object, spaces: number = 2): string {
		return JSON.stringify(jsonString, null, spaces);
	}

	/**
	 * Tests if a path is invalid json
	 * @param root String representing the json path
	 * @returns Return true if the path is invalid otherwise false
	 */
	static isInvalidJsonPath(root: string): boolean {
		let result = false;
		try {
			jsonpath.parse(root);
		} catch (e) {
			result = true;
		}
		return result;
	}
}
