import angular from "angular";
import { AngularConstants } from "../../../../common/angular-constants";

import { CanvasModeEnum, InjectNames, Numbers, SaveFlagsEnum, SettingTypes, WizardFocusEnum } from "ng2/common/constants/common.constant";
import { BindingTypeEnum } from "ng2/common/enums/binding-type.enum";
import { IDataPair } from "ng2/common/model/data-pair.model";
import { IJsonComponent } from "ng2/common/model/databearer/components/json-component.model";
import { Interaction } from "ng2/components/interaction/interaction";
import { InteractionParameter } from "ng2/components/interaction/interaction-parameter";
import { IDialogBaseScope, KeyCodeEnum } from "../../../../common/defines";
import { IComponent } from "../../../../common/IComponent";
import { IDialog, IDialogEvent, IDialogResult } from "../../../../common/services/dialog-service";
import type { IEditorComponent } from "common/interface/ieditor-component";
import { SourceComponent } from "../../defines";
import { EditorService } from "../../services/editor-service";
import { InteractionManagementService } from "../../services/interaction-management-service";
import { IWizardStep } from "../../wizard/wizard-ctrl";

export enum WizardTick {
	TRIGGER_EVENT,
	ACTION,
	DATA_MAPPING,
	SUMMARY,
}

export enum WizardStates {
	SOURCE_COMPONENT,
	TARGET_COMPONENT,
	SUMMARY,
	CLOSE_WIZARD
}

export interface IInteractionWizardScope extends IDialogBaseScope<IEditorComponent> {
	component: IEditorComponent;
}

export interface IDataMappingSummaryItem {
	targetField: string;
	sourceContext: string;
	sourceField: string;
	isMandatory: boolean;
}

export class InteractionWizardCtrl {
	//Keeping title and usertip for when implementing the new wizard is made.
	public steps: IWizardStep[] = [
		{
			id: "interaction-wizard-source.html",
			title: "",
			userTip: "",
			template: require("./interaction-wizard-source.html"),
		}, {
			id: "interaction-wizard-target.html",
			title: "",
			userTip: "",
			template: require("./interaction-wizard-target.html"),
		}, {
			id: "interaction-wizard-datamapping.html",
			title: "",
			userTip: "",
			template: require("./interaction-wizard-datamapping.html"),
		}, {
			id: "interaction-wizard-summary.html",
			title: "",
			userTip: "",
			template: require("./interaction-wizard-summary.html"),
		}
	];
	public currentTick: number;
	public maxTick = 3;
	public userTip: string;
	public nextDisabled: boolean = true;

	public settingTargetAction: IDataPair;
	public settingSourceTrigger: IDataPair;
	public selectedInteraction: Interaction;
	public settingSourceComponent: IJsonComponent;
	public settingTargetComponent: IJsonComponent;
	public dataMappingSummaryItems: IDataMappingSummaryItem[];
	private component: IComponent;
	private wizardElement: ng.IAugmentedJQuery;
	private dialog: IDialog<IComponent>;

	protected unsubscribers: Function[] = [];

	public get isSummaryPage(): boolean { return this.currentTick === this.maxTick; }

	static $inject = [
		AngularConstants.SCOPE,
		AngularConstants.TIMEOUT,
		AngularConstants.ELEMENT,
		AngularConstants.TEMPLATE_CACHE,
		AngularConstants.Q,
		InjectNames.EDITOR_SERVICE,
		InjectNames.INTERACTION_MANAGEMENT_SERVICE,
	];

	constructor(
		private $scope: IInteractionWizardScope,
		private $timeout: ng.ITimeoutService,
		private $element: ng.IAugmentedJQuery,
		private $templateCache: ng.ITemplateCacheService,
		private $q: ng.IQService,
		private editorService: EditorService,
		private interactionManagementService: InteractionManagementService,
	) {
		this.initTemplates();
		this.initializeUnsubscribers();

		const dialog: IDialog<IEditorComponent> = $scope.eirDialog;
		this.dialog = dialog;
		if (dialog.parameter) {
			$scope.component = dialog.parameter;
			this.component = $scope.component;
			this.settingSourceComponent = this.editorService.getSelectedComponent().getDatabearer();
			this.settingTargetComponent = this.editorService.getApplicationParent().getDatabearer();
			this.selectedInteraction = this.interactionManagementService.createNew(dialog.parameter);
			//To remove selected component label
			angular.element("#" + this.component.getId()).addClass("inWizard");
			this.editorService.newWizardInteractionEvent.raise(this.selectedInteraction);
			this.editorService.isWizardChangedEvent.raise(true);
		}

		const dialogElement = $element.parents(".draggable-dialog");
		dialogElement.draggable({
			distance: 10,
			start: () => {
				dialogElement.css("transition", "none");
				dialogElement.css("transform", "none");
			}
		});

		dialog.closing = (dialogEvent?: IDialogEvent<any>) => {
			this.editorService.selectedInteractionChangedEvent.raise(null);
			this.editorService.setFinderComponent();
			this.exitWizard();
			dialogElement.fadeOut();

			const cancelOnEscape = (results: IDialogResult<any>): IDialogResult<any> => {
				if (results.value === KeyCodeEnum.ESCAPE) {
					this.cancel();
				}
				return results;
			};

			return dialogEvent && dialogEvent.dialog.getPromise().then(cancelOnEscape);
		};

		this.wizardElement = $element.find(".wizard");
		const self = this;
		this.wizardElement.on("activated", (event: JQueryEventObject, element: JQuery) => {
			const selectedTick: number = self.wizardElement.data("wizard").ticks.index(element);
			self.gotoWizardTick(selectedTick);
		});

		this.wizardElement.on("afteractivate", (event: JQueryEventObject, element: JQuery) => {
			$scope.$apply();
		});

		this.currentTick = 0;
		$timeout(() => {
			this.gotoWizardTick(this.currentTick);
		}, Numbers.TEN);

		$scope.$on(AngularConstants.DESTROY, () => {
			angular.forEach(this.unsubscribers, (unsubscribe) => {
				unsubscribe();
			});
		});
	}

	public initTemplates(): void {
		this.steps.forEach(step => {
			if (!this.$templateCache.get(step.id)) {
				this.$templateCache.put(step.id, step.template);
			}
		});
	}

	public initializeUnsubscribers(): void {
		this.unsubscribers.push(this.editorService.wizardCloseEvent.on(() => {
			this.cancel();
		}));

		this.unsubscribers.push(this.editorService.triggerSourceComponentChangedEvent.on((component: IComponent) => {
			this.settingSourceComponent = component.getDatabearer();
		}));

		this.unsubscribers.push(this.editorService.targetActionComponentChangedEvent.on((component: IComponent) => {
			this.settingTargetComponent = component.getDatabearer();
		}));

		this.unsubscribers.push(this.editorService.setSourceTriggerEvent.on((sourceTrigger: IDataPair) => {
			this.settingSourceTrigger = sourceTrigger;
			this.updateDisabled();
		}));

		this.unsubscribers.push(this.editorService.setTargetActionEvent.on((targetAction: IDataPair) => {
			this.settingTargetAction = targetAction;
			this.updateDisabled();
		}));

		this.unsubscribers.push(this.editorService.wizardTickChangedEvent.on((tick: number) => {
			this.gotoWizardTick(tick);
		}));
	}

	public selectWizardStep(stepNumber: number): void {
		this.setWizardTick(stepNumber);
		this.gotoWizardTick(stepNumber);
	}

	public gotoWizardTick(tickNumber: number): void {
		this.userTip = "";

		switch (tickNumber) {
			case WizardTick.TRIGGER_EVENT: // Trigger Event
				// Setting canvas mode to selection will reject the useFinder promise
				this.editorService.setCanvasMode(CanvasModeEnum.SELECTION);
				this.userTip = `Please select the event that will trigger the action. If you want to change component
					simply click on a component on the canvas. You can also choose to select an application event by clicking  `;
				this.editorService.wizardSetFinderModeEvent.raise("source");
				this.checkWizardStates(tickNumber);
				break;
			case WizardTick.ACTION: // Action
				// Setting canvas mode to selection will reject the useFinder promise
				this.editorService.setCanvasMode(CanvasModeEnum.SELECTION);
				this.editorService.wizardSetFinderModeEvent.raise("target");
				this.userTip = `Please select a target component and action. If you want to change component simply click on a
					component on the canvas or select the `;
				this.checkWizardStates(tickNumber);
				break;
			case WizardTick.DATA_MAPPING: // Data Mapping
				this.editorService.setCanvasMode(CanvasModeEnum.WIZARD);
				this.userTip = `Configure the data field mapping by selecting target action data field,
					and the source component and field.`;
				this.checkWizardStates(tickNumber);
				break;
			default: // WizardTick.SUMMARY //Summary
				this.refreshDataMappingSummaryItems();
				break;
		}
		this.currentTick = tickNumber;
	}

	public cancel(): void {
		this.interactionManagementService.removeInteraction(this.selectedInteraction);
		this.checkWizardStates(WizardStates.CLOSE_WIZARD);

		this.dialog.close();
	}

	public next(): void {
		if (this.isSummaryPage) {
			this.dialog.close();
		} else {
			this.selectWizardStep(this.currentTick + 1);
		}
	}

	public back(): void {
		this.selectWizardStep(this.currentTick - 1);
	}

	private setWizardTick(tickNumber: number): void {
		// Do not select the same tick as current.
		if (tickNumber !== this.currentTick) {
			this.wizardElement.data("wizard").select(tickNumber);
		}
	}

	private updateDisabled(): void {
		switch (this.currentTick) {
			case WizardTick.TRIGGER_EVENT:
				this.nextDisabled = !this.selectedInteraction.sourceName;
				break;
			case WizardTick.ACTION:
				this.nextDisabled = !this.selectedInteraction.targetName;
				break;
			case WizardTick.DATA_MAPPING:
				this.nextDisabled = !this.selectedInteraction.sourceName || !this.selectedInteraction.targetName;
				break;
			default: //WizardTick.SUMMARY
				this.nextDisabled = false;
				break;
		}
	}

	private checkWizardStates(state: number): void {
		switch (state) {
			case WizardStates.CLOSE_WIZARD:
				this.editorService.setWizardFocusComponent(
					WizardFocusEnum.UNFOCUSED, this.settingTargetComponent, SettingTypes.TARGET);
				this.editorService.setWizardFocusComponent(
					WizardFocusEnum.UNFOCUSED, this.settingSourceComponent, SettingTypes.SOURCE);
				angular.element("#" + this.component.getId()).removeClass("inWizard"); //To show selected component label
				break;
			case WizardStates.SOURCE_COMPONENT:
				this.editorService.setWizardFocusComponent(
					WizardFocusEnum.UNFOCUSED, this.settingTargetComponent, SettingTypes.TARGET);
				this.editorService.setWizardFocusComponent(
					WizardFocusEnum.FOCUSED, this.settingSourceComponent, SettingTypes.SOURCE);
				break;
			case WizardStates.TARGET_COMPONENT:
				if (this.settingSourceComponent.id !== this.settingTargetComponent.id) {
					this.editorService.setWizardFocusComponent(
						WizardFocusEnum.RESTING, this.settingSourceComponent, SettingTypes.SOURCE);
					this.editorService.setWizardFocusComponent(
						WizardFocusEnum.FOCUSED, this.settingTargetComponent, SettingTypes.TARGET);
				}
				break;
			default: //Data Mapping or Summary
				if (this.settingSourceComponent.id !== this.settingTargetComponent.id) {
					this.editorService.setWizardFocusComponent(
						WizardFocusEnum.RESTING, this.settingTargetComponent, SettingTypes.TARGET);
					this.editorService.setWizardFocusComponent(
						WizardFocusEnum.RESTING, this.settingSourceComponent, SettingTypes.SOURCE);
				}
		}
		this.updateDisabled();
	}

	private exitWizard(): void {
		this.checkWizardStates(WizardStates.CLOSE_WIZARD);
		this.editorService.isWizardChangedEvent.raise(false);
		this.editorService.setFinderActivity(false);
		this.editorService.setCanvasMode(CanvasModeEnum.SELECTION);
		this.editorService.saveCurrent(SaveFlagsEnum.INTERACTIONS);
	}

	private refreshDataMappingSummaryItems(): ng.IPromise<void> {
		return this.makeDataMappingSummaryItems().then(summaryItems => {
			this.dataMappingSummaryItems = summaryItems;
		}).catch(err => {
			this.dataMappingSummaryItems = [];
			console.warn("[InteractionWizardCtrl] Failed to make setting parameters:", err);
		});
	}

	private makeDataMappingSummaryItems(): ng.IPromise<IDataMappingSummaryItem[]> {
		const interactionParameters = this.selectedInteraction.getParameters();
		const promisedSummaryItems = interactionParameters.map(parameter => {
			return this.makeDataMappingSummaryItem(parameter);
		});
		return this.$q.all(promisedSummaryItems);
	}

	private makeDataMappingSummaryItem(parameter: InteractionParameter): ng.IPromise<IDataMappingSummaryItem> {
		switch (parameter.valueSource.Type) {
			case BindingTypeEnum.STATIC_VALUE:
				return this.makeStaticDataMappingSummaryItem(parameter);
			case BindingTypeEnum.BINDING:
				return this.makeComponentBindingDataMappingSummaryItem(parameter);
			case BindingTypeEnum.CONTEXT_BINDING:
				return this.makeContextBindingDataMappingSummaryItem(parameter);
			default:
				return this.$q.reject(`Unknown parameter type: ${parameter.valueSource.Type}`);
		}
	}

	private makeStaticDataMappingSummaryItem(parameter: InteractionParameter): ng.IPromise<IDataMappingSummaryItem> {
		return this.$q.resolve({
			targetField: parameter.targetField.Meta.name,
			sourceContext: SourceComponent.STATIC_VALUE_OBJECT.name,
			sourceField: parameter.valueSource.value,
			isMandatory: parameter.targetField.isMandatory,
		});
	}

	private makeComponentBindingDataMappingSummaryItem(
		parameter: InteractionParameter
	): ng.IPromise<IDataMappingSummaryItem> {
		const componentId = parameter.valueSource.sourceName;
		const component = this.editorService.getComponentInstanceById(componentId);
		return component.getBindingMetaData().then(bindings => {
			const { visibleName } = bindings.find(binding => binding.sourceKey === parameter.valueSource.sourceKey);
			return {
				targetField: parameter.targetField.Meta.name,
				sourceContext: component.getDisplayName(),
				sourceField: visibleName,
				isMandatory: parameter.targetField.isMandatory,
			};
		});
	}

	private makeContextBindingDataMappingSummaryItem(
		parameter: InteractionParameter
	): ng.IPromise<IDataMappingSummaryItem> {
		const targetComponentId = this.selectedInteraction.targetName;
		const targetComponent = this.editorService.getComponentInstanceById(targetComponentId);
		return targetComponent.getContextMetaData().then(bindings => {
			const { visibleName } = bindings.find(binding => binding.sourceKey === parameter.valueSource.sourceKey);
			return {
				targetField: parameter.targetField.Meta.name,
				sourceContext: SourceComponent.TARGET_CONTEXT_OBJECT.name,
				sourceField: visibleName,
				isMandatory: parameter.targetField.isMandatory,
			};
		});
	}

	static add(m: ng.IModule): void {
		m.controller("eirInteractionWizardCtrl", InteractionWizardCtrl);
	}
}
