import { IJsonPersistentComponent } from "ng2/common/model/databearer/components/base/json-persistent-components.model";
import { IJsonComponent, IJsonComponentSettings } from "ng2/common/model/databearer/components/json-component.model";
import type { IJsonValueSource } from "ng2/common/model/databearer/json-binding.model";
import type { IMap } from "ng2/common/model/imap.model";

export interface IJsonDropdown extends IJsonComponent, IJsonPersistentComponent {
	displayValue: IJsonValueSource;
	label: IJsonValueSource;
	showLabel: IJsonValueSource; // Deprecated
	boundarySettings: IMap<IJsonDropdownSettings>;
}

export interface IJsonDropdownSettings extends IJsonComponentSettings {
	showLabel: boolean;
	boxWidth: string;
}

export interface IStaticValueDropdown {
	value: number;
	label: string;
}
