import { IValidateOptions } from "../../model/sohoxi/sohoxi-validate.model";

/**
 * Validation Directive.
 *
 * Validates input based on the options. Possible validations are "required", "date" and "time".
 * If validation is "time", the "timeFormat" option can be set.
 * Instead of providing av options object, the following attributes can be used:
 * data-validate
 * data-time-format
 *
 * Examples:
 * <input xi-validate="time" xi-time-format="HH"/>
 */
export class ValidateDirective implements ng.IDirective {
	private static readonly DIRECTIVE_NAME = "xiValidate";

	public require = "?ngModel";
	public restrict = "A";
	public scope = false;

	public link(
		scope: ng.IScope,
		element: ng.IAugmentedJQuery,
		attributes: ng.IAttributes
	): void {
		const options = attributes[ValidateDirective.DIRECTIVE_NAME];
		const unbindWatcher = scope.$watch(
			options,
			(newOptions: IValidateOptions) => {
				// Use options object or attributes
				if (!newOptions) {
					// Use attributes
					element.validate();
				} else {
					if (newOptions.validation) {
						element.attr("data-validate", newOptions.validation);
					}
					if (newOptions.timeFormat) {
						element.attr("data-time-format", newOptions.timeFormat);
					}
					element.validate();
				}
			}
		);
		// Tear down
		element.on("$destroy", () => {
			unbindWatcher();
		});
	}

	public static add(m: ng.IModule): void {
		m.directive(ValidateDirective.DIRECTIVE_NAME, [
			() => {
				return new ValidateDirective();
			}
		]);
	}
}
