import { IComponentSettingCategory } from "ng2/common/model/components/settings/component-setting-category.model";
import { IDataPair } from "ng2/common/model/data-pair.model";
import { IJsonComponent, IJsonSize } from "ng2/common/model/databearer/components/json-component.model";
import { IJsonBindableField } from "ng2/common/model/databearer/json-binding.model";
import { IMap } from "ng2/common/model/imap.model";
import { IComponent } from "common/IComponent";
import { IComponentScope } from "common/interface/icomponent-scope";
import { ILayoutComponent } from "common/interface/ilayout-component";

import { ApplicationParent } from "components/application-parent";
import { Button } from "components/button/button";
import { Chart } from "components/chart/chart";
import { Checkbox } from "components/checkbox/checkbox";
import { Datagrid } from "components/datagrid/datagrid";
import { Datepicker } from "components/datepicker/datepicker";
import { Dropdown } from "components/dropdown/dropdown";
import { Flexbox } from "components/flexbox/flexbox";
import { Hyperlink } from "components/hyperlink/hyperlink";
import { Image } from "components/image/image";
import { Label } from "components/label/label";
import { LayoutComponent } from "components/layout-component";
import { List } from "components/list/list";
import { Lookup } from "components/lookup/lookup";
import { RadioButton } from "components/radiobutton/radiobutton";
import { TabPanel } from "components/tabs/tab-panel";
import { Tabs } from "components/tabs/tabs";
import { TextArea } from "components/textarea/textarea";
import { TextBox } from "components/textbox/textbox";
import { Timepicker } from "components/timepicker/timepicker";

import { FrameworkService } from "components/services/framework-service";

export interface IStaticComponent {
	new(
		obj: IJsonComponent,
		$element: ng.IAugmentedJQuery,
		parentInstance: ILayoutComponent,
		frameworkService: FrameworkService
	): IComponent;
	directive: any[];

	BINDABLE_FIELDS_2: IJsonBindableField[];
	SETTING_FIELDS: IComponentSettingCategory[];
	DEFAULT_WIDTH: IJsonSize;
	DEFAULT_HEIGHT: IJsonSize;

	SOURCE_TRIGGERS?: IDataPair[];
	TARGET_ACTIONS?: IDataPair[];

	DEFAULT_TRIGGER?: IDataPair;
	DEFAULT_ACTION?: IDataPair;
}

export interface IComponentListEntry {
	class: IStaticComponent;
	displayName?: string;
	available: boolean;
	isLayoutComponent: boolean;
}

export class Defines {
	static get allComponents(): IMap<IComponentListEntry> {
		const componentList: IMap<IComponentListEntry> = {};
		componentList[ApplicationParent.name] = {
			class: ApplicationParent,
			available: false,
			isLayoutComponent: false,
		};
		componentList[Button.name] = {
			class: Button,
			available: true,
			isLayoutComponent: false,
		};
		componentList[Chart.name] = {
			class: Chart,
			available: true,
			isLayoutComponent: false,
		};
		componentList[Checkbox.name] = {
			class: Checkbox,
			available: true,
			isLayoutComponent: false,
		};
		componentList[Datagrid.name] = {
			class: Datagrid,
			available: true,
			isLayoutComponent: false,
		};
		componentList[Datepicker.name] = {
			class: Datepicker,
			available: true,
			isLayoutComponent: false,
		};
		componentList[Dropdown.name] = {
			class: Dropdown,
			available: true,
			isLayoutComponent: false,
		};
		componentList[Flexbox.name] = {
			class: Flexbox,
			available: false,
			isLayoutComponent: true,
		};
		componentList[Hyperlink.name] = {
			class: Hyperlink,
			available: true,
			isLayoutComponent: false,
		};
		componentList[Image.name] = {
			class: Image,
			available: true,
			isLayoutComponent: false,
		};
		componentList[Label.name] = {
			class: Label,
			available: true,
			isLayoutComponent: false,
		};
		componentList[List.name] = {
			class: List,
			available: true,
			isLayoutComponent: false,
		};
		componentList[Lookup.name] = {
			class: Lookup,
			available: true,
			isLayoutComponent: false,
		};
		componentList[RadioButton.name] = {
			class: RadioButton,
			available: true,
			isLayoutComponent: false,
		};
		componentList[Tabs.name] = {
			class: Tabs,
			available: true,
			isLayoutComponent: true,
		};
		componentList[TabPanel.name] = {
			class: TabPanel,
			available: false,
			isLayoutComponent: true,
		};
		componentList[TextBox.name] = {
			class: TextBox,
			available: true,
			isLayoutComponent: false,
		};
		componentList[TextArea.name] = {
			class: TextArea,
			available: true,
			isLayoutComponent: false,
		};
		componentList[Timepicker.name] = {
			class: Timepicker,
			available: true,
			isLayoutComponent: false,
		};
		return componentList;
	}

	static addComponents(m: ng.IModule): void {
		for (const key in Defines.allComponents) {
			if (Defines.allComponents.hasOwnProperty(key)) {
				const componentName = key.toLowerCase();
				const componentClass: IStaticComponent = Defines.allComponents[key].class;
				const method = componentClass.directive[0];
				// Fix for incorrect directive definition in chart.ts
				if (typeof method === "function") {
					const directive = componentClass.directive[0]();
					// Only append link functions to real components
					if (directive && (directive.templateUrl || directive.template) && !directive.link) {
						this.appendLinkFunction(directive);
					}
					m.directive("d" + componentName, [() => {
						return directive;
					}]);
				}
			}
		}
	}

	// Appends a link function to a component directive to get more accurate componentReady
	private static appendLinkFunction(directive: any): void {
		directive.link = ($scope: IComponentScope) => {
			if (!($scope.componentInstance instanceof LayoutComponent)) {
				$scope.componentReady({ componentInstance: $scope.componentInstance });
			}
		};
	}

	static get CustomComponentType(): string { return "CustomComponent"; }
}
