import angular from "angular";
import { AngularConstants } from "../../../../common/angular-constants";

import { InjectNames, ServerPaths } from "ng2/common/constants/common.constant";
import { IJsonCompositeComponent } from "ng2/common/model/databearer/components/json-composite-component.model";
import { IJsonDatasource } from "ng2/common/model/databearer/dataservice/json-datasource.model";
import { IDataGridOptions, IDataItem } from "ng2/common/model/sohoxi/sohoxi-datagrid.model";

import { IDialogBaseScope } from "../../../../common/defines";
import { IErrorResponse } from "../../../../common/error-response";
import { DatagridHelper, IRowItem } from "../../../../common/sohoxi/datagrid-helper";
import { InteractionManagementService } from "../../services/interaction-management-service";

import { Helper } from "../../../../common/helper";
import { ConflictResolutonService, IResolutionResult } from "../../../../common/services/conflict-resolution-service";
import {
	DialogButtonType, DialogService, IDialog, IDialogOptions, IDialogResult,
	IMessageDialogOptions, StandardDialogButtons
} from "../../../../common/services/dialog-service";
import { HttpService } from "../../../../common/services/http-service";
import { RepositoryService } from "../../../repository/repository-service";
import { ProjectService } from "../../../services/project-service";
import { EditorService } from "../../services/editor-service";

export interface IItemSpecificData {
	dataName: string;
	conflictKey: string;
	urlName: string;
	maxAllowed: number;
	maxErrorMessage: string;
}

export abstract class ManageDialogBase<T extends IJsonDatasource | IJsonCompositeComponent> {
	public itemSelected = false;
	public loading = false;
	public datagridOptions: IDataGridOptions<T>;

	protected dialog: IDialog<void>;
	protected selectedItems: T[] = [];

	public static $inject = [
		AngularConstants.SCOPE,
		AngularConstants.ELEMENT,
		AngularConstants.WINDOW,
		InjectNames.REPOSITORY_SERVICE,
		InjectNames.DIALOG_SERVICE,
		InjectNames.EDITOR_SERVICE,
		InjectNames.CONFLICT_RESOLUTION_SERVICE,
		InjectNames.HTTP_SERVICE,
		InjectNames.PROJECT_SERVICE,
		InjectNames.INTERACTION_MANAGEMENT_SERVICE,
	];

	public constructor(
		protected $scope: IDialogBaseScope<void>,
		protected $element: ng.IAugmentedJQuery,
		protected $window: ng.IWindowService,
		protected repositoryService: RepositoryService,
		protected dialogService: DialogService,
		protected editorService: EditorService,
		protected conflictResolutionService: ConflictResolutonService,
		protected httpService: HttpService,
		protected projectService: ProjectService,
		protected interactionManagementService: InteractionManagementService,
	) {
		this.dialog = $scope.eirDialog;
		this.initGrid();
		this.$element.find("#local-grid").on("selected", (e: any, args: IRowItem<T>[]) => {
			// When datagrid get unselected args[0] is undefined
			this.$scope.$evalAsync(() => {
				if (args.length > 0) {
					this.itemSelected = true;
					this.selectedItems = args.map(
						rowObject => {
							return rowObject.data;
						});
				} else {
					this.selectedItems = [];
					this.itemSelected = false;
				}
			});

		});
	}

	protected abstract addItemsToProject(items: T[]): void;

	protected abstract deleteItems(ids: string[]): ng.IPromise<T[]>;

	protected abstract export(): void;

	protected abstract getDataList(): T[];

	protected abstract getItemDisplayName(item: T): string;

	protected abstract getItemId(item: T): string;

	protected abstract initGrid(): void;

	protected abstract getItemSpecificData(): IItemSpecificData;

	protected abstract save(): ng.IPromise<void>;

	protected abstract setItemId(item: T, id: string): void;

	public openGlobalRepository(): void {
		this.loading = true;
		const repoDatagridOptions = angular.copy(this.datagridOptions);
		repoDatagridOptions.toolbar.title = "Global";
		this.repositoryService.getDataByName<T>(this.getItemSpecificData().urlName).then((items) => {
			repoDatagridOptions.dataset = items;
			const options: IDialogOptions<any> = {
				title: "Repository",
				template: require("./manage-repository-dialog/manage-repository-dialog.html"),
				style: "min-width: 1000px; max-width: 1125px;",
				parameter: repoDatagridOptions,
				cssClass: "repository-dialog",
			};
			this.dialogService.show(options).then((result: IDialogResult<any[]>) => {
				if (result.button === DialogButtonType.Ok) {
					const notAdded: string[] = [];
					const newItems = result.value;
					const newUniqueItems = newItems.filter((newItem) => {
						const isUnique = this.isUnique(newItem);
						if (!isUnique) {
							notAdded.push(this.getItemDisplayName(newItem));
						}
						return isUnique;
					});
					if (notAdded.length > 0) {
						this.dialogService.showError(
							"Import",
							"Following item(s) already exist in your project<br />" + notAdded.join("<br />")
						);
					}
					newUniqueItems.forEach(DatagridHelper.clearSohoProperties);
					this.addItems(newUniqueItems);
				}
			});
		}).catch((errorResponse) => {
			this.datagridOptions.dataset = [];
			this.dialogService.showError("Load from Repository", errorResponse.message);
		}).finally(() => {
			this.loading = false;
		});
	}

	// Adding from import-files or repository
	protected addItems(newItems: T[]): ng.IPromise<T[]> | null {
		const actionTitle = "Import";
		if (newItems.length === 0) {
			return null;
		} else if (this.exceedsMaximum(newItems.length)) {
			this.dialogService.showError(actionTitle, "Tried to add more than max allowed");
			return null;
		}

		const importPromise = this.importToCurrentProject(newItems);
		importPromise.then((response) => {
			if (response.length !== newItems.length) {
				this.dialogService.showError(
					actionTitle,
					`One or more ${this.getItemSpecificData().dataName}(s) was not imported due to error in them`
				);
			}
			this.addItemsToProject(newItems);
			this.datagridOptions.dataset = this.getDataList();
			return response;
		}).catch((errorResponse: IErrorResponse) => {
			// When save fails revert all the newly added
			this.dialogService.showError(
				actionTitle,
				`Failed to import any ${this.getItemSpecificData().dataName}(s).` +
				"Due to " + errorResponse.message + "."
			);
		});
		return importPromise;
	}

	public import(): void {
		const actionTitle = "Import New";
		if (this.exceedsMaximum()) {
			this.dialogService.showError(actionTitle, this.getItemSpecificData().maxErrorMessage);
		} else {
			const options: IDialogOptions<any> = {
				title: actionTitle,
				template: require("../../../../common/dialogs/import/import-dialog.html"),
			};

			this.dialogService.show(options).then((result: IDialogResult<FileList>) => {
				if (result.button === DialogButtonType.Ok) {
					this.loading = true;
					this.repositoryService.readJsonFile<any[]>(result.value[0])
						.then((itemsToImport) => {
							if (this.exceedsMaximum(itemsToImport.length)) {
								this.dialogService.showError(
									actionTitle,
									`You are trying to import too many ${this.getItemSpecificData().dataName}s`
								);
							} else {
								this.importItems(itemsToImport);
							}
						}).catch((errorResponse: IErrorResponse) => {
							// Unable to read input file, probable JSON parse error
							console.warn("Could not read file");
							this.dialogService.showError(actionTitle, "Unable to read file due to: <br \>" + errorResponse.message);
						}).finally(() => {
							this.loading = false;
						});
				}
			});
		}
	}

	private importItems(itemsToImport: T[]): void {
		this.conflictResolutionService.resolve<T>(
			itemsToImport,
			this.datagridOptions.dataset,
			this.getItemSpecificData().conflictKey,
			this.getItemSpecificData().dataName,
			this.getItemSpecificData().maxAllowed
		).then((conflictResolutionObject: IResolutionResult<T>) => {
			const idsToDelete = conflictResolutionObject.toRemove.map(
				(itemToImport: any) => { return itemToImport.id; }
			);
			this.deleteItems(idsToDelete)
				.then(deletedItems => {
					conflictResolutionObject.toAdd.forEach((item: T) => {
						this.setItemId(item, Helper.generateUniqueId());
					});
					this.addItems(conflictResolutionObject.toAdd).catch(() => {
						// If import fails revert deleted items
						this.addItems(deletedItems);
					});
				});
		}).catch(() => {
			// User cancel
			this.loading = false;
		});
	}

	public deleteItem(): void {
		const actionTitle = "Delete Item";
		const displayNames: string[] = [];
		const idsToDelete: string[] = this.selectedItems.map((item) => {
			if ((item as IJsonDatasource).id) {
				displayNames.push((item as IJsonDatasource).displayName);
				return (item as IJsonDatasource).id;
			} else {
				displayNames.push((item as IJsonCompositeComponent).manifest.displayName);
				return (item as IJsonCompositeComponent).manifest.id;
			}
		});
		const options: IMessageDialogOptions = {
			title: actionTitle,
			message: "Are you sure you want to delete these Items?<br \>" + displayNames.join(", "),
			standardButtons: StandardDialogButtons.YesNo
		};
		this.dialogService.showMessage(options).then((result) => {
			if (result.button === DialogButtonType.Yes) {
				this.deleteItems(idsToDelete).then(deletedItems => {
					if (deletedItems.length > 0) {
						this.itemSelected = false;
						this.selectedItems = [];
						this.save();
					}
				});
			}
		}).finally(() => {
			this.datagridOptions.dataset = this.getDataList();
		});
	}

	public close(): void {
		this.getDataList().forEach(dataItem => {
			DatagridHelper.clearSohoProperties(dataItem as IDataItem);
		});
		this.dialog.close({ button: DialogButtonType.Ok });
	}

	private exceedsMaximum(withExtra?: number): boolean {
		let amount = this.getDataList().length;
		if (withExtra) {
			amount += withExtra;
		}
		return amount > this.getItemSpecificData().maxAllowed;
	}

	private importToCurrentProject(newItems: T[]): ng.IPromise<T[]> {
		const currentProject = this.editorService.getCurrentProject();
		const url = ServerPaths.PROJECT +
			currentProject.getId() + "/" +
			this.getItemSpecificData().urlName + "import";
		return this.httpService.executePost<T[]>(url, newItems);
	}

	private isUnique(item: T): boolean {
		const idToFind = this.getItemId(item);
		const existingItems = this.getDataList();
		return !existingItems.some((existingItem) => {
			return this.getItemId(existingItem) === idToFind;
		});
	}

}
