import angular from "angular";
import cloneDeep from "lodash-es/cloneDeep";
import isNumber from "lodash-es/isNumber";
import isUndefined from "lodash-es/isUndefined";

import { Helper } from "../../common/helper";
import type { ILayoutComponent } from "../../common/interface/ilayout-component";
import type { ISohoDropdown } from "../../common/model/sohoxi/sohoxi-dropdown.model";
import { Component } from "../component";
import { ComponentsHelper } from "../helper";
import { FrameworkService } from "../services/framework-service";

import {
	InteractionActionEnum, InteractionPairOperation,
	InteractionTriggerEnum, SettingInputKeys,
	SettingKeys, SidebarTabTypes
} from "ng2/common/constants/common.constant";
import { InteractionDefines } from "ng2/common/constants/interaction-defines.constant";

import { BindingTypeEnum } from "ng2/common/enums/binding-type.enum";
import { SizeTypeEnum } from "ng2/common/enums/size-type.enum";
import { ValueTypeEnum } from "ng2/common/enums/value-type.enum";

import type { IComponentSettingCategory } from "ng2/common/model/components/settings/component-setting-category.model";
import type { IDataPair } from "ng2/common/model/data-pair.model";
import type { IJsonSize } from "ng2/common/model/databearer/components/json-component.model";
import type { IJsonDropdown, IStaticValueDropdown } from "ng2/common/model/databearer/components/json-dropdown.model";
import type { IJsonBindableField, IJsonValueSource } from "ng2/common/model/databearer/json-binding.model";
import type { IMap } from "ng2/common/model/imap.model";
import type { IInteractionParameter } from "ng2/common/model/interaction/interaction-parameter.model";
import type{ IInteraction } from "ng2/common/model/interaction/interaction.model";

import { BackwardCompatible } from "ng2/common/project/backward-compatible";

export interface ICurrentItem extends IMap<any> { }

export class Dropdown extends Component {
	static AVAILABLE_WIDTHS: IDataPair[] = [
		{ name: "Small", value: "input-sm" },
		{ name: "Medium", value: "input-mm" },
		{ name: "Large", value: "input-default" }
	];
	static DEFAULT_WIDTH: IJsonSize = { sizeType: SizeTypeEnum.AUTO };
	static DEFAULT_HEIGHT: IJsonSize = { sizeType: SizeTypeEnum.AUTO };
	static SOURCE_TRIGGERS: IDataPair[] = InteractionDefines.SOURCE_TRIGGERS.concat(
		[InteractionPairOperation.ON_ITEM_CHANGED]
	);
	static TARGET_ACTIONS: IDataPair[] = [
		InteractionPairOperation.LIST,
		InteractionPairOperation.SET_CURRENT_ITEM,
		InteractionPairOperation.CLEAR
	];
	static DEFAULT_TRIGGER: IDataPair = InteractionPairOperation.ON_ITEM_CHANGED;
	static DEFAULT_ACTION: IDataPair = InteractionPairOperation.LIST;
	static SETTING_FIELDS =
		([
			{
				title: "Options",
				tab: SidebarTabTypes.CONTENT,
				settings: [{
					title: "Keep Selection",
					tooltip: "Keep previous selected item on reload. (With ION APIs, an object ID is preferred if using this)",
					ref: SettingKeys.KEEP_SELECTED,
					inputType: SettingInputKeys.CHECKBOX,
					defaultValue: true
				}]
			},
			{
				title: "Style",
				tab: SidebarTabTypes.STYLE,
				settings: [{
					title: "Show Label",
					boundarySetting: true,
					ref: SettingKeys.SHOW_LABEL,
					inputType: SettingInputKeys.CHECKBOX,
					defaultValue: true,
				}, {
					title: "Disabled",
					ref: SettingKeys.IS_READONLY,
					inputType: SettingInputKeys.CHECKBOX,
					defaultValue: false,
					button: {
						icon: "#icon-unlocked",
						tooltip: "Disable Component When...",
						dialog: true
					}
				}]
			},
			{
				title: "Width",
				tab: SidebarTabTypes.STYLE,
				settings: [{
					boundarySetting: true,
					ref: SettingKeys.BOX_WIDTH,
					inputType: SettingInputKeys.RADIO,
					dataset: Dropdown.AVAILABLE_WIDTHS,
					defaultValue: "input-default",
				}]
			}
		] as IComponentSettingCategory[]).concat(Component.SETTING_FIELDS);

	static BINDABLE_FIELDS_2: IJsonBindableField[] = [{
		name: "Selected Item",
		ref: SettingKeys.CURRENT_ITEM,
		valueType: ValueTypeEnum.OBJECT,
		write: false,
		read: true
	}, {
		name: "Label",
		ref: SettingKeys.LABEL,
		valueType: ValueTypeEnum.LOCALIZED_STRING,
		write: true,
		read: false,
		defaultValue: { Type: BindingTypeEnum.STATIC_VALUE, value: "" }
	}, {
		name: "Display Value",
		ref: SettingKeys.DISPLAY_VALUE,
		valueType: ValueTypeEnum.STRING,
		write: true,
		read: false,
		defaultValue: { Type: BindingTypeEnum.STATIC_VALUE, value: "" }
	}
	];

	label: string;
	showLabel: boolean;
	currentItem: ICurrentItem;
	displayValue: IJsonValueSource;
	currentItemIndex: number;
	emptyValue: IStaticValueDropdown[];
	boxWidth: string;
	protected databearer: IJsonDropdown;
	private keepSelected: boolean;
	private previousLength: number;

	constructor(
		obj: IJsonDropdown,
		$element: ng.IAugmentedJQuery,
		parent: ILayoutComponent,
		frameworkService: FrameworkService
	) {
		super(obj, $element, parent, frameworkService);
		if (obj) {
			this.keepSelected = obj.keepSelected;
			this.showLabel = BackwardCompatible.getShowLabel(obj);
		}
		this.currentItemIndex = 0;
		//Static default value
		this.emptyValue = [{
			value: 0,
			label: ""
		}];
	}

	newBoundaryEntered(boundaryId: string): void {
		super.newBoundaryEntered(boundaryId);
		this.setBoundarySettingIfChanged<boolean>(SettingKeys.SHOW_LABEL);
		this.setBoundarySettingIfChanged<string>(SettingKeys.BOX_WIDTH);
	}

	clear(): void {
		this.currentItemIndex = 0;
		this.currentItem = null;
		this.setSelection();
	}

	/**
	 *
	 * @param currentItem
	 * Update current Item and trigger on item changed event.
	 * This also triggers when the dropdown is bound to a list of data and a new selection is made.
	 * This will not trigger on same data due to angular ng-change.
	 */
	setCurrentItem(currentItem: ICurrentItem): void {
		this.updateCurrentItem(currentItem);
		this.trigger(InteractionTriggerEnum.CURRENT_ITEM_CHANGED);
		this.frameworkService.triggerExpressions();
	}

	private updateCurrentItem(newCurrentItem: ICurrentItem): void {
		this.currentItem = newCurrentItem;
		angular.extend(this.getContext(), newCurrentItem);
		const listResult = this.getContext().ListResult;
		if (listResult.length > 0) {
			this.currentItemIndex = listResult.findIndex((item) => {
				return item === newCurrentItem;
			});
			this.previousLength = angular.copy(listResult.length);
		}
		this.setPseudoElemDisplayText();
	}

	private onListUpdated(): void {
		const firstItem = this.getContext().ListResult[0];
		if (firstItem) {
			this.setCurrentItem(firstItem);
		} else {
			this.setPseudoElemDisplayText("");
		}
		this.trigger(InteractionTriggerEnum.CURRENT_ITEM_CHANGED);
	}

	setBindableValue(bindableReference: string, value: any): void {
		super.setBindableValue(bindableReference, value);
		if (bindableReference === SettingKeys.DISPLAY_VALUE &&
			this.currentItem && this.databearer.displayValue.sourceKey
		) {
			this.setPseudoElemDisplayText();
		}
	}

	private setPseudoElemDisplayText(
		text: string = this.currentItem[this.databearer.displayValue.sourceKey]
	): void {
		const dropdown = this.getSoHoDropdown();
		if (dropdown) {
			dropdown.pseudoElem.find("span").text(text);
		}
	}

	private getSoHoDropdown(): ISohoDropdown {
		return this.element.find("#" + this.getId() + "select").data("dropdown") as ISohoDropdown;
	}

	loadInteraction(interaction: IInteraction): ng.IPromise<any | any[]> {
		let interactionPromise: ng.IPromise<any | any[]>;
		switch (interaction.targetActionName) {
			case InteractionActionEnum.LIST:
				interactionPromise = super.loadInteraction(interaction);
				interactionPromise.then((listResult) => {
					this.getContext().ListResult = cloneDeep(listResult);
					this.trigger(InteractionTriggerEnum.ON_LOAD);
					this.setSelection();
				});
				break;
			case InteractionActionEnum.SET_CURRENT_ITEM:
				interactionPromise = this.executeSetSelectedItemAction(interaction.getParameters());
				interactionPromise.then((newValue) => {
					if (newValue) {
						this.setCurrentItem(newValue);
					} else {
						this.onListUpdated();
					}
				});
				break;
			default:
				interactionPromise = super.loadInteraction(interaction);
				break;
		}
		return interactionPromise;
	}

	private setSelection(): void {
		if (!this.currentItem || !this.keepSelected) {
			this.onListUpdated();
		} else {
			this.moveIndex();
		}
	}

	// needed to maintain right index when adding or deleting items in the dropdown
	private moveIndex(): void {
		const listResult = this.getContext().ListResult;
		const index = ComponentsHelper.findItemIndexById(listResult, this.currentItem.id);
		if (isUndefined(index)) {
			this.setErrorTooltip("Selection failed, Item ID is not unique.");
		} else {
			this.setErrorTooltip();
		}

		if (isNumber(index) && index !== -1) {
			this.currentItemIndex = index;
			this.currentItem = listResult[this.currentItemIndex];
		} else if (index) { //if item id does not exists, try to find correct index
			if (this.previousLength < listResult.length) {
				//Item added.
				this.currentItemIndex = ComponentsHelper.incrementIndex(
					this.currentItemIndex,
					this.getContext(),
					this.currentItem,
					this.getDatasource()
				);
				this.currentItem = listResult[this.currentItemIndex];
			} else if (this.previousLength > listResult.length) {
				//item deleted.
				this.onListUpdated();
			} else {
				//Toggle selected
				this.currentItem = listResult[this.currentItemIndex];
			}
		} else {
			this.onListUpdated();
		}
		this.setPseudoElemDisplayText();
	}

	private executeSetSelectedItemAction(parameters: IInteractionParameter[]): ng.IPromise<any> {
		this.setErrorTooltip();
		const interactionDeferred = this.frameworkService.getNewDeferred<any | any[]>();
		const context = this.getContext();
		if (context.ListResult) {
			let possibleItems = context.ListResult;
			parameters.forEach((parameter) => {
				const valueToFind = this.frameworkService.readValueSource(parameter.valueSource, context);
				possibleItems = possibleItems.filter((listItem) => {
					const actualValue = listItem[parameter.targetKey];
					return Helper.trimIfPossible(actualValue) === Helper.trimIfPossible(valueToFind); // Trim to fix MI problem
				});
			});
			if (possibleItems.length === 1) {
				interactionDeferred.resolve(possibleItems[0]);
				this.removeEmptyValue();
			} else if (possibleItems.length > 1) {
				this.setErrorTooltip("Unable to select: Multiple items match");
				this.setEmptyValue();
			} else {
				this.setErrorTooltip("Unable to select: No items match");
				this.setEmptyValue();
			}
		} else {
			this.setErrorTooltip("Unable to select: No items to select");
		}
		interactionDeferred.resolve();
		return interactionDeferred.promise;
	}

	private setEmptyValue(): void {
		const listResult = this.getContext().ListResult;
		if (listResult[0] !== this.emptyValue) {
			listResult.unshift(this.emptyValue);
		}
		this.clear();
	}

	private removeEmptyValue(): void {
		const listResult = this.getContext().ListResult;
		if (listResult[0] === this.emptyValue) {
			listResult.shift();
		}
	}

	static directive = [() => {
		return {
			template: require("./dropdown.html"),
			replace: true
		};
	}];
}
