import { FeatureIdEnum } from "ng2/common/constants/common.constant";
import { FeatureSwitchService } from "../../services/feature-switch-service";

export abstract class FeatureSwitchCtrl {

	constructor(
		private featureService: FeatureSwitchService
	) { }

	public isFeatureTranslationEnabled(): boolean {
		/*const isEnabled = this.featureService
			.isFeatureEnabled(FeatureIdEnum.TranslationService);*/
		return true; //isEnabled;
	}

	public isFeatureCatalogEnabled(): boolean {
		const isEnabled =
			this.featureService.isFeatureEnabled(FeatureIdEnum.ShowFeatureCatalog);
		return isEnabled;
	}
}
