import type { IBuildLinkParameter, IBuildLinkResponse } from "../component-settings/build-link-dialog/build-link-dialog";

import { ComponentClassNames, SettingKeys } from "ng2/common/constants/common.constant";

import { BindingTypeEnum } from "ng2/common/enums/binding-type.enum";

import type { IJsonHyperlink } from "common/model/component/hyperlink.model";
import type { IComponentFieldSetting } from "ng2/common/model/components/settings/component-field-setting.model";
import type { IJsonValueSource } from "ng2/common/model/databearer/json-binding.model";

import type { IEditorComponent } from "common/interface/ieditor-component";
import { Hyperlink } from "components/hyperlink/hyperlink";
import { EditorComponentCtrl, IEditorComponentScope } from "editor/components/editor-component";
import type { IEditorFlexbox } from "editor/components/editor-flexbox";

import { DialogButtonType, IDialogOptions, IDialogResult } from "common/services/dialog-service";

export interface IEditorHyperlink extends IEditorComponent, Hyperlink {
	parent: IEditorFlexbox;
	// Overrides to make dual extend work
	getScope(): IEditorComponentScope;
	getDatabearer(): IJsonHyperlink;
}

export class EditorHyperlinkCtrl extends EditorComponentCtrl {
	static add(m: ng.IModule): void {
		m.controller(
			ComponentClassNames.EDITOR_COMPONENT_PREFIX + Hyperlink.name,
			EditorHyperlinkCtrl
		);
	}

	protected component: IEditorHyperlink;

	openDialog(settingKey: string): ng.IPromise<void> {
		super.openDialog(settingKey);
		const deferred = this.editorService.getNewDeferred<void>();
		if (settingKey === SettingKeys.LINK_VALUE) {
			const options: IDialogOptions<IBuildLinkParameter> = {
				title: "Hyperlink - Build link",
				parameter: {
					link: this.component.link,
					sources: this.component.sources,
					component: this.component
				},
				template: require("../component-settings/build-link-dialog/build-link-dialog.html")
			};

			this.dialogService.show(options).then((result: IDialogResult<IBuildLinkResponse>) => {
				const okButton = result.button === DialogButtonType.Ok;
				if (okButton) {
					this.component.link = result.value.link;
					this.component.sources = result.value.sources;
					this.component.setProperty(SettingKeys.LINK_VALUE, result.value.link);
					this.component.setProperty("sources", result.value.sources);
					this.component.renderURL();
					this.registerSourceBindings();
					deferred.resolve();
				}
			});
		}
		return deferred.promise;
	}

	setSetting(settingMeta: IComponentFieldSetting, newSettingValue: string): void {
		super.setSetting(settingMeta, newSettingValue);
		if (settingMeta.ref === SettingKeys.LINK_VALUE) {
			this.component.renderURL();
		}
	}

	private registerSourceBindings(): void {
		this.component.sources.forEach((source) => {
			const valueSourceObject: IJsonValueSource = {
				Type: BindingTypeEnum.BINDING,
				sourceKey: source.value,
				sourceName: source.type
			};
			this.bindingManagementService.registerBinding(this.component, source.alias, valueSourceObject);
		});
	}
}
