import angular from "angular";

import { DEFAULT_BOUNDARY, FlexSizeTypeEnum, InjectNames, ProjectLimit, SaveFlagsEnum } from "ng2/common/constants/common.constant";
import { ValueTypeEnum } from "ng2/common/enums/value-type.enum";
import { IJsonComponent } from "ng2/common/model/databearer/components/json-component.model";
import { IJsonCompositeComponent } from "ng2/common/model/databearer/components/json-composite-component.model";
import { IJsonFlexbox } from "ng2/common/model/databearer/components/json-flexbox.model";
import { ILanguage } from "ng2/common/model/language.model";
import { Helper } from "../../../common/helper";
import { IJsonTabs } from "../../../common/model/component/tabs.model";
import {
	DialogButtonType, DialogService, IDialogOptions, IDialogResult,
	IMessageDialogOptions, StandardDialogButtons
} from "../../../common/services/dialog-service";
import { Defines } from "../../../components/defines";
import { TranslationManagementService } from "../../../components/services/translation-management-service";
import { Tabs } from "../../../components/tabs/tabs";
import { DesignerService } from "../../services/designer-service";
import { EditorService } from "./editor-service";

export class CompositeComponentManagementService {
	private idCounter: number = 0;
	private isActive: boolean;
	private compositeComponentDialogPromise: ng.IPromise<IDialogResult<IJsonCompositeComponent>>;

	static $inject = [
		InjectNames.EDITOR_SERVICE,
		InjectNames.DESIGNER_SERVICE,
		InjectNames.DIALOG_SERVICE,
		InjectNames.TRANSLATION_MANAGEMENT_SERVICE
	];

	constructor(
		private editorService: EditorService,
		private designerService: DesignerService,
		private dialogService: DialogService,
		private translationManagementService: TranslationManagementService
	) {
	}

	createNewViaWizard(component: IJsonComponent): void {
		component = angular.copy(component);
		if (Helper.exceedAllowed(
			ProjectLimit.MAX_COMPOSITE_COMPONENTS,
			this.editorService.getCurrentProject().getCompositeComponents())
		) {
			this.dialogService.showError(
				"Create Composite Component", ProjectLimit.COMPOSITE_COMPONENT_ERROR_MESSAGE);
		} else {
			this.startCompositeComponentWizard(component)
				.then((dialogResult: IDialogResult<IJsonCompositeComponent>) => {
					if (dialogResult.button === DialogButtonType.Ok) {
						const compositeComponent = dialogResult.value;
						const project = this.editorService.getCurrentProject();
						this.designerService.requestInfo().then(() => {
							const id = this.generateId("compositeComponent");
							compositeComponent.manifest.id = id;
							compositeComponent.manifest.creationDate = Date.now();
							compositeComponent.manifest.author = this.designerService.getInfo().getUserName();
							compositeComponent.manifest.authorDisplayName =
								this.designerService.getInfo().getUserDisplayName();

							let includeDatasources = false;
							if (compositeComponent.datasources) {
								includeDatasources = true;
							}

							// Remove posible static width setting. The composit should adapt to the layout it is put into.
							if (Defines.allComponents[compositeComponent.component.typedef].isLayoutComponent) {
								const component = compositeComponent.component as IJsonFlexbox;
								component.boundarySettings[DEFAULT_BOUNDARY.id].sizeType = FlexSizeTypeEnum.FLEX;
								component.boundarySettings[DEFAULT_BOUNDARY.id].size = 1;
							}

							// Clear datasoruces if not included
							this.editorService.forEachComponent(compositeComponent.component, (component) => {
								if (!includeDatasources) {
									component.datasource = null;
								}
							});

							project.addCompositeComponent(compositeComponent);
							/* tslint:disable:no-bitwise */
							this.editorService.saveCurrent(SaveFlagsEnum.COMPOSITE_COMPONENTS | SaveFlagsEnum.MANIFEST);
							/* tslint:enable:no-bitwise */
							this.editorService.compositeComponentsChangedEvent.raise(project.getCompositeComponents());
						});
					}
				});
		}
	}

	manageCompositeComponents(): void {
		const options: IDialogOptions<any> = {
			title: "Project Composite Components",
			template: require("../component-settings/manage-dialogs/composites-dialog.html"),
			cssClass: "composites-manage-dialog",
			style: "min-width: 1000px; max-width: 1125px;",
		};

		this.dialogService.show(options).then(() => {
			this.editorService.compositeComponentsChangedEvent
				.raise(this.editorService.getCurrentProject().getCompositeComponents());
			/* tslint:disable:no-bitwise */
			this.editorService.saveCurrent(SaveFlagsEnum.COMPOSITE_COMPONENTS | SaveFlagsEnum.MANIFEST);
			/* tslint:enable:no-bitwise */
		});
	}

	startCompositeComponentWizard(
		component: IJsonComponent
	): ng.IPromise<IDialogResult<IJsonCompositeComponent>> {
		if (!this.isActive) {
			this.isActive = true;
			const options: IDialogOptions<IJsonComponent> = {
				title: "Create Composite Component",
				parameter: component,
				template: "<eir-wizard ng-controller='eirCompositeComponentWizardCtrl as wizardCtrl'></eir-wizard>",
				cssClass: "wizard-dialog",
				style: "min-width: 450px; min-height: 500px; max-width: 480px;",
				id: "composite-component-wizard",
				draggable: true,
			};

			this.compositeComponentDialogPromise =
				this.dialogService.show<IJsonComponent, IJsonCompositeComponent>(options)
					.finally(() => {
						this.isActive = false;
					});
		}
		return this.compositeComponentDialogPromise;
	}

	removeViaDialog(compositeId: string): void {
		const currentProject = this.editorService.getCurrentProject();
		const compositeComponent = currentProject.getCompositeComponent(compositeId);
		const confirmDialogOptions: IMessageDialogOptions = {
			title: "Really delete?",
			message: "Do you really want to delete composite component '" + compositeComponent.manifest.displayName + "'?",
			standardButtons: StandardDialogButtons.YesNo
		};
		this.dialogService.showMessage(confirmDialogOptions).then((result: IDialogResult<void>) => {
			if (result.button === DialogButtonType.Yes) {
				currentProject.removeCompositeComponent(compositeId);
				this.editorService.saveCurrent(SaveFlagsEnum.COMPOSITE_COMPONENTS);
				this.editorService.compositeComponentsChangedEvent.raise(currentProject.getCompositeComponents());
			}
		});
	}

	private generateId(compositeComponent: string): string {
		const newId = compositeComponent + this.idCounter++;
		const compositeComponents = this.editorService.getCurrentProject().getCompositeComponents();
		for (const i in compositeComponents) {
			if (compositeComponents[i].manifest.id === newId) {
				return this.generateId(compositeComponent);
			}
		}

		return newId;
	}

	private getTranslation(languages: ILanguage[]): string {
		let value: string;
		const cut = 2;
		for (let i = 0; i < languages.length; i++) {
			const locale = languages[i].locale.substring(0, cut);
			const currentLocale = this.translationManagementService.getSelectedLocale().substring(0, cut);
			if (locale === currentLocale) {
				value = languages[i].value;
				break;
			}
		}
		return value ? value : languages[0].value; //default english
	}

	private addLanguagesFromComposite(languages: ILanguage[]): void {
		languages.forEach((language) => {
			if (!this.translationManagementService.projectHasLanguage(language.locale)) {
				this.translationManagementService.addLanguage({ key: language.locale, value: "" });
			}
		});
	}

	setCurrentValuesToCurrentLanguage(composite: IJsonCompositeComponent): void {
		if (composite.translations) {
			this.editorService.forEachComponent(composite.component, (childComponent) => {
				this.editorService.forEachBindable(childComponent, (field) => {
					if (field.valueType === ValueTypeEnum.LOCALIZED_STRING) {
						const componentTranslations = composite.translations[childComponent.id];

						componentTranslations.forEach((translation) => {
							if (field.ref === translation.property) {
								this.addLanguagesFromComposite(translation.translations);
								(childComponent as any)[field.ref].value = this.getTranslation(translation.translations);
							}
						});
					}
				});
				switch (childComponent.typedef) {
					case Tabs.name:
						const componentTranslations = composite.translations[childComponent.id];
						(childComponent as IJsonTabs).componentTabs.forEach((tab) => {
							componentTranslations.forEach((componentTranslation) => {
								if (tab.id === componentTranslation.property) {
									tab.name = this.getTranslation(componentTranslation.translations);
								}
							});
						});
						break;
					default:
					// Add new cases
				}
			});
		}
	}

	static add(ngModule: ng.IModule): void {
		ngModule.service(InjectNames.COMPOSITE_COMPONENT_MANAGEMENT_SERVICE, CompositeComponentManagementService);
	}
}
