import { ISearchFieldOptions } from "../../model/sohoxi/sohoxi-searchfield.model";
import { Utils } from "../utils";

/**
 * Searchfield Directive.
 *
 * Suggests values in given data (source) that matches the input.
 *
 * Example:
 * scope["options"]: ISearchfieldOptions = { "source": ["Alpha", "Bravo"], "template": "#template" }
 * <input xi-searchfield="options" />
 */
export class SearchFieldDirective implements ng.IDirective {
	private static readonly DIRECTIVE_NAME = "xiSearchfield";
	public restrict = "A";
	public scope = false;

	public link(
		scope: ng.IScope,
		element: ng.IAugmentedJQuery,
		attributes: ng.IAttributes
	): void {
		const options = attributes[SearchFieldDirective.DIRECTIVE_NAME];
		const unbindWatcher = scope.$watch(
			options,
			(newOptions: ISearchFieldOptions) => {
				if (newOptions) {
					// Set the data-autocomplete attribute to source (external in options object)
					element.searchfield(newOptions);
				} else {
					element.searchfield();
				}
			}
		);
		// Tear down
		element.on("$destroy", () => {
			const searchFieldData = Utils.getData(element, "searchfield");
			if (searchFieldData) {
				searchFieldData.destroy();
			}
			unbindWatcher();
		});
	}

	public static add(m: ng.IModule): void {
		m.directive(SearchFieldDirective.DIRECTIVE_NAME, () => {
			return new SearchFieldDirective();
		});
	}
}
