import angular from "angular";
import { AngularConstants } from "../../angular-constants";

import { IdoProperties, InjectNames } from "ng2/common/constants/common.constant";
import { DatasourceTypeEnum } from "ng2/common/enums/datasource-type.enum";
import {
	IJsonDatasource,
	IJsonGenericDatasource2,
} from "ng2/common/model/databearer/dataservice/json-datasource.model";
import { IDataGridOptions } from "ng2/common/model/sohoxi/sohoxi-datagrid.model";

import { IDialogBaseScope, KeyCodeEnum } from "../../defines";
import { IErrorResponse } from "../../error-response";

import { JsonHelper } from "ng2/common/helpers/json.helper";
import { DatasourceService } from "../../../components/services/datasource-service";
import { Helper } from "../../helper";
import { DialogService, IDialog } from "../../services/dialog-service";

export enum TestDataserviceDialogType {
	UNFORMATTED = "UNFORMATTED",
	FORMATTED = "FORMATTED",
}
export type TTestDataserviceDialogType
	= typeof TestDataserviceDialogType.FORMATTED
	| typeof TestDataserviceDialogType.UNFORMATTED;

export interface ITestParameter {
	id: string;
	key: string;
	value: string;
	mandatory: boolean;
}

export interface ITestDataserviceParameters {
	type: TTestDataserviceDialogType;
	dataservice: IJsonDatasource;
}

export class TestDataserviceDialogCtrl {
	public dataservice: IJsonDatasource;
	public datagridOptions: IDataGridOptions<any>;
	public parameters: ITestParameter[]; // The inputparams in the dialog
	public responseData: string = "";
	public type: TTestDataserviceDialogType;
	public fetching: boolean = false;

	private dialog: IDialog<IErrorResponse>;
	private testDialogTypes = TestDataserviceDialogType;

	static $inject = [
		AngularConstants.SCOPE,
		AngularConstants.TIMEOUT,
		AngularConstants.Q,
		InjectNames.DATASOURCE_SERVICE,
		InjectNames.DIALOG_SERVICE,
	];

	constructor(
		private scope: IDialogBaseScope<any>,
		private $timeout: ng.ITimeoutService,
		private $q: ng.IQService,
		private datasourceService: DatasourceService,
		private dialogService: DialogService,
	) {
		const dialog = scope.eirDialog;
		this.dialog = dialog;

		if (!dialog.parameter.dataservice) {
			// This is to make sure that the errordialog shows up AFTER the normal dialog.
			this.$timeout(() => {
				this.handleError(new Error("Could not find the data service")).finally(() => this.close());
			});
			return;
		}

		this.dataservice = angular.copy(dialog.parameter.dataservice);
		this.type = dialog.parameter.type;
		this.datagridOptions = this.initDatagridOptions();
		this.initGenericState(this.dataservice as IJsonGenericDatasource2);
	}

	public runApi(): void {
		if (!this.fetching) {
			this.fetching = true;
			const request: ng.IPromise<any> = this.runGenericApi(this.dataservice as IJsonGenericDatasource2);

			request
				// Need to bind this as the unit tests doesnt like the scope otherwise
				.catch(this.handleError.bind(this))
				.finally(() => this.fetching = false);
		}
	}

	public copyToClipboard(): void {
		angular.element("#testDataserviceTextareaField").select();
		try {
			const successful = document.execCommand("copy");
			if (!successful) {
				throw new Error("Was not able to copy");
			}
		} catch (error) {
			this.handleError(error);
		}
	}

	public close(): void {
		this.dialog.close();
	}

	public onKeyPress($event: JQueryEventObject): void {
		if ($event.which === KeyCodeEnum.ENTER) {
			this.runApi();
		}
	}

	private initGenericState(dataservice: IJsonGenericDatasource2): void {
		if (dataservice.inputParameters) {
			this.parameters = dataservice.inputParameters.map((param) => {
				return {
					id: param.id,
					key: param.key,
					value: "",
					mandatory: param.required,
				};
			});
		}
	}

	private runGenericApi(dataservice: IJsonGenericDatasource2): ng.IPromise<any> {
		let paramForInput = [];
		if (this.parameters) {
			paramForInput = this.parameters.reduce((acc: any, param) => {
				acc[param.id] = param.value;
				return acc;
			}, {});
		}

		switch (this.type) {
			case TestDataserviceDialogType.UNFORMATTED:
				return this.getUnFormattedData(dataservice, paramForInput);
			case TestDataserviceDialogType.FORMATTED:
				return this.getFormattedData(dataservice, paramForInput);
			default:
				return this.$q.reject(new Error("Request format is not supported"));
		}
	}

	private getFormattedData(dataservice: IJsonGenericDatasource2, parameters: any): ng.IPromise<any> {

		const formatResults = (result: any) => {
			// As we are using getDetailData, this always returns result as a object?
			if (dataservice.outputParameters.length === 0) {
				throw Error("No output parameter(s) specified.");
			}

			const formattedResult = Object.keys(result).map((key) => {
				const maybeNewKey = dataservice.outputParameters.find((outputParam) => outputParam.id === key);
				let newKey = key;
				if (maybeNewKey) {
					newKey = !!maybeNewKey.alias ? maybeNewKey.alias : maybeNewKey.key;
				}
				return { name: newKey, value: result[key] };
			});
			return formattedResult;
		};

		const setResults = (result: any) => {
			this.fillResponseData(result);
			this.datagridOptions.dataset = result;
			return result;
		};

		return this.datasourceService
			.parseDatasourceToTS(dataservice)
			.getDetailData(parameters)
			.then(result => formatResults(result))
			.then(setResults);
	}

	private getUnFormattedData(dataservice: IJsonGenericDatasource2, parameters: any): ng.IPromise<any> {
		const setResults = (result: any) => {
			if (dataservice.typedef === DatasourceTypeEnum.IDO) {
				const hasRowPointer = dataservice.outputParameters.find((item => item.key === IdoProperties.ROW_POINTER));
				if (!hasRowPointer) {
					for (let i = 0; i < result.length; i++) {
						delete result[i][IdoProperties.ROW_POINTER];
					}
				}
			}
			this.fillResponseData(result);
		};

		return this.datasourceService
			.parseDatasourceToTS(dataservice)
			.getNonParsedData(parameters)
			.then(setResults);
	}

	private fillResponseData(result: any): void {
		this.$timeout(() => {
			this.responseData = JsonHelper.prettify(result);
		});
	}

	private handleError(error: Error): ng.IPromise<any> {
		this.responseData = Helper.cleanTemplateLiteral(error.message);
		console.error(error.message);
		return this.dialogService.showError("Error", error.message);
	}

	private initDatagridOptions(): IDataGridOptions<any> {
		const columns = [
			{
				id: "name",
				name: "Name",
				field: "name",
				resizable: true,
				sortable: true,
				formatter: Soho.Formatters.Ellipsis
			},
			{
				id: "description",
				name: "Description",
				field: "value",
				resizable: true,
				sortable: true,
				formatter: Soho.Formatters.Ellipsis
			},
		];
		return {
			columns: columns,
			dataset: [{}],
		};
	}

	static add(m: ng.IModule): void {
		m.controller("eirTestDataserviceCtrl", TestDataserviceDialogCtrl);
	}
}
