import { Config, MGPlatform } from "ng2/common/constants/common.constant";

import { Helper } from "common/helper";

enum DebugState {
	ENABLED = "1",
	DISABLED = "0",
}

export class Debugger {
	static readonly STORAGE_KEY = "debug";

	static $inject = [
		"$routeParams"
	]
	constructor(
		private $routeParams: ng.route.IRouteParamsService
	) {
		console.info("Hello World");
	}

	static isEnabled(): boolean {
		return localStorage.getItem(Debugger.STORAGE_KEY) === DebugState.ENABLED;
	}

	static enable(): void {
		localStorage.setItem(Debugger.STORAGE_KEY, DebugState.ENABLED);
	}

	static disable(): void {
		localStorage.setItem(Debugger.STORAGE_KEY, DebugState.DISABLED);
	}

	static initialize(): void {
		const url = Config.MG_PLATFORM === MGPlatform.WEB || DEV_SERVER ?
			window.parent.location.search.substring(1) :
			window.location.search.substring(1);

		const query = Helper.parseQueryString(url);
		if (query.debug) {
			if (query.debug === "true") {
				Debugger.enable();
			} else {
				Debugger.disable();
			}
		} else {
			Debugger.disable();
		}

		if (Debugger.isEnabled()) {
			console.warn("AppBuilder is running in debug mode. This may impact performance.");
		}

		(window as Window).Debugger = Debugger;
	}

	static log(classObject: any, message: any): void {
		const originStyle = "color: darkred; font-weight: bold;";
		const messageStyle = "color: black; font-weight: bold;";

		if (Debugger.isEnabled() && Config.IS_DEV) {
			/* tslint:disable:no-console */
			console.log(`%c[${classObject.constructor.name}] %c${message}`, originStyle, messageStyle);
			/* tslint:enable:no-console */
		}
	}
}
