import { AngularConstants } from "../../angular-constants";

import { IMap } from "ng2/common/model/imap.model";
import { FilterPopupmenuCtrl } from "./filter-popupmenu-ctrl";

export class FilterPopupmenuComponent implements ng.IComponentOptions {
	public controller: any;
	public controllerAs: string;
	public template: string;
	public bindings: IMap<string>;

	constructor() {
		this.bindings = {
			filterOptions: "<",
			filterValues: "=",
			localPrefix: "<"
		};

		this.controller = [
			AngularConstants.FILTER,
			FilterPopupmenuCtrl
		];
		this.controllerAs = "ctrl";
		this.template = require("./filter-popupmenu-template.html");
	}

	static add(m: ng.IModule): void {
		m.component("eirFilterPopupmenuComponent", new FilterPopupmenuComponent());
	}
}
