import angular from "angular";

import { InjectNames } from "ng2/common/constants/common.constant";
import { IJsonComponent } from "ng2/common/model/databearer/components/json-component.model";
import { IMap } from "ng2/common/model/imap.model";
import { IJsonLanguageComponent } from "ng2/common/model/language.model";
import { DesignerMashup } from "ng2/common/project/designer-mashup";
import { Interaction } from "ng2/components/interaction/interaction";

import { InstanceEvent } from "../../../common/instance-event";

export interface IUndoAction {
	stateRootComponent: IJsonComponent;
	stateInteractions: Interaction[];
	stateTranslations: IMap<IJsonLanguageComponent[]>;
}

export class UndoService {
	public undoActionEvent = new InstanceEvent<IUndoAction>();
	private undoStack: IUndoAction[];
	private currentIndex: number;
	private maxUndoCount = 11;

	constructor() {
		this.initialize();
	}

	private initialize(): void {
		this.undoStack = [];
		this.currentIndex = 0;
	}

	public pop(): void {
		this.undoStack.pop();
	}

	public getCurrentUndo(): IUndoAction {
		return this.undoStack[this.currentIndex - 1];
	}

	public undoAction(): void {
		if (this.currentIndex > 0) {
			this.currentIndex -= 1;
		}

		// fire undo event
		this.undoActionEvent.raise(angular.copy(this.undoStack[this.currentIndex]));
	}

	public redoAction(): void {
		if (this.currentIndex < this.undoStack.length - 1) {
			this.currentIndex += 1;
		}

		// fire redo event
		this.undoActionEvent.raise(angular.copy(this.undoStack[this.currentIndex]));
	}

	public pushUndoAction(project: DesignerMashup): void {
		const undoAction = {
			stateRootComponent: project.rootComponent,
			stateInteractions: project.getInteractions(),
			stateTranslations: project.getTranslations()
		};
		this.undoStack.splice(this.currentIndex + 1, this.undoStack.length - 1, angular.copy(undoAction));

		// if stacklength > max -> remove first undo action
		if (this.undoStack.length > this.maxUndoCount) {
			this.undoStack.shift();
		}

		// always point to the end of the stack
		this.currentIndex = this.undoStack.length - 1;
	}

	public clearActions(): void {
		this.initialize();
	}

	public isUndoAvailable(): boolean {
		return this.currentIndex > 0;
	}

	public isRedoAvailable(): boolean {
		return this.currentIndex < this.undoStack.length - 1;
	}

	static add(ngModule: ng.IModule): void {
		ngModule.service(InjectNames.UNDO_SERVICE, UndoService);
	}
}
