import { IDatasource } from "common/model/datasource/datasource.model";
import { IContext } from "ng2/common/model/components/base/context.model";
import { IComponentFieldSetting } from "ng2/common/model/components/settings/component-field-setting.model";
import { IJsonComponent } from "ng2/common/model/databearer/components/json-component.model";
import { IJsonBindableField, IJsonValueSource } from "ng2/common/model/databearer/json-binding.model";
import { IInteraction } from "ng2/common/model/interaction/interaction.model";
import { IComponentScope } from "./interface/icomponent-scope";
import { ILayoutComponent } from "./interface/ilayout-component";
import { ISizeObject } from "./ISizeObject";
import { ISourceItem } from "./model/component/hyperlink.model";

export interface IComponent {
	cssClasses: string;
	isVisible: boolean;
	isReadonly: boolean;
	element: ng.IAugmentedJQuery;
	parent: ILayoutComponent;
	isGenerated: boolean;
	//copy-paste stuff
	canCopy: boolean;
	canCut: boolean;
	canDelete(): boolean;
	generatedId: string;
	errorTooltip: string;
	datasource: IDatasource;
	// "is" component methods/vars
	isLoading: boolean;
	isLayoutComponent(): boolean;
	isTranslatableComponent(): boolean;
	// Getters for databearer
	getDatabearer(): IJsonComponent;
	getDatasource(): IDatasource;
	getDisplayName(): string;
	getId(): string;
	getTypedef(): string;
	is(classs: any): boolean;
	// Void methods
	calcNewHeight(): void;
	calcNewWidth(): void;
	calcNewSizes(): void;
	forEachChild(fn: (child: IComponent) => void, doGenerated: boolean): void;
	forEachBindable(fn: (field: IJsonBindableField, curr: IJsonValueSource) => void): void;
	forEachFieldSetting(fn: (field: IComponentFieldSetting) => void): void;
	forEachParent(fn: (parent: ILayoutComponent) => void): void;
	initializeBindings(): void;
	loadInteraction(interaction: IInteraction): ng.IPromise<any | any[]>;
	clear(): void;
	newBoundaryEntered(boundaryId: string): void;
	updateClasses(): void;
	updateClassesAndStyle(): void;
	updateShowError(): void;
	updateVisibility(): void;
	resetLayout(): void;
	resetContext(): void;
	// Getters
	getBindableValue(bindableReference: string): any;
	getBoundarySetting<T>(settingId: string, boundaryId?: string, noTraverse?: boolean): T;
	getContext(): IContext;
	getGeneratingParent(): ILayoutComponent;
	getPredictedSize(): ISizeObject;
	getScope(): IComponentScope;
	getSohoXiApi?(): any;
	getValue(key: string): any;
	// Setters
	setBindableValue(bindableReference: string, value: any): void;
	setGenerated(id: string): void;
	setErrorTooltip(errorMessage?: string): void;
	// on value updated
	onUpdateValue(): void;
	// build link
	buildLink(link: string, sources: ISourceItem[]): string;
}
