import angular from "angular";

import {
	CanvasModeEnum, CssConstantKeys, CssConstantValues,
	EventTypes, InjectNames, InsertEventTypes
} from "ng2/common/constants/common.constant";
import { IJsonComponent } from "ng2/common/model/databearer/components/json-component.model";
import { IMap } from "ng2/common/model/imap.model";
import { AngularConstants } from "../../../../common/angular-constants";
import { IJQueryDragEventObject } from "../../../../common/defines";
import { Helper } from "../../../../common/helper";
import { Defines, IComponentListEntry } from "../../../../components/defines";
import { ComponentsHelper } from "../../../../components/helper";
import { TranslationManagementService } from "../../../../components/services/translation-management-service";
import { ComponentSettingsCtrl } from "../../component-settings/component-settings";
import { ISettingsClassInstance } from "../../defines";
import { EditorHelper } from "../../helper";
import { EditorService } from "../../services/editor-service";
import { InsertComponentService } from "../../services/insert-component-service";

export class NewComponentCtrl {
	public components: IMap<IComponentListEntry>;
	public selectedNewComponent: IJsonComponent;

	private unsubscribers: Function[] = [];
	private bodyElement: ng.IAugmentedJQuery;
	private designerElement: ng.IAugmentedJQuery;

	public static $inject = [
		AngularConstants.SCOPE,
		AngularConstants.TIMEOUT,
		InjectNames.EDITOR_SERVICE,
		InjectNames.INSERT_COMPONENT_SERVICE,
		InjectNames.TRANSLATION_MANAGEMENT_SERVICE
	];

	public constructor(
		$scope: ng.IScope,
		$timeout: ng.ITimeoutService,
		private editorService: EditorService,
		private insertComponentService: InsertComponentService,
		private translationManagementService: TranslationManagementService
	) {
		this.bodyElement = angular.element("body");
		this.designerElement = angular.element("#designer");
		this.components = Defines.allComponents;

		this.unsubscribers.push(editorService.canvasModeChangedEvent.on((canvasMode) => {
			if (canvasMode !== CanvasModeEnum.INSERT && this.selectedNewComponent) {
				this.insertComponentService.cancelInserting();
			}
		}));

		// Timeout needed to wait for ng-repeat, alternative is to create new controller for registering startdragevent
		$timeout(() => {
			$(".newComponent").on(EventTypes.DRAGSTART, (event: IJQueryDragEventObject) => {
				this.selectDrag(event);
			});
		});

		const bodyClickListener = this.cancel.bind(this);
		this.designerElement.on(EventTypes.CLICK, bodyClickListener);

		$scope.$on(AngularConstants.DESTROY, () => {
			this.designerElement.off(EventTypes.CLICK, bodyClickListener);
			this.cancel();
			this.unsubscribers.forEach((unsubscriber) => {
				unsubscriber();
			});
		});
	}

	private cancel(): void {
		if (this.selectedNewComponent) {
			this.insertComponentService.cancelInserting();
		}
	}

	public selectDrag(event: IJQueryDragEventObject): void {
		const target = $(event.target);
		const className = target.data("componentClass");
		if (Helper.isSafari() || Helper.isEdge()) {
			$(".componentIconOverlay").css(CssConstantKeys.CURSOR, "no-drop");
		}
		this.select(className);
	}

	public selectClick(className: string): void {
		const insertComponent = this.insertComponentService.getInsertComponent();
		if (insertComponent && insertComponent.typedef === className) {
			// Deselect
			this.insertComponentService.cancelInserting();
			this.resetToSelectionMode();
		} else {
			this.bodyElement.css(CssConstantKeys.CURSOR, CssConstantValues.CURSOR_NODROP);
			this.select(className);
		}
	}

	private resetToSelectionMode(): void {
		this.editorService.setCanvasMode(CanvasModeEnum.SELECTION);
		delete this.selectedNewComponent;
		this.bodyElement.css(CssConstantKeys.CURSOR, CssConstantValues.AUTO);
	}

	private select(className: string): void {
		let newComponent: IJsonComponent;
		const newId = this.editorService.generateId(ComponentsHelper.getComponentDisplayName(className));
		const componentSettingsClass: ISettingsClassInstance = EditorHelper.getComponentSettingsClass(className);
		//If component has a settingclass on its own
		if (componentSettingsClass) {
			newComponent = componentSettingsClass.create(newId);
		} else {
			const componentClass = Defines.allComponents[className].class;
			newComponent = ComponentSettingsCtrl.create(newId, componentClass);
		}
		this.selectedNewComponent = newComponent;

		this.insertComponentService.startInserting(newComponent, InsertEventTypes.COPY).then(() => {
			// A component was inserted
			this.translationManagementService.createDefaultLanguageComponents(newComponent);
			this.editorService.saveCanvas();
			this.resetToSelectionMode();
		}).catch(() => {
			// Remain in insert mode when clicking on a new component type
			if (this.selectedNewComponent && this.selectedNewComponent.typedef === newComponent.typedef) {
				this.resetToSelectionMode();
			}
		});
	}
}

export class NewComponentDirective {
	static add(m: ng.IModule): void {
		m.directive("eirNewComponent", () => {
			return {
				scope: true,
				template: require("./new-component.html"),
				controller: NewComponentCtrl,
				controllerAs: "eirNewComponentCtrl"
			};
		});
	}
}
