import { AngularConstants } from "../../../common/angular-constants";

import { ReplaceEnum } from "ng2/common/constants/common.constant";
import { IDialogBaseScope } from "../../../common/defines";
import { DialogButtonType, IDialog, IDialogResult } from "../../../common/services/dialog-service";

export interface IConflictChoice {
	option: ReplaceEnum;
	newDisplayName: string;
}

export interface IConflictDialogParameter {
	message: string;
	oldDisplayName: string;
	newDisplayName: string;
	newDisplayNameOverwrite?: string;
}

export class IdConflictCtrl {
	public ReplaceEnum = ReplaceEnum;
	public message: string;
	public option: ReplaceEnum;
	public newDisplayName: string;
	public newDisplayNameOverwrite: string;
	private dialog: IDialog<IConflictDialogParameter>;

	static $inject = [AngularConstants.SCOPE];

	constructor(private scope: IDialogBaseScope<IConflictDialogParameter>) {
		this.dialog = scope.eirDialog;
		const parameter = this.dialog.parameter;
		this.message = parameter.message;
		this.newDisplayName = parameter.newDisplayName;
		this.newDisplayNameOverwrite = parameter.newDisplayNameOverwrite;
		this.option = ReplaceEnum.OVERWRITE;
	}

	public close(): void {
		this.dialog.close();
	}

	public import(): void {
		const result: IDialogResult<IConflictChoice> = {
			value: {
				option: this.option,
				newDisplayName: this.option === ReplaceEnum.COPYNEW ? this.newDisplayName : this.newDisplayNameOverwrite
			},
			button: DialogButtonType.Ok
		};
		this.dialog.close(result);
	}

	static add(m: ng.IModule): void {
		m.controller("eirIdConflictCtrl", IdConflictCtrl);
	}
}
