import angular, { IPromise } from "angular";
import isUndefined from "lodash-es/isUndefined";

import { AngularConstants } from "../../common/angular-constants";

import { InjectNames, Numbers } from "ng2/common/constants/common.constant";
import { DatasourceTypeEnum } from "ng2/common/enums/datasource-type.enum";
import { HttpVerbEnum } from "ng2/common/enums/http-verb.enum";

import { HttpService } from "../../common/services/http-service";
import { IonApiService } from "../../components/services/ion-api-service";
import { LocalIdoService } from "../../components/services/local-ido-service";

import { HttpAcceptEnum } from "ng2/common/enums/http-accept-enum";
import {
	IHttpHeader, IJsonDatasource, IJsonGenericDatasource2,
	IJsonInlineListDatasource, IJsonLocalIdoDatasource } from "ng2/common/model/databearer/dataservice/json-datasource.model";
import { IMap } from "ng2/common/model/imap.model";
import { IIonApiRequestOptions } from "ng2/common/model/ion-api/ion-api.model";
import { BackwardCompatible } from "ng2/common/project/backward-compatible";
import { ErrorResponse } from "../../common/error-response";

import { IDatasource } from "common/model/datasource/datasource.model";
import { Generic2 } from "../../common/project/datasource/generic2";
import { InlineList } from "../../common/project/datasource/inline-list";
import { LocalIdo } from "../../common/project/datasource/local-ido";
import { Mongoose } from "../../common/project/datasource/mongoose";

export class DatasourceService {
	public cache: ng.ICacheObject;

	public static $inject = [
		AngularConstants.CACHE_FACTORY,
		AngularConstants.Q,
		InjectNames.HTTP_SERVICE,
		InjectNames.ION_API_SERVICE,
		InjectNames.LOCAL_IDO_SERVICE,
	];

	public constructor(
		private $cacheFactory: ng.ICacheFactoryService,
		public $q: ng.IQService,
		private httpService: HttpService,
		private ionApiService: IonApiService,
		private localIdoService: LocalIdoService
	) {
		this.cache = this.$cacheFactory("DatasourceService");
	}

	public getDeferred<T>(): ng.IDeferred<T> {
		return this.$q.defer<T>();
	}

	public localIdoRequest(
		datasource: IJsonLocalIdoDatasource,
		inputParameterValues?: IMap<any>
	): ng.IPromise<any> {

		switch (datasource.method) {

			case HttpVerbEnum.PUT:
				return this.localIdoService.updateIdoCollectionEntry(datasource, inputParameterValues);
			case HttpVerbEnum.POST:
				return this.localIdoService.insertIdoCollectionEntry(datasource, inputParameterValues);
			case HttpVerbEnum.DELETE:
				return this.localIdoService.deleteIdoCollectionEntry(datasource, inputParameterValues);
			case HttpVerbEnum.GET:
			default:
				return this.localIdoService.getIdoCollection(datasource, inputParameterValues);
		}
	}

	public ionApiRequest(
		urlPath: string,
		isBinaryResponse: boolean = false,
		method: HttpVerbEnum = HttpVerbEnum.GET,
		data: string = null,
		headers: IHttpHeader = {}
	): Promise<any> {
		const def: ng.IDeferred<any> = this.$q.defer();

		headers = BackwardCompatible
			.setDataServiceHeader(headers);

		const options: IIonApiRequestOptions = {
			url: urlPath,
			method,
			headers: angular.copy(headers),
		};

		if (isBinaryResponse) {
			options.responseType = "blob";
		} else if (isUndefined(options.headers.Accept)) {
			options.headers.Accept = HttpAcceptEnum.ALL;
		}

		if (method !== HttpVerbEnum.GET) {
			options.data = data;
		}
		this.ionApiService.executeIonApiAsync(options).then(response => {
			this.ionApiResponse(def, response.data, isBinaryResponse);
		}).catch((response: ng.IHttpPromiseCallbackArg<any>) => {
			let error: ErrorResponse;

			if (isBinaryResponse) {
				// Convert binary to text
				const fr = new FileReader();
				fr.readAsText(response.data);
				fr.onloadend = () => {
					let data = fr.result as string;

					try {
						data = JSON.parse(data);
					} catch (e) {
						console.warn(e);
					}

					response.data = data;
					error = this.ionApiResponseError(response);
					def.reject(error);
				};
			} else {
				error = this.ionApiResponseError(response);
				def.reject(error);
			}
		}).catch(error => def.reject(error));

		return def.promise as Promise<any>;
	}

	private ionApiResponseError(response: ng.IHttpPromiseCallbackArg<any>): ErrorResponse {
		let errorMessage: string = "";

		if (response.data && response.data.error && response.data.error.message) {
			// High likely to be error message from ION API
			if (response.data.source) {
				errorMessage += "Source: " + response.data.source + "; ";
			}

			errorMessage += response.data.error.message;
		} else if (Numbers.FOUR_HUNDRED <= response.status && response.status < Numbers.SIX_HUNDRED) {
			errorMessage = response.status + " " + response.statusText;
		} else {
			console.warn("[Datasource service] Unknown ionApiRequest error", response);
			errorMessage += "[Datasource service] Unknown ionApiRequest error";
		}

		const error: ErrorResponse = new ErrorResponse(errorMessage, response.status);
		return error;
	}

	private ionApiResponse(def: ng.IDeferred<any>, data: any, isBinary: boolean = false): void {
		if (isBinary) {
			// Reading response as dataURL to prevent tainting of canvas for html2canvas-library
			const fr = new FileReader();
			fr.readAsDataURL(data);
			fr.onloadend = () => {
				def.resolve(fr.result);
			};
		} else {
			def.resolve(data);
		}
	}

	public parseDatasourceToTS(datasourceDatabearer: IJsonDatasource): IDatasource {
		// Only accept generic version 2
		const version: number = 2;
		const isCorrectVersion = datasourceDatabearer.version === version;

		if (isCorrectVersion) {
			const generic2Databearer =
				datasourceDatabearer as IJsonGenericDatasource2;

			switch (datasourceDatabearer.typedef) {
				case DatasourceTypeEnum.generic:
					return new Generic2(generic2Databearer, this);
				case DatasourceTypeEnum.MONGOOSE:
					return new Mongoose(generic2Databearer, this);
				case DatasourceTypeEnum.IDO:
					return new LocalIdo(
						datasourceDatabearer as IJsonGenericDatasource2, this);
				case DatasourceTypeEnum.INLINE_LIST:
					return new InlineList(
						datasourceDatabearer as IJsonInlineListDatasource, this);
				default:
					throw Error(`Dataservice of type '${datasourceDatabearer.typedef}' is not supported`);

			}
		} else {
			console.warn("[Helper] Invalid typedef on ", datasourceDatabearer);
			return null;
		}
	}

	public static add(ngModule: ng.IModule): void {
		ngModule.service(InjectNames.DATASOURCE_SERVICE, DatasourceService);
	}
}
