import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";

import { DatasourceTypeEnum } from "ng2/common/enums/datasource-type.enum";

import type {
	IJsonDatasource,
	IJsonGenericDatasource2,
} from "ng2/common/model/databearer/dataservice/json-datasource.model";
import type { IDataService } from "ng2/common/model/dataservice/data-service.model";

import { Generic } from "ng2/common/project/data-service/generic";

import { DynamicComponentService } from "ng2/components/services/dynamic-component.service";

@Injectable()
export class DataServiceBuilder {

	constructor(
		private http: HttpClient,
		private dynamicComponentService: DynamicComponentService,
	) {

	}

	create(rawJson: IJsonDatasource): IDataService {

		const version = 2;
		const isCorrectVersion = rawJson.version === version;

		if (isCorrectVersion) {
			const generic2Databearer =
				rawJson as IJsonGenericDatasource2;

			switch (rawJson.typedef) {
				case DatasourceTypeEnum.generic:
					return new Generic(
						this.http,
						this.dynamicComponentService,
						generic2Databearer,
					) as any;

				default:
					throw Error(`Dataservice of type '${rawJson.typedef}' is not supported`);

			}
		} else {
			console.warn("[Helper] Invalid typedef on ", rawJson);
			return null;
		}
	}

}
