import { AngularConstants } from "../../../../common/angular-constants";

import { IBoundary } from "ng2/common/model/project/boundary.model";
import { IDialogBaseScope } from "../../../../common/defines";
import { DialogButtonType, IDialog, IDialogResult } from "../../../../common/services/dialog-service";

export class BoundaryEditorCtrl {
	private boundary: IBoundary;
	private dialog: IDialog<IBoundary>;

	// View variables
	public settingLimitX: string; // Soho ticker component uses text

	static $inject = [AngularConstants.SCOPE];

	constructor(private scope: IDialogBaseScope<IBoundary>) {
		const dialog: IDialog<IBoundary> = scope.eirDialog;
		this.dialog = dialog;
		const boundary = dialog.parameter;
		this.settingLimitX = String(boundary.limitX);

		this.boundary = boundary;
	}

	public close(): void {
		this.dialog.close();
	}

	private getNewLimitX(): number {
		let newLimitX: number;
		const parsedNumber = parseInt(this.settingLimitX);
		if (!parsedNumber) { // If falsey, this covers NaN and 0
			newLimitX = this.boundary.limitX;
		} else {
			newLimitX = parsedNumber;
		}
		return newLimitX;
	}

	public save(): void {
		//When pressing ENTER after changing boundary limit the
		//focus must be on name input for the data to be saved
		$("#boundaryName").focus();
		this.boundary.limitX = this.getNewLimitX();
		const result: IDialogResult<IBoundary> = {
			value: this.boundary,
			button: DialogButtonType.Ok };
		this.dialog.close(result);
	}

	static add(m: ng.IModule): void {
		m.controller("eirBoundaryEditorCtrl", BoundaryEditorCtrl);
	}
}
