import { IJsonInlineListDatasource } from "ng2/common/model/databearer/dataservice/json-datasource.model";
import { IListDataItem } from "ng2/common/model/dataservice/list-data-item.model";
import { IMetaDataField } from "ng2/common/model/dataservice/meta-data-field.model";
import { IMetaData } from "ng2/common/model/dataservice/meta-data.model";
import { DatasourceService } from "../../../components/services/datasource-service";
import { Datasource } from "./datasource";

export class InlineList extends Datasource {
	protected databearer: IJsonInlineListDatasource;

	constructor(
		obj: IJsonInlineListDatasource,
		private datasourceService: DatasourceService
	) {
		super(obj);
	}

	public getDetailData(): Promise<any> {
		return this.loadData().then(result => result[0]);
	}

	public getNonParsedData(): Promise<any[]> {
		const defer = this.datasourceService.$q.defer<any>();
		const output = this.databearer.dataset.map(entry => {
			const newEntry: IListDataItem = {};
			this.databearer.outputParameters.forEach(parameter => {
				newEntry[parameter.key] = entry[parameter.field];
			});
			return newEntry;
		});
		defer.resolve(output);
		return defer.promise as Promise<any>;
	}

	public getListData(): Promise<any> {
		return this.loadData()
			.then((result) => {
				return result;
			});
	}

	private loadData(): Promise<any> {
		const defer = this.datasourceService.$q.defer<any>();
		defer.resolve(this.databearer.dataset);
		return defer.promise as Promise<any>;
	}

	public getMetaData(): Promise<IMetaData> {
		const deferredMetaData: ng.IDeferred<any> = this.datasourceService.getDeferred();
		const outputFields: IMetaDataField[] = [];
		this.databearer.outputParameters.forEach((param) => {
			outputFields.push({
				FLNM: param.id,
				FLDS: param.key,
				MAND: "0",
				type: "string"
			});
		});
		deferredMetaData.resolve({
			inputFields: [],
			outputFields: outputFields
		});
		return deferredMetaData.promise as Promise<any>;
	}
}
