
/**
* HTTP utility functions.
*/
export class HttpUtil {

	public static combine(url1: string, url2: string): string {
		if (!url1) {
			return url2;
		}
		if (!url2) {
			return url1;
		}
		const end = url1.length - 1;
		if (url1.charAt(end) === "/") {
			url1 = url1.substring(0, end);
		}
		if (url2.charAt(0) === "/") {
			url2 = url2.substring(1);
		}
		return url1 + "/" + url2;
	}
}
