import angular from "angular";

import {
	ChartTypeEnum, InteractionActionEnum, InteractionPairOperation, SettingKeys,
} from "ng2/common/constants/common.constant";
import { InteractionDefines } from "ng2/common/constants/interaction-defines.constant";
import { BindingTypeEnum } from "ng2/common/enums/binding-type.enum";
import { SizeTypeEnum } from "ng2/common/enums/size-type.enum";
import { ValueTypeEnum } from "ng2/common/enums/value-type.enum";
import { IDataPair } from "ng2/common/model/data-pair.model";
import { IJsonChart, IJsonChartDataset } from "ng2/common/model/databearer/components/json-chart.model";
import { IJsonSize } from "ng2/common/model/databearer/components/json-component.model";
import { IJsonBindableField } from "ng2/common/model/databearer/json-binding.model";
import { IInteraction } from "ng2/common/model/interaction/interaction.model";
import { BackwardCompatible } from "ng2/common/project/backward-compatible";

import { IChartDataItem, IChartDataset } from "../../common/model/sohoxi/sohoxi-chart.model";
import { Component } from "../component";
import { FrameworkService } from "../services/framework-service";

import { IDatasource } from "common/model/datasource/datasource.model";
import { ILayoutComponent } from "../../common/interface/ilayout-component";

export class Chart extends Component {

	public static DEFAULT_WIDTH: IJsonSize = { sizeType: SizeTypeEnum.PERCENT };
	public static DEFAULT_HEIGHT: IJsonSize = { sizeType: SizeTypeEnum.PERCENT };
	public static SOURCE_TRIGGERS: IDataPair[] = InteractionDefines.SOURCE_TRIGGERS.concat(
		[InteractionPairOperation.ON_ITEM_CHANGED]
	);
	public static TARGET_ACTIONS: IDataPair[] = [InteractionPairOperation.LIST];
	public static DEFAULT_TRIGGER: IDataPair = InteractionPairOperation.ON_ITEM_CHANGED;
	public static DEFAULT_ACTION: IDataPair = InteractionPairOperation.LIST;

	public static BINDABLE_FIELDS_2: IJsonBindableField[] = [{
		name: "Header",
		ref: SettingKeys.CHART_TITLE,
		valueType: ValueTypeEnum.LOCALIZED_STRING,
		write: true,
		read: false,
		defaultValue: { Type: BindingTypeEnum.STATIC_VALUE, value: "" }
	}, {
		name: "Category axis title",
		ref: SettingKeys.CHART_AXIS_CATEGORY_TITLE,
		valueType: ValueTypeEnum.LOCALIZED_STRING,
		write: true,
		read: false,
		defaultValue: { Type: BindingTypeEnum.STATIC_VALUE, value: "" }
	}, {
		name: "Value axis title",
		ref: SettingKeys.CHART_AXIS_VALUES_TITLE,
		valueType: ValueTypeEnum.LOCALIZED_STRING,
		write: true,
		read: false,
		defaultValue: { Type: BindingTypeEnum.STATIC_VALUE, value: "" }
	}];

	protected databearer: IJsonChart;
	// Bindable2
	public title: string;
	public showLabel: boolean;
	public axisCategoryTitle: string;
	public axisValuesTitle: string;
	public textType: any;

	constructor(
		obj: IJsonChart,
		$element: ng.IAugmentedJQuery,
		parent: ILayoutComponent,
		frameworkService: FrameworkService,
	) {
		super(obj, $element, parent, frameworkService);
		if (!this.databearer.datasets) {
			this.databearer.datasets = [];
		}

		BackwardCompatible.tryConvertLegacyChart(this.databearer, obj);
		this.showLabel = BackwardCompatible.getShowLabel(obj);

		this.updateWhenExist();
	}

	// Wait for the chart element to exist before rendering
	private updateWhenExist(): void {
		const checkInterval = 100; // check every 100ms
		const checkExist = setInterval(() => {
			if ($(`#chart-${this.getId()}`).length > 0) {
				this.updateChart();
				clearInterval(checkExist);
			}
		}, checkInterval);
	}

	public newBoundaryEntered(boundaryId: string): void {
		super.newBoundaryEntered(boundaryId);
		this.setBoundarySettingIfChanged<boolean>(SettingKeys.SHOW_LABEL);
		this.updateChart();
	}

	public updateVisibility(): void {
		super.updateVisibility();
		this.updateWhenExist();
	}

	public loadInteraction(interaction: IInteraction): ng.IPromise<any | any[]> {
		const interactionPromise = super.loadInteraction(interaction);
		if (interaction.targetActionName === InteractionActionEnum.LIST) {
			interactionPromise.then(() => {
				this.updateChart();
			});
		}
		return interactionPromise;
	}

	public datasourceChanged(newDatasource: IDatasource): void {
		if (!newDatasource) {
			this.datasource = undefined;
			this.getDatabearer().datasets = [];
		}
	}

	public setBindableValue(bindableReference: string, value: any): void {
		super.setBindableValue(bindableReference, value);
		this.updateChart();
	}

	public getDatabearer(): IJsonChart {
		return this.databearer;
	}

	private getSohoChartType(chartEnum: ChartTypeEnum): string {
		const sohoString = chartEnum.toString().split(/(?=[A-Z])/)
			.map((s) => s.charAt(0).toLowerCase() + s.substring(1))
			.join("-");
		return sohoString;
	}

	private getExampleData(): IChartDataset[] {
		const exampleDatasets: IChartDataset[] = [
			{
				name: "Component A",
				data: [
					{
						name: "Jan",
						value: 12,
					}, {
						name: "Feb",
						value: 11
					}, {
						name: "Mar",
						value: 14
					}, {
						name: "Apr",
						value: 10
					}, {
						name: "May",
						value: 14
					}, {
						name: "Jun",
						value: 8
					}
				],
			},
			{
				name: "Component B",
				data: [
					{
						name: "Jan",
						value: 22,
					}, {
						name: "Feb",
						value: 21
					}, {
						name: "Mar",
						value: 24
					}, {
						name: "Apr",
						value: 20
					}, {
						name: "May",
						value: 24
					}, {
						name: "Jun",
						value: 28
					}
				],
			},
			{
				name: "Component C",
				data: [
					{
						name: "Jan",
						value: 32,
					}, {
						name: "Feb",
						value: 31
					}, {
						name: "Mar",
						value: 34
					}, {
						name: "Apr",
						value: 30
					}, {
						name: "May",
						value: 34
					}, {
						name: "Jun",
						value: 38
					}
				],
			}
		];
		return exampleDatasets;
	}

	private getDataset(list: any[]): IChartDataset[] {
		const datasets: IChartDataset[] = [];
		if (list) {
			const datasetsMetadata: IJsonChartDataset[] = this.getDatabearer().datasets;
			const sohoDatasetMaxSize = 20;
			list.splice(sohoDatasetMaxSize, list.length); // soho dataset size limit

			datasetsMetadata.forEach(datasetMetadata => {
				const dataset: IChartDataset = {
					name: datasetMetadata.name,
					data: [],
				};

				list.forEach(row => {
					const keys = Object.keys(row);
					const dataItem: IChartDataItem = { name: "", value: 0 };
					keys.forEach(key => {
						if (key === datasetMetadata.categories) {
							dataItem.name = row[key];
						} else if (key === datasetMetadata.series) {
							let value = row[key];
							if (typeof value !== "number") {
								value = parseInt(value, 10);
							}
							dataItem.value = value;
						}
					});
					if (!isNaN(dataItem.value) && dataItem.name) {
						dataset.data.push(dataItem);
					}
				});
				datasets.push(dataset);
			});

			switch (this.getBoundarySetting(SettingKeys.CHART_TYPE)) {
				case ChartTypeEnum.Bar:
				case ChartTypeEnum.Column:
				case ChartTypeEnum.Donut:
				case ChartTypeEnum.Pie:
					datasets.splice(1, datasets.length); // splice down to one dataset
					break;
				case ChartTypeEnum.BarGrouped:
				case ChartTypeEnum.BarStacked:
				case ChartTypeEnum.ColumnGrouped:
				case ChartTypeEnum.ColumnStacked:
					if (datasets.length === 1) { // soho needs at least 2 datasets
						datasets.push({ name: "", data: [] } as IChartDataset); // dummy dataset
					}
					break;
				default:
					break;
			}
		}

		if (datasets.length > 0) {
			return datasets;
		} else if (this.getDatabearer().datasets.length > 0) {
			return [];
		} else {
			return this.getExampleData();
		}
	}

	public updateChart(): void {
		const chartElement = $(`#chart-${this.getId()}`);
		if (chartElement.length > 0 && chartElement.parent().height() > 0) {
			let dataset: IChartDataset[] = [];
			const listResult = angular.copy(this.getContext().ListResult);
			dataset = this.getDataset(listResult);
			chartElement.removeClass();
			chartElement.addClass("chart-container");
			const hasLabel = this.getBoundarySetting<boolean>(SettingKeys.SHOW_LABEL);
			const chartWrapper = $("div.chart > div.widget-content");
			$("header.application-header").length > 0 ?
				chartWrapper.addClass("has-header") :
				chartWrapper.removeClass("has-header");
			hasLabel ? chartWrapper.addClass("has-title") : chartWrapper.removeClass("has-title");
			const chartType = this.getBoundarySetting(SettingKeys.CHART_TYPE) as ChartTypeEnum;
			chartElement.chart({
				type: this.getSohoChartType(chartType),
				dataset,
				axisLabels: {
					left: this.axisValuesTitle,
					bottom: this.axisCategoryTitle,
				},
				showLegend: this.getBoundarySetting(SettingKeys.CHART_LEGEND_DISPLAY),
				animate: this.getBoundarySetting(SettingKeys.CHART_STARTUP_ANIMATION_ENABLE),
				isArea: chartType === ChartTypeEnum.Area,
				isDonut: chartType === ChartTypeEnum.Donut,
				// TODO: Fix this MUPS-2682
				isGrouped: (chartType as any).includes("grouped"),
				isStacked: (chartType as any).includes("stacked"),
			});
		}
	}

	public static directive = [() => {
		return {
			template: require("./chart.html"),
			replace: true,
		};
	}];
}
