import type { IJsonValueSource } from "ng2/common/model/databearer/json-binding.model";
import type { IJsonInteractionParameter } from "ng2/common/model/databearer/json-interaction.model";
import type { IAvailableTargetField } from "ng2/common/model/interaction/available-target-field.model";
import type { IInteractionParameter } from "ng2/common/model/interaction/interaction-parameter.model";

export class InteractionParameter implements IInteractionParameter {

	private databearer: IJsonInteractionParameter;

	targetField: IAvailableTargetField;

	constructor(obj: IJsonInteractionParameter) {
		this.databearer = obj;
	}

	getDatabearer(): IJsonInteractionParameter {
		return this.databearer;
	}

	get targetKey(): string {
		return this.databearer.targetKey;
	}
	set targetKey(targetKey: string) {
		this.databearer.targetKey = targetKey;
	}

	get valueSource(): IJsonValueSource {
		return this.databearer.valueSource;
	}
	set valueSource(valueSource: IJsonValueSource) {
		this.databearer.valueSource = valueSource;
	}

}
