import { AngularConstants } from "../../../../../common/angular-constants";
import { IDialogBaseScope } from "../../../../../common/defines";
import { CollectionProperty } from "../../collection-data";

export class PreviewCtrl {
	public static $inject = [
		AngularConstants.SCOPE
	];

	public dataset: CollectionProperty[];

	public headTitle =
		`Below is a preview of properties.
		Keep in note that it is including default properties which will be created together with this collection.`;

	public constructor(
		private scope: IDialogBaseScope<CollectionProperty[]>
	) {
		this.dataset = scope.eirDialog.parameter;
	}

	public close(): void {
		this.scope.eirDialog.close();
	}

	public static add(m: ng.IModule): void {
		m.controller("eirPreviewCtrl", PreviewCtrl);
	}
}
