import { InjectNames } from "ng2/common/constants/common.constant";
import { IBoundary } from "ng2/common/model/project/boundary.model";
import { AngularConstants } from "../../../../common/angular-constants";
import type { IEditorComponent } from "common/interface/ieditor-component";
import { EditorService } from "../../services/editor-service";
import { IGenericSettingScope, ISettingController } from "../generic-settings";
import { SettingsBase } from "../settings-base";

export class TextSettingsCtrl extends SettingsBase implements ISettingController {
	public textSetting: string;
	public inputType: string;

	public static $inject = [
		AngularConstants.SCOPE,
		InjectNames.EDITOR_SERVICE
	];

	public constructor(
		protected $scope: IGenericSettingScope,
		editorService: EditorService
	) {
		super($scope, editorService);
		$scope.register({ settingCtrl: this, settingRef: $scope.settingMeta.ref });
		this.inputType = $scope.settingMeta.inputType;
		this.button = $scope.settingMeta.button;
		this.settingsRef = $scope.settingMeta.ref;
		this.onComponentChanged(editorService.getSelectedComponent());
	}

	public refresh(): void {
		this.textSetting = this.readSettingValue<string>(this.$scope.settingMeta);
	}

	protected onComponentChanged(newComponent: IEditorComponent): void {
		this.selectedComponent = newComponent;
		if (this.selectedComponent) {
			this.refresh();
		}
	}

	protected onBoundaryChanged(newBoundary: IBoundary): void {
		super.onBoundaryChanged(newBoundary);
		this.refresh();
	}
}

export class TextSettingsDirective implements ng.IDirective {
	template = require("./text-setting.html");
	scope = {
		settingMeta: "=",
		register: "&"
	};
	controller = TextSettingsCtrl;
	controllerAs = "eirTextSettingsCtrl";

	public static add(m: ng.IModule): void {
		m.directive("eirTextSetting", () => new TextSettingsDirective());
	}
}
