import { AngularConstants } from "common/angular-constants";

import {
	InjectNames,
	SettingKeys
} from "ng2/common/constants/common.constant";

import type { IJsonComponent } from "ng2/common/model/databearer/components/json-component.model";
import type { IBoundary } from "ng2/common/model/project/boundary.model";
import type { IEditorComponent } from "common/interface/ieditor-component";

import { EditorService } from "editor/services/editor-service";
import { SettingsBase } from "editor/component-settings/settings-base";
import { ListSettings } from "editor/component-settings/list/list-settings";

export interface IEditorList extends IEditorComponent {
	refresh(): void;
	forEachView(fn: (view: IJsonComponent) => void): void;
	forEachItemTemplate(fn: (view: IJsonComponent) => void): void;
}

export class ListStyleSettingsCtrl extends SettingsBase {

	public get availableTemplates(): { id: string, displayName: string }[] {
		return ListSettings.getInstance().availableTemplates;
	}

	protected selectedComponent: IEditorList;

	public settingTemplate: { id: string, displayName: string };

	public static $inject = [
		AngularConstants.SCOPE,
		InjectNames.EDITOR_SERVICE
	];

	public constructor(
		$scope: ng.IScope,
		editorService: EditorService
	) {
		super($scope, editorService);

		this.onComponentChanged(editorService.getSelectedComponent());
	}

	protected onBoundaryChanged(currentBoundary: IBoundary): void {
		this.updateTemplateComponent();
	}

	protected onComponentChanged(newComponent: IEditorComponent): void {
		super.onComponentChanged(newComponent);
		if (this.selectedComponent) {
			this.updateTemplateComponent();
		}
	}

	private updateTemplateComponent(): void {
		const templateComponent =
			this.selectedComponent.getBoundarySetting<IJsonComponent>(SettingKeys.ITEM_TEMPLATE);
		const availableTemplates = this.availableTemplates;
		for (let i = 0; i < availableTemplates.length; i++) {
			if (templateComponent.id === availableTemplates[i].id) {
				this.settingTemplate = availableTemplates[i];
				break;
			}
		}
	}

	public onChangeTemplate(): void {
		const template = ListSettings.getInstance().getTemplate(
			this.settingTemplate.id,
			this.selectedComponent.getId()
		);
		this.selectedComponent.setBoundarySetting<IJsonComponent>(
			SettingKeys.ITEM_TEMPLATE,
			template
		);
		if (this.selectedComponent.datasource) {
			this.selectedComponent.datasource.getMetaData().then((metaData) => {
				this.editorService.setDefaultBindings(template, metaData.outputFields);
			}).finally(() => {
				this.editorService.saveCanvas();
			});
		} else {
			this.editorService.saveCanvas();
		}
	}
}

export class ListStyleSettingsDirective implements ng.IDirective {
	template = require("./list-style-settings.html");
	scope = true;
	controller = ListStyleSettingsCtrl;
	controllerAs = "eirListStyleSettingsCtrl";
}
