import { IMap } from "ng2/common/model/imap.model";
import { PaginatorCtrl } from "./paginator-ctrl";

export class PaginatorComponent implements ng.IComponentOptions {
	public controller: any;
	public controllerAs: string;
	public template: string;
	public bindings: IMap<string>;

	constructor() {
		this.bindings = {
			pagingValues: "<",
			currentPage: "=",
		};

		this.controller = PaginatorCtrl;
		this.controllerAs = "ctrl";
		this.template = require("./paginator-template.html");
	}

	static add(m: ng.IModule): void {
		m.component("eirPaginator", new PaginatorComponent());
	}
}
