// TODO: MUPS-2682 Needs to be fixed later
// (window as any).setContextRoots();

export const commonModule = angular.module("commonModule", [
	ngAnimate
]);

commonModule.run(() => {
	const setContextRoots = () => {

		let pathname: string;
		if (DEV_SERVER) {
			pathname = "api";
		} else {
			pathname = location.pathname;
		}

		// Setting context roots, used by angular for rest calls and templates
		const pathnameNoIndex = pathname.substr(0, pathname.indexOf("index.html"));

		Config.APP_ROOT = pathnameNoIndex; // MUPS-2682: Remove as this property is probably obsolete now

		if (pathname.indexOf("appbuilder") !== -1) {
			Config.REST_ROOT = Config.APP_BUILDER_REST_ROOT;
		} else if (pathname.indexOf("apphub") !== -1) {
			Config.REST_ROOT = Config.APP_RUNTIME_REST_ROOT;
		} else {
			Config.REST_ROOT = `http://localhost:3000/${pathname}`;
		}
	};

	setContextRoots();
});

// Add angularAnimate to any component we want ngAnimate on
commonModule
	.config(["$animateProvider", (
		$animateProvider: ng.animate.IAnimateProvider
	) => {
		$animateProvider.classNameFilter(/angularAnimate/);
	}]);

//tslint:disable:no-magic-numbers
// Adds custom slide and fade animation using ngAnimate
commonModule
	.animation(".angularAnimate-slide", [() => {
		return {
			enter: (element: JQuery, doneFn: any) => {
				$(element).slideDown(1000, doneFn);
			},
			leave: (element: JQuery, doneFn: any) => {
				$(element).slideUp(1000, doneFn);
			},
		} as ng.animate.IAnimateCallbackObject;
	}])
	.animation(".angularAnimate-fade", [() => {
		return {
			enter: (element: JQuery, doneFn: any) => {
				$(element).fadeIn(1500, doneFn);
			},
			leave: (element: JQuery, doneFn: any) => {
				$(element).fadeOut(500, doneFn);
			},
		} as ng.animate.IAnimateCallbackObject;
	}]);
//tslint:enable

commonModule.config(["$qProvider", ($qProvider: ng.IQProvider) => {
	$qProvider.errorOnUnhandledRejections(false);
}]);

