import {
	ChangeDetectorRef,
	Component,
	ElementRef,
	Renderer2,
	ViewChild,
} from "@angular/core";
import {
	SohoListViewComponent,
	SohoMessageService
} from "ids-enterprise-ng";
import { Observable, of } from "rxjs";

import {
	ListTemplateIdEnum,
	SubstitutionTags,
} from "ng2/common/constants/common.constant";

import type { IJsonLabel } from "ng2/common/model/databearer/components/json-label.model";
import type { IJsonList } from "ng2/common/model/databearer/components/json-list.model";
import type { IJsonLayoutComponent } from "ng2/common/model/databearer/json-layout-component.model";
import type { IListDataItem } from "ng2/common/model/dataservice/list-data-item.model";

import { ListMetadata } from "ng2/components/list/list.metadata";

import { BackwardCompatible } from "ng2/common/project/backward-compatible";
import { BaseComponent } from "ng2/components/base/component";

import { DynamicComponentService } from "ng2/components/services/dynamic-component.service";
import { FrameworkService } from "ng2/components/services/framework.service";

@Component({
	selector: "ng2-list",
	template: `
		<div class="full-height"
			[id]="id"
			soho-busyindicator
		>
			<soho-toolbar-flex
				*ngIf="showLabel"
			>
				<soho-toolbar-flex-section
					*ngIf="showLabel"
					[isTitle]="true"
					[isTitleFavor]="true"
				>
					<h2>{{ label }}</h2>
				</soho-toolbar-flex-section>
			</soho-toolbar-flex>

			<soho-listview
				[ngSwitch]="template.id"
				(selected)="onSelected($event)"
			>
				<li soho-listview-item
					*ngFor="let item of ( contextData$ | async )"
				>
					<ng-container
						*ngSwitchCase="'OneLabelFlexbox'"
					>
						<p soho-listview-header>{{ item[templateId[0]] }}</p>
					</ng-container>
					<ng-container
						*ngSwitchCase="'TwoVerticalLabels'"
					>
						<p soho-listview-header>{{ item[templateId[0]] }}</p>
						<p soho-listview-subheader>{{ item[templateId[1]] }}</p>
					</ng-container>
					<ng-container
						*ngSwitchDefault
					>
						Yolo
					</ng-container>
				</li>
			</soho-listview>
		</div>
	`
})
export class ListComponent extends BaseComponent {

	label: string;
	showLabel: boolean;

	currentItem: IListDataItem;

	template: IJsonLayoutComponent;
	templateId: string[] = [];

	databearer: IJsonList;

	contextData$: Observable<any[]>;

	@ViewChild(SohoListViewComponent, {
		static: true
	}) listComponent: SohoListViewComponent;

	constructor(
		elementRef: ElementRef,
		renderer: Renderer2,
		changeDetectorRef: ChangeDetectorRef,
		frameworkService: FrameworkService,
		dynamicComponentService: DynamicComponentService,
		sohoMessageService: SohoMessageService,
	) {
		super(
			elementRef,
			renderer,
			changeDetectorRef,
			frameworkService,
			dynamicComponentService,
			sohoMessageService,
		);
	}

	onInitComponent(): void {
		super.onInitComponent();

		this.showLabel = BackwardCompatible.getShowLabel(this.databearer);
	}

	initializeBinding(componentDatabearer: IJsonList): void {
		ListMetadata.BINDABLE_FIELDS_2.forEach(field => {
			const self: any = this;
			const bindingValue = componentDatabearer[field.ref]?.value;
			self[field.ref] = bindingValue === SubstitutionTags.VALUE ?
				"" : bindingValue;

			// TODO: look into how to make this generic and not specific way to set boundary values
			const currentBoundary = this.frameworkService.currentBoundary;
			const boundarySetting = componentDatabearer.boundarySettings[currentBoundary.displayName];

			this.template = boundarySetting.itemTemplate;
			console.info(`List Template: ${ this.template.id }`);
		});
	}

	dataServiceCompleted(component: ListComponent): void {
		super.dataServiceCompleted(component);

		this.setTemplateBindings();
		component.contextData$ = of(component.context.ListResult);

		// TODO: look into how to make this generic and not specific
		this.changeDetectorRef.detectChanges();
	}

	/**
	 * TODO: POC Parser
	 * Refactor old list editor into new List Component. Will become much more simple in its design
	 */
	private setTemplateBindings(): void {

		this.templateId.length = 0;
		switch (this.template.id) {
			case ListTemplateIdEnum.OneLabelFlexbox:
			case ListTemplateIdEnum.TwoVerticalLabels:
			default:
				this.template.children
					.forEach((item: IJsonLabel) => {
						this.templateId.push(item.content.sourceKey);
					});
		}
	}

	onSelected(): void {

		const selectedItemIndex = this.listComponent.getSelectedItems[0] as number;
		this.currentItem = this.context.ListResult[selectedItemIndex];

		this.updateBindings();
	}
}
