import angular from "angular";
import { AngularConstants } from "../../angular-constants";

import { IPagerApi, IPageRequest, IPagerInfo } from "ng2/common/model/sohoxi/sohoxi-pager.model";

export interface IPaginatorChangesObject<T> {
	pagingValues: ng.IChangesObject<T>;
}

export class PaginatorCtrl<T> {
	public pagingValues: T[];
	private currentPage: T[];
	private pages: T[][];
	private pagerOptions: IPagerInfo;
	private pageNumber: number;
	private static pagesize = 16;
	public static $inject = [
		AngularConstants.SCOPE,
		AngularConstants.ELEMENT,
	];

	public constructor(
		private $scope: ng.IScope,
		private $element: ng.IAugmentedJQuery
	) { }

	public $onInit(): void {
		// tslint:disable-next-line:no-magic-numbers
		const pagesizes = [16, 28, 48, 76];
		this.createPages(PaginatorCtrl.pagesize);

		this.pagerOptions = {
			type: "standalone",
			pagesize: PaginatorCtrl.pagesize,
			pageSizeSelectorText: "{0} Projects per page",
			pagesizes: pagesizes,
			showPageSizeSelector: true,
			showFirstButton: true,
			enableFirstButton: false,
			showPreviousButton: true,
			enablePreviousButton: false,
			showNextButton: true,
			enableNextButton: true,
			showLastButton: true,
			enableLastButton: true,
			onFirstPage: (_elem: any, _args: IPageRequest) => {
				this.resetCurrentPage();
				this.$scope.$apply();
			},
			onPreviousPage: (_elem: any, _args: IPageRequest) => {
				--this.pageNumber;
				this.previousPage(this.pageNumber);
				this.$scope.$apply();
			},
			onNextPage: (_elem: any, _args: IPageRequest) => {
				++this.pageNumber;
				this.nextPage(this.pageNumber);
				this.$scope.$apply();
			},
			onLastPage: (_elem: any, _args: IPageRequest) => {
				this.pageNumber = this.pages.length - 1;
				this.currentPage = this.pages[this.pageNumber];
				this.updateButtons(false, false, true, true);
				this.$scope.$apply();
			},
			onPageSizeChange: (_elem: any, args: IPageRequest) => {
				this.createPages(args.pagesize);
				this.resetCurrentPage();
				this.$scope.$apply();
			},
		};
		this.getPagerElement().pager(this.pagerOptions).data("pager");
		this.resetCurrentPage();
	}

	public $onChanges(value: IPaginatorChangesObject<T[]>): void {
		const currentList = value.pagingValues.currentValue;
		const previousList = value.pagingValues.previousValue;
		if (previousList.length && (currentList !== previousList)) {
			this.pagingValues = value.pagingValues.currentValue;
			this.createPages(PaginatorCtrl.pagesize);
			this.resetCurrentPage();
		}
	}

	public nextPage(page: number): void {
		this.currentPage = this.pages[page];
		if (page === (this.pages.length - 1)) {
			this.updateButtons(false, false, true, true);
		} else {
			this.updateButtons(true, true, true, true);
		}
	}

	public previousPage(page: number): void {
		this.currentPage = this.pages[page];
		if (page === 0) {
			this.updateButtons(true, true, false, false);
		} else {
			this.updateButtons(true, true, true, true);
		}
	}

	private createPages(pagesize: number): void {
		this.pages = [];
		const pagingTarget = angular.copy(this.pagingValues);

		for (let pageIndex = 0; pagingTarget.length !== 0; pageIndex++) {
			const page = pagingTarget.splice(0, pagesize);
			this.pages.push(page);
		}
	}

	private getPagerElement(): any {
		return this.$element.find(".pager-container");
	}

	private getPagerApi(): IPagerApi {
		return this.getPagerElement().data("pager");
	}

	private resetCurrentPage(): void {
		this.pageNumber = 0;
		this.currentPage = this.pages[this.pageNumber];
		if (this.pages.length > 1) {
			this.updateButtons(true, true, false, false);
		} else {
			this.updateButtons(false, false, false, false);
		}
	}

	private updateButtons(last: boolean, next: boolean, first: boolean, previous: boolean): void {
		this.getPagerApi().updated({
			enableLastButton: last,
			enableNextButton: next,
			enableFirstButton: first,
			enablePreviousButton: previous
		});
	}
}
