import angular from "angular";

import { IContext } from "ng2/common/model/components/base/context.model";
import { IDataPair } from "ng2/common/model/data-pair.model";
import { IMap } from "ng2/common/model/imap.model";
import {
	IComponentStyle
} from "../common/defines";
import { IComponent } from "../common/IComponent";

import { Config, InteractionPairOperation, SettingKeys, ThemeEnum } from "ng2/common/constants/common.constant";
import { OrientationEnum } from "ng2/common/enums/orientation.enum";
import { SizeTypeEnum } from "ng2/common/enums/size-type.enum";
import { ValueTypeEnum } from "ng2/common/enums/value-type.enum";
import { IJsonComponent, IJsonComponentSettings, IJsonSize } from "ng2/common/model/databearer/components/json-component.model";
import { IJsonBindableField } from "ng2/common/model/databearer/json-binding.model";
import { IDesignerManifest } from "ng2/common/model/project/mashup/designer-manifest.model";
import { IComponentScope } from "../common/interface/icomponent-scope";
import { ILayoutComponent } from "../common/interface/ilayout-component";
import { Component } from "./component";
import { LayoutComponent } from "./layout-component";
import { FrameworkService } from "./services/framework-service";

export class ApplicationParent extends LayoutComponent {
	public static applicationParentDatabearer: IJsonComponent = {
		typedef: "ApplicationParent",
		id: "Application",
		displayName: "Application",
		isReadonly: false,
		boundarySettings: {} as IMap<IJsonComponentSettings>
	};
	public static BINDABLE_FIELDS_2: IJsonBindableField[] = [{
		name: "Locale",
		ref: SettingKeys.LANGUAGE,
		valueType: ValueTypeEnum.STRING,
		write: false,
		read: true
	}, {
		name: "Language Code",
		ref: SettingKeys.LANGUAGE_SHORT,
		valueType: ValueTypeEnum.STRING,
		write: false,
		read: true
	}, {
		name: "User Name",
		ref: SettingKeys.USER_NAME,
		valueType: ValueTypeEnum.STRING,
		write: false,
		read: true
	}, {
		name: "User Description",
		ref: SettingKeys.USER_DESCRIPTION,
		valueType: ValueTypeEnum.STRING,
		write: false,
		read: true
	}, {
		name: "Current Timestamp",
		ref: SettingKeys.TIMESTAMP,
		valueType: ValueTypeEnum.STRING,
		write: false,
		read: true
	}];
	public static DEFAULT_WIDTH: IJsonSize = { sizeType: SizeTypeEnum.PERCENT };
	public static DEFAULT_HEIGHT: IJsonSize = { sizeType: SizeTypeEnum.PERCENT };
	public static DEFAULT_TRIGGER: IDataPair = InteractionPairOperation.ON_STARTUP;
	public static DEFAULT_ACTION: IDataPair = InteractionPairOperation.NONE;
	public static SOURCE_TRIGGERS: IDataPair[] = [InteractionPairOperation.ON_STARTUP];
	public static TARGET_ACTIONS: IDataPair[] = [InteractionPairOperation.NONE];

	public currentTheme: ThemeEnum = 1;
	public language: string;
	public languageShort: string;
	public userName: string;
	public userDescription: string;
	public timestamp: number;
	protected databearer: IJsonComponent;

	private manifest: IDesignerManifest;

	constructor(
		databearer: IJsonComponent,
		element: ng.IAugmentedJQuery,
		parent: ILayoutComponent,
		frameworkService: FrameworkService) {
		super(databearer, element, parent, frameworkService);

		this.manifest = frameworkService.getCurrentProject().getManifest();
		this.language = frameworkService.getCurrentLanguage();
		if (this.language) {
			// tslint:disable-next-line:no-magic-numbers
			this.languageShort = this.language.substr(0, 2);
		}
		this.userName = Config.USER.name;
		this.userDescription = Config.USER.displayName;
		this.timestamp = Date.now();
		this.getScope().componentInstance = this;
	}

	public static createSpoofedElement(scope: IComponentScope): ng.IAugmentedJQuery {
		const element = angular.element("<div></div>");
		element.data("eir-scope", scope);
		return element;
	}

	public getId(): string {
		return this.databearer.id;
	}

	public addChildInstance(child: IComponent): void {
		this.children[0] = child;
	}

	public resizableChildren(direction: OrientationEnum): boolean {
		// Rootcomponent is never resizeable
		return false;
	}

	public hasIndividualChildSizes(ori: OrientationEnum): boolean {
		if (this.manifest.showTitleBar) {
			return true;
		}
		return false;
	}

	public getContext(): IContext {
		return {};
	}

	public forEachChild(fn: (ch: Component) => void, doGenerated: boolean): void {
		// forEachChild
	}

	public forEachParent(fn: (ch: ILayoutComponent) => void): void {
		// forEachParent
	}

	public calcNewSizes(orientation?: OrientationEnum): void {
		if (this.children && this.children.length > 0) {
			this.children[0].calcNewHeight();
			this.children[0].calcNewWidth();
		}
	}

	// Method needed for interface implementation
	public getPxPerPercentComponent(ori: OrientationEnum): number {
		return 0;
	}

	public updateVisibility(): void {
		// Overloading, ApplicationParent is always invisible
	}

	public updateShowError(): void {
		// Overloading, ApplicationParent never shows error
	}

	protected getInlineStyle(): IComponentStyle {
		// Overload, ApplicationParent has no size
		return {
			width: "0",
			height: "0"
		};
	}

	public static directive = [() => {
		return console.debug("[ApplicationParent] Rootcomponent has no directive");
	}];
}