import isNil from "lodash-es/isNil";

import {
	Config,
	DEFAULT_BOUNDARY,
	InjectNames,
	MGPlatform,
	SaveFlagsEnum,
	ServerPaths,
	ThemeEnum,
} from "ng2/common/constants/common.constant";

import type { IJsonGenericDatasource2 } from "ng2/common/model/databearer/dataservice/json-datasource.model";
import type { IJsonRuntimeManifest } from "ng2/common/model/databearer/json-manifest.model";
import type { IJsonMashup, IJsonRuntimeMashup } from "ng2/common/model/databearer/json-mashup.model";
import type { IJsonUrlVariable } from "ng2/common/model/databearer/json-url-variable.model";
import type { IEnvironmentSettings } from "ng2/common/model/environment-settings.model";
import type { IBoundary } from "ng2/common/model/project/boundary.model";
import type { IDesignerMashup } from "ng2/common/model/project/mashup/designer-mashup.model";

import { AngularConstants } from "common/angular-constants";
import type { IAvailableSourceField } from "common/defines";
import { InstanceEvent } from "common/instance-event";
import type { ILayoutComponent } from "common/interface/ilayout-component";
import { WSForm } from "etc-assets/user-control-es";

import { DesignerManifest } from "ng2/common/project/manifest/designer-manifest";
import { Mashup } from "ng2/common/project/mashup";
import type { IProjectInfo } from "ng2/common/project/project-info";

import { Helper, SmartBlob } from "common/helper";

import { HttpService } from "common/services/http-service";
import { LanguageService } from "common/services/language-service";
import { ThemeService } from "common/services/theme-service";
import { DatasourceService } from "components/services/datasource-service";

export interface IRootScope extends ng.IRootScopeService {
	currentProject: IDesignerMashup;
}

export class CommonService {
	projectSettingsChangedEvent = new InstanceEvent<DesignerManifest>();
	currentBoundaryChangedEvent = new InstanceEvent<IBoundary>();
	currentCanvasThemeChangedEvent = new InstanceEvent<ThemeEnum>();
	onDuplicateProjectChangedEvent = new InstanceEvent<void>();
	private urlMetaData: IAvailableSourceField[] = [];
	private currentProject: Mashup;
	private currentRootComponentInstance: ILayoutComponent;
	private currentCanvasTheme: ThemeEnum;
	private currentBoundary: IBoundary = DEFAULT_BOUNDARY;

	static $inject = [
		AngularConstants.Q,
		AngularConstants.TIMEOUT,
		InjectNames.HTTP_SERVICE,
		InjectNames.DATASOURCE_SERVICE,
		InjectNames.THEME_SERVICE,
		InjectNames.LANGUAGE_SERVICE,
	];

	constructor(
		private $q: ng.IQService,
		public $timeout: ng.ITimeoutService,
		public httpService: HttpService,
		private datasourceService: DatasourceService,
		private themeService: ThemeService,
		private languageService: LanguageService
	) { }

	loadProjects(): ng.IPromise<IProjectInfo[]> {
		return this.httpService.executeGet(`${ServerPaths.PROJECT}`);
	}

	loadActivatedApps(): ng.IPromise<IJsonRuntimeManifest[]> {
		return this.httpService.executeGet(ServerPaths.RUNTIME + "active");
	}

	loadProject(projectId: string, canvasWidth?: number): ng.IPromise<IJsonMashup> {
		return this.httpService.executeGet<IJsonMashup>(ServerPaths.PROJECT + projectId);
	}

	loadPublishedProject(projectId: string, canvasWidth: number): ng.IPromise<IJsonRuntimeMashup> {
		const lastLanguageCharIndex = 2;
		const locale = this.languageService.getLanguage().substring(0, lastLanguageCharIndex);
		return this.httpService.executeGet<IJsonRuntimeMashup>(
			ServerPaths.RUNTIME + projectId,
			{
				locale: locale
			}
		);
	}

	loadPublishedProjectNoConstraints(
		projectId: string,
		canvasWidth: number
	): ng.IPromise<IJsonRuntimeMashup> {
		return this.httpService.executeGet<IJsonRuntimeMashup>(
			ServerPaths.ADMINISTRATION + ServerPaths.PROJECT + projectId);
	}

	setUrlMetaData(newUrlMetaData: IAvailableSourceField[]): void {
		this.urlMetaData = newUrlMetaData;
	}

	getUrlMetaData(): IAvailableSourceField[] {
		return this.urlMetaData;
	}

	private cleanOldDataservices(project: Mashup): void {
		for (const datasourceId in project.datasources) {
			const datasourceInstance = this.datasourceService.parseDatasourceToTS(project.datasources[datasourceId]);
			if (!datasourceInstance) {
				project.removeDatasource(datasourceId);
				console.info("Removed invalid data service from project");
			}
		}
	}

	loadEnvironmentSettings(): ng.IPromise<IEnvironmentSettings> {
		const path = DEV_SERVER ?
			ServerPaths.SETTINGS + "config/" :
			ServerPaths.SETTINGS;

		return this.httpService.executeGet(path);
	}

	loadDBVersions(): ng.IPromise<string> {
		return this.httpService.executeGet<string>(ServerPaths.SETTINGS + "dbversions");
	}

	loadLicensesHtml(): ng.IPromise<string> {
		return this.httpService.executeGet<string>(ServerPaths.RUNTIME + "about/licenses");
	}

	loadApplicationVersion(): ng.IPromise<string> {
		return this.httpService.executeGet<string>(ServerPaths.SETTINGS + "version");
	}

	unloadProject(): void {
		this.currentProject = null;
		this.currentRootComponentInstance = null;
	}

	getCurrentProject(): Mashup {
		return this.currentProject;
	}

	setCurrentProject(newCurrentProject: Mashup, updateTheme: boolean = true): void {
		this.currentProject = newCurrentProject;
		if (updateTheme) {
			const defaultTheme = this.currentProject.manifest.defaultTheme;
			this.setCanvasTheme(defaultTheme, this.isDesigner());
		}
	}

	buildPrerequisites(mashup: Mashup, canvasWidth: number): ng.IPromise<Mashup> {
		return this.generateUrlMetaData(mashup).then((mashup) => {
			this.setCurrentProject(mashup);
			this.updateCurrentBoundary(canvasWidth);
			this.cleanOldDataservices(mashup);
			return mashup;
		});
	}

	generateUrlMetaData(mashup: Mashup): ng.IPromise<Mashup> {
		const deferred: ng.IDeferred<Mashup> = this.$q.defer();
		this.urlMetaData = [];
		const urlAlias = mashup.getUrlVariables();
		if (urlAlias && urlAlias.length > 0) {
			urlAlias.forEach( (urlVar: IJsonUrlVariable) => {
				WSForm.getVarValue(urlVar.name, (value: string) => {
					const sourceField: IAvailableSourceField = {
						visibleName: urlVar.name,
						sourceKey: urlVar.name,
						sourceKeyShort: urlVar.name,
						currentValue: value,
					};
					this.urlMetaData.push(sourceField);

					if (this.urlMetaData.length === urlAlias.length) {
						deferred.resolve(mashup);
					}
				});
			});
		} else {
			deferred.resolve(mashup);
		}
		return deferred.promise;
	}

	getCurrentBoundary(): IBoundary {
		return this.currentBoundary;
	}

	setCurrentBoundary(boundary: IBoundary): void {
		this.currentBoundary = boundary;
		this.currentBoundaryChangedEvent.raise(boundary);
	}

	setCurrentRootComponentInstance(componentInstance: ILayoutComponent): void {
		this.currentRootComponentInstance = componentInstance;
		componentInstance.forEachChild((child) => {
			child.initializeBindings();
		}, true);
	}

	getCurrentRootComponentInstance(): ILayoutComponent {
		return this.currentRootComponentInstance;
	}

	getCurrentTheme(): ThemeEnum {
		return this.currentCanvasTheme;
	}

	setCanvasTheme(newTheme: ThemeEnum, isDesigner: boolean): void {
		if (this.currentCanvasTheme !== newTheme) {
			this.currentCanvasTheme = newTheme;
			this.themeService.changeThemeCss(newTheme, isDesigner).finally(() => {
				this.currentCanvasThemeChangedEvent.raise(newTheme);
			});
		}
	}

	updateCurrentBoundary(canvasWidth: number): void {
		const newBoundary: IBoundary = this.getBoundaryForWidth(canvasWidth);
		if (this.getCurrentBoundary() !== newBoundary) {
			this.setCurrentBoundary(newBoundary);
		}
	}

	private getBoundaryForWidth(width: number): IBoundary {
		const projectBoundaries = this.currentProject.manifest.mashupBoundaries;

		if (projectBoundaries && !isNil(width)) {
			// Sort the array, could be method?
			projectBoundaries.sort((a, b) => {
				if (a.limitX < b.limitX) {
					return -1;
				} else if (b.limitX < a.limitX) {
					return 1;
				}

				return 0;
			});

			for (const index in projectBoundaries) {
				const boundary = projectBoundaries[index];
				if (boundary.limitX && width <= boundary.limitX) {
					return boundary;
				}
			}
		}

		return DEFAULT_BOUNDARY;
	}

	isDesigner(): boolean {
		return location.hash.indexOf("#!/editor") === 0;
		//return window.location.pathname.indexOf("/appbuilder/editor") === 0;
	}

	exportProjectContent(
		contentIds: string[],
		filePath: string,
		saveFlagEnum: SaveFlagsEnum,
		projectId: string = undefined,
	): void {
		const resourcePathForContentToSave =
			this.getProjectContentResourcePath(projectId, saveFlagEnum);

		const resourceName = resourcePathForContentToSave
			.concat(Helper.createQueryParameters(contentIds, "id"));
		this.httpService
			.executeGet<IJsonGenericDatasource2[]>(resourceName, filePath)
			.then(response => {
				if (Config.MG_PLATFORM === MGPlatform.WEB) {
					const rawJson = JSON.stringify(response);
					const smartBlob = new SmartBlob(
						[rawJson], { type: "application/json" });
					smartBlob.saveAs(filePath);
				}
			});
	}

	/**
	 * convertImageUrlToBase64
	 */
	convertImageUrlToBase64(url: string): ng.IPromise<string> {
		return this.httpService
			.executeGet<{base64: string, contentType: string}>(`${ServerPaths.DESIGNER}base64/?urlname=${url}`)
			.then(result => {
				const filteredBase64 = result.base64.replace(/url\(['"]{0,}|['"]{0,}\)$/ig, "");
				return `data:${result.contentType};base64,${filteredBase64}`;
			});
	}

	private getProjectContentResourcePath(
		projectId: string,
		saveFlagEnum: SaveFlagsEnum
	): string {
		switch (saveFlagEnum) {
			case SaveFlagsEnum.DATASOURCES:
				return projectId ?
					`${ServerPaths.PROJECT}${projectId}/datasources/export/?` :
					`${ServerPaths.REPOSITORY}datasources/export/?`;
			case SaveFlagsEnum.COMPOSITE_COMPONENTS:
				return projectId ?
					`${ServerPaths.PROJECT}${projectId}/compositeComponents/export/?` :
					`${ServerPaths.REPOSITORY}compositeComponents/export/?`;
			default:
				throw new Error(`SaveFlagsEnum '${saveFlagEnum}' not handled`);
		}
	}

	getLanguage(): string {
		return this.languageService.getLanguage();
	}

	openHelp(): void {
		window.open(`${Config.MG_HELP_BASE_URL}/default.html?helpcontent=mgabahug/cover.html#`, "_blank");
	}

	static add(ngModule: ng.IModule): void {
		ngModule.service(InjectNames.COMMON_SERVICE, CommonService);
	}
}
