import { IToolbarOptions } from "../../model/sohoxi/sohoxi-toolbar.model";
import { Utils } from "../utils";

/**
 * Toolbar directive.
 *
 * Example:
 * scope["options"] = {rightAligned: false, maxVisibleButtons: 5}
 * <div class="toolbar" xi-toolbar="options"> ... </div>
 */
export class ToolbarDirective implements ng.IDirective {
	private static readonly DIRECTIVE_NAME = "xiToolbar";
	public restrict = "A";
	public scope = false;
	public replace = true;

	public link(
		scope: ng.IScope,
		element: ng.IAugmentedJQuery,
		attributes: ng.IAttributes
	): void {
		const options = attributes[ToolbarDirective.DIRECTIVE_NAME];
		const unbindWatcher = scope.$watch(
			options,
			(newOptions: IToolbarOptions) => {
				if (newOptions) {
					element.toolbar(newOptions);
				} else {
					element.toolbar();
				}
			}
		);
		element.on("$destroy", () => {
			const toolbarData = Utils.getData(element, "toolbar");
			if (toolbarData) {
				toolbarData.destroy();
			}
			unbindWatcher();
		});
	}

	public static add(m: ng.IModule): void {
		m.directive(ToolbarDirective.DIRECTIVE_NAME, () => {
			return new ToolbarDirective();
		});
	}
}
