import { AngularConstants } from "../../../../../common/angular-constants";

import { IJsonLocalIdoDatasource } from "ng2/common/model/databearer/dataservice/json-datasource.model";
import { IDataGridColumn, IDataGridOptions } from "ng2/common/model/sohoxi/sohoxi-datagrid.model";
import { IdoCollection, IdoProperty } from "../../../../../components/services/local-ido-service";

import { IDialogBaseScope } from "../../../../../common/defines";
import { CustomFormatters } from "../../../../../common/sohoxi/custom-formatters";
import { RepositoryHelper } from "../../../repository-helper";

export interface ILocalIdoParameter {
	operation: IdoCollection;
	properties: IdoProperty[];
	datasource?: IJsonLocalIdoDatasource;
}

export class LocalIdoOperationDetails {
	public static $inject = [
		AngularConstants.SCOPE,
	];

	public get operation(): IdoCollection {
		return this.$scope.eirDialog.parameter.operation;
	}

	public get properties(): IdoProperty[] {
		return this.$scope.eirDialog.parameter.properties;
	}

	public parameterDatagridOptions: IDataGridOptions<any>;

	constructor(private $scope: IDialogBaseScope<ILocalIdoParameter>) {
		this.initParameterDatagrid();

		this.parameterDatagridOptions.dataset = this.properties.map(property => {
			property.dataType = RepositoryHelper.getAppbuilderDataType(property.dataType);
			return property;
		});
	}

	private initParameterDatagrid(): any {
		const columns: IDataGridColumn[] = [
			{
				id: "propertyName",
				name: "Property Name",
				field: "propertyName",
				sortable: true,
				width: "17%",
				formatter: Soho.Formatters.Ellipsis,
			},
			{
				id: "dataType",
				name: "Data Type",
				field: "dataType",
				width: "17%",
				sortable: false
			},
			{
				id: "dataLength",
				name: "Length",
				field: "dataLength",
				sortable: false,
				width: "17%"
			},
			{
				id: "isReadOnly",
				name: "Read Only",
				field: "isReadOnly",
				sortable: false,
				width: "17%",
				formatter: CustomFormatters.RequiredFormatter("isReadOnly")
			},
			{
				id: "isRequired",
				name: "Required",
				field: "isRequired",
				sortable: false,
				width: "17%",
				formatter: CustomFormatters.RequiredFormatter("isRequired")
			},
			{
				id: "keySequence",
				name: "Primary Key",
				field: "keySequence",
				sortable: false,
				width: "17%",
				formatter: CustomFormatters.RequiredFormatter("keySequence", true)
			}
		];

		this.parameterDatagridOptions = {
			columns: columns,
			dataset: [],
			selectable: false,
			isList: true,
			saveColumns: false,
			enableTooltips: true,
		};
	}

	static add(module: ng.IModule): void {
		module.controller(
			"eirLocalIdoOperationDetails",
			LocalIdoOperationDetails
		);
	}
}
