import { AngularConstants } from "../../../../../../common/angular-constants";

import { IDialogBaseScope } from "../../../../../../common/defines";
import { DialogButtonType, IDialog, IDialogResult } from "../../../../../../common/services/dialog-service";
import { IValidationResult, IValidatorError } from "../../../../../../common/shared-components/base/field-validation";
import { IOnComponentChangeNotification, NotifiableComponent } from "../../../../../../common/shared-components/base/notifiable-component";

export class AddNewColumnDialogCtrl extends NotifiableComponent {
	public dialog: IDialog<string[]>;
	public columnName: string = "";
	private columnNames: string[] = [];
	public errorList: IValidatorError[] = [];
	public get isNameInvalid(): boolean {
		return this.errorList.length !== 0;
	}
	static $inject = [
		AngularConstants.SCOPE
	];

	constructor(
		scope: IDialogBaseScope<string[]>
	) {
		super();
		const dialog: IDialog<string[]> = scope.eirDialog;
		this.dialog = dialog;
		this.errorList = [];
		this.columnNames = this.dialog.parameter;
		this.columnName = "NewColumn" + (this.columnNames.length + 1);
	}

	public saveForm(): void {
		const result: IDialogResult<string> = {
			button: DialogButtonType.Ok,
			value: this.columnName
		};
		this.dialog.close(result);
	}

	public getValidation($data: IOnComponentChangeNotification): boolean | IValidationResult {
		const foundName = this.columnNames.find(item => item === $data.value);
		if (foundName) {
			return {
				status: false,
				errorMessage: `The column name '${$data.value}' is already in use.`
			};
		}
		return {
			status: true
		};
	}

	public close(): void {
		this.dialog.close();
	}

	static add(m: ng.IModule): void {
		m.controller("eirAddNewColumnDialogCtrl", AddNewColumnDialogCtrl);
	}
}