	public static instantiateComponent<T extends IComponent>(
		obj: IJsonComponent,
		$element: ng.IAugmentedJQuery,
		parentInstance: ILayoutComponent,
		frameworkService: FrameworkService
	): T {
		let componentClass: IStaticComponent;
		let instance: IComponent;
		if (obj) {
			if (Defines.allComponents[obj.typedef]) {
				componentClass = Defines.allComponents[obj.typedef].class;
			} else {
				throw new Error("Unknown class " + obj.typedef);
			}

			instance = new componentClass(obj, $element, parentInstance, frameworkService);
		}
		return instance as T;
	}

