import angular from "angular";

import { Helper } from "../../common/helper";
import type { ILayoutComponent } from "../../common/interface/ilayout-component";
import type { IJsonHyperlink, ISourceItem } from "../../common/model/component/hyperlink.model";
import { Component } from "../component";
import { FrameworkService } from "../services/framework-service";

import {
	BindingTypes, DirectionType, InteractionPairOperation, InteractionTriggerEnum,
	SettingInputKeys, SettingKeys, SidebarTabTypes
} from "ng2/common/constants/common.constant";
import { InteractionDefines } from "ng2/common/constants/interaction-defines.constant";

import { BindingTypeEnum } from "ng2/common/enums/binding-type.enum";
import { SizeTypeEnum } from "ng2/common/enums/size-type.enum";
import { ValueTypeEnum } from "ng2/common/enums/value-type.enum";

import type { IComponentSettingCategory } from "ng2/common/model/components/settings/component-setting-category.model";
import type { IDataPair } from "ng2/common/model/data-pair.model";
import type { IJsonSize } from "ng2/common/model/databearer/components/json-component.model";
import type { IJsonBindableField, IJsonValueSource } from "ng2/common/model/databearer/json-binding.model";

export class Hyperlink extends Component {
	static availableDirectionTypes: IDataPair[] = [
		{ name: "None", value: DirectionType.NONE },
		{ name: "Back", value: DirectionType.BACK },
		{ name: "Forward", value: DirectionType.DIRECTIONAL }
	];
	static DEFAULT_WIDTH: IJsonSize = { sizeType: SizeTypeEnum.AUTO };
	static DEFAULT_HEIGHT: IJsonSize = { sizeType: SizeTypeEnum.AUTO };
	static SOURCE_TRIGGERS: IDataPair[] =
		InteractionDefines.SOURCE_TRIGGERS.concat([InteractionPairOperation.ON_CLICK]);
	static TARGET_ACTIONS: IDataPair[] = InteractionDefines.TARGET_ACTIONS;
	static DEFAULT_ACTION: IDataPair = InteractionPairOperation.GET;
	static DEFAULT_TRIGGER: IDataPair = InteractionPairOperation.ON_CLICK;
	static BINDABLE_FIELDS_2: IJsonBindableField[] = [{
		name: "Link Name",
		ref: SettingKeys.CONTENT,
		valueType: ValueTypeEnum.LOCALIZED_STRING,
		write: true,
		read: true,
		defaultValue: { Type: BindingTypeEnum.STATIC_VALUE, value: "" }
	},
	{
		name: "Link Value",
		ref: "renderedLink",
		valueType: ValueTypeEnum.STRING,
		write: false,
		read: true
	}];
	static SETTING_FIELDS: IComponentSettingCategory[] =
		([{
			title: "Style",
			tab: SidebarTabTypes.STYLE,
			settings: [{
				title: "Disabled",
				ref: SettingKeys.IS_READONLY,
				inputType: SettingInputKeys.CHECKBOX,
				defaultValue: false,
				button: {
					icon: "#icon-unlocked",
					tooltip: "Disable Component When...",
					dialog: true
				}
			}]
		},
		{
			title: "Caret",
			tab: SidebarTabTypes.STYLE,
			settings: [{
				ref: SettingKeys.DIRECTION,
				inputType: SettingInputKeys.RADIO,
				dataset: Hyperlink.availableDirectionTypes,
				defaultValue: DirectionType.NONE
			}]
		},
		{
			title: "Link Value",
			tab: SidebarTabTypes.CONTENT,
			settings:
			[
				{
					title: "Address",
					ref: SettingKeys.LINK_VALUE,
					inputType: SettingInputKeys.TEXT,
					button: {
						icon: "#icon-compose",
						tooltip: "Build link",
						dialog: true
					},
					defaultValue: "https://"
				},
				{
					title: "Use as Drillback",
					ref: SettingKeys.IS_DRILLBACK,
					inputType: SettingInputKeys.CHECKBOX,
					defaultValue: false
				}
			]
		}] as IComponentSettingCategory[]).concat(Component.SETTING_FIELDS);

	content: string;
	renderedLink: string;
	direction: string;
	sources: ISourceItem[] = [];
	link: string;
	isDrillback: boolean;

	constructor(
		obj: IJsonHyperlink,
		element: ng.IAugmentedJQuery,
		parent: ILayoutComponent,
		frameworkService: FrameworkService
	) {
		super(obj, element, parent, frameworkService);

		this.direction = obj.direction;
		this.link = obj.link;
		this.isDrillback = obj.isDrillback;
		if (obj.sources) {
			this.sources = obj.sources;
		}
	}

	setBindableValue(bindableRef: string, value: any): void {
		super.setBindableValue(bindableRef, value);
		if (this.sources) {
			const mySource = this.sources.find((source) => {
				return source.alias === bindableRef;
			});
			if (mySource) {
				this.renderURL();
			}
		}
	}

	initializeBindings(): void {
		super.initializeBindings();
		if (this.sources) {
			this.sources.forEach((source) => {
				const valueSourceObject: IJsonValueSource = {
					Type: BindingTypeEnum.BINDING,
					sourceKey: source.value,
					sourceName: source.type
				};
				this.frameworkService.registerBinding(this, source.alias, valueSourceObject, null);
			});
			this.renderURL();
		}
	}

	onLinkClick(): void {
		if (this.isDrillback) {
			this.frameworkService.sendDrillbackMessage(this.renderedLink);
		} else {
			this.frameworkService.openWindow(this.renderedLink);
		}
		this.trigger(InteractionTriggerEnum.CLICK);
	}

	renderURL(): void {
		this.renderedLink = this.buildLink(this.link, this.sources);
	}

	static directive = [() => {
		return {
			template: require("./hyperlink.html"),
			replace: true
		};
	}];
}
