import { IDatePickerOptions } from "../../model/sohoxi/sohoxi-datepicker.model";

/**
 * Datepicker directive.
 *
 * Example:
 * scope["options"] = {	dateFormat: "YYYY/M/D" }
 *
 * <input xi-datepicker="options" id="date-field" class="datepicker" type="text"/>
 */
export class DatepickerDirective implements ng.IDirective {
	private static readonly DIRECTIVE_NAME = "xiDatepicker";
	public restrict = "A";
	public scope = false;

	public link(
		scope: ng.IScope,
		element: ng.IAugmentedJQuery,
		attributes: ng.IAttributes
	): void {
		const options = attributes[DatepickerDirective.DIRECTIVE_NAME];

		const unbindWatcher = scope.$watch(
			options,
			(
				newOptions: IDatePickerOptions,
				oldOptions: IDatePickerOptions
			) => {
				if (newOptions) {
					element.datepicker(newOptions);
				}
			}
		);
		element.on("$destroy", () => {
			unbindWatcher();
		});
	}

	public static add(m: ng.IModule): void {
		m.directive(DatepickerDirective.DIRECTIVE_NAME, () => {
			return new DatepickerDirective();
		});
	}
}
