import { AngularConstants } from "../../angular-constants";

export abstract class CacheableCtrl {

	public static $inject = [
		AngularConstants.CACHE_FACTORY
	];

	private cacheObject: ng.ICacheObject;

	constructor(
		$cacheFactory: ng.ICacheFactoryService
	) {
		this.setCacheObject($cacheFactory);
	}

	private setCacheObject($cacheFactory: ng.ICacheFactoryService): void {
		const cacheId = (this.constructor as any).name;
		const cacheObject = $cacheFactory.get(cacheId);

		this.cacheObject = cacheObject === undefined ?
			$cacheFactory(cacheId) :
			cacheObject;
	}

	protected getFromCache<T>(key: string): T {
		return this.cacheObject.get(key);
	}

	protected saveToCache<T>(key: string, value: T): void {
		this.cacheObject.put<T>(key, value);
	}
}
