import angular from "angular";

import {
	DashboardStates, DEFAULT_BOUNDARY, FlexSizeTypeEnum, INewProject,
	InjectNames, LanguageConsts, MimeTypes, Numbers, ProjectFileKeys,
	ProjectLayout, SettingKeys,
} from "ng2/common/constants/common.constant";
import { OrientationEnum } from "ng2/common/enums/orientation.enum";
import { IJsonLayoutComponent } from "ng2/common/model/databearer/json-layout-component.model";
import { IJsonDesignerMashup } from "ng2/common/model/databearer/json-mashup.model";
import { IJsonLanguageComponent, ILanguage } from "ng2/common/model/language.model";
import { DesignerMashup } from "ng2/common/project/designer-mashup";
import { DesignerManifest } from "ng2/common/project/manifest/designer-manifest";
import { FileMetaData } from "../../../../../app-builder/common/project/manifest/file-metadata";
import { AppState } from "../../../../app.state";
import { AngularConstants } from "../../../../common/angular-constants";
import { IErrorResponse } from "../../../../common/error-response";
import { IValidatorError } from "../../../../common/shared-components/base/field-validation";

import { Helper } from "../../../../common/helper";

import { IJsonFlexbox } from "ng2/common/model/databearer/components/json-flexbox.model";
import { DialogService } from "../../../../common/services/dialog-service";
import { FlexboxSettings } from "../../../editor/component-settings/flexbox/flexbox-settings";
import { EditorService } from "../../../editor/services/editor-service";
import { ProjectService } from "../../../services/project-service";

export class CreateToolbarComponent {
	public state: number;
	public newProject: INewProject;

	public static $inject = [
		AngularConstants.STATE_SERVICE,
		AngularConstants.TIMEOUT,
		InjectNames.EDITOR_SERVICE,
		InjectNames.PROJECT_SERVICE,
		InjectNames.DIALOG_SERVICE
	];

	private errorList: IValidatorError[];
	public get isDisabled(): boolean {
		return this.errorList.length !== 0;
	}

	constructor(
		private $state: ng.ui.IStateService,
		private $timeout: ng.ITimeoutService,
		private editorService: EditorService,
		private projectService: ProjectService,
		private dialogService: DialogService,
	) {
		this.$timeout(() => {
			angular.element("#new-project-name-field").focus();
		}, 1);
	}

	public setState(): void {
		this.state = DashboardStates.LIST;
	}

	public createProject(): void {
		const hasError = !this.newProject.projectName
			|| !this.newProject.templateNumber;

		if (hasError) {
			this.showCreateProjectErrorMessage("Please fill in a project name and choose a template");
		} else {
			const newProjectDatabearer = this.prepareNewProject(this.newProject);

			this.projectService.createProject(newProjectDatabearer)
				.then((mashup: DesignerMashup) => {
					this.editorService.clearIdCounter();
					this.editorService.setCurrentBoundary(DEFAULT_BOUNDARY);
					this.$state.go(AppState.EDITOR.name, {
						id: mashup.manifest.id,
						reloadProject: true, // MUPS-2682: reloadProject
					});
				}).catch((errorResponse: IErrorResponse) => {
					this.showCreateProjectErrorMessage(errorResponse.message);
				});
		}
	}

	private prepareNewProject(newProjectDefinition: INewProject): IJsonDesignerMashup {
		const manifest = new DesignerManifest(this.newProject.projectName, this.newProject.projectDescription);
		const rootComponent = this.getTemplate(this.newProject.templateNumber);
		manifest.fileMetaData[ProjectFileKeys.ROOT_COMPONENT] =
			new FileMetaData(rootComponent.id, MimeTypes.COMPONENT);
		manifest.mashupBoundaries = [DEFAULT_BOUNDARY];
		const headerTranslations: ILanguage[] = [{
			locale: LanguageConsts.DEFAULT.key,
			value: manifest.title
		}];
		const translations: IJsonLanguageComponent[] = [
			{
				id: Helper.generateUniqueId(),
				componentId: rootComponent.id,
				property: SettingKeys.APPLICATION_HEADER,
				translations: headerTranslations
			}
		];
		const tags = newProjectDefinition.tags;
		return { manifest, rootComponent, translations, tags };
	}

	private showCreateProjectErrorMessage(message: string): void {
		this.dialogService.showError("Create New Project", message);
	}

	// Page templates for new projects and insertions into layout components
	// TODO ids to better explained layouts
	private getTemplate(id: number): IJsonLayoutComponent {
		let newIDs: string[];
		let template: IJsonFlexbox;

		const flexPanelClassName = "Flexbox";
		const templateComponent = FlexboxSettings.getInstance().createWithoutPadding("AppPanel");
		switch (id) {
			case ProjectLayout.ONE:
				return templateComponent;
			case ProjectLayout.TWO:
				newIDs = this.generateIds(flexPanelClassName, Numbers.TWO);
				return this.buildTemplate(templateComponent, newIDs, OrientationEnum.HORIZONTAL);
			case ProjectLayout.THREE:
				newIDs = this.generateIds(flexPanelClassName, Numbers.THREE);
				return this.buildTemplate(templateComponent, newIDs, OrientationEnum.HORIZONTAL);
			case ProjectLayout.FOUR:
				newIDs = this.generateIds(flexPanelClassName, Numbers.TWO);
				template = this.buildTemplate(templateComponent, newIDs, OrientationEnum.HORIZONTAL);

				(template.children[0] as IJsonFlexbox).boundarySettings[DEFAULT_BOUNDARY.id].sizeType =
					FlexSizeTypeEnum.PIXELS;
				(template.children[0] as IJsonFlexbox).boundarySettings[DEFAULT_BOUNDARY.id].size =
					Numbers.THREE_HUNDRED;

				return template;
			case ProjectLayout.FIVE:
				newIDs = this.generateIds(flexPanelClassName, Numbers.FOUR);
				template = this.buildTemplate(templateComponent, newIDs.slice(0, Numbers.TWO), OrientationEnum.HORIZONTAL);

				(template.children[0] as IJsonFlexbox).boundarySettings[DEFAULT_BOUNDARY.id].sizeType =
					FlexSizeTypeEnum.PIXELS;
				(template.children[0] as IJsonFlexbox).boundarySettings[DEFAULT_BOUNDARY.id].size =
					Numbers.THREE_HUNDRED;

				this.buildTemplate(template.children[1] as any, newIDs.slice(Numbers.TWO, Numbers.FOUR));

				return template;
			case ProjectLayout.SIX:
				newIDs = this.generateIds(flexPanelClassName, Numbers.TWO);
				template = this.buildTemplate(templateComponent, newIDs);

				return template;
			case ProjectLayout.SEVEN:
				newIDs = this.generateIds(flexPanelClassName, Numbers.THREE);
				template = this.buildTemplate(templateComponent, newIDs);

				return template;
			case ProjectLayout.EIGHT:
				newIDs = this.generateIds(flexPanelClassName, Numbers.SIX);
				template = this.buildTemplate(templateComponent, newIDs.slice(0, Numbers.TWO));

				this.buildTemplate(
					template.children[0] as any,
					newIDs.slice(Numbers.TWO, Numbers.FOUR),
					OrientationEnum.HORIZONTAL);
				this.buildTemplate(
					template.children[1] as any,
					newIDs.slice(Numbers.FOUR, Numbers.SIX),
					OrientationEnum.HORIZONTAL);

				return template;
			case ProjectLayout.NINE:
				newIDs = this.generateIds(flexPanelClassName, Numbers.FOUR);
				template = this.buildTemplate(templateComponent, newIDs.slice(0, Numbers.TWO));

				this.buildTemplate(
					template.children[0] as any,
					newIDs.slice(Numbers.TWO, Numbers.FOUR),
					OrientationEnum.HORIZONTAL);

				return template;
			case ProjectLayout.TEN:
				newIDs = this.generateIds(flexPanelClassName, Numbers.FOUR);
				template = this.buildTemplate(templateComponent, newIDs.slice(0, Numbers.TWO));

				this.buildTemplate(
					template.children[1] as any,
					newIDs.slice(Numbers.TWO, Numbers.FOUR),
					OrientationEnum.HORIZONTAL);

				return template;
			case ProjectLayout.ELEVEN:
				newIDs = this.generateIds(flexPanelClassName, Numbers.THREE);
				template = this.buildTemplate(templateComponent, newIDs);

				(template.children[1] as IJsonFlexbox).boundarySettings[DEFAULT_BOUNDARY.id].size = Numbers.TWO;
				(template.children[Numbers.TWO] as IJsonFlexbox).boundarySettings[DEFAULT_BOUNDARY.id].size = Numbers.TWO;

				return template;

			case ProjectLayout.TWELVE:
				newIDs = this.generateIds(flexPanelClassName, Numbers.SEVEN);
				template = this.buildTemplate(templateComponent, newIDs.slice(0, Numbers.THREE));

				(template.children[1] as IJsonFlexbox).boundarySettings[DEFAULT_BOUNDARY.id].size = Numbers.TWO;
				(template.children[Numbers.TWO] as IJsonFlexbox).boundarySettings[DEFAULT_BOUNDARY.id].size = Numbers.TWO;

				this.buildTemplate(
					template.children[1] as any,
					newIDs.slice(Numbers.THREE, Numbers.FIVE),
					OrientationEnum.HORIZONTAL);
				this.buildTemplate(
					template.children[Numbers.TWO] as any,
					newIDs.slice(Numbers.FIVE, Numbers.SEVEN),
					OrientationEnum.HORIZONTAL);

				return template;
			case ProjectLayout.THIRTEEN:
				newIDs = this.generateIds(flexPanelClassName, Numbers.FOUR);
				template = this.buildTemplate(templateComponent, newIDs.slice(0, Numbers.TWO));
				template.boundarySettings[DEFAULT_BOUNDARY.id].orientation = OrientationEnum.VERTICAL;

				(template.children[1] as IJsonFlexbox).boundarySettings[DEFAULT_BOUNDARY.id].size = Numbers.FOUR;

				this.buildTemplate(
					template.children[1] as any,
					newIDs.slice(Numbers.TWO, Numbers.FOUR),
					OrientationEnum.HORIZONTAL);

				return template;
			default:
				console.warn("[DashboardCtrl] Invalid template chosen");
		}

		return null;
	}

	private buildTemplate(
		template: IJsonFlexbox,
		numberOfCells: string[],
		orientation: OrientationEnum = OrientationEnum.VERTICAL
	): IJsonFlexbox {
		const cells: IJsonFlexbox[] = [];
		for (let i = 0; i < numberOfCells.length; i++) {
			cells[i] = FlexboxSettings.getInstance().create(numberOfCells[i]);
			template.children.push(cells[i]);
		}
		template.boundarySettings[DEFAULT_BOUNDARY.id].orientation = orientation;
		return template;
	}

	private generateIds(componentTypeName: string, numberOfIds: number): string[] {
		const newIDs: string[] = [];

		for (let index = 0; index < numberOfIds; index++) {
			newIDs[index] = componentTypeName + index;
		}

		return newIDs;
	}

	static add(m: ng.IModule): void {
		m.component("eirCreateToolbar", {
			bindings: {
				state: "=",
				newProject: "<",
				errorList: "<",
			},
			template: require("./create-toolbar.html"),
			controller: CreateToolbarComponent,
			controllerAs: "ctrl",
		});
	}
}
