/**
 * Service used by components in runtime to communicate with our Framework
 */
export class FrameworkService {

	datasourceChangedEvent = new InstanceEvent<string>();
	currentCanvasThemeChangedEvent: InstanceEvent<ThemeEnum>;
	projectSettingsChangedEvent: InstanceEvent<DesignerManifest>;

	static $inject = [
		AngularConstants.WINDOW,
		AngularConstants.COMPILE,
		AngularConstants.Q,
		AngularConstants.TIMEOUT,
		AngularConstants.CACHE_FACTORY,
		InjectNames.COMMON_SERVICE,
		InjectNames.DATASOURCE_SERVICE,
		InjectNames.LANGUAGE_SERVICE,
		InjectNames.TRANSLATION_MANAGEMENT_SERVICE,
		InjectNames.MINGLE_SERVICE,
		InjectNames.EXPRESSION_SERVICE,
		InjectNames.DIALOG_SERVICE
	];

	constructor(
		private $window: ng.IWindowService,
		private $compile: ng.ICompileService,
		private $q: ng.IQService,
		private $timeout: ng.ITimeoutService,
		private $cacheFactoryService: ng.ICacheFactoryService,
		private commonService: CommonService,
		private datasourceService: DatasourceService,
		private languageService: LanguageService,
		private translationManagementService: TranslationManagementService,
		private mingleService: MingleService,
		private expressionService: ExpressionService,
		private dialogService: DialogService
	) {
		this.currentCanvasThemeChangedEvent = commonService.currentCanvasThemeChangedEvent;
		this.projectSettingsChangedEvent = commonService.projectSettingsChangedEvent;

		commonService.currentBoundaryChangedEvent.on((newBoundary) => {
			const rootComponent = commonService.getCurrentRootComponentInstance();
			if (rootComponent) {
				rootComponent.newBoundaryEntered(newBoundary.id);
			}
		});
	}

	openComponentDialog(options: IDialogOptions<any>): IPromise<IDialogResult<any>> {
		return this.dialogService.show(options);
	}

	/**
	* Backwards compatibility, checks all components if they have a languageComponent
	* if not, one is created for that component
	* @private
	* @memberof FrameworkService
	*/
	createLanguageComponentFromOldComponents(): void {
		const rootComponent = this.commonService.getCurrentRootComponentInstance();
		// TODO: for applicationHeader, remove when header is a component
		const currentManifest = this.getCurrentProject().getManifest();
		this.translationManagementService.createGeneralLanguageComponent(
			rootComponent.getId(),
			SettingKeys.APPLICATION_HEADER,
			currentManifest.title
		);

		this.projectSettingsChangedEvent.raise(currentManifest);
		rootComponent.forEachChild((componentInstance) => {
			componentInstance.forEachBindable((binding, currentValueSource) => {
				const isLocalized = binding.valueType === ValueTypeEnum.LOCALIZED_STRING;
				if (isLocalized) {
					this.translationManagementService.createGeneralLanguageComponent(
						componentInstance.getId(),
						binding.ref,
						currentValueSource.value
					);
				}
			});
			switch (componentInstance.getTypedef()) {
				case ComponentNames.Tabs:
					(componentInstance as ITabsComponent).componentTabs.forEach((tab) => {
						this.translationManagementService.createGeneralLanguageComponent(
							componentInstance.getId(),
							tab.id,
							tab.name
						);
					});
					break;
				case ComponentNames.RadioButton:
					(componentInstance as IRadioButtonComponent)
						.radioButtons.forEach((radiobutton) => {
							this.translationManagementService.createGeneralLanguageComponent(
								componentInstance.getId(),
								radiobutton.id,
								radiobutton.name
							);
						});
					break;
				case ComponentNames.Datagrid:
					(componentInstance as any).datagridOptions.columns
						.forEach((column: IExtendedDataGridColumn) => {
							this.translationManagementService.createGeneralLanguageComponent(
								componentInstance.getId(),
								column.id,
								column.name
							);

							if (column.text) {
								this.translationManagementService.createGeneralLanguageComponent(
									componentInstance.getId(),
									column.id + "CellText",
									column.text
								);
							}
						});
					break;
				default:
				// TODO: other cases
			}
		}, false);
	}

	openWindow(url: string): void {
		this.$window.open(url, "_blank");
	}

	/**
	 * get the current language
	 *
	 * @public
	 * @memberof FrameworkService
	 */
	getCurrentLanguage(): string {
		return this.translationManagementService.getCurrentLanguage();
	}

	getSelectedLocale(): string {
		return this.translationManagementService.getSelectedLocale();
	}

	/**
	 * Get the translatable string by the current language.
	 * Used in component.ts therefore in this service.
	 *
	 * @param {string} componentId
	 * @param {string} property
	 *	@param {string} currentLanguageKey
		*
		* @memberof FrameworkService
		*/
	getTranslatableStringByProperty(componentId: string, property: string): string {
		return this.translationManagementService.getTranslatableStringByProperty(componentId, property);
	}

	setTranslatableStringByProperty(
		componentId: string,
		property: string,
		updatedValue: ILanguage
	): void {
		this.translationManagementService.setTranslatableStringByProperty(componentId, property, updatedValue);
	}

	sendDrillbackMessage(message: string): void {
		this.mingleService.sendDrillbackMessage(message);
	}

	getGlobalValue(value: string, component: IComponent): string | null {
		let stringValue = null;
		if (!isNil(value) && typeof (value) !== "boolean") {
			const trimmedValue = value.replace(SubstitutionTags.VALUE, "");
			const cleanedValue = trimmedValue.match(RegexStrings.GLOBALSTRING);
			if (cleanedValue && cleanedValue[1]) {
				const globalValue = this.languageService.getGlobalString(cleanedValue[1]);
				if (globalValue) {
					stringValue = value.replace(cleanedValue[0], globalValue);
				} else {
					this.translationManagementService.openNewStringDialog(cleanedValue[1]).then((result) => {
						if (result.button === DialogButtonType.Ok) {
							this.translationManagementService.addGlobalString(result.value).then(() => {
								component.initializeBindings();
							});
						}
					});
				}
			}
		}
		return stringValue;
	}

	/**
	* get the translatable string on the header via translation management service
	* TODO: Remove when header is a component
	*
	* @returns {string}
	* @memberof FrameworkService
	*/
	getTranslatableHeaderString(): string {
		return this.translationManagementService.getTranslatableHeaderString();
	}

	getCurrentProject(): DesignerMashup {
		return this.commonService.getCurrentProject() as DesignerMashup;
	}

	getUrlMetaData(): IAvailableSourceField[] {
		return this.commonService.getUrlMetaData();
	}

	setUrlMetaData(newUrlMetaData: IAvailableSourceField[]): void {
		return this.commonService.setUrlMetaData(newUrlMetaData);
	}

	generateUrlMetaData(): ng.IPromise<DesignerMashup> {
		const project = this.commonService.getCurrentProject();
		return this.commonService.generateUrlMetaData(project) as ng.IPromise<DesignerMashup>;
	}

	getUrlValue(sourceKey: string): string {
		let value = "";
		const metaData = this.getUrlMetaData();
		const result = metaData.find( data => data.sourceKey === sourceKey);

		if (result && result.currentValue) {
			value = result.currentValue;
		}
		return value;
	}

	getTimeout(): ng.ITimeoutService {
		return this.$timeout;
	}

	getProjectCacheObject(): ng.ICacheObject {
		const cacheId  = `${this.getCurrentProject().getId()}`;
		return this.getCacheObject(cacheId);
	}

	getCacheObject(cacheId?: string): ng.ICacheObject {
		cacheId = cacheId !== undefined ?
			cacheId : `${this.getCurrentProject().getId()}`;

		const cacheObject = this.$cacheFactoryService.get(cacheId);

		return !cacheObject ?
			this.$cacheFactoryService(cacheId) : cacheObject;
	}

	removeCacheObject(cacheId: string): void {
		const cacheObject = this.getCacheObject(cacheId);

		cacheObject.destroy();
	}

	checkIfListTemplate(componentId: string): boolean {
		if (componentId === "TwoHorizontalLabels" || componentId === "ImageAndLabels" || componentId === "Stack") {
			return true;
		} else {
			return false;
		}
	}

	forEachBindable(
		component: IJsonComponent, fn: (field: IJsonBindableField, curr: IJsonValueSource) => void
	): void {
		Defines
			.allComponents[component.typedef]
			.class
			.BINDABLE_FIELDS_2.forEach((field: any) => {
				fn(field, (component as any)[field.ref]);
			});
	}

	/**
	 * Recursively iterate through all the components and run a function with the component
	 * as an input
	 *
	 * @param {IJsonComponent} component
	 * @param {(component: IJsonComponent, parent: IJsonLayoutComponent) => void} fn
	 *
	 * @memberof FrameworkService
	 */
	forEachComponent(
		component: IJsonComponent,
		fn: (component: IJsonComponent, parent: IJsonLayoutComponent) => void,
		parent: IJsonLayoutComponent = null
	): void {
		fn(component, parent);
		if (Defines.allComponents[component.typedef].isLayoutComponent) {
			(component as IJsonLayoutComponent).children.forEach((child: IJsonComponent) => {
				this.forEachComponent(child, fn, component);
			});
		}
	}

	getNextProjectBoundary(boundaryId: string): IBoundary {
		if (boundaryId === DEFAULT_BOUNDARY.id) {
			return DEFAULT_BOUNDARY;
		}
		const projectBoundaries = this.getCurrentProject().manifest.mashupBoundaries;

		if (projectBoundaries) {
			// Sort the array, could be method?
			projectBoundaries.sort((a, b) => {
				if ("Default" === a.id) {
					return 1;
				} else if ("Default" === b.id) {
					return -1;
				} else if (a.limitX < b.limitX) {
					return -1;
				} else if (b.limitX < a.limitX) {
					return 1;
				}

				return 0;
			});

			for (let i = 0; i < projectBoundaries.length; i++) {
				if (projectBoundaries[i].id === boundaryId) {
					return projectBoundaries[i + 1];
				}
			}
		}
		return DEFAULT_BOUNDARY;
	}

	triggerExpressions(): void {
		const components = this.getAllComponentInstances();
		this.expressionService.trigger(components);
	}

	setCanvasTheme(theme: ThemeEnum): void {
		this.commonService.setCanvasTheme(theme, this.isDesigner());
	}

	getCurrentBoundary(): IBoundary {
		return this.commonService.getCurrentBoundary();
	}

	getTranslatableFieldDisplayName(componentId: string, reference: string): string {

		let displayName = "";
		const componentInstance = this.getComponentInstance(componentId);

		if (componentInstance) {
			switch (componentInstance.getTypedef()) {
				case ComponentNames.Tabs: {
					const newTabIndex = ((componentInstance as ITabsComponent)
						.componentTabs
						.findIndex((tab) => {
							return tab.id === reference;
						}) + 1); // Index starting on 1 for humans
					displayName = "Tab " + newTabIndex;
					break;
				}
				case ComponentNames.RadioButton:
					if (reference === SettingKeys.LABEL) {
						displayName = this.getBindableDisplayName(reference, componentId);
					} else {
						const newRadioIndex = ((componentInstance as IRadioButtonComponent).radioButtons
							.findIndex((radiobutton) => {
								return radiobutton.id === reference;
							}) + 1); // Index starting on 1 for humans
						displayName = "Radio Button " + newRadioIndex;
					}
					break;
				case ComponentNames.Datagrid: {
					if (reference === SettingKeys.LABEL) {
						displayName = this.getBindableDisplayName(reference, componentId);
					} else {
						let cellName = "";
						if (reference.includes("CellText")) {
							cellName = " Cell Text";
						}
						const newColumnIndex = (componentInstance as any).datagridOptions.columns
							.findIndex((column: IExtendedDataGridColumn) => {
								return column.id + cellName.replace(/\s+/g, "") === reference;
							}) + 1; // Index starting on 1 for humans
						displayName = "Column " + newColumnIndex + cellName;
					}
					break;
				}
				case "Lookup": {
					if (reference === SettingKeys.LABEL) {
						displayName = this.getBindableDisplayName(reference, componentId);
					} else {
						const newColumnIndex = (componentInstance.getDatabearer() as IJsonLookup).columns
							.findIndex((column) => {
								return column.id === reference;
							}) + 1; // Index starting on 1 for humans
						displayName = "Column " + newColumnIndex;
					}
					break;
				}
				default:
				//  more cases
			}

			if (!displayName) {
				displayName = this.getBindableDisplayName(reference, componentId);
			}
		}

		return displayName;
	}

	private getBindableDisplayName(reference: string, componentId: string): string {
		let displayName = "";
		const componentInstance = this.getComponentInstance(componentId);
		const staticClass = Defines.allComponents[componentInstance.getTypedef()].class;
		const bindables: IJsonBindableField[] = staticClass.BINDABLE_FIELDS_2;
		if (bindables.length > 0) {
			const maybeDefinition = bindables.find((bindableDefinition) => {
				return bindableDefinition.ref === reference;
			});
			displayName = maybeDefinition ? maybeDefinition.name : displayName;
		}
		return displayName;
	}

	/**
	 * Get component instance with given componentID
	 *
	 * @param componentId
	 * @param fromRoot (optional) if not given will default to ApplicationParent
	 */
	getComponentInstance(componentId: string, fromRoot?: IComponent): IComponent {
		if (!componentId) {
			return null;
		}
		const recursive = (component: IComponent) => {
			let foundChild: IComponent;
			if (component.getId() === componentId) {
				return component;
			} else if (Helper.instanceOfLayoutComponent(component)) {
				for (const index in component.children) {
					foundChild = recursive(component.children[index]);
					if (foundChild) {
						return foundChild;
					}
				}
			} else if (Helper.instanceOfListComponent(component)) {
				for (const index in component.children) {
					foundChild = recursive(component.children[index]);
					if (foundChild) { // Return first one of the generated instances
						return foundChild;
					}
				}
			}

			return null;
		};
		if (!fromRoot) {
			const rootComponent = this.commonService.getCurrentRootComponentInstance();
			fromRoot = rootComponent.parent;
		}
		return recursive(fromRoot);
	}

	/*
		* Get all component instances that the attribute has the specified value
	*/
	getComponentInstances(attrName: string, attrValue: string, fromRoot?: IComponent): IComponent[] {
		const components: IComponent[] = [];
		if (!fromRoot) {
			fromRoot = this.commonService.getCurrentRootComponentInstance();
		}
		fromRoot.forEachChild((child) => {
			if (Helper.getDeepValue(child, attrName) === attrValue) {
				components.push(child);
			}
		}, true);
		return components;
	}

	/*
		* Get all component instances
	*/
	getAllComponentInstances(): IComponent[] {
		const components: IComponent[] = [];
		const rootComponent = this.commonService.getCurrentRootComponentInstance();
		rootComponent.forEachChild((child) => {
			components.push(child);
		}, true);
		return components;
	}

	getCustomComponentDefinition(customId: string): ICustomComponentDefinition {
		return this.commonService.getCurrentProject().getCustomComponentDefinitions()[customId];
	}

	getNewDeferred<T>(): ng.IDeferred<T> {
		return this.$q.defer<T>();
	}

	isRuntimeMode(): boolean {
		// MUPS-495 should be applied here:
		// A better way to get the runtimeMode would be to save it something global, eg.the rootscope
		const container = angular.element("." + CSSClasses.mashupRootContainer);
		return container.length === 0 || container.hasClass("runtimeMode");
	}

	isDesigner(): boolean {
		return this.commonService.isDesigner();
	}

	getDatasource(datasourceId: string): IJsonDatasource {
		return this.commonService.getCurrentProject().getDatasource(datasourceId);
	}

	/**
	 *
	 * @param {string} datasourceId
	 * @returns {IDatasource}
	 *
	 * @memberOf FrameworkService
	 */
	getDatasourceObject(datasourceId: string): IDatasource {
		const datasourceObject: IJsonDatasource = this.commonService.getCurrentProject().getDatasource(datasourceId);
		let datasourceInstance: IDatasource;
		if (datasourceObject) {
			datasourceInstance = this.datasourceService.parseDatasourceToTS(datasourceObject);
		}
		return datasourceInstance;
	}

	readParameters(
		parameters: IInteractionParameter[],
		context: IMap<string>
	): IMap<any> {
		const values: IMap<any> = {};
		parameters.forEach((parameter) => {
			const value = this.readValueSource(parameter.valueSource, context);
			if (value) {
				//Assume %% is a delimiter
				const multipleValue = value.split("%%");
				if (multipleValue.length > 1 && parameter.valueSource.Type !== BindingTypeEnum.STATIC_VALUE) {
					for (let i = 0; i < multipleValue.length; i++) {
						multipleValue[i] = Helper.trimIfPossible(multipleValue[i]);
					}
					values[parameter.targetKey] = multipleValue;
				} else {
					values[parameter.targetKey] = Helper.trimIfPossible(value); // Trim used to fix MI problem
				}
			} else {
				values[parameter.targetKey] = "";
			}
		});
		return values;
	}

	setParameter<T>(parameter: IInteractionParameter, newValue: T): void {
		const valueSource = parameter.valueSource;
		const sourceComponent = this.getComponentInstance(valueSource.sourceName);
		const path = valueSource.sourceKey;

		Helper.setDeepValue(sourceComponent, path, newValue);
	}

	/**
	 * Method for applying string concatenation formatter on Binding and Context type bindings
	 *
	 * @param valuesourceObject
	 * @param value
	 * @returns valuesourceObject.value with {$value} substitution of value parameter
	 */
	private applyValuesourceFormatter(value: string, newValue: string): string {
		// Fallback for old projects that (unnessesarily) saved current value in the "value"-property
		if (value && value.indexOf(SubstitutionTags.VALUE) > -1) {
			return Helper.joinWithDefauldEmptyString(
				value.split(SubstitutionTags.VALUE),
				newValue
			);
		} else {
			return newValue;
		}
	}

	readValueSource(valueSource: IJsonValueSource, context: IMap<string>): string | null {
		let value: string;
		if (valueSource.Type === BindingTypeEnum.BINDING) {
			const sourceComponent = this.getComponentInstance(valueSource.sourceName);
			if (sourceComponent && valueSource.sourceKey) {
				const result = sourceComponent.getValue(valueSource.sourceKey);
				value = result === null ? "" : result;
				value = this.applyValuesourceFormatter(valueSource.value, value);
			} else {
				if (!sourceComponent) {
					console.error("[DatasourceService] BINDING failed, source component not found");
				}
				if (!valueSource.sourceKey) {
					console.error("[DatasourceService] BINDING failed, source key not set");
				}
				value = null;
			}
		} else if (valueSource.Type === BindingTypeEnum.CONTEXT_BINDING) {
			if (context) {
				value = context[valueSource.sourceKey];
				value = this.applyValuesourceFormatter(valueSource.value, value);
			} else {
				console.error("[DatasourceService] BINDING failed, no context for CONTEXT_BINDING");
			}
		} else if (valueSource.Type === BindingTypeEnum.URL_BINDING) {
			if (this.isRuntimeMode()) {
				value = this.getUrlValue(valueSource.sourceKey);
			} else {
				value = valueSource.value;
			}
		} else {
			// Static value
			value = valueSource.value;
		}

		return value;
	}

	private activeBindings: IMap<Function> = {};

	getActiveBinding(): IMap<Function> {
		return this.activeBindings;
	}

	registerContextBinding(
		targetComponent: IComponent,
		targetKey: string,
		sourceKey: string,
		value: string
	): void {
		const targetComponentScope = targetComponent.getScope();
		if (targetComponentScope) {
			const targetComponentId = targetComponent.isGenerated ?
				targetComponent.generatedId : targetComponent.getId();

			const bindingId = this.getContextBindingId(targetComponentId, targetKey, sourceKey);
			if (this.activeBindings[bindingId]) {
				this.activeBindings[bindingId]();
			}
			const dereg = targetComponentScope.$watch(
				"context[\"" + sourceKey + "\"]",
				(newValue: string) => {
					newValue = this.applyValuesourceFormatter(value, newValue);
					targetComponent.setBindableValue(targetKey, newValue ? newValue : "");
				});
			this.activeBindings[bindingId] = dereg;

			targetComponentScope.$on(AngularConstants.DESTROY, () => {
				dereg();
				delete this.activeBindings[bindingId];
			});
		} else {
			console.warn("[FrameworkService] No element/scope on target component, CONTEXT_BINDING not registered");
		}
	}

	getBindingId(targetComponentId: string, targetKey: string, valueSourceObject: IJsonValueSource): string {
		const sourceName = valueSourceObject.sourceName;
		const sourceKey = valueSourceObject.sourceKey;
		return sourceName + sourceKey + targetComponentId + targetKey;
	}

	getContextBindingId(targetComponentId: string, targetKey: string, sourceKey: string): string {
		return "context" + sourceKey + targetComponentId + targetKey;
	}

	registerBinding(
		targetComponent: IComponent,
		targetKey: string,
		valuesourceObject: IJsonValueSource,
		value: string
	): void {
		const sourceComponentId = valuesourceObject.sourceName;
		const sourceComponentKey = valuesourceObject.sourceKey;
		const isLayoutComponentWithDatasource = (component: IComponent) => {
			return component.isLayoutComponent() && component.getDatasource() !== undefined;
		};
		const sourceComponent = this.getComponentInstance(sourceComponentId);
		if (sourceComponent) {
			const bindingId = this.getBindingId(targetComponent.getId(), targetKey, valuesourceObject);
			if (this.activeBindings[bindingId]) {
				this.activeBindings[bindingId]();
			}
			let sourcePath = Helper.wrapKeyInBrackets(sourceComponentKey);
			sourcePath = isLayoutComponentWithDatasource(sourceComponent) ?
				".scope.context" + sourcePath : sourcePath;
			const watchKey = "componentInstance" + sourcePath;
			const dereg = sourceComponent.getScope().$watch(
				watchKey,
				(newValue: string) => {
					if (newValue === undefined || newValue === null) {
						newValue = "";
					}
					newValue = this.applyValuesourceFormatter(value, newValue);
					targetComponent.setBindableValue(targetKey, newValue);
					targetComponent.onUpdateValue();
				});

			this.activeBindings[bindingId] = dereg;
			sourceComponent.getScope().$on(AngularConstants.DESTROY, () => {
				dereg();
				delete this.activeBindings[bindingId];
			});
		} else {
			console.warn(`[FrameworkService] Dead binding on component id: ${targetComponent.getId()}`);
		}
	}

	trigger(source: Component, triggerName: string): void {
		this.commonService.getCurrentProject().getInteractions().forEach((interaction) => {
			if (interaction.sourceName === source.getId() &&
				interaction.sourceTriggerName === triggerName
			) {
				this.runInteraction(interaction);
			}
		});
	}

	runInteraction(interaction: Interaction): void {
		if (interaction.targetName && interaction.targetActionName) {
			const targetCtrls = this.getComponentInstances("databearer.id", interaction.targetName);
			targetCtrls.forEach((component: IComponent) => {
				component.loadInteraction(interaction);
			});
		} else {
			console.warn("[FrameworkService] Interaction: " + interaction.getId() +
				" did not run, not all prerequisites were present");
		}
	}

	runStartupInteractions(): void {
		const startupInteraction = this.commonService.getCurrentProject().getInteractions().filter((ev) => {
			return (ev.sourceTriggerName === InteractionTriggerEnum.STARTUP);
		});

		startupInteraction.forEach((ev) => {
			this.runInteraction(ev);
		});
	}

	convertImageUrlToBase64(url: string): ng.IPromise<string> {
		return this.commonService.convertImageUrlToBase64(url);
	}

	static add(ngModule: ng.IModule): void {
		ngModule.service(InjectNames.FRAMEWORK_SERVICE, FrameworkService);
	}
}
