import { AngularConstants } from "../../../common/angular-constants";

import { InjectNames } from "ng2/common/constants/common.constant";
import { EditorService } from "../services/editor-service";

export class NewComponentTabsCtrl {

	public static $inject = [
		AngularConstants.SCOPE,
		InjectNames.EDITOR_SERVICE,
	];

	public constructor(
		$scope: ng.IScope,
		private editorService: EditorService
	) { }

	public hideSidebar(): void {
		this.editorService.setSidebarVisibility(false);
	}
}

export class NewComponentTabsDirective {
	public static add(m: ng.IModule): void {
		m.directive("eirNewComponentTabs", () => {
			return {
				template: require("./new-component-tabs.html"),
				controller: NewComponentTabsCtrl,
				controllerAs: "eirNewComponentTabsCtrl"
			};
		});
	}
}
