import angular from "angular";
import { AngularConstants } from "../../../common/angular-constants";

import { SaveFlagsEnum } from "ng2/common/constants/common.constant";
import { IComponentFieldSetting } from "ng2/common/model/components/settings/component-field-setting.model";
import { IFieldSettingButton } from "ng2/common/model/components/settings/field-setting-button.model";
import { IMap } from "ng2/common/model/imap.model";
import { IBoundary } from "ng2/common/model/project/boundary.model";
import type { IEditorComponent } from "common/interface/ieditor-component";
import { EditorService } from "../services/editor-service";

export abstract class SettingsBase {
	protected selectedComponent: IEditorComponent;
	protected button: IFieldSettingButton;
	protected settingsRef: string;
	protected currentBoundary: IBoundary;
	protected unsubscribers: Function[] = [];

	constructor(
		protected $scope: ng.IScope,
		protected editorService: EditorService
	) {
		this.unsubscribers.push(editorService.selectedComponentChangedEvent.on((newComponent) => {
			if (newComponent) {
				this.onComponentChanged(newComponent);
			} else {
				this.onComponentChanged(null);
			}
		}));

		this.unsubscribers.push(editorService.currentBoundaryChangedEvent.on((newBoundary: IBoundary) => {
			this.onBoundaryChanged(newBoundary);
		}));
		this.selectedComponent = this.editorService.getSelectedComponent();
		this.currentBoundary = this.editorService.getCurrentBoundary();

		$scope.$on(AngularConstants.DESTROY, () => {
			angular.forEach(this.unsubscribers, (unsubscribe) => {
				unsubscribe();
			});
		});
	}

	//Setting value type varies
	public onChangeSetting(settingMeta: IComponentFieldSetting, settingValue: any): void {
		this.selectedComponent.setSetting(settingMeta, settingValue);
		this.editorService.saveCanvas();
	}

	public onIconClick(): void {
		if (this.button && this.button.dialog) {
			this.selectedComponent.openDialog(this.settingsRef).then(() => {
				this.refresh();
				// tslint:disable-next-line:no-bitwise
				this.editorService.saveCurrent(SaveFlagsEnum.CANVAS_COMPONENTS | SaveFlagsEnum.MANIFEST);
			});
		} //else add more functions?
	}

	protected refresh(): void {
		// overriden
	}

	protected onBoundaryChanged(newBoundary: IBoundary): void {
		this.currentBoundary = newBoundary;
	}

	protected onComponentChanged(newComponent: IEditorComponent): void {
		// If selected component or newComponent is null -
		// the current setting view is destructing and a new component will be selected through the new settings construct
		if (!(this.selectedComponent && newComponent) || this.selectedComponent.getTypedef() === newComponent.getTypedef()) {
			this.selectedComponent = newComponent;
		} else {
			this.selectedComponent = null;
		}
	}

	protected readSettingValue<T>(setting: IComponentFieldSetting): T {
		const component = this.selectedComponent;
		const reference = setting.ref;
		let value: T = undefined;
		if (setting.boundarySetting) {
			value = component.getBoundarySetting<T>(reference, this.currentBoundary.id);
			if (value === null) {
				value = setting.defaultValue;
			}
		} else if ((component as IMap<any>)[reference] !== undefined) {
			value = (component as IMap<any>)[reference];
		} else if ((component.getDatabearer() as IMap<any>)[reference] !== undefined) {
			//Datagrids isList is only accessable via databearer due to refresh-datagrid
			value = (component.getDatabearer() as IMap<any>)[reference];
		}
		return value;
	}
}
