import jsonpath from "jsonpath";
import isArray from "lodash-es/isArray";

import { Subcodes } from "ng2/common/constants/common.constant";
import { ParameterTypeEnum } from "ng2/common/enums/parameter-type.enum";
import { IJsonGenericDatasource2 } from "ng2/common/model/databearer/dataservice/json-datasource.model";
import { IListDataItem } from "ng2/common/model/dataservice/list-data-item.model";
import { IMetaDataField } from "ng2/common/model/dataservice/meta-data-field.model";
import { IMetaData } from "ng2/common/model/dataservice/meta-data.model";
import { IMap } from "ng2/common/model/imap.model";

import { ErrorResponse, IErrorResponse } from "../../error-response";
import { Datasource } from "./datasource";

import { DataServiceHelper } from "ng2/common/helpers/data-service.helper";
import { JsonHelper } from "ng2/common/helpers/json.helper";
import { DatasourceService } from "../../../components/services/datasource-service";
import { Helper } from "../../helper";
import { IParameterObject } from "ng2/common/model/dataservice/parameter-object.model";

interface IIonApiCall {
	relativeUrl: string;
	requestBody: string;
}

export class Generic2 extends Datasource {
	protected databearer: IJsonGenericDatasource2;

	constructor(
		obj: IJsonGenericDatasource2,
		protected datasourceService: DatasourceService
	) {
		super(obj);
	}

	getDatabearer(): IJsonGenericDatasource2 {
		return this.databearer;
	}

	getDetailData(params: IMap<any>): Promise<any> {
		const databearer = this.databearer;
		const formatResponse = (result: any) => {
			const detailObject: any = {};

			const resultToFormat = this.getVerifiedResult(result);

			if (databearer.isOutputFile) {
				detailObject[databearer.outputParameters[0].id] = resultToFormat;
			} else {
				// Json response
				try {
					databearer.outputParameters.forEach((param) => {
						detailObject[param.id] = jsonpath.value(
							resultToFormat,
							databearer.outputRootElement + param.key
						);
					});
				} catch (e) {
					throw new Error(
						"Unable to parse data. The response is not in JSON format."
					);
				}
			}
			return detailObject;
		};
		return this.loadData(params).then(formatResponse);
	}

	private getVerifiedResult(obj: any): any {
		return isArray(obj) ? obj[0] : obj;
	}

	getListData(params: IMap<any>): Promise<IListDataItem[]> {
		const databearer = this.databearer;
		return this.loadData(params).then((result) => {
			const list: IListDataItem[] = [];
			const listResult = this.getVerifiedResult(result);

			const rows = jsonpath.query(listResult, databearer.outputRootElement);
			rows.forEach((row, index) => {
				const listObject: IListDataItem = {};
				databearer.outputParameters.forEach((param) => {
					listObject[param.id] = jsonpath.value(row, "$." + param.key);
				});
				if (databearer.objectIdPath) {
					listObject.id = jsonpath.value(row, "$." + databearer.objectIdPath); //setting the id on the item
				} else {
					listObject.id = index.toString();
				}
				list.push(listObject);
			});
			return list;
		});
	}

	getNonParsedData(params: IMap<any>): Promise<any[]> {
		return this.loadData(params).then((result) => {
			return this.getVerifiedResult(result);
		});
	}

	private setSubstitutionTags(
		url: string,
		bodyParameter: string,
		parameters: IMap<any>
	): IIonApiCall {
		let value: any;
		let substitutionTag: string;
		let requestBody;

		this.databearer.inputParameters.forEach((datasourceParameter) => {
			value = parameters[datasourceParameter.id];
			switch (datasourceParameter.parameterType) {
				case ParameterTypeEnum.PATH:
				case ParameterTypeEnum.MATRIX:
				case ParameterTypeEnum.QUERY:
					substitutionTag = DataServiceHelper.getURLVariable(
						datasourceParameter.key
					);
					if (url.indexOf(substitutionTag) !== -1) {
						if (value !== null && value !== undefined && value !== "") {
							// Exclude all false cases except 0
							url = url.replace(substitutionTag, encodeURIComponent(value));
						} else if (datasourceParameter.required) {
							// If required throw error
							throw new Error("Parameter is required");
						} else {
							// Null, undefined or "", remove substitution tags
							if (
								datasourceParameter.parameterType === ParameterTypeEnum.MATRIX
							) {
								// Matrix
								const strToReplace =
									";" +
									DataServiceHelper.getURLVariable(
										datasourceParameter.key,
										ParameterTypeEnum.MATRIX
									);
								url = url.replace(strToReplace, "");
							} else {
								// Query
								const fullSubstitutionTag = DataServiceHelper.getURLVariable(
									datasourceParameter.key,
									ParameterTypeEnum.QUERY
								);
								url = url.replace(fullSubstitutionTag, "");
								url = url.replace("?&", "?");
								url = url.replace("&&", "&");

								// Rm if there is a & at the end.
								// E.g: LstItmByItmGr?ITGR=004&
								if (url.charAt(url.length - 1) === "&") {
									url = url.substr(0, url.length - 1);
								}
							}
						}
					} else {
						throw new Error("Substitution tag does not exist!");
					}
					break;
				case ParameterTypeEnum.BODY_PART: {
					requestBody = this.getRequestBody(
						bodyParameter,
						datasourceParameter,
						value
					);

					break;
				}
				default:
				// Invalid inparameter type
			}
		});
		return {
			relativeUrl: url,
			requestBody,
		};
	}

	private getRequestBody(
		bodyParameter: string,
		datasourceParameter: IParameterObject,
		value: any
	): any {
		const contentType = this.databearer.headers?.["Content-Type"];
		switch (contentType) {
			case "text/plain":
				return value;
			case "application/json":
			default: {
				const bodyParameters = JSON.parse(bodyParameter);
				const schema = JsonHelper.generateSchema(bodyParameters);
				const propertyType = JsonHelper.getPropertyType(
					schema,
					datasourceParameter.key
				);
				switch (propertyType) {
					case "number":
						value = parseFloat(value);
						break;
					case "string":
					default:
						break;
				}
				jsonpath.value(bodyParameters, datasourceParameter.key, value);

				return bodyParameters;
			}
		}
	}

	loadData(inputParameterValues: IMap<any>): Promise<any> {
		const defer: ng.IDeferred<any> = this.datasourceService.getDeferred();
		const databearer = this.databearer;
		const relativeUrlPath =
			databearer.suite + databearer.endpoint + databearer.resource;
		const requestBody = databearer.bodyTemplate;
		const parameters: IMap<any>[] = [{}];
		const ionApiCalls: IIonApiCall[] = [];
		const extraParameters: IMap<any> = {};

		try {
			this.databearer.inputParameters.forEach((ionapiParameter) => {
				const inputparameter = inputParameterValues[ionapiParameter.id];
				if (inputparameter && isArray(inputparameter)) {
					// if multiple parameters
					inputparameter.forEach((parameter, index) => {
						if (!parameters[index]) {
							parameters.push({});
						}
						parameters[index][ionapiParameter.id] = parameter;
					});
				} else if (inputparameter) {
					extraParameters[ionapiParameter.id] = inputparameter;
					if (parameters.length === 1) {
						parameters[0] = inputParameterValues;
					}
				}
			});

			this.fillInParameters(
				extraParameters,
				this.databearer.inputParameters,
				parameters
			);
			parameters.forEach((parameter, index) => {
				ionApiCalls.push(
					this.setSubstitutionTags(relativeUrlPath, requestBody, parameter)
				);
				const matches = ionApiCalls[index].relativeUrl.match(/{.+}/g);
				if (matches) {
					throw new Error("Not all string replace positions were replaced");
				}
			});
		} catch (e) {
			const error: ErrorResponse = new ErrorResponse(
				e.message,
				Subcodes.DATA_SERVICE_ERROR,
				e.stack
			);

			defer.reject(error);
			console.error(e);
			return defer.promise as Promise<any>;
		}

		const promises: Promise<any>[] = [];

		ionApiCalls.forEach((ionApiCall) => {
			promises.push(
				this.datasourceService
					.ionApiRequest(
						ionApiCall.relativeUrl,
						databearer.isOutputFile,
						databearer.method,
						ionApiCall.requestBody,
						databearer.headers
					)
					.then((response: any) => {
						if (ionApiCall.relativeUrl.indexOf("righttrim=true") > 0) {
							response = Helper.deepObjectTrim(response);
						}
						return response;
					})
			);
		});

		return this.datasourceService.$q.all(promises) as Promise<any>;
	}

	getMetaData(): Promise<IMetaData> {
		const deferredMetaData: ng.IDeferred<any> =
			this.datasourceService.getDeferred();
		const inputFields: IMetaDataField[] = [];
		const outputFields: IMetaDataField[] = [];

		this.databearer.inputParameters.forEach((param) => {
			inputFields.push({
				FLNM: param.id,
				FLDS: param.alias ? param.alias : param.key,
				MAND: param.required ? "1" : "0",
				type: param.dataType,
			});
		});

		this.databearer.outputParameters.forEach((param) => {
			outputFields.push({
				FLNM: param.id,
				FLDS: param.alias ? param.alias : param.key,
				MAND: "0",
				type: param.dataType,
			});
		});

		deferredMetaData.resolve({
			inputFields: inputFields,
			outputFields: outputFields,
		});

		return deferredMetaData.promise as Promise<any>;
	}
}
