import { Config, InjectNames } from "ng2/common/constants/common.constant";
import { IJsonDatasource } from "ng2/common/model/databearer/dataservice/json-datasource.model";
import { AngularConstants } from "../../../../../common/angular-constants";
import { IErrorResponse } from "../../../../../common/error-response";
import {
	DialogButtonType, DialogService, IDialog, IDialogOptions
} from "../../../../../common/services/dialog-service";
import { IdoCollection, IdoProject, IdoProperty, LocalIdoService } from "../../../../../components/services/local-ido-service";
import { BaseSearchCtrl, ISearchDialogFieldScope } from "../base-search-ctrl";
import { ILocalIdoParameter } from "./operation-details-ctrl";

export class LocalIdoSearchCtrl extends BaseSearchCtrl {

	public filterOptions: any[] = [];
	public dropdownOptions: IdoProject[];
	public operations: IdoCollection[];
	public focusedOperation: IdoCollection;

	public metadataService: LocalIdoService;

	static $inject = [
		AngularConstants.SCOPE,
		AngularConstants.ELEMENT,
		AngularConstants.CACHE_FACTORY,
		InjectNames.DIALOG_SERVICE,
		InjectNames.LOCAL_IDO_SERVICE,
	];

	constructor(
		$scope: ISearchDialogFieldScope,
		$element: ng.IAugmentedJQuery,
		$cacheFactory: ng.ICacheFactoryService,
		dialogService: DialogService,
		localIdoService: LocalIdoService,
	) {
		super($scope, $element, $cacheFactory, dialogService, localIdoService);
	}

	public setDropdownOptions(): void {
		this.metadataService.getIdoProjectList().then(response => {
			this.dropdownOptions = response;
			this.dropdownOptions.unshift({ serverName: "All", serverDesc: undefined, accessAs: undefined });
			this.$scope.selectedOption = this.dropdownOptions[0];
		});

	}

	public getListItems(selectedOption: IdoProject, query: string): ng.IPromise<any> {
		let projectName = undefined;

		if (selectedOption && selectedOption.serverName) {
			projectName = selectedOption.serverName !== "All" ? selectedOption.serverName : undefined;
		}

		return this.metadataService.getIdoCollectionList(projectName, query, this.pageNo);
	}

	public showOperationDetails(operation: IdoCollection): void {
		const openDialog = (properties: IdoProperty[]) => {
			const options: IDialogOptions<ILocalIdoParameter> = {
				title: `IDO details`,
				cssClass: "operation-details",
				template: require("./operation-details.html"),
				buttons: [{
					text: "Close",
					click: (event: Event, modal: IDialog<void>) => modal.close
				}],
				id: "dataservice-search-wizard",
				parameter: {
					operation,
					properties
				}
			};

			this.$scope.eirDialog.hide();
			this.dialogService.show(options)
				.then(() => {
					this.$scope.eirDialog.show();
				});
		};

		const cachedOperationProperties: IdoProperty[] =
			this.cacheObject.get(operation.collectionName);

		if (cachedOperationProperties) {
			openDialog(cachedOperationProperties);
		} else {
			this.isBusy = true;
			this.metadataService.getIdoProperties(operation.collectionName)
				.then(properties => {
					this.cacheObject.put(operation.collectionName, properties);
					openDialog(properties);
				})
				.finally(() => this.isBusy = false);

		}
	}

	public runCreateNewWizard(operation: IdoCollection): void {
		const openDialog = (properties: IdoProperty[]) => {
			const options: IDialogOptions<ILocalIdoParameter> = {
				title: "Create Data Service",
				parameter: {
					operation,
					properties
				},
				template: "<eir-wizard ng-controller='eirLocalIdoDataserviceWizardCtrl as wizardCtrl'></eir-wizard>",
				cssClass: "wizard-dialog large",
				id: "datasource-wizard"
			};

			this.$scope.eirDialog.hide();
			this.dialogService
				.show<ILocalIdoParameter, IJsonDatasource>(options)
				.then(result => {
					if (result.button === DialogButtonType.Ok) {
						this.$scope.eirDialog.close(result);
					} else { // cancel
						this.$scope.eirDialog.show();
					}
				});
		};

		const properties: IdoProperty[] = this.cacheObject.get(operation.collectionName);
		if (properties) {
			openDialog(properties);
		} else {
			this.isBusy = true;
			this.metadataService.getIdoProperties(operation.collectionName)
				.then(properties => {
					this.cacheObject.put(operation.collectionName, properties);
					openDialog(properties);
				})
				.catch((errorResponse) => {
					this.showError(errorResponse);
				}).finally(() => {
					this.isBusy = false;
				});
		}
	}

	public showError(error: IErrorResponse): void {
		console.error(error);
	}

	static add(module: ng.IModule): void {
		module.controller("eirLocalIdoSearchCtrl", LocalIdoSearchCtrl);
	}
}
