import { IDatasource } from "common/model/datasource/datasource.model";
import { DatasourceTypeEnum } from "ng2/common/enums/datasource-type.enum";
import { IJsonDatasource } from "ng2/common/model/databearer/dataservice/json-datasource.model";
import { IMetaData } from "ng2/common/model/dataservice/meta-data.model";
import { IParameterObject } from "ng2/common/model/dataservice/parameter-object.model";
import { IMap } from "ng2/common/model/imap.model";
import { IComponent } from "../../IComponent";

export abstract class Datasource implements IDatasource {

	public usedBy: IComponent[] = [];

	protected databearer: IJsonDatasource;

	constructor(obj: IJsonDatasource) {
		this.databearer = obj;
	}

	public getDatabearer(): IJsonDatasource {
		return this.databearer;
	}

	get id(): string {
		return this.databearer.id;
	}
	set id(id: string) {
		this.databearer.id = id;
	}

	get typedef(): DatasourceTypeEnum {
		return this.databearer.typedef;
	}
	set typedef(typedef: DatasourceTypeEnum) {
		this.databearer.typedef = typedef;
	}

	get displayName(): string {
		return this.databearer.displayName;
	}
	set displayName(displayName: string) {
		this.databearer.displayName = displayName;
	}

	public abstract getDetailData(params: IMap<any>): Promise<any>;

	public abstract getListData(params: IMap<any>): Promise<any>;

	public abstract getMetaData(): Promise<IMetaData>;

	public abstract getNonParsedData(params: IMap<any>): Promise<any[]>;

	/**
	 * fill in extra parameters for multi parameter calls
	 */
	public fillInParameters(
		extraParameters: IMap<any>,
		inputParameters: IParameterObject[],
		parameters: IMap<any>[]
	): void {
		if (Object.keys(extraParameters).length > 0) {
			inputParameters.forEach((ionApiParameter) => {
				parameters.forEach((parameter) => {
					if (extraParameters[ionApiParameter.id]) {
						parameter[ionApiParameter.id] = extraParameters[ionApiParameter.id];
					}
				});
			});
		}
	}

}
