import angular from "angular";

import { AngularConstants } from "../../../../common/angular-constants";
import type { IDialogBaseScope } from "../../../../common/defines";
import type { IRadioButton } from "../../../../common/model/component/radiobutton.model";
import { DialogButtonType, IDialog, IDialogResult } from "../../../../common/services/dialog-service";
import { FeatureSwitchService } from "../../../../common/services/feature-switch-service";
import { FeatureSwitchCtrl } from "../../../../common/shared-components/base/feature-switch-ctrl";
import { TranslationManagementService } from "../../../../components/services/translation-management-service";
import { IEditorRadioButtons } from "../../components/editor-radiobutton";
import { EditorService } from "../../services/editor-service";

import { InjectNames } from "ng2/common/constants/common.constant";

import type { IAutoCompleteOptions } from "common/model/sohoxi/sohoxi-autocomplete.model";
import type { ILookupOptions } from "common/model/sohoxi/sohoxi-lookup.model";
import type { ILanguage } from "ng2/common/model/language.model";

export interface IEditRadioButtonDialogParameter  {
	radio: IRadioButton;
	component: IEditorRadioButtons;
}

export class EditRadioButtonCtrl extends FeatureSwitchCtrl {
	radioButton: IRadioButton;
	lookupOptions: ILookupOptions;
	autocompleteOptions: IAutoCompleteOptions;
	private dialog: IDialog<IEditRadioButtonDialogParameter>;
	private selectedComponent: IEditorRadioButtons;

	static $inject = [
		AngularConstants.SCOPE,
		InjectNames.EDITOR_SERVICE,
		InjectNames.TRANSLATION_MANAGEMENT_SERVICE,
		InjectNames.FEATURE_SWITCH_SERVICE,
	];

	constructor(
		private scope: IDialogBaseScope<IEditRadioButtonDialogParameter>,
		private editorService: EditorService,
		private translationManagementService: TranslationManagementService,
		featureSwitchService: FeatureSwitchService
	) {
		super(featureSwitchService);
		const dialog: IDialog<IEditRadioButtonDialogParameter> = scope.eirDialog;
		this.dialog = dialog;
		const parameter = dialog.parameter;
		this.radioButton = parameter.radio;
		this.selectedComponent = parameter.component;

		//Init xi-components
		this.lookupOptions = this.translationManagementService.getLookupOptions();
		this.autocompleteOptions = this.translationManagementService.getAutoOptions();
	}

	openTranslations(): void {
		this.translationManagementService.openTranslations(this.selectedComponent, this.radioButton.id)
			.then((result) => {
				if (result.button === DialogButtonType.Ok) {
					const changes = result.value;
					if (changes.savedComponentStrings.length > 0) {
						const selectedChanged = changes.savedComponentStrings.find((translation) => {
							return translation.component === this.selectedComponent.getId()
								&& translation.reference === this.radioButton.id;
						});
						if (selectedChanged) {
							this.radioButton.name = selectedChanged.newValue;
						}
						this.editorService.updateTranslatables();
					}
				}
				//If global translations is changed
				if (result.button === DialogButtonType.Ok && !angular.equals({}, result.value.savedGlobalStrings)) {
					this.translationManagementService.saveGlobalTranslations(result.value.savedGlobalStrings)
						.then(() => {
							this.editorService.updateTranslatables();
						});
				}
				//If global translations is deleted
				if (result.button === DialogButtonType.Ok && !angular.equals({}, result.value.deletedGlobalStrings)) {
					this.translationManagementService.deleteGlobalTranslations(result.value.deletedGlobalStrings)
						.then(() => {
							this.editorService.updateTranslatables();
						});
				}
			});
	}

	onChangeRadioButtonName(): void {
		const currentLanguageKey = this.translationManagementService.getSelectedLocale();
		const newValue: ILanguage = {
			locale: currentLanguageKey,
			value: this.radioButton.name
		};
		this.translationManagementService.setTranslatableStringByProperty(
			this.selectedComponent.getId(),
			this.radioButton.id,
			newValue
		);
	}

	cancel(): void {
		this.dialog.close();
	}

	save(): void {
		//clear possible global string
		delete this.radioButton.value;

		const result: IDialogResult<any> = {
			value: this.radioButton,
			button: DialogButtonType.Ok
		};
		this.dialog.close(result);
		this.selectedComponent.initializeBindings();
	}

	static add(m: ng.IModule): void {
		m.controller("eirEditRadioButtonCtrl", EditRadioButtonCtrl);
	}
}
