import { HttpClient } from "@angular/common/http";
import { Observable } from "rxjs/internal/Observable";

import { Config, DeveloperConstants } from "ng2/common/constants/common.constant";

import type { IJsonDatasource, IHttpHeader } from "ng2/common/model/databearer/dataservice/json-datasource.model";
import type { IDataService } from "ng2/common/model/dataservice/data-service.model";
import type { IMap } from "ng2/common/model/imap.model";
import type { DatasourceTypeEnum } from "ng2/common/enums/datasource-type.enum";
import type { IComponent } from "ng2/common/model/components/base/component.model";
import type { IIonApiCall } from "ng2/common/model/ion-api/ion-api.model";

import { DynamicComponentService } from "ng2/components/services/dynamic-component.service";

export abstract class DataService implements IDataService {

	private isDevelopment: boolean;
	private ionapiToken: string;

	constructor(
		protected http: HttpClient,
		protected dynamicComponentService: DynamicComponentService,
		obj: IJsonDatasource,
	) {

		this.databearer = obj;
		this.initService();
	}

	databearer: IJsonDatasource;

	usedBy: IComponent[];
	typedef?: DatasourceTypeEnum;
	version?: number;
	id?: string;
	displayName?: string;
	description?: string;
	author?: string;
	authorDisplayName?: string;
	creationDate?: number;
	changedBy?: string;
	changedByDisplayName?: string;
	changedDate?: number;
	headers?: IHttpHeader;

	abstract getDatabearer(): IJsonDatasource;
	abstract getDetailData(params: IMap<any>): Observable<any>;
	abstract getListData(params?: IMap<any>): Observable<any[]>;

	private initService(): void {
		this.isDevelopment =
			Config.IS_DEV && localStorage.getItem(DeveloperConstants.ION_PROXY) === "true";
	}

	ionApiRequest(ionapiCall: IIonApiCall, parameters: IMap<any>): Observable<any> {

		/* FIXME:
			Config.ION_API_URL, and Config.TENANT_ID comes from Mongoose
			and should be changed in the final version */
		const url = this.isDevelopment ?
			`http://localhost:8083/infor${ionapiCall.relativeUrl}` :
			`${Config.ION_API_URL}/${Config.TENANT_ID}${ionapiCall.relativeUrl}`;

		const headers: IMap<string> = {
			Accept: "application/json",
			Authorization: `Bearer ${Config.ION_API_TOKEN}`
		};

		return this.http
			.get(url, {
				headers
			});
	}
}