import {	InjectNames, InteractionActionEnum, InteractionTriggerEnum, SaveFlagsEnum } from "ng2/common/constants/common.constant";
import { BindingTypeEnum } from "ng2/common/enums/binding-type.enum";
import { IJsonComponent } from "ng2/common/model/databearer/components/json-component.model";
import { IJsonInteraction, IJsonInteractionParameter } from "ng2/common/model/databearer/json-interaction.model";
import { IMap } from "ng2/common/model/imap.model";
import { IInteraction } from "ng2/common/model/interaction/interaction.model";
import { Interaction } from "ng2/components/interaction/interaction";

import { InstanceEvent } from "../../../common/instance-event";
import { Component } from "../../../components/component";
import type { IEditorComponent } from "common/interface/ieditor-component";

import { Helper } from "../../../common/helper";
import { DialogService, IDialogOptions } from "../../../common/services/dialog-service";
import { ComponentsHelper } from "../../../components/helper";
import { FrameworkService } from "../../../components/services/framework-service";
import { EditorService } from "./editor-service";

export class InteractionManagementService {
	projectInteractionsChanged = new InstanceEvent<Interaction[]>();
	private isActive: boolean;

	static $inject = [
		InjectNames.FRAMEWORK_SERVICE,
		InjectNames.EDITOR_SERVICE,
		InjectNames.DIALOG_SERVICE
	];

	constructor(
		private frameworkService: FrameworkService,
		private editorService: EditorService,
		private dialogService: DialogService
	) {
	}

	runStartupInteractions(): void {
		this.frameworkService.runStartupInteractions();
	}

	startInteractionWizard(component: IEditorComponent): void {
		if (!this.isActive) {
			this.isActive = true;
			const options: IDialogOptions<IEditorComponent> = {
				title: "Create Interaction",
				parameter: component,
				template: require("../component-settings/interaction/interaction-wizard.html"),
				cssClass: "interaction-wizard-dialog",
				style: "min-width: 450px; min-height: 595px; max-width: 450px;",
				hideOverlay: true,
				draggable: true,
			};

			this.dialogService.show(options).finally(() => {
				this.isActive = false;
			});
		}
	}

	runInteraction(interaction: Interaction): void {
		this.frameworkService.runInteraction(interaction);
	}

	runStartupOnComponent(component: IJsonComponent): void {
		const componentInstance = this.editorService.getComponentInstanceById(component.id);

		this.editorService.getCurrentProject().getInteractions().filter((ev) => {
			if (ev.targetName === componentInstance.getId() && ev.sourceTriggerName === InteractionTriggerEnum.STARTUP) {
				componentInstance.loadInteraction(ev);
				return true;
			}
			return false;
		});
	}

	trigger(source: Component, triggerName: string): void {
		this.editorService.getCurrentProject().getInteractions().forEach((interaction) => {
			if (interaction.sourceName === source.getId() && interaction.sourceTriggerName === triggerName) {
				this.frameworkService.runInteraction(interaction);
			}
		});
	}

	createNew(sourceComponent?: IEditorComponent, targetComponent?: IEditorComponent): Interaction {
		const id = this.generateInteractionId();
		const interaction = new Interaction({ id: id } as any);
		if (sourceComponent) {
			interaction.sourceName = sourceComponent.getId();
			interaction.sourceTriggerName = sourceComponent.getDefaultTrigger().value;
		}
		if (targetComponent) {
			interaction.targetName = targetComponent.getId();
			interaction.targetActionName = targetComponent.getDefaultAction().value;
		}
		interaction.displayName = this.getInteractionDisplayName(interaction);
		this.editorService.getCurrentProject().addInteraction(interaction);
		this.projectInteractionsChanged.raise(this.editorService.getCurrentProject().getInteractions());
		return interaction;
	}

	generateInteractionId(): string {
		const interactions = this.editorService.getCurrentProject().getInteractions();

		// List of interaction id
		let interactionIds: string[] = interactions.map((interaction: Interaction) => {
			return interaction.getId();
		});

		// Deleted componenents still has interactions sometimes
		interactionIds = interactionIds.filter((interactionId) => {
			return interactionId;
		});

		const newId: number = ComponentsHelper.nextNumber(interactionIds);
		return "Interaction" + newId;
	}

	removeInteraction(interaction: Interaction): void {
		this.editorService.getCurrentProject().removeInteraction(interaction);
		this.resetErrorTooltip(interaction.sourceName);
		this.resetErrorTooltip(interaction.targetName);
		this.projectInteractionsChanged.raise(this.editorService.getCurrentProject().getInteractions());
	}

	createNewOnTheFly(targetActionName: string, parameters?: IMap<any>): Interaction {
		const jsonInteraction: IJsonInteraction = {
			id: "Ontheflyevent",
			sourceName: "<onTheFly>",
			sourceTriggerName: "",
			targetName: "",
			targetActionName: targetActionName,
			parameters: []
		};
		if (parameters) {
			for (const key in parameters) {
				const parameter: IJsonInteractionParameter = {
					targetKey: key,
					valueSource: { Type: BindingTypeEnum.STATIC_VALUE, value: parameters[key] }
				};
				jsonInteraction.parameters.push(parameter);
			}
		}

		return new Interaction(jsonInteraction);
	}

	updateAndSaveParameters(sourceKey: string, value: string): void {
		const urlInteractions = this.getAllInteractionsWithUrlParameter(sourceKey);
		urlInteractions.forEach((interaction) => {
			interaction.getParameters().forEach((parameter) => {
				if (parameter.valueSource.Type === BindingTypeEnum.URL_BINDING) {
					parameter.valueSource.value = value;
				}
			});
		});
		if (urlInteractions.length > 0) {
			this.editorService.saveCurrent(SaveFlagsEnum.INTERACTIONS);
		}
	}

	getInteractionsWithTarget(component: IEditorComponent): Interaction[] {
		const interactions = this.editorService.getCurrentProject().getInteractions();
		const filteredInteractions = interactions.filter((interaction) => {
			return interaction.targetName === component.getId();
		});

		return filteredInteractions;
	}

	//TODO: Clean up this code to make it more readable.
	getInteractionDisplayName(interaction: Interaction): string {
		const selectedComponent = this.editorService.getSelectedComponent();
		const sourceComponent = this.editorService.getComponentInstanceById(interaction.sourceName);
		const targetComponent = this.editorService.getComponentInstanceById(interaction.targetName);
		let triggerName: string = "";
		let actionName: string = "";
		let displayName: string = "";

		if (sourceComponent) {
			const sourceTrigger = sourceComponent.getSourceTriggers().filter((sourceTrigger) => {
				return sourceTrigger.value === interaction.sourceTriggerName;
			});
			if (sourceTrigger.length) {
				triggerName = sourceTrigger[0].name; //get displayname instead of value.
			} else {
				const errorMessage = `[${((this as any).constructor.name)}]
					Could not find display for interaction with source trigger name '${interaction.sourceTriggerName}'`;

				console.error(Helper.cleanTemplateLiteral(errorMessage));
			}
			displayName += (interaction.sourceName === selectedComponent.getId() ? "this" : sourceComponent.getDisplayName());
		} else {
			// TODO: Add warning for nonexisting component
			triggerName = this.editorService.getApplicationParent().getDefaultTrigger().name;
			displayName += interaction.sourceName;
			displayName = Helper.htmlEncode(displayName);
		}
		displayName += "." + (triggerName || "");
		displayName += " -> ";
		if (targetComponent) {
			actionName = this.getActionName(targetComponent, interaction);
			displayName += Helper.htmlEncode(
				(interaction.targetName === selectedComponent.getId() ? "this" : targetComponent.getDisplayName()));
		} else {
			// TODO: Add warning for nonexisting component
			displayName += interaction.targetName;
		}
		displayName += "." + (actionName || "");

		return displayName;
	}

	private getAllInteractionsWithUrlParameter(urlVariableName: string): Interaction[] {
		const interactions = this.editorService.getCurrentProject().getInteractions();
		const filteredInteractions = interactions.filter((interaction) => {
			return interaction.getParameters().find((parameter) => {
				return parameter.valueSource.Type === BindingTypeEnum.URL_BINDING &&
					parameter.valueSource.sourceKey === urlVariableName;
			});
		});

		return filteredInteractions;
	}

	private getActionName(component: IEditorComponent, interaction: IInteraction): string {
		let actionName = "";
		if (interaction.targetActionName === InteractionActionEnum.LIST ||
			interaction.targetActionName === InteractionActionEnum.GET) {
			actionName = component.datasource ? "Run " + component.datasource.displayName : "";
		} else {
			actionName = interaction.targetActionName;
		}

		return actionName;
	}

	private resetErrorTooltip(componentName: string): void {
		const component = this.frameworkService.getComponentInstance(componentName);
		if (component) {
			component.setErrorTooltip();
		}
	}

	static add(ngModule: ng.IModule): void {
		ngModule.service(InjectNames.INTERACTION_MANAGEMENT_SERVICE, InteractionManagementService);
	}
}
