import { AngularConstants } from "../../angular-constants";

import { InjectNames } from "ng2/common/constants/common.constant";
import { IDialogBaseScope } from "../../defines";
import { CommonService } from "../../services/common-service";
import { IDialog } from "../../services/dialog-service";

export class LicensesDialogCtrl {
	private dialog: IDialog<void>;
	public licensesHtml: String;
	public applicationVersion: String;
	public dbVersions: String;

	static $inject = [
		AngularConstants.SCOPE,
		InjectNames.COMMON_SERVICE,
	];

	constructor(
		private scope: IDialogBaseScope<void>,
		private commonService: CommonService,
		private $sce: any
	) {
		const dialog = scope.eirDialog;
		this.dialog = dialog;
		this.licensesHtml = "Loading...";
		/*commonService.loadLicensesHtml().then((licensesHtml: any) => {
			this.licensesHtml = licensesHtml;
		});*/

		commonService.loadApplicationVersion().then((applicationVersion: string) => {
			this.applicationVersion = applicationVersion;
		});

		commonService.loadDBVersions().then((dbVersions: string) => {
			this.dbVersions = dbVersions;
		});
	}

	public close(): void {
		this.dialog.close();
	}

	static add(m: ng.IModule): void {
		m
			.controller("eirLicensesDialogCtrl", LicensesDialogCtrl)
			.filter("sanitize", ["$sce", ($sce: any) => {
				return (htmlCode: any) => {
					return $sce.trustAsHtml(htmlCode);
				};
			}]);
	}
}
