import {
	ChangeDetectorRef,
	Component,
	ElementRef,
	Renderer2,
	ViewChild,
} from "@angular/core";
import {
	SohoLookupComponent,
	SohoMessageService,
} from "ids-enterprise-ng";
import isNil from "lodash-es/isNil";

import {
	InteractionTriggerEnum,
	SubstitutionTags
} from "ng2/common/constants/common.constant";

import type { IJsonLookup } from "ng2/common/model/databearer/components/json-lookup.model";

import { LookupMetadata } from "ng2/components/lookup/lookup.metadata";

import { BackwardCompatible } from "ng2/common/project/backward-compatible";
import { BaseComponent } from "ng2/components/base/component";

import { DynamicComponentService } from "ng2/components/services/dynamic-component.service";
import { FrameworkService } from "ng2/components/services/framework.service";

@Component({
	selector: "ng2-lookup",
	template: `
		<div class="field">
			<label
				[for]="id"
				soho-label
				*ngIf="showLabel"
			>
				{{ label }}
			</label>
			<input
				soho-lookup
				soho-busyindicator
				[columns]="columns"
				[dataset]="dataset"
				[beforeShow]="onBeforeShow.bind(this)"
			/>
		</div>
	`
})
export class LookupComponent extends BaseComponent {

	label: string;
	showLabel: boolean;

	columns: any;
	dataset: any;
	@ViewChild(SohoLookupComponent) sohoLookupComponent: SohoLookupComponent;
	databearer: IJsonLookup;

	constructor(
		elementRef: ElementRef,
		renderer: Renderer2,
		changeDetectorRef: ChangeDetectorRef,
		frameworkService: FrameworkService,
		dynamicComponentService: DynamicComponentService,
		sohoMessageService: SohoMessageService,
	) {
		super(
			elementRef,
			renderer,
			changeDetectorRef,
			frameworkService,
			dynamicComponentService,
			sohoMessageService,
		);
	}

	onInitComponent(): void {
		super.onInitComponent();

		this.showLabel = BackwardCompatible.getShowLabel(this.databearer);
	}

	initializeBinding(componentDatabearer: IJsonLookup): void {
		LookupMetadata.BINDABLE_FIELDS_2.forEach(field => {
			const self: any = this;
			const bindingValue = componentDatabearer[field.ref].value;
			self[field.ref] = bindingValue === SubstitutionTags.VALUE ?
				"" : bindingValue;
		});

		this.columns = [
			{
				id: "productId",
				name: "Product Id",
				field: "productId",
				formatter: Soho.Formatters.Readonly
			},
		];

		this.dataset = [
			{
				id: 1,
				productId: 2142201,
				productName: "Compressor",
				activity:  "Assemble Paint",
				quantity: 1,
				price: 210.99,
				status: "OK",
				orderDate: new Date(2014, 12, 8),
				action: "Action",
			}
		];
	}

	// TODO: Needs to be completed when completed
	// https://github.com/infor-design/enterprise-ng/issues/899
	onBeforeShow(
		api: SohoLookupStatic,
		response: SohoLookupBeforeShowResponse
	): void {
		if (isNil(this.datasource)) {
			this.showErrorDialog(
				`No data service available on component: ${this.databearer.displayName}`);

			this.busyIndicator.activated = false;

			return response(false as any);
		} else {
			this.trigger(InteractionTriggerEnum.CLICK);

			return response();
		}
	}

}
