import { AngularConstants } from "../common/angular-constants";

import { ComponentClassNames, InjectNames } from "ng2/common/constants/common.constant";
import { IComponentScope } from "../common/interface/icomponent-scope";
import { LayoutComponent } from "./layout-component";

import { ComponentsHelper } from "./helper";
import { FrameworkService } from "./services/framework-service";

export class ComponentDirective implements ng.IDirective {
	// Found guide to make directive in typescript
	//http://blog.aaronholmes.net/writing-angularjs-directives-as-typescript-classes/
	public restrict = "E";
	public scope = {
		component: "=",
		parent: "=",
		context: "=",
		generatedId: "=",
		componentReady: "&",
		componentDestroyed: "&"
	};
	public replace = true;
	public link: (scope: ng.IScope, element: ng.IAugmentedJQuery, attrs: ng.IAttributes) => void;
	public template: (element: ng.IAugmentedJQuery, attrs: ng.IAttributes) => string;
	public controllerExists: (controllerName: string) => boolean;

	constructor(
		private $compile: ng.ICompileService,
		private frameworkService: FrameworkService,
		private $controller: ng.IControllerService
	) {
		ComponentDirective.prototype.controllerExists = (controllerName: string) => {
			try {
				this.$controller(controllerName, null, true);
				return true;
			} catch (e) {
				return false;
			}
		};

		ComponentDirective.prototype.template = (element: ng.IAugmentedJQuery, attrs: ng.IAttributes) => {
			// Keeping the eir-component node name because of css-reasons
			const template = $("<eir-component></eir-component>");
			template.attr("id", "{{component.id}}");
			template.attr("xi-busy", "componentInstance.isLoading");
			template.attr("ng-show", "componentInstance.isVisible");

			if (frameworkService.isDesigner()) {
				const editorControllerName = ComponentClassNames.EDITOR_COMPONENT_PREFIX + element.attr("typedef");
				if (!this.controllerExists(editorControllerName)) {
					// TODO: Decide on fallback or error
					//editorControllerName = ComponentClassNames.EDITOR_COMPONENT_PREFIX + ComponentClassNames.COMPONENT;
					throw new Error(
						"[Component directive] Not implemented controller for component of type: " + element.attr("typedef"));
				}

				template.attr("eir-editor-component", editorControllerName);
				template.attr("ng-click", "componentInstance.onClick($event);");
				template.attr("ng-attr-draggable", "{{eirEditorComponent.isDraggable}}");
				template.attr("ng-dragenter", "componentInstance.onDragenter($event);");
				template.attr("ng-dragleave", "componentInstance.onDragleave($event);");
				template.attr("ng-dragover", "componentInstance.onDragover($event);");
				template.attr("ng-dragstart", "componentInstance.onDragstart($event);");
				template.attr("ng-drop", "componentInstance.onDrop($event);");
				template.attr("ng-mouseleave", "componentInstance.onMouseLeave($event);");
				template.attr("ng-mousemove", "componentInstance.onMouseMove($event);");
				template.attr("ng-mouseover", "componentInstance.onMouseOver($event);");
				const classes: string[] = [];
				classes.push("{ isHovered: eirEditorComponent.isHovered }");
				classes.push("{ isSelected: eirEditorComponent.isSelected }");
				classes.push("{ isWizardFocus: eirEditorComponent.wizardFocusType===1 }");
				classes.push("{ isWizardRest: eirEditorComponent.wizardFocusType===2 }");
				classes.push("{ isFinder: eirEditorComponent.isFinder }");
				classes.push("{ isInsertParent: eirEditorComponent.isInsertParent }");
				classes.push("{ isInsertChild: eirEditorComponent.isInsertChild }");

				template.attr("ng-class", "[" + classes.join(", ") + "]");
			}
			return template[0].outerHTML;
		};

		ComponentDirective.prototype.link = (
			$scope: IComponentScope,
			$element: ng.IAugmentedJQuery,
			$attrs: ng.IAttributes
		) => {

			if (!$scope.component) { throw new Error("[ComponentDirective] Component not set"); }
			if (!$scope.context) { throw new Error("[ComponentDirective] Context not provided"); }
			if (!$scope.parent) { throw new Error("[ComponentDirective] Parent not provided"); }

			const component = $scope.component;
			const parentInstance = $scope.parent;
			$scope.context = Object.create($scope.context);
			// Passing scope via the $element
			$element.data("eir-scope", $scope);

			const componentInstance = ComponentsHelper.instantiateComponent(
				component, $element, parentInstance, this.frameworkService);
			$scope.componentInstance = componentInstance;

			const template = document.createElement("d" + componentInstance.getTypedef().toLowerCase());

			this.$compile(template)($scope, (clone, scope) => {
				$element.empty();
				const commonPart = this.$compile("<eir-component-common></eir-component-common>")($scope);
				$element.append(commonPart);
				$element.append(clone);

				// Components that are not Layout resolve via linking function added from defines
				if (componentInstance instanceof LayoutComponent) {
					$scope.onChildrenReady = () => {
						componentInstance.onChildrenReady();
						$scope.componentReady({ componentInstance: componentInstance });
					};
				}

				scope.$on(AngularConstants.DESTROY, (ev: ng.IAngularEvent) => {
					$scope.componentDestroyed({ componentInstance: componentInstance });
					componentInstance.element.empty();
					componentInstance.element.remove();
				});
			});
		};
	}

	static add(m: ng.IModule): void {
		const directive: ng.IDirectiveFactory = (
			$compile: ng.ICompileService,
			frameworkService: FrameworkService,
			$controller) => {
			return new ComponentDirective($compile, frameworkService, $controller);
		};
		directive.$inject = [
			AngularConstants.COMPILE,
			InjectNames.FRAMEWORK_SERVICE,
			AngularConstants.CONTROLLER
		];
		m.directive("eirComponentDirective", directive);
	}
}
