import {
	InteractionPairOperation,
	SettingKeys,
	ThemeEnum,
} from "ng2/common/constants/common.constant";

import { OrientationEnum } from "ng2/common/enums/orientation.enum";
import { SizeTypeEnum } from "ng2/common/enums/size-type.enum";
import { ValueTypeEnum } from "ng2/common/enums/value-type.enum";

import type { ILayoutComponent } from "ng2/common/model/components/base/layout-component.model";
import type { IDataPair } from "ng2/common/model/data-pair.model";
import type { IJsonComponent, IJsonComponentSettings, IJsonSize, TypeDef } from "ng2/common/model/databearer/components/json-component.model";
import type { IJsonBindableField } from "ng2/common/model/databearer/json-binding.model";
import type { IJsonRuntimeManifest } from "ng2/common/model/databearer/json-manifest.model";
import type { IMap } from "ng2/common/model/imap.model";
import type { IDesignerManifest } from "ng2/common/model/project/mashup/designer-manifest.model";

import { LayoutComponent } from "ng2/components/base//layout-component";

export class ApplicationParent extends LayoutComponent {

	static APPLICATION_PARENT_DATABEARER: IJsonComponent = {
		typedef: ApplicationParent.name as TypeDef,
		id: "Application",
		displayName: "Application",
		isReadonly: false,
		boundarySettings: {} as IMap<IJsonComponentSettings>
	};
	static BINDABLE_FIELDS_2: IJsonBindableField[] = [{
		name: "$Language",
		ref: SettingKeys.LANGUAGE,
		valueType: ValueTypeEnum.STRING,
		write: false,
		read: true
	}];
	static DEFAULT_WIDTH: IJsonSize = { sizeType: SizeTypeEnum.PERCENT };
	static DEFAULT_HEIGHT: IJsonSize = { sizeType: SizeTypeEnum.PERCENT };
	static DEFAULT_TRIGGER: IDataPair = InteractionPairOperation.ON_STARTUP;
	static DEFAULT_ACTION: IDataPair = InteractionPairOperation.NONE;
	static SOURCE_TRIGGERS: IDataPair[] = [InteractionPairOperation.ON_STARTUP];
	static TARGET_ACTIONS: IDataPair[] = [InteractionPairOperation.NONE];

	currentTheme: ThemeEnum = 1;
	language: string;
	databearer: IJsonComponent;

	private manifest: IDesignerManifest;

	get hasIndividualChildSizes(): boolean {
		if (this.manifest.showTitleBar) {
			return true;
		}
		return false;
	}

	constructor(
		databearer: IJsonComponent,
		element: JQuery,
		parent: ILayoutComponent,
		manifest?: IDesignerManifest|IJsonRuntimeManifest
	) {
		super(null, null, null, null, null, null);

		this.manifest = manifest as IDesignerManifest;
		this.language = null;
		// this.getScope().componentInstance = this;
	}

	addChildInstance(child: ILayoutComponent): void {
		this.children[0] = child;
	}

	static createSpoofElement(): JQuery {
		const element = $("<div></div>");
		element.data("eir-scope", this);
		return element;
	}

	getPxPerPercentComponent(ori: OrientationEnum): number {
		return 0;
	}

}