import { IDatasource } from "common/model/datasource/datasource.model";
import { availableDatasourceTypes, InjectNames } from "ng2/common/constants/common.constant";
import { DatasourceTypeEnum } from "ng2/common/enums/datasource-type.enum";
import { HttpVerbEnum } from "ng2/common/enums/http-verb.enum";
import { IJsonDatasource, IJsonGenericDatasource } from "ng2/common/model/databearer/dataservice/json-datasource.model";
import { AngularConstants } from "../../../../common/angular-constants";
import { IDialogBaseScope } from "../../../../common/defines";
import { ILookupOptions } from "../../../../common/model/sohoxi/sohoxi-lookup.model";
import { DialogButtonType, DialogService, IDialog, StandardDialogButtons } from "../../../../common/services/dialog-service";
import { DatasourceService } from "../../../../components/services/datasource-service";
import { DesignerService } from "../../../services/designer-service";
import { IUpsertDialogParameter } from "../datasource-tab-ctrl";

/*
	Old and not used anymore except for BI data services which is legacy
*/

export class DatasourceUpsertDialogCtrl {

	public validForm = false;
	public buttonTitle: string;
	public datasource: IJsonDatasource;

	public datasourceTypeEnum = DatasourceTypeEnum;

	public datasourceType: string;
	public datasourceTypeOptions: ILookupOptions;

	private dialog: IDialog<IUpsertDialogParameter>;
	private datasources: IJsonDatasource[];

	static $inject = [
		AngularConstants.SCOPE,
		InjectNames.DIALOG_SERVICE,
		InjectNames.DATASOURCE_SERVICE,
		InjectNames.DESIGNER_SERVICE
	];

	constructor(
		protected scope: IDialogBaseScope<IUpsertDialogParameter>,
		protected dialogService: DialogService,
		protected datasourceService: DatasourceService,
		protected designerService: DesignerService
	) {
		this.dialog = scope.eirDialog;
		const parameter = this.dialog.parameter;
		if (parameter.isEdit) { // Edit
			this.buttonTitle = "Save";
			this.datasource = parameter.datasource;
			this.validForm = true;
			this.datasourceType =
				availableDatasourceTypes[this.datasource.typedef as any].name;
		} else { // Create
			this.buttonTitle = "Create";

			this.datasource = {
				typedef: null,
				id: "",
				displayName: "",
				version: 1
			};
		}

		this.datasources = parameter.datasources;
	}

	public close(): void {
		this.dialog.close();
	}

	protected saveable(): boolean {
		if (this.dialog.parameter.isEdit) {
			// Exclude itself datasource
			this.datasources = this.datasources.filter((datasource: IDatasource) => {
				return datasource.id !== this.datasource.id;
			});
		}

		const saveable = !this.hasNameConflict(this.datasources, this.datasource.displayName);
		return saveable;
	}

	private hasNameConflict(datasources: IJsonDatasource[], displayName: string): boolean {
		return datasources.some((datasource: IJsonDatasource) => {
			return datasource.displayName.toLowerCase() === displayName.toLowerCase();
		});
	}

	public showErrorDialog(message: string): void {
		this.dialogService.showMessage({
			title: "Operation Failed",
			message: message,
			standardButtons: StandardDialogButtons.Ok,
			isError: true,
		});
	}

	private removeDisabledFields(): IJsonGenericDatasource {
		const datasource: IJsonGenericDatasource = this.datasource;
		const method = datasource.method;
		if (method === HttpVerbEnum.GET || method === HttpVerbEnum.HEAD) {
			delete datasource.bodyParameters;
		}
		if (datasource.isOutputFile) {
			delete datasource.outputParameters;
		}
		return datasource;
	}

	public upsertDatasource(): void {
		let newDatasource: IJsonDatasource = this.datasource;

		if (this.datasource.typedef === DatasourceTypeEnum.generic) {
			newDatasource = this.removeDisabledFields();
		}
		if (this.saveable()) {
			this.dialog.close({ button: DialogButtonType.Ok, value: newDatasource });
		}
	}

	static add(m: ng.IModule): void {
		m.controller("eirDatasourceUpsertDialogCtrl", DatasourceUpsertDialogCtrl);
	}
}
