import { FeatureIdEnum, InjectNames, ServerPaths } from "ng2/common/constants/common.constant";
import { AngularConstants } from "../angular-constants";
import { HttpService } from "./http-service";

export interface IFeature {
	readonly id: FeatureIdEnum;
	readonly description: string;
	readonly defaultFromDate: number;
	readonly isInternal: boolean;
	readonly togglable: boolean;
	readonly status?: IFeatureStatus;
}

export interface IFeatureStatus {
	readonly id: FeatureIdEnum;
	readonly enabled: boolean;
	readonly lastChangedBy: string;
	readonly lastChangedByDisplayName: string;
	readonly lastChangedDate: number;
}

/**
 * This service is used to read/update information about feature switches.
 * It will synchronize with the backend upon injection, and whenever something is expected
 * to change (e.g enabling/disabling a feature).
 *
 * Use this service to check whether a feature is enabled or not.
 *
 * Administrators can use it to enable/disable features.
 */
export class FeatureSwitchService {
	private readonly features: IFeature[] = [];
	static $inject = [
		InjectNames.HTTP_SERVICE,
		AngularConstants.Q
	];

	constructor(private httpService: HttpService, private $q: ng.IQService) {
		this.fetchFeatures()
			.catch(error => {
				// Could happen, but how should it be displayed?
			});
	}

	private fetchFeatures(): ng.IPromise<void> {
		const path = ServerPaths.SETTINGS + ServerPaths.SWITCHABLE_FEATURES;
		return this.httpService.executeGet<IFeature[]>(path)
			.then(fetchedFeatures => {
				this.features.splice(0, this.features.length);
				fetchedFeatures.forEach(feature => {
					this.features.push(feature);
				});
			});
	}

	public getFeatures(): IFeature[] {
		return this.features;
	}

	/**
	 * Get the feature with a given id.
	 *
	 * Returns undefined if feature does not exist, i.e it wasn't one of the features retrieved from the backend.
	 */
	public getFeature(id: FeatureIdEnum): (IFeature | undefined) {
		const feature = this.features.find(feature => feature.id === id);
		return feature;
	}

	public isFeatureEnabled(id: FeatureIdEnum): boolean {
		const feature = this.getFeature(id);
		return feature && feature.status && feature.status.enabled;
	}

	public toggleFeatureStatus(id: FeatureIdEnum): ng.IPromise<void> {
		const feature = this.getFeature(id);
		if (!feature) {
			return this.$q.reject(`Feature with id '${id}' does not exist`);
		}
		const enable = !this.isFeatureEnabled(id);
		const path = ServerPaths.SETTINGS + ServerPaths.SWITCHABLE_FEATURES + id + "/status";
		return this.httpService.executePost(path, undefined, { enable }).then(() => {
			return this.fetchFeatures();
		});
	}

	static add(ngModule: ng.IModule): void {
		ngModule.service(InjectNames.FEATURE_SWITCH_SERVICE, FeatureSwitchService);
	}
}
