import angular from "angular";

import { commonModule } from "../common/module";

import { DatasourceService } from "./services/datasource-service";
import { FrameworkService } from "./services/framework-service";
import { IonApiService } from "./services/ion-api-service";
import { IonMetadataService } from "./services/ion-metadata-service";
import { LocalIdoService } from "./services/local-ido-service";
import { TranslationManagementService } from "./services/translation-management-service";

import { Defines } from "./defines";

import { ChildrenDirective } from "./children-directive";
import { ComponentCommonDirective } from "./component-common";
import { ComponentDirective } from "./component-directive";
import { TitlebarDirective } from "./titlebar/titlebar";

export const componentsModule = angular.module("componentsModule", [
	commonModule.name
]);

componentsModule.config(["$qProvider", ($qProvider: ng.IQProvider) => {
	$qProvider.errorOnUnhandledRejections(false);
}]);

// Add services
DatasourceService.add(componentsModule);
FrameworkService.add(componentsModule);
IonApiService.add(componentsModule);
IonMetadataService.add(componentsModule);
LocalIdoService.add(componentsModule);
TranslationManagementService.add(componentsModule);

// Add components
Defines.addComponents(componentsModule);

// Add directives
ChildrenDirective.add(componentsModule);
ComponentCommonDirective.add(componentsModule);
ComponentDirective.add(componentsModule);
TitlebarDirective.add(componentsModule);
