import angular from "angular";
import { AngularConstants } from "../../../../common/angular-constants";

import {
	CssConstantKeys,
	CssConstantValues,
	IChanged,
	InjectNames,
	LanguageConsts,
	SettingKeys,
	SubstitutionTags,
} from "ng2/common/constants/common.constant";
import { SelectableEnum } from "ng2/common/constants/sohoxi.constant";
import { IMingleLanguage, IMongooseString } from "ng2/common/model/language.model";
import { IDataGridColumn, IDatagridColumnArgs, IDataGridOptions } from "ng2/common/model/sohoxi/sohoxi-datagrid.model";
import { IPageRequest } from "ng2/common/model/sohoxi/sohoxi-pager.model";

import { IDialogBaseScope } from "../../../../common/defines";
import { Helper } from "../../../../common/helper";
import { IComponent } from "../../../../common/IComponent";
import { DialogButtonType, DialogService, IDialog, IDialogResult, IMessageDialogOptions, StandardDialogButtons } from "../../../../common/services/dialog-service";
import { DatagridHelper, IRowItem } from "../../../../common/sohoxi/datagrid-helper";
import { FrameworkService } from "../../../../components/services/framework-service";
import { IComponentProperty, TranslationManagementService } from "../../../../components/services/translation-management-service";

export interface ITreePosition {
	row: number;
	column: number;
}

export interface ITranslationDataset {
	componentId: string;
	reference: string;
	locale: string;
	displayName: string;
	value: string;
}

export interface ILanguageDataset {
	id: string;
	component: {
		id: string,
		displayName: string
	};
	property: {
		ref: string,
		displayName: string
	};
	expanded: boolean;
	children: ITranslationDataset[];
}

export enum TranslationTabEnum { COMPONENT, GLOBAL }

export class TranslationDialogCtrl {
	public componentDatagridOptions: IDataGridOptions<ILanguageDataset>;
	public globalDatagridOptions: IDataGridOptions<{}>;
	public supportedLanguages: IMingleLanguage[] = [];
	public usedLanguages: IMingleLanguage[] = [];
	public currentLanguageDisplayName: string;
	public tabEnum = TranslationTabEnum;
	public currentTab: TranslationTabEnum = TranslationTabEnum.COMPONENT;
	public selectedGlobalString: IMongooseString;
	private dialog: IDialog<{}>;
	private dataset: ILanguageDataset[] = [];
	private deletedLanguages: IMingleLanguage[] = [];
	private addedLanguages: IMingleLanguage[] = [];
	private isChanged: IChanged = {
		savedComponentStrings: [],
		savedGlobalStrings: {},
		deletedGlobalStrings: {}
	};
	private invalidPositions: ITreePosition[] = [];
	private validPositions: ITreePosition[] = [];
	private selectedComponent: IComponent;
	private originalComponent: IComponent;
	private selectedProperty: string;
	private currentLocale: string;
	private selectedFilterLanguage: string = "All";
	private selectedGlobalLocale: string;
	private globalDataset: IMongooseString[];

	public static $inject = [
		AngularConstants.SCOPE,
		AngularConstants.ELEMENT,
		InjectNames.FRAMEWORK_SERVICE,
		InjectNames.DIALOG_SERVICE,
		InjectNames.TRANSLATION_MANAGEMENT_SERVICE
	];

	public constructor(
		private scope: IDialogBaseScope<IComponentProperty>,
		private $element: ng.IAugmentedJQuery,
		private frameworkService: FrameworkService,
		private dialogService: DialogService,
		private translationManagementService: TranslationManagementService,
	) {
		const dialog = scope.eirDialog;
		this.dialog = dialog;
		this.selectedComponent = this.originalComponent = dialog.parameter.component;
		this.selectedProperty = dialog.parameter.property;
		this.buildDataset();
		this.initComponentGridOptions();

		this.buildGlobalDataset();
		this.initGlobalGridOptions();
		this.usedLanguages = this.translationManagementService.getProjectLanguages();
		this.supportedLanguages = this.translationManagementService.getSupportedLanguages();

		this.selectedGlobalLocale = this.currentLocale = this.translationManagementService.getCurrentLanguage();
		this.currentLanguageDisplayName = this.translationManagementService.getLocaleDisplayName(this.currentLocale);

		this.updateDataset(); //component vs all
	}

	public onChangeTab(tab: TranslationTabEnum): void {
		this.currentTab = tab;
	}

	public save(): void {
		this.saveToProject();
		const result: IDialogResult<IChanged> = {
			button: DialogButtonType.Ok,
			value: this.isChanged
		};
		this.dialog.close(result);
	}

	public close(): void {
		this.dialog.close({
			button: DialogButtonType.Cancel
		});
	}

	public openLanguage(language: IMingleLanguage): void {
		this.translationManagementService.fetchGlobalStrings(language.key).then((globalStrings) => {
			this.selectedGlobalLocale = language.key;

			const stringList = [];
			for (const key in globalStrings) {
				if (globalStrings.hasOwnProperty(key) && typeof globalStrings[key] !== "string") {
					stringList.push(globalStrings[key]);
				}
			}
			this.reloadGlobalStringDatagrid(stringList);
		});
	}

	public toggleAllTranslations(): void {
		if (!this.selectedComponent) {
			this.selectedComponent = this.originalComponent;
		} else {
			this.selectedComponent = null;
		}
		this.reloadComponentDatagrid();
	}

	public filterLanguage(localeKey: string): void {
		if (localeKey !== this.selectedFilterLanguage) {
			if (this.selectedFilterLanguage === localeKey) {
				this.selectedFilterLanguage = "All";
			} else {
				this.selectedFilterLanguage = localeKey;
			}
			this.reloadComponentDatagrid();
		}
	}

	public addLanguage(language: IMingleLanguage): void {
		const languageExists = this.usedLanguages.some((usedLanguage) => {
			return usedLanguage.key === language.key;
		});
		if (!languageExists) {
			if (!this.translationManagementService.projectHasLanguage(language.key)) {
				this.updateLanguageLists(language, this.addedLanguages, this.deletedLanguages);
			} else {//if in project delete only from added-list
				this.deletedLanguages = this.deletedLanguages.filter((deletedLanguage) => {
					return language.key !== deletedLanguage.key;
				});
			}
			this.usedLanguages.push(language);
			this.dataset.forEach((data) => {
				data.expanded = this.isExpanded(data.id);
				const value = data.children[0].value; // set default value
				data.children.push({
					componentId: data.component.id,
					reference: data.property.ref,
					locale: language.key,
					displayName: language.value,
					value: value
				});
			});
			this.updateDataset();
		} else {
			this.dialogService.showError("Add Language", "Language already exists.");
		}
	}

	public addNewString(): void {
		this.translationManagementService.openNewStringDialog().then((result) => {
			if (result.button === DialogButtonType.Ok) {
				const newString = result.value as IMongooseString;
				let alreadyAddedItem = null;
				if (this.isChanged.savedGlobalStrings.hasOwnProperty(this.selectedGlobalLocale)) {
					alreadyAddedItem = this.isChanged.savedGlobalStrings[this.selectedGlobalLocale].find((item) => {
						return item.Name === newString.Name;
					});
				}

				if (!alreadyAddedItem) {
					this.globalDataset.unshift(result.value);
					this.globalDatagridOptions.dataset = this.globalDataset;
					this.saveString(newString);
				} else {
					this.dialogService.showError("Add String", "String already added");
				}
			}
		});
	}

	public deleteGlobalString(): void {
		const deletedString = this.selectedGlobalString;
		const language = this.translationManagementService.getLocaleDisplayName(this.selectedGlobalLocale);

		const options: IMessageDialogOptions = {
			title: "Delete " + this.selectedGlobalString.Name,
			message:
				`Are you sure you want to delete this string on language "${language}"?`,
			standardButtons: StandardDialogButtons.YesNo
		};
		this.dialogService.showMessage(options).then((result) => {
			if (result.button === DialogButtonType.Yes) {
				const deletedStrings = this.isChanged.deletedGlobalStrings[this.selectedGlobalLocale];
				if (deletedStrings) {
					const existingIndex = deletedStrings.findIndex((stringItem) => {
						return stringItem.Name === deletedString.Name;
					});
					if (existingIndex === -1) {
						deletedStrings.push(deletedString);
					} else {
						deletedStrings[existingIndex] = deletedString;
					}
				} else {
					this.isChanged.deletedGlobalStrings[this.selectedGlobalLocale] = [deletedString];
				}
				this.deleteStringFromDataset(deletedString.Name);
			}
		});
	}

	public deleteLanguage(language: IMingleLanguage): void {
		if (language.key !== LanguageConsts.DEFAULT.key) {
			if (this.translationManagementService.projectHasLanguage(language.key)) {
				this.updateLanguageLists(language, this.deletedLanguages, this.addedLanguages);
			} else { //if not in project delete only from added-list
				this.addedLanguages = this.addedLanguages.filter((addedLanguage) => {
					return language.key !== addedLanguage.key;
				});
			}
			this.usedLanguages = this.usedLanguages.filter((usedLanguage) => {
				return usedLanguage.key !== language.key;
			});
			this.dataset.forEach((data, datasetIndex) => {
				data.expanded = this.isExpanded(data.id);
				const translations = data.children;
				for (let i = 0; i < translations.length; i++) {
					if (this.translationManagementService.localeEquals(translations[i].locale, language.key)) {
						translations.splice(i, 1);
						break;
					}
				}
			});
			this.updateDataset();
		} else {
			this.dialogService.showError("Delete Language", "Can not delete Language: " + LanguageConsts.DEFAULT.value);
		}
	}

	private isExpanded(id: string): boolean {
		let expanded = false;
		const component = this.componentDatagridOptions.dataset.find((languageComponent) => {
			return languageComponent.id === id;
		});
		if (component) {
			expanded = component.expanded;
		}

		return expanded;
	}

	private reloadComponentDatagrid(): void {
		this.componentDatagridOptions = null;
		this.initComponentGridOptions();
		this.updateDataset();
		this.$element.find("#component-tab").find(".datagrid-container")
			.data("datagrid").updateColumns(this.componentDatagridOptions.columns);
	}

	private updateDataset(): void {
		let filteredDataset = angular.copy(this.dataset);
		if (this.selectedComponent) {
			filteredDataset = filteredDataset.filter((languageComponent) => {
				return languageComponent.component.id === this.selectedComponent.getId();
			});
		}
		for (let i = 0; i < filteredDataset.length; i++) {
			filteredDataset[i].children = filteredDataset[i].children.filter((child) => {
				return this.usedLanguages.find((usedLanguage) => {
					return usedLanguage.key === child.locale;
				});
			});
		}
		this.filterDatasetOnLocale(filteredDataset);
	}

	private filterDatasetOnLocale(dataset: ILanguageDataset[] = this.dataset): void {
		const filteredDataset = angular.copy(dataset);
		if (this.selectedFilterLanguage !== "All" && filteredDataset[0].children.length > 0) {
			for (let i = 0; i < filteredDataset.length; i++) {
				filteredDataset[i].children = filteredDataset[i].children.filter((child) => {
					return child.locale === this.selectedFilterLanguage;
				});
			}
		}
		this.componentDatagridOptions.dataset = filteredDataset;
	}

	private deleteStringFromDataset(stringName: string): void {
		const index = this.globalDataset.findIndex((stringItem) => {
			return stringItem.Name === stringName;
		});

		if (index !== -1) {
			this.globalDataset.splice(index, 1);
			this.selectedGlobalString = null;
		}
	}

	private reloadGlobalStringDatagrid(globalStrings: IMongooseString[]): void {
		this.globalDataset = globalStrings;
		this.initGlobalGridOptions();
		this.$element.find("#global-language-grid").data("datagrid").updated();
	}

	private saveString(modifiedString: IMongooseString): void {
		const modifiedStrings = this.isChanged.savedGlobalStrings[this.selectedGlobalLocale];
		if (modifiedStrings) {
			const existingIndex = modifiedStrings.findIndex((stringItem) => {
				return stringItem.Name === modifiedString.Name;
			});
			if (existingIndex === -1) {
				modifiedStrings.push(modifiedString);
			} else {
				modifiedStrings[existingIndex] = modifiedString;
			}
		} else {
			this.isChanged.savedGlobalStrings[this.selectedGlobalLocale] = [modifiedString];
		}
	}

	private buildGlobalDataset(): void {
		this.globalDataset = angular.copy(this.translationManagementService.getCurrentStringsList());
	}

	private buildDataset(): void {
		const newDataset: ILanguageDataset[] = [];
		const languageComponents = this.frameworkService.getCurrentProject().getTranslationsList();
		languageComponents.forEach((languageComponent, componentIndex) => {
			const componentInstance = this.frameworkService.getComponentInstance(languageComponent.componentId);

			if (componentInstance) {
				let isExpanded = false;
				const datasetComponent = this.dataset[componentIndex];
				if (datasetComponent) {
					isExpanded = this.dataset[componentIndex].expanded;
				} else if (this.originalComponent) {
					if (languageComponent.componentId === this.originalComponent.getId() &&
					languageComponent.property === this.selectedProperty) {
						isExpanded = true;
					}
				} else if (languageComponent.property === this.selectedProperty) {
					isExpanded = true;
				}
				const componentId = languageComponent.componentId;
				const property = languageComponent.property;
				let componentName = Helper.htmlEncode(componentInstance.getDisplayName());
				let propertyName = this.frameworkService.getTranslatableFieldDisplayName(componentId, property);
				if (property === SettingKeys.APPLICATION_HEADER) {
					// TODO: remove when header is component
					componentName = "Application Header";
					propertyName = "Content";
				}
				const datasetLanguage: ILanguageDataset = {
					id: languageComponent.id,
					component: {
						displayName: componentName,
						id: componentId
					},
					property: {
						ref: languageComponent.property,
						displayName: propertyName
					},
					children: [],
					expanded: isExpanded
				};
				languageComponent.translations.forEach((translation, translationIndex) => {
					if (translation.locale === this.selectedFilterLanguage || this.selectedFilterLanguage === "All") {
						let value: string;
						const childExists = datasetComponent && datasetComponent.children[translationIndex];
						if (childExists) {
							value = datasetComponent.children[translationIndex].value;
						}
						datasetLanguage.children.push({
							componentId: componentId,
							reference: property,
							displayName: this.translationManagementService.getLocaleDisplayName(translation.locale),
							locale: translation.locale,
							value: value ? value : translation.value
						});
					}
				});
				newDataset.push(datasetLanguage);
			} else {
				// Out of sync, delete corrupt translation.
				// Should not happen.
				this.translationManagementService.removeTranslation(languageComponent.componentId);
			}

		});
		this.dataset = newDataset;
	}

	/*
	* This method handle the binding and checks if it has substitutiontags before
	* it is edited. Saving locally to list of valids and invalids determine if you can
	* save or not.
	*/
	private validate(args: IDatagridColumnArgs<ITranslationDataset>): void {
		const cellElement = args.target[0];
		const position: ITreePosition = { row: args.row, column: args.cell };
		const tagExistsOld = args.rowData.value.indexOf(SubstitutionTags.VALUE) !== -1;
		const tagExistsNew = args.value.indexOf(SubstitutionTags.VALUE) !== -1;
		const isInvalid = this.existsInArray(position, this.invalidPositions);
		const isValid = this.existsInArray(position, this.validPositions);

		if (tagExistsOld || isInvalid) {
			if (!tagExistsNew && !isValid) {
				this.invalidPositions.push(position);
				cellElement.classList.add("error");
				cellElement.setAttribute("data-errormessage", "Required");
			} else if (tagExistsNew) {
				const updatedInvalids = this.invalidPositions.filter((position) => {
					return position.row !== args.row && position.column !== args.cell;
				});
				this.invalidPositions = updatedInvalids;
				cellElement.classList.remove("error");
			}
		} else {
			this.validPositions.push(position);
		}
		this.updateSaveButton();
	}

	private existsInArray(position: ITreePosition, array: ITreePosition[]): boolean {
		return array.some((invalidPosition, index) => {
			return invalidPosition.row === position.row && invalidPosition.column === position.column;
		});
	}

	private updateSaveButton(): void {
		// FIXME: ng-disable does not work so it had to be like this
		// some issue with calling this inside a element.on() ?
		const saveButtonElement = angular.element(".translation-save");
		const errorTextElement = this.$element.find(".error-text");
		if (this.invalidPositions.length > 0) {
			saveButtonElement.prop("disabled", true);
			errorTextElement.css(CssConstantKeys.DISPLAY, CssConstantValues.INLINE_BLOCK);
		} else {
			saveButtonElement.prop("disabled", false);
			errorTextElement.css(CssConstantKeys.DISPLAY, CssConstantValues.NONE);
		}
	}

	private updateLanguageLists(
		updatedLanguage: IMingleLanguage,
		firstLanguageList: IMingleLanguage[],
		secondLanguageList: IMingleLanguage[]
	): void {
		const languageExistsInList = firstLanguageList.some((language) => {
			return language.key === updatedLanguage.key;
		});
		if (!languageExistsInList) {
			firstLanguageList.push(updatedLanguage);
			secondLanguageList = secondLanguageList.filter((language) => {
				return language.key !== updatedLanguage.key;
			});
		}
	}

	private saveToProject(): void {
		if (this.addedLanguages.length > 0) {
			this.addedLanguages.forEach((language) => {
				this.translationManagementService.addLanguage(language);
			});
		}
		if (this.deletedLanguages.length > 0) {
			this.deletedLanguages.forEach((language) => {
				this.translationManagementService.deleteLanguage(language);
			});
		}
		this.dataset.forEach((data, componentIndex) => {
			data.children.forEach((translation, translationIndex) => {
				this.translationManagementService.setTranslatableStringByTranslationIndex(
					componentIndex,
					translationIndex,
					translation.value
				);
			});
		});
	}

	private initGlobalGridOptions(): void {
		const columns: IDataGridColumn[] = [];
		columns.push({
			id: "Name",
			name: "Name",
			field: "Name",
			width: "50%",
			resizable: false,
			sortable: true,
			formatter: Soho.Formatters.Ellipsis
		});
		columns.push({
			id: "Value",
			name: "Value",
			field: "Value",
			width: "50%",
			resizable: false,
			sortable: true,
			editor: Soho.Editors.Input,
			isEditable: () => {
				return true;
			},
			formatter: Soho.Formatters.Ellipsis
		});

		this.globalDatagridOptions = {
			columns: columns,
			dataset: [],
			selectable: SelectableEnum.Single,
			editable: true,
			isList: true,
			paging: true,
			toolbar: {
				title: "Translations",
				keywordFilter: true
			},
			source: (req: IPageRequest, response: Function) => {
				req.total = this.globalDataset.length;
				let datasetCopy = angular.copy(this.globalDataset);
				if (req.filterExpr && req.filterExpr[0]) {
					datasetCopy = DatagridHelper.getSearchResultOnPaths(datasetCopy, req.filterExpr[0], "Name");
				}
				const page = DatagridHelper.getPage(datasetCopy, req);
				response(page, req);
			},
		};

		this.$element.find("#global-language-grid").on("selected",
			(e: JQueryEventObject, args: IRowItem<IMongooseString>[]) => {
				if (args.length > 0) {
					this.scope.$evalAsync(() => {
						this.selectedGlobalString = {
							Name: args[0].data.Name,
							Value: args[0].data.Value
						};
					});
				}
			}
		);

		this.$element.find("#global-language-grid").on("cellchange",
			(e: JQueryEventObject, args: IDatagridColumnArgs<IMongooseString>) => {
				const hasNewValue = args.value !== args.oldValue;
				if (hasNewValue && args.oldValue !== undefined) {
					const modifiedString: IMongooseString = {
						Name: args.rowData.Name,
						Value: args.rowData.Value,
						ScopeInfo: {
							ScopeType: args.rowData.ScopeInfo ? args.rowData.ScopeInfo.ScopeType : null
						}
					};
					this.saveString(modifiedString);
				}
			}
		);
	}

	private initComponentGridOptions(): void {
		const columns: IDataGridColumn[] = [];
		if (!this.selectedComponent) {
			columns.push({
				id: "component",
				name: "Component",
				field: "component.displayName",
				width: "25%",
				sortable: true,
				formatter: Soho.Formatters.Tree
			});
		}
		columns.push({
			id: "property",
			name: "Property",
			field: "property.displayName",
			width: "25%",
			sortable: true,
			formatter: this.selectedComponent ? Soho.Formatters.Tree : Soho.Formatters.Ellipsis
		});
		columns.push({
			id: "id",
			name: "Id",
			field: "id",
			hidden: true
		});
		columns.push({
			id: "name",
			name: "Language",
			field: "displayName",
			width: "25%",
			sortable: true,
		});
		columns.push({
			id: "value",
			name: "Translation",
			field: "value",
			width: "25%",
			sortable: true,
			editor: Soho.Editors.Input,
			isEditable: () => {
				return true;
			},
			formatter: Soho.Formatters.Ellipsis
		});

		this.componentDatagridOptions = {
			columns: columns,
			dataset: [],
			treeGrid: true,
			selectable: SelectableEnum.Single,
			editable: true,
			isList: true,
			toolbar: {
				title: "Translations",
				keywordFilter: true,
				results: true,
				dateFilter: false,
				advancedFilter: true,
				actions: true,
				views: true,
				rowHeight: true
			}
		};

		this.$element.find("#component-language-grid").on("cellchange",
			(_, args: IDatagridColumnArgs<ITranslationDataset>) => {
				const hasNewValue = args.value !== args.oldValue;
				if (hasNewValue && args.rowData.value !== undefined) {
					this.validate(args);
					this.syncDataset(args);
					const currentLocale = this.translationManagementService.getCurrentLanguage();
					if (this.translationManagementService.localeEquals(currentLocale, args.rowData.locale)) {
						this.updateTranslationChange(args);
					}
				}
			});
	}

	private syncDataset(item: IDatagridColumnArgs<ITranslationDataset>): void {
		for (let i = 0; i < this.dataset.length; i++) {
			const correctComponent = this.dataset[i].component.id === item.rowData.componentId;
			const correctProperty = this.dataset[i].property.ref === item.rowData.reference;
			if (correctComponent && correctProperty) {
				for (let j = 0; j < this.dataset[i].children.length; j++) {
					const correctLocale = this.dataset[i].children[j].locale === item.rowData.locale;
					if (correctLocale) {
						this.dataset[i].children[j].value = item.value;
					}
				}
			}
		}
	}

	private updateTranslationChange(datagridArgs: IDatagridColumnArgs<ITranslationDataset>): void {
		const componentId = datagridArgs.rowData.componentId;
		const ref = datagridArgs.rowData.reference;
		const newValue = datagridArgs.value;
		const existingValue = this.isChanged.savedComponentStrings.find((value) => {
			return value.component === componentId && value.reference === ref;
		});
		if (!existingValue) {
			this.isChanged.savedComponentStrings.push({
				component: componentId,
				reference: ref,
				newValue: newValue
			});

		} else if (existingValue.newValue !== newValue) {
			existingValue.newValue = newValue;
		}
	}

	public static add(m: ng.IModule): void {
		m.controller("eirTranslationDialogCtrl", TranslationDialogCtrl);
	}
}
