import { DEFAULT_BOUNDARY } from "ng2/common/constants/common.constant";
import { IJsonTabPanel } from "../../../../common/model/component/tabs.model";
import { TabPanel } from "../../../../components/tabs/tab-panel";
import { IEditorFlexbox } from "../../components/editor-flexbox";
import { ComponentSettingsCtrl } from "../component-settings";
import { FlexboxContentSettingsDirective } from "../flexbox/flexbox-content-settings";

export interface IEditorTabPanel extends IEditorFlexbox {
}

export class TabPanelSettings {

	private static instance: TabPanelSettings = new TabPanelSettings();

	static getInstance(): TabPanelSettings {
		return TabPanelSettings.instance;
	}

	constructor() {
		if (TabPanelSettings.instance) {
			throw new Error("Error: Instantiation failed: Use FlexboxSettings.getInstance() instead of new.");
		}
	}

	contentSettingsDirective: ng.IDirective = new FlexboxContentSettingsDirective();

	create(id: string, panelIndex?: number): IJsonTabPanel {
		const newPanel = ComponentSettingsCtrl.create(id, TabPanel) as IJsonTabPanel;
		newPanel.children = [];

		const boundaryId = DEFAULT_BOUNDARY.id;
		newPanel.boundarySettings[boundaryId].visibility = panelIndex === 1 ? true : false;

		return newPanel;
	}
}
