import { DatasourceTypeEnum } from "ng2/common/enums/datasource-type.enum";
import { HttpAcceptEnum } from "ng2/common/enums/http-accept-enum";
import { HttpVerbEnum } from "ng2/common/enums/http-verb.enum";
import { IListDataItem } from "ng2/common/model/dataservice/list-data-item.model";
import { ILocalIdoParameterObject, IParameterObject } from "ng2/common/model/dataservice/parameter-object.model";
import { IMap } from "ng2/common/model/imap.model";

import * as sw from "ng2/common/model/swagger.model";

export interface IDatasourceDataset {
	name: string;
	description: string;
}

export interface IJsonDatasource {
	typedef?: DatasourceTypeEnum;
	version?: number;
	id?: string;
	displayName?: string;
	description?: string;
	// Set on server
	author?: string;
	authorDisplayName?: string;
	creationDate?: number;
	changedBy?: string;
	changedByDisplayName?: string;
	changedDate?: number;
	headers?: IHttpHeader;
}

export interface IJsonGenericDatasource extends IJsonDatasource {
	// suite, endpoint and resource will together construct a Url
	suite?: string;
	endpoint?: string;
	resource?: string;

	method?: HttpVerbEnum;
	// bodyParameters are used only when method are not GET
	bodyParameters?: string;

	/* Key Value object where containing "query variable of {}s" */
	inputParameters?: IMap<string>;
	// TODO: change datatype in future
	outputParameters?: string;

	isOutputFile?: boolean;
}

export interface IJsonGenericDatasource2 extends IJsonDatasource {
	// suite, endpoint and resource will together construct a Url
	suite: string;
	endpoint: string;
	resource: string;

	method: HttpVerbEnum;

	inputSchema?: sw.ISchemaObject;
	outputSchema?: sw.ISchemaObject;
	originalSchema?: sw.ISchemaObject;
	inputParameters?: IParameterObject[];
	outputParameters?: IParameterObject[];
	bodyTemplate?: string;
	outputRootElement?: string;
	inputRootElement?: string;
	outputJson?: string;

	isOutputFile?: boolean;
	objectIdPath?: string;
}

export interface IJsonLocalIdoDatasource extends IJsonGenericDatasource2 {
	orderBy?: string;
	recordCap: IRecordCapOption;
	inputParameters: ILocalIdoParameterObject[];
	outputParameters: ILocalIdoParameterObject[];
}

export interface IJsonInlineListDatasource extends IJsonDatasource {
	suite: string;
	outputParameters: IJsonInlineListParameter[];
	dataset: IListDataItem[];
}

export interface IJsonInlineListParameter {
	id: string;
	key: string;
	field: string;
}

export interface IRecordCapOption {
	name: string;
	id: string;
	limit?: number;
}

export interface IJsonM3Datasource extends IJsonGenericDatasource {
	operations: IJsonM3Operation[];
}

export interface IJsonM3Operation {
	dataParameters: IMap<any>;
}

export interface IJsonM3CloudData{
	results: [
		{
			transaction: string,
			records: any[],
			errorMessage: string,
			errorType: string
		}
	],
	wasTerminated: boolean,
	nrOfSuccessfullTransactions: number,
	nrOfFailedTransactions: number,
}

export interface IJsonLNDatasource extends IJsonDatasource {
	SoapEnvelope: string;
	RelativeURL: string;
	ResultPath: string;
	AttrPrefix: string;
	Attributes: string[];
	Filter: IJsonLNComparison[];
	MaxNoObjects: number;
}

export interface IJsonLNComparison {
	Operator: string;
	Attribute: string;
	Value: string;
}

export interface IHttpHeader {
	// Informs the server about the types of data that can be sent back.
	Accept?: HttpAcceptEnum;
	// Contains the credentials to authenticate a user-agent with a server.
	Authorization?: string;
	"X-Infor-MongooseConfig"?: string;
	"Content-Type"?: ContentType;
}
export type ContentType = "application/json" | "text/plain";
