import angular from "angular";

import { IDataPair } from "ng2/common/model/data-pair.model";
import { AngularConstants } from "../../angular-constants";

export class FilterPopupmenuCtrl<T> {

	// component bindings
	public filterOptions: IDataPair[];
	public localPrefix: string;
	public filterValues: T[];

	private originalValues: T[];
	private previousFilterValues: T[];
	private currentSelectedFilter: IDataPair;
	private readonly filterOn = "filter-on";

	private orderBy: ng.IFilterOrderBy = this.$filter("orderBy");
	private filter: ng.IFilterFilter = this.$filter("filter");

	$inject = [
		AngularConstants.FILTER
	];

	constructor(
		private $filter: ng.IFilterService
	) { }

	public $onInit(): void {
		const storageName = this.localPrefix + this.filterOn;
		const storedFilter: IDataPair = JSON.parse(localStorage.getItem(storageName));
		this.originalValues = angular.copy(this.filterValues);

		if (!storedFilter) {
			const filterOption = this.filterOptions[0];
			localStorage.setItem(storageName, JSON.stringify(filterOption));
			this.currentSelectedFilter = filterOption;
		} else {
			this.currentSelectedFilter =
				this.filterOptions.find(x => x.name === storedFilter.name);
		}

		this.doFilter();
	}

	public $doCheck(): void {
		const previousValues = this.previousFilterValues;
		const currentValues = this.filterValues;

		if (previousValues.length !== currentValues.length ||
			this.equalLengthsNotSameContent(previousValues, currentValues)
		) {
			this.originalValues = angular.copy(currentValues);
			this.doFilter();
		}
	}

	public onFilter(filterOption: IDataPair): void {
		const storageName = this.localPrefix + this.filterOn;

		this.currentSelectedFilter = angular.copy(filterOption);
		localStorage.setItem(storageName, JSON.stringify({ name: filterOption.name }));

		this.doFilter();
	}

	private doFilter(): void {
		const currentSelectedFilter = this.currentSelectedFilter.value;

		if (currentSelectedFilter) {
			this.filterValues = this.filter(this.originalValues, currentSelectedFilter);
		} else {
			this.filterValues = this.originalValues;
		}
		this.previousFilterValues = this.filterValues;
	}

	private equalLengthsNotSameContent(listA: T[], listB: T[]): boolean {
		return (listA.length === listB.length &&
			!angular.equals(
				this.orderBy(listA, "manifest.createdTime"),
				this.orderBy(listB, "manifest.createdTime")));
	}

}
