import isEmpty from "lodash-es/isEmpty";

import { InjectNames } from "ng2/common/constants/common.constant";
import { IMap } from "ng2/common/model/imap.model";
import { DialogButtonType, DialogService, IDialogOptions, IDialogResult } from "../../../../../common/services/dialog-service";
import { IFieldValidationResult } from "../../../../../common/shared-components/base/field-validation";

export class RequestHeaderEditorCtrl implements ng.IComponentController {

	public static $inject: string[] = [
		InjectNames.DIALOG_SERVICE,
	];

	// Component Bindings
	public headers: IMap<string>;
	public wizardNextDisabled: ($item: any) => void;

	// Members
	public errorList: any[] = [];
	public isValid: boolean;
	private _parsedHeaders: string;
	public get parsedHeaders(): string { return this._parsedHeaders; }
	public set parsedHeaders(value: string) {
		this.isValid =
			this.isValidParsedHeaders(value);

		if (this.isValid && !isEmpty(value)) {
			this.headers = value
				.split("\n")
				// convert parsed header to header object
				.reduce((output: any, keyValuePair) => {
					const indexOfFirstColon = keyValuePair.indexOf(":");
					if (indexOfFirstColon !== -1) {
						const key = keyValuePair.substring(0, indexOfFirstColon);
						const value = keyValuePair.substring(indexOfFirstColon + 1);
						output[key] = value;
					} else {
						output[keyValuePair] = "";
					}
					return output;
				}, {});
		}

		if (isEmpty(value) && !isEmpty(this.headers)) {
			this.headers = {};
		}

		this._parsedHeaders = value;
	}

	public constructor(
		private dialogService: DialogService,
	) { }

	public $onInit(): void {
		this.parseHeader();
	}

	private parseHeader(): void {
		this.parsedHeaders = JSON
			.stringify(this.headers)
			.slice(1)		// removes {
			.slice(0, -1)	// removes }
			.split(",") // get rows of key:value
			.map(keyValue => keyValue.replace(/\"/g, "")) // replace occurrence of " with empty
			.join("\n");

	}

	public isValidParsedHeaders(value: string): boolean {
		const validHeaderRequestRow = /.+:{1}.+[\n]?/;
		const isNextDisabled = {
			$item: {
				id: RequestHeaderEditorComponent.componentName
			} as IFieldValidationResult
		};
		let hasInvalid = false;

		/* verify if parsed headers looks as following:
			foo1:bar2\nfoo2:bar2 */
		if (value.indexOf("\n") !== -1) {
			hasInvalid = value
				.split("\n")
				.some(keyValuePair => !validHeaderRequestRow.test(keyValuePair));
		} else if (isEmpty(value)) {
			hasInvalid = false;
		} else {
			hasInvalid = !validHeaderRequestRow.test(value);
		}

		isNextDisabled.$item.status = hasInvalid;

		if (this.wizardNextDisabled) {
			this.wizardNextDisabled(isNextDisabled);
		}

		return !isNextDisabled.$item.status;

	}

	public onEdit(): void {
		const dialogOptions: IDialogOptions<IMap<string>> = {
			title: "Request Header Editor",
			template: require("./dialog/request-editor-template.html"),
			parameter: this.headers
		};

		this.dialogService.show(dialogOptions)
			.then((dialogResult: IDialogResult<string>) => {
				if (dialogResult.button === DialogButtonType.Ok) {
					this.parsedHeaders = dialogResult.value;
					this.headers = JSON.parse(this.parsedHeaders);
				}
			});
	}
}

export class RequestHeaderEditorComponent implements ng.IComponentOptions {
	public static readonly componentName: string = "eirRequestHeaderEditor";
	public static readonly componentRoot: string =
		"designer/repository/datasource/create-wizard/request-header-editor/";
	public controller = RequestHeaderEditorCtrl;
	public controllerAs = "$ctrl";
	public template = require("./request-header-component-template.html");
	public bindings: IMap<string> = {
		headers: "=",
		wizardNextDisabled: "&?",
		readonly: "<?"
	};

	static add(m: ng.IModule): void {
		m.component(
			RequestHeaderEditorComponent.componentName,
			new RequestHeaderEditorComponent());
	}
}
