
import {
	ComponentClassNames,
	InteractionActionEnum,
	SettingKeys,
} from "ng2/common/constants/common.constant";

import { OrientationEnum } from "ng2/common/enums/orientation.enum";

import type { IDatasource } from "common/model/datasource/datasource.model";
import type { IComponentFieldSetting } from "ng2/common/model/components/settings/component-field-setting.model";
import type { IJsonList } from "ng2/common/model/databearer/components/json-list.model";

import type { IEditorComponent } from "common/interface/ieditor-component";
import { List } from "components/list/list";
import { EditorComponentCtrl, IEditorComponentScope } from "editor/components/editor-component";
import type { IEditorFlexbox } from "editor/components/editor-flexbox";

export interface IEditorList extends IEditorComponent, List {
	parent: IEditorFlexbox;
	// Overrides to make dual extend work
	getScope(): IEditorComponentScope;
	getDatabearer(): IJsonList;
	calcNewSizes(orientation?: OrientationEnum): void;
}

export class EditorListCtrl extends EditorComponentCtrl {
	static add(m: ng.IModule): void {
		m.controller(
			ComponentClassNames.EDITOR_COMPONENT_PREFIX + List.name,
			EditorListCtrl
		);
	}

	protected component: IEditorList;

	getMetaData(fieldRef: string, onSuccess: Function): void {
		const datasource = this.component.getDatasource();
		if (fieldRef === SettingKeys.CURRENT_ITEM && datasource) {
			datasource.getMetaData().then((metaData) => {
				onSuccess(metaData.outputFields);
			});
		}
	}

	setDatasource(newDatasource: IDatasource | null): ng.IPromise<void> {
		const promise = super.setDatasource(newDatasource);

		if (newDatasource) {
			newDatasource.getMetaData().then((metaData) => {
				this.component.forEachItemTemplate((templateComponent) => {
					this.editorService.setDefaultBindings(templateComponent, metaData.outputFields);
				});
				this.component
					.loadInteraction(this.interactionManagementService.createNewOnTheFly(InteractionActionEnum.LIST));
			});
		}
		return promise;
	}

	setSetting(settingMeta: IComponentFieldSetting, newSettingValue: any): void {
		super.setSetting(settingMeta, newSettingValue);
		switch (settingMeta.ref) {
			case SettingKeys.SEARCH:
				this.component.clearSearch();
				break;
		}
	}

	protected clearData(): void {
		super.clearData();
		this.component.refresh();
	}
}
