import { HttpClient } from "@angular/common/http";
import { query, value } from "jsonpath";
import isArray from "lodash-es/isArray";
import { Observable, throwError } from "rxjs";
import { map } from "rxjs/internal/operators/map";

import { DatasourceTypeEnum } from "ng2/common/enums/datasource-type.enum";
import { ParameterTypeEnum } from "ng2/common/enums/parameter-type.enum";

import type {
	IHttpHeader,
	IJsonDatasource,
	IJsonGenericDatasource2
} from "ng2/common/model/databearer/dataservice/json-datasource.model";
import type { IComponent } from "ng2/common/model/components/base/component.model";
import type { IListDataItem } from "ng2/common/model/dataservice/list-data-item.model";
import type { IInteractionParameter } from "ng2/common/model/interaction/interaction-parameter.model";
import type { IIonApiCall } from "ng2/common/model/ion-api/ion-api.model";
import type { IMap } from "ng2/common/model/imap.model";

import { DataServiceHelper } from "ng2/common/helpers/data-service.helper";
import { JsonHelper } from "ng2/common/helpers/json.helper";
import { DataService } from "ng2/common/project/data-service/base-data-service";
import { DynamicComponentService } from "ng2/components/services/dynamic-component.service";

export class Generic extends DataService {

	usedBy: IComponent[];

	typedef?: DatasourceTypeEnum;
	version?: number;
	id?: string;
	displayName?: string;
	description?: string;
	author?: string;
	authorDisplayName?: string;
	creationDate?: number;
	changedBy?: string;
	changedByDisplayName?: string;
	changedDate?: number;
	headers?: IHttpHeader;

	constructor(
		http: HttpClient,
		dynamicComponentService: DynamicComponentService,
		obj: IJsonDatasource,
	) {

		super(
			http,
			dynamicComponentService,
			obj,
		);
	}

	getDatabearer(): IJsonDatasource {
		return this.databearer;
	}

	// TODO: Look into why getDetailData is needed.
	getDetailData(interactionParameters: IInteractionParameter[]): Observable<any> {

		const parameters = this.readParameters(interactionParameters);
		const databearer = this.databearer as IJsonGenericDatasource2;

		const requestBody: any = undefined;
		const url = `${databearer.suite}${databearer.endpoint}${databearer.resource}`;
		const substituedUrl = this.setSubstitutionTags(url, requestBody, parameters);

		return this
			.ionApiRequest(substituedUrl, parameters)
			.pipe(
				map(data => {

					const list: IListDataItem[] = [];

					query(data, databearer.outputRootElement)
						.forEach((row) => {
							const listObject: IListDataItem = {};
							databearer.outputParameters.forEach((param) => {
								listObject[param.id] = value(row, "$." + param.key);
							});
							if (databearer.objectIdPath) {
								listObject.id = value(row, "$." + databearer.objectIdPath); //setting the id on the item
							}
							list.push(listObject);
						});

					return list;
				})
			);
	}

	getListData(interactionParameters: IInteractionParameter[]): Observable<any[]> {

		try {
			const parameters = this.readParameters(interactionParameters);
			const databearer = this.databearer as IJsonGenericDatasource2;
			const extraParameters: IMap<any> = {};

			const requestBody: any = undefined;
			const url = `${databearer.suite}${databearer.endpoint}${databearer.resource}`;

			databearer.inputParameters.forEach((ionapiParameter) => {
				const inputparameter = parameters[ionapiParameter.id];
				if (inputparameter && isArray(inputparameter)) { // if multiple parameters
					inputparameter.forEach((parameter, index) => {
						if (!parameters[index]) {
							parameters.push({});
						}
						parameters[index][ionapiParameter.id] = parameter;
					});
				} else if (inputparameter) {
					extraParameters[ionapiParameter.id] = inputparameter;
					if (parameters.length === 1) {
						parameters[0] = parameters;
					}
				}
			});

			// do something with substitution parameters in url
			const substituedUrl = this.setSubstitutionTags(url, requestBody, parameters);

			return this
				.ionApiRequest(substituedUrl, parameters)
				.pipe(
					map(data => {

						const list: IListDataItem[] = [];

						query(data, databearer.outputRootElement)
							.forEach((row) => {
								const listObject: IListDataItem = {};
								databearer.outputParameters.forEach((param) => {
									listObject[param.id] = value(row, "$." + param.key).trim();
								});
								if (databearer.objectIdPath) {
									listObject.id = value(row, "$." + databearer.objectIdPath); //setting the id on the item
								}
								list.push(listObject);
							});

						return list;
					})
				);
		} catch (error) {
			return throwError(error);
		}
	}

	// public getMetaData(): Promise<IMetaData>;

	// public getNonParsedData(params: IMap<any>): Promise<any[]>;

	private setSubstitutionTags(url: string, bodyParameter: string, parameters: IMap<any>): IIonApiCall {
		let value: any;
		let substitutionTag: string;

		const bodyParameterJson = bodyParameter ? JSON.parse(bodyParameter) : "";
		const databearer = this.databearer as IJsonGenericDatasource2;

		databearer.inputParameters.forEach((datasourceParameter) => {
			value = parameters[datasourceParameter.id];
			switch (datasourceParameter.parameterType) {
				case ParameterTypeEnum.PATH:
				case ParameterTypeEnum.MATRIX:
				case ParameterTypeEnum.QUERY:
					substitutionTag = DataServiceHelper.getURLVariable(datasourceParameter.key);
					if (url.indexOf(substitutionTag) !== -1) {
						if (value !== null && value !== undefined && value !== "") { // Exclude all false cases except 0
							url = url.replace(substitutionTag, encodeURIComponent(value));
						} else if (datasourceParameter.required) { // If required throw error
							throw Error("Parameter is required");
						} else { // Null, undefined or "", remove substitution tags
							if (datasourceParameter.parameterType === ParameterTypeEnum.MATRIX) { // Matrix
								const strToReplace = ";" + DataServiceHelper
									.getURLVariable(datasourceParameter.key, ParameterTypeEnum.MATRIX);
								url = url.replace(strToReplace, "");
							} else { // Query
								const fullSubstitutionTag =
									DataServiceHelper.getURLVariable(datasourceParameter.key, ParameterTypeEnum.QUERY);
								url = url.replace(fullSubstitutionTag, "");
								url = url.replace("?&", "?");
								url = url.replace("&&", "&");

								// Rm if there is a & at the end.
								// E.g: LstItmByItmGr?ITGR=004&
								if (url.charAt(url.length - 1) === "&") {
									url = url.substr(0, url.length - 1);
								}
							}
						}
					} else {
						throw Error("Substitution tag does not exist!");
					}
					break;
				case ParameterTypeEnum.BODY_PART: {
					const schema = JsonHelper.generateSchema(bodyParameterJson);
					const propertyType = JsonHelper.getPropertyType(schema, datasourceParameter.key);
					switch (propertyType) {
						case "number":
							value = parseFloat(value);
							break;
						case "string":
						default:
							break;
					}
					value(bodyParameterJson, datasourceParameter.key, value);

					break;
				}
				default:
				// Invalid inparameter type
			}
		});
		return {
			relativeUrl: url,
			requestBody: JSON.stringify(bodyParameterJson)
		};
	}

	private readParameters(
		parameters: IInteractionParameter[],
	): IMap<any> {

		const interactionParameters: IMap<any> = {};

		parameters.forEach(parameter => {

			const sourceComponentId = parameter.valueSource.sourceName;
			const sourceComponentKey = parameter.valueSource.sourceKey;

			// TODO: Static values not handled yet e.g. RIGHTTRIM
			if (sourceComponentKey) {
				const sourceComponentInstance = this.dynamicComponentService.componentRefs
					.find(componentRef => componentRef.instance.id === sourceComponentId)
					.instance;

				const sourceComponentValue = sourceComponentInstance[sourceComponentKey];
				interactionParameters[parameter.targetKey] = sourceComponentValue.trim();
			}
		});

		return interactionParameters;
	}
}
