import type { IJsonInteraction } from "ng2/common/model/databearer/json-interaction.model";
import type { IInteraction } from "ng2/common/model/interaction/interaction.model";

import { InteractionParameter } from "ng2/components/interaction/interaction-parameter";

export class Interaction implements IInteraction {
	displayName: string; //not saved in databearer
	triggerString: string; //not saved in databearer
	targetString: string; //not saved in databearer
	private databearer: IJsonInteraction;
	private parameters: InteractionParameter[] = [];

	constructor(obj: IJsonInteraction) {
		this.databearer = obj;
		if (obj.parameters) {
			obj.parameters.forEach((parameter) => {
				this.parameters.push(new InteractionParameter(parameter));
			});
		}
	}

	getDatabearer(): IJsonInteraction {
		return this.databearer;
	}

	getId(): string {
		return this.databearer.id;
	}

	get sourceName(): string {
		return this.databearer.sourceName;
	}
	set sourceName(sourceName: string) {
		this.databearer.sourceName = sourceName;
	}

	get sourceTriggerName(): string {
		return this.databearer.sourceTriggerName;
	}
	set sourceTriggerName(sourceTriggerName: string) {
		this.databearer.sourceTriggerName = sourceTriggerName;
	}

	get targetName(): string {
		return this.databearer.targetName;
	}
	set targetName(targetName: string) {
		this.databearer.targetName = targetName;
	}

	get targetActionName(): string {
		return this.databearer.targetActionName;
	}
	set targetActionName(targetActionName: string) {
		this.databearer.targetActionName = targetActionName;
	}

	getParameters(): InteractionParameter[] {
		return this.parameters;
	}

	getParameter(targetKey: string): InteractionParameter {
		return this.parameters.filter((param) => {
			return (param.targetKey === targetKey);
		}).pop();
	}

	addParameter(parameter: InteractionParameter): void {
		if (!this.databearer.parameters) {
			this.databearer.parameters = [];
		}
		this.databearer.parameters.push(parameter.getDatabearer());
		this.parameters.push(parameter);
	}

	removeParameter(index: number): void {
		this.databearer.parameters.splice(index, 1);
		this.parameters.splice(index, 1);
	}

	removeAllParameters(): void {
		this.databearer.parameters = null;
		this.parameters.length = 0;
	}

}
