import { NgModuleRef, NgZone } from "@angular/core";
import { platformBrowserDynamic } from "@angular/platform-browser-dynamic";
import { UrlService } from "@uirouter/core";

let ng1Import;
let ng2Import;

if (APP_BUILDER) {
	ng1Import = import("./appbuilder/app")
		.then(module => module.appBuilder);

	ng2Import = import("./app-builder/app.module")
		.then(module => module.RootModule);

} else if (APP_HUB) {
	ng1Import = import("./appruntime/app")
		.then(module => module.runtimeApp);

	ng2Import = import("./app-hub/app.module")
		.then(module => module.RootModule);

}

ng1Import.then(ng1RootModule => {
	// Using AngularJS config block, call `deferIntercept()`.
	// This tells UI-Router to delay the initial URL sync (until all bootstrapping is complete)
	ng1RootModule.config([
		"$urlServiceProvider",
		($urlService: UrlService) => $urlService.deferIntercept(),
	]);
});

ng2Import.then(ng2RootModule => {
	platformBrowserDynamic()
		.bootstrapModule(ng2RootModule)
		.then((platformRef: NgModuleRef<any>) => {
			// Intialize the Angular Module
			// get() the UIRouter instance from DI to initialize the router
			const urlService: UrlService = platformRef.injector.get(UrlService);

			// Instruct UIRouter to listen to URL changes
			const startUIRouter = () => {
				urlService.listen();
				urlService.sync();
			};

			const ngZone = platformRef.injector.get<NgZone>(NgZone);
			ngZone.run(startUIRouter);
		});
});
