import { Helper } from "../../common/helper";
import type { ILayoutComponent } from "../../common/interface/ilayout-component";
import type { IJsonImage } from "../../common/model/component/image.model";
import { Component } from "../component";
import { ComponentsHelper } from "../helper";
import { FrameworkService } from "../services/framework-service";

import {
	ImageBase64, ImagePositions,
	InteractionPairOperation, InteractionTriggerEnum, SettingInputKeys, SettingKeys, SidebarTabTypes
} from "ng2/common/constants/common.constant";
import { InteractionDefines } from "ng2/common/constants/interaction-defines.constant";

import { BindingTypeEnum } from "ng2/common/enums/binding-type.enum";
import { SizeTypeEnum } from "ng2/common/enums/size-type.enum";
import { ValueTypeEnum } from "ng2/common/enums/value-type.enum";

import type { IComponentSettingCategory } from "ng2/common/model/components/settings/component-setting-category.model";
import type { IDataPair } from "ng2/common/model/data-pair.model";
import type { IJsonSize } from "ng2/common/model/databearer/components/json-component.model";
import type { IJsonBindableField } from "ng2/common/model/databearer/json-binding.model";
import type { IInteraction } from "ng2/common/model/interaction/interaction.model";

export class Image extends Component {
	static availablePositionTypes: IDataPair[] = [
		{ name: "Fill", value: ImagePositions.FILL },
		{ name: "Fit", value: ImagePositions.FIT },
		{ name: "Center", value: ImagePositions.CENTER }
	];
	static DEFAULT_WIDTH: IJsonSize = { sizeType: SizeTypeEnum.PERCENT };
	static DEFAULT_HEIGHT: IJsonSize = { sizeType: SizeTypeEnum.PERCENT };
	static DEFAULT_ACTION: IDataPair = InteractionPairOperation.GET;
	static DEFAULT_TRIGGER: IDataPair = InteractionPairOperation.ON_LOAD;
	static SOURCE_TRIGGERS = InteractionDefines.SOURCE_TRIGGERS.concat([InteractionPairOperation.ON_CLICK]);
	static TARGET_ACTIONS: IDataPair[] = InteractionDefines.TARGET_ACTIONS;
	static SETTING_FIELDS: IComponentSettingCategory[] =
	([
		{
			title: "Position",
			tab: SidebarTabTypes.STYLE,
			settings: [{
				ref: SettingKeys.POSITION,
				inputType: SettingInputKeys.RADIO,
				dataset: Image.availablePositionTypes,
				defaultValue: ImagePositions.CENTER
			}]
		}] as IComponentSettingCategory[]).concat(Component.SETTING_FIELDS);

	static BINDABLE_FIELDS_2: IJsonBindableField[] = [{
		name: "Source",
		ref: SettingKeys.SOURCE,
		valueType: ValueTypeEnum.STRING,
		write: true,
		read: false,
		defaultValue: { Type: BindingTypeEnum.STATIC_VALUE, value: "" }
	}];

	position: string;
	source: any;

	constructor(
		obj: IJsonImage,
		$element: ng.IAugmentedJQuery,
		parent: ILayoutComponent,
		frameworkService: FrameworkService
	) {
		super(obj, $element, parent, frameworkService);

		if (obj) {
			this.position = obj.position;
		}
	}

	private setImageSource(newValue: Blob | string): void {
		if (newValue instanceof Blob) {
			this.source = (window.URL || window.webkitURL).createObjectURL(newValue);
		} else if (typeof newValue === "string" && this.getContext().DetailResult) {
			// e.g. 	newValue http://{replaceValue}/
			// 		DetailResult: {replaceValue: Value}
			this.source = Helper.stringReplace(newValue, this.getContext().DetailResult);
		} else if (typeof newValue === "string" && newValue.length > 0) {
			if (newValue.indexOf("http") === 0) {
				this.setImageFromUrlToSource(newValue);
			} else if (ComponentsHelper.isBase64(newValue)) {
				this.source = Helper.parseBase64(newValue);
			} else {
				this.source = newValue;
			}
		} else {
			this.source = ImageBase64.DEFAULT;
		}
	}

	private setImageFromUrlToSource(url: string): void {
		const result: string = this.cache.get(url);

		if (Helper.isUndefined(result)) {
			this.frameworkService.convertImageUrlToBase64(url)
				.then(base64result => this.source = base64result)
				.then(() => this.cache.put(url, this.source))
				.then(() => this.setErrorTooltip())
				.catch(error => {
					this.setErrorTooltip(error.message);
					this.source = ImageBase64.DEFAULT;
				});
		} else {
			this.source = result;
		}
	}

	loadInteraction(interaction: IInteraction): ng.IPromise<any | any[]> {
		const interactionPromise = super.loadInteraction(interaction);
		interactionPromise.catch(() => {
			this.source = ImageBase64.DEFAULT;
		});

		return interactionPromise;
	}

	setBindableValue(bindableReference: string, newValue: any): void {
		super.setBindableValue(bindableReference, newValue);
		if (bindableReference === SettingKeys.SOURCE) {
			this.setImageSource(newValue);
		}
	}

	click(): void {
		this.trigger(InteractionTriggerEnum.CLICK);
	}

	static directive = [() => {
		return {
			template: require("./image.html"),
			replace: true
		};
	}];
}
