import { Numbers, SettingKeys } from "ng2/common/constants/common.constant";
import { OrientationEnum } from "ng2/common/enums/orientation.enum";
import { SizeTypeEnum } from "ng2/common/enums/size-type.enum";
import { IJsonComponent, IJsonMargin, IJsonSize } from "ng2/common/model/databearer/components/json-component.model";
import { IJsonLayoutComponent } from "ng2/common/model/databearer/json-layout-component.model";
import { IMap } from "ng2/common/model/imap.model";

import { ILayoutComponentStyle } from "../common/defines";
import { IComponent } from "../common/IComponent";
import { ILayoutComponent } from "../common/interface/ilayout-component";
import { Component } from "./component";

export abstract class LayoutComponent extends Component implements ILayoutComponent {

	public abstract getPxPerPercentComponent(ori: OrientationEnum, percent: number): number;

	public abstract hasIndividualChildSizes(ori: OrientationEnum): boolean;

	public abstract resizableChildren(direction: OrientationEnum): boolean;

	public children: IComponent[] = [];
	protected databearer: IJsonLayoutComponent;

	public addChild(child: IJsonComponent, index: number): void {
		this.databearer.children.splice(index, 0, child);
	}

	public onChildrenReady(): void {
		// onChildReady
	}

	// Generic addFunction for all LayoutComponent
	public addChildInstance(child: IComponent): void {
		this.children.push(child);
	}

	public generateId(index: number): string {
		if (this.isGenerated) {
			return this.generatedId + "index" + index;
		} else {
			return null;
		}
	}

	public getPadding(): IMap<string> {
		const margin = this.getBoundarySetting<IJsonMargin>(SettingKeys.MARGIN);
		const padding: IMap<string> = {
			padding: margin.top + "px " + margin.right + "px " + margin.bottom + "px " + margin.left + "px"
		};
		return padding;
	}

	public newBoundaryEntered(boundaryId: string): void {
		super.newBoundaryEntered(boundaryId);

		this.children.forEach((child) => {
			child.newBoundaryEntered(boundaryId);
		});
	}

	protected getClassList(): string[] {
		const classList = super.getClassList();
		classList.push("layoutComponent");
		classList.push("col-md4");
		return classList;
	}

	protected getInlineStyle(): ILayoutComponentStyle {
		const style: ILayoutComponentStyle = super.getInlineStyle();
		style.padding = style.margin;
		style.margin = null;
		return style;
	}

	protected calcNewSize(sizeObject: IJsonSize, orientation: OrientationEnum, margin: number = 0): string {
		// Overloading because of margin calculation
		let retSize = "";
		if (this.parent.hasIndividualChildSizes(orientation)) {
			if (sizeObject.sizeType === SizeTypeEnum.PERCENT) {
				const pxPerPercentComponent = this.parent.getPxPerPercentComponent(orientation, sizeObject.value);
				retSize = "calc(" + (sizeObject.value || Numbers.HUNDRED) + "% - " + (pxPerPercentComponent) + "px)";
			} else if (sizeObject.sizeType === SizeTypeEnum.PIXELS) {
				retSize = "calc(" + sizeObject.value + "px)";
			}
		}
		return retSize;
	}

	// Overriding calcNewSizes in Component, important for inheritance
	public calcNewSizes(orientation?: OrientationEnum): void {
		super.calcNewSizes();
	}

	public forEachChild(fn: (child: IComponent) => void, doGenerated: boolean): void {
		super.forEachChild(fn, doGenerated);
		this.children.forEach((ch) => {
			ch.forEachChild(fn, doGenerated);
		});
	}

	public forEachParent(fn: (parent: ILayoutComponent) => void): void {
		super.forEachParent(fn);
		fn(this as any);
	}

}