import { AngularConstants } from "../angular-constants";

import { Config, InjectNames, LanguageConsts, ServerPaths } from "ng2/common/constants/common.constant";
import { HttpVerbEnum } from "ng2/common/enums/http-verb.enum";
import { IMap } from "ng2/common/model/imap.model";
import { IIonApiRequestOptions } from "ng2/common/model/ion-api/ion-api.model";
import { IMingleLanguage, IMongooseString } from "ng2/common/model/language.model";

import { IonApiService } from "../../components/services/ion-api-service";
import { Helper } from "../helper";
import { FeatureSwitchCtrl } from "../shared-components/base/feature-switch-ctrl";
import { FeatureSwitchService } from "./feature-switch-service";
import { HashService } from "./hash-service";
import { HttpService } from "./http-service";

export type UrlSearchParams = any;

interface ILocalStorageValue {
	locale: string;
	username: string;
}

export class LanguageService extends FeatureSwitchCtrl {
	private language: string;
	private currentStrings: IMap<IMongooseString> = {};
	private supportedLanguages: IMingleLanguage[] = LanguageConsts.SUPPORTED_LANGUAGES; //This will be overwritten

	static $inject = [
		AngularConstants.WINDOW,
		InjectNames.ION_API_SERVICE,
		InjectNames.FEATURE_SWITCH_SERVICE,
		InjectNames.HTTP_SERVICE,
		InjectNames.HASH_SERVICE,
	];

	constructor(
		private $window: ng.IWindowService,
		private ionApiService: IonApiService,
		featureSwitchService: FeatureSwitchService,
		private httpService: HttpService,
		private hashService: HashService,
	) {
		super(featureSwitchService);
		//What to do with this? Keep, discard?
		//this.fetchSupportedMingleLanguages();
	}

	initializeLanguageSettings(): ng.IPromise<void> {
		return this.loadSupportedLanguages().then(() => {
			this.detectLanguage();
			this.loadSohoLocale(this.language);
		}).finally(() => {
			return this.loadGlobalStrings();
		});
	}

	loadGlobalStrings(language?: string): ng.IPromise<void> {
		const locale = language ? language : "";
		return this.httpService.executeGet(ServerPaths.SETTINGS + "language/" + locale)
			.then((strings: IMap<IMongooseString>) => {
				this.currentStrings = strings;
			});
	}

	getGlobalString(name: string): string | null {
		let stringValue = null;
		if (this.currentStrings.hasOwnProperty(name)) {
			stringValue = this.currentStrings[name].Value;
		}

		return stringValue;
	}

	addGlobalString(stringItem: IMongooseString): ng.IPromise<void> {
		//Save with current language
		const savedValue: IMap<IMongooseString[]> = {};
		savedValue[this.getLanguage()] = [stringItem];
		return this.saveGlobalStrings(savedValue);
	}

	getCurrentStrings(): IMap<IMongooseString> {
		return this.currentStrings;
	}

	fetchGlobalStrings(locale: string): ng.IPromise<IMap<IMongooseString>> {
		return this.httpService.executeGet(ServerPaths.SETTINGS + "language/" + locale);
	}

	saveGlobalStrings(modifiedStrings: IMap<IMongooseString[]>): ng.IPromise<void> {
		for (const languageKey in modifiedStrings) {
			modifiedStrings[languageKey].forEach((globalString) => {
				this.currentStrings[globalString.Name] = globalString;
			});
		}
		return this.httpService.executePut(ServerPaths.SETTINGS + "translation/", modifiedStrings);
	}

	deleteGlobalStrings(deletedStrings: IMap<IMongooseString[]>): ng.IPromise<void> {
		for (const languageKey in deletedStrings) {
			deletedStrings[languageKey].forEach((globalString) => {
				delete this.currentStrings[globalString.Name];
			});
		}
		return this.httpService.executeDelete(ServerPaths.SETTINGS + "translation/", deletedStrings);
	}

	changeCurrentLanguage(locale: string): ng.IPromise<void> {
		return this.httpService.executePut(ServerPaths.SETTINGS + "language", locale).then(() => {
			this.language = locale;
			const localStorageValue: ILocalStorageValue = {
				locale: locale,
				username: Config.USER.name
			};
			localStorage.setItem("language", JSON.stringify(localStorageValue) );
		});
	}

	/**
	 * Detects the current language.and sets it to the singleton value
	 * Run this function if the singleton language value should change
	 *
	 * @memberof LanguageService
	 */
	detectLanguage(): void {
		const urlSearchParams = this.$window.parent.location.search;
		this.language = this.getLanguageThroughSearchParams(urlSearchParams);
	}

	getSupportedLanguages(): IMingleLanguage[] {
		return this.supportedLanguages;
	}

	/**
	 * Gets the current language through the singleton value.
	 *	Use this function to get the language stored in this singleton
		*	Has multiple fallbacks:
		*
		* 1. "$language" in url search params
		* 2. "inforCurrentLanguage" in url search params
		* 3. Browsers own language through window.navigator.language
		* 4. Default language which is en-US
		*
		* @returns {string}
		* @memberof LanguageService
		*/
	getLanguage(): string {
		if (this.isFeatureTranslationEnabled()) {
			if (this.language) {
				return this.language;
			}
			this.detectLanguage();
		}
		return this.language;
	}

	/**
	 * Get language with any url search params.
	 * Use this function if you want to get a new language without overriding the singleton value
	 * Has multiple fallbacks:
	 *
	 * 1. "$language" in url search params
	 * 2. "inforCurrentLanguage" in url search params
	 * 3. Browsers own language through window.navigator.language
	 * 4. Default language which is en-US
	 *
	 * @param {UrlSearchParams} urlSearchParams
	 * @returns {string}
	 * @memberof LanguageService
	 */
	getLanguageThroughSearchParams(urlSearchParams: UrlSearchParams): string {
		let language: string;

		language = this.getUrlLanguage(urlSearchParams);

		if (this.isValid(language) === false) {
			language = this.getMingleLanguage(urlSearchParams);
		}

		if (this.isValid(language) === false) {
			language = this.getLocalStorageLanguage();
		}

		if (this.isValid(language) === false) {
			language = Config.LOCALE; //Mongoose user-preference language
		}

		if (this.isValid(language) === false) {
			language = this.getBrowserLanguage();
		}

		if (this.isValid(language) === false || this.isSupported(language) === false) {
			language = this.getDefaultLanguage();
		}

		return language;
	}

	/**
	 * Loads Soho Locale without setting it
	 */
	loadSohoLocale(locale: string): JQueryDeferred<any> {
		let currentLanguage = this.supportedLanguages.find(language => {
			return language.key === locale;
		});

		if (Helper.isUndefinedOrNull(currentLanguage)) {
			currentLanguage = this.supportedLanguages[0];
		}

		return Soho.Locale.getLocale(currentLanguage.key, currentLanguage.filename);
	}

	private getLocalStorageLanguage(): string | null {
		let locale: string = null;
		const localStorageValue = JSON.parse(localStorage.getItem("language")) as ILocalStorageValue;
		if (localStorageValue && Config.USER.name === localStorageValue.username) {
			locale = localStorageValue.locale;
		}

		return locale;
	}

	/**
	 * Returns the language based on any $language in the url search params
	 *
	 * @private
	 * @param {string} urlSearchParams
	 * @returns {string}
	 * @memberof LanguageService
	 */
	private getUrlLanguage(urlSearchParams: string): string {
		const params = Helper.parseQueryString(urlSearchParams);
		return params.$language;
	}

	/**
	 * Returns the language based on any url search params containing "inforCurrentLanguage"
	 *
	 * @private
	 * @param {string} urlSearchParams
	 * @returns {string}
	 * @memberof LanguageService
	 */
	private getMingleLanguage(urlSearchParams: string): string {
		const params = Helper.parseQueryString(urlSearchParams);
		return params.inforCurrentLanguage;
	}

	/**
	 * Returns the language based on the window.navigator.language
	 *
	 * @private
	 * @returns {string}
	 * @memberof LanguageService
	 */
	private getBrowserLanguage(): string {
		const language = this.$window.navigator && this.$window.navigator.language;
		return language;
	}

	/**
	 * Returns a default languagee
	 *
	 * @public
	 * @param {UrlSearchParams} urlSearchParams
	 * @returns {string}
	 * @memberof LanguageService
	 */
	getDefaultLanguage(): string {
		return LanguageConsts.DEFAULT.key;
	}

	/**
	 * Checks if the language is a valid languagee
	 *
	 * @private
	 * @param {string} languagee
	 * @returns {boolean}
	 * @memberof LanguageService
	 */
	private isValid(language: string): boolean {
		if (language && /^[a-z0-9-_]+$/i.test(language)) {
			return true;
		}
		return false;
	}

	/**
	 * Checks if the language is supported
	 *
	 * @private
	 * @param {string} language
	 * @returns {boolean}
	 * @memberof LanguageService
	 */
	private isSupported(language: string): boolean {
		const supported = this.supportedLanguages.some((supportedLanguage) => {
			return this.isEqual(supportedLanguage.key, language);
		});
		/*if (supported) {
			if (LanguageConsts.UNSUPPORTED_LANGUAGES.indexOf(language) === -1) {
				return true;
			}
		}*/
		return supported;
	}

	/**
	 * Compare two locales.
	 * if browser gives two letters compare only the two first
	 *
	 * @private
	 * @memberof LanguageService
	 */
	private isEqual(supportedLanguageKey: string, languageKey: string): boolean {
		const lengthOfString = 2;
		if (languageKey.length === lengthOfString) {
			supportedLanguageKey = supportedLanguageKey.substring(0, lengthOfString);
		}
		return supportedLanguageKey === languageKey;
	}

	/**
	 * fetch all supported languages from mongoose, used once.
	 *
	 * @public
	 * @memberof LanguageService
	 */
	loadSupportedLanguages(): ng.IPromise<any> {
		return this.httpService
			.executeGet<IMingleLanguage[]>(ServerPaths.SETTINGS + "supportedLanguages/")
			.then(languages => {
				this.supportedLanguages = languages;
				const hashedFilenames = this.hashService.getHashMap();

				this.supportedLanguages.forEach(language => {
					this.mapFilenameToSupportedLanguages(language, hashedFilenames);
				});
			});
	}

	private mapFilenameToSupportedLanguages(
		language: IMingleLanguage,
		cacheBustedFilenames: IMap<string>
	): void {
		for (const item in cacheBustedFilenames) {
			if (cacheBustedFilenames[item].indexOf(language.key) !== -1) {
				const re = new RegExp(`${language.key}.[0-9a-z]*`);
				const isMatch = cacheBustedFilenames[item].match(re);
				if (isMatch) {
					language.filename = isMatch[0];
				}
			}
		}
	}

	/**
	 * fetch all supported languages from ming.le, used once.
	 * NOT USED, DEPRECATED
	 * @private
	 * @memberof LanguageService
	 */
	private fetchSupportedMingleLanguages(): ng.IPromise<any> {
		const options: IIonApiRequestOptions = {
			url: "/Mingle/Admin/System/Language",
			method: HttpVerbEnum.GET,
			headers: {},
		};
		return this.ionApiService.executeIonApiAsync(options).then((response) => {
			this.supportedLanguages = (response.data as any).LanguageList;
		});
	}

	/**
	 * get all supported languages,
	 * if IonApi is down for some reason it fall backs to static list
	 *
	 * @public
	 * @returns {IMingleLanguage[]}
	 * @memberof LanguageService
	 */
	/*getSupportedMingleLanguages(): IMingleLanguage[] {
		// Filter to remove the unsupported languages;
		const supportedLanguages = angular.copy(this.supportedLanguages);
		LanguageConsts.UNSUPPORTED_LANGUAGES.forEach((unsupportedLanguage) => {
			for (let i = 0; i < supportedLanguages.length; i++) {
				if (unsupportedLanguage === supportedLanguages[i].key) {
					supportedLanguages.splice(i, 1);
					break;
				}
			}
		});
		return supportedLanguages;
	}*/

	static add(ngModule: ng.IModule): void {
		ngModule.service(InjectNames.LANGUAGE_SERVICE, LanguageService);
	}
}
