import { IParameterObject } from "ng2/common/model/dataservice/parameter-object.model";
import { IDataGridColumn, IDataGridOptions } from "ng2/common/model/sohoxi/sohoxi-datagrid.model";

import { CustomFormatters } from "../../../../../../common/sohoxi/custom-formatters";

export class LocalIdoDataserviceSummaryDatagridCtrl {

	// component bindings
	public headTitle: string;
	public dataset: IParameterObject[];

	public datagridOptions: IDataGridOptions<IParameterObject>;

	public $onInit(): void {
		this.initDatagridOptions();
	}

	private initDatagridOptions(): void {
		const columns: IDataGridColumn[] = [
			{
				width: "30%",
				id: "key",
				name: "Name",
				field: "key",
				sortable: false
			},
			{
				width: "30%",
				id: "parameterType",
				name: "Type",
				field: "parameterType",
				sortable: false,
				formatter: CustomFormatters.DataSourceParameterType()
			},
			{
				width: "30%",
				id: "alias",
				name: "Alias",
				field: "alias",
				sortable: false
			}
		];

		this.datagridOptions = {
			columns: columns,
			isList: true,
			selectable: false,
			dataset: this.dataset,
			rowHeight: "short"
		};
	}
}
