import { DateFormatEnum } from "ng2/common/constants/sohoxi.constant";

import { Helper } from "common/helper";

export class LocaleHelper {
	static getDateFormat(
		format: string,
		locale?: string
	): string {
		if (Helper.isUndefined(locale)) {
			locale = Soho.Locale.currentLocale.name;
		}
		const localeName = Soho.Locale.cultures[locale];
		const calendar = localeName.calendars[0];

		if (format === DateFormatEnum.Timeformat) {
			return calendar.timeFormat;
		}
		return calendar.dateFormat[format];
	}
}
