import { Helper } from "./helper";

import { Config, InjectNames, MGPlatform, Numbers } from "ng2/common/constants/common.constant";

import type { IEnvironmentSettings } from "ng2/common/model/environment-settings.model";

import { AngularConstants } from "common/angular-constants";
import { Debugger } from "common/debugger";
import { WSUserControl } from "etc-assets/user-control-es";

import { CommonService } from "common/services/common-service";
import { LanguageService } from "common/services/language-service";

interface IInitializeScope extends ng.IScope {
	done: boolean;
	isLoading: boolean;
}

class InitializeCtrl {

	static $inject = [
		AngularConstants.SCOPE,
		AngularConstants.INTERVAL,
		InjectNames.COMMON_SERVICE,
		InjectNames.LANGUAGE_SERVICE
	];

	constructor(
		private $scope: IInitializeScope,
		private $interval: ng.IIntervalService,
		private commonService: CommonService,
		private languageService: LanguageService
	) {
		if (DEV_SERVER) {
			this.loadEnvironmentSettings();
		}
		WSUserControl.onReady(() => {
			this.loadEnvironmentSettings();
		});
	}

	private loadEnvironmentSettings(): void {
		this.commonService.loadEnvironmentSettings()
			.then((settings: IEnvironmentSettings) => {
				Config.IS_DEV = settings.isDevelopment;
				Config.ION_API_URL = settings.url;
				Config.MAX_PROJECTS = settings.maxProjects;
				Config.MAX_APPS = settings.maxApps;
				Config.MAX_COMPOSITES = settings.maxComposites;
				Config.MAX_DATASOURCES = settings.maxDatasources;
				Config.LOCALE = settings.locale;
				Config.TENANT_ID = settings.tenantId;
				Config.USER = settings.user;
				Config.MG_FORM_NAME = settings.mgFormName;
				Config.MG_FORM_ONLY = settings.mgFormOnly;
				Config.MG_PLATFORM = settings.mgPlatform;
				Config.MG_CONFIG_NAME = settings.mgConfigName;
				Config.MG_HELP_BASE_URL = settings.mgHelpBaseUrl;
				Debugger.initialize();
				this.$scope.done = Config.IS_CONFIG_LOADED = true;
			})
			.then(() => {
				this.languageService.initializeLanguageSettings();
				this.setBaseBuilder();
			})
			.then(() => {
				const stop = this.$interval(() => {
					if (!Config.IS_FIRST_LOAD) {
						this.$scope.isLoading = Config.IS_FIRST_LOAD;
						this.$interval.cancel(stop);
					}
				}, Numbers.HUNDRED);
			})
			.catch(error => {
				console.error(error);
			});
	}

	private setBaseBuilder(): void {
		const url = Config.MG_PLATFORM === MGPlatform.WEB || DEV_SERVER ?
			window.parent.location.search.substring(1) :
			window.location.search.substring(1);

		const query = Helper.parseQueryString(url);
		if (query.basebuilder) {
			Config.IS_BASE_BUILDER = true;
		}
	}
}

export class InitializeDirective {
	static add(m: ng.IModule): void {
		m.directive("eirInitialize", () => {
			return {
				restrict: "A",
				scope: {
					done: "=eirInitialize",
					isLoading: "="
				},
				controller: InitializeCtrl,

			};
		});
	}
}
