import { HttpService } from "../common/services/http-service";

import { ServerPaths } from "ng2/common/constants/common.constant";
import { IJsonDesigner } from "../common/model/designer.model";

export class Designer {
	private userDisplayName: string;

	public getUserDisplayName(): string { return this.userDisplayName; }

	private userName: string;

	public getUserName(): string { return this.userName; }

	public fromJson(obj: IJsonDesigner): void {
		if (obj === null) {
			throw new Error("Json object was null and could therefore not be loaded.");
		}

		this.userDisplayName = obj.userDisplayName;
		this.userName = obj.userName;
	}

	public static info(httpService: HttpService): ng.IPromise<Designer> {
		return httpService.executeGet(ServerPaths.DESIGNER).then((data: IJsonDesigner) => {
			const designer: Designer = new Designer();
			designer.fromJson(data);
			return designer;
		});
	}
}
