import { AngularConstants } from "../../../common/angular-constants";

import isNil from "lodash-es/isNil";
import { Config, InjectNames } from "ng2/common/constants/common.constant";
import { SelectableEnum } from "ng2/common/constants/sohoxi.constant";
import { IJsonCompositeComponent } from "ng2/common/model/databearer/components/json-composite-component.model";
import { IDataGridColumn, IDataGridOptions } from "ng2/common/model/sohoxi/sohoxi-datagrid.model";

import { IErrorResponse } from "../../../common/error-response";
import { ConflictResolutonService } from "../../../common/services/conflict-resolution-service";
import {
	DialogButtonType, DialogService, IDialogOptions, IDialogResult,
	IMessageDialogOptions, StandardDialogButtons
} from "../../../common/services/dialog-service";
import { RepositoryService } from "../repository-service";
import { RepositoryTab } from "../repository-tab-ctrl";

export class CompositeTabCtrl extends RepositoryTab {
	public anySelectedDataItem: boolean = false;
	public datagridOptions: IDataGridOptions<IJsonCompositeComponent>;
	public selectedDataItems: IJsonCompositeComponent[] = [];

	protected datagridId: string = "composite-grid";

	private importActionTitle: string = "Import Composite Components";

	private dataset: IJsonCompositeComponent[];
	private set Dataset(value: IJsonCompositeComponent[]) {
		this.dataset = value;
		this.saveToCache(this.CACHE_KEY, value);
	}
	private readonly CACHE_KEY = "compositeComponents";

	public static $inject = [
		AngularConstants.SCOPE,
		AngularConstants.ELEMENT,
		AngularConstants.CACHE_FACTORY,
		InjectNames.REPOSITORY_SERVICE,
		InjectNames.DIALOG_SERVICE,
		InjectNames.CONFLICT_RESOLUTION_SERVICE
	];

	constructor(
		$scope: ng.IScope,
		$element: ng.IAugmentedJQuery,
		$cacheFactory: ng.ICacheFactoryService,
		repositoryService: RepositoryService,
		private dialogService: DialogService,
		private conflictResolutionService: ConflictResolutonService
	) {
		super($scope, $element, $cacheFactory, repositoryService);
	}

	protected initDatagridOptions(): void {
		const columns: IDataGridColumn[] = [];
		this.dataset = [];

		columns.push({
			id: "selectionCheckbox",
			sortable: false,
			resizable: false,
			formatter: Soho.Formatters.SelectionCheckbox,
			align: "center"
		});
		columns.push({
			id: "displayName",
			name: "Name",
			field: "manifest.displayName",
			sortable: true,
			width: "350px",
			formatter: Soho.Formatters.Ellipsis
		});
		columns.push({
			id: "author",
			name: "Author",
			field: "manifest.authorDisplayName",
			sortable: true,
			width: "350px",
			formatter: Soho.Formatters.Ellipsis
		});
		columns.push({
			id: "date",
			name: "Created",
			field: "manifest.creationDate",
			sortable: true,
			formatter: Soho.Formatters.Date
		});
		columns.push({
			id: "datasources",
			name: "Data Services",
			field: "datasources.length",
			sortable: true,
			width: "150px",
			formatter: Soho.Formatters.Integer
		});
		columns.push({
			id: "interactions",
			name: "Interactions",
			field: "interactions.length",
			width: "150px",
			sortable: true,
			formatter: Soho.Formatters.Integer
		});
		columns.push({
			id: "expressions",
			name: "Expressions",
			field: "expressions.length",
			width: "150px",
			sortable: true,
			formatter: Soho.Formatters.Integer
		});

		this.datagridOptions = {
			columns: columns,
			dataset: [],
			selectable: SelectableEnum.Multiple,
			paging: true,
			pagesize: 25,
			toolbar: {
				title: "Global Composite Components",
				results: true,
				dateFilter: false,
				keywordFilter: true,
				advancedFilter: true,
				actions: true,
				views: true,
				rowHeight: true
			},
			enableTooltips: true
		};
		this.loadCompositeComponents();
	}

	private loadCompositeComponents(): void {
		const cachedDataset =
			this.getFromCache<IJsonCompositeComponent[]>(this.CACHE_KEY);

		if (isNil(cachedDataset)) {
			this.loading = true;
			this.repositoryService
				.loadCompositeComponents()
				.then((compositeComponents: IJsonCompositeComponent[]) => {
					this.Dataset = this.datagridOptions.dataset = compositeComponents;
				}).catch((errorResponse) => {
					this.datagridOptions.dataset = [];
					this.dialogService.showError("Load Composite Components", errorResponse.message);
				}).finally(() => {
					this.loading = false;
				});
		} else {
			this.datagridOptions.dataset = cachedDataset;
			this.clearSelected();
		}
	}

	private importComposites(compositeComponentsToImport: IJsonCompositeComponent[]): void {
		//let deferredResolutions = this.repositoryService.newDeferred<d.IResolutionResult<IJsonCompositeComponent>>();
		this.conflictResolutionService.resolve(
			compositeComponentsToImport,
			this.datagridOptions.dataset,
			"manifest.displayName",
			"Composite component",
			Config.MAX_COMPOSITES
		).then((conflictResolutionObject) => {
			const compositesToAdd = conflictResolutionObject.toAdd;
			const idsToDelete = conflictResolutionObject.toRemove
				.map(compositeToDelete => compositeToDelete.manifest.id);

			if (idsToDelete.length > 0) {
				this.deleteComposites(idsToDelete)
					.then(() => this.addComposites(compositesToAdd))
					.catch((errorResponse: IErrorResponse) => {
						// Deleting of the composites supposed to replace was unsuccessful
						console.warn("[CompositeTabCtrl] Could not replace, unable to remove old CompositeComponents");
						this.loading = false;
						this.dialogService.showError(
							this.importActionTitle,
							"Unable to replace old composites: <br \>" + errorResponse.message
						);
					});
			} else {
				this.addComposites(compositesToAdd);
			}
		}).catch(() => {
			// User cancel
			console.warn("[CompositeTabCtrl] Cancelled");
			this.loading = false;
		});
		//deferredResolutions.promise;
	}

	private addComposites(composites: IJsonCompositeComponent[]): void {
		// The server will generate new IDs
		this.repositoryService.addCompositeComponents(composites)
			.then(addedCompositeComponents => {
				this.conflictResolutionService.checkIfInvalidImportedItems<IJsonCompositeComponent>(
					"composite(s)",
					addedCompositeComponents
				).catch((errorDialogOptions) => this.dialogService.showMessage(errorDialogOptions));
				for (const composite of addedCompositeComponents) {
					if (composite) {
						this.datagridOptions.dataset.unshift(composite);
					}
				}
				this.Dataset = this.datagridOptions.dataset;
				this.clearSelected();
			}).catch((errorResponse: IErrorResponse) => {
				// Network problems
				this.dialogService.showError(
					this.importActionTitle,
					"Unable to import composite components due to: \n" + errorResponse.message
				);
			}).finally(() => {
				this.loading = false;
			});
	}

	private validate(compositeComponentsToValidate: IJsonCompositeComponent[]): boolean {
		// TODO: validate
		return true;
	}

	public import(): void {
		const options: IDialogOptions<any> = {
			title: this.importActionTitle,
			template: require("../../../common/dialogs/import/import-dialog.html"),
		};
		this.dialogService.show(options).then((result: IDialogResult<FileList>) => {
			if (result.button === DialogButtonType.Ok) {
				this.loading = true;
				this.repositoryService.readJsonFile<IJsonCompositeComponent[]>(result.value[0])
					.then((compositeComponentsToImport) => {
						if (this.validate(compositeComponentsToImport)) {
							this.importComposites(compositeComponentsToImport);
						} else {
							this.dialogService.showError(this.importActionTitle, "Failed to validate CompositeComponents");
						}
					}).catch((errorResponse: IErrorResponse) => {
						// Unable to read input file, probable JSON parse error
						console.warn("[CompositeTabCtrl] Could not read file");
						this.dialogService.showError(
							this.importActionTitle,
							"Unable to read file due to: <br \>" + errorResponse.message
						);
					}).finally(() => {
						this.loading = false;
					});
			}
		});
	}

	public delete(): void {
		const actionTitle = "Delete Composite Components";
		const displayNames: string[] = [];
		const idsToDelete: string[] = this.selectedDataItems.map(composite => {
			displayNames.push(composite.manifest.displayName);
			return composite.manifest.id;
		});
		const options: IMessageDialogOptions = {
			title: actionTitle,
			message:
				`Are you sure you want to delete these ${displayNames.length}` +
				`Composite Components?<br \> ${displayNames.join(", ")}`,
			standardButtons: StandardDialogButtons.YesNo
		};
		this.dialogService.showMessage(options).then((result) => {
			if (result.button === DialogButtonType.Yes) {
				this.loading = true;
				this.deleteComposites(idsToDelete).catch((errorResponse: IErrorResponse) => {
					this.dialogService.showError(actionTitle, "Unable to delete due to:<br \>" + errorResponse.message);
				}).finally(() => {
					this.loading = false;
				});
			}
		});
	}

	private deleteComposites(ids: string[]): ng.IPromise<void> {
		return this.repositoryService.deleteCompositeComponents(ids).then(() => {
			this.Dataset = this.datagridOptions.dataset = this.datagridOptions.dataset.filter((composite) => {
				return !ids.some((deletedId) => {
					return deletedId === composite.manifest.id;
				});
			});
			this.clearSelected();
		});
	}

	public export(): void {
		const ids: string[] = this.selectedDataItems.map((composite) => {
			return composite.manifest.id;
		});
		this.repositoryService.exportCompositeComponents(ids);
	}

}

export class CompositeTabComponent implements ng.IComponentOptions {
	public controller = CompositeTabCtrl;
	public controllerAs = "eirCompositeTabCtrl";
	public template = require("./composite-tab.html");

	public static add(m: ng.IModule): void {
		m.component("eirCompositeTab", new CompositeTabComponent());
	}
}
