import { AppState } from "../../app.state";
import { AngularConstants } from "../../common/angular-constants";

import { InjectNames } from "ng2/common/constants/common.constant";
import { ViewEnum } from "../../common/defines";
import { DesignerService } from "../services/designer-service";

enum RepositoryTabEnum {
	DATASOURCE,
	COMPOSITE,
	IDO,
}

class RepositoryCtrl {
	public canNavigateBack: boolean;
	public tabEnum = RepositoryTabEnum;
	public currentTab: RepositoryTabEnum = RepositoryTabEnum.DATASOURCE;

	private fromEditor: boolean;

	static $inject = [
		AngularConstants.STATE_SERVICE,
		InjectNames.DESIGNER_SERVICE,
	];

	constructor(
		private $state: ng.ui.IStateService,
		private designerService: DesignerService,
	) {
		this.setCanNavigateBack();

		designerService.setView(ViewEnum.REPOSITORY);
	}

	public onChangeTab(tab: RepositoryTabEnum): void {
		this.currentTab = tab;
	}

	public navigateBack(): void {
		if (this.canNavigateBack) {
			if (this.fromEditor) {
				this.$state.go(AppState.EDITOR.name, {
					id: this.designerService.getCurrentProject().getId(),
					reloadProject: false,
				});
			} else {
				this.$state.go(AppState.HOME.name);
			}
		}
	}

	private setCanNavigateBack(): void {
		const currentView = this.designerService.getView();

		this.canNavigateBack = currentView === ViewEnum.DASHBOARD || currentView === ViewEnum.EDITOR;
		if (this.canNavigateBack) {
			this.fromEditor = currentView === ViewEnum.EDITOR;
		}
	}
}

export class RepositoryDirective {
	static add(m: ng.IModule): void {
		m.directive("eirRepository", () => {
			return {
				template: require("./repository.html"),
				controller: RepositoryCtrl,
				controllerAs: "eirRepositoryCtrl"
			};
		});
	}
}
