import angular from "angular";

import { AngularConstants } from "../../../../common/angular-constants";

import type { IDialogBaseScope } from "../../../../common/defines";
import { DialogButtonType, DialogService, IDialog, IDialogOptions, IDialogResult } from "../../../../common/services/dialog-service";

import { InjectNames } from "ng2/common/constants/common.constant";
import { SelectableEnum } from "ng2/common/constants/sohoxi.constant";

import { QuestionDefinitionsEnum } from "ng2/common/model/questions/questions.enum";

import type { IJsonQuestion, IJsonSettingQuestion } from "ng2/common/model/questions/questions.model";
import type { IDatagridApi, IDataGridOptions } from "ng2/common/model/sohoxi/sohoxi-datagrid.model";

import type { IRowItem } from "common/sohoxi/datagrid-helper";

import { EditorService } from "designer/editor/services/editor-service";

export interface IDatasetQuestion {
	id: string;
	component: string,
	type: string,
	setting?: IJsonSettingQuestion;
}

export class BaseDialogController {
	selectedQuestion: IDatasetQuestion;
	dialog: IDialog<string>;
	datagridOptions: IDataGridOptions<IDatasetQuestion>;

	static $inject = [
		AngularConstants.SCOPE,
		AngularConstants.ELEMENT,
		InjectNames.DIALOG_SERVICE,
		InjectNames.EDITOR_SERVICE
	];

	constructor(
		private $scope: IDialogBaseScope<string>,
		private $element: ng.IAugmentedJQuery,
		private dialogService: DialogService,
		private editorService: EditorService
	) {
		const dialog: IDialog<string> = $scope.eirDialog;
		this.dialog = dialog;

		this.initGridOptions();
		this.initQuestionsData();
	}

	private initGridOptions(): void {
		const columns = [
			{
				id: "component",
				name: "Component",
				field: "component",
				sortable: true,
				formatter: Soho.Formatters.Ellipsis,
				width: "25%"
			}, {
				id: "type",
				name: "Type",
				field: "type",
				sortable: true,
				formatter: Soho.Formatters.Ellipsis,
				width: "25%"
			}, {
				id: "setting",
				name: "Setting",
				field: "setting.key",
				sortable: true,
				formatter: Soho.Formatters.Ellipsis,
				width: "25%"
			}, {
				id: "text",
				name: "Text",
				field: "setting.text",
				sortable: true,
				formatter: Soho.Formatters.Ellipsis,
				width: "25%"
			}
		];

		const options: IDataGridOptions<IDatasetQuestion> = {
			dataset: [],
			columns: columns,
			isList: true,
			selectable: SelectableEnum.Single,
			toolbar: {
				title: "Questions",
				results: true,
				dateFilter: false,
				keywordFilter: true,
				advancedFilter: true,
				actions: true,
				views: true,
				rowHeight: true
			}
		};

		this.datagridOptions = options;

		this.$element.find("#datagrid-questions").on("selected", (e: any, args: IRowItem<IDatasetQuestion>[]) => {
			// When datagrid get unselected args[0] is undefined
			this.$scope.$evalAsync(() => {
				if (args.length > 0) {
					this.selectedQuestion = args[0].data;
				} else {
					this.selectedQuestion = null;
				}
			});
		});
	}

	private initQuestionsData(): void {
		const projectQuestions = this.editorService.getCurrentProject().getQuestions();
		const dataset: IDatasetQuestion[] = [];
		projectQuestions.forEach((question) => {
			const datasetQuestion: IDatasetQuestion = {
				id: question.id,
				component: question.componentId,
				type: this.getTypeName(question.type)
			};
			if (question.type === QuestionDefinitionsEnum.COMPONENT_SETTINGS) {
				question.settings.forEach((questionSetting) => {
					datasetQuestion.setting = {
						id: questionSetting.id,
						text: questionSetting.text,
						key: questionSetting.key
					};
					dataset.push(angular.copy(datasetQuestion));
				});
			} else {
				dataset.push(datasetQuestion);
			}
		});
		this.datagridOptions.dataset = dataset;
	}

	deleteQuestion(): void {
		this.dialogService.showYesNo("Delete Question", "Do you want to delete this Question?").then((result) => {
			if (result.button === DialogButtonType.Yes) {
				const questionId = this.selectedQuestion.id;
				const settingId = this.selectedQuestion.setting?.id;
				this.editorService.getCurrentProject().deleteQuestion(questionId, settingId);
				this.clearSelected();
				this.initQuestionsData();
			}
		});
	}

	addQuestion(): void {
		const options: IDialogOptions<string> = {
			title: "New Base Question",
			template: require("./upsert-question-dialog/upsert-question-dialog.html")
		};

		this.dialogService.show(options).then((result: IDialogResult<IJsonQuestion>) => {
			if (result.button === DialogButtonType.Ok) {
				this.editorService.getCurrentProject().addQuestion(result.value);
				this.initQuestionsData();
			}
		});
	}

	private clearSelected(): void {
		this.selectedQuestion = null;
		const api = this.$element.find("#datagrid-questions").data("datagrid") as IDatagridApi;

		if (api) {
			api.deSelectAllRows();
		}
	}

	private getTypeName(type: QuestionDefinitionsEnum): string {
		switch (type) {
			case QuestionDefinitionsEnum.COMPONENT_SETTINGS:
				return "Setting";
			case QuestionDefinitionsEnum.SEEKS_BINDING:
				return "Binding";
			default:
				// type do not exist
		}
	}

	close(): void {
		this.dialog.close();
	}

	save(): void {
		const result: IDialogResult<void> = {
			button: DialogButtonType.Ok
		};
		this.dialog.close(result);
	}

	static add(m: ng.IModule): void {
		m.controller("eirBaseDialogController", BaseDialogController);
	}
}
