import { IJsonComponent } from "ng2/common/model/databearer/components/json-component.model";
import { IDialogBaseScope } from "../../../common/defines";
import { IWizardTick } from "../../../common/model/sohoxi/sohoxi.model";
import { DialogButtonType, IDialog, IDialogResult } from "../../../common/services/dialog-service";
import { IValidationResult } from "../../../common/shared-components/base/field-validation";
import { IOnComponentChangeNotification, NotifiableComponent } from "../../../common/shared-components/base/notifiable-component";

import { FeatureSwitchService } from "../../../common/services/feature-switch-service";

export interface IWizardStep {
	id: string;
	title: string;
	userTip: string;
	template: string;
}

export abstract class WizardCtrl
	extends NotifiableComponent implements ng.IController {
	static dataKeyWizard = "wizard";

	isBusy: boolean;
	abstract steps: IWizardStep[];

	currentStep: IWizardStep;
	nextDisabled = true;
	backDisabled = true;

	protected dialog: IDialog<IJsonComponent>;

	private wizardElement: ng.IAugmentedJQuery;

	errorList: any[] = [];
	get ErrorList(): any[] {
		this.nextDisabled = this.errorList.length !== 0;
		return this.errorList;
	}

	constructor(
		protected $scope: IDialogBaseScope<any>,
		protected $element: ng.IAugmentedJQuery,
		protected $templateCache: ng.ITemplateCacheService,
		featureService: FeatureSwitchService,
	) {
		super(featureService);
		this.dialog = $scope.eirDialog;
	}

	initWizard(): void {
		this.initTemplates(this.steps);

		const ticks = this.initTicks(this.steps);
		this.initWizardElement(ticks);

		this.setCurrentStep(this.steps[0]);
	}

	protected initWizardElement(ticks: IWizardTick[]): void {
		this.wizardElement = this.$element.find(".wizard");

		this.$scope.$applyAsync(() => {
			this.wizardElement.wizard({ ticks: ticks });
			this.wizardElement.on("activated", (event: JQueryEventObject, element: JQuery) => {
				this.$scope.$evalAsync(() => {
					const selectedTick: number = this.wizardElement.data(WizardCtrl.dataKeyWizard).ticks.index(element);
					this.setCurrentStep(this.steps[selectedTick]);
					this.onChangeCurrentStep();
				});
			});
			this.wizardElement.on("beforeactivate", (event, tick) => {
				// return false to not go to next tick;
				return !this.nextDisabled ? tick : false;
			});

			// Removing href attribute from a-tags, to avoid navigating to #
			$.each(this.wizardElement.find("a"), (index, element) => {
				$(element).removeAttr("href");
			});
		});
	}

	protected setCurrentStep(step: IWizardStep): void {
		this.currentStep = step;
		this.updateBackDisabled();
	}

	protected initTicks(steps: IWizardStep[]): IWizardTick[] {
		const ticks: IWizardTick[] = [];
		steps.forEach((step: IWizardStep, index: number) => {
			ticks.push({
				state: index === 0 ? "current" : "",
				label: step.title
			});
		});

		return ticks;
	}

	protected initTemplates(steps: IWizardStep[]): void {
		steps.forEach(step => {
			if (!this.$templateCache.get(step.id)) {
				this.$templateCache.put(step.id, step.template);
			}
		});
	}

	getEndStep(): IWizardStep {
		return this.steps[this.steps.length - 1];
	}

	private updateWizardTick(step: IWizardStep): void {
		const tickIndex = this.steps.indexOf(step);
		this.wizardElement.data(WizardCtrl.dataKeyWizard).select(tickIndex);
	}

	selectWizardStepByIndex(newStepIndex: number): void {
		if (0 <= newStepIndex && newStepIndex < this.steps.length) {
			this.selectWizardStep(this.steps[newStepIndex]);
		}
	}

	selectWizardStep(step: IWizardStep): void {
		this.updateWizardTick(step);
		this.setCurrentStep(step);
		this.onChangeCurrentStep();
	}

	updateBackDisabled():void {
		this.backDisabled = this.currentStep === this.steps[0];
	}

	protected abstract onChangeCurrentStep(): void;

	protected abstract getFinishParameter(): any;

	next(): void {
		if (this.currentStep === this.getEndStep()) {
			const result: IDialogResult<any> = {
				button: DialogButtonType.Ok,
				value: this.getFinishParameter()
			};
			this.close(result);
		} else {
			const nextStepIndex = this.steps.indexOf(this.currentStep) + 1;
			this.selectWizardStep(this.steps[nextStepIndex]);
		}
	}

	back(): void {
		const previousStepIndex = this.steps.indexOf(this.currentStep) - 1;
		this.selectWizardStep(this.steps[previousStepIndex]);
	}

	cancel(): void {
		this.close({ button: DialogButtonType.Cancel });
	}

	close(parameter?: IDialogResult<any>): void {
		this.dialog.close(parameter);
	}

	protected getValidation($data?: IOnComponentChangeNotification):  boolean | IValidationResult {
		return null;
	}
}

export class WizardDirective implements ng.IDirective {
	restrict = "E";
	template = require("./wizard.html");
	scope = true;

	static add(m: ng.IModule): void {
		m.directive("eirWizard", () => new WizardDirective());
	}
}
