import angular from "angular";

import { AngularConstants } from "../../../../common/angular-constants";
import { Helper } from "../../../../common/helper";
import type { IComponent } from "../../../../common/IComponent";
import { InstanceEvent } from "../../../../common/instance-event";
import {
	DialogButtonType, DialogService, IDialogResult, IMessageDialogOptions,
	StandardDialogButtons
} from "../../../../common/services/dialog-service";
import { ComponentsHelper } from "../../../../components/helper";
import { Lookup } from "../../../../components/lookup/lookup";
import type { IFinderModeOptions } from "../../../defines";

import type { EditorService } from "../../services/editor-service";
import type { InteractionManagementService } from "../../services/interaction-management-service";
import { SettingsBase } from "../settings-base";

import {
	InjectNames, InteractionPairOperation, IWizardFocusType, Numbers, SaveFlagsEnum,
	SettingTypes, WizardFocusEnum
} from "ng2/common/constants/common.constant";

import type { IDatasource } from "common/model/datasource/datasource.model";
import type { IDataPair } from "ng2/common/model/data-pair.model";
import type { IJsonComponent } from "ng2/common/model/databearer/components/json-component.model";

import type { IEditorComponent } from "common/interface/ieditor-component";
import type { Interaction } from "ng2/components/interaction/interaction";

import "./interaction-settings.less";

export class InteractionSettingsCtrl extends SettingsBase {
	selectedInteraction: Interaction;
	relevantInteractions: Interaction[];

	availableSources: IJsonComponent[];
	availableTargets: IJsonComponent[];
	availableSourceTriggers: IDataPair[];
	availableTargetActions: IDataPair[] = [];
	private isWizard: boolean;

	isFinderActive: boolean;
	settingSourceComponent: IJsonComponent;
	settingTargetComponent: IJsonComponent;
	settingSourceTrigger: IDataPair;
	settingTargetAction: IDataPair;

	interactionChangedEvent = new InstanceEvent<Interaction>();
	targetActionChangedEvent = new InstanceEvent<string>();
	sourceTriggerChangedEvent = new InstanceEvent<string>();

	get hasAvailableTargetActions(): boolean {
		return this.availableTargetActions.length > 0 && this.availableTargetActions[0].value !== "";
	}

	get COMPONENT_HAS_NO_DATASERVICE(): string {
		return "Component has no data service";
	}

	static $inject = [
		AngularConstants.SCOPE,
		AngularConstants.COMPILE,
		AngularConstants.TEMPLATE_CACHE,
		InjectNames.EDITOR_SERVICE,
		InjectNames.DIALOG_SERVICE,
		InjectNames.INTERACTION_MANAGEMENT_SERVICE,
	];

	constructor(
		protected $scope: ng.IScope,
		private $compile: ng.ICompileService,
		private $templateCache: ng.ITemplateCacheService,
		editorService: EditorService,
		private dialogService: DialogService,
		private interactionManagementService: InteractionManagementService
	) {
		super($scope, editorService);

		this.onComponentChanged(editorService.getSelectedComponent());

		this.unsubscribers.push(this.interactionManagementService
			.projectInteractionsChanged.on((interaction) => {
				this.updateRelevantInteractions();
			})
		);

		this.unsubscribers.push(this.editorService.wizardFocusComponentChangedEvent.on(
			(focusSettings: IWizardFocusType) => {
				this.setWizardFocusType(focusSettings);
			}
		));

		this.unsubscribers.push(this.editorService.selectedInteractionChangedEvent.on((interaction: Interaction) => {
			this.selectInteraction(interaction);
		}));

		this.unsubscribers.push(this.editorService.finderModeChangedEvent.on((value: boolean) => {
			this.isFinderActive = value;
		}));

		this.unsubscribers.push(this.editorService.newWizardInteractionEvent.on((interaction: Interaction) => {
			this.selectInteraction(interaction);
		}));

		this.unsubscribers.push(this.editorService.isWizardChangedEvent.on((value: boolean) => {
			this.isWizard = value;
		}));

		this.unsubscribers.push(this.editorService.setSourceTriggerEvent.on(() => {
			if (this.selectedInteraction) {
				this.updateUI();
			}
		}));

		this.unsubscribers.push(this.editorService.setTargetActionEvent.on(() => {
			if (this.selectedInteraction) {
				this.updateUI();
			}
		}));

		this.unsubscribers.push(this.editorService.selectedDatasourceChangedEvent.on((newDatasource?: IDatasource) => {
			const currentInteractionsForSelectedComponent =
				this.interactionManagementService.getInteractionsWithTarget(this.selectedComponent);
			if (currentInteractionsForSelectedComponent.length > 0) {
				currentInteractionsForSelectedComponent.forEach((interaction) => {
					interaction.removeAllParameters();
				});
				this.refreshInteractionDisplayName(currentInteractionsForSelectedComponent);
			}
			if (newDatasource) {
				if (currentInteractionsForSelectedComponent.length === 0) {
					this.createDefaultInteraction();
					this.editorService.saveCurrent(SaveFlagsEnum.INTERACTIONS);
				}
			}
			if (this.selectedInteraction) {
				this.updateUI();
				this.targetActionChangedEvent.raise();
			}
			this.initAvailableComponents();
		}));

		this.unsubscribers.push(this.editorService.wizardSetFinderModeEvent.on((value: string) => {
			let tick = 0;
			if (value === "target") {
				tick = 1;
			}
			this.useFinder(value).then(() => {
				this.editorService.wizardTickChangedEvent.raise(tick);
			});
		}));
	}

	selectSourceApplication(): void {
		this.editorService.setWizardFocusComponent(
			WizardFocusEnum.UNFOCUSED,
			this.settingSourceComponent,
			SettingTypes.SOURCE
		);
		this.settingSourceComponent = this.editorService.getApplicationParent().getDatabearer();
		this.onChangeSourceComponent();

		this.settingTargetComponent = this.selectedComponent.getDatabearer();
		this.onChangeTargetComponent();
	}

	setSourceAsTarget(): void {
		this.editorService.setWizardFocusComponent(
			WizardFocusEnum.UNFOCUSED,
			this.settingTargetComponent,
			SettingTypes.TARGET
		);
		this.settingTargetComponent = angular.copy(this.settingSourceComponent);
		this.onChangeTargetComponent();
	}

	selectTargetApplication(): void {
		this.editorService.setWizardFocusComponent(
			WizardFocusEnum.UNFOCUSED,
			this.settingTargetComponent,
			SettingTypes.TARGET);
		this.settingTargetComponent = this.editorService.getApplicationParent().getDatabearer();
		this.onChangeTargetComponent();
	}

	private setDisplayStrings(interaction: Interaction): void {
		if (interaction.displayName) {
			const strings = interaction.displayName.split("->");
			if (strings.length === 2) {
				interaction.triggerString = strings[0];
				interaction.targetString = strings[1];
			}
		} else {
			interaction.triggerString = "";
			interaction.targetString = "";
		}
	}

	private refreshInteractionDisplayName(interactions: Interaction[]): void {
		interactions.forEach(interaction => {
			interaction.displayName = this.interactionManagementService.getInteractionDisplayName(interaction);
			this.setDisplayStrings(interaction);
		});
	}

	private updateRelevantInteractions(): void {
		const interactions = this.editorService.getCurrentProject().getInteractions();
		this.relevantInteractions = [];
		if (this.selectedComponent) {
			const selectedComponentId = this.selectedComponent.getId();
			this.relevantInteractions = interactions.filter((interaction) => {
				interaction.displayName = this.interactionManagementService.getInteractionDisplayName(interaction);
				this.setDisplayStrings(interaction);
				return (interaction.sourceName === selectedComponentId || interaction.targetName === selectedComponentId);
			});
		}

		if (this.selectedInteraction && interactions.find(interaction =>
			interaction.getId() === this.selectedInteraction.getId()) === undefined) {
			this.selectedInteraction = null;
		}
	}

	protected onComponentChanged(newComponent: IEditorComponent): void {
		this.selectedComponent = null;
		super.onComponentChanged(newComponent);
		this.updateRelevantInteractions();
		this.initAvailableComponents();
		this.selectInteraction(null);
	}

	private initAvailableComponents(): void {
		const allComponents: IJsonComponent[] = this.editorService.getAllInteractiveComponents()
			.map((item) => {
				return item.getDatabearer();
			});
		this.availableSources = allComponents;
		this.availableTargets = allComponents;
	}

	selectInteraction(newInteraction: Interaction): void {
		if (newInteraction) {
			this.selectedInteraction = newInteraction;
			this.updateUI();
		} else {
			this.selectedInteraction = null;
		}
		this.interactionChangedEvent.raise(this.selectedInteraction);
	}

	updateUI(): void {
		const sourceComponentInstance: IComponent =
			this.editorService.getComponentInstanceById(this.selectedInteraction.sourceName);
		const targetComponentInstance: IComponent =
			this.editorService.getComponentInstanceById(this.selectedInteraction.targetName);

		this.settingTargetComponent = targetComponentInstance ? targetComponentInstance.getDatabearer() : undefined;
		if (this.settingTargetComponent && targetComponentInstance) {
			this.setAvailableTargetActions();
		}

		this.settingSourceComponent = sourceComponentInstance ? sourceComponentInstance.getDatabearer() : undefined;
		if (this.settingSourceComponent && sourceComponentInstance) {
			this.setAvailableSourceTriggers();
		} else {
			this.settingSourceComponent = this.editorService.getApplicationParent().getDatabearer();
			this.availableSourceTriggers = this.editorService.getApplicationParent().getSourceTriggers();
		}

		this.availableSourceTriggers.filter((trigger) => {
			if (trigger.value === this.selectedInteraction.sourceTriggerName) {
				this.settingSourceTrigger = trigger;
			}
		});
		this.availableTargetActions.filter((action) => {
			if (action.value === this.selectedInteraction.targetActionName) {
				this.settingTargetAction = action;
			}
		});
		// update the interaction with new displayName for the UI
		this.selectedInteraction.displayName = this.getSelectedInteractionDisplayName();
		this.setDisplayStrings(this.selectedInteraction);

		// elements needs to update because sometimes the dropdown can't be opened.
		angular.element("#interactionSourceSelect").data("dropdown").updated();
		angular.element("#interactionTargetSelect").data("dropdown").updated();

	}

	private getSelectedInteractionDisplayName(): string {
		return this.interactionManagementService.getInteractionDisplayName(this.selectedInteraction);
	}

	private setAvailableSourceTriggers(): void {
		const sourceComponentInstance = this.editorService.getComponentInstanceById(this.settingSourceComponent.id);
		let sourceTriggers = sourceComponentInstance.getSourceTriggers();
		const datasource = sourceComponentInstance.datasource;
		if (datasource) {
			const needsIdentifier = sourceTriggers.length > 1;
			sourceTriggers = sourceTriggers.map((sourceTrigger) => {
				if (sourceTrigger === InteractionPairOperation.ON_LOAD) {
					const identifier = needsIdentifier ? " (" + sourceTrigger.name + ")" : "";
					return {
						name: datasource.displayName + " has run " + identifier,
						value: sourceTrigger.value
					};
				} else {
					return sourceTrigger;
				}
			});
		} else {
			sourceTriggers = sourceTriggers.map((sourceTrigger) => {
				if (sourceTrigger === InteractionPairOperation.ON_LOAD) {
					return {
						name: sourceTrigger.name + " (No Data Service) ",
						value: sourceTrigger.value
					};
				} else {
					return sourceTrigger;
				}
			});
		}
		this.availableSourceTriggers = sourceTriggers;
	}

	private setAvailableTargetActions(): void {
		this.availableTargetActions = this.getTargetActions();
		if (this.availableTargetActions.length > 0) {
			this.settingTargetAction = this.availableTargetActions[0];
		} else {
			this.settingTargetAction = {
				name: this.COMPONENT_HAS_NO_DATASERVICE,
				value: ""
			};
			this.availableTargetActions.push(this.settingTargetAction);
		}
	}

	private getTargetActions(): IDataPair[] {
		const targetComponentInstance = this.editorService.getComponentInstanceById(this.settingTargetComponent.id);
		let targetActions: IDataPair[] = targetComponentInstance.getTargetActions();
		const datasource = targetComponentInstance.datasource;
		if (datasource) {
			const needsIdentifier = targetActions.length > 1;
			targetActions = targetActions.map((targetAction) => {
				if (targetAction === InteractionPairOperation.GET ||
					targetAction === InteractionPairOperation.LIST
				) {
					const identifier = needsIdentifier ? " (" + targetAction.name + ")" : "";
					return {
						name: "Run " + datasource.displayName + identifier,
						value: targetAction.value
					};
				} else {
					return targetAction;
				}
			});
		} else {
			targetActions = targetActions.map((targetAction) => {
				if (targetAction === InteractionPairOperation.GET ||
					targetAction === InteractionPairOperation.LIST
				) {
					return {
						name: targetAction.name + " (No Data Service) ",
						value: targetAction.value
					};
				} else {
					return targetAction;
				}
			});
		}

		return targetActions;
	}

	manualRunSelected(): void {
		this.interactionManagementService.runInteraction(this.selectedInteraction);
	}

	getSelectedInteraction(): Interaction {
		return this.selectedInteraction;
	}

	setWizardFocusType(focusSettings: IWizardFocusType): void {
		//Determine the new component
		if (focusSettings.setting === SettingTypes.SOURCE) {
			this.settingSourceComponent = focusSettings.component;
		} else {
			this.settingTargetComponent = focusSettings.component;
		}
		//Remove or add the new focus
		const encodedFocusSettings = Helper.htmlEncode(focusSettings.setting);
		if (focusSettings.focus === WizardFocusEnum.UNFOCUSED) {
			angular.element(".id-" + focusSettings.component.id).removeClass(encodedFocusSettings);
		} else {
			angular.element(".id-" + focusSettings.component.id).addClass(encodedFocusSettings);
		}
		//update the new classes
		if (!ComponentsHelper.isApplicationParent(focusSettings.component)) {
			const editorComponent = this.editorService.getComponentInstanceById(focusSettings.component.id) as IEditorComponent;
			editorComponent.setIsWizardFocus(focusSettings.focus);
		}

	}

	createNewInteraction(): void {
		const interactionComponent = this.selectedComponent;
		const targetComponent = this.editorService.getComponentInstanceById(this.availableTargets[0].id);
		const newInteraction = this.interactionManagementService.createNew(interactionComponent, targetComponent);
		this.selectInteraction(newInteraction);
		this.editorService.saveCurrent(SaveFlagsEnum.INTERACTIONS);
	}

	removeSelectedInteraction(): void {
		const interaction = this.selectedInteraction;
		const options = {
			title: "Confirm Delete",
			message: "Do you really want to delete this Interaction?",
			standardButtons: StandardDialogButtons.YesNo
		} as IMessageDialogOptions;

		this.dialogService.showMessage(options).then((result: IDialogResult<any>) => {
			if (result && result.button === DialogButtonType.Yes) {
				this.interactionManagementService.removeInteraction(interaction);
				this.editorService.saveCurrent(SaveFlagsEnum.INTERACTIONS);
				this.selectInteraction(null);
			}
		});

	}

	onChangeSourceComponent(): void {
		const interaction = this.selectedInteraction;
		let interactionSource: IEditorComponent;

		if (this.settingSourceComponent && !ComponentsHelper.isApplicationParent(this.settingSourceComponent)) {
			interactionSource = this.editorService.getComponentInstanceById(this.settingSourceComponent.id);
		} else {
			interactionSource = this.editorService.getApplicationParent();
		}
		if (interactionSource) {
			interaction.sourceName = interactionSource.getId();
			this.availableSourceTriggers = interactionSource.getSourceTriggers();
			const index = this.availableSourceTriggers.map((trigger) => {
				return trigger.value;
			}).indexOf(interaction.sourceTriggerName);
			if (!interaction.sourceTriggerName || index === -1) {
				this.settingSourceTrigger = interactionSource.getDefaultTrigger();
			}
		} else {
			this.settingSourceTrigger = {} as IDataPair;
			interaction.sourceName = "";
			this.availableSourceTriggers = [];
		}
		this.sourceTriggerChangedEvent.raise(this.selectedInteraction.sourceName);
		this.editorService.setTriggerSourceComponent(interactionSource as IEditorComponent);
		this.onChangeSourceTrigger();
	}

	onChangeSourceTrigger(): void {
		this.selectedInteraction.sourceTriggerName = this.settingSourceTrigger.value;
		this.editorService.setSourceTrigger(this.settingSourceTrigger);

		if (!this.isWizard) {
			this.editorService.saveCurrent(SaveFlagsEnum.INTERACTIONS);
		}
	}

	onChangeTargetComponent(): void {
		const interaction = this.selectedInteraction;
		if (!this.settingTargetComponent || ComponentsHelper.isApplicationParent(this.settingTargetComponent)) {
			this.settingTargetComponent = this.availableTargets[0];
		}
		const interactionTarget = this.editorService.getComponentInstanceById(this.settingTargetComponent.id);

		if (interactionTarget) {
			interaction.targetName = interactionTarget.getId();
			this.setAvailableTargetActions();
		} else {
			this.settingTargetAction = {} as IDataPair;
			interaction.targetName = "";
			this.availableTargetActions = [];
		}

		this.targetActionChangedEvent.raise(this.selectedInteraction.targetActionName);
		this.editorService.setTargetActionComponent(interactionTarget as IEditorComponent);
		this.onChangeTargetAction();
	}

	onChangeTargetAction(): void {
		this.selectedInteraction.removeAllParameters();
		if (!this.settingTargetAction) {
			return;
		}

		const newTargetAction = this.settingTargetAction;
		if (newTargetAction.name !== undefined) {
			this.selectedInteraction.targetActionName = newTargetAction.value;
			this.editorService.setTargetAction(newTargetAction);
		}

		if (newTargetAction.value === InteractionPairOperation.LIST.value ||
			newTargetAction.value === InteractionPairOperation.GET.value) {
			if (this.settingTargetComponent.datasource) {
				this.editorService.getComponentInstanceById(this.settingTargetComponent.id).datasource.getMetaData();
			}
		}

		this.targetActionChangedEvent.raise(this.selectedInteraction.targetActionName);
		if (!this.isWizard) {
			//Wait for targetAction to be set before saving.
			this.editorService.getTimeout()(()=> {
				this.editorService.saveCurrent(SaveFlagsEnum.INTERACTIONS);
			}, Numbers.TEN);
		}
	}

	private setMode(component: IEditorComponent, settingComponent: IJsonComponent, mode: string): void {
		if (this.isWizard && (component.wizardFocusType === WizardFocusEnum.UNFOCUSED)) {
			if (settingComponent) {
				this.editorService.setWizardFocusComponent(WizardFocusEnum.UNFOCUSED, settingComponent, mode);
			}
			this.triggerChangeComponent(component, mode);
		} else if (this.isWizard) {
			//block pointer
		} else {
			this.triggerChangeComponent(component, mode);
		}
	}

	private triggerChangeComponent(component: IEditorComponent, mode: string): void {
		if (mode === SettingTypes.SOURCE) {
			this.settingSourceComponent = component.getDatabearer();
			this.onChangeSourceComponent();
		} else {
			this.settingTargetComponent = component.getDatabearer();
			this.onChangeTargetComponent();
		}
	}

	/*
	* Enables pre-selection of target component, use selectedComponent if it's not set as source
	*/
	private selectedComponentAsTarget(component: IEditorComponent): void {
		const selectedComponent = this.selectedComponent;
		const componentId = component.getId();
		const selectedId = selectedComponent.getId();
		const isTarget = (this.settingTargetComponent && this.settingTargetComponent.id === componentId);
		if (selectedComponent) {
			if (selectedId !== componentId) {
				this.settingTargetComponent = selectedComponent.getDatabearer();
				this.onChangeTargetComponent();
			} else if (selectedId === componentId && isTarget) {
				delete this.settingTargetComponent;
				this.onChangeTargetComponent();
			}
		}
	}

	useFinder(componentToSet: string): ng.IPromise<IEditorComponent> {
		this.isFinderActive = true;
		const isTarget = componentToSet === "target";

		const finderOptions: IFinderModeOptions = { title: "Select Interaction " + (isTarget ? "Target" : "Source") };
		if (this.isWizard) {
			finderOptions.closeOnSelect = false;
			finderOptions.title = `Create Interaction: Select the ${isTarget ? "target" : "source"} component on the canvas.`;
			finderOptions.cancel = () => {
				this.editorService.wizardCloseEvent.raise();
			};
		}

		const promise = this.editorService.startFinderMode(finderOptions);
		promise.then((component: IEditorComponent) => {
			if (!isTarget && this.isWizard && ComponentsHelper.isApplicationParent(component)) {
				this.selectSourceApplication();
			} else if (isTarget) {
				this.setMode(component, this.settingTargetComponent, SettingTypes.TARGET);
			} else if (!isTarget) {
				this.setMode(component, this.settingSourceComponent, SettingTypes.SOURCE);
				this.selectedComponentAsTarget(component);
			}
		}).finally(() => {
			if (!this.isWizard) {
				this.isFinderActive = false;
			}
		});

		return promise;
	}

	private createDefaultInteraction(): void {
		const typedef = this.selectedComponent.getDatabearer().typedef;
		switch (typedef) {
			case Lookup.name:
				this.interactionManagementService.createNew(
					this.selectedComponent,
					this.selectedComponent
				);
				break;
			default:
				this.interactionManagementService.createNew(
					this.editorService.getApplicationParent(),
					this.selectedComponent
				);
		}
	}

	static add(m: ng.IModule): void {
		m.controller("eirInteractionSettingsCtrl", InteractionSettingsCtrl);
	}
}

export class InteractionSettingsDirective {
	static add(m: ng.IModule): void {
		m.directive("eirInteractionSettings", () => {
			return {
				restrict: "E",
				template: require("./interaction-settings.html"),
				scope: true,
				controller: InteractionSettingsCtrl,
				controllerAs: "eirInteractionSettingsCtrl"
			};
		});
	}
}
