// TODO: This will be replaced when enterprise-ng datagrid is finished

import { SelectableEnum } from "ng2/common/constants/sohoxi.constant";
import { IPagerApi, IPagerInfo, IPagerSource } from "ng2/common/model/sohoxi/sohoxi-pager.model";

export interface IDataItem {

	/**
	 * Soho private property
	 */
	isFiltered?: boolean;

	/**
	 * Soho private property
	 */
	_isFilteredOut?: boolean;

	/**
	 * Soho private property
	 */
	_selected?: boolean;
}

/**
 * Interface for the Data Grid Directive
 */
export interface IDataGridOptions<DataItem> {
	/**
	 * If actionableMode is "true”, tab and shift tab behave like left and right arrow key,
	*/
	actionableMode?: boolean;
	/**
	 * If cellNavigation is "false”, will show border around whole row on focus
	 */
	cellNavigation?: boolean;
	/**
	 * If rowNavigation is "false”, will NOT show border around the row
	 */
	rowNavigation?: boolean;
	/**
	 * Sets shading for readonly grids
	 * Default value: false
	 */
	alternateRowShading?: boolean;
	/**
	 * Column properties
	 */
	columns?: IDataGridColumn[];
	/**
	 * Data set to populate the grid
	 */
	dataset: DataItem[];
	/**
	 * Allow Column reorder
	 */
	columnReorder?: boolean;
	/**
	 * Save Column Reorder and resize
	 */
	saveColumns?: boolean;
	/**
	 * Enable cell editing
	 * Default value: false
	 */
	editable?: boolean;
	/**
	 * Make a readonly "list"
	 * Default value: false
	 */
	isList?: boolean;
	/**
	 * Id to link a right click context menu element
	 */
	menuId?: string;
	/**
	 * Unique ID to use as local storage reference and internal variable names
	 */
	uniqueId?: string;
	/**
	 * Row height, possbile values: "short", "medium" or "tall"
	 * Default value: "medium"
	 */
	rowHeight?: string;
	/**
	 * Selectable rows, possbile values: "single" or "multiple"
	 * Default value: false
	 */
	selectable?: SelectableEnum | boolean;
	/**
	 * Controls fields to use for data grouping Use Data grouping fx.
	 * {fields: ['incidentId'], supressRow: true, aggregator: 'list', aggregatorOptions: ['unitName1']}
	 */
	groupable?: Object;
	/**
	 * Controls if using a selection mode if you can click the rows to select
	 * Default value: true
	 */
	clickToSelect?: boolean;
	/**
	 * Show toolbar. False or toolbar object
	 * Default value: false
	 */
	toolbar?: IDataGridToolbarOptions;
	/**
	 * Set to false if you will initialize the toolbar yourself
	 */
	initializeToolbar?: boolean;
	/**
	 * Enable paging mode
	 * Default value: false
	 */
	paging?: boolean;
	/**
	 * Number of rows per page
	 * Default value: 25
	 */
	pagesize?: number;
	/**
	 * Array of page sizes to show in the page size dropdown.
	 * Default value: [10, 25, 50, 75]
	 */
	pagesizes?: number[];
	/**
	 * Disable the ability to go to a specific page when paging.
	 * Default value: false
	 */
	indeterminate?: boolean;
	/**
	 * Callback function for paging
	 */
	source?: IPagerSource;
	/**
	 * If true, hides the pager if there's only one page worth of results.
	 */
	hidePagerOnOnePage?: boolean;
	/**
	 * Enable Column Filtering, This will require column filterTypes as well.
	 */
	filterable?: boolean;
	/**
	 * Disable Filter Logic client side and let your server do it
	 */
	disableClientFilter?: boolean;
	/**
	 * Disable Sort Logic client side and let your server do it
	 */
	disableClientSort?: boolean;
	/**
	 * Can provide a custom function to adjust results text on the toolbar
	 */
	resultsText?: string;
	/**
	 * If set you can reorder rows. Requires rowReorder formatter
	 */
	rowReorder?: boolean;
	/**
	 * If true the dirty indicator will be shown on the rows
	 */
	showDirty?: boolean;
	/**
	 * Controls if you cna expand more than one expandable row.
	 */
	allowOneExpandedRow?: boolean;
	/**
	 * Process tooltip logic at a cost of performance
	 */
	enableTooltips?: boolean;
	/**
	 * If a row is activated the user should not be able to deactivate it by clicking on the activated row
	 */
	disableRowDeactivation?: boolean;
	/**
	 * If true make all the columns equal width
	 */
	sizeColumnsEqually?: boolean;
	/**
	 * If true we append an expandable row area without the rowTemplate feature being needed.
	 */
	expandableRow?: boolean;
	/**
	 * If set to false we skip redraw logic on the resize of the page.
	 */
	redrawOnResize?: boolean;
	/**
	 * If true a tree grid is expected so addition calculations will be used to calculate of the row children
	 */
	treeGrid?: any;
	/**
	 * Allows you to provide a row template
	 */
	rowTemplate?: string;
	/**
	 * When using set the column id for the columns you want to freeze. Id is required for this to work.
	 */
	frozenColumns?: any;
	/**
	 * Enable or disable a summary row at the bottom of the data-grid.
	 */
	summaryRow?: boolean;
	/**
	 * Allows you to provide a function so you can set some rows to disabled base on data or row index.
	 */
	isRowDisabled?: (idx: number, item: any) => boolean;

	/**
	 * A callback function that fires when expanding rows. To be used. when expandableRow is true.
	 * The function gets eventData about the row and grid and a response function callback.
	 * Call the response function with markup to append and delay opening the row.
	 */
	onExpandRow?: (e: any, response: Function) => void;

	/***************************************
	 * Custom property outside of IDS framework.
	 ***************************************/

	/**
	 * This property is used to trigger Datagrid.updated in our datagrid directive.
	 * Set property to true to update datagrid
	 */
	updateDatagrid?: boolean;

	/**
	 * Allow to hide the checkbox header (true to show, false to hide)
	 */
	showSelectAllCheckBox?: boolean;

	/**
	 * Optional to give datagrid a unique id
	 */
	id?: string;
}

export interface IDataGridToolbarOptions {
	title?: string;
	results?: boolean;
	dateFilter?: boolean;
	keywordFilter?: boolean;
	advancedFilter?: boolean;
	actions?: boolean;
	views?: boolean;
	rowHeight?: boolean;
	maxVisibleButtons?: number;
	collapsibleFilter?: boolean;
	exportToExcel?: boolean;
}

export interface IDataGridRow<T> {
	cell: number;
	row: number;
	item: T;
	originalEvent: JQueryEventObject;
}

export interface IDataGridColumn {
	/**
	 * ID of column
	 */
	id: string;
	/**
	 * Name displayed in the column header
	 */
	name?: string;
	/**
	 * Property in dataset to show in the column field
	 */
	field?: string;
	/**
	 * Show\hide
	 */
	hidden?: boolean;
	/**
	 * Alignment
	 */
	align?: string;
	/**
	 * Width of column
	 */
	width?: number|string;
	/**
		 * Minimum width of the column
		 */
	minWidth?: number;
	/**
	 * Maximum width of the column
	 */
	maxWidth?: number;
	/**
	 * Formatter
	 */
	formatter?: IFormatterFunction;
	/**
	 * Editor
	 */
	editor?: IEditorFunction;
	/**
	 * On click method (hyperlink)
	 */
	click?: (event: JQueryEventObject, rowsSelected: IDataGridRow<any>[]|IDataGridRow<any>) => void;

	/**
	 * Used with Date formatters and editors.
	 * You can use it to specify the display format for date values.
	 * fx yyyy-MM-dd. See locale for date formatting patterns.
	 */
	dateFormat?: string;

	/**
	 *  Used with Time and 'Date' + Time formatters and editors.
	 *  You can use it to specify the display format for date values.
	 *  fx HH;mm See locale for time formatting patterns.
	 */
	timeFormat?: string;

	/**
	 * Number format
	 */
	numberFormat?: string;

	editorOptions?: any;
	/**
	 * Sortable
	 */
	sortable?: boolean;
	/**
	 * Resizeable
	 */
	resizable?: boolean;
	/**
	 * Text overflow
	 * Can be set to 'ellipsis'.
	 */
	textOverflow?: string;
	/**
	* Tooltip
	* Set a manual tooltip.
	*/
	tooltip?: any;
	required?: boolean;
	validate?: string;
	isEditable?: IIsEditableFunction;
	readonly?: boolean;
	summaryRowFormatter?: IFormatterFunction;
	aggregator?: string;
	/**
	 * Setting text for buttons
	 *
	 */
	text?: string;
	href?: any;
	target?: string;
	options?: IColumnOptions[];
	icon?: string;
	cssClass?: string;
	isChecked?: (value: string | number) => boolean;
}

export interface IColumnOptions {
	id: string;
	value: string;
	label: string;
}

export interface IDatagridColumnArgs<T> {
	/**
	 * The cell value.
	 */
	value: string;

	/**
	 * The previous cell value.
	 */
	oldValue: string;

	/**
	 * An array of selected rows.
	 */
	row: number;

	/**
	 * An array of selected rows.
	 */
	cell: number;

	/**
	 *
	 */
	rowData: T;

	/**
	 * The cell html element that was entered.
	 */
	target: JQuery;

	/**
	 * The column object
	 */
	column: IDataGridColumn;
}

export interface IDatagridSettings {
	sortOrder: ISortOrderSetting;
	activePage: number;
}

export interface ISortOrderSetting {
	sortField: string;
	sortAsc: boolean;
	sortId: string;
}

export interface IDatagridApi {
	/**
	 * Private IDS method
	 */
	afterPaging(pagingInfo: IPagerInfo): void;

	/**
	 *  IDS Pager component API.
	 */
	pagerAPI: IPagerApi;

	element: any;

	emptyMessage: any;

	/**
	 * Get the column index from the col's id
	 * @param id
	 * @returns The corresponding columns current position.
	 */
	columnIdxById(id: number): IDatagridColumnArgs<any>;

	/**
	 * Return all of the currently dirty rows by row index.
	 * @returns array: An array of dirty rows.
	 */
	dirtyRows(): any[];

	/**
	 * Clear all dirty cells.
	 */
	clearDirty(): void;

	/**
	 * Toggle selection on a single row
	 * @param idx
	 * @param nosync
	 * @param noTrigger
	 */
	selectRow(idx: number, nosync?: boolean, noTrigger?: boolean): void;

	/**
	 * Get the currently selected rows.
	 * @returns array: An array containing the selected rows
	 */
	selectedRows(): any[];
	/**
	 * Set the selected rows by passing the row index or an array of row indexes.
	 * @param idx
	 * @param nosync
	 * @param noTrigger
	 * @param forceSelect
	 */
	selectRows(idx: number | number[], nosync?: boolean, noTrigger?: boolean, forceSelect?: boolean): void;

	/**
	 * Returns an array of row numbers for the rows containing the value for the specified field.
	 * @param fieldName
	 * @param value
	 * @returns array: an array of row numbers.
	 */
	findRowsByValue(fieldName: string, value: any): number[];

	/**
	 * Unwrap the grid back to a simple div, and destory all events and pointers.
	 * @returns object: The plugin api for chaining.
	 */
	destroy(): object;

	/**
	 * Deselect all rows that are currently selected.
	 */
	unSelectAllRows(): void;

	/**
	 * Select all rows. If serverside paging, this will be only the current page.
	 * For client side paging, all rows across all pages are selected.
	 */
	selectAllRows(): void;

	/**
	 * Given a new column set update the rows and reload
	 * @param columns
	 * @param columnGroups
	 */
	updateColumns(columns: IDataGridColumn[], columnGroups?: any): void;

	/**
	 * Update the datagrid and optionally apply new settings.
	 * The datagrid api for chaining.
	 * @param settings
	 */
	updated(settings?: IDataGridOptions<any>): IDatagridApi;

	/**
	 * Send in a new data set to display in the datagrid in the lookup.
	 * This will work whether or not the lookup is open or closed.
	 * @param {object} dataset The array of data to show in the datagridgrid.
	 * @param {object} pagerInfo The extra pager info object with information like activePage and pagesize.
	 */
	updateDataset(dataset: any[], pagerInfo: IPagerInfo): IDatagridApi;

	/**
	 * Render or render both the header and row area.
	 * @param isToggleFilter
	 * @param pagerInfo
	 */
	render(isToggleFilter: string, pagerInfo: IPagerInfo): void;

	/**
	 * Remove all selected rows from the grid and dataset.
	 */
	removeSelected(): void;

	/**
	 * Show the cell errors.
	 * @param row
	 * @param cell
	 * @param message
	 * @param type
	 */
	showCellError(row: number, cell: number, message: string, type: string): void;

	/**
	 * Get or Set the row height.
	 * @param height
	 */
	rowHeight(height: string): void;

	/**
	 * Deselect all rows that are currently selected.
	 */
	deSelectAllRows(): void;

	/**
	* Run throught the array and remark the idx's after a row reorder.
	*/
	syncSelectedRowsIdx(): void;

	/**
	 * Set the current datagrid sort column
	 * @param id
	 * @param ascending
	 */
	setSortColumn(id: string, ascending: boolean): void;

	/**
	 * Run validation for the column, for a particular cell.
	 */
	validateCell?: (row: number, cell: number) => void;

	/**
	 * Export datagrid to csv
	 * @param name
	 */
	exportToCsv(name: string): void;

	/**
	 * Export datagrid to excel
	 * @param name
	 */
	exportToExcel(name: string): void;
}

/**
 * Data Grid Directive
 *
 * Example:
 *
 * scope["options"] = {
		dataset: [
			{ name: "Name #1", age: 12, phone: "12345" },
			{ name: "Name #2", age: 22, phone: "1234567" },
			{ name: "Name #3", age: 32, phone: "1234589" }
		]
	}
	* <div xi-datagrid="ctrl.options"></div>
	*/

export interface IFormatterFunction {
	(
		row: number,
		cell: number,
		value: string | number,
		col: IDataGridColumn,
		item: any,
		datagrid: IDatagridApi
	): string;
}

export interface IEditorFunction {
	(
		row: number,
		cell: number,
		value: string,
		container: any,
		column: IDataGridColumn,
		e: any,
		api: any,
		item: any
	): string;
}

export interface IIsEditableFunction {
	(
		row: number,
		cell: number,
		value: string,
		col: IDataGridColumn,
		item: any
	): boolean;
}
