import cloneDeep from "lodash-es/cloneDeep";
import { AngularConstants } from "common/angular-constants";

import {
	ComponentNames,
	DEFAULT_BOUNDARY,
	InjectNames,
	SettingKeys,
} from "ng2/common/constants/common.constant";
import { OrientationEnum } from "ng2/common/enums/orientation.enum";

// import { Datagrid } from "components/datagrid/datagrid";
// import { Dropdown } from "components/dropdown/dropdown";
// import { Flexbox } from "components/flexbox/flexbox";
// import { List } from "components/list/list";

import type { IContext } from "ng2/common/model/components/base/context.model";
import type { IJsonComponent } from "ng2/common/model/databearer/components/json-component.model";
import type { IPreviousAttributes } from "ng2/common/model/databearer/components/json-datagrid.model";
import type { IJsonFlexbox } from "ng2/common/model/databearer/components/json-flexbox.model";
import type { IBoundary } from "ng2/common/model/project/boundary.model";
import type { IJQueryMouseEventObject } from "common/defines";
import type { IEditorComponent } from "common/interface/ieditor-component";
import type { IEditorDatagrid } from "editor/components/editor-datagrid";
import type { IEditorDropdown } from "editor/components/editor-dropdown";
import type { IEditorFlexbox } from "editor/components/editor-flexbox";
import type { IEditorList } from "editor/components/editor-list";

import { SettingsBase } from "editor/component-settings/settings-base";
import { FlexboxSettings } from "editor/component-settings/flexbox/flexbox-settings";

import { Helper } from "common/helper";
import { ComponentsHelper } from "components/helper";
import { EditorService } from "editor/services/editor-service";

export interface IDetachedChild {
	context: IContext;
	currentItemOptions: string | number | IPreviousAttributes;
}

export class FlexboxContentSettingsCtrl extends SettingsBase {
	private static readonly MIN_CHILDREN_COUNT = 2;

	public currentOrientation: number;
	public flexChildrenAmount: number;
	public htmlEncode = Helper.htmlEncode;
	protected selectedComponent: IEditorFlexbox;

	public static $inject = [
		AngularConstants.SCOPE,
		InjectNames.EDITOR_SERVICE
	];

	public constructor(
		$scope: ng.IScope,
		editorService: EditorService
	) {
		super($scope, editorService);

		this.onComponentChanged(editorService.getSelectedComponent());
		this.unsubscribers.push(this.editorService.currentFlexOrientationChangedEvent.on((value: OrientationEnum) => {
			this.currentOrientation = value;
		}));
	}

	public addFirstColumn(): void {
		this.addFirstChildren(OrientationEnum.HORIZONTAL);
	}

	public addFirstRow(): void {
		this.addFirstChildren(OrientationEnum.VERTICAL);
	}

	public onMouseEnter(component: IEditorComponent, event: IJQueryMouseEventObject): void {
		component.onMouseOver(event);
	}

	public onMouseLeave(component: IEditorComponent, event: IJQueryMouseEventObject): void {
		component.onMouseLeave(event);
	}

	public selectComponent(component: IEditorComponent, event: IJQueryMouseEventObject): void {
		component.onClick(event);
	}

	public addChild(): void {
		this.pushChild();
		this.editorService.saveCanvas();
	}

	public removeChild(): void {
		if (this.selectedComponent.getDatabearer().children.length > FlexboxContentSettingsCtrl.MIN_CHILDREN_COUNT) {
			this.popChild();
		} else { // 1 or 2 left
			this.removeLastChildren();
		}

		this.editorService.saveCanvas();
	}

	protected onComponentChanged(newComponent: IEditorComponent): void {
		super.onComponentChanged(newComponent);

		if (this.selectedComponent) {
			this.countflexChildren();
			const currentBoundary = this.editorService.getCurrentBoundary();
			this.currentOrientation =
				this.selectedComponent.getBoundarySetting<OrientationEnum>(SettingKeys.ORIENTATION, currentBoundary.id);
		}
	}

	protected onBoundaryChanged(currentBoundary: IBoundary): void {
		this.currentOrientation =
			this.selectedComponent.getBoundarySetting<OrientationEnum>(SettingKeys.ORIENTATION, currentBoundary.id);
	}

	private countflexChildren(): void {
		let flexChildrenCount: number = 0;
		this.selectedComponent.children.forEach((child) => {
			if (child.getTypedef() === ComponentNames.Flexbox) {
				flexChildrenCount++;
			}
		});
		this.flexChildrenAmount = flexChildrenCount;
	}

	private pushChild(): IJsonFlexbox {
		const newFlexChild = FlexboxSettings.getInstance().create(
			this.editorService.generateId(ComponentsHelper.getComponentDisplayName("Flexbox"))
		);
		this.flexChildrenAmount++;
		this.selectedComponent.insert(newFlexChild);
		return newFlexChild;
	}

	private popChild(): IJsonFlexbox {
		this.flexChildrenAmount--;
		const flexboxDatabearer = this.selectedComponent.getDatabearer();
		const childIdToRemove = flexboxDatabearer.children[flexboxDatabearer.children.length - 1].id;
		return this.selectedComponent.detachChild(childIdToRemove) as IJsonFlexbox;
	}

	private addFirstChildren(orientation: OrientationEnum): void {
		const flexbox = this.selectedComponent;
		const detachedChildren: IJsonComponent[] = [];
		const childrenContext: IDetachedChild[] = [];

		if (!flexbox.isFlexParent) {
			const childArray = flexbox.children;
			while (childArray.length > 0) {
				let currentItem = null;
				switch (childArray[0].getTypedef()) {
					case ComponentNames.Dropdown:
						currentItem = cloneDeep((childArray[0] as IEditorDropdown).currentItemIndex);
						break;
					case ComponentNames.List:
						currentItem = cloneDeep((childArray[0] as IEditorList).selectedGeneratedId);
						break;
					case ComponentNames.Datagrid:
						currentItem = cloneDeep((childArray[0] as IEditorDatagrid).previousAttributes);
						break;
					default:
						//no extra properties
				}
				childrenContext.push({
					context: cloneDeep(childArray[0].getScope().context),
					currentItemOptions: currentItem
				});
				detachedChildren.push(childArray[0].detach());
			}
		}

		const firstFlexChild = this.pushChild();
		this.pushChild();

		detachedChildren.forEach((child) => {
			firstFlexChild.children.push(child);
		});

		flexbox.setBoundarySetting<OrientationEnum>(SettingKeys.ORIENTATION, orientation);
		flexbox.isFlexParent = true;
		this.currentOrientation = orientation;

		this.editorService.currentFlexIsParentChangedEvent.raise(flexbox.isFlexParent);
		this.editorService.currentFlexOrientationChangedEvent.raise(orientation);
		this.editorService.refreshSettingEvent.raise(SettingKeys.ORIENTATION);

		this.editorService.saveCanvas();
		this.editorService.getTimeout()(() => {
			//Reset data when instances is created
			if (this.selectedComponent.children.length > 0) {
				this.resetPreviousData(childrenContext);
			}
		});
	}

	private resetPreviousData(childrenContext: IDetachedChild[]): void {
		childrenContext.forEach((childContext, index) => {
			const newFlexboxParent = this.selectedComponent.children[0] as IEditorFlexbox;
			newFlexboxParent.children[index].getScope().context = childContext.context;
			const componentInstance = newFlexboxParent.children[index];
			switch (componentInstance.getTypedef()) {
				case ComponentNames.List:
					(componentInstance as IEditorList).setCurrentItem(childContext.currentItemOptions as string);
					break;
				case ComponentNames.Dropdown:
					const currentIndex = childContext.currentItemOptions as number;
					(componentInstance as IEditorDropdown).setCurrentItem(childContext.context.ListResult[currentIndex]);
					break;
				case ComponentNames.Datagrid:
					const datagrid = (componentInstance as IEditorDatagrid);
					datagrid.previousAttributes = (childContext.currentItemOptions as IPreviousAttributes);
					datagrid.datagridOptions.dataset = childContext.context.ListResult;
					this.editorService.getTimeout()(() => {
						//Wait for dataset to fill the grid
						datagrid.toggleSelected();
					});
					break;
				default:
				//no selection needed
			}
		});
	}

	private removeLastChildren(): void {
		const flexbox = this.selectedComponent;
		const flexChildren = flexbox.getDatabearer().children;
		if (flexChildren.length === FlexboxContentSettingsCtrl.MIN_CHILDREN_COUNT) {
			this.popChild();
		}
		const lastChild = this.popChild();
		if (lastChild.children.length > 0) {
			this.transferChildrenToGrandParent(lastChild);
		} else {
			flexbox.isFlexParent = false;
		}
		this.editorService.currentFlexIsParentChangedEvent.raise(flexbox.isFlexParent);
		this.editorService.currentFlexOrientationChangedEvent.raise(
			this.selectedComponent.getBoundarySetting<OrientationEnum>(SettingKeys.ORIENTATION, this.currentBoundary.id)
		);
	}

	private transferChildrenToGrandParent(child: IJsonFlexbox): void {
		const flexbox = this.selectedComponent;

		child.children.forEach((subChild) => {
			flexbox.insert(subChild);
		});
		flexbox.isFlexParent =
			flexbox.getDatabearer().children[0].typedef === ComponentNames.Flexbox;
		flexbox.setBoundarySetting(
			SettingKeys.ORIENTATION,
			child.boundarySettings[DEFAULT_BOUNDARY.id].orientation
		);
	}
}

export class FlexboxContentSettingsDirective implements ng.IDirective {
	template = require("./flexbox-content-settings.html");
	scope = true;
	controller = FlexboxContentSettingsCtrl;
	controllerAs = "eirFlexboxContentSettingsCtrl";
}
