import { AngularConstants } from "../../../../../common/angular-constants";

import { InjectNames } from "ng2/common/constants/common.constant";
import { IJsonCompositeComponent } from "ng2/common/model/databearer/components/json-composite-component.model";
import { IJsonDatasource } from "ng2/common/model/databearer/dataservice/json-datasource.model";
import { IDataGridOptions } from "ng2/common/model/sohoxi/sohoxi-datagrid.model";

import { IDialogBaseScope } from "../../../../../common/defines";
import { DialogButtonType, DialogService, IDialog, IDialogResult } from "../../../../../common/services/dialog-service";
import { IRowItem } from "../../../../../common/sohoxi/datagrid-helper";
import { RepositoryService } from "../../../../repository/repository-service";

export class ManageRepositoryCtrl {
	private dialog: IDialog<IDataGridOptions<IJsonCompositeComponent | IJsonDatasource>>;
	private selectedItems: (IJsonCompositeComponent | IJsonDatasource)[] = [];
	public loading: boolean = false;
	public repoDatagridOptions: IDataGridOptions<IJsonCompositeComponent | IJsonDatasource>;

	static $inject = [
		AngularConstants.SCOPE,
		AngularConstants.ELEMENT,
		InjectNames.REPOSITORY_SERVICE,
		InjectNames.DIALOG_SERVICE
	];

	constructor(
		private scope: IDialogBaseScope<IDataGridOptions<IJsonCompositeComponent | IJsonDatasource>>,
		private $element: ng.IAugmentedJQuery,
		private repositoryService: RepositoryService,
		private dialogService: DialogService
	) {
		const dialog = scope.eirDialog;
		this.dialog = dialog;
		this.repoDatagridOptions = dialog.parameter;

		// Any cast needed since jQuery only expects one argument on handler
		this.$element.find("#repository-grid")
			.on("selected", (e: any, args: IRowItem<IJsonCompositeComponent | IJsonDatasource>[]) => {
				if (args.length > 0) {
					this.selectedItems = args.map((rowObject) => { return rowObject.data; });
				} else {
					this.selectedItems = [];
				}
			});
	}

	public close(result?: IDialogResult<(IJsonCompositeComponent | IJsonDatasource)[]>): void {
		this.dialog.close(result);
	}

	public addToProject(): void {
		const result: IDialogResult<(IJsonCompositeComponent | IJsonDatasource)[]> = {
			value: this.selectedItems,
			button: DialogButtonType.Ok
		};
		this.close(result);
	}

	static add(m: ng.IModule): void {
		m.controller("eirManageRepositoryCtrl", ManageRepositoryCtrl);
	}
}
