import {
	Ng1StateDeclaration,
	StateProvider,
	UrlRouterProvider,
} from "@uirouter/angularjs";

import { AppTypeEnum, Config } from "ng2/common/constants/common.constant";

import { AngularConstants } from "common/angular-constants";

export class AppState {
	static HOME: Ng1StateDeclaration = {
		url: "/",
		name: "home",
		template: "<eir-dashboard></eir-dashboard>",
		reloadOnSearch: false,
	};

	static EDITOR: Ng1StateDeclaration = {
		url: "/editor/:id/:reloadProject?",
		name: "editor",
		template: "<eir-editor></eir-editor>",
		reloadOnSearch: false,
	};

	static APP_LAUNCHER: Ng1StateDeclaration = {
		url: "/" + Config.RUN_PATH + ":id",
		name: "appLauncher",
		component: "eirAppLauncher",
		reloadOnSearch: false,
		resolve: {
			appType: (): AppTypeEnum => AppTypeEnum.DESIGNER_PREVIEW,
			projectId: [
				AngularConstants.STATE_PARAMS,
				($stateParams: ng.ui.IStateParamsService): void => $stateParams.id
			],
		}
	};

	static REPOSITORY: Ng1StateDeclaration = {
		url: "/repository",
		name: "repository",
		template: "<eir-repository></eir-repository>",
	};

	static DEVELOPER: Ng1StateDeclaration = {
		url: "/developer",
		name: "developer",
		template: "<eir-developer-tool></eir-developer-tool>",
	};

	static REFRESH: Ng1StateDeclaration = {
		url: "/refresh",
		name: "refresh",
		template: "",
		reloadOnSearch: false,
	};

	static add(module: ng.IModule): void {
		module.config([
			"$stateProvider",
			"$urlRouterProvider", (
				$stateProvider: StateProvider,
				$urlRouterProvider: UrlRouterProvider,
			) => {
				$urlRouterProvider.otherwise("/");

				$stateProvider
					.state(AppState.HOME)
					.state(AppState.EDITOR)
					.state(AppState.APP_LAUNCHER)
					.state(AppState.REPOSITORY)
					.state(AppState.REFRESH)
					.state(AppState.DEVELOPER);
			}
		]);
	}
}
