import { InjectNames, Numbers } from "ng2/common/constants/common.constant";

export class MingleService {

	public static MESSAGETYPE_BUSINESSCONTEXT: string = "inforBusinessContext";
	public static MESSAGETYPE_CLEARCONTEXT: string = "clearAllContext";
	public static MESSAGETYPE_APPINITCOMPLETE: string = "appInitComplete";
	public static MESSAGETYPE_APPLICATIONDRILLBACK: string = "applicationDrillback";
	public static MESSAGETYPE_FAVORITECONTEXT: string = "showFavoriteContext";
	public static MESSAGETYPE_SHORTCUTCONTEXT: string = "setShortcutContext";

	public static DEFAULT_LID: string = "lid://infor.app-builder.1";

	private logicalId: string;

	public getLogicalId(): string {
		return this.logicalId || MingleService.DEFAULT_LID;
	}

	public setupMessageHandler(messageType: string, callback: Function): void {
		infor.companyon.client.registerMessageHandler(messageType, callback);
	}

	sendApplicationInitComplete(): void {
		infor.companyon.client.sendMessage(MingleService.MESSAGETYPE_APPINITCOMPLETE, window.name);
	}

	public sendDrillbackMessage(message: string): void {
		infor.companyon.client.sendPrepareDrillbackMessage(message);
	}

	public sendBusinessContextMessage(messageType: string, appId: string, appName: string): void {
		let data: Object;

		switch (messageType) {
			case MingleService.MESSAGETYPE_BUSINESSCONTEXT:
				data = {
					"type": messageType,
					"screenId": appId,
					"visible": true,
					"readOnly": false,
					"originatingTime": this.getOriginatingTime(),
					"contextId": this.generateContextId(),
					"logicalId": this.getLogicalId(),
					"entities": [
						{
							"entityType": "InforApp",
							"id1": appId,
							"drillbackURL": this.getDrillbackUrl(this.getLogicalId(), appId)
						}
					]
				};
				break;
			case MingleService.MESSAGETYPE_SHORTCUTCONTEXT:
				data = {
					"name": appName,
					"description": "An Infor App in AppHub",
					"absoluteURL": false,
					"shortcutContext": this.getDrillbackUrl(this.getLogicalId(), appId)
				};
				break;
			default:
				data = {};
		}

		infor.companyon.client.sendMessage(messageType, data);
	}

	public sendClearContextMessage(): void {
		infor.companyon.client.sendMessage(MingleService.MESSAGETYPE_BUSINESSCONTEXT, {});
		infor.companyon.client.sendMessage(MingleService.MESSAGETYPE_SHORTCUTCONTEXT, {});
	}

	private getDrillbackUrl(logicalId: string, appId: string): string {
		return "?LogicalId=" + encodeURIComponent(logicalId) + "&$appId=" + appId;
	}

	private generateContextId(): string {
		return this.getOriginatingTime().toString() + "" + Math.floor((Math.random() * Numbers.HUNDRED) + 1);
	}

	private getOriginatingTime(): number {
		const d: Date = new Date();
		return d.getTime();
	}

	static add(m: ng.IModule): void {
		m.service(InjectNames.MINGLE_SERVICE, MingleService);
	}
}
