import { IdoProperties } from "ng2/common/constants/common.constant";
import { IDataGridColumn, IDataGridOptions } from "ng2/common/model/sohoxi/sohoxi-datagrid.model";

import { Helper } from "../../../../../common/helper";
import { BaseDatagridComponent, BaseDatagridCtrl } from "../../../../../common/shared-components/base-datagrid/base-datagrid-component";
import { CustomFormatters } from "../../../../../common/sohoxi/custom-formatters";
import { CollectionProperty } from "../../collection-data";

export class IdoPropertyDatagridCtrl extends BaseDatagridCtrl<CollectionProperty> {
	public datagridOptions: IDataGridOptions<CollectionProperty>;
	public datasetToDisplay: CollectionProperty[];

	// bindings
	public dataset: CollectionProperty[];
	public includeDefaultProperties: boolean;

	public $onInit(): void {
		if (this.includeDefaultProperties) {
			this.setIdoDefaultProperties();
		}
		this.initDatagridOptions();
	}

	public initDatagridOptions(): void {
		const columns: IDataGridColumn[] = [
			{
				id: "name",
				name: "Name",
				field: "name",
				width: "25%",
				sortable: true,
				resizable: false,
				formatter: Soho.Formatters.Ellipsis
			},
			{
				id: "dataType",
				name: "Data Type",
				field: "dataType",
				width: "20%",
				sortable: true,
				resizable: false,
				formatter: Soho.Formatters.Ellipsis
			},
			{
				id: "propertyLength",
				name: "Length",
				field: "propertyLength",
				width: "15%",
				sortable: true,
				resizable: false,
				formatter: Soho.Formatters.Integer
			},
			{
				id: "primaryKey",
				name: "Primary Key",
				field: "primaryKey",
				width: "14%",
				sortable: true,
				resizable: false,
				formatter: CustomFormatters.RequiredFormatter("primaryKey")
			},
			{
				id: "required",
				name: "Required",
				field: "required",
				width: "13%",
				sortable: true,
				resizable: false,
				formatter: CustomFormatters.RequiredFormatter("required")
			},
			{
				id: "readonly",
				name: "Read Only",
				field: "readonly",
				width: "13%",
				sortable: true,
				resizable: false,
				formatter: CustomFormatters.RequiredFormatter("readonly")
			}
		];
		this.datagridOptions = {
			id: "idoPropertyDatagrid",
			columns,
			dataset: Helper.isUndefined(this.datasetToDisplay) ?
				this.dataset : this.datasetToDisplay,
			isList: true
		};
	}

	private setIdoDefaultProperties(): void {
		const defaultIdoProperties: CollectionProperty[] = [
			{
				id: Helper.generateUniqueId(),
				name: "CreateDate",
				dataType: "Date",
				required: false,
				readonly: true,
			},
			{
				id: Helper.generateUniqueId(),
				name: "CreatedBy",
				dataType: "String",
				propertyLength: "128",
				required: false,
				readonly: true,
			},
			{
				id: Helper.generateUniqueId(),
				name: "InWorkflow",
				dataType: "Byte",
				required: false,
				readonly: false,
			},
			{
				id: Helper.generateUniqueId(),
				name: "NoteExistsFlag",
				dataType: "Byte",
				required: false,
				readonly: false,
			},
			{
				id: Helper.generateUniqueId(),
				name: IdoProperties.RECORD_DATE,
				dataType: "Date",
				required: false,
				readonly: true,
			},
			{
				id: Helper.generateUniqueId(),
				name: IdoProperties.ROW_POINTER,
				dataType: "GUID",
				required: false,
				readonly: true,
			},
			{
				id: Helper.generateUniqueId(),
				name: "UpdatedBy",
				dataType: "String",
				propertyLength: "128",
				required: false,
				readonly: true,
			}
		];

		this.datasetToDisplay = this.dataset.concat(defaultIdoProperties);
	}

	constructor($element: ng.IAugmentedJQuery) {
		super($element);
	}
}

export class IdoPropertyDatagridComponent extends BaseDatagridComponent {
	public controller = IdoPropertyDatagridCtrl;

	constructor() {
		super();

		this.bindings.includeDefaultProperties = "<?";
	}

	public static add(m: ng.IModule): void {
		m.component(
			"idoPropertyDatagridComponent",
			new IdoPropertyDatagridComponent()
		);
	}
}
