import angular from "angular";
import { AppState } from "../../app.state";
import { AngularConstants } from "../../common/angular-constants";

import { CSSClasses, InjectNames, Numbers } from "ng2/common/constants/common.constant";
import { IJsonManifest } from "ng2/common/model/databearer/json-manifest.model";
import { IDesignerMashup } from "ng2/common/model/project/mashup/designer-mashup.model";
import { ISidebarView, SidebarViewEnum } from "./defines";

import { DialogService } from "../../common/services/dialog-service";
import { EditorService } from "./services/editor-service";
import { UndoService } from "./services/undo-service";

export interface IEditorStateParameter extends ng.ui.IStateParamsService {
	id: string;
	reloadProject?: string;
}

export class EditorCtrl {
	public showEditor: boolean = false;
	public sidebarView: SidebarViewEnum;
	public isSidebarVisible: boolean;

	private windowResizeTimerPromise: ng.IPromise<void>;
	private unsubscribers: Array<Function> = [];

	static $inject = [
		AngularConstants.SCOPE,
		AngularConstants.STATE_PARAMS,
		AngularConstants.STATE_SERVICE,
		AngularConstants.TIMEOUT,
		InjectNames.EDITOR_SERVICE,
		InjectNames.UNDO_SERVICE,
		InjectNames.DIALOG_SERVICE
	];

	constructor(
		$scope: ng.IScope,
		$stateParams: IEditorStateParameter,
		$state: ng.ui.IStateService,
		private $timeout: ng.ITimeoutService,
		private editorService: EditorService,
		undoService: UndoService,
		dialogService: DialogService
	) {
		const params: IEditorStateParameter = $stateParams;

		if (params && params.id) {
			const currentProject = editorService.getCurrentProject();
			const reloadProject = params.reloadProject === "true";

			if (!reloadProject && currentProject &&
				currentProject.manifest.id === params.id
			) {
				this.resetProjectState(currentProject);
				this.showEditor = true;
			} else { // Load project
				if (currentProject) {
					editorService.removeCacheObject(currentProject.getId());
				}

				editorService.loadProject(params.id, this.getCanvasWidth())
					.then((designerMashup) => {
						undoService.clearActions();
						editorService.sidebarChangedEvent.raise(true);
						this.showEditor = true;
					}).catch((e: any) => {
						console.error("[EditorCtrl] Failed to load project with ID " + params.id, e);
						dialogService.showError(
							"Unable to load project",
							"See console for details, you will be redirected to dashboard"
						).then(() => {
							$state.go(AppState.HOME.name);
						});
						editorService.unloadProject();
					});
			}
		} else {
			console.warn("[EditorCtrl] No project ID was provided");
			$state.go(AppState.HOME.name);
		}

		editorService.setView();
		const sidebarView = editorService.getSidebarView();
		this.sidebarView = sidebarView.view;
		this.isSidebarVisible = sidebarView.isSidebarVisible;

		this.unsubscribers.push(editorService.sidebarViewChangedEvent.on((view: ISidebarView) => {
			if (view) {
				this.sidebarView = view.view;
				this.isSidebarVisible = view.isSidebarVisible;
			}
		}));

		angular.element(window).bind("resize", this.windowResizeHandler.bind(this));

		$scope.$on(AngularConstants.DESTROY, () => {
			angular.element(window).unbind("resize");
			angular.forEach(this.unsubscribers, (unsubscribe) => {
				unsubscribe();
			});
		});
	}

	/**
	 * Method to reset project to its startup states
	 *
	 * - Reset selected component
	 * - Reset theme to manifest's default theme
	 */
	private resetProjectState(project: IDesignerMashup): void {
		this.editorService.deselectComponent();

		const defaultTheme = (project.manifest as IJsonManifest).defaultTheme;
		this.editorService.setCanvasTheme(defaultTheme);

	}

	private getCanvasWidth(): number {
		let canvasWidth: number;
		const rootContainer = angular.element("." + CSSClasses.mashupRootContainer);
		if (rootContainer.length) {
			canvasWidth = rootContainer.width();
		} else {
			const canvasOffsetLeft = 319;
			canvasWidth = window.innerWidth - canvasOffsetLeft;
		}
		return canvasWidth;
	}

	private windowResizeHandler(): void {
		if (this.windowResizeTimerPromise) {
			this.$timeout.cancel(this.windowResizeTimerPromise);
		}
		this.windowResizeTimerPromise = this.$timeout(() => {
			this.updateCurrentBoundary();
		}, Numbers.FIFTY);
	}

	private updateCurrentBoundary(): void {
		this.editorService.updateCurrentBoundary(this.getCanvasWidth());
	}
}

export class EditorDirective {
	static add(m: ng.IModule): void {
		m.directive("eirEditor", () => {
			return {
				template: require("./editor.html"),
				controller: EditorCtrl,
				controllerAs: "eirEditorCtrl"
			};
		});
	}
}
