import { Flexbox } from "../flexbox/flexbox";
import { Tabs } from "./tabs";

import { DEFAULT_BOUNDARY, SettingInputKeys, SettingKeys, SidebarTabTypes } from "ng2/common/constants/common.constant";
import type { IComponentSettingCategory } from "ng2/common/model/components/settings/component-setting-category.model";
import type { IJsonFlexbox } from "ng2/common/model/databearer/components/json-flexbox.model";
import type { IJsonTabPanel } from "../../common/model/component/tabs.model";

import { FrameworkService } from "../services/framework-service";

export class TabPanel extends Flexbox {
	static SETTING_FIELDS: IComponentSettingCategory[] = Flexbox.SETTING_FIELDS
		.filter((settingCategory) => {
			const toBeRemoved = settingCategory.settings.some((setting) => {
				switch (setting.ref) {
					case SettingKeys.SIZE:
					case SettingKeys.VISIBILITY:
					case SettingKeys.MARGIN:
						return true;
					default:
						return false;
				}
			});
			const removeSetting = false;
			const keepSetting = true;
			return toBeRemoved ? removeSetting : keepSetting;
		}).concat({
			title: "Padding",
			tab: SidebarTabTypes.STYLE,
			settings: [{
				title: "Padding",
				ref: SettingKeys.MARGIN,
				boundarySetting: true,
				inputType: SettingInputKeys.MARGIN,
				defaultValue: { left: 0, top: 10, right: 0, bottom: 0 }
			}]
		}, {
			title: "Visibility",
			tab: SidebarTabTypes.STYLE,
			condition: "hide",
			settings: [{
				title: "Show Component",
				ref: SettingKeys.VISIBILITY,
				inputType: SettingInputKeys.SWITCH,
				boundarySetting: true,
				defaultValue: true
			}] //Hide setting but getting the default value
		});

	parent: Tabs;

	constructor(
		obj: IJsonTabPanel,
		$element: ng.IAugmentedJQuery,
		parent: Tabs,
		frameworkService: FrameworkService
	) {
		super(obj as IJsonFlexbox, $element, parent, frameworkService);
		this.newBoundaryEntered(frameworkService.getCurrentBoundary().id);
		this.parent = parent;

		this.canCut = !this.tabsParent();

		if (obj) {
			this.backgroundColor = obj.backgroundColor;
		}
	}

	canDelete(): boolean {
		return false;
	}

	private tabsParent(): boolean {
		return this.parent.getTypedef() === Tabs.name;
	}

	updateVisibility(): void {
		super.updateVisibility();

		if (this.tabsParent()) {
			const isVisible: boolean = this.getId() === this.parent.currentTab.id;
			this.isVisible = isVisible;
			this.databearer.boundarySettings[DEFAULT_BOUNDARY.id].visibility = isVisible;
		}
	}

	static directive = [() => {
		return {
			template: require("./tab-panel.html"),
			replace: true
		};
	}];
}
