import angular from "angular";
import { AngularConstants } from "../../angular-constants";

import { InjectNames } from "ng2/common/constants/common.constant";
import { IJsonTag } from "ng2/common/model/databearer/json-tag.model";
import { IProjectInfo } from "ng2/common/project/project-info";

import { TagService } from "../../services/tag-service";

export class FilterTagCtrl<T> {

	public selectedTags: IJsonTag[] = [];
	public currentTag: IJsonTag = {
		id: "select",
		name: "All"
	};
	public filterValues: IProjectInfo[];
	public filterOptions: IJsonTag[] = [];

	private originalValues: IProjectInfo[];
	private previousFilterValues: IProjectInfo[];
	private unsubscribers: Function[] = [];

	private filter: ng.IFilterFilter = this.$filter("filter");

	public static $inject = [
		AngularConstants.FILTER,
		InjectNames.TAG_SERVICE
	];

	public constructor(
		private $filter: ng.IFilterService,
		private tagService: TagService
	) {
		this.unsubscribers.push(tagService.projectInfoChangedEvent.on((projects) => {
			this.updateValues(projects);
			this.doFilter();
		}));
		this.getAvailableTags();
	}

	public $onInit(): void {
		this.originalValues = angular.copy(this.filterValues);
		this.doFilter();
	}

	private doFilter(): void {
		if (this.selectedTags.length > 0) {
			this.filterValues = this.filter(this.originalValues,
				(projectItem: IProjectInfo) => {
					return this.filterProject(projectItem);
				});
		} else {
			this.filterValues = angular.copy(this.originalValues);
		}
	}

	public addTag(): void {
		const alreadyExists = this.selectedTags.some((tag) => {
			return tag.id === this.currentTag.id;
		});
		if (!alreadyExists && this.currentTag.id !== "select") {
			this.selectedTags.push(this.currentTag);
			this.doFilter();
		}
	}

	public deleteTag(tag: IJsonTag): void {
		const selectedTags = angular.copy(this.selectedTags);
		this.selectedTags = selectedTags.filter((selectedTag) => {
			return tag.id !== selectedTag.id;
		});
		this.doFilter();
	}

	private filterProject(project: IProjectInfo): boolean {
		return this.selectedTags.some((selectedTag) => {
			return project.tags.some((projectTag) => {
				return projectTag.name === selectedTag.name;
			});
		});
	}

	private updateValues(projects: IProjectInfo[]): void {
		// Update original values with new setting data
		// TODO: refactor to more isolated method
		const originalValues = angular.copy(this.originalValues);
		originalValues.forEach((originalProject, index) => {
			projects.forEach((currentProject) => {
				if (originalProject.manifest.id === currentProject.manifest.id) {
					this.originalValues[index] = currentProject;
				}
			});
		});
		this.getAvailableTags();
	}

	private getAvailableTags(): void {
		this.filterOptions = [this.currentTag];
		this.tagService.getAvailableTags().then((tags) => {
			this.filterOptions.push(...tags);
		});
	}
}
