import { IDialogBaseScope } from "../../../../../common/defines";
import { IDialog } from "../../../../../common/services/dialog-service";

export class JsonpathHelpDialogCtrl {
	private dialog: IDialog<void>;

	public constructor(
		private $scope: IDialogBaseScope<void>
	) {
		this.dialog = $scope.eirDialog;
	}

	public close(): void {
		this.dialog.close();
	}

	public static add(m: ng.IModule): void {
		m.controller("eirJsonpathHelpDialogCtrl", JsonpathHelpDialogCtrl);
	}
}
