import angular from "angular";

import { AngularConstants } from "../../../../common/angular-constants";

import type { IAvailableSourceField } from "../../../../common/defines";
import type { IJsonTabComponent } from "../../../../common/model/component/tabs.model";
import { DialogButtonType, DialogService, IDialogOptions, IDialogResult } from "../../../../common/services/dialog-service";
import { TranslationManagementService } from "../../../../components/services/translation-management-service";
import type { IEditorFlexbox } from "../../components/editor-flexbox";
import type { IEditorTabs } from "../../components/editor-tabs";
import type { IInsertPoints } from "../../defines";
import { EditorService } from "../../services/editor-service";
import { SettingsBase } from "../settings-base";

import type { IEditTabDialogParameter } from "./edit-tab-dialog";

import { InjectNames, SaveFlagsEnum } from "ng2/common/constants/common.constant";

import type { IArrangeEvent } from "common/model/sohoxi/sohoxi.model";

import type { IEditorComponent } from "common/interface/ieditor-component";

export class TabsContentSettingsCtrl extends SettingsBase {
	protected selectedComponent: IEditorTabs;

	static $inject = [
		AngularConstants.SCOPE,
		InjectNames.EDITOR_SERVICE,
		InjectNames.DIALOG_SERVICE,
		InjectNames.TRANSLATION_MANAGEMENT_SERVICE
	];

	constructor(
		$scope: ng.IScope,
		editorService: EditorService,
		private dialogService: DialogService,
		private translationManagementService: TranslationManagementService
	) {
		super($scope, editorService);
		this.onComponentChanged(editorService.getSelectedComponent());
	}

	protected onComponentChanged(newComponent: IEditorComponent): void {
		super.onComponentChanged(newComponent);
	}

	onArrange(event: IArrangeEvent): void {
		this.selectedComponent.updateTabPosition(event);
		this.save();
	}

	getComponentTabs(): IJsonTabComponent[] {
		return this.selectedComponent.getDatabearer().componentTabs;
	}

	openTab(chosenTab: IJsonTabComponent): void {
		this.selectedComponent.switchFocusTab(chosenTab);
	}

	addTab(): void {
		const tabComponent = this.selectedComponent.addTab();

		this.translationManagementService.createGeneralLanguageComponent(
			this.selectedComponent.getId(),
			tabComponent.id,
			tabComponent.name
		);

		this.save();
	}

	updateTab(tab: IJsonTabComponent): void {
		this.selectedComponent.getContextMetaData()
			.then((sourceFields: IAvailableSourceField[]) => {
				this.openEditTabDialog(tab, sourceFields)
					.then((result: IDialogResult<IJsonTabComponent | string>) => {
						if (result.button === DialogButtonType.Ok) {
							const tabResult = result.value as IJsonTabComponent;
							this.selectedComponent.updateTab(tab, tabResult);
							this.selectedComponent.updateValuesWithTranslations();
						}

						if (result.button !== DialogButtonType.Cancel) {
							this.save();
						}
					});
			});
	}

	deleteTab(tab: IJsonTabComponent): void {
		this.selectedComponent.deleteTab(tab);
		this.save();
	}

	private save(saveTranslations = true): void {
		this.editorService.saveCanvas();
		if (saveTranslations) {
			this.editorService.saveCurrent(SaveFlagsEnum.TRANSLATIONS);
		}
	}

	private openEditTabDialog(
		tab: IJsonTabComponent,
		sourceFields: IAvailableSourceField[]
	): ng.IPromise<IDialogResult<IJsonTabComponent | string>> {
		const options: IDialogOptions<IEditTabDialogParameter> = {
			title: "Edit Tab",
			parameter: {
				tab: angular.copy(tab),
				sourceFields: sourceFields,
				component: this.selectedComponent
			},
			template: require("./edit-tab-dialog.html")
		};

		return this.dialogService.show(options);
	}

	// eslint-disable-next-line @typescript-eslint/no-unused-vars
	static getInsertPoints(flexbox: IEditorFlexbox): IInsertPoints {
		return null;
	}
}

export class TabsContentSettingsDirective implements ng.IDirective {
	template = require("./tabs-content-settings.html");
	scope = true;
	controller = TabsContentSettingsCtrl;
	controllerAs = "eirTabsContentSettingsCtrl";
}
