import { IJsonFileMetaData } from "ng2/common/model/databearer/json-manifest.model";

export class FileMetaData implements IJsonFileMetaData {
	mimeType: string;
	visibleName: string;

	constructor(visibleName: string, mimeType: string) {
		this.mimeType = mimeType;
		this.visibleName = visibleName;
	}
}
