import type { IEditorComponent } from "common/interface/ieditor-component";
import { SettingsBase } from "../settings-base";
import { AngularConstants } from "../../../../common/angular-constants";
import { InjectNames, ChartTypeEnum, SettingKeys } from "ng2/common/constants/common.constant";
import { EditorService } from "../../services/editor-service";
import { IEditorChart } from "../../components/editor-chart";
import { IComponentFieldSetting } from "ng2/common/model/components/settings/component-field-setting.model";

export class ChartStyleSettingsCtrl extends SettingsBase {
	public availableChartTypes = [
		{ name: "Area", value: ChartTypeEnum.Area },
		{ name: "Bar", value: ChartTypeEnum.Bar },
		{ name: "Bar (Grouped)", value: ChartTypeEnum.BarGrouped },
		{ name: "Bar (Stacked)", value: ChartTypeEnum.BarStacked },
		{ name: "Column", value: ChartTypeEnum.Column },
		{ name: "Column (Grouped)", value: ChartTypeEnum.ColumnGrouped },
		{ name: "Column (Stacked)", value: ChartTypeEnum.ColumnStacked },
		{ name: "Donut", value: ChartTypeEnum.Donut },
		{ name: "Line", value: ChartTypeEnum.Line },
		{ name: "Pie", value: ChartTypeEnum.Pie },
		{ name: "Radar", value: ChartTypeEnum.Radar },
	];

	protected selectedComponent: IEditorChart;
	public chartType: ChartTypeEnum;
	public legendDisplay: boolean;
	public startupAnimationEnable: boolean;
	public showLabel: boolean;
	public animationCheckboxDisabled: boolean;

	public static $inject = [
		AngularConstants.SCOPE,
		InjectNames.EDITOR_SERVICE,
	];

	public constructor(
		protected $scope: ng.IScope,
		editorService: EditorService,
	) {
		super($scope, editorService);

		this.onComponentChanged(editorService.getSelectedComponent());
	}

	protected onComponentChanged(newComponent: IEditorComponent): void {
		super.onComponentChanged(newComponent);
		if (this.selectedComponent) {
			this.refresh();
		}
	}

	private getAnimationEnabled(): boolean {
		if (this.hasNoAnimation()) {
			return false;
		}
		return this.selectedComponent.getBoundarySetting(SettingKeys.CHART_STARTUP_ANIMATION_ENABLE);
	}

	private hasNoAnimation(): boolean {
		return this.chartType === ChartTypeEnum.Donut ||
		this.chartType === ChartTypeEnum.Pie ||
		this.chartType === ChartTypeEnum.Radar;
	}

	public refresh(): void {
		this.chartType = this.selectedComponent.getBoundarySetting(SettingKeys.CHART_TYPE);
		this.showLabel = this.selectedComponent.getBoundarySetting(SettingKeys.SHOW_LABEL);
		this.legendDisplay = this.selectedComponent.getBoundarySetting(SettingKeys.CHART_LEGEND_DISPLAY);
		this.startupAnimationEnable = this.getAnimationEnabled();
		this.animationCheckboxDisabled = this.hasNoAnimation();
	}

	public onChangeValue(ref: string, newValue: any): void {
		const settingsMeta: IComponentFieldSetting = { ref, boundarySetting: true };
		super.onChangeSetting(settingsMeta, newValue);
		this.refresh();
	}
}

export class ChartStyleSettingsDirective implements ng.IDirective {
	template = require("./chart-style-settings.html");
	scope = true;
	controller = ChartStyleSettingsCtrl;
	controllerAs = "eirChartStyleSettingsCtrl";
}
