import { Config, InjectNames } from "ng2/common/constants/common.constant";
import { IMap } from "ng2/common/model/imap.model";
import { IDocument } from "ng2/common/model/swagger.model";
import { AngularConstants } from "../../common/angular-constants";
import { ErrorResponse } from "../../common/error-response";
import { IApiSuite, IOperation, IOperationQuery, ISuiteQuery } from "../../common/model/ion-metadata.model";
import { ComponentHelper } from "../component-helper";
import { IonApiService } from "./ion-api-service";

export class IonMetadataService {
	public cache: ng.ICacheObject;

	/** Page size when searching for operations */
	public static OPERATIONS_PAGE_SIZE = 20;

	public static $inject = [
		AngularConstants.CACHE_FACTORY,
		AngularConstants.Q,
		InjectNames.ION_API_SERVICE
	];

	public constructor(
		private $cacheFactory: ng.ICacheFactoryService,
		public $q: ng.IQService,
		private ionApiService: IonApiService
	) {
		this.initCache();
	}

	private initCache(): void {
		this.cache = this.$cacheFactory.get("IonMetadataService");
		if (!this.cache) {
			this.cache = this.$cacheFactory("IonMetadataService");
		}
	}

	public getApiSuites(): ng.IPromise<IApiSuite[]> {
		const options: ISuiteQuery = {
			pageSize: 50
		};
		return this.makeIonRequest("metadata/v1/" + Config.TENANT_ID + "/apisuites", false, options);
	}

	public searchOperations(
		suiteContext: string = "",
		query: string = "",
		pageNo: number = 0): ng.IPromise<IOperation[]> {
		const encodedSuiteContext = encodeURI(suiteContext);
		const options: IOperationQuery = {
			suiteContext: encodedSuiteContext,
			searchCriteria: query,
			pageSize: IonMetadataService.OPERATIONS_PAGE_SIZE,
			pageNo,
		};

		return this.makeIonRequest("metadata/v1/" + Config.TENANT_ID + "/operations", false, options);
	}

	public getSwaggerDoc(fullPath: string): ng.IPromise<IDocument> {
		const deferred = this.$q.defer<IDocument>();
		const regex = new RegExp(".*\/" + Config.TENANT_ID + "\/(.*)", "i");
		const pathMatch = fullPath.match(regex);
		if (pathMatch && pathMatch[1]) {
			const swaggerPath = pathMatch[1];
			this.makeIonRequest<IDocument>(swaggerPath, true).then(resp => {
				const dereferenced = ComponentHelper.dereferenceSwagger(resp);
				deferred.resolve(dereferenced);
			}).catch((errorResponse: ng.IHttpResponse<any>) => {
				const error = new ErrorResponse(errorResponse.statusText, errorResponse.status);
				deferred.reject(error);
			});
		} else {
			const error = new ErrorResponse(`Unable to handle path "${fullPath}"`);
			deferred.reject(error);
		}

		return deferred.promise;
	}

	private makeIonRequest<T>(
		relativePath: string,
		setTenant: boolean,
		params: IMap<any> = {},
		method: string = "GET"
	): ng.IPromise<T> {
		return this.ionApiService.executeIonApiAsync<T>({
			url: relativePath,
			method,
			setTenant,
			params,
			cache: this.cache,
		}).then(response => response.data);
	}

	public static add(ngModule: ng.IModule): void {
		ngModule.service(InjectNames.ION_METADATA_SERVICE, IonMetadataService);
	}
}
