import { QuestionDefinitionsEnum } from "../model/questions/questions.enum";
import type { IJsonQuestion } from "../model/questions/questions.model";

import { MimeTypes, ProjectFileKeys } from "ng2/common/constants/common.constant";

import type { ICustomComponentDefinition } from "ng2/common/model/components/custom-component-definition.model";
import type { IJsonCompositeComponent } from "ng2/common/model/databearer/components/json-composite-component.model";
import type { IJsonDatasource } from "ng2/common/model/databearer/dataservice/json-datasource.model";
import type { IJsonLayoutComponent } from "ng2/common/model/databearer/json-layout-component.model";
import type { IJsonDesignerMashup } from "ng2/common/model/databearer/json-mashup.model";
import type { IDesignerMashup } from "ng2/common/model/project/mashup/designer-mashup.model";

import { DesignerManifest } from "ng2/common/project/manifest/designer-manifest";
import { Mashup } from "ng2/common/project/mashup";
import { Interaction } from "ng2/components/interaction/interaction";

export class DesignerMashup extends Mashup implements IDesignerMashup {
	manifest: DesignerManifest;
	questions?: IJsonQuestion[] = [];

	getManifest(): DesignerManifest {
		return this.manifest;
	}

	//#region /* Questions */

	deleteQuestion(questionId: string, settingId: string): void {
		const question = this.questions.find(question => question.id === questionId);
		if (settingId || question.settings?.length > 1) {
			question.settings = question.settings.filter((setting => setting.id !== settingId));
		} else {
			this.questions = this.questions.filter((question => question.id !== questionId));
		}
	}
	addQuestion(newQuestion: IJsonQuestion): void {
		if (newQuestion.type === QuestionDefinitionsEnum.COMPONENT_SETTINGS) {
			const sameQuestion = this.getQuestion(newQuestion, QuestionDefinitionsEnum.COMPONENT_SETTINGS);
			if (sameQuestion) {
				const settingExist = sameQuestion.settings.some(setting => setting.key === newQuestion.settings[0].key);
				if (!settingExist) {
					sameQuestion.settings.push(newQuestion.settings[0]);
				}
			} else {
				this.questions.push(newQuestion);
			}
		} else {
			const foundSameQuestion = this.getQuestion(newQuestion, QuestionDefinitionsEnum.SEEKS_BINDING);
			if (!foundSameQuestion) {
				this.questions.push(newQuestion);
			}
		}
	}

	private getQuestion(newQuestion: IJsonQuestion, type: QuestionDefinitionsEnum): IJsonQuestion {
		return this.questions.find((question) => {
			return question.componentId === newQuestion.componentId && question.type === type;
		});
	}
	getQuestions(): IJsonQuestion[] {
		return this.questions;
	}

	setQuestions(questions: IJsonQuestion[]): void {
		this.questions = questions;
	}

	getQuestionById(id: string): IJsonQuestion {
		return this.questions.find(question => question.id === id);
	}

	////#endregion

	setRoot(componentObject: IJsonLayoutComponent): void {
		super.setRoot(componentObject);
		this.manifest.setHasFile(ProjectFileKeys.ROOT_COMPONENT, MimeTypes.COMPONENT, componentObject.id);
	}

	/* Datasources */

	addDatasource(datasource: IJsonDatasource): void {
		super.addDatasource(datasource);
		this.manifest.setHasFile(ProjectFileKeys.DATASOURCE, MimeTypes.DATASOURCE, ProjectFileKeys.DATASOURCE);
	}

	/* Interactions */

	addInteraction(interaction: Interaction): void {
		super.addInteraction(interaction);
		this.manifest.setHasFile(ProjectFileKeys.INTERACTION, MimeTypes.INTERACTION, ProjectFileKeys.INTERACTION);
	}

	/* Custom Components */

	addCustomComponentDefinition(customComponent: ICustomComponentDefinition): void {
		super.addCustomComponentDefinition(customComponent);
		this.manifest.setHasFile(
			ProjectFileKeys.CUSTOM_COMPONENT,
			MimeTypes.CUSTOM_COMPONENT,
			ProjectFileKeys.CUSTOM_COMPONENT
		);
	}

	/* Composite Components */

	addCompositeComponent(compositeComponent: IJsonCompositeComponent): void {
		super.addCompositeComponent(compositeComponent);
		this.manifest.setHasFile(
			ProjectFileKeys.COMPOSITE_COMPONENTS,
			MimeTypes.COMPOSITE_COMPONENTS,
			ProjectFileKeys.COMPOSITE_COMPONENTS
		);
	}

	static fromObj(obj: IJsonDesignerMashup): DesignerMashup {
		const entry = Mashup.create(DesignerMashup, DesignerManifest, obj);
		// designer only
		if (obj.questions) {
			entry.questions = obj.questions;
		}
		return entry;
	}
}
