import {
	AfterViewInit,
	Component,
	Input,
	OnInit,
	QueryList,
	ViewChildren,
} from "@angular/core";
import { SohoPersonalizeDirective } from "ids-enterprise-ng";

import { ThemeEnum } from "ng2/common/constants/common.constant";

import type { IJsonDesignerManifest } from "ng2/common/model/databearer/json-manifest.model";

@Component({
	selector: "ng2-titlebar",
	template: `
		<soho-header *ngIf="manifest.showTitleBar"
			[hasToolbar]="true"
		>
			<soho-toolbar-flex maxVisibleButtons="5" hasMoreButton="true">
				<soho-toolbar-flex-section [isTitle]="true">
					<h1>
						<span soho-toolbar-flex-page-title>{{ manifest.title }}</span>
						<!-- <span soho-toolbar-flex-section-title>{{ manifest.title }}</span> -->
					</h1>
				</soho-toolbar-flex-section>

				<soho-toolbar-flex-more-button>
					<ul class="popupmenu">
						<!-- <li *ngFor="let theme of themes" class="is-selectable">
							<a href="#"
								data-theme-variant="theme.id"
								data-translate="text">{{ theme.variantName }}</a>
						</li> -->
						<li class="is-selectable">
							<a href="#"
								data-theme-variant="light"
								data-translate="text">Light</a>
						</li>
						<li class="is-selectable">
							<a href="#"
								data-theme-variant="dark"
								data-translate="text">Dark</a>
						</li>
						<li class="is-selectable">
							<a href="#"
								data-theme-variant="contrast"
								data-translate="text">High Contrast</a>
						</li>
					</ul>
					<div soho-personalize
						(changetheme)="onChangeTheme($event)"
						(changecolors)="onChangeColors($event)"
					></div>
				</soho-toolbar-flex-more-button>
			</soho-toolbar-flex>
		</soho-header>
	`
})
export class TitlebarComponent implements OnInit, AfterViewInit {

	@Input() manifest: IJsonDesignerManifest;

	@ViewChildren(SohoPersonalizeDirective)
	private queryList: QueryList<SohoPersonalizeDirective>;
	private personalize: SohoPersonalizeDirective;

	themes: SohoTheme[];

	ngOnInit(): void { }

	ngAfterViewInit(): void {

		this.personalize = this.queryList.first;

		this.themes = this.personalize.themes();
		this.personalize.theme = this.getCurrentTheme();
	}

	private getCurrentTheme(): string {

		// tslint:disable:no-magic-numbers
		switch (this.manifest.defaultTheme) {
			case ThemeEnum.DARK:
				return this.themes[1].id;
			case ThemeEnum.HIGH_CONTRAST:
				return this.themes[2].id;
			case ThemeEnum.LIGHT_TRANSPARENT:
				return this.themes[3].id;
			case ThemeEnum.DARK_TRANSPARENT:
				return this.themes[4].id;
			case ThemeEnum.DEFAULT:
			case ThemeEnum.LIGHT:
			default:
				return this.themes[0].id;
		}
		// tslint:enable:no-magic-numbers
	}

	onChangeTheme(event: SohoChangeThemePersonalizeEvent): void {
		console.info(event);
	}
}
