import angular from "angular";

import { IJsonRadioButtons, IRadioButton } from "../../../../common/model/component/radiobutton.model";
import { ISettingsClassInstance } from "../../defines";
import { ComponentSettingsCtrl } from "../component-settings";

import { RadioButton } from "../../../../components/radiobutton/radiobutton";
import { RadioButtonContentSettingsDirective } from "./radiobutton-content-settings";

export class RadioButtonSettings implements ISettingsClassInstance {
	private static instance: RadioButtonSettings = new RadioButtonSettings();

	public static getInstance(): RadioButtonSettings {
		return RadioButtonSettings.instance;
	}

	public constructor() {
		if (RadioButtonSettings.instance) {
			throw new Error("Error: Instantiation failed: Use RadioButtonSettings.getInstance() instead of new.");
		}
	}

	public contentSettingsDirective: ng.IDirective = new RadioButtonContentSettingsDirective();

	public create(id: string): IJsonRadioButtons {
		const newRadioButton = ComponentSettingsCtrl.create(id, RadioButton) as IJsonRadioButtons;
		const numberOfButtons = 3;
		const componentRadioButton: IRadioButton = {
			id: "radiobuttonId",
			name: "radiobuttonNames"
		};

		newRadioButton.componentRadioButtons = [];
		for (let i = 1; i <= numberOfButtons; i++) {
			const newId: string = id + "Button" + i;
			componentRadioButton.id = newId;
			componentRadioButton.name = "Radio " + i;

			newRadioButton.componentRadioButtons.push(angular.copy(componentRadioButton));
		}
		return newRadioButton;
	}
}
