import {
	AppEnvType,
	AppEnvTypes,
	BuildEnvType,
	BuildEnvTypes,
	Config,
	InjectNames,
} from "ng2/common/constants/common.constant";

import type { IMap } from "ng2/common/model/imap.model";

import { AngularConstants } from "common/angular-constants";

import { HttpService } from "common/services/http-service";

export interface IHashResponse {
	data?: any;
}

export enum HashSources {
	DESIGNER_HASH_SRC = "cache-bust-map-designer.json",
	RUNTIME_HASH_SRC = "cache-bust-map-runtime.json",
}

export enum HashSourceTypes {
	DESIGNER = "designer",
	RUNTIME = "runtime",
}

export type HashSourceType = HashSourceTypes.DESIGNER | HashSourceTypes.RUNTIME;

export class HashService {
	private designerMapOfHashes: IMap<string>;
	private runtimeMapOfHashes: IMap<string>;
	static $inject = [
		AngularConstants.Q,
		InjectNames.HTTP_SERVICE,
		// InjectNames.BUILD_ENV_CONSTANT,
		// InjectNames.APP_ENV_CONSTANT,
	];

	constructor(
		private q: ng.IQService,
		private httpService: HttpService,
		private buildEnv: BuildEnvType,
		private appEnv: AppEnvType
	) {
		// TODO: MUPS-2682: Fix this
		this.buildEnv = BuildEnvTypes.DEV;
		this.appEnv = AppEnvTypes.DESIGNER;

		if (this.appEnv === AppEnvTypes.DESIGNER) {
			this.getHashDataSrc(HashSourceTypes.DESIGNER).then((response: IHashResponse) => {
				this.designerMapOfHashes = response.data;
			});
		} else if (this.appEnv === AppEnvTypes.RUNTIME) {
			this.getHashDataSrc(HashSourceTypes.RUNTIME).then((response: IHashResponse) => {
				this.runtimeMapOfHashes = response.data;
			});
		}

		// switch (this.appEnv) {
		// 	case AppEnvTypes.DESIGNER:
		// 		this.getHashDataSrc(HashSourceTypes.DESIGNER).then((response: IHashResponse) => {
		// 			this.designerMapOfHashes = response.data;
		// 		});
		// 		break;
		// 	case AppEnvTypes.RUNTIME:
		// 		this.getHashDataSrc(HashSourceTypes.RUNTIME).then((response: IHashResponse) => {
		// 			this.runtimeMapOfHashes = response.data;
		// 		});
		// 		break;
		// 	default:
		// 		break;
		// }
	}

	/**
	 * Use to get any hash for a file. Mainly used in production
	 * The srcString needs to be in the format of:
	 *
	 * /WSWebClient/dynamic/metadata/appbuilder/ || WSWebClient/dynamic/metadata/appbuilder/
	 * OR
	 * /WSWebClient/dynamic/metadata/appruntime/ || WSWebClient/dynamic/metadata/appruntime/
	 *
	 *	Example:
		* 	/WSWebClient/dynamic/metadata/appbuilder/assets/xi/light-theme.css
		* 	will return
		* 	/WSWebClient/dynamic/metadata/appbuilder/assets/xi/light-theme.myHash123adfs.css
		*
		* @param {string} srcString
		* @param {HashSourceType} hashSrc
		* @returns {ng.IPromise<string>}
		* @memberof HashService
		*/
	getHashFromMapOfHashes(srcString: string, hashSrc: HashSourceType): ng.IPromise<string> {
		const formattedSrcString = srcString.replace(/^\//, ""); // rm the "/" for proper checkup in json structure
		const getHash = () => {
			switch (hashSrc) {
				case HashSourceTypes.DESIGNER:
					return this.getHashFromHashDataDesigner(formattedSrcString);
				case HashSourceTypes.RUNTIME:
					return this.getHashFromHashDataRuntime(formattedSrcString);
				default: // Did not find any source that has that hash. Just give back the formatted value
					return this.q.resolve(formattedSrcString);
			}
		};
		return getHash()
			// Re-add the "/" that we removed at the beginning
			.then((newSrcString) => "/" + newSrcString)
			// If there are any errors, just send back the original srcString
			.catch((err: any) => srcString);
	}

	/**
	 * Use to get all hash for every hashed asset.
	 */
	getHashMap(): IMap<string> {
		switch (this.appEnv) {
			case AppEnvTypes.DESIGNER:
				return this.designerMapOfHashes;
			case AppEnvTypes.RUNTIME:
			default:
				return this.runtimeMapOfHashes;
		}
	}

	private getHashFromHashDataDesigner(srcString: string): ng.IPromise<string> {
		if (this.designerMapOfHashes) {
			return this.q.resolve(
				this.getValueFromMapAndParseToMongooseAssetsPath(this.designerMapOfHashes, srcString) || srcString);
		} else {
			return this.getHashDataSrc(HashSourceTypes.DESIGNER).then((response: IHashResponse) => {
				this.designerMapOfHashes = response.data;
				return this.getValueFromMapAndParseToMongooseAssetsPath(this.designerMapOfHashes, srcString) || srcString;
			});
		}
	}

	private getValueFromMapAndParseToMongooseAssetsPath(mapWithHashes: IMap<string>, key: string): string {
		let result = "";
		for (const value in mapWithHashes) {
			if (key.indexOf(value) > 0) {
				result = key.replace(value, mapWithHashes[value]);
			}
		}
		return result;
	}

	private getHashFromHashDataRuntime(srcString: string): ng.IPromise<string> {
		if (this.runtimeMapOfHashes) {
			return this.q.resolve(
				this.getValueFromMapAndParseToMongooseAssetsPath(this.runtimeMapOfHashes, srcString) || srcString);
		} else {
			return this.getHashDataSrc(HashSourceTypes.RUNTIME).then((response: IHashResponse) => {
				this.runtimeMapOfHashes = response.data;
				return this.getValueFromMapAndParseToMongooseAssetsPath(this.runtimeMapOfHashes, srcString) || srcString;
			});
		}
	}

	private getHashDataSrc(hashSrc: HashSourceType): ng.IPromise<IHashResponse> {
		if (this.buildEnv === BuildEnvTypes.DEV) {
			return this.q.resolve({
				data: {}
			});
		}
		let src = "";
		switch (hashSrc) {
			case HashSourceTypes.DESIGNER:
				src = Config.APP_ROOT + HashSources.DESIGNER_HASH_SRC;
				break;
			case HashSourceTypes.RUNTIME:
				src = Config.APP_ROOT + HashSources.RUNTIME_HASH_SRC;
				break;
			default:
				break;
		}
		return this.httpService.executeRequest<IHashResponse>({ method: "GET", url: src });
	}

	static add(ngModule: ng.IModule): void {
		ngModule.service(InjectNames.HASH_SERVICE, HashService);
	}
}
