import { AngularConstants } from "../../../../common/angular-constants";
import { Debugger } from "../../../../common/debugger";
import { DialogService } from "../../../../common/services/dialog-service";
import { Defines } from "../../../../components/defines";
import { Lookup } from "../../../../components/lookup/lookup";
import { TranslationManagementService } from "../../../../components/services/translation-management-service";
import { EditorService } from "../../services/editor-service";
import { UpsertDatagridColumnContentSettings } from "../upsert-datagrid-columns-content-settings";

import { InjectNames } from "ng2/common/constants/common.constant";

import type { IDatasource } from "common/model/datasource/datasource.model";
import type { IComponentFieldSetting } from "ng2/common/model/components/settings/component-field-setting.model";
import type { IJsonLookup } from "ng2/common/model/databearer/components/json-lookup.model";
import type { IMetaData } from "ng2/common/model/dataservice/meta-data.model";

export interface ILookupContentSettingsScope extends ng.IScope {
	eirLookupContentSettingsCtrl: LookupContentSettingsCtrl;
}

export class LookupContentSettingsCtrl extends UpsertDatagridColumnContentSettings {
	static $inject = [
		AngularConstants.SCOPE,
		InjectNames.EDITOR_SERVICE,
		InjectNames.DIALOG_SERVICE,
		InjectNames.TRANSLATION_MANAGEMENT_SERVICE
	];

	protected addTitle = "Add Lookup Datagrid Column";
	protected editTitle = "Edit Lookup Datagrid Column";

	constructor(
		$scope: ng.IScope,
		editorService: EditorService,
		dialogService: DialogService,
		translationManagementService: TranslationManagementService
	) {
		super(
			$scope,
			editorService,
			dialogService,
			translationManagementService
		);

		const dataservice = this.selectedComponent.getDatasource();
		if (dataservice) {
			dataservice.getMetaData()
				.then(metadata => this.setLookupOutputOptions(metadata));
		}
	}

	refreshMetaSettings(dataservice: IDatasource): ng.IPromise<void> {
		const newDeferred = this.editorService.getNewDeferred<void>();
		if (dataservice) {
			const result = dataservice.getMetaData()
				.then(metadata => {
					this.setLookupOutputOptions(metadata);
					this.addColumns(metadata.outputFields, true);
				});
			newDeferred.resolve(result);
		} else {
			newDeferred.resolve(this.setLookupOutputOptions());
		}

		return newDeferred.promise;
	}

	protected refreshComponent(): void {
		super.refreshComponent();
		this.updateLookupColumns();
	}

	private setLookupOutputOptions(metadata?: IMetaData): void {
		const fieldSettings =
			Defines.allComponents[this.selectedComponent.getTypedef()].class.SETTING_FIELDS;

		const lookupOutputSetting = fieldSettings.find(fieldSetting => {
			return fieldSetting.title === Lookup.OUTPUT_SETTING_TITLE;
		}).settings[0] as IComponentFieldSetting;

		lookupOutputSetting.dataset = metadata ? metadata.outputFields
			.map(outputField => {
				return {
					name: outputField.FLDS,
					value: outputField.FLNM
				};
			}) : [];

		this.editorService.refreshSettingEvent.raise(lookupOutputSetting.ref);

		const lookup = this.selectedComponent.getDatabearer() as IJsonLookup;
		if (!lookup.lookupOutputId && lookupOutputSetting.dataset.length > 0) {
			lookup.lookupOutputId = lookupOutputSetting.dataset[0].value;
		}
	}

	private updateLookupColumns(): void {
		const databearer = this.selectedComponent.getDatabearer();
		const lookupApi = this.selectedComponent.getSohoXiApi();

		if (lookupApi) {
			const settings = lookupApi.settings;
			settings.options.columns = databearer.columns;
			lookupApi.updated(settings);
		}
	}
}

export class LookupContentSettingsDirective implements ng.IDirective {
	template = require("./lookup-content-settings.html");
	scope = true;
	controller = LookupContentSettingsCtrl;
	controllerAs = "eirLookupContentSettingsCtrl";

	link(scope: ILookupContentSettingsScope): void {
		scope.$on(AngularConstants.DESTROY, () => {
			Debugger.log(this, "Destroy");
			scope.eirLookupContentSettingsCtrl.unsubscribers.forEach(unsubscribe => {
				unsubscribe();
			});
		});
	}
}
