import { Defines } from "../defines";
import { EditorHelper } from "../helper";

export class SettingsSwitchers {
	private static contentSettingsTemplate: string;
	private static styleSettingsTemplate: string;

	private static commonStyleSettingsTemplate =
		"<eir-generic-settings settings-tab=\"'style'\"></eir-generic-settings>";
	private static commonContentSettingsTemplate =
	"<eir-attribute-settings>" +
	"</eir-attribute-settings>" +
	"<eir-generic-settings settings-tab=\"'content'\"></eir-generic-settings>";

	private static addComponentSettingsSwitches(m: ng.IModule): void {
		let contentSettingsTemplate = "<div>";
		contentSettingsTemplate += "<div ng-switch on='eirSettingsCtrl.selectedComponent.getTypedef()'>";

		let styleSettingsTemplate = "<div>";
		styleSettingsTemplate += "<div ng-switch on='eirSettingsCtrl.selectedComponent.getTypedef()'>";

		for (const componentName in Defines.allSettingsComponents) {
			const settingsClass = EditorHelper.getComponentSettingsClass(componentName);
			if (settingsClass.contentSettingsDirective) {
				const contentSettingDirectiveName = "contentsetting" + componentName.toLowerCase();
				m.directive(contentSettingDirectiveName, () => settingsClass.contentSettingsDirective);
				contentSettingsTemplate +=
					`<${contentSettingDirectiveName} ng-switch-when=${componentName}></${contentSettingDirectiveName}>`;
			}
			if (settingsClass.styleSettingsDirective) {
				const styleSettingDirectiveName = "stylesetting" + componentName.toLowerCase();
				m.directive(styleSettingDirectiveName, () => settingsClass.styleSettingsDirective);

				styleSettingsTemplate +=
					`<${styleSettingDirectiveName} ng-switch-when=${componentName}></${styleSettingDirectiveName}>`;
			}
		}

		contentSettingsTemplate += "</div>" + this.commonContentSettingsTemplate + "</div>";
		styleSettingsTemplate += "</div>" + this.commonStyleSettingsTemplate + "</div>";
		this.contentSettingsTemplate = contentSettingsTemplate;
		this.styleSettingsTemplate = styleSettingsTemplate;
	}

	static addComponentSettings(m: ng.IModule): void {
		this.addComponentSettingsSwitches(m);

		m.directive("eirStyleSettingsSwitch", [() => {
			return {
				restrict: "E",
				replace: true,
				scope: true,
				template: this.styleSettingsTemplate,
				link: ($scope: ng.IScope, $element: ng.IAugmentedJQuery) => {
					// important to stop "updated" from bubbling to tab-control
					$element.on("updated", (e: JQueryEventObject) => {
						e.stopPropagation();
					});
				}
			};
		}]);

		m.directive("eirContentSettingsSwitch", [() => {
			return {
				restrict: "E",
				replace: true,
				scope: true,
				template: this.contentSettingsTemplate,
				link: ($scope: ng.IScope, $element: ng.IAugmentedJQuery) => {
					// important to stop "updated" from bubbling to tab-control
					$element.on("updated", (e: JQueryEventObject) => {
						e.stopPropagation();
					});
				}
			};
		}]);
	}
}
