/**
 * Dropdown Directive.
 *
 * Example:
 * scope["datas"] = [{ value: 1, label: "first" }, { value: 2, label: "second" }]
 *	<select id="dropdown-1" class="dropdown"
		ng-model="modelVal"
		ng-options="data.label as data.value for data in datas"
		xi-drop-down
	></select>
	*/

import * as xi from "../../model/sohoxi/sohoxi-dropdown.model";
import { Utils } from "../../sohoxi/utils";

export class DropDownDirective implements ng.IDirective {
	public restrict = "A";
	public scope = false;

	private static readonly DIRECTIVE_NAME = "xiDropDown";

	constructor(private $timeout: ng.ITimeoutService) {}

	public link(
		scope: ng.IScope,
		element: ng.IAugmentedJQuery,
		attributes: ng.IAttributes
	): void {
		const options = attributes[DropDownDirective.DIRECTIVE_NAME];
		let api: any;

		const unbindWatcher = scope.$watch(
			options,
			(newOptions: xi.IDropDownOptions) => {
				this.$timeout(() => {
					if (newOptions) {
						element.dropdown(newOptions);
					} else {
						element.dropdown();
					}
					api = Utils.getData(element, "dropdown");
					api.setDisplayedValues();
				}, 100); // tslint:disable-line:no-magic-numbers
			},
			true
		);

		let modelWatcher: any;
		const modelAttr = attributes.ngModel;

		if (modelAttr) {
			modelWatcher = scope.$watch(modelAttr, () => {
				if (api) {
					this.$timeout(() => {
						api.setDisplayedValues();
					}, 100); // tslint:disable-line:no-magic-numbers
				}
			});
		}

		let disabledWatcher: any;
		const disabledAttr = attributes.ngDisabled;

		if (disabledAttr) {
			disabledWatcher = scope.$watch(disabledAttr, newValue => {
				if (api) {
					if (newValue === false) {
						api.enable();
					} else if (newValue === true) {
						api.disable();
					}
				}
			});
		}
		// Tear down
		element.on("$destroy", () => {
			if (api) {
				api.destroy();
			}
			unbindWatcher();
			if (modelWatcher) {
				modelWatcher();
			}
			if (disabledWatcher) {
				disabledWatcher();
			}
		});
	}

	public static add(m: ng.IModule): void {
		m.directive(DropDownDirective.DIRECTIVE_NAME, [
			"$timeout",
			timeout => {
				return new DropDownDirective(timeout);
			}
		]);
	}
}
