/**
 * Track Dirty Directive.
 *
 * Indicates if an element is dirty.
 */
export class TrackDirtyDirective implements ng.IDirective {
	private static readonly DIRECTIVE_NAME = "xiTrackDirty";
	public restrict = "A";
	public scope = false;
	public replace = true;

	public link(
		scope: ng.IScope,
		element: ng.IAugmentedJQuery,
		attributes: ng.IAttributes
	): void {
		element.attr("data-trackdirty", "true");
		element.trackdirty();
	}

	public static add(m: ng.IModule): void {
		m.directive(TrackDirtyDirective.DIRECTIVE_NAME, () => {
			return new TrackDirtyDirective();
		});
	}
}
