import angular from "angular";

import { AngularConstants } from "../../../../common/angular-constants";
import { Helper } from "../../../../common/helper";
import { EditorService } from "../../services/editor-service";
import type { IGenericSettingScope, ISettingController } from "../generic-settings";
import { SettingsBase } from "../settings-base";

import { InjectNames, SettingInputKeys } from "ng2/common/constants/common.constant";

import type { IBoundary } from "ng2/common/model/project/boundary.model";

import type { IEditorComponent } from "common/interface/ieditor-component";

class DropdownSettingsCtrl extends SettingsBase implements ISettingController {
	settingValue: string;
	tooltip: string;
	isColorPicker: boolean;

	static $inject = [
		AngularConstants.SCOPE,
		InjectNames.EDITOR_SERVICE
	];

	constructor(
		protected $scope: IGenericSettingScope,
		editorService: EditorService
	) {
		super($scope, editorService);
		$scope.register({ settingCtrl: this, settingRef: $scope.settingMeta.ref });
		this.isColorPicker = $scope.settingMeta.inputType === SettingInputKeys.COLOR_PICKER;
		this.button = $scope.settingMeta.button;
		this.tooltip = $scope.settingMeta.tooltip;
		this.settingsRef = $scope.settingMeta.ref;
		this.onComponentChanged(editorService.getSelectedComponent());

		if (this.isColorPicker) {
			this.initColorpicker();
		}
	}

	/**
	* angularjs ng-change does not work well with color-picker
	* hijacking the change event instead
	*/
	private initColorpicker(): void {
		setTimeout(() => {
			const colorpickerElement = angular.element("#colorpickerLabel" + this.settingsRef);
			colorpickerElement.on("change", () => {
				this.onChangeSetting(this.$scope.settingMeta, this.settingValue);
			});
		});
	}

	refresh(): void {
		const settingMeta = this.$scope.settingMeta;
		let value = this.readSettingValue<string>(settingMeta);
		if (Helper.isUndefinedOrNull(value)) {
			value = settingMeta.defaultValue;

			if (Helper.isUndefined(value) && settingMeta.dataset?.length > 0) {
				value = settingMeta.dataset[0].value;
			}
		}
		this.settingValue = value;
	}

	protected onComponentChanged(newComponent: IEditorComponent): void {
		this.selectedComponent = newComponent;
		if (this.selectedComponent) {
			this.refresh();
		}
	}

	protected onBoundaryChanged(newBoundary: IBoundary): void {
		super.onBoundaryChanged(newBoundary);
		this.refresh();
	}
}

export class DropdownSettingsDirective implements ng.IDirective {
	template = require("./dropdown-setting.html");
	scope = {
		settingMeta: "=",
		register: "&"
	};
	controller = DropdownSettingsCtrl;
	controllerAs = "eirDropdownSettingsCtrl";

	static add(m: ng.IModule): void {
		m.directive("eirDropdownSetting", () => new DropdownSettingsDirective());
	}
}
