import { Numbers } from "ng2/common/constants/common.constant";
import { IMap } from "ng2/common/model/imap.model";
import * as sw from "ng2/common/model/swagger.model";
/**
 * Render a Swagger schema with expandable arrays and objects.
 */
export class SwaggerSchemaExpanderComponent {
	public schemaName: string;
	public schema: sw.ISchemaObject;
	public depth: number = 0;
	public expanded = false;

	public getIndentationStyle (): IMap<string> {
		return {
			"margin-left": `${this.depth * Numbers.TWENTY}px`,
		};
	}

	public getExpandedStyle (): IMap<string> {
		return {
			"margin-left": `${this.expanded ? this.depth * Numbers.TWENTY : 0}px`,
		};
	}

	public toggleExpanded (): void {
		this.expanded = !this.expanded;
	}

	public schemaIsObject(): boolean {
		if (this.schema) {
			return this.schema.type === "object" || this.schema.properties !== undefined;
		} else {
			return false;
		}
	}

	public schemaIsArray(): boolean {
		if (this.schema) {
			return this.schema.type === "array" || this.schema.items !== undefined;
		} else {
			return false;
		}
	}

	public schemaIsPrimitive(): boolean {
		if (this.schema) {
			const isNumber = this.schema.type === "number";
			const isBoolean = this.schema.type === "boolean";
			const isString = this.schema.type === "string";
			const isInteger = this.schema.type === "integer";
			const isNull = this.schema.type === "null";
			return isNumber || isBoolean || isString || isInteger || isNull;
		} else {
			return false;
		}
	}

	static add (m: ng.IModule): void {
		m.component("swaggerSchemaExpander", {
			template: require("./swagger-schema-expander.html"),
			controller: SwaggerSchemaExpanderComponent,
			bindings: {
				schemaName: "=",
				schema: "=",
				depth: "="
			}
		});
	}
}
