import { RegexStrings } from "ng2/common/constants/common.constant";
import { IParentAnnounce } from "./notifiable-component";

import { Helper } from "../../helper";

export interface IValidationResult {
	status: boolean;
	errorMessage?: string;
}

export interface IFieldValidationResult {
	id?: string;
	status?: boolean;
}

export interface IFieldValidationChangeObjects {
	validation?: angular.IChangesObject<IValidation>;
	readonly?: angular.IChangesObject<boolean>;
	id?: angular.IChangesObject<string>;
	parentAnnounce?: angular.IChangesObject<any>;

}
export type ValidatorType
	= "max-length"
	| "not-empty"
	| "min-length"
	| "numeric"
	| "numeric-no-negative"
	| "no-space"
	| "no-numeric"
	| "custom";

export interface IValidator {
	type: ValidatorType;
	value?: any;
	custom?: {
		validator: Function;
		errorMessage: string;
	};
}

export interface IValidatorError {
	id: string;
	type: ValidatorType;
	message: string;
	field: string;
}

export interface IValidation {
	validators: IValidator[];
	errorList: IValidatorError[];
}

export class FieldValidation implements ng.IController {
	public readonly DEFAULT_INPUT_FIELD_NAME: string = "inputFieldName";

	// NOTICE: Labelname should always be unique
	// bindings
	public id: string;
	public isRequired: boolean;
	public labelName: string;
	public fieldValue: string;
	public fieldName: string;
	public validation: IValidation;

	public parentAnnounce: IParentAnnounce[] = [];

	public isInvalid: boolean;

	public fieldSpecificErrors: IValidatorError[];

	public get displayStatus(): string {
		return this.isInvalid ? "block" : "none";
	}

	public $onInit(): void {
		// Do something here with init
		if (Helper.isUndefinedEmpty(this.fieldName)) {
			this.fieldName = this.DEFAULT_INPUT_FIELD_NAME;
		}

		if (this.validation) {
			// Set isRequired icon on label
			this.isRequired = this.validation
				.validators
				.findIndex(item => item.type === "not-empty") !== -1;

			if (!Helper.isUndefinedEmpty(this.fieldValue)) {
				this.validate();
			} else {
				this.fieldValue = "";
			}
		}
	}

	public $onChanges(changeObjects: IFieldValidationChangeObjects): void {
		const parentAnnounce = changeObjects.parentAnnounce;
		let isCallback: boolean;
		if (
			parentAnnounce &&
			!parentAnnounce.isFirstChange() &&
			parentAnnounce.currentValue.id !== this.id // don't run it on self
		) {
			isCallback = true;
		} else if (
			parentAnnounce &&
			!parentAnnounce.isFirstChange() &&
			parentAnnounce.currentValue.id === this.id
		) {
			return;
		}

		const currentComponentFieldError = this.fieldSpecificErrors;
		const sharedError = this.validation?.errorList;

		if (!changeObjects.id?.isFirstChange() &&
			changeObjects.id?.previousValue !== changeObjects.id?.currentValue) {
			this.validate(isCallback);
		} else
		// if currentComponentFieldError is undefined it means it hasn't been validated yet
		if (changeObjects && changeObjects.validation && changeObjects.validation.isFirstChange() ||
			!currentComponentFieldError ||
			(currentComponentFieldError && currentComponentFieldError.length === 0)
		) {
			return;
		} else {
		// Uncomment to print debugging log
		// 				Debugger.log(this,
		// `$onChanges called on:
		// 	currentField=${this.fieldValue}, id=${this.id},
		// 	fieldError=${JSON.stringify(currentComponentFieldError)},
		// 	sharedError=${JSON.stringify(sharedError)}
		// 				`);
			const customValidator = this.validation.validators
				.find(validator => validator.type === "custom");

			// if sharedList is empty means no error in any component
			if (sharedError && sharedError.length === 0) {
				currentComponentFieldError.length = 0;
			} else if (this.fieldValue !== "" && customValidator) {
				// only validate field if it's not empty and has custom validator
				// for now related field validation only for "custom" validators
				this.validate(isCallback);
			}
		}
	}

	public onComponentChange: Function;

	/**
	 * Returns an error if the value cant go through the validators
	 *
	 * @private
	 * @param {string} value
	 * @param {IValidator} validator
	 * @returns {(IValidatorError | undefined)}
	 * @memberof FieldValidation
	 */
	private getError(
		value: string,
		validator: IValidator,
		isCallback: boolean = false
	): IValidatorError | undefined {
		let validatorError: IValidatorError;
		const id = this.id;

		switch (validator.type) {
			case "max-length":
				if (value.length > validator.value) {
					validatorError = {
						id,
						field: this.labelName,
						message: "Max length is " + validator.value,
						type: validator.type
					};
				}
				break;
			case "min-length":
				if (value.length - 1 <= validator.value) {
					validatorError = {
						id,
						field: this.labelName,
						message: "Min length is " + validator.value,
						type: validator.type
					};
				}
				break;
			case "not-empty":
				if (value.length === 0) {
					validatorError = {
						id,
						field: this.labelName,
						message: "Field cannot be empty",
						type: validator.type
					};
				}
				break;
			case "numeric":
				if (!Helper.isUndefinedEmpty(value) &&
					!RegexStrings.NUMERIC.test(value)
				) {
					validatorError = {
						id,
						field: this.labelName,
						message: "Field has to be a number",
						type: validator.type
					};
				}
				break;
			case "no-numeric":
				// RegexStrings.NO_NUMERIC behaves weird here, therefore I add the regex directly
				if (!Helper.isUndefinedEmpty(value) && (/\d+/g).test(value)) {
					validatorError = {
						id,
						field: this.labelName,
						message: "Field cannot contain numbers",
						type: validator.type
					};
				}
				break;
			case "numeric-no-negative":
				if (!Helper.isUndefinedEmpty(value) &&
					!RegexStrings.NUMERIC_NO_NEGATIVE.test(value)
				) {
					validatorError = {
						id,
						field: this.labelName,
						message: "Field has to be a positive number",
						type: validator.type
					};
				}
				break;
			case "no-space":
				if (value.indexOf(" ") !== -1) {
					validatorError = {
						id,
						field: this.labelName,
						message: "Name cannot contain spaces",
						type: validator.type
					};
				}
				break;
			case "custom":
				let result: boolean | IValidationResult;
				const target = validator.custom.validator;

				if (typeof target === "function") {
					result = target(value, this.id);
				} else if (typeof target === "string") {
					if (target === "void") {
						this.onComponentChange({
							$data: {
								id,
								value
							}
						});
					} else {
						result = this.onComponentChange({
							$data: {
								validator: target,
								value,
								id,
								callback: isCallback
							}
						});
					}
				}

				if (typeof result === "boolean" && result === false) {
					validatorError = {
						id,
						field: this.labelName ? this.labelName : this.id,
						message: validator.custom.errorMessage,
						type: validator.type
					};
				} else if (this.isValidationResult(result) && result.status === false) {
					validatorError = {
						id,
						field: this.labelName ? this.labelName : this.id,
						message: result.errorMessage,
						type: validator.type
					};
				}
				break;
			default:
				break;
		}
		return validatorError;
	}

	/**
	 * Validate if object is IValdationResult
	 * @param object
	 */
	private isValidationResult(object: any): object is IValidationResult {
		return typeof object === "object" &&
			"status" in object &&
			"errorMessage" in object;
	}

	/**
	 * Remove all the errors, in the shared errorlist, that are related to this component
	 *
	 * @private
	 * @memberof FieldValidation
	 */
	private removeFieldRelatedErrorsFromErrorList(): void {
		const sharedErrors = this.validation.errorList;
		if (sharedErrors) {
			sharedErrors.forEach((error, index) => {
				if (error.id === this.id &&
					(error.field === this.labelName || error.field === this.id)
				) {
					sharedErrors.splice(index, 1);
				}
			});
		}
	}

	/**
	 * Used to validate the field based on the validation object
	 *
	 * @memberof FieldValidation
	 */
	public validate(isCallback: boolean = false): void {
		// Reset all the validator values
		this.isInvalid = false;
		this.fieldSpecificErrors = [];
		this.removeFieldRelatedErrorsFromErrorList();

		const sharedErrors = this.validation.errorList;
		const hasFieldValue = !Helper.isUndefinedOrNull(this.fieldValue);

		if (hasFieldValue) {
			this.validation.validators.forEach((validator: IValidator) => {

				const validatorError =
					this.getError(this.fieldValue, validator, isCallback);
				if (validatorError) {
					this.fieldSpecificErrors.push(validatorError);
				}
			});
		}

		// Do we have errors? Then this is invalid
		if (this.fieldSpecificErrors.length !== 0) {
			this.isInvalid = true;
		}

		// Push all the new errors to the shared errorlist
		if (sharedErrors) {
			if (this.fieldSpecificErrors.length !== 0) {
				this.fieldSpecificErrors.forEach((error) => {
					sharedErrors.push(error);
				});
			}
		}
	}
}
