import angular from "angular";
import { AngularConstants } from "../../../../common/angular-constants";

import { CanvasModeEnum, InjectNames, InsertEventTypes, SaveFlagsEnum } from "ng2/common/constants/common.constant";
import { IJsonCompositeComponent, IJsonCompositeComponentManifest } from "ng2/common/model/databearer/components/json-composite-component.model";
import { IJsonDatasource } from "ng2/common/model/databearer/dataservice/json-datasource.model";
import { IJsonExpression } from "ng2/common/model/databearer/json-expression.model";
import { IJsonInteraction } from "ng2/common/model/databearer/json-interaction.model";
import { IMap } from "ng2/common/model/imap.model";
import { Interaction } from "ng2/components/interaction/interaction";
import { Helper } from "../../../../common/helper";

import type { IEditorComponent } from "common/interface/ieditor-component";
import { EditorService } from "../../services/editor-service";
import { InsertComponentService } from "../../services/insert-component-service";
import { InteractionManagementService } from "../../services/interaction-management-service";

import { TranslationManagementService } from "../../../../components/services/translation-management-service";
import { CompositeComponentManagementService } from "../../services/composite-component-management-service";

export class NewCompositeCtrl {
	public selectedComponent: IEditorComponent;
	public htmlEncode = Helper.htmlEncode;
	public selectedManifest: IJsonCompositeComponentManifest;
	public compositeComponents: IJsonCompositeComponent[];
	public compositeComponentManifests: IJsonCompositeComponentManifest[];

	private unsubscribers: Function[] = [];
	private bodyClickListener: (eventObject: JQueryEventObject, ...args: any[]) => any;

	public static $inject = [
		AngularConstants.SCOPE,
		InjectNames.EDITOR_SERVICE,
		InjectNames.INSERT_COMPONENT_SERVICE,
		InjectNames.COMPOSITE_COMPONENT_MANAGEMENT_SERVICE,
		InjectNames.INTERACTION_MANAGEMENT_SERVICE,
		InjectNames.TRANSLATION_MANAGEMENT_SERVICE,
	];

	public constructor(
		$scope: ng.IScope,
		private editorService: EditorService,
		private insertComponentService: InsertComponentService,
		private compositeComponentManagementService: CompositeComponentManagementService,
		private interactionManagementService: InteractionManagementService,
		private translationManagementService: TranslationManagementService,
	) {

		this.unsubscribers.push(editorService.compositeComponentsChangedEvent
			.on((compositeComponents: IJsonCompositeComponent[]) => {
				this.updateCompositeComponents(compositeComponents);
			}));

		this.unsubscribers.push(editorService.canvasModeChangedEvent.on((canvasMode) => {
			if (canvasMode !== CanvasModeEnum.INSERT && this.selectedManifest) {
				this.insertComponentService.cancelInserting();
				this.selectedManifest = null;
			}
		}));

		this.editorService.selectedComponentChangedEvent.on((component: IEditorComponent) => {
			this.selectedComponent = component;
		});
		this.selectedComponent = editorService.getSelectedComponent();

		const currentProject = this.editorService.getCurrentProject();
		this.updateCompositeComponents(currentProject.getCompositeComponents());

		this.bodyClickListener = this.cancel.bind(this);
		angular.element("body").on("click", this.bodyClickListener);

		$scope.$on(AngularConstants.DESTROY, () => {
			this.unsubscribers.forEach((unsubscriber) => {
				unsubscriber();
			});
			angular.element("body").off("click", this.bodyClickListener);
			this.cancel();
		});
	}

	private cancel(): void {
		if (this.selectedManifest) {
			this.insertComponentService.cancelInserting();
		}
	}

	public manageCompositeComponents(): void {
		this.compositeComponentManagementService.manageCompositeComponents();
	}

	private updateCompositeComponents(compositeComponents: IJsonCompositeComponent[]): void {
		this.compositeComponents = compositeComponents;
		this.compositeComponentManifests =
			this.compositeComponents.map((compositeComponent) => { return compositeComponent.manifest; });
	}

	public createComposite(): void {
		this.compositeComponentManagementService.createNewViaWizard(this.selectedComponent.getDatabearer());
	}

	public deleteComposite(compositeId: string): void {
		this.compositeComponentManagementService.removeViaDialog(compositeId);
	}

	public selectDrag(id: string): void {
		this.select(id);
	}

	public selectClick(id: string): void {
		angular.element("body").css("cursor", "no-drop");
		if (this.selectedManifest && this.selectedManifest.id === id) {
			// Deselect
			this.insertComponentService.cancelInserting();
			this.selectedManifest = null;
		} else {
			this.select(id);
		}
	}

	private getCompositeComponentById(id: string): IJsonCompositeComponent {
		return this.compositeComponents.find((composite) => {
			return composite.manifest.id === id;
		});
	}

	private redirectInteraction(interaction: IJsonInteraction, oldToNewIdMap: IMap<string>): void {
		interaction.sourceName = oldToNewIdMap[interaction.sourceName];
		interaction.targetName = oldToNewIdMap[interaction.targetName];
		if (interaction.parameters) {
			interaction.parameters.forEach((parameter) => {
				parameter.valueSource.sourceName = oldToNewIdMap[parameter.valueSource.sourceName];
			});
		}
	}

	private redirectExpression(expression: IJsonExpression, oldToNewIdMap: IMap<string>): void {
		if (expression.source !== "<static>" && expression.source !== "Application") {
			expression.source = oldToNewIdMap[expression.source];
		}
		if (expression.target !== "<static>" && expression.target !== "Application") {
			expression.target = oldToNewIdMap[expression.target];
		}

		expression.usedBy = oldToNewIdMap[expression.usedBy];
	}

	private select(id: string): void {
		const currentProject = this.editorService.getCurrentProject();
		const compositeComponent = this.getCompositeComponentById(id);
		this.selectedManifest = compositeComponent.manifest;
		this.compositeComponentManagementService.setCurrentValuesToCurrentLanguage(compositeComponent);
		const oldToNewIdMap: IMap<string> = {};
		const newComponent = this.editorService.copy(compositeComponent.component, oldToNewIdMap);
		const addedDatasources: IJsonDatasource[] = [];
		const addedInteractions: Interaction[] = [];

		if (compositeComponent.datasources) {
			compositeComponent.datasources.forEach((datasource) => {
				if (!currentProject.getDatasource(datasource.id)) {
					currentProject.addDatasource(datasource);
					addedDatasources.push(datasource);
				}
			});
		}
		if (compositeComponent.interactions) {
			angular.copy(compositeComponent.interactions).forEach((interactionCopy) => {
				this.redirectInteraction(interactionCopy, oldToNewIdMap);
				interactionCopy.id = this.interactionManagementService.generateInteractionId();
				const newInteraction = new Interaction(interactionCopy);
				currentProject.addInteraction(newInteraction);
				addedInteractions.push(newInteraction);
			});

			this.interactionManagementService.projectInteractionsChanged
				.raise(currentProject.getInteractions());
		}
		this.insertComponentService.startInserting(newComponent, InsertEventTypes.COPY).then(() => {
			// CompositeComponent was inserted
			if (compositeComponent.translations) {
				this.editorService.copyTranslationsToCurrentProject(
					compositeComponent.translations,
					oldToNewIdMap,
					newComponent
				);
			} else {
				this.editorService.forEachComponent(newComponent, (child) => {
					this.translationManagementService.createDefaultLanguageComponents(child);
				});
			}

			if (compositeComponent.expressions) {
				angular.copy(compositeComponent.expressions).forEach((expressionCopy) => {
					this.redirectExpression(expressionCopy, oldToNewIdMap);
					expressionCopy.id = Helper.generateUniqueId();
					if (expressionCopy.source && expressionCopy.target) {
						currentProject.addExpression(expressionCopy);
					}
				});
			}

			/* tslint:disable:no-bitwise */
			this.editorService.saveCanvas();
			this.editorService.saveCurrent(
				SaveFlagsEnum.DATASOURCES |
				SaveFlagsEnum.INTERACTIONS |
				SaveFlagsEnum.TRANSLATIONS |
				SaveFlagsEnum.EXPRESSIONS
			);
			/* tslint:enable:no-bitwise */
		}).catch(() => {
			addedDatasources.forEach((datasource) => {
				currentProject.removeDatasource(datasource.id);
			});
			addedInteractions.forEach((interaction) => {
				currentProject.removeInteraction(interaction);
			});
		}).finally(() => {
			if (this.selectedManifest && this.selectedManifest.id === id) { // Just inserted composite
				this.selectedManifest = null;
				this.editorService.setCanvasMode(CanvasModeEnum.SELECTION);
				angular.element("body").css("cursor", "auto");
			} else if (!this.selectedManifest) { // Deselected composite
				this.editorService.setCanvasMode(CanvasModeEnum.SELECTION);
				angular.element("body").css("cursor", "auto");
			} else { // New composite selected
			}
		});
	}
}

export class NewCompositeDirective {
	public static add(m: ng.IModule): void {
		m.directive("eirNewComposite", () => {
			return {
				scope: true,
				template: require("./new-composite.html"),
				controller: NewCompositeCtrl,
				controllerAs: "eirNewCompositeCtrl"
			};
		});
	}
}
