/**
 * Application Menu Directive.
 *
 * Example:
 * <nav xi-app-menu class="application-menu"</nav>
 */
export class ApplicationMenuDirective implements ng.IDirective {
	public restrict = "A";
	public scope = false;

	public link(scope: ng.IScope, element: ng.IAugmentedJQuery): void {
		element.applicationMenu();
	}

	public static add(m: ng.IModule): void {
		m.directive("xiAppMenu", () => {
			return new ApplicationMenuDirective();
		});
	}
}
