import { BindingTypes, ValueObject, ComponentNames } from "ng2/common/constants/common.constant";
import { OrientationEnum } from "ng2/common/enums/orientation.enum";
import { IJsonComponent } from "ng2/common/model/databearer/components/json-component.model";
import { IMap } from "ng2/common/model/imap.model";

import { ChartSettingsCtrl } from "./component-settings/chart/chart-settings";
import { DatagridSettings } from "./component-settings/datagrid/datagrid-settings";
import { FlexboxSettings } from "./component-settings/flexbox/flexbox-settings";
import { ListSettings } from "./component-settings/list/list-settings";
import { LookupSettings } from "./component-settings/lookup/lookup-settings";
import { RadioButtonSettings } from "./component-settings/radiobutton/radiobutton-settings";
import { TabPanelSettings } from "./component-settings/tabs/tab-panel-settings";
import { TabsSettings } from "./component-settings/tabs/tabs-settings";
import { TextboxSettings } from "./component-settings/textbox/textbox-settings";

export interface IInsertPoints {
	orientation: OrientationEnum;
	points: number[];
	length: number;
	position: number;
}

export interface ISettingsClassInstance {
	create(id: string): IJsonComponent;
	contentSettingsDirective?: ng.IDirective;
	styleSettingsDirective?: ng.IDirective;
}

export interface ISettingsClass {
	new (): ISettingsClassInstance;
	getInstance(): ISettingsClassInstance;
}

export interface ISettingsComponentListEntry {
	settingsClass: ISettingsClass;
}

export class SourceComponent {
	public static STATIC_VALUE_OBJECT = {
		id: BindingTypes.STATIC,
		name: ValueObject.STATIC_VALUE,
		isComponent: false
	};
	public static TARGET_CONTEXT_OBJECT = {
		id: BindingTypes.CONTEXT,
		name: ValueObject.TARGET_CONTEXT,
		isComponent: false
	};
	public static URL_VALUE_OBJECT = {
		id: BindingTypes.URL,
		name: ValueObject.URL_VALUE,
		isComponent: false
	};
}

export interface ISourceComponent {
	id: string;
	name: string;
	isComponent: boolean;
}

export class Defines {
	static get allSettingsComponents(): IMap<ISettingsComponentListEntry> {
		const settingsComponentsList: IMap<ISettingsComponentListEntry> = {};
		settingsComponentsList[ComponentNames.Chart] = {
			settingsClass: ChartSettingsCtrl
		};
		settingsComponentsList[ComponentNames.Datagrid] = {
			settingsClass: DatagridSettings
		};
		settingsComponentsList[ComponentNames.Flexbox] = {
			settingsClass: FlexboxSettings
		};
		settingsComponentsList[ComponentNames.List] = {
			settingsClass: ListSettings
		};
		settingsComponentsList[ComponentNames.Lookup] = {
			settingsClass: LookupSettings
		};
		settingsComponentsList[ComponentNames.RadioButton] = {
			settingsClass: RadioButtonSettings
		};
		settingsComponentsList[ComponentNames.Tabs] = {
			settingsClass: TabsSettings
		};
		settingsComponentsList[ComponentNames.TabPanel] = {
			settingsClass: TabPanelSettings
		};
		settingsComponentsList[ComponentNames.TextBox] = {
			settingsClass: TextboxSettings
		};
		return settingsComponentsList;
	}

}

export class SidebarViewEnum {
	constructor(public value: string) { }

	public toString(): string {
		return this.value;
	}

	static NEW_COMPONENT = new SidebarViewEnum("newcomponent");
	static SETTINGS = new SidebarViewEnum("settings");
	static BOTH = new SidebarViewEnum("both");
}

export interface ISidebarView {
	view: SidebarViewEnum;
	isSidebarVisible: boolean;
}
