import angular from "angular";
import { AngularConstants } from "../common/angular-constants";

import { AppTypeEnum, InjectNames, Numbers } from "ng2/common/constants/common.constant";
import { ErrorResponse } from "../common/error-response";

import { RuntimeApp } from "ng2/common/project/runtime-app";

import { BackwardCompatible } from "ng2/common/project/backward-compatible";
import { Helper } from "../common/helper";
import { DialogService } from "../common/services/dialog-service";
import { RuntimeService } from "./runtime-service";

export interface IAppLauncherStateParameter extends ng.ui.IStateParamsService {
	projectId: string;
	appType: string;
}

export class AppLauncher {
	showMashup: boolean = false;

	projectId: string;
	appType: AppTypeEnum;

	private windowResizeTimerPromise: ng.IPromise<void>;

	static $inject = [
		AngularConstants.TIMEOUT,
		AngularConstants.Q,
		InjectNames.RUNTIME_SERVICE,
		InjectNames.DIALOG_SERVICE,
	];

	constructor(
		private $timeout: ng.ITimeoutService,
		private $q: ng.IQService,
		private runtimeService: RuntimeService,
		private dialogService: DialogService
	) { }

	$onInit(): void {
		angular.element(window).bind("resize", this.windowResizeHandler.bind(this));

		this.loadProject(this.projectId).then((project: RuntimeApp) => {
			this.showMashup = true;
			this.initProjectUrlVariables(project);
		}).catch((error: ErrorResponse) => {
			console.error("[RuntimeDirective] Failed to load project with id: " + this.projectId);
			this.dialogService.showError("Load Project Error", error.message);
		});
	}

	$onDestroy(): void {
		angular.element(window).unbind("resize");
	}

	private initProjectUrlVariables(project: RuntimeApp): void {
		if (this.appType === AppTypeEnum.APPHUB) {
			const runtimeManifest = project.getManifest();
			BackwardCompatible.tryUpdateUrlName(runtimeManifest);
			const myList = Helper.getListByRegex("\(SETVARVALUES((.*))\)", runtimeManifest.urlName);
			this.runtimeService.setCurrentUrlValues(myList);
		}
	}

	private loadProject(id: string): ng.IPromise<RuntimeApp> {
		let promise: ng.IPromise<RuntimeApp>;
		switch (this.appType) {
			case AppTypeEnum.ADMIN_PREVIEW:
				promise = this.runtimeService.loadPublishedProjectNoConstraint(id, this.getCanvasWidth());
				break;
			case AppTypeEnum.RUNTIME:
			case AppTypeEnum.APPHUB:
				promise = this.runtimeService.loadPublishedProject(id, this.getCanvasWidth());
				break;
			case AppTypeEnum.DESIGNER_PREVIEW:
				promise = this.runtimeService.loadRuntimeProject(id, this.getCanvasWidth());
				break;
			default:
				const deferredError = this.$q.defer<any>();
				deferredError.reject({
					message: "Error",
					detail: "Invalid appType: " + this.appType
				});
				promise = deferredError.promise;
				break;
		}
		return promise;
	}

	private windowResizeHandler(): void {
		if (this.windowResizeTimerPromise) {
			this.$timeout.cancel(this.windowResizeTimerPromise);
		}
		this.windowResizeTimerPromise = this.$timeout(() => {
			this.updateCurrentBoundary();
		}, Numbers.FIFTY);
	}

	private updateCurrentBoundary(): void {
		this.runtimeService.updateCurrentBoundary(this.getCanvasWidth());
	}

	private getCanvasWidth(): number {
		return window.innerWidth;
	}

	static add(m: ng.IModule): void {
		m.component("eirAppLauncher", {
			template: "<eir-runtime-canvas ng-if='eirRuntimeCtrl.showMashup'></eir-runtime-canvas>",
			bindings: {
				appType: "@",
				projectId: "@"
			},
			controller: AppLauncher,
			controllerAs: "eirRuntimeCtrl"
		});
	}
}
