import {
	IJsonComponent,
	IJsonComponentSettings
} from "ng2/common/model/databearer/components/json-component.model";
import { IJsonValueSource } from "ng2/common/model/databearer/json-binding.model";
import { IMap } from "ng2/common/model/imap.model";

export interface IJsonTextBox extends IJsonComponent {
	text: IJsonValueSource;
	label: IJsonValueSource;
	placeholder: IJsonValueSource;
	showLabel: IJsonValueSource; // Deprecated
	mask: IJSonMaskSettings;
	boundarySettings: IMap<IJsonTextBoxSettings>;
}

export interface IJsonTextBoxSettings extends IJsonComponentSettings {
	boxWidth: string;
	showLabel: boolean;
}

export interface IJSonMaskSettings {
	process: string;
	allowNegative?: boolean;
	integerLimit?: number;
	decimalLimit?: number;
	allowThousandsSeparator?: boolean;
	prefix?: string;
	format?: string; //for date & string
}
