import { IArrangeEvent } from "common/model/sohoxi/sohoxi.model";

export class ArrangeDirective implements ng.IDirective {
	private static readonly DIRECTIVE_NAME = "xiArrange";
	restrict = "A";
	scope = false;

	link(
		scope: ng.IScope,
		element: ng.IAugmentedJQuery,
		attributes: ng.IAttributes,
	): void {

		if (element.length > 0) {
			this.$timeout(() => {
				//DOM has finished rendering
				const arrangeElement = element as any;
				arrangeElement.arrange().off("arrangeupdate");
				arrangeElement.arrange().on("arrangeupdate", (e: any, event: IArrangeEvent) => {
					const controllScope = scope as any;
					controllScope[attributes.xiArrange].onArrange(event);
				});

				const unbindWatcher = scope.$watch(
					function () { return element[0].childNodes.length; },
					function (newValue, oldValue) {
						if (newValue !== oldValue) {
							// DOM changes
							arrangeElement.arrange();
						}
					}
				);

				// Tear down
				arrangeElement.on("$destroy", () => {
					unbindWatcher();
				});
			});
		}
	}

	constructor(private $timeout: ng.ITimeoutService) {
	}

	static add(m: ng.IModule): void {
		m.directive(ArrangeDirective.DIRECTIVE_NAME, ($timeout: ng.ITimeoutService) => {
			return new ArrangeDirective($timeout);
		});
	}
}
