/**
 * Button Directive.
 *
 * Example:
 * <button xi-button class="btn-primary" type="button">Action</button>
 */
export class ButtonDirective implements ng.IDirective {
	public restrict = "A";
	public scope = false;

	public link(scope: ng.IScope, element: ng.IAugmentedJQuery): void {
		element.button();
	}

	public static add(m: ng.IModule): void {
		m.directive("xiButton", () => {
			return new ButtonDirective();
		});
	}
}
