import angular from "angular";
import { isNil } from "lodash";
import { AngularConstants } from "../../../../common/angular-constants";

import { IChanged, InjectNames, RegexStrings, SaveFlagsEnum, SubstitutionTags } from "ng2/common/constants/common.constant";
import { BindingTypeEnum } from "ng2/common/enums/binding-type.enum";
import { ValueTypeEnum } from "ng2/common/enums/value-type.enum";
import { IJsonBindableField, IJsonValueSource } from "ng2/common/model/databearer/json-binding.model";
import { ILanguage, IMongooseString } from "ng2/common/model/language.model";
import { IAvailableSourceField } from "../../../../common/defines";
import { Helper } from "../../../../common/helper";
import { IComponent } from "../../../../common/IComponent";
import { IAutoCompleteOptions } from "../../../../common/model/sohoxi/sohoxi-autocomplete.model";
import { ILookupOptions } from "../../../../common/model/sohoxi/sohoxi-lookup.model";
import { DialogButtonType, DialogService, IDialogOptions, IDialogResult } from "../../../../common/services/dialog-service";
import { FeatureSwitchService } from "../../../../common/services/feature-switch-service";
import { FeatureSwitchCtrl } from "../../../../common/shared-components/base/feature-switch-ctrl";
import { Datagrid } from "../../../../components/datagrid/datagrid";
import { Dropdown } from "../../../../components/dropdown/dropdown";
import { List } from "../../../../components/list/list";
import { TranslationManagementService } from "../../../../components/services/translation-management-service";
import type { IEditorComponent } from "common/interface/ieditor-component";
import { ISourceComponent, SourceComponent } from "../../defines";
import { BindingManagementService } from "../../services/binding-management-service";
import { EditorService } from "../../services/editor-service";
import { InteractionManagementService } from "../../services/interaction-management-service";
import { IEditBindingDialogParameter } from "./edit-binding-dialog";

export interface IValueSourceSettingsScope extends ng.IScope {
	targetComponent: IEditorComponent;
	createBindings: boolean;
	targetFieldMetadata: IJsonBindableField;
	valueSourceObject: IJsonValueSource;
	availableSources: ISourceComponent[];
	isContextAvailable: boolean;
	expandableSetting: boolean;
	onSave: Function;
}

export class ValuesourceSettingsCtrl extends FeatureSwitchCtrl {
	// createBindings is set to false from ParameterHandlerCtrl
	private createBindings: boolean;
	public expandableSetting: boolean;

	public htmlEncode = Helper.htmlEncode;
	public ValueTypeEnum = ValueTypeEnum;
	public BindingTypeEnum = BindingTypeEnum;
	public targetComponent: IEditorComponent;
	public isFinderActive = false;

	// all sources. dropdown 1
	public availableSources: ISourceComponent[];
	// all sources fields. dropdown 2
	public availableSourceFields: IAvailableSourceField[] = [];

	// chosen value from dropdown 1
	public settingSource: ISourceComponent;
	// chosen value from dropdown 2
	public settingSourceField: IAvailableSourceField;

	// static value in field 2
	public settingStaticValue: string | boolean;
	// resulting value from substituted string
	public filteredValue = {
		name: "",
		tooltip: ""
	};
	public onSave: Function;
	public lookupOptions: ILookupOptions;
	public autocompleteOptions: IAutoCompleteOptions;
	protected unsubscribers: Function[] = [];
	private selectedComponent: IEditorComponent;
	public isLocalized: boolean;

	public static $inject = [
		AngularConstants.SCOPE,
		AngularConstants.ELEMENT,
		AngularConstants.TIMEOUT,
		InjectNames.EDITOR_SERVICE,
		InjectNames.BINDING_MANAGEMENT_SERVICE,
		InjectNames.INTERACTION_MANAGEMENT_SERVICE,
		InjectNames.DIALOG_SERVICE,
		InjectNames.TRANSLATION_MANAGEMENT_SERVICE,
		InjectNames.FEATURE_SWITCH_SERVICE
	];

	public constructor(
		protected $scope: IValueSourceSettingsScope,
		protected $element: ng.IAugmentedJQuery,
		protected $timeout: ng.ITimeoutService,
		protected editorService: EditorService,
		private bindingManagementService: BindingManagementService,
		private interactionManagementService: InteractionManagementService,
		private dialogService: DialogService,
		private translationManagementService: TranslationManagementService,
		featureService: FeatureSwitchService
	) {
		super(featureService);
		this.createBindings = !($scope.createBindings === false);
		this.expandableSetting = !Helper.isUndefinedOrNull($scope.expandableSetting);

		this.availableSources = $scope.availableSources;
		this.targetComponent = $scope.targetComponent;
		this.onSave = $scope.onSave;
		this.isLocalized = this.$scope.targetFieldMetadata.valueType === ValueTypeEnum.LOCALIZED_STRING;
		this.selectedComponent = this.targetComponent;

		this.onComponentChanged(editorService.getSelectedComponent());

		this.unsubscribers.push(editorService.selectedDatasourceChangedEvent.on(() => {
			if (this.createBindings) { // Changeing Datasource sometimes auto-sets bindableField
				this.$scope.valueSourceObject = this.selectedComponent.getBindableField(this.$scope.targetFieldMetadata.ref);
			}
			this.onComponentChanged(editorService.getSelectedComponent());
		}), editorService.urlVariableChangedEvent.on(() => {
			this.onComponentChanged(editorService.getSelectedComponent());
			this.onSourceChanged();
		}), editorService.translationChangedEvent.on((componentTranslation) => {
			if (this.selectedComponent.getId() === componentTranslation.component
				&& this.$scope.targetFieldMetadata.ref === componentTranslation.reference) {
				this.settingStaticValue = componentTranslation.newValue;
				this.onStaticValueChanged();
			}
		}));

		$scope.$on(AngularConstants.DESTROY, () => {
			angular.forEach(this.unsubscribers, (unsubscribe) => {
				unsubscribe();
			});
		});

		//Init xi-components
		this.lookupOptions = this.translationManagementService.getLookupOptions();
		this.autocompleteOptions = this.translationManagementService.getAutoOptions();
	}

	public $onInit(): void {
		this.$timeout(() => {
			this.initInputElement();
		});
	}

	private initInputElement(): void {
		const inputElement = this.$element.find("input#staticvalue" + this.$scope.targetFieldMetadata.ref);
		inputElement.on("selected", () => {
			this.settingStaticValue = "{" + inputElement.val() + "}";
			this.onStaticValueChanged();
		});
	}

	private save(): void {
		this.onSave({ $saveTranslations: this.isLocalized });
	}

	/**
	 * Helper method to reset settingSource, and scope.valueSourceObject
	 *
	 * @private
	 *
	 * @memberOf ValuesourceSettingsCtrl
	 */
	private resetSources(): void {
		// reset setting source
		this.settingSource = SourceComponent.STATIC_VALUE_OBJECT;
		this.settingSourceField = null;

		const valueSourceObject = this.$scope.valueSourceObject;

		// remove bindings
		const oldSourceKey = valueSourceObject.sourceKey;
		if (oldSourceKey) {
			this.unregisterPossibleBindings();
		}

		// reset scope.valueSourceObject
		valueSourceObject.Type = BindingTypeEnum.STATIC_VALUE;
		valueSourceObject.value = "";

	}

	/**
	 * This method is for very long values for dropdowns in the UI
	 *
	 * @public
	 * @param {string} currentValue
	 * @returns {string}
	 * @memberOf ValuesourceSettingsCtrl
	 */
	public getExampleData(currentValue: string): string {
		const maxLength = 30;
		return currentValue.length > maxLength ? currentValue.substr(0, maxLength) + "..." : currentValue;
	}

	public getTypedef(): string {
		return this.selectedComponent.getTypedef();
	}

	/**
	 * Method to open the translation dialog
	 *
	 * @public
	 *
	 * @memberOf ValuesourceSettingsCtrl
	 */
	public openTranslations(): void {
		const propertyName = this.$scope.targetFieldMetadata.ref;
		this.translationManagementService.openTranslations(this.selectedComponent, propertyName).then((result) => {
			const okButton = result.button === DialogButtonType.Ok;
			const data = result.value as IChanged;
			const selectedChanged = data.savedComponentStrings.filter((translation) => {
				return translation.component === this.selectedComponent.getId();
			});
			if (okButton && selectedChanged) {
				selectedChanged.forEach((updatedTranslation) => {
					if (updatedTranslation.reference === this.$scope.targetFieldMetadata.ref) {
						this.settingStaticValue = updatedTranslation.newValue;
						this.onStaticValueChanged();
					}
				});

				// if selected component changed add undo
				this.editorService.addUndoStack(this.editorService.getCurrentProject());
			}
			if (okButton) {
				if (data.savedComponentStrings.length > 0) {
					data.savedComponentStrings.forEach((value) => {
						const component = this.editorService.getComponentInstanceById(value.component);
						component.forEachBindable((binding) => {
							if (binding.ref === value.reference) {
								const field = component.getBindableField(value.reference);
								field.value = value.newValue;
							}
						});
					});
				}
				this.editorService.updateTranslatables();
			}

			//If global translations is changed
			if (okButton && !angular.equals({}, data.savedGlobalStrings)) {
				this.translationManagementService.saveGlobalTranslations(data.savedGlobalStrings).then(() => {
					this.editorService.updateTranslatables();
				});
			}
			//If global translations is deleted
			if (okButton && !angular.equals({}, data.deletedGlobalStrings)) {
				this.translationManagementService.deleteGlobalTranslations(result.value.deletedGlobalStrings).then(() => {
					this.editorService.updateTranslatables();
				});
			}
			if (okButton) {
				this.save();
			}
		});
	}

	public useFinder(componentToSet: string): void {
		this.isFinderActive = true;
		this.editorService.startFinderMode({ title: "Select Component" })
			.then((component: IEditorComponent) => {
				const typedef = component.getTypedef();
				if ((component.isLayoutComponent() ||
					typedef === Dropdown.name ||
					typedef === List.name ||
					typedef === Datagrid.name) &&
					isNil(component.getDatasource())) {
					const title = "Error";
					const message = "The selected " + typedef.toLowerCase() + " component is empty and does not have any data to bind from.\n" +
						"Please select another component or attach a data service to the selected component.";
					this.dialogService.showError(title, message);
				} else {
					if (componentToSet === "parameterSource") {
						this.settingSource = this.createSettingSource(component);
						this.onSourceChanged();
					}
				}
			}).finally(() => {
				this.isFinderActive = false;
			});
	}

	protected onComponentChanged(newComponent: IEditorComponent): void {
		const valueSourceObject = this.$scope.valueSourceObject;
		const targetContextComponent = this.targetComponent ? this.targetComponent : this.selectedComponent;
		const targetFieldMetadata = this.$scope.targetFieldMetadata;

		if (targetContextComponent) {
			if ((valueSourceObject && targetFieldMetadata && targetFieldMetadata.valueType !== ValueTypeEnum.BOOLEAN) ||
				!targetFieldMetadata
			) {
				const bindingType = valueSourceObject.Type;
				this.setSettingSource(bindingType, targetContextComponent);
			}
		}
	}

	private setSettingSource(bindingType: BindingTypeEnum, component: IEditorComponent): void {
		const bindingValueValid = (value: string) => {
			// Fallback for old projects that (unnessesarily) saved current value in the "value"-property
			if (value && value.indexOf(SubstitutionTags.VALUE) > -1) {
				return true;
			}
			return false;
		};
		const valueSourceObject = this.$scope.valueSourceObject;
		switch (bindingType) {
			case BindingTypeEnum.STATIC_VALUE:
				this.settingSource = SourceComponent.STATIC_VALUE_OBJECT;
				this.settingSourceField = null;
				this.settingStaticValue = valueSourceObject.value;
				break;
			case BindingTypeEnum.BINDING:
				if (!bindingValueValid(valueSourceObject.value)) {
					valueSourceObject.value = SubstitutionTags.VALUE;
				}
				const sourceComponentInstance =
					this.editorService.getComponentInstanceById(valueSourceObject.sourceName);
				if (sourceComponentInstance) {
					this.settingSource = this.createSettingSource(sourceComponentInstance);
					sourceComponentInstance.getBindingMetaData().then((availableBindings) => {
						this.setAvailableBindings(availableBindings);
						this.setSelectedBinding(valueSourceObject.sourceKey);
						this.updateBinding(valueSourceObject.sourceKey, valueSourceObject.value);
						this.updatePreviewValue();
					});
				} else {// If the target component has been removed, go to static mode
					this.resetSources();
				}
				break;
			case BindingTypeEnum.CONTEXT_BINDING:
				if (!bindingValueValid(valueSourceObject.value)) {
					valueSourceObject.value = SubstitutionTags.VALUE;
				}
				this.settingSource = SourceComponent.TARGET_CONTEXT_OBJECT;
				// MUPS-488 Should be applied here, optimize by getting contextMetaData in attribute-settings/parameterHandler
				component.getContextMetaData().then((availableBindings) => {
					this.setAvailableBindings(availableBindings);
					this.setSelectedBinding(valueSourceObject.sourceKey);
					this.updateBinding(valueSourceObject.sourceKey, valueSourceObject.value);
					this.updatePreviewValue();
				});
				break;
			case BindingTypeEnum.URL_BINDING: {
				this.settingSource = SourceComponent.URL_VALUE_OBJECT;
				const availableBindings = this.editorService.getUrlMetaData();
				this.setAvailableBindings(availableBindings);
				this.setSelectedBinding(valueSourceObject.sourceKey);
				this.updatePreviewValue();
				this.settingStaticValue = valueSourceObject.value;
				break;
			}
			default:
				console.warn(
					"[ValuesourceSettingsDirective] Unknown binding type", valueSourceObject.Type);

				// elements needs to update because sometimes the dropdown can't be opened.
				angular.element("#srckey" + this.$scope.targetFieldMetadata.ref).data("dropdown").updated();
				angular.element("#srcctrl" + this.$scope.targetFieldMetadata.ref).data("dropdown").updated();
		}
	}

	private setAvailableBindings(availableBindings: IAvailableSourceField[]): void {
		this.availableSourceFields = availableBindings;
	}

	/**
	 * Creates source setting
	 *
	 * @private
	 * @param {IComponent} componentInstance
	 * @returns {ISourceComponent}
	 *
	 * @memberOf ValuesourceSettingsCtrl
	 */
	private createSettingSource(componentInstance: IComponent): ISourceComponent {
		const settingSource = {
			id: componentInstance.getId(),
			name: componentInstance.getDisplayName(),
			isComponent: true
		};
		return settingSource;
	}

	/**
	 * Sets selected binding if any
	 *
	 * @private
	 * @param {string} sourceKey
	 *
	 * @memberOf ValuesourceSettingsCtrl
	 */
	private setSelectedBinding(sourceKey: string): void {
		if (sourceKey) {
			this.settingSourceField = this.availableSourceFields.find(sourceField =>
				sourceField.sourceKey.toLowerCase() === sourceKey.toLowerCase());

			if (!this.settingSourceField) {
				this.settingSourceField = this.availableSourceFields[0];
			}
		}
	}

	public onSourceChanged(): void {
		const valueSourceObject = this.$scope.valueSourceObject;
		const settingSource = this.settingSource;

		this.unregisterPossibleBindings();

		// Reset all available resources and
		this.availableSourceFields = [];
		this.settingSourceField = null;
		valueSourceObject.value = "";

		if (settingSource.isComponent) {
			valueSourceObject.Type = BindingTypeEnum.BINDING;
			const componentInstance = this.editorService.getComponentInstanceById(settingSource.id);
			this.setAvailableBindingsByComponentMetaData(componentInstance);

		} else { // Local context,static value or Url value selected
			valueSourceObject.sourceName = null;
			valueSourceObject.sourceKey = null;

			switch (settingSource.id) {
				case SourceComponent.STATIC_VALUE_OBJECT.id:
					valueSourceObject.Type = BindingTypeEnum.STATIC_VALUE;
					this.onStaticValueChanged();
					break;
				case SourceComponent.URL_VALUE_OBJECT.id:
					valueSourceObject.Type = BindingTypeEnum.URL_BINDING;
					const availableBindings = this.editorService.getUrlMetaData();
					this.availableSourceFields = availableBindings;
					this.settingSourceField = availableBindings[0];
					if (this.settingSourceField) {
						this.onSourceFieldChanged();
						this.updateCurrentSource();
					} else {
						this.settingStaticValue = "";
					}
					break;
				default: //case SourceComponent.TARGET_CONTEXT_OBJECT.id:
					valueSourceObject.Type = BindingTypeEnum.CONTEXT_BINDING;
					const contextComponent = this.targetComponent ? this.targetComponent : this.selectedComponent;

					contextComponent.getContextMetaData().then((availableBindings: IAvailableSourceField[]) => {
						this.availableSourceFields = availableBindings;
						this.settingSourceField = availableBindings[0];
						this.onSourceFieldChanged();
					});
			}
		}
	}

	public onUrlValueChanged(): void {
		const valueSourceObject = this.$scope.valueSourceObject;
		if (this.settingStaticValue !== valueSourceObject.value) {
			const urlVariables = this.editorService.getUrlMetaData();
			const myUrlVariable = urlVariables.find((urlVariable) => {
				return urlVariable.sourceKey === valueSourceObject.sourceKey;
			});
			myUrlVariable.currentValue = this.settingStaticValue;
			this.editorService.setUrlMetaData(urlVariables);
			valueSourceObject.value = this.settingStaticValue;

			// Update potential bindings on all components/interactions
			this.updateBindingValues();
			this.interactionManagementService.updateAndSaveParameters(
				myUrlVariable.sourceKey,
				this.settingStaticValue as string
			);
			this.editorService.initializeUrlBindings(myUrlVariable.currentValue, myUrlVariable.sourceKey);

			//Save without translations
			this.editorService.saveCurrent(SaveFlagsEnum.CANVAS_COMPONENTS);
		}
	}

	public openUrlDialog(): void {
		this.editorService.openURLDialog();
	}

	private setAvailableBindingsByComponentMetaData(component: IEditorComponent): void {
		component.getBindingMetaData().then((availableBindings) => {
			if (availableBindings.length) {
				this.availableSourceFields = availableBindings;
				this.settingSourceField = availableBindings[0];
				this.onSourceFieldChanged();
			} else { // If it's a component with no available bindings, set to static
				this.resetSources();
			}
		});
	}

	/**
	 * For updating the current source to correct one
	 *
	 * @private
	 * @memberof ValuesourceSettingsCtrl
	 */
	private updateCurrentSource(): void {
		const valueSourceObject = this.$scope.valueSourceObject;
		valueSourceObject.sourceName = this.settingSource.id;
		valueSourceObject.sourceKey = this.settingSourceField.sourceKey;
	}

	private unregisterPossibleBindings(): void {
		this.bindingManagementService.unregisterBinding(
			this.selectedComponent,
			this.$scope.targetFieldMetadata.ref,
			this.$scope.valueSourceObject
		);
		this.bindingManagementService.unregisterContextBinding(
			this.selectedComponent,
			this.$scope.valueSourceObject.sourceKey,
			this.$scope.targetFieldMetadata.ref
		);

		//Removing the sourceKey on potential URL-binding
		this.$scope.valueSourceObject.sourceKey = null;
	}

	public onSourceFieldChanged(): void {
		const newSourceField = this.settingSourceField;
		const valueSourceObject = this.$scope.valueSourceObject;
		const oldSourceKey = this.$scope.valueSourceObject.sourceKey;
		const targetKey = this.$scope.targetFieldMetadata.ref;

		if (oldSourceKey && this.createBindings) {
			this.unregisterPossibleBindings();
		}

		if (newSourceField) {
			const isContextBinding = valueSourceObject.Type === BindingTypeEnum.CONTEXT_BINDING;
			const isBinding = valueSourceObject.Type === BindingTypeEnum.BINDING;
			const isUrlBinding = valueSourceObject.Type === BindingTypeEnum.URL_BINDING;
			if (isContextBinding || isBinding) {
				if (valueSourceObject.value === "") {
					valueSourceObject.value = SubstitutionTags.VALUE;
				}
				// Special fix for List. Should be removed once List is refactored.
				const generatingParent = this.selectedComponent.getGeneratingParent() as IComponent;
				if (generatingParent.getTypedef() === List.name) {
					const parentListComponent = generatingParent as List;
					parentListComponent.refresh();
				}
				this.updateBinding(targetKey, valueSourceObject.value);
			}
			if (isUrlBinding) {
				if (newSourceField.currentValue) {
					this.settingStaticValue = newSourceField.currentValue;
				} else {
					this.settingStaticValue = "";
				}
				this.updateCurrentSource();
				this.onUrlValueChanged();
			}
			if (isContextBinding) {
				valueSourceObject.visibleName = newSourceField.visibleName.replace("(" + newSourceField.sourceKeyShort + ")", "");
			}
			this.save();
		}
	}

	private updateBinding(targetKey: string, value: string): void {
		this.updateCurrentSource();
		this.updateBindingValues();
		if (!this.selectedComponent.isGenerated &&
			this.selectedComponent.isTranslatableComponent()
		) {
			this.setTranslation(this.selectedComponent.getId(), targetKey, value);
		}
	}

	public onStaticValueChanged(): void {
		this.$scope.valueSourceObject.value = this.settingStaticValue;
		const property = this.$scope.targetFieldMetadata.ref;

		if (typeof this.settingStaticValue === "string") {
			let exists = true;
			const trimmedValue = this.settingStaticValue.match(RegexStrings.GLOBALSTRING);
			if (trimmedValue && trimmedValue[1]) {
				if (trimmedValue[0] !== SubstitutionTags.VALUE) {
					exists = this.checkGlobalValue(trimmedValue[1]);
				}
			}

			if (exists || !trimmedValue) {
				this.updateSave();
			}
		} else {
			this.updateSave();
		}
	}

	private checkGlobalValue(value: string): boolean {
		let exists = true;
		const globalValue = this.translationManagementService.getGlobalString(value);
		if (!globalValue) {
			exists = false;
			angular.element("#autocomplete-list").hide();
			this.createNewString(value).then((result) => {
				if (result.button === DialogButtonType.Ok) {
					this.updateSave();
					this.editorService.updateTranslatables();
				}
			});
		}
		return exists;
	}

	private updateSave(): void {
		this.updateBindingValues();
		this.save();
	}

	private createNewString(value: string): ng.IPromise<IDialogResult<IMongooseString>> {
		return this.translationManagementService.openNewStringDialog(value).then((result) => {
			if (result.button === DialogButtonType.Ok) {
				this.settingStaticValue = "{" + result.value.Name + "}";
				this.translationManagementService.addGlobalString(result.value);
			}
			return result;
		});
	}

	private updatePreviewValue(): void {
		const valueSourceObject = this.$scope.valueSourceObject;
		const value = valueSourceObject.value;
		const isContextBinding = valueSourceObject.Type === BindingTypeEnum.CONTEXT_BINDING;
		const isBinding = valueSourceObject.Type === BindingTypeEnum.BINDING;
		if (isContextBinding || isBinding) {
			// If value is falsy or only contains substitution tag, default to ""
			if (!value || value === SubstitutionTags.VALUE) {
				this.filteredValue = {
					name: "",
					tooltip: ""
				};
			} else {
				this.filteredValue.name = Helper.joinWithDefauldEmptyString(
					valueSourceObject.value.split(SubstitutionTags.VALUE),
					this.settingSourceField.currentValue
				);
				this.filteredValue.tooltip = Helper.htmlEncode(this.filteredValue.name);
			}
		}
	}

	private updateBindingValues(): void {
		const valueSourceObject = this.$scope.valueSourceObject;
		const metadata = this.$scope.targetFieldMetadata;
		const selectedComponent = this.selectedComponent;

		this.updatePreviewValue();
		// dont register if valueSource is on Event Parameter
		if (this.createBindings) {
			if (selectedComponent.isGenerated) {
				const generatingParent = selectedComponent.getGeneratingParent() as IComponent;
				if (generatingParent.getTypedef() === List.name) {
					const parentListComponent = generatingParent as List;
					this.editorService.forEachComponent(parentListComponent.currentTemplate, (subComponent) => {
						if (subComponent.id === selectedComponent.getId()) {
							(subComponent as any)[metadata.ref] = valueSourceObject;
							// TODO: break
						}
					});
				}
			} else {
				switch (valueSourceObject.Type) {
					case BindingTypeEnum.BINDING:
						if (metadata.valueType === ValueTypeEnum.LOCALIZED_STRING) {
							this.setTranslation(selectedComponent.getId(), metadata.ref, valueSourceObject.value);
						}
						this.bindingManagementService.registerBinding(
							selectedComponent,
							metadata.ref,
							valueSourceObject
						);
						break;
					case BindingTypeEnum.CONTEXT_BINDING:
						if (metadata.valueType === ValueTypeEnum.LOCALIZED_STRING) {
							this.setTranslation(selectedComponent.getId(), metadata.ref, valueSourceObject.value);
						}
						this.bindingManagementService.registerContextBinding(
							selectedComponent,
							metadata.ref,
							valueSourceObject
						);
						break;
					case BindingTypeEnum.URL_BINDING:
						selectedComponent.setBindableValue(metadata.ref,
							this.editorService.getUrlValue(valueSourceObject.sourceKey));
						break;
					case BindingTypeEnum.STATIC_VALUE:
					default:
						if (metadata.valueType === ValueTypeEnum.LOCALIZED_STRING) {
							this.setTranslation(selectedComponent.getId(), metadata.ref, this.settingStaticValue as string);
						}
						selectedComponent.setBindableValue(metadata.ref, this.settingStaticValue);
				}
				// if selected component value is changed, add undo.
				this.editorService.addUndoStack(this.editorService.getCurrentProject());
			}
		}
	}

	private setTranslation(componentId: string, reference: string, value: string): void {
		const currentLanguageKey = this.translationManagementService.getSelectedLocale();
		const newValue: ILanguage = {
			locale: currentLanguageKey,
			value: value
		};
		this.translationManagementService.setTranslatableStringByProperty(componentId, reference, newValue);
	}

	public editBinding(): void {
		const valueSourceObject = this.$scope.valueSourceObject;
		const dialogOptions: IDialogOptions<IEditBindingDialogParameter> = {
			template: require("./edit-binding-dialog.html"),
			title: "Edit binding",
			cssClass: "edit-binding-dialog",
			parameter: {
				bindingString: valueSourceObject.value,
				valueType: this.$scope.targetFieldMetadata.valueType,
				property: this.$scope.targetFieldMetadata.ref
			}
		};
		this.dialogService.show(dialogOptions).then((result) => {
			const buttonOK = result.button === DialogButtonType.Ok;
			const isValueChanged = valueSourceObject.value !== result.value;
			if (buttonOK && isValueChanged) {
				valueSourceObject.value = result.value;
				this.updateBindingValues();
				this.save();
			}
		});
	}
}

export class ValuesourceSettingsDirective {
	public static add(m: ng.IModule): void {
		m.directive("eirValuesourceSettings", () => {
			return {
				template: require("./valuesource-settings.html"),
				scope:
				{
					targetComponent: "=?",
					targetFieldMetadata: "=",
					valueSourceObject: "=",
					availableSources: "=",
					createBindings: "=?",
					expandableSetting: "<?",
					onSave: "&"
				},
				controller: ValuesourceSettingsCtrl,
				controllerAs: "eirValuesourceSettingsCtrl"
			};
		});
	}
}
