import type {
	IJsonDesignerManifest,
	IJsonRuntimeManifest,
} from "ng2/common/model/databearer/json-manifest.model";
import type { IJsonTag } from "ng2/common/model/databearer/json-tag.model";

import { DesignerManifest } from "ng2/common/project/manifest/designer-manifest";
import { RuntimeManifest } from "ng2/common/project/manifest/runtime-manifest";

export interface IProjectInfo {
	manifest: IJsonRuntimeManifest | IJsonDesignerManifest;
	tags?: IJsonTag[];
}
export class ProjectInfo {

	manifest: IJsonDesignerManifest | IJsonRuntimeManifest;
	tags: IJsonTag[] = [];

	constructor(projectInfo: IProjectInfo) {
		if (projectInfo.manifest instanceof DesignerManifest) {
			this.manifest = new DesignerManifest(projectInfo.manifest);
		} else {
			this.manifest = new RuntimeManifest(projectInfo.manifest as IJsonRuntimeManifest);
		}

		this.tags = projectInfo.tags;
	}
}
