import { IDataPair } from "ng2/common/model/data-pair.model";
import { IDesignerManifest } from "ng2/common/model/project/mashup/designer-manifest.model";

export class FilterToolbarComponent {

	// component bindings
	public filterTarget: IDesignerManifest[];
	public filterTypeOptions: IDataPair[];
	public sortPropertiesOptions: IDataPair[];
	public localPrefix: string;

	static add(m: ng.IModule): void {
		m.component("eirFilterToolbarComponent", {
			bindings: {
				filterTypeOptions: "<",
				sortPropertiesOptions: "<",
				filterTarget: "=",
				localPrefix: "@"
			},
			controller: FilterToolbarComponent,
			controllerAs: "ctrl",
			template: require("./filter-toolbar.html"),
		});
	}
}
