import { AngularConstants } from "../../../../common/angular-constants";

import { InjectNames, RegexStrings } from "ng2/common/constants/common.constant";
import { IComponentRendered, SelectableEnum } from "ng2/common/constants/sohoxi.constant";

import { IDialogBaseScope } from "../../../../common/defines";
import { ErrorResponse } from "../../../../common/error-response";
import { Helper } from "../../../../common/helper";
import { IListViewApi, IListViewOptions } from "../../../../common/model/sohoxi/sohoxi-listview.model";
import { DialogButtonType, DialogService, IDialogOptions, IDialogResult } from "../../../../common/services/dialog-service";
import { FeatureSwitchService } from "../../../../common/services/feature-switch-service";
import { IValidationResult, IValidatorError } from "../../../../common/shared-components/base/field-validation";
import { IOnComponentChangeNotification } from "../../../../common/shared-components/base/notifiable-component";
import { LISTVIEW_RENDERED } from "../../../../common/sohoxi/component/listview-directive";
import { LocalIdoService } from "../../../../components/services/local-ido-service";
import { IWizardStep, WizardCtrl } from "../../../editor/wizard/wizard-ctrl";
import { RepositoryService } from "../../repository-service";
import { CollectionData, CollectionProperty } from "../collection-data";

export class IdoValidator {
	static isValidName(value: string): IValidationResult {
		if (value.indexOf(" ") !== -1) {
			return { status: false, errorMessage: "Name cannot contain spaces" };
		} else if (RegexStrings.SPECIAL_CHARACTERS.test(value)) {
			return { status: false, errorMessage: "Name cannot contain special characters" };
		} else if (RegexStrings.STARTS_WITH_NUMBER.test(value)) {
			return { status: false, errorMessage: "Name cannot begin with a number" };
		}

		return { status: true };
	}

	static isUniqueName(value: string, collections: string[]): IValidationResult {
		const isDuplicateName = (value: string): boolean => {
			return collections
				.find(item => item.toLowerCase() === value.toLowerCase()) !== undefined;
		};

		if (isDuplicateName(value)) {
			return {
				status: false, errorMessage: `The name '${value}' is already used by an existing IDO.` };
		}

		return { status: true };
	}
}

export class IdoWizardCtrl extends WizardCtrl {
	steps: IWizardStep[] = [
		{
			id: "ido-wizard-basic.html",
			title: "Name & Description",
			userTip: "",
			template: require("./ido-wizard-basic.html")
		},
		{
			id: "ido-wizard-properties.html",
			title: "Create Properties",
			userTip: "",
			template: require("./ido-wizard-properties.html")
		},
		{
			id: "ido-wizard-summary.html",
			title: "Summary",
			userTip: "",
			template: require("./ido-wizard-summary.html")
		},
	];

	static $inject = [
		AngularConstants.SCOPE,
		AngularConstants.ELEMENT,
		AngularConstants.TEMPLATE_CACHE,
		InjectNames.DIALOG_SERVICE,
		InjectNames.FEATURE_SWITCH_SERVICE,
		InjectNames.LOCAL_IDO_SERVICE,
		InjectNames.REPOSITORY_SERVICE,
	];

	get isCurrentPropertyValid(): boolean {
		const currentProperty = this.selectedProperty;

		const isCurrentUndefined = Helper.isUndefined(currentProperty);
		if (isCurrentUndefined) {
			return isCurrentUndefined;
		}

		// check mandatory fields are set and errorList is empty
		let invalidProperty = this.verifyErrorList() ||
			(Helper.isUndefinedEmpty(currentProperty.dataType) ||
			Helper.isUndefinedEmpty(currentProperty.propertyLength) ||
			Helper.isUndefinedEmpty(currentProperty.name));

		// check if data type is 'date' then property is valid without property length
		if (invalidProperty &&
			!Helper.isUndefinedEmpty(currentProperty.dataType) &&
			currentProperty.dataType === "Date" &&
			!Helper.isUndefinedEmpty(currentProperty.name)
		) {
			invalidProperty = false;
		}

		// check mandatory fields are set for dataType 'Decimal'
		if (!invalidProperty &&
			!Helper.isUndefinedEmpty(currentProperty.dataType) &&
			currentProperty.dataType === "Decimal" &&
			Helper.isUndefinedEmpty(currentProperty.decimalLength)
		) {
			invalidProperty = true;
		}

		return !invalidProperty;
	}

	private verifyErrorList(): boolean {
		return this.errorList
			.filter(item => typeof item !== "boolean").length !== 0;
	}

	get addPropertyToolTip(): string {
		if (this.verifyErrorList()) {
			return "Correct errors in current property before adding new.";
		}
		return this.isCurrentPropertyValid ?
			"Add Property" :
			"You are missing one or more mandatory fields. Complete before adding new";
	}

	get ErrorList(): any[] {
		this.setNextDisabled();
		this.setErrorToListItem();
		return this.errorList;
	}

	private setErrorToListItem(): void {
		this.collection.properties.forEach(property => {
			delete property.alert;
			delete property.alertClass;
		});
		this.errorList.forEach((error: IValidatorError) => {
			const propertyFound = this.collection.properties
				.find(property => property.id === error.id);
			if (propertyFound) {
				propertyFound.alert = true;
				propertyFound.alertClass = "error";
			}
		});
	}

	private setNextDisabled(): void {
		const hasCollectionPrimaryProperty = () => {
			return this.collection.properties
				.some(property => property.primaryKey);
		};

		const currentStepTitle = this.currentStep.title;
		let isDisabled: boolean;

		if (currentStepTitle === this.steps[0].title) {
			isDisabled = Helper.isUndefinedEmpty(this.collection.name) ||
				Helper.isUndefinedEmpty(this.collection.idoProject) ||
				Helper.isUndefinedEmpty(this.collection.tableAlias);
		} else if (currentStepTitle === this.steps[1].title) {
			isDisabled = this.collection.properties.length === 0 ||
				!hasCollectionPrimaryProperty() ||
				!this.isCurrentPropertyValid;
		}

		this.nextDisabled = isDisabled || this.errorList.length !== 0;
	}

	collection: CollectionData;
	listviewOptions: IListViewOptions;
	listviewId = "properties-listview";
	selectedProperty: CollectionProperty;
	idoProjects: string[] = [];

	private listApi: IListViewApi;
	private idoCollectionUsedToValidateNameIsUnique: string[];

	constructor(
		$scope: IDialogBaseScope<any>,
		$element: ng.IAugmentedJQuery,
		$templateCache: ng.ITemplateCacheService,
		private dialogService: DialogService,
		featureService: FeatureSwitchService,
		private localIdoService: LocalIdoService,
		private repositoryService: RepositoryService,

	) {
		super(
			$scope,
			$element,
			$templateCache,
			featureService,
		);
		this.initWizard();
		this.collection = {
			properties: []
		} as any;

		this.initIdoWizard();
		this.$element.off()
			.on(
				LISTVIEW_RENDERED,
				(event, result: IComponentRendered) => {
					if (result.id === this.listviewId) {
						this.listApi = result.api;
						this.listApi.element.on("click",
							(event: JQueryEventObject, selectedItem: any) => {
								if (this.selectedProperty.id !== selectedItem.data.id) {
									$scope.$applyAsync(() => {
										this.selectedProperty = selectedItem.data;
									});
								}
							});
					}
				});
	}

	getIdoProjects(): ng.IPromise<string[]> {
		return this.localIdoService.getIdoProjectList()
			.then(result => {
				return result.map(item => item.serverName);
			});
	}

	add(): void {
		const newProperty = {
			id: Helper.generateUniqueId()
		};
		this.collection.properties.push(newProperty);
		this.selectedProperty = newProperty;
	}

	delete(): void {
		const deleteRow = (id: string): number => {
			const index = this.collection.properties
				.findIndex(item => item.id === id);

			// delete entry
			this.collection.properties.splice(index, 1);

			return index;
		};

		const setNextSelectedRow = (index: number) => {
			let nextEntryIndex = index - 1;
			if (nextEntryIndex === -1) {
				nextEntryIndex = 0;
			}

			if (this.collection.properties.length === 0) {
				this.selectedProperty = undefined;
			} else {
				this.selectedProperty =
					this.collection.properties[nextEntryIndex];
			}
		};

		const selectedRows = this.listApi.getSelected();
		let index: number;

		this.dialogService
			.showYesNo(
				"Delete Property",
				`Do you want to delete ${Helper.isUndefinedEmpty(this.selectedProperty.name) ?
					"the selected property" :
					"'" + this.selectedProperty.name + "'"}?`)
			.then(result => {
				if (result.button === DialogButtonType.Yes) {
					if (selectedRows.length > 1) { // multiple delete
						selectedRows.each((_, row) => {
							index = deleteRow(row.id);
						});
					} else if (selectedRows.length === 1 || this.selectedProperty) {
						index = deleteRow(this.selectedProperty.id);
					}
					setNextSelectedRow(index);

					this.setNextDisabled();
				}
			});
	}

	preview(): void {
		const option: IDialogOptions<CollectionProperty[]> = {
			title: "Preview IDO Properties",
			template: require("./preview-dialog/preview-template.html"),
			parameter: this.collection.properties,
			cssClass: "wizard-dialog large"
		};

		this.dialogService.show(option);
	}

	openTypeHelp = this.showTypeHelp.bind(this);

	showTypeHelp(): void {
		// tslint:disable:max-line-length
		const message =
			"<b>Boolean</b> - Used for True(1) or False(0) values. <br><br>" +
			"<b>Date</b> -  Used for date and/or time values. <br><br>" +
			"<b>Decimal</b> - Used for decimal number values that have a fixed precision and scale. Valid precision values are 1 to 38. <br><br>" +
			"<b>File</b> - Used for Binary Large OBject (BLOB) data, such as image or audio files. <br><br>" +
			"<b>Integer</b> - Used for integer values. <br><br>" +
			"<b>String</b> - Used for text strings. Valid characters include all alphanumeric characters such as #, %, & and so on. ";
		// tslint:enable:max-line-length

		const showOptions = {
			message: message,
			title: "Data Type Help",
		};
		this.dialogService.showMessage(showOptions);
	}

	isValidName(
		value: string
	): IValidationResult {
		return IdoValidator.isValidName(value);
	}

	isUniqueName(
		value: string
	): IValidationResult {
		return IdoValidator
			.isUniqueName(value, this.idoCollectionUsedToValidateNameIsUnique);
	}

	protected onChangeCurrentStep(): void {
		this.errorList.length = 0;
	}

	protected getFinishParameter(): CollectionData {
		this.collection.tableName = this.collection.name;

		return this.collection;
	}

	private initIdoWizard(): void {
		this.nextDisabled = true;
		this.initIdoProjectDropdown();
		this.initListviewOptions();
		this.loadIdoCollections();
	}

	private initIdoProjectDropdown(): void {
		this.getIdoProjects().then(result => {
			this.$scope
				.$applyAsync(() => this.idoProjects = result);
		});
	}

	private initListviewOptions(): void {
		const template =
		`<ul>
			{{#dataset}}
				{{#alert}}
				<li class="{{alertClass}}">
					<p class="listview-heading">{{name}}</p>
					<p class="listview-micro">{{dataType}}</p>
				</li>
				{{/alert}}
				{{^alert}}
				<li id="{{id}}">
					<p class="listview-heading">{{name}}</p>
					<p class="listview-micro">{{dataType}}</p>
				</li>
				{{/alert}}
			{{/dataset}}
		</ul>`;

		this.listviewOptions = {
			id: this.listviewId,
			dataset: this.collection.properties,
			template,
			selectable: SelectableEnum.Mixed,
			showCheckboxes: false,
			selectOnFocus: false,
		};
	}

	private loadIdoCollections(): void {

		this.localIdoService.getIdoCollectionList()
			.then(result => {
				this.idoCollectionUsedToValidateNameIsUnique =
					result.map(collection => collection.collectionName);
			});
	}

	private setTableAlias(idoName: string): void {
		let alias: string;
		if (idoName) {
			const shortHand = idoName.replace(/[a-z]/g, "");
			if (shortHand) {
				alias = shortHand.slice(0,3) + "_AB";
			} else {
				alias = idoName.slice(0, 3) + "_AB";
			}
		}
		this.collection.tableAlias = alias;
	}

	sharedErrors: any;

	getValidation($data: IOnComponentChangeNotification): IValidationResult {
		let result: IValidationResult;
		switch ($data.validator) {
			case "isValidName":
				result = this.isValidName($data.value);
				break;
			case "isUniqueName":
				result = this.isUniqueName($data.value);
				break;
			default:
		}

		return result;
	}

	notify($data: IOnComponentChangeNotification): boolean | IValidationResult {
		this.setTableAlias($data.value);
		return super.notify($data);
	}

	close(parameter: IDialogResult<CollectionData>): void {
		if (parameter.button === DialogButtonType.Ok) {
			this.isBusy = true;
			this.repositoryService
				.createIDO(parameter.value)
				.then(successful => {
					this.isBusy = false;
					if (successful) {
						this.dialog.close(parameter);
					}
				})
				.catch((error: ErrorResponse) => {
					this.isBusy = false;
					this.dialogService.showError(
						"Could not create table",
						`Error due to "<b>${error.message}</b>"<br />
						This could happen when an IDO was not correctly removed. Please contact your system administrator.`);
				});
		} else { // close is called if cancel as well. this route happens on cancel
			this.dialog.close(parameter);
		}
	}

	static add(m: ng.IModule): void {
		m.controller("eirIdoWizardCtrl", IdoWizardCtrl);
	}
}
