import { IEditorOptions } from "../../model/sohoxi/sohoxi-editor.model";
import { Utils } from "../utils";

/**
 * Editor Directive.
 *
 * Example:
 * scope["options"]: IEditorOptions = {
 * 	anchor: {
 * 		url: "https://www.example.com,
 * 		class: "hyperlink",
 * 		target: "_blank"
 * 	}
 * };
 * <div class="editor" id="my-editor" role="textbox" aria-multiline="true" aria-label="notes" xi-editor></div>
 */
export class EditorDirective implements ng.IDirective {
	public restrict = "A";
	public scope = false;

	private static readonly DIRECTIVE_NAME = "xiEditor";

	public link(
		scope: ng.IScope,
		element: ng.IAugmentedJQuery,
		attributes: ng.IAttributes
	): void {
		const options = attributes[EditorDirective.DIRECTIVE_NAME];
		const unbindWatcher = scope.$watch(
			options,
			(newOptions: IEditorOptions) => {
				if (newOptions) {
					element.editor(newOptions);
				} else {
					element.editor();
				}
			}
		);
		element.on("$destroy", () => {
			const editorData = Utils.getData(element, "editor");
			if (editorData) {
				editorData.destroy();
			}
			unbindWatcher();
		});
	}

	public static add(m: ng.IModule): void {
		m.directive(EditorDirective.DIRECTIVE_NAME, () => {
			return new EditorDirective();
		});
	}
}
