import { InjectNames } from "ng2/common/constants/common.constant";
import { ParameterTypeEnum } from "ng2/common/enums/parameter-type.enum";
import { IParameterObject } from "ng2/common/model/dataservice/parameter-object.model";
import { IMap } from "ng2/common/model/imap.model";

import { DialogButtonType, DialogService, IDialogOptions } from "../../../../../common/services/dialog-service";

import { DataServiceHelper } from "ng2/common/helpers/data-service.helper";
import { Helper } from "../../../../../common/helper";

export class ResourceUrlEditorCtrl {

	public static $inject = [
		InjectNames.DIALOG_SERVICE
	];

	public datasourceResourceUrl: string;
	public datasourceRestUrl: string;
	public htmlEncode = Helper.htmlEncode;

	public bindingDatasourceParameters: IParameterObject[];
	// public datasourceParameters: IParameterObject[];

	public get datasourceParameters(): IParameterObject[] { return this.bindingDatasourceParameters; }
	public set datasourceParameters(value: IParameterObject[]) {
		this.bindingDatasourceParameters = value;
	}

	constructor(
		private dialogService: DialogService
	) { }

	public onEditDatasourceUrl(): void {
		const options: IDialogOptions<IMap<string>> = {
			// TODO
			title: "Edit Relative URL",
			template: require("./edit-url-dialog/edit-url-dialog.html"),
			parameter: {
				resource: this.datasourceResourceUrl
			},
		};
		this.dialogService.show<IMap<string>, string>(options).then((result) => {
			if (result.button === DialogButtonType.Ok) {
				this.datasourceResourceUrl = result.value;

				const newParameters = DataServiceHelper.parse(result.value);
				const resultParameters: IParameterObject[] = [];
				newParameters.forEach((newParameter) => {
					const existingIndex = this.bindingDatasourceParameters.findIndex((existingParameter) => {
						return existingParameter.key === newParameter.key &&
							existingParameter.parameterType === newParameter.parameterType;
					});
					if (existingIndex !== -1) {
						resultParameters.push(this.bindingDatasourceParameters[existingIndex]); // Adding existing to keep ID, alias, etc
					} else {
						resultParameters.push(newParameter);
					}
				});
				this.bindingDatasourceParameters.forEach((existingParameter, index: number, existingParameters) => {
					if (existingParameter.parameterType === ParameterTypeEnum.BODY_PART) {
						resultParameters.push(existingParameter); // Not modifying body parameters from URL
					}
				});
				this.bindingDatasourceParameters = resultParameters;
			}
		});
	}
}
