import { IPopoverOptions } from "../../model/sohoxi/sohoxi-popover.model";
import { Utils } from "../utils";
/**
 * Popover Directive
 *
 * Same control as tooltip.
 *
 * Example:
 * scope["options"]: IXiTooltipOptions = {
 * 	content: $("#popover-contents"),
 * 	trigger: "click",
 * 	placement: "left"
 * }
 * <button class="btn-primary" type="button" xi-popover="options">Click Activated</button>
 * <div id="popover-contents">
 * 	<p>This is the popover content</p>
 * </div>
 */
export class PopoverDirective implements ng.IDirective {
	private static readonly DIRECTIVE_NAME = "xiPopover";
	public restrict = "A";
	public scope = false;

	public link(
		scope: ng.IScope,
		element: ng.IAugmentedJQuery,
		attributes: ng.IAttributes
	): void {
		const unbindWatcher = scope.$watch(
			attributes[PopoverDirective.DIRECTIVE_NAME],
			(newValue: IPopoverOptions) => {
				if (newValue) {
					if (!newValue.placement) {
						newValue.placement = "right";
					}
					element.popover(newValue);
				} else {
					return;
				}
			}
		);
		// Tear down
		element.on("$destroy", () => {
			const tooltipData = Utils.getData(element, "tooltip");
			if (tooltipData) {
				tooltipData.destroy();
			}
			unbindWatcher();
		});
	}

	public static add(m: ng.IModule): void {
		m.directive(PopoverDirective.DIRECTIVE_NAME, () => {
			return new PopoverDirective();
		});
	}
}
