import { IDialogBaseScope } from "../../../../common/defines";
import { ErrorResponse, IErrorResponse } from "../../../../common/error-response";
import { DialogButtonType, DialogService } from "../../../../common/services/dialog-service";

export interface IDataServiceSearch {
	setDropdownOptions: () => void;
	getListItems: (selectedOption: any, query: string) => ng.IPromise<any[]>;
	showOperationDetails: (operation: any) => void;
}

export interface ISearchDialogFieldScope extends IDialogBaseScope<void> {
	query: string;
	selectedOption: any;
}

export abstract class BaseSearchCtrl implements IDataServiceSearch {
	// Form
	public isBusy: boolean = false;
	public abstract dropdownOptions: any[];
	public abstract filterOptions: any[];
	public abstract operations: any[];
	public abstract focusedOperation: any;

	// Lazy load parameters
	protected resultListContainerElement: ng.IAugmentedJQuery;
	protected resultListElement: ng.IAugmentedJQuery;
	protected previousPageHadResults: boolean;
	protected pageNo = 0;
	protected previousIndex: number;

	// Cache
	protected cacheObject: ng.ICacheObject;

	constructor(
		protected $scope: ISearchDialogFieldScope,
		protected $element: ng.IAugmentedJQuery,
		protected $cacheFactory: ng.ICacheFactoryService,
		protected dialogService: DialogService,
		protected metadataService: any,
	) {
		this.setDropdownOptions();

		this.resultListContainerElement = $element.find("#search-result-list");
		this.resultListElement = this.resultListContainerElement.children("ul");
		this.resultListContainerElement.on("scroll", () =>
			this.onResultListScroll()
		);

		const childName = (this.constructor as any).name;
		this.cacheObject =
			this.$cacheFactory.get(childName) || this.$cacheFactory(childName);
	}

	public abstract setDropdownOptions(): void;

	public abstract getListItems(selectedOption: any, query: string): ng.IPromise<any[]>;

	public abstract showOperationDetails(operation: any): void;

	public abstract showError(error: IErrorResponse): void;

	protected onToggleSelected(operation: any, index: number): void {
		if (this.previousIndex !== undefined) {
			this.$element.find("#list-item-" + this.previousIndex).toggleClass("is-selected");
		}
		this.previousIndex = index;
		this.$element.find("#list-item-" + index).toggleClass("is-selected");

		this.focusedOperation = operation;
	}

	protected cancel(): void {
		this.$scope.eirDialog.close({
			button: DialogButtonType.Cancel
		});
	}

	protected back(): void {
		this.$scope.eirDialog.close({
			button: DialogButtonType.Back
		});
	}

	/**
	 * Perform a search with the given suite and query.
	 * When this method is called with getNextPage, new results will
	 * be appended to the result list, and pageNo will be incremented.
	*/
	public searchOperations(selectedOption: any, query: string, getNextPage?: boolean): void {
		this.$scope.query = query;

		if (!getNextPage) {
			this.previousIndex = undefined;
		}
		if (getNextPage) {
			this.pageNo += 1;
		} else {
			this.previousPageHadResults = false;
			this.operations = [];
			this.pageNo = 0;
		}
		this.isBusy = true;
		this.getListItems(selectedOption, query)
			.then(operations => {
				this.operations = this.operations.concat(operations);
				this.previousPageHadResults = operations.length > 0;
			})
			.catch(this.handleRequestError.bind(this))
			.finally(() => this.isBusy = false);
	}

	public handleRequestError(errorResponse: ng.IHttpPromiseCallbackArg<any>): void {
		this.previousPageHadResults = false;
		const error = new ErrorResponse(errorResponse.statusText, errorResponse.status);
		this.showError(error);
	}

	/**
	 * When the search result list is scrolled, fetch more results if:
	 *
	 * Scroll-bar is at the bottom
	 * Not currently busy with fetching results
	 * The previous page returned results
	 */
	private onResultListScroll(): void {
		const scrollPositionBottomLimit = 0.95; // 95%
		const scrollPosition = this.getResultListScrollPosition();
		if (
			scrollPosition > scrollPositionBottomLimit &&
			!this.isBusy &&
			this.previousPageHadResults
		) {
			const selectedOption = this.$scope.selectedOption;
			const query = this.$scope.query;
			this.searchOperations(selectedOption, query, true);
		}
	}

	/**
	 * Get the scroll position (ratio [0, 1]) of the search result list.
	 */
	private getResultListScrollPosition(): number {
		const scrollHeight = this.resultListContainerElement.scrollTop();
		const containerHeight = this.resultListContainerElement.height();
		const listHeight = this.resultListElement.height();
		return scrollHeight / (listHeight - containerHeight);
	}
}
