import angular from "angular";
import { AngularConstants } from "../../../../common/angular-constants";

import { InjectNames, SaveFlagsEnum } from "ng2/common/constants/common.constant";
import { IAvailableSourceField } from "../../../../common/defines";
import { IRadioButton } from "../../../../common/model/component/radiobutton.model";
import { DialogButtonType, DialogService, IDialogOptions, IDialogResult } from "../../../../common/services/dialog-service";
import { TranslationManagementService } from "../../../../components/services/translation-management-service";
import type { IEditorComponent } from "common/interface/ieditor-component";
import { IEditorRadioButtons } from "../../components/editor-radiobutton";
import { EditorService } from "../../services/editor-service";
import { SettingsBase } from "../settings-base";
import { IEditRadioButtonDialogParameter } from "./edit-radiobutton-dialog";
import type { IArrangeEvent } from "common/model/sohoxi/sohoxi.model";

import { ComponentsHelper } from "../../../../components/helper";
import { EditorHelper } from "../../helper";

export class RadioButtonContentSettingsCtrl extends SettingsBase {
	protected selectedComponent: IEditorRadioButtons;

	static $inject = [
		AngularConstants.SCOPE,
		InjectNames.EDITOR_SERVICE,
		InjectNames.DIALOG_SERVICE,
		InjectNames.TRANSLATION_MANAGEMENT_SERVICE
	];

	constructor(
		$scope: ng.IScope,
		editorService: EditorService,
		private dialogService: DialogService,
		private translationManagementService: TranslationManagementService
	) {
		super($scope, editorService);

		this.onComponentChanged(editorService.getSelectedComponent());
	}

	protected onComponentChanged(newComponent: IEditorComponent): void {
		super.onComponentChanged(newComponent);
	}

	getComponentRadioButton(): IRadioButton[] {
		return this.selectedComponent.getDatabearer().componentRadioButtons;
	}

	onArrange(event: IArrangeEvent): void {
		const componentRadioButtons = this.selectedComponent.getDatabearer().componentRadioButtons;
		EditorHelper.moveByIndex(componentRadioButtons, event.startIndex, event.endIndex);
		this.save(false);
	}

	addRadioButton(): void {
		const databearer = this.selectedComponent.getDatabearer();

		const radiobuttonIds: string[] = databearer.componentRadioButtons.map((radioButton: any) => {
			return radioButton.id;
		});

		const index: number = ComponentsHelper.nextNumber(radiobuttonIds);
		const newId: string = this.selectedComponent.getId() + "Button" + index;
		const newName: string = "Radio " + index;

		databearer.componentRadioButtons.push({
			id: newId,
			name: newName
		});

		this.translationManagementService.createGeneralLanguageComponent(databearer.id, newId, newName);
		this.save();
	}

	editRadioButton(radioButton: IRadioButton): void {
		this.selectedComponent.getContextMetaData().then((sourceFields: IAvailableSourceField[]) => {
			const options: IDialogOptions<IEditRadioButtonDialogParameter> = {
				title: "Edit Radio",
				parameter: {
					component: this.selectedComponent,
					radio: angular.copy(radioButton)
				},
				template: require("./edit-radiobutton-dialog.html")
			};

			this.dialogService.show(options).then((result: IDialogResult<IRadioButton | string>) => {
				if (result && result.button === DialogButtonType.Ok) {
					this.updateRadioButton(result.value as IRadioButton);
					// TODO: use events for updating this MUPS-1654
					this.selectedComponent.updateValuesWithTranslations();
					this.save();
				}
			});
		});
	}

	deleteRadioButton(deletedRadioButton: IRadioButton): void {
		const databearer = this.selectedComponent.getDatabearer();
		const radioButtons: IRadioButton[] = databearer.componentRadioButtons;

		const index = radioButtons.indexOf(deletedRadioButton);
		radioButtons.splice(index, 1);

		if (this.selectedComponent.selectedRadioButtonId === deletedRadioButton.id) {
			this.selectedComponent.setCurrentItem(radioButtons[0]);
		}

		this.translationManagementService.removeTranslation(databearer.id, deletedRadioButton.id);
		this.save();
	}

	disableDelete(): boolean {
		const minimalRadioButtonCount = 2;
		return this.selectedComponent.getDatabearer().componentRadioButtons.length === minimalRadioButtonCount;
	}

	private updateRadioButton(editedRadioButton: IRadioButton): void {
		const selectedComponent = this.selectedComponent;
		const radioButton = this.selectedComponent.getDatabearer().componentRadioButtons.find((radiobutton) => {
			return editedRadioButton.id === radiobutton.id;
		});
		radioButton.field = editedRadioButton.field;
		if (selectedComponent.selectedRadioButtonId === editedRadioButton.id) {
			selectedComponent.currentItemValue = editedRadioButton.field;
			selectedComponent.currentItemName = editedRadioButton.name;
		}
	}

	private save(saveTranslations = true): void {
		this.editorService.saveCanvas();
		if (saveTranslations) {
			this.editorService.saveCurrent(SaveFlagsEnum.TRANSLATIONS);
		}
	}
}

export class RadioButtonContentSettingsDirective implements ng.IDirective {
	template = require("./radiobutton-content-settings.html");
	scope = true;
	controller = RadioButtonContentSettingsCtrl;
	controllerAs = "eirRadioButtonContentSettingsCtrl";
}
