import type { ICustomComponentDefinition } from "ng2/common/model/components/custom-component-definition.model";
import type { IJsonCompositeComponent } from "ng2/common/model/databearer/components/json-composite-component.model";
import type { IJsonCustomComponentDefinition } from "ng2/common/model/databearer/components/json-custom-component-def.model";
import type { IJsonDatasource } from "ng2/common/model/databearer/dataservice/json-datasource.model";
import type { IJsonBoundary } from "ng2/common/model/databearer/json-boundary.model";
import type { IJsonExpression } from "ng2/common/model/databearer/json-expression.model";
import type { IJsonInteraction } from "ng2/common/model/databearer/json-interaction.model";
import type { IJsonLayoutComponent } from "ng2/common/model/databearer/json-layout-component.model";
import type { IJsonManifest } from "ng2/common/model/databearer/json-manifest.model";
import type { IJsonMashup } from "ng2/common/model/databearer/json-mashup.model";
import type { IJsonTag } from "ng2/common/model/databearer/json-tag.model";
import type { IJsonUrlVariable } from "ng2/common/model/databearer/json-url-variable.model";
import type { IMap } from "ng2/common/model/imap.model";
import type { IJsonLanguageComponent } from "ng2/common/model/language.model";
import type { IMashup } from "ng2/common/model/project/mashup/mashup.model";

import { Interaction } from "ng2/components/interaction/interaction";

/**
 * Possibly refactor so that Mashup corresponds to the actual metadata of the Application
 */
export abstract class Mashup implements IMashup {
	manifest: IJsonManifest;
	rootComponent: IJsonLayoutComponent;
	databearer: IJsonMashup;

	datasources: IMap<IJsonDatasource> = {};
	interactions: Interaction[] = [];
	customComponentDefinitions: IMap<IJsonCustomComponentDefinition> = {};
	compositeComponents: IJsonCompositeComponent[] = [];
	translations: IMap<IJsonLanguageComponent[]> = {};
	tags: IJsonTag[] = [];
	expressions?: IJsonExpression[] = [];

	getManifest(): IJsonManifest {
		return this.manifest;
	}

	getId(): string {
		return this.manifest.id;
	}

	getDatabearer(): IJsonMashup {
		return this.databearer;
	}

	setRoot(componentObject: IJsonLayoutComponent): void {
		this.rootComponent = componentObject;
	}

	//#region /* Expressions */
	getExpressions(): IJsonExpression[] {
		return this.expressions;
	}

	getExpressionsByComponentId(componentId: string): IJsonExpression[] {
		return this.expressions.filter((expression) => {
			return expression.usedBy === componentId;
		});
	}

	addExpression(expression: IJsonExpression): void {
		this.expressions.push(expression);
	}

	deleteExpression(deletedExpression: IJsonExpression): void {
		this.expressions = this.expressions.filter((expression) => {
			return expression.id !== deletedExpression.id;
		});
	}

	removeExpression(componentId: string): void {
		this.expressions = this.expressions.filter((expression) => {
			return expression.source !== componentId && expression.target !== componentId;
		});
	}

	saveExpression(savedExpression: IJsonExpression): void {
		const expressionIndex = this.expressions.findIndex((expression) => {
			return expression.id === savedExpression.id;
		});
		this.expressions[expressionIndex] = savedExpression;
	}

	//#endregion

	//#region /* Datasources */
	getDatasources(): IMap<IJsonDatasource> {
		return this.datasources;
	}

	getDatasource(dsName: string): IJsonDatasource {
		return this.datasources[dsName];
	}

	getDatasourceDatabearers(): IJsonDatasource[] {
		const datasourceDatabearers: IJsonDatasource[] = [];
		for (const key in this.datasources) {
			datasourceDatabearers.push(this.datasources[key]);
		}
		return datasourceDatabearers;
	}

	addDatasource(datasource: IJsonDatasource): void {
		this.datasources[datasource.id] = datasource;
	}

	removeDatasource(datasourceId: string): IJsonDatasource {
		const removedDataservice = this.datasources[datasourceId];
		delete this.datasources[datasourceId];
		return removedDataservice;
	}

	//#endregion

	//#region /* Interactions */
	getInteractions(): Interaction[] {
		return this.interactions;
	}

	getInteractionDatabearers(): IJsonInteraction[] {
		return this.interactions.map<IJsonInteraction>((interaction) => {
			return interaction.getDatabearer();
		});
	}

	setInteractions(interactions: Interaction[]): void {
		this.interactions = interactions;
	}

	addInteraction(interaction: Interaction): void {
		this.interactions.push(interaction);
	}

	removeInteraction(toRemoveInteraction: Interaction): void {
		const index = this.interactions
			.findIndex(interaction => interaction.getId() === toRemoveInteraction.getId());
		this.interactions.splice(index, 1);
	}

	//#endregion

	//#region /* Custom Components */
	getCustomComponentDefinitions(): IMap<ICustomComponentDefinition> {
		return this.customComponentDefinitions;
	}

	addCustomComponentDefinition(customComponent: ICustomComponentDefinition): void {
		this.customComponentDefinitions[customComponent.id] = customComponent;
	}

	//#endregion

	//#region /* Composite Components */
	getCompositeComponents(): IJsonCompositeComponent[] {
		return this.compositeComponents;
	}

	addCompositeComponent(compositeComponent: IJsonCompositeComponent): void {
		this.compositeComponents.push(compositeComponent);
	}

	getCompositeComponent(compositeId: string): IJsonCompositeComponent {
		return this.compositeComponents.filter((cmp) => {
			return cmp.manifest.id === compositeId;
		})[0];
	}

	removeCompositeComponent(compositeId: string): IJsonCompositeComponent {
		const compositeToDelete = this.getCompositeComponent(compositeId);
		return this.compositeComponents.splice(this.compositeComponents.indexOf(compositeToDelete), 1).pop();
	}

	//#endregion

	//#region /* Tags */
	getTags(): IJsonTag[] {
		return this.tags;
	}

	setTags(tags: IJsonTag[]): void {
		this.tags = tags;
	}

	//#endregion

	//#region /* URL Variables */
	getUrlVariables(): IJsonUrlVariable[] {
		return this.manifest.urlVariables;
	}

	setUrlVariables(variables: IJsonUrlVariable[]): void {
		this.manifest.urlVariables = variables;
	}

	//#endregion

	//#region Boundaries
	getBoundaries(): IJsonBoundary[] {
		return this.manifest.mashupBoundaries;
	}

	//#endregion

	//#region /* Translations */
	setTranslations(translations: IMap<IJsonLanguageComponent[]>): void {
		this.translations = translations;
	}

	getTranslations(): IMap<IJsonLanguageComponent[]> {
		return this.translations;
	}

	getTranslationsList(): IJsonLanguageComponent[] {
		let translationList: IJsonLanguageComponent[] = [];
		for (const key in this.translations) {
			translationList = translationList.concat(this.translations[key]);
		}
		return translationList;
	}

	getTranslation(componentId: string): IJsonLanguageComponent[] {
		return this.translations[componentId];
	}

	removeTranslation(componentId: string): void {
		delete this.translations[componentId];
	}

	/**
	* Mapping the translations on componentId.
	* One componentId can have several translations so need to check if on exists before
	* adding new, if exists push to same.
	*
	* @	* @param {IJsonLanguageComponent} translation
	* @returns {void}
	* @memberof DesignerMashup
	*/
	addTranslation(translation: IJsonLanguageComponent): void {
		if (this.getTranslation(translation.componentId)) {
			this.translations[translation.componentId].push(translation);
		} else {
			this.translations[translation.componentId] = [translation];
		}
	}

	//#endregion

	//#region Other
	setPreviewImage(base64Img: string): void {
		this.manifest.previewImg = base64Img;
	}

	//#endregion

	static create<AppType extends Mashup, ManifestType extends IJsonManifest>(
		appType: (new () => AppType),
		manifestType: (new (obj: any) => ManifestType),
		obj: IJsonMashup,
	): AppType {

		const entry = new appType();
		entry.manifest = new manifestType(obj.manifest);

		if (obj.datasources) {
			obj.datasources.forEach((datasource: IJsonDatasource) => {
				entry.addDatasource(datasource);
			});
		}
		if (obj.rootComponent) {
			entry.setRoot(obj.rootComponent);
		}

		if (obj.interactions) {
			obj.interactions.forEach((interaction) => {
				const newInteraction = new Interaction(interaction);
				entry.addInteraction(newInteraction);
			});
		}

		if (obj.translations) {
			obj.translations.forEach((translation: IJsonLanguageComponent) => {
				entry.addTranslation(translation);
			});
		}

		if (obj.customComponentDefinitions) {
			entry.customComponentDefinitions = obj.customComponentDefinitions;
		}

		if (obj.compositeComponents) {
			entry.compositeComponents = obj.compositeComponents;
		}

		if (obj.tags) {
			entry.tags = obj.tags;
		}

		if (obj.expressions) {
			entry.expressions = obj.expressions;
		}

		return entry;
	}
}
