import { InteractionActionEnum, SettingKeys } from "ng2/common/constants/common.constant";
import { BindingTypeEnum } from "ng2/common/enums/binding-type.enum";
import { ValueTypeEnum } from "ng2/common/enums/value-type.enum";
import { IJsonDropdown } from "ng2/common/model/databearer/components/json-dropdown.model";
import { IJsonValueSource } from "ng2/common/model/databearer/json-binding.model";
import { IMetaDataField } from "ng2/common/model/dataservice/meta-data-field.model";
import { IAvailableTargetField } from "ng2/common/model/interaction/available-target-field.model";

import type { IDatasource } from "common/model/datasource/datasource.model";
import type { IEditorComponent } from "common/interface/ieditor-component";
import type { IEditorFlexbox } from "editor/components/editor-flexbox";

import { Helper } from "common/helper";
import { Dropdown } from "components/dropdown/dropdown";
import { EditorComponentCtrl, IEditorComponentScope } from "editor/components/editor-component";

export interface IEditorDropdown extends IEditorComponent, Dropdown {
	parent: IEditorFlexbox;
	// Overrides to make dual extend work
	getScope(): IEditorComponentScope;
	getDatabearer(): IJsonDropdown;
}

export class EditorDropdownCtrl extends EditorComponentCtrl {
	public static add(m: ng.IModule): void {
		m.controller("editor-Dropdown", EditorDropdownCtrl);
	}

	protected component: IEditorDropdown;

	public getParameterMetadata(action: InteractionActionEnum): ng.IPromise<IAvailableTargetField[]> {
		const component = this.component;
		const datasource = this.component.datasource;
		if (action === InteractionActionEnum.SET_CURRENT_ITEM && datasource) {
			return datasource.getMetaData().then((dataserviceMetadata) => {
				return dataserviceMetadata.outputFields.map((outputField) => {
					let currentValue: string;
					if (component.currentItem) {
						currentValue = Helper.trimIfPossible(component.currentItem[outputField.FLNM]);
					}
					return {
						visibleName: outputField.FLDS + (currentValue !== undefined ? `(${currentValue})` : ""),
						targetKey: outputField.FLNM,
						isMandatory: false,
						Meta: {
							name: outputField.FLDS,
							ref: outputField.FLNM,
							valueType: ValueTypeEnum.STRING,
							write: true,
							read: true
						}
					} as IAvailableTargetField;
				});
			});
		} else {
			return super.getParameterMetadata(action);
		}
	}

	public getMetaData(fieldRef: string, onSuccess: Function): void {
		const datasource = this.component.getDatasource();
		if (fieldRef === SettingKeys.CURRENT_ITEM && datasource) {
			datasource.getMetaData().then((metaData) => {
				onSuccess(metaData.outputFields);
			});
		}
	}

	public setDatasource(newDatasource: IDatasource | null): ng.IPromise<void> {
		const promise = super.setDatasource(newDatasource);
		if (newDatasource) {
			newDatasource.getMetaData().then((metaData) => {
				const component = this.component;
				const availableOutput: IMetaDataField[] = $.extend([], metaData.outputFields, true).reverse();
				const newValueSource: IJsonValueSource = {
					Type: BindingTypeEnum.CONTEXT_BINDING,
					sourceKey: availableOutput.pop().FLNM
				};
				this.bindingManagementService.registerContextBinding(component, SettingKeys.DISPLAY_VALUE, newValueSource);
				component.getDatabearer().displayValue = newValueSource;
				component.displayValue = newValueSource;
				component.loadInteraction(this.interactionManagementService.createNewOnTheFly(InteractionActionEnum.LIST));
			});
		}
		return promise;
	}

	protected clearData(): void {
		super.clearData();
		const component = this.component;
		const dropdownDatabearer = component.getDatabearer();
		const sourceKey = dropdownDatabearer.displayValue.sourceKey;
		this.bindingManagementService.unregisterContextBinding(component, sourceKey, SettingKeys.DISPLAY_VALUE);
		const newvalueSource: IJsonValueSource = { Type: BindingTypeEnum.STATIC_VALUE, value: "" };
		dropdownDatabearer.displayValue = newvalueSource;
		component.displayValue = newvalueSource;
	}
}
