import angular from "angular";
import isUndefined from "lodash-es/isUndefined";

import {
	BindingTypes, DEFAULT_BOUNDARY, IdoProperties, InteractionActionEnum, InteractionTriggerEnum,
	Numbers, SettingInputKeys, SettingKeys, SidebarTabTypes } from "ng2/common/constants/common.constant";

import { BindingTypeEnum } from "ng2/common/enums/binding-type.enum";
import { DatasourceTypeEnum } from "ng2/common/enums/datasource-type.enum";
import { HttpVerbEnum } from "ng2/common/enums/http-verb.enum";
import { OrientationEnum } from "ng2/common/enums/orientation.enum";
import { SizeTypeEnum } from "ng2/common/enums/size-type.enum";
import { ValueTypeEnum } from "ng2/common/enums/value-type.enum";

import type { IDatasource } from "common/model/datasource/datasource.model";
import type { IContext } from "ng2/common/model/components/base/context.model";
import type { IComponentFieldSetting } from "ng2/common/model/components/settings/component-field-setting.model";
import type { IComponentSettingCategory } from "ng2/common/model/components/settings/component-setting-category.model";
import type { IComponentSettings } from "ng2/common/model/components/settings/component-settings.model";
import type { IJsonComponent, IJsonMargin, IJsonSize } from "ng2/common/model/databearer/components/json-component.model";
import type { IJsonGenericDatasource2 } from "ng2/common/model/databearer/dataservice/json-datasource.model";
import type { IJsonBindableField, IJsonValueSource } from "ng2/common/model/databearer/json-binding.model";
import type { IMap } from "ng2/common/model/imap.model";
import type { IInteraction } from "ng2/common/model/interaction/interaction.model";

import type { IComponentStyle } from "common/defines";
import type { IErrorResponse } from "common/error-response";
import type { IComponent } from "common/IComponent";
import type { IComponentScope } from "common/interface/icomponent-scope";
import type { ILayoutComponent } from "common/interface/ilayout-component";
import type { ISizeObject } from "common/ISizeObject";

import { Defines } from "components/defines";

import { Helper } from "common/helper";

import { FrameworkService } from "components/services/framework-service";
import { ISourceItem } from "common/model/component/hyperlink.model";

export abstract class Component implements IComponent {

	// TODO: View variables, could probably be private
	protected cssHeight: string;
	protected cssWidth: string;
	cssClasses: string;
	isVisible = true;
	isReadonly: boolean;
	isShowError = true;
	isLoading: boolean;
	errorTooltip: string;

	// TODO: Member variables that should be methods
	canCut = true;
	canCopy = true;

	// TODO: Member variables that should be protected / private
	protected databearer: IJsonComponent;
	datasource: IDatasource;
	element: ng.IAugmentedJQuery;
	generatedId: string;
	isGenerated = false;
	parent: ILayoutComponent;

	protected cache: ng.ICacheObject;
	protected scope: IComponentScope;

	static SETTING_FIELDS: IComponentSettingCategory[] =
	[
		{
			title: "Margin",
			tab: SidebarTabTypes.STYLE,
			settings: [{
				title: "Margin",
				ref: SettingKeys.MARGIN,
				boundarySetting: true,
				inputType: SettingInputKeys.MARGIN,
				defaultValue: { left: 0, top: 0, right: 0, bottom: 0 }
			}]
		},
		{
			title: "Visibility",
			tab: SidebarTabTypes.STYLE,
			settings: [{
				title: "Show Component",
				ref: SettingKeys.VISIBILITY,
				inputType: SettingInputKeys.SWITCH,
				boundarySetting: true,
				defaultValue: true,
				button: {
					icon: "#icon-preview",
					tooltip: "Hide Component When...",
					dialog: true
				},
			}]
		},
		{
			title: "Show Error",
			tab: SidebarTabTypes.CONTENT,
			settings: [{
				title: "Show Error",
				ref: SettingKeys.SHOW_ERROR,
				inputType: SettingInputKeys.SWITCH,
				defaultValue: true,
				boundarySetting: true
			}]
		}
	];

	constructor(
		databearer: IJsonComponent,
		$element: ng.IAugmentedJQuery,
		parent: ILayoutComponent,
		protected frameworkService: FrameworkService
	) {
		this.databearer = databearer;
		if (databearer.datasource) {
			this.datasource = frameworkService.getDatasourceObject(databearer.datasource);
			if (this.datasource) {
				this.datasource.usedBy.push(this);
			} else {
				console.warn("[Component] Missing data service unsetting the property");
				databearer.datasource = null;
			}
		}
		this.element = $element;
		this.scope = this.element.data("eir-scope");
		this.parent = parent;
		if (databearer && databearer.typedef !== "Datagrid") {
			this.isReadonly = databearer.isReadonly;
		}
		this.updateClassesAndStyle();
		// Flexbox gives the generatedId on the scope
		if (this.scope.generatedId) {
			this.setGenerated(this.scope.generatedId);
		// While the List gives the generatedId to the child databearers
		} else if (this.databearer.generatedId) {
			this.setGenerated(this.databearer.generatedId);
		}

		this.createCacheObject();
	}

	canDelete(): boolean {
		if (this.isGenerated) {
			return false;
		}
		return true;
	}

	getScope(): IComponentScope {
		return this.scope;
	}

	getDatabearer(): IJsonComponent {
		return this.databearer;
	}

	getTypedef(): string {
		return this.databearer.typedef;
	}

	getId(): string {
		return this.databearer.id;
	}

	getDisplayName(): string {
		return this.databearer.displayName;
	}

	getDatasource(): IDatasource {
		return this.datasource;
	}

	setGenerated(generatedId: string): void {
		this.isGenerated = true;
		this.generatedId = generatedId;
	}

	is(classs: any): boolean {
		return this instanceof classs;
	}

	/**
	 * To be called when new boundary is entered
	 * Could be called when you need component trees to update their values
	 */
	newBoundaryEntered(boundaryId: string): void {
		this.updateClassesAndStyle();
	}

	calcNewSizes(): void {
		this.calcNewWidth();
		this.calcNewHeight();
	}

	calcNewWidth(): void {
		const margin = this.getBoundarySetting<IJsonMargin>(SettingKeys.MARGIN);
		this.cssWidth = this.calcNewSize(
			this.getBoundarySetting<IJsonSize>(SettingKeys.WIDTH),
			OrientationEnum.HORIZONTAL, margin.left + margin.right
		);
	}

	calcNewHeight(): void {
		const margin = this.getBoundarySetting<IJsonMargin>(SettingKeys.MARGIN);
		this.cssHeight = this.calcNewSize(
			this.getBoundarySetting<IJsonSize>(SettingKeys.HEIGHT),
			OrientationEnum.VERTICAL, margin.top + margin.bottom
		);
	}

	protected calcNewSize(sizeObject: IJsonSize, orientation: OrientationEnum, margin = 0): string {
		let retSize = "";
		if (this.parent.hasIndividualChildSizes(orientation)) {
			if (sizeObject.sizeType === SizeTypeEnum.PERCENT) {
				const pxPerPercentComponent = this.parent.getPxPerPercentComponent(orientation, sizeObject.value);
				retSize = "calc(" + (sizeObject.value || Numbers.HUNDRED) + "% - " + (pxPerPercentComponent + margin) + "px)";
			} else if (sizeObject.sizeType === SizeTypeEnum.PIXELS) {
				retSize = "calc(" + sizeObject.value + "px)";
			}
		}
		return retSize;
	}

	setBoundarySettingIfChanged<T>(settingId: string): void {
		const value = this.getBoundarySetting<T>(settingId);
		if (!Helper.isUndefinedOrNull(value) && (this as any)[settingId] !== value) {
			(this as any)[settingId] = value;
		}
	}

	getBoundarySetting<T>(settingId: string, boundaryId?: string, noTraverse?: boolean): T {
		const frameworkService = this.frameworkService;
		const componentBoundarySettings = this.databearer.boundarySettings;

		if (!boundaryId) {
			boundaryId = frameworkService.getCurrentBoundary().id;
		}
		if (noTraverse) {
			//Should return a copy, List needs some work
			return (componentBoundarySettings[boundaryId] as IMap<T>)[settingId];
		}

		let boundarySettings: IComponentSettings;
		while (!boundarySettings) {
			if (!componentBoundarySettings[boundaryId] ||
				(componentBoundarySettings[boundaryId] as IMap<T>)[settingId] === undefined ||
				(componentBoundarySettings[boundaryId] as IMap<T>)[settingId] === null) {
				boundaryId = frameworkService.getNextProjectBoundary(boundaryId).id;
				if (boundaryId === DEFAULT_BOUNDARY.id) {
					boundarySettings = componentBoundarySettings[DEFAULT_BOUNDARY.id];
					break;
				}
			} else {
				boundarySettings = componentBoundarySettings[boundaryId];
				break;
			}

		}
		return (boundarySettings as IMap<T>)[settingId];
	}

	getBindableValue(bindableReference: string): any {
		const bindablevalue = (this as any)[bindableReference];
		return bindablevalue;
	}

	setBindableValue(bindableReference: string, value: any): void {
		if (bindableReference === SettingKeys.TOOLTIP) {
			// always encode tooltip bindables
			(this as any)[bindableReference] = Helper.htmlEncode(value);
		} else {
			(this as any)[bindableReference] = value;
		}
	}

	/**
	 * Those components with changable values in runtime triggers this.
	 */
	onUpdateValue(): void {
		this.frameworkService.triggerExpressions();
	}

	isLayoutComponent(): boolean {
		return Defines.allComponents[this.getTypedef()].isLayoutComponent;
	}

	isTranslatableComponent(): boolean {
		return this.getTypedef() !== Image.name
			&& !this.isLayoutComponent();
	}

	/**
	 * All update methods is to be called on three occations:
	 * - On startup (via newBoundaryEntered)
	 * - On change setting (when changing a setting that affects the classes)
	 * - On change currentBoundary (via newBoundary Entered)
	 */
	updateClasses(): void {
		this.element.removeClass(this.cssClasses);
		this.cssClasses = this.getClassList().join(" ");
		this.element.addClass(this.cssClasses);
	}

	protected updateInlineStyle(): void {
		this.calcNewSizes();
		const style = this.getInlineStyle();
		this.element.css(style);
	}

	updateVisibility(): void {
		this.isVisible = this.getBoundarySetting<boolean>(SettingKeys.VISIBILITY);
	}

	updateShowError(): void {
		this.isShowError = this.getBoundarySetting<boolean>(SettingKeys.SHOW_ERROR);
	}

	/**
	 * Reload all component values
	 * - Boundary settings
	 * - Static settings
	 *
	 */
	updateClassesAndStyle(): void {
		this.updateClasses();
		this.updateInlineStyle();
		this.updateVisibility();
		this.updateShowError();
	}

	protected getClassList(): string[] {
		const classList = [
			"eir-component",
			"eir" + this.databearer.typedef
		];
		if (this.isGenerated) {
			classList.push("generatedComponent");
		}
		return classList;
	}

	protected getInlineStyle(): IComponentStyle {
		const margin = this.getBoundarySetting<IJsonMargin>(SettingKeys.MARGIN);
		return {
			width: this.cssWidth,
			height: this.cssHeight,
			margin: `${margin.top}px ${margin.right}px ${margin.bottom}px ${margin.left}px`,
		};
	}

	getGeneratingParent(): ILayoutComponent {
		if (this.parent.isGenerated) {
			return this.parent.getGeneratingParent();
		}
		return this.parent;
	}

	getValue(key: string): any {
		let value = Helper.getDeepValue(this, key);
		// cover case of Flexbox which has values inside scope.context
		if (Helper.isUndefinedOrNull(value) && this.getContext()) {
			const path = "scope.context." + key;
			value = Helper.getDeepValue(this, path);
		}
		return value;
	}

	/**
	 * Used for resetting the layout of a component. The following is currently reset:
	 *
	 * 1. Error tooltip
	 * 2. Loading is set to false
	 *
	 * @memberof Component
	 */
	resetLayout(): void {
		this.setErrorTooltip(null);
		this.stopLoading();
	}

	setErrorTooltip(errorMessage?: string): void {
		this.errorTooltip = errorMessage
			? errorMessage
			: null;
	}

	private startLoading(): void {
		this.isLoading = true;
	}

	private stopLoading(): void {
		this.isLoading = false;
	}

	loadInteraction(interaction: IInteraction): ng.IPromise<any | any[]> {
		const deferred = this.frameworkService.getNewDeferred<any>();
		this.resetLayout();
		this.startLoading();
		const dataserviceAction = this.isDataserviceAction(interaction);

		if (dataserviceAction && this.datasource) {
			const parameters = this.frameworkService.readParameters(
				interaction.getParameters(),
				this.getContext()
			);

			switch (interaction.targetActionName) {
				case InteractionActionEnum.LIST:
					this.datasource.getListData(parameters).then((listResult) => {
						this.getContext().ListResult = listResult;
						deferred.resolve(listResult);
					}).catch((errResponse: IErrorResponse) => {
						this.resetContext();
						deferred.reject(errResponse);
					});
					break;
				case InteractionActionEnum.GET:
					this.datasource.getDetailData(parameters).then((detailData: any) => {
						this.setDetailResultForGetAction(detailData);
						this.updateItemID(detailData, interaction);

						const detailResult = this.getContext().DetailResult;
						if (detailResult) {
							deferred.resolve(detailResult);
						} else {
							deferred.reject({
								message: "Nothing in response"
							} as IErrorResponse);
						}

						// Timeout needed so that interaction doesn't run before parameters been fully set
						this.frameworkService.getTimeout()(() => {
							this.trigger(InteractionTriggerEnum.ON_LOAD);
							this.refreshBindings();
							this.frameworkService.triggerExpressions();
						}, Numbers.TEN);

					}).catch((errResponse: IErrorResponse) => {
						this.resetContext();
						deferred.reject(errResponse);
					});
					break;
				default:
					console.warn("[Component] Invalid interaction action");
			}
		} else if (!dataserviceAction) {
			this.runComponentAction(interaction);
			deferred.resolve();
		} else {
			const errorTooltip = `No Data Service available on component: ${this.databearer.displayName}`;
			this.setErrorTooltip(errorTooltip);
			const errorResponse: IErrorResponse = {
				message: errorTooltip,
				detail: errorTooltip
			};
			deferred.reject(errorResponse);
		}
		deferred.promise
			.catch((errorResponse) => {
				this.setErrorTooltip(errorResponse.message);
			}).finally(() => {
				this.stopLoading();
			});
		return deferred.promise;
	}

	/**
	 * Builds a link address from a link string and a sources array
	 * Used by datagrid and hyperlink for their build-link-dialog
	 * @param link - the link string to interpret
	 * @param sources - an array of ISourceItem
	 * @return {string} - link address string
	 */
	buildLink(link: string, sources: ISourceItem[]): string {
		let renderedLink = angular.copy(link);
		sources.forEach((source) => {
			let value = "";
			const component = this.frameworkService.getComponentInstance(source.type);
			if (component) {
				if (component.getDatasource()) {
					value = component.getValue(source.value);
				} else {
					value = component.getBindableValue(source.value);
				}
				if (Helper.isUndefinedOrNull(value)) {
					value = "";
				}
			} else if (source.type === BindingTypes.CONTEXT) {
				const valuesource: IJsonValueSource = {
					Type: BindingTypeEnum.CONTEXT_BINDING,
					sourceKey: source.value
				};
				value = this.frameworkService.readValueSource(valuesource, this.getContext());
			}
			renderedLink = renderedLink.replace(new RegExp(`{${source.alias}}`, "gi"), value);
		});
		return renderedLink;
	}

	/**
	 * Refreshes potential values inherited from parent component via context bindings and regular bindings
	 * if value has changed.
	 */
	private refreshBindings(): void {
		if (!isUndefined(this.getContext().DetailResult)) {
			this.forEachChild((child) => {
				child.forEachBindable((bindable, valueSource) => {
					if (valueSource) {
						const isBinding =
							valueSource.sourceName === this.getId() && valueSource.Type === BindingTypeEnum.BINDING;
						const isContextBinding =
							valueSource.sourceName === BindingTypes.CONTEXT &&
							valueSource.Type === BindingTypeEnum.CONTEXT_BINDING;

						if (isBinding || isContextBinding) {
							const newValue = this.getContext().DetailResult[valueSource.sourceKey];
							const oldValue = child.getBindableValue(bindable.ref);
							if (!isUndefined(oldValue) && !isUndefined(newValue) && (newValue !== oldValue)) {
								child.setBindableValue(bindable.ref, newValue);
							}
						}
					}
				});
			}, false);
		}
	}

	/**
	 * Updates current selectedItem's ItemID
	 * if the data services is an local IDO using method PUT
	 * @param detailData
	 * @param interaction
	 */
	private updateItemID(detailData: any, interaction: IInteraction): void {
		const generic2Databearer = this.datasource.getDatabearer() as IJsonGenericDatasource2;

		if (generic2Databearer.typedef === DatasourceTypeEnum.IDO &&
			generic2Databearer.method === HttpVerbEnum.PUT
		) {
			const itemIdParameter = generic2Databearer.inputParameters
				.find(parameter => parameter.key === IdoProperties.ITEM_ID);

			if (!Helper.isUndefined(itemIdParameter) &&
				!Helper.isUndefined(detailData[itemIdParameter.id])) {
				const interactionParameter = interaction.getParameter(itemIdParameter.id);

				this.frameworkService.setParameter(
					interactionParameter,
					detailData[itemIdParameter.id]
				);
			}
		}
	}

	trigger(triggerName: string): void {
		this.frameworkService.trigger(this, triggerName);
	}

	getContext(): IContext {
		if (this.getScope()) {
			return this.getScope().context;
		} else {
			// TODO: Find out what case this is, tell Anton if found
			return this.parent.getScope().context;
		}
	}

	initializeBindings(): void {
		this.forEachBindable((bindableField, currentValueSource) => {
			if (currentValueSource && bindableField.write) {
				let value = "";
				if (currentValueSource.Type !== BindingTypeEnum.URL_BINDING) {
					const currentStringValue = this.getComponentTranslation(bindableField, currentValueSource.value);
					value = this.frameworkService.getGlobalValue(currentStringValue, this);
					if (!value) {
						currentValueSource.value = value = currentStringValue;
					}
				}
				switch (currentValueSource.Type) {
					case BindingTypeEnum.CONTEXT_BINDING:
						this.frameworkService.registerContextBinding(
							this,
							bindableField.ref,
							currentValueSource.sourceKey,
							value
						);
						break;
					case BindingTypeEnum.BINDING:
						this.frameworkService.registerBinding(this, bindableField.ref, currentValueSource, value);
						break;
					case BindingTypeEnum.STATIC_VALUE:
						this.setBindableValue(bindableField.ref, value);
						break;
					case BindingTypeEnum.URL_BINDING:
						value = this.frameworkService.getUrlValue(currentValueSource.sourceKey);
						if (value === "" && !this.frameworkService.isRuntimeMode()) {
							value = currentValueSource.value;
						}
						this.setBindableValue(bindableField.ref, value);
						break;
					default:
						console.warn("[Component] Invalid valueSource type");
						break;
				}
			}
		});
	}

	private setDetailResultForGetAction(detailData: any): void {
		const databearer2 = this.datasource.getDatabearer() as IJsonGenericDatasource2;

		const localContext = this.getContext();
		if (detailData && typeof detailData === "object") {
			angular.extend(localContext, detailData);
		}

		if (databearer2.isOutputFile) {
			localContext.DetailResult = detailData[databearer2.outputParameters[0].id];
		} else {
			localContext.DetailResult = detailData;
		}
	}

	private runComponentAction(interaction: IInteraction): void {
		switch (interaction.targetActionName) {
			case InteractionActionEnum.CLEAR:
				this.clear();
				break;
			default:
				//Do nothing
		}
	}

	private isDataserviceAction(interaction: IInteraction): boolean {
		switch (interaction.targetActionName) {
			case InteractionActionEnum.CLEAR:
				return false;
				break;
			default:
				return true;
		}
	}

	private getComponentTranslation(field: IJsonBindableField, value: string): string {
		let translationValue;
		if (field.valueType === ValueTypeEnum.LOCALIZED_STRING) {
			const newValue = this.frameworkService.getTranslatableStringByProperty(this.getId(), field.ref);
			if (newValue || newValue === "") {
				translationValue = newValue;
			} else {
				translationValue = value;
			}
		} else {
			translationValue = value;
		}
		return translationValue;
	}

	forEachParent(fn: (parent: ILayoutComponent) => void): void {
		this.parent.forEachParent(fn);
	}

	getPredictedSize(): ISizeObject {
		const sizes: ISizeObject = {};
		let sizePredicter: JQuery;

		if (this.getBoundarySetting<IJsonSize>(SettingKeys.HEIGHT).sizeType === SizeTypeEnum.AUTO) {
			//let dummyText = this.Content ? this.Content : "My dummystring";
			sizePredicter = $("<div class='sizePredicter'></div>").appendTo("body");
			sizePredicter.append(this.element.clone());
			sizes.height = sizePredicter.outerHeight(true);

			sizePredicter.remove();
		}

		return sizes;
	}

	forEachBindable(fn: (field: IJsonBindableField, curr: IJsonValueSource) => void): void {
		Defines.allComponents[this.databearer.typedef].class.BINDABLE_FIELDS_2
			.forEach((field: any) => {

				const valueSource: IJsonValueSource = (this.databearer as any)[field.ref] ?
					(this.databearer as any)[field.ref] :
					(this as any)[field.ref]; // Quickfix for currentItem
				fn(field, valueSource);
			});
	}

	forEachFieldSetting(fn: (field: IComponentFieldSetting) => void): void {
		Defines.allComponents[this.databearer.typedef].class.SETTING_FIELDS.forEach((field: any) => {
			fn(field);
		});
	}

	// To be able to call forEachChild on arbitrary component
	forEachChild(fn: (child: IComponent) => void, doGenerated: boolean): void {
		fn(this);
	}

	resetContext(): void {
		const context = this.getContext();
		for (const key in context) {
			delete context[key];
		}
	}

	clear(): void {
		//Do cool stuff in the future
	}

	private createCacheObject(): void {
		this.cache = this.frameworkService.getProjectCacheObject();
	}
}
