import { AlignmentEnum, FlexOverflowEnum, FlexSizeTypeEnum } from "ng2/common/constants/common.constant";
import { OrientationEnum } from "ng2/common/enums/orientation.enum";
import { IJsonComponentSettings } from "ng2/common/model/databearer/components/json-component.model";
import { IJsonLayoutComponent } from "ng2/common/model/databearer/json-layout-component.model";
import { IMap } from "ng2/common/model/imap.model";

export interface IJsonFlexbox extends IJsonLayoutComponent {
	backgroundColor?: string;
	border?: IJsonBorder;
	borderColor?: string;
	boundarySettings: IMap<IJsonFlexboxSettings>;
}

export interface IJsonFlexboxSettings extends IJsonComponentSettings {
	orientation: OrientationEnum;
	size: number;
	sizeType: FlexSizeTypeEnum;
	overflow: FlexOverflowEnum;
	horizontalAlignment: AlignmentEnum;
	verticalAlignment: AlignmentEnum;
}

export interface IJsonFlexboxAlignments {
	horizontalAlignment: AlignmentEnum;
	verticalAlignment: AlignmentEnum;
}

export interface IAvailableFlexSize {
	displayName: string;
	value: FlexSizeTypeEnum;
}

export interface IAvailableAlignments {
	name: string;
	value: AlignmentEnum;
}

export interface IJsonBorder extends IMap<number> {
	top: number;
	right: number;
	bottom: number;
	left: number;
}
