import angular from "angular";
import html2canvas from "html2canvas";
import { AngularConstants } from "../../../common/angular-constants";

import { InjectNames, SaveFlagsEnum, ServerPaths } from "ng2/common/constants/common.constant";
import { IJsonDatasource } from "ng2/common/model/databearer/dataservice/json-datasource.model";
import { IJsonInteraction } from "ng2/common/model/databearer/json-interaction.model";
import { IJsonManifest } from "ng2/common/model/databearer/json-manifest.model";
import { IJsonLanguageComponent } from "ng2/common/model/language.model";
import { DesignerMashup } from "ng2/common/project/designer-mashup";
import { DesignerManifest } from "ng2/common/project/manifest/designer-manifest";
import { ErrorResponse, IErrorResponse } from "../../../common/error-response";
import { Helper } from "../../../common/helper";
import { InstanceEvent } from "../../../common/instance-event";
import { FeatureSwitchCtrl } from "../../../common/shared-components/base/feature-switch-ctrl";

import { FeatureSwitchService } from "../../../common/services/feature-switch-service";
import { HttpService } from "../../../common/services/http-service";

/* tslint:disable:no-bitwise */
export class SaveService extends FeatureSwitchCtrl {

	public saveStates = {
		inProgress: "Saving...",
		saved: "Last saved at ",
		failed: "Failed to save"
	};
	public saveStateEvent = new InstanceEvent<string | IErrorResponse>();

	public static $inject = [
		AngularConstants.Q,
		InjectNames.HTTP_SERVICE,
		InjectNames.FEATURE_SWITCH_SERVICE
	];

	public constructor(
		private $q: ng.IQService,
		private httpService: HttpService,
		featureService: FeatureSwitchService,
	) {
		super(featureService);
	}

	private raiseSaveStateEvent(value: string | IErrorResponse): void {
		this.saveStateEvent.raise(value);
	}

	public save(project: DesignerMashup, flags: number): ng.IPromise<void> {
		const manifest = project.manifest;
		const deferred = this.$q.defer<void>();
		const promises: ng.IPromise<IJsonManifest>[] = [];
		this.raiseSaveStateEvent(this.saveStates.inProgress);

		if ((flags & SaveFlagsEnum.MANIFEST) !== 0) {
			promises.push(this.saveManifest(manifest));
		}
		if ((flags & SaveFlagsEnum.CANVAS_COMPONENTS) !== 0) {
			promises.push(this.saveRootComponent(project));
		}
		if ((flags & SaveFlagsEnum.INTERACTIONS) !== 0) {
			promises.push(this.saveInteractions(project));
		}
		if ((flags & SaveFlagsEnum.DATASOURCES) !== 0) {
			promises.push(this.saveDatasources(project));
		}
		if ((flags & SaveFlagsEnum.SCREENSHOT) !== 0) {
			promises.push(this.saveScreenshot(manifest));
		}
		if ((flags & SaveFlagsEnum.CUSTOMCOMPONENTS) !== 0) {
			promises.push(this.saveCustomComponentDefinitions(project));
		}
		if ((flags & SaveFlagsEnum.COMPOSITE_COMPONENTS) !== 0) {
			promises.push(this.saveCompositeComponents(project));
		}
		if ((flags & SaveFlagsEnum.TRANSLATIONS) !== 0 && this.isFeatureTranslationEnabled()) {
			promises.push(this.saveTranslations(project));
		}
		if ((flags & SaveFlagsEnum.TAGS) !== 0) {
			promises.push(this.saveTags(project));
		}
		if ((flags & SaveFlagsEnum.URL_VARIABLES) !== 0) {
			promises.push(this.saveUrlVariables(project));
		}
		if ((flags & SaveFlagsEnum.EXPRESSIONS) !== 0) {
			promises.push(this.saveExpressions(project));
		}
		if ((flags & SaveFlagsEnum.QUESTIONS) !== 0) {
			promises.push(this.saveQuestions(project));
		}

		this.$q.all(promises).then((responses) => {
			this.raiseSaveStateEvent(this.saveStates.saved);
			const newManifest = responses[0];
			const manifest = project.getManifest();
			manifest.updateLastChangedInformation(newManifest);
			deferred.resolve();
		}).catch((errorResponse: ErrorResponse) => {
			let saveState: ErrorResponse | string = errorResponse;
			if (!errorResponse) {
				saveState = "Failed to save.";
			}
			this.raiseSaveStateEvent(saveState);
			deferred.reject(errorResponse);
		});

		return deferred.promise;
	}

	public renameProject(id: string, newDisplayName: string): ng.IPromise<IJsonManifest> {
		return this.httpService.executePost<IJsonManifest>(ServerPaths.PROJECT + "rename/" + id, newDisplayName);
	}

	private saveManifest(manifest: DesignerManifest): ng.IPromise<IJsonManifest> {
		return this.httpService.executePut<IJsonManifest>(ServerPaths.PROJECT + "manifest", manifest);
	}

	private saveScreenshot(manifest: DesignerManifest): ng.IPromise<IJsonManifest> {
		const deferred = this.$q.defer<IJsonManifest>();
		const half = 0.5;
		let width = 1600; // default canvas width on 1920x1080
		const height = 822; // default canvas height on 1920x1080
		const canvasArea = $("#insideRuler");
		const style = canvasArea.prop("style");
		let inlineWidth = "";

		// check if inline style for width exist (active boundary)
		const widthProp = "width";
		if (style && style[widthProp] !== "") {
			const attributeLength = 2;
			inlineWidth = style[widthProp].slice(0, -attributeLength);
			width = parseInt(inlineWidth);
		}

		// set canvas width and height for html2canvas to properly capture the whole area
		canvasArea.width(width);
		canvasArea.height(height);

		const options: Html2Canvas.Html2CanvasOptions = {
			//Safe enough to use cross-origin in html2canvas for images?
			useCORS: true,
			allowTaint: true
		};

		// Available options here: http://html2canvas.hertzen.com/documentation.html
		html2canvas($("#insideRuler")[0], options).then((canvas: HTMLCanvasElement) => {
			manifest.previewImg = canvas.toDataURL("image/jpeg", half);
			this.httpService.executePut(ServerPaths.PROJECT + "manifest", manifest).then(() => {
				deferred.resolve(manifest);
			}).catch((errorResponse: IErrorResponse) => {
				deferred.reject(errorResponse);
			});
		});
		// reset canvas width and height
		canvasArea.width(inlineWidth);
		canvasArea.height("");

		return deferred.promise;
	}

	private saveRootComponent(project: DesignerMashup): ng.IPromise<IJsonManifest> {
		const rootComponent = project.rootComponent;
		const url = ServerPaths.PROJECT + project.getId() + "/rootComponent";
		return this.httpService.executePut(url, rootComponent);
	}

	private saveInteractions(project: DesignerMashup): ng.IPromise<IJsonManifest> {
		const jsonInteractions: IJsonInteraction[] = project.getInteractionDatabearers();
		const url = ServerPaths.PROJECT + project.getId() + "/interactions";
		return this.httpService.executePut<IJsonManifest>(url, jsonInteractions);
	}

	private saveTranslations(project: DesignerMashup): ng.IPromise<IJsonManifest> {
		const jsonTranslations: IJsonLanguageComponent[] = project.getTranslationsList();
		const url = ServerPaths.PROJECT + project.getId() + "/translations";
		return this.httpService.executePut<IJsonManifest>(url, jsonTranslations);
	}

	private saveTags(project: DesignerMashup): ng.IPromise<IJsonManifest> {
		const tags = Helper.getListNames(project.getTags());
		const url = "tags/" + ServerPaths.PROJECT + project.getId() + "?tagName=" + tags;
		return this.httpService.executePost<IJsonManifest>(url);
	}

	private saveCustomComponentDefinitions(project: DesignerMashup): ng.IPromise<IJsonManifest> {
		const deferred = this.$q.defer<IJsonManifest>();
		const customComponentDefinitions = project.customComponentDefinitions;

		if (customComponentDefinitions) {
			const url = ServerPaths.PROJECT + project.getId() + "/customComponentDefinitions";
			this.httpService.executePut(url, customComponentDefinitions).then(() => {
				deferred.resolve();
			}).catch((errorResponse: IErrorResponse) => {
				deferred.reject(errorResponse);
			});
		} else {
			// No customComponentDefinitions in project
			deferred.resolve();
		}

		return deferred.promise;
	}

	private saveDatasources(project: DesignerMashup): ng.IPromise<IJsonManifest> {
		const jsonDatasources: IJsonDatasource[] = project.getDatasourceDatabearers();
		jsonDatasources.forEach((jsonDatasource) => {
			Helper.cleanNulls(jsonDatasource);
		});
		const url = ServerPaths.PROJECT + project.getId() + "/datasources";
		return this.httpService.executePut<IJsonManifest>(
			url,
			JSON.parse(angular.toJson(jsonDatasources)));
	}

	private saveCompositeComponents(project: DesignerMashup): ng.IPromise<IJsonManifest> {
		const deferred = this.$q.defer<IJsonManifest | null>();
		const compositeComponentDefinitions = project.compositeComponents;

		if (compositeComponentDefinitions) {
			const url = ServerPaths.PROJECT + project.getId() + "/compositeComponents";
			this.httpService.executePut<IJsonManifest>(url, compositeComponentDefinitions).then((response) => {
				deferred.resolve(response);
			}).catch((errorResponse: IErrorResponse) => {
				deferred.reject(errorResponse);
			});
		} else {
			// No compositeComponents in project
			deferred.resolve();
		}

		return deferred.promise;
	}

	private saveUrlVariables(project: DesignerMashup): ng.IPromise<IJsonManifest> {
		const urlVariables = project.manifest.urlVariables;
		const url = ServerPaths.PROJECT + project.getId() + "/urlvariables";
		return this.httpService.executePut<IJsonManifest>(url, urlVariables);
	}

	private saveExpressions(project: DesignerMashup): ng.IPromise<IJsonManifest> {
		const expressions = project.expressions;
		const url = ServerPaths.PROJECT + project.getId() + "/expressions";
		return this.httpService.executePut<IJsonManifest>(url, expressions);
	}

	private saveQuestions(project: DesignerMashup): ng.IPromise<IJsonManifest> {
		const questions = project.questions;
		const url = ServerPaths.PROJECT + project.getId() + "/questions";
		return this.httpService.executePut<IJsonManifest>(url, questions);
	}

	public static add(ngModule: ng.IModule): void {
		ngModule.service(InjectNames.SAVE_SERVICE, SaveService);
	}
}
