import { DesignerManifest } from "ng2/common/project/manifest/designer-manifest";
import { InstanceEvent } from "../../common/instance-event";
import { Designer } from "../../designer/designer";

import { InjectNames, ThemeEnum } from "ng2/common/constants/common.constant";
import { IJsonDesignerMashup } from "ng2/common/model/databearer/json-mashup.model";
import { IBoundary } from "ng2/common/model/project/boundary.model";
import { DesignerMashup } from "ng2/common/project/designer-mashup";
import { Mashup } from "ng2/common/project/mashup";
import { ViewEnum } from "../../common/defines";
import { ILayoutComponent } from "../../common/interface/ilayout-component";

import { CommonService } from "../../common/services/common-service";
import { HttpService } from "../../common/services/http-service";
import { ThemeService } from "../../common/services/theme-service";

export class DesignerService {

	public projectSettingsChangedEvent: InstanceEvent<DesignerManifest>;
	public currentBoundaryChangedEvent: InstanceEvent<IBoundary>;
	public currentCanvasThemeChangedEvent: InstanceEvent<ThemeEnum>;

	public viewChangedEvent = new InstanceEvent<ViewEnum>();
	private info: Designer;
	private view: ViewEnum;
	// Used to cache request
	private requestInfoPromise: ng.IPromise<Designer>;

	static $inject = [
		InjectNames.HTTP_SERVICE,
		InjectNames.COMMON_SERVICE,
		InjectNames.THEME_SERVICE
	];

	constructor(
		private httpService: HttpService,
		private commonService: CommonService,
		private themeService: ThemeService
	) {
		this.projectSettingsChangedEvent = commonService.projectSettingsChangedEvent;
		this.currentBoundaryChangedEvent = commonService.currentBoundaryChangedEvent;
		this.currentCanvasThemeChangedEvent = commonService.currentCanvasThemeChangedEvent;
	}

	public loadProject(projectId: string, canvasWidth: number): ng.IPromise<DesignerMashup> {
		const promise = this.commonService.loadProject(projectId, canvasWidth) as ng.IPromise<IJsonDesignerMashup>;
		return this.parseAndSetProject(promise, canvasWidth);
	}

	public unloadProject(): void {
		return this.commonService.unloadProject();
	}

	public updateCurrentBoundary(canvasWidth: number): void {
		this.commonService.updateCurrentBoundary(canvasWidth);
	}

	public setView(view: ViewEnum): void {
		this.view = view;

		let theme = ThemeEnum.LIGHT;
		if (view === ViewEnum.EDITOR) {
			const currentProject = this.getCurrentProject();
			if (currentProject) {
				theme = currentProject.manifest.defaultTheme;
				this.commonService.setCanvasTheme(theme, true);
			}
			theme = ThemeEnum.DARK;
		}

		this.themeService.changeThemeCss(theme, false).finally(() => {
			this.viewChangedEvent.raise(view);
		});
	}

	public getView(): ViewEnum {
		return this.view;
	}

	public requestInfo(): ng.IPromise<Designer> {
		if (!this.requestInfoPromise) {
			this.requestInfoPromise = Designer.info(this.httpService).then((designer: Designer) => {
				// Cache to be used in getInfo method
				this.info = designer;
				return designer;
			}).catch((e: any) => {
				// reset request cahce
				this.requestInfoPromise = null;
				return e;
			});
		}
		return this.requestInfoPromise;
	}

	public getInfo(): Designer {
		return this.info;
	}

	public getCurrentProject(): Mashup {
		return this.commonService.getCurrentProject();
	}

	public getCurrentBoundary(): IBoundary {
		return this.commonService.getCurrentBoundary();
	}

	public setCurrentBoundary(boundary: IBoundary): void {
		return this.commonService.setCurrentBoundary(boundary);
	}

	public getCurrentRootComponentInstance(): ILayoutComponent {
		return this.commonService.getCurrentRootComponentInstance();
	}

	public setCurrentRootComponentInstance(component: ILayoutComponent): void {
		this.commonService.setCurrentRootComponentInstance(component);
	}

	public getCanvasTheme(): ThemeEnum {
		return this.commonService.getCurrentTheme();
	}

	public setCanvasTheme(theme: ThemeEnum): void {
		this.commonService.setCanvasTheme(theme, true);
	}

	private parseAndSetProject(
		promise: ng.IPromise<IJsonDesignerMashup>,
		canvasWidth: number): ng.IPromise<DesignerMashup> {
		return promise.then((jsonMashup: IJsonDesignerMashup) => {

			const mashup = DesignerMashup.fromObj(jsonMashup);

			return this.commonService.buildPrerequisites(mashup, canvasWidth) as ng.IPromise<DesignerMashup>;
		});
	}

	static add(ngModule: ng.IModule): void {
		ngModule.service(InjectNames.DESIGNER_SERVICE, DesignerService);
	}
}
