/**
 * Class contain limit setting for max datasource, and composite component in project
 *
 * @export
 * @class ProjectLimit
 */
export class ProjectLimit {
	static readonly MAX_BOUNDARIES: number = 11; //10 + default boundary
	private static maxDatasource = 50;
	static get MAX_DATASOURCES(): number { return this.maxDatasource; }

	private static maxCompositeComponent = 50;
	static get MAX_COMPOSITE_COMPONENTS(): number { return this.maxCompositeComponent; }

	private static errorMessage(item: string): string {
		return "The maximum amount of " + item + " has been reached. Please delete " + item + "(s) before adding new ones.";
	}

	private static importErrorMessage(item: string): string {
		return "Import would exceed allowed " + item + ". Please delete " + item + "(s) before importing new ones.";
	}

	static get COMPOSITE_COMPONENT_ERROR_MESSAGE(): string { return this.errorMessage("composite components"); }
	static get COMPOSITE_COMPONENT_IMPORT_ERROR_MESSAGE(): string {
		return this.importErrorMessage("composite component");
	}

	static get DATASOURCE_ERROR_MESSAGE(): string { return this.errorMessage("data services"); }
	static get DATASOURCE_IMPORT_ERROR_MESSAGE(): string {
		return this.importErrorMessage("data service");
	}
}

