export class RegexStrings {
	static GLOBALSTRING = "\{([^)]+)\}";

	/* tslint:disable-next-line:max-line-length */
	static EMAIL = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
	static SPECIAL_CHARACTERS = /[?!@#\$%\^\&*\)\(+=._-]+/;
	static NUMERIC = /^-?[0-9]+$/;
	static NO_NUMERIC = /\d+/g;
	static NUMERIC_NO_NEGATIVE = /^[0-9]+$/;
	static NEW_LINES = /(\r\n|\n|\r)/gm;
	static STARTS_WITH_NUMBER = /^\d/;
	static NO_SPECIAL = /[^a-zA-Z0-9-_.]/g;
	static TIME = /(?:(?:0?[0-9]|1[0-2]):[0-5][0-9](:[0-5][0-9])? [aApP][mM]|(?:[01][0-9]|2[0-3]):[0-5][0-9](:[0-5][0-9])?)$/;
	static FIRST_CHAR_LETTER = /^[a-zA-Z]/;
}

