import { InjectNames } from "ng2/common/constants/common.constant";
import { IJsonGenericDatasource } from "ng2/common/model/databearer/dataservice/json-datasource.model";
import { AngularConstants } from "../../../../common/angular-constants";
import { IDialogBaseScope } from "../../../../common/defines";
import { DialogButtonType, DialogService, IDialog, IDialogOptions, IDialogResult } from "../../../../common/services/dialog-service";

export enum SourceEnum {
	ION_API = "ION API",
	LOCAL_IDO = "Local IDO",
	INLINE_LIST = "Static List"
}

export class SelectSourceCtrl {
	public readonly sourceList: SourceEnum[] = [
		SourceEnum.ION_API,
		SourceEnum.LOCAL_IDO,
		SourceEnum.INLINE_LIST
	];

	static $inject = [
		AngularConstants.SCOPE,
		AngularConstants.ELEMENT,
		InjectNames.DIALOG_SERVICE,
	];

	public readonly SELECT_SOURCE_ELEMENT_ID: string = "select-source";

	public selectedSource: SourceEnum;

	private dialog: IDialog<void>;

	constructor(
		$scope: IDialogBaseScope<any>,
		private $element: ng.IAugmentedJQuery,
		private dialogService: DialogService,
	) {
		this.dialog = $scope.eirDialog;
		this.initDialogEvents();

		this.selectedSource = SourceEnum.ION_API;
	}

	public next(): void {
		switch (this.selectedSource) {
			case SourceEnum.ION_API:
				this.openIonApiSearch();
				break;
			case SourceEnum.INLINE_LIST:
				this.openCreateInlineList();
				break;
			case SourceEnum.LOCAL_IDO:
			default:
				this.openCreateLocalIdo();
		}
	}

	public close(): void {
		this.dialog.close();
	}

	private openCreateLocalIdo(): void {
		const options: IDialogOptions<void> = {
			title: "Create Data Service - Find IDO",
			template: require("../search/local-ido/search-wizard.html"),
			cssClass: "create-modal",
			id: "dataservice-search-wizard"
		};

		this.openDialog(options);
	}

	private openCreateInlineList(): void {
		const template =
			`<eir-wizard ng-controller="eirInlineListDataserviceWizardCtrl as wizardCtrl"></eir-wizard>`;

		const options: IDialogOptions<void> = {
			title: "Create Data Service - Static List",
			template,
			cssClass: "wizard-dialog large",
			id: "datasource-wizard"
		};

		this.openDialog(options);
	}

	private openIonApiSearch(): void {
		const options: IDialogOptions<void> = {
			title: "Create Data Service - Find API",
			template: require("../search/ion-api/ion-api-search-wizard.html"),
			cssClass: "create-modal",
			id: "dataservice-search-wizard"
		};

		this.openDialog(options);
	}

	private openDialog(options: IDialogOptions<void>): void {
		this.dialogService.show(options)
			.then((result: IDialogResult<IJsonGenericDatasource>) => {
				if (result.button === DialogButtonType.Ok) {
					if (result.value) {
						this.dialog.close(result);
					}
				} else if (result.button === DialogButtonType.Back) {
					this.dialog.show();
				} else { // cancel
					this.dialog.close();
				}
			});

		this.dialog.hide();
	}

	private initDialogEvents(): void {
		this.$element.find(`#${this.SELECT_SOURCE_ELEMENT_ID}`)
			.on("selected", event => {
				const selectOption = event.currentTarget as unknown as HTMLOptionElement;
				this.selectedSource = selectOption.value as SourceEnum;
			});
	}

	static add(module: ng.IModule): void {
		module.controller("eirSelectSourceCtrl", SelectSourceCtrl);
	}
}
