/**
 * Former component/helper.ts
 */
import { Injectable } from "@angular/core";

import type { IComponentFieldSetting } from "ng2/common/model/components/settings/component-field-setting.model";
import type { IComponentSettingCategory } from "ng2/common/model/components/settings/component-setting-category.model";

@Injectable()
export class ComponentService {

	/**
	 * * Get default component setting value on ref for setting category with title
	 * @param settingCategories
	 * @param title
	 * @param ref
	 */
	getDefaultComponentSettingValue<T>(
		settingCategories: IComponentSettingCategory[], ref: string, title: string
	): T {
		const componentCategory =
			settingCategories.find(category => category.title === title);

		let componentFieldSetting: IComponentFieldSetting;
		if (componentCategory.settings.length > 0) {
			componentFieldSetting = componentCategory.settings.find(setting => setting.ref === ref);
		}
		return componentFieldSetting.defaultValue;
	}
}
