import angular from "angular";

import {
	IChanged,
	InjectNames,
	LanguageConsts,
	SettingKeys,
	ComponentNames,
} from "ng2/common/constants/common.constant";
import { ValueTypeEnum } from "ng2/common/enums/value-type.enum";
import { IMap } from "ng2/common/model/imap.model";
import {
	IJsonLanguageComponent, ILanguage, IMingleLanguage, IMongooseString
} from "ng2/common/model/language.model";
import { IComponent } from "common/IComponent";
import { InstanceEvent } from "common/instance-event";

import { IJsonComponent } from "ng2/common/model/databearer/components/json-component.model";
import { IJsonDatagrid } from "ng2/common/model/databearer/components/json-datagrid.model";
import { IJsonValueSource } from "ng2/common/model/databearer/json-binding.model";

import { IJsonLookup } from "ng2/common/model/databearer/components/json-lookup.model";
import { IJsonRadioButtons } from "common/model/component/radiobutton.model";
import { IJsonTabs } from "common/model/component/tabs.model";

import { Helper } from "common/helper";
import { CommonService } from "common/services/common-service";
import {
	DialogService, IDialogOptions, IDialogResult
} from "common/services/dialog-service";
import { LanguageService } from "common/services/language-service";

import { IAutoCompleteOptions } from "common/model/sohoxi/sohoxi-autocomplete.model";
import { ILookupOptions } from "common/model/sohoxi/sohoxi-lookup.model";

import { SelectableEnum } from "ng2/common/constants/sohoxi.constant";
import { IDataGridColumn } from "ng2/common/model/sohoxi/sohoxi-datagrid.model";
import { IPageRequest } from "ng2/common/model/sohoxi/sohoxi-pager.model";
import { DatagridHelper } from "common/sohoxi/datagrid-helper";
import { Defines } from "components/defines";

export interface IComponentProperty {
	component: IComponent;
	property: string;
}

export class TranslationManagementService {
	private lookupOptions: ILookupOptions;
	public onTranslationsChangedEvent = new InstanceEvent<IMap<IJsonLanguageComponent[]>>();
	public static $inject = [
		InjectNames.LANGUAGE_SERVICE,
		InjectNames.COMMON_SERVICE,
		InjectNames.DIALOG_SERVICE
	];

	public constructor(
		private languageService: LanguageService,
		private commonService: CommonService,
		private dialogService: DialogService
	) {
		this.initLookupOptions();
	}

	/**
	* Open the translation dialog with component as parameter
	* This returns a promise with a value that contain a status change and a new value if the
	* selected component was changed.
	*
	* @public
	* @param {EditorComponent} component
	* @returns {ng.IPromise<IDialogResult<IChanged>>}
	* @memberof translationManagementService
	*/
	public openTranslations(component?: IComponent, property?: string): ng.IPromise<IDialogResult<IChanged>> {
		const dialogOptions: IDialogOptions<IComponentProperty> = {
			template: require("../../designer/editor/component-settings/valuesource-settings/translation-dialog.html"),
			title: "Translations",
			cssClass: "language-dialog",
			style: "min-width: 1050px; max-width: 1050px;",
			parameter: {
				component: component,
				property: property
			}
		};
		return this.dialogService.show(dialogOptions);
	}

	public openNewStringDialog(value?: string): ng.IPromise<IDialogResult<IMongooseString>> {
		const options: IDialogOptions<string> = {
			title: "Add New String",
			template: require("../../common/dialogs/add-string/add-string.html"),
			parameter: value
		};
		return this.dialogService.show(options);
	}

	public addGlobalString(globalString: IMongooseString): ng.IPromise<void> {
		return this.languageService.addGlobalString(globalString);
	}

	public getGlobalString(name: string): string {
		return this.languageService.getGlobalString(name);
	}

	public fetchGlobalStrings(locale?: string): ng.IPromise<IMap<IMongooseString>> {
		return this.languageService.fetchGlobalStrings(locale);
	}

	public getCurrentStringsList(): IMongooseString[] {
		const currentStringList = [];
		const currentStringDictionary = this.languageService.getCurrentStrings();
		for (const key in currentStringDictionary) {
			if (currentStringDictionary.hasOwnProperty(key) && typeof currentStringDictionary[key] !== "string") {
				currentStringList.push(currentStringDictionary[key]);
			}
		}
		return currentStringList;
	}

	public saveGlobalTranslations(globalStrings: IMap<IMongooseString[]>): ng.IPromise<void> {
		return this.languageService.saveGlobalStrings(globalStrings).then(() => {
			this.languageService.loadGlobalStrings();
		});
	}

	public deleteGlobalTranslations(globalStrings: IMap<IMongooseString[]>): ng.IPromise<void> {
		return this.languageService.deleteGlobalStrings(globalStrings).then(() => {
			this.languageService.loadGlobalStrings();
		});
	}

	public getSelectedLocale(): string {
		const existNewDefault = this.getProjectLanguages().some((language) => {
			return this.localeEquals(language.key, this.getCurrentLanguage());
		});
		return existNewDefault ? this.getCurrentLanguage() : this.getDefaultLanguage();
	}

	public getCurrentLanguage(): string {
		return this.languageService.getLanguage();
	}

	/**
	 * Removes translation from current project
	 * Either all translations for a component (to be used when removing a component)
	 * Or remove a specific property's translations
	 *
	 * @public
	 * @param {string} componentId
	 * @param {string} propertyId
	 *
	 */
	public removeTranslation(
		componentId: string,
		propertyId?: string,
		triggerEvent: boolean = true,
	): void {
		const currentProject = this.commonService.getCurrentProject();
		if (!propertyId) {
			currentProject.removeTranslation(componentId);
		} else {
			const translations = currentProject.getTranslation(componentId);
			const index = translations.findIndex((translation) => {
				return translation.property === propertyId;
			});
			translations.splice(index, 1);
		}

		if (triggerEvent) {
			this.onTranslationsChangedEvent.raise(this.commonService.getCurrentProject().getTranslations());
		}
	}

	/**
	* Get supported languages
	*
	* @public
	* @returns {IMingleLanguage[]}
	* @memberof translationManagementService
	*/
	public getSupportedLanguages(): IMingleLanguage[] {
		return this.languageService.getSupportedLanguages();
	}

	/**
	* Add new language to all languageComponents.
	* This method can also take a value as argument instead of the default value
	*
	* @public
	* @param {IMingleLanguage} newLanguage
	* @param {string} translatedValue
	* @memberof translationManagementService
	*/
	public addLanguage(newLanguage: IMingleLanguage, translatedValue?: string): void {
		const currentTranslations = this.commonService.getCurrentProject().getTranslationsList();
		currentTranslations.forEach((componentLanguage) => {
			const newComponentLanguage = {
				locale: newLanguage.key,
				value: translatedValue ? translatedValue : componentLanguage.translations[0].value
			};
			componentLanguage.translations.push(angular.copy(newComponentLanguage));
		});
		this.onTranslationsChangedEvent.raise(this.commonService.getCurrentProject().getTranslations());
	}

	/**
	* Delete a language on all languageComponents
	*
	* @public
	* @param {IMingleLanguage} deletedlanguage
	* @memberof translationManagementService
	*/
	public deleteLanguage(deletedlanguage: IMingleLanguage): void {
		const currentTranslations = this.commonService.getCurrentProject().getTranslationsList();
		currentTranslations.forEach((componentLanguage) => {
			componentLanguage.translations.forEach((language, index) => {
				if (this.localeEquals(language.locale, deletedlanguage.key)) {
					componentLanguage.translations.splice(index, 1);
				}
			});
		});
		this.onTranslationsChangedEvent.raise(this.commonService.getCurrentProject().getTranslations());
	}

	/**
	* Checks whether the locale keys is equal
	*
	* @public
	* @param {string} locale1
	* @param {string} locale2
	* @returns {boolean}
	* @memberof translationManagementService
	*/
	public localeEquals(locale1: string, locale2: string): boolean {
		// Some keys is only the two first characters
		const stringLength = 3;
		if (locale1.length < stringLength || locale2.length < stringLength) {
			const endIndex = 2;
			return locale1.substring(0, endIndex) === locale2.substring(0, endIndex);
		} else {
			return locale1 === locale2;
		}
	}

	/**
	* Checks whether the component has a corresponding languageComponent
	*
	* @public
	* @param {string} componentId
	* @returns {boolean}
	* @memberof translationManagementService
	*/
	public hasLanguageComponent(componentId: string, reference: string): boolean {
		const currentTranslations = this.commonService.getCurrentProject().getTranslations()[componentId];
		let hasTranslations: boolean = false;
		if (currentTranslations && reference) {
			hasTranslations = currentTranslations.some((translation) => {
				return translation.property === reference;
			});
		}
		return hasTranslations;
	}

	/**
	* Set a new value to the languageComponent with the right component & property
	*
	* @public
	* @param {string} componentId
	* @param {string} property
	* @param {ILanguage} updatedTranslation
	* @memberof translationManagementService
	*/
	public setTranslatableStringByProperty(
		componentId: string,
		property: string,
		updatedTranslation: ILanguage,
		triggerEvent: boolean = true,
	): void {
		const componentTranslations = this.commonService.getCurrentProject().getTranslations()[componentId];
		componentTranslations.forEach((componentLanguage) => {
			if (componentLanguage.property === property) {
				componentLanguage.translations.forEach((language) => {
					if (this.localeEquals(language.locale, updatedTranslation.locale)) {
						language.value = updatedTranslation.value;
					}
				});
			}
		});

		if (triggerEvent) {
			this.onTranslationsChangedEvent.raise(this.commonService.getCurrentProject().getTranslations());
		}
	}

	public setTranslatableStringByTranslationIndex(
		componentIndex: number,
		translationIndex: number,
		newValue: string
	): void {
		const projectLanguageComponents = this.commonService.getCurrentProject().getTranslationsList();
		const oldValue =  projectLanguageComponents[componentIndex].translations[translationIndex].value;
		if (oldValue !== newValue) { //if not changed, do nothing.
			projectLanguageComponents[componentIndex].translations[translationIndex].value = newValue;
			this.onTranslationsChangedEvent.raise(this.commonService.getCurrentProject().getTranslations());
		}
	}

	/**
	* returns the default language
	*
	* @public
	* @returns {string}
	* @memberof translationManagementService
	*/
	public getDefaultLanguage(): string {
		return this.languageService.getDefaultLanguage();
	}

	/**
	* returns the translated value from component & property.
	* The method can also take in a locale-argument if you want to prioritize on a specific language
	*
	* @public
	* @param {string} componentId
	* @param {string} property
	* @param {string} locale
	* @returns {string}
	* @memberof translationManagementService
	*/
	public getTranslatableStringByProperty(componentId: string, property: string, locale?: string): string {
		const currentTranslations = this.commonService.getCurrentProject().getTranslation(componentId);
		let currentLanguageKey = this.languageService.getLanguage();
		let languageValue;
		if (!currentTranslations) {
			return languageValue;
		}
		// first priority is the locale added
		// if not included check if current language is used.
		if (locale) {
			currentLanguageKey = locale;
		} else if (!this.projectHasLanguage(currentLanguageKey)) {
			currentLanguageKey = this.getDefaultLanguage();
		}
		currentTranslations.forEach((componentLanguage) => {
			if (componentLanguage.property === property) {
				languageValue = this.setTranslationFromComponentLanguage(
					componentLanguage.translations,
					currentLanguageKey,
					componentId,
					property
				);
			}
		});
		return languageValue;
	}

	/**
	* returns the translated value on the header.
	* TODO: remove when header is a component
	* The method can also take in a locale-argument if you want to prioritize on a specific language
	*
	* @public
	* @param {string} locale
	* @returns {string}
	* @memberof translationManagementService
	*/
	public getTranslatableHeaderString(locale?: string): string {
		const project = this.commonService.getCurrentProject();
		let languageValue;
		const currentTranslations = project.getTranslations()[project.rootComponent.id];
		if (currentTranslations) {
			let currentLanguageKey = this.languageService.getLanguage();
			// first priority is the locale added
			// if not included check if current language is used.
			if (locale) {
				currentLanguageKey = locale;
			} else if (!this.projectHasLanguage(currentLanguageKey)) {
				currentLanguageKey = this.getDefaultLanguage();
			}
			currentTranslations.forEach((componentLanguage) => {
				if (componentLanguage.property === SettingKeys.APPLICATION_HEADER) {
					for (let i = 0; i < componentLanguage.translations.length; i++) {
						const languageComponent = componentLanguage.translations[i];
						if (this.localeEquals(languageComponent.locale, currentLanguageKey)) {
							if (languageComponent.value === "" && languageComponent.locale !== LanguageConsts.DEFAULT.key) {
								//if value is empty and not default language, run this again with default language
								languageValue = this.getTranslatableHeaderString(LanguageConsts.DEFAULT.key);
							} else {
								languageValue = languageComponent.value;
							}
							break;
						}
					}
				}
			});
		}
		return languageValue;
	}

	/**
	* set the translated value to the languageComponent by id
	*
	* @public
	* @param {string} Id
	* @param {ILanguage} updatedTranslation
	* @memberof translationManagementService
	*/
	public setTranslatableStringById(id: string, updatedTranslation: ILanguage): void {
		const currentTranslations = this.commonService.getCurrentProject().getTranslationsList();
		currentTranslations.forEach((componentLanguage) => {
			if (componentLanguage.id === id) {
				componentLanguage.translations.forEach((language) => {
					if (this.localeEquals(language.locale, updatedTranslation.locale)) {
						language.value = updatedTranslation.value;
					}
				});
			}
		});
		this.onTranslationsChangedEvent.raise(this.commonService.getCurrentProject().getTranslations());
	}

	/**
	* checks whether the language is used or not
	*
	* @public
	* @param {string} locale
	* @returns {boolean}
	* @memberof translationManagementService
	*/
	public projectHasLanguage(locale: string): boolean {
		const currentTranslations = this.commonService.getCurrentProject().getTranslationsList();
		if (currentTranslations.length === 0) {
			return false;
		}
		// Need only to look at first
		return currentTranslations[0].translations.some((translation) => {
			return this.localeEquals(translation.locale, locale);
		});
	}

	/**
	* returns all the used languages
	*
	* @public
	* @returns {IMingleLanguage[]}
	* @memberof translationManagementService
	*/
	public getProjectLanguages(): IMingleLanguage[] {
		const projectLanguages: IMingleLanguage[] = [LanguageConsts.DEFAULT];
		const firstTranslation: IJsonLanguageComponent = this.commonService.getCurrentProject().getTranslationsList()[0];
		if (firstTranslation) {
			firstTranslation.translations.forEach((language) => {
				if (!this.localeEquals(language.locale, LanguageConsts.DEFAULT.key)) {
					projectLanguages.push({
						key: language.locale,
						value: this.getLocaleDisplayName(language.locale)
					});
				}
			});
		}
		return projectLanguages;
	}

	/**
	* returns the corresponding displayname from locale if any
	* or else return default.
	*
	* @public
	* @param {string} locale
	* @returns {string}
	* @memberof translationManagementService
	*/
	public getLocaleDisplayName(locale: string): string {
		const localeDisplayName = this.getSupportedLanguages().find((language) => {
			return this.localeEquals(language.key, locale);
		});
		return localeDisplayName ? localeDisplayName.value : LanguageConsts.DEFAULT.value;
	}

	public createGeneralLanguageComponent(
		componentId: string,
		reference: string,
		value: string,
		triggerEvent: boolean = true,
	): void {
		const newId = Helper.generateUniqueId();
		const languageComponent: IJsonLanguageComponent = {
			id: newId,
			componentId: componentId,
			property: reference,
			translations: []
		};
		if (this.hasLanguageComponent(componentId, reference)) {
			return;
		}

		this.getProjectLanguages().forEach((language) => {
			const newComponentLanguage: ILanguage = {
				locale: language.key,
				value: value
			};
			languageComponent.translations.push(newComponentLanguage);
		});
		this.commonService.getCurrentProject().addTranslation(languageComponent);

		if (triggerEvent) {
			this.onTranslationsChangedEvent.raise(this.commonService.getCurrentProject().getTranslations());
		}
	}

	public createDefaultLanguageComponents(component: IJsonComponent): void {
		this.createLanguageComponentFromBindableField(component);
		switch (component.typedef) {
			case ComponentNames.Tabs:
				(component as IJsonTabs).componentTabs.forEach((tab) => {
					this.createGeneralLanguageComponent(component.id, tab.id, tab.name);
				});
				break;
			case ComponentNames.RadioButton:
				(component as IJsonRadioButtons).componentRadioButtons.forEach((radioButton) => {
					this.createGeneralLanguageComponent(component.id, radioButton.id, radioButton.name);
				});
				break;
			case ComponentNames.Lookup:
			case ComponentNames.Datagrid:
				(component as IJsonDatagrid|IJsonLookup).columns.forEach((column) => {
					this.createGeneralLanguageComponent(component.id, column.id, column.name);
				});
				break;
			default:
			// Add new cases
		}
	}

	public getLookupOptions(): ILookupOptions {
		return this.lookupOptions;
	}

	public getAutoOptions(): IAutoCompleteOptions {
		const autoOptions: IAutoCompleteOptions = {
			source: (searchInput: string, done: Function) => {
				if (searchInput.charAt(0) === "{" && searchInput.length > 1) {
					const autoCompleteGlobalStrings: string[] = [];
					const maxItemsToShow = 11;
					const trimmedInput = searchInput.replace("{", "");

					const filteredList = this.getCurrentStringsList().filter((item) => {
						return item.Name.substring(0, trimmedInput.length) === trimmedInput;
					});
					const maxItems = filteredList.length < maxItemsToShow ? filteredList.length : maxItemsToShow;
					for (let i = 0; i < maxItems; i++) {
						autoCompleteGlobalStrings.push(filteredList[i].Name);

					}
					done(trimmedInput, autoCompleteGlobalStrings);
				}
			}
		};

		return autoOptions;
	}

	private initLookupOptions(): void {
		const columns: IDataGridColumn[] = [];
		columns.push({
			id: "Name",
			name: "Name",
			field: "Name",
			width: 250,
			resizable: false,
			sortable: true,
		});
		columns.push({
			id: "Value",
			name: "Value",
			field: "Value",
			width: 250,
			resizable: false,
			sortable: true,
			formatter: Soho.Formatters.Ellipsis
		});

		this.lookupOptions = {
			title: "Select Item",
			field: (selectedRow: IMap<string>) => {
				const propertyName = "Name";
				return "{" + selectedRow[propertyName] + "}";
			},
			match: () => {
				// fake function to bypass an issue on soho-code.
			},
			options: {
				paging: true,
				pagesize: 10,
				columns: columns,
				dataset: [],
				source: (request: IPageRequest, done: Function) => {
					const stringList = this.getCurrentStringsList();
					request.total = stringList.length;
					let datasetCopy = angular.copy(stringList);
					if (request.filterExpr && request.filterExpr[0]) {
						datasetCopy = DatagridHelper.getSearchResultOnPaths(datasetCopy, request.filterExpr[0], "Name");
					}
					if (request.sortId) {
						datasetCopy = DatagridHelper.sortDatasetByField(datasetCopy, request.sortField, request.sortAsc);
					}
					const page = DatagridHelper.getPage(datasetCopy, request);
					done(page, request);
				},
				selectable: SelectableEnum.Single,
				toolbar: {
					keywordFilter: true,
				}
			},
		};
	}

	private setTranslationFromComponentLanguage(
		translations: ILanguage[],
		locale: string,
		componentId: string,
		reference: string
	): string {
		let languageValue: string;
		for (let i = 0; i < translations.length; i++) {
			const languageComponent = translations[i];
			const localeEquals = this.localeEquals(languageComponent.locale, locale);
			if (localeEquals) {
				const localeNotDefault = languageComponent.locale !== LanguageConsts.DEFAULT.key;
				if (languageComponent.value === "" && localeNotDefault) {
					//if value is empty and not default language, run this again with default language
					languageValue = this.getTranslatableStringByProperty(componentId, reference, LanguageConsts.DEFAULT.key);
				} else {
					languageValue = languageComponent.value;
				}
				break;
			}
		}
		return languageValue;
	}

	/**
	* create a new language component either by existing values or default values.
	*
	* @public
	* @param {cp.IStaticComponent} componentClass
	* @param {IJsonComponent} component
	* @param {string[]} values
	* @memberof translationManagementService
	*/
	private createLanguageComponentFromBindableField(component: IJsonComponent): void {
		const componentClass = Defines.allComponents[component.typedef].class;
		componentClass.BINDABLE_FIELDS_2.forEach((bindable, index) => {
			if (bindable.valueType === ValueTypeEnum.LOCALIZED_STRING) {
				const value: string = ((component as any)[bindable.ref] as IJsonValueSource).value;
				this.createGeneralLanguageComponent(
					component.id,
					bindable.ref,
					value ? value : bindable.defaultValue.value
				);
			}
		});
	}

	public static add(ngModule: ng.IModule): void {
		ngModule.service(InjectNames.TRANSLATION_MANAGEMENT_SERVICE, TranslationManagementService);
	}
}
