import { ComponentClassNames, SettingKeys } from "ng2/common/constants/common.constant";
import { IComponentFieldSetting } from "ng2/common/model/components/settings/component-field-setting.model";
import { IJsonDatepicker } from "common/model/component/datepicker.model";
import { IDatepickerApi } from "common/model/sohoxi/sohoxi-datepicker.model";

import type { IEditorComponent } from "common/interface/ieditor-component";
import type { IEditorFlexbox } from "editor/components/editor-flexbox";

import { Datepicker } from "components/datepicker/datepicker";
import { EditorComponentCtrl, IEditorComponentScope } from "editor/components/editor-component";

export interface IEditorDatepicker extends IEditorComponent, Datepicker {
	parent: IEditorFlexbox;
	// Overrides to make dual extend work
	getScope(): IEditorComponentScope;
	getDatabearer(): IJsonDatepicker;
	getSohoXiApi(): IDatepickerApi;
}

export class EditorDatepickerCtrl extends EditorComponentCtrl {
	public static add(m: ng.IModule): void {
		m.controller(
			ComponentClassNames.EDITOR_COMPONENT_PREFIX + Datepicker.name,
			EditorDatepickerCtrl
		);
	}

	protected component: IEditorDatepicker;

	public setSetting(settingMeta: IComponentFieldSetting, newSettingValue: any): void {
		super.setSetting(settingMeta, newSettingValue);
		switch (settingMeta.ref) {
			case SettingKeys.DATE_FORMAT_SHOW:
				this.refresh();
				/* TODO: This timeout could be removed if we use syncronous way to update datepicker,
					i.e. not using xi-datepicker, but run element.datepicker(options); in DatePicker class
				*/
				const waitTimeForXidatepickerOptionsToUpdate = 10;
				this.$timeout(() => {
					this.component.datePickerApi.setValue(this.component.dateValue);
				}, waitTimeForXidatepickerOptionsToUpdate);
				break;
			case SettingKeys.DATE_FORMAT_BINDING:
				// Resetting value will update formatted binding
				this.component.setDateValue(this.component.dateValue);
				break;
			case SettingKeys.DATE_FORMAT_INPUT:
				const unparsedDateValue = this.editorService.readValueSource(
					this.component.getDatabearer().dateValue,
					this.component.getContext()
				);
				// Resetting bindable value will re-parse input
				this.component.setBindableValue(SettingKeys.DATE_VALUE, unparsedDateValue);
				break;
			case SettingKeys.DATE_TODAY:
				this.component.updateDatepickerValue();
				break;
			case SettingKeys.IS_READONLY:
				this.component.updateDatepickerReadonlyValue();
				break;
			default:
				// Other settings
		}
	}

	private refresh(): void {
		const inputElement = this.component.datePickerApi.element;
		// nulling placeholder to make soho update it
		inputElement.attr("placeholder", "");
		// Soho does not clean this attribute
		inputElement.attr("data-validate", "");
		this.component.setDatepickerOptions();
	}
}
