import angular from "angular";

import { componentsModule } from "../components/module";

import { AppLauncher } from "./app-launcher.component";
import { RuntimeCanvasDirective } from "./runtime-canvas-directive";
import { RuntimeService } from "./runtime-service";

export const runtimeModule = angular.module("runtimeModule", [
	componentsModule.name
]);

runtimeModule.config(["$qProvider", ($qProvider: any) => {
	$qProvider.errorOnUnhandledRejections(false);
}]);

AppLauncher.add(runtimeModule);
RuntimeCanvasDirective.add(runtimeModule);
RuntimeService.add(runtimeModule);
