import { WSForm } from "etc-assets/user-control-es";

import {
	ABFormScriptMethod,
	Config,
	MGPlatform,
	ProjectLimit,
	SaveFlagsEnum,
	ServerPaths,
} from "ng2/common/constants/common.constant";
import { SelectableEnum } from "ng2/common/constants/sohoxi.constant";
import { IJsonCompositeComponent } from "ng2/common/model/databearer/components/json-composite-component.model";
import { IDataGridColumn, IDataGridOptions } from "ng2/common/model/sohoxi/sohoxi-datagrid.model";

import { IExportDialogParams } from "../../../../common/dialogs/export/export-dialog";
import { Helper } from "../../../../common/helper";
import { DialogButtonType, IDialogOptions, IDialogResult } from "../../../../common/services/dialog-service";
import { IItemSpecificData, ManageDialogBase } from "./manage-dialog-base";

export class ManageCompositesCtrl extends ManageDialogBase<IJsonCompositeComponent> {

	public export(): void {
		const ids: string[] = this.selectedItems.map((item) => {
			return item.manifest.id;
		});
		const projectId = this.editorService.getCurrentProject().getId();
		const exportDefaultName = Helper.getFormattedDate();

		if (Config.MG_PLATFORM === MGPlatform.WEB) {
			const options: IDialogOptions<IExportDialogParams> = {
				title: "Export Composite Components",
				template: require("../../../../common/dialogs/export/export-dialog.html"),
				parameter: {
					type: "composite component",
					filename: exportDefaultName,
				}
			};
			this.dialogService.show(options).then((result: IDialogResult<string>) => {
				if (result.button === DialogButtonType.Ok) {
					const fileName = result.value;
					this.save().then(() => {
						this.projectService.exportProjectContent(
							projectId, ids, fileName, SaveFlagsEnum.COMPOSITE_COMPONENTS);
					});
				}
			});
		} else { // MG_PLATFORM === WinStudio
			WSForm.call({
				name: ABFormScriptMethod.SHOW_SAVE_FILE_DIALOG,
				parameters: [
					`${exportDefaultName}.iac`, // Filename
					"Export Filename",  // Caption
					"AppBuilder Composite Component (*.iac)|*.iac" // Filter
				],
				onCompleted: filePath => {
					this.projectService.exportProjectContent(
						projectId, ids, filePath, SaveFlagsEnum.COMPOSITE_COMPONENTS);
				}
			});
		}
	}

	protected getItemSpecificData(): IItemSpecificData {
		return {
			dataName: "Composite component",
			conflictKey: "manifest.displayName",
			urlName: ServerPaths.COMPOSITE_COMPONENTS,
			maxAllowed: ProjectLimit.MAX_COMPOSITE_COMPONENTS,
			maxErrorMessage: ProjectLimit.COMPOSITE_COMPONENT_ERROR_MESSAGE
		};
	}

	protected getDataList(): IJsonCompositeComponent[] {
		return this.editorService.getCurrentProject().getCompositeComponents();
	}

	protected addItemsToProject(composites: IJsonCompositeComponent[]): void {
		composites.forEach((composite) => {
			this.editorService.getCurrentProject().addCompositeComponent(composite);
		});
	}

	protected initGrid(): void {
		const columns: IDataGridColumn[] = [];
		columns.push({
			id: "selectionCheckbox",
			sortable: false,
			resizable: false,
			formatter: Soho.Formatters.SelectionCheckbox,
			align: "center"
		});
		columns.push({
			id: "displayName",
			name: "Name",
			field: "manifest.displayName",
			width: 266,
			resizable: false,
			sortable: true,
			formatter: Soho.Formatters.Ellipsis
		});
		columns.push({
			id: "author",
			name: "Author",
			field: "manifest.authorDisplayName",
			width: 240,
			resizable: false,
			sortable: true,
			formatter: Soho.Formatters.Ellipsis
		});
		columns.push({
			id: "date",
			name: "Created",
			field: "manifest.creationDate",
			width: 150,
			resizable: false,
			sortable: true,
			formatter: Soho.Formatters.Date
		});
		columns.push({
			id: "datasources",
			name: "Data Services",
			field: "datasources.length",
			width: 120,
			resizable: false,
			sortable: true,
			formatter: Soho.Formatters.Integer
		});
		columns.push({
			id: "interactions",
			name: "Interactions",
			field: "interactions.length",
			width: 120,
			resizable: false,
			sortable: true,
			formatter: Soho.Formatters.Integer
		});

		columns.push({
			id: "expressions",
			name: "Expressions",
			field: "expressions.length",
			width: 120,
			resizable: false,
			sortable: true,
			formatter: Soho.Formatters.Integer
		});

		const datagridOptions: IDataGridOptions<IJsonCompositeComponent> = {
			columns: columns,
			dataset: [],
			selectable: SelectableEnum.Multiple,
			enableTooltips: true,
			toolbar: {
				title: "Local Composite Components",
				results: false,
				dateFilter: false,
				keywordFilter: true,
				advancedFilter: false,
				actions: false,
				views: true,
				rowHeight: true,
				maxVisibleButtons: 10,
			}
		};

		this.datagridOptions = datagridOptions;
		const currentProject = this.editorService.getCurrentProject();
		this.datagridOptions.dataset = currentProject.getCompositeComponents();
	}

	protected save(): ng.IPromise<void> {
		return this.editorService.saveCurrent(SaveFlagsEnum.COMPOSITE_COMPONENTS + SaveFlagsEnum.MANIFEST);
	}

	protected deleteItems(idsToDelete: string[]): ng.IPromise<IJsonCompositeComponent[]> {
		const currentProject = this.editorService.getCurrentProject();
		const removedComposites: IJsonCompositeComponent[] = [];
		const deferred = this.editorService.getNewDeferred<IJsonCompositeComponent[]>();
		idsToDelete.forEach((id) => {
			removedComposites.push(currentProject.removeCompositeComponent(id));
		});
		deferred.resolve(removedComposites);
		return deferred.promise;
	}

	protected getItemDisplayName(item: IJsonCompositeComponent): string {
		return item.manifest.displayName;
	}

	protected getItemId(item: IJsonCompositeComponent): string {
		return item.manifest.id;
	}

	protected setItemId(item: IJsonCompositeComponent, newId: string): void {
		item.manifest.id = newId;
	}

	static add(m: ng.IModule): void {
		m.controller("eirManageCompositesCtrl", ManageCompositesCtrl);
	}
}
