import jsonpath from "jsonpath";

import { AngularConstants } from "../../../../../common/angular-constants";

import type { IDialogBaseScope } from "../../../../../common/defines";

import { DialogButtonType, DialogService } from "../../../../../common/services/dialog-service";

import { InjectNames } from "ng2/common/constants/common.constant";

import type { IJsonGenericDatasource2 } from "ng2/common/model/databearer/dataservice/json-datasource.model";

import type { ISchemaObject } from "ng2/common/model/swagger.model";

import { JsonHelper } from "ng2/common/helpers/json.helper";

export interface IViewJsonDialogParameters {
	dataservice: IJsonGenericDatasource2;
	isInput: boolean;
	showHelp?: Function;
}

export class ViewJsonDialogCtrl {

	schemaString = "";
	sampleString = "";
	schemaHasError: boolean;
	sampleHasError: boolean;
	isModified: boolean;
	isInput: boolean;
	root: string;
	resetEnabled = false;
	sampleError: string;
	rootElementInvalid = false;
	private originalSchema: ISchemaObject;
	private showHelp: Function;

	get saveDisabled(): boolean {
		return !this.isModified || this.schemaHasError || this.sampleHasError || this.rootElementInvalid;
	}

	static $inject = [
		AngularConstants.SCOPE,
		InjectNames.DIALOG_SERVICE,
	];

	constructor(
		private $scope: IDialogBaseScope<IViewJsonDialogParameters>,
		private dialogService: DialogService,
	) {
		const parameters = $scope.eirDialog.parameter;
		const schema = parameters.isInput ? parameters.dataservice.inputSchema : parameters.dataservice.outputSchema;
		this.originalSchema = parameters.dataservice.originalSchema; //output
		const json = parameters.isInput ? parameters.dataservice.bodyTemplate : parameters.dataservice.outputJson;
		this.root = parameters.isInput ?
			parameters.dataservice.inputRootElement : parameters.dataservice.outputRootElement;
		this.showHelp = parameters.showHelp;
		this.isInput = parameters.isInput;

		if (this.originalSchema) {
			this.resetEnabled = true;
		}

		if (schema) {
			this.schemaString = JSON.stringify(schema, null, "  ");
			if (json) {
				this.sampleString = json;
			} else {
				this.generateSample();
			}
		} else if (json) {
			this.sampleString = json;
			this.generateSchema();
		}
	}

	generateSample(): void {
		try {
			if (this.schemaString) {
				const parsedSchema = JSON.parse(this.schemaString);
				const sample = JsonHelper.generateSample(parsedSchema);
				this.sampleString = JSON.stringify(sample, null, "  ");
				this.schemaHasError = false;
				this.sampleHasError = false;
				this.sampleError = "";
			}
		} catch (error) {
			console.warn("Schema has error:", error);
			this.schemaHasError = true;
			if (error instanceof Error) {
				this.sampleError = error.message.split("<br/>").join("");
			} else {
				this.sampleError = "Ops, something went wrong. See Console for details.";
			}
		}
	}

	generateSchema(): void {
		try {
			const parsedSample = JSON.parse(this.sampleString);
			const schema = JsonHelper.generateSchema(parsedSample);
			this.schemaString = JSON.stringify(schema, null, "  ");
			this.sampleHasError = false;
			this.schemaHasError = false;
		} catch (error) {
			console.warn("Sample has error:", error);
			this.sampleHasError = true;
		}
	}

	onChangeRootElement(): void {
		this.rootElementInvalid = JsonHelper.isInvalidJsonPath(this.root);
	}

	save(): void {
		this.$scope.eirDialog.close({
			button: DialogButtonType.Ok,
			value: {
				json: this.sampleString,
				schema: JsonHelper.generateSchema(JSON.parse(this.sampleString)),
				root: this.root,
			},
		});
	}

	refreshSchema(): void {
		this.dialogService.showYesNo(
			"Warning",
			"All user changes will be discarded, continue?",
			"warning-text")
			.then(result => {
				if (result.button === DialogButtonType.Yes) {
					this.schemaString = JSON.stringify(this.originalSchema, null, "  ");
					this.isModified = true;
					this.generateSample();
					this.generateSchema();
				}
			});
	}

	close(): void {
		this.$scope.eirDialog.close({
			button: DialogButtonType.Cancel,
			value: null,
		});
	}

	static add(m: ng.IModule): void {
		m.controller("eirViewJsonDialogCtrl", ViewJsonDialogCtrl);
	}
}
