import angular from "angular";
import { AngularConstants } from "common/angular-constants";

import { EditorComponentCtrl } from "editor/components/editor-component";

import { DesignerService } from "../../services/designer-service";
import { EditorService } from "editor/services/editor-service";

import { Helper } from "common/helper";
import { EditorHelper } from "editor/helper";

import {
	CanvasModeEnum,
	CssConstantKeys,
	CssConstantValues,
	EventTypes,
	InjectNames,
	InsertEventTypes,
	Numbers,
} from "ng2/common/constants/common.constant";
import { OrientationEnum } from "ng2/common/enums/orientation.enum";
import { IJsonComponent } from "ng2/common/model/databearer/components/json-component.model";

import type { IEditorComponent } from "common/interface/ieditor-component";
import type { IEditorFlexbox } from "editor/components/editor-flexbox";

import { IJQueryDragEventObject, IJQueryMouseEventObject, ViewEnum } from "common/defines";
import { InstanceEvent } from "common/instance-event";
import { Defines } from "components/defines";
import { SidebarViewEnum } from "editor/defines";

interface IMousePosition {
	x: number;
	y: number;
}

export class InsertComponentService {
	private currentIndex: number;
	private currentParent: IEditorComponent;
	private lastMousePosition: IMousePosition;
	private timerPromise: ng.IPromise<any>;
	private undoFunction: Function;

	private insertComponent: IJsonComponent;
	private insertType: string;
	private clipboardComponent: IJsonComponent;
	private insertParent: IEditorFlexbox;

	private previousInsertParent: IEditorFlexbox;
	private finishedDeferred: ng.IDeferred<IEditorFlexbox>;

	public clipboardComponentChangedEvent = new InstanceEvent<IJsonComponent>();

	public static $inject = [
		AngularConstants.Q,
		AngularConstants.TIMEOUT,
		InjectNames.EDITOR_SERVICE,
		InjectNames.DESIGNER_SERVICE
	];

	public constructor(
		private $q: ng.IQService,
		private $timeout: ng.ITimeoutService,
		private editorService: EditorService,
		private designerService: DesignerService
	) {

		designerService.viewChangedEvent.on((view: ViewEnum) => {
			if (view !== ViewEnum.EDITOR) {
				// Clear selection when navigating from editor
				this.clearSelection();
				this.cancelInserting();
			}
		});

		$(document).on(EventTypes.DRAGEND, (event) => {
			EditorComponentCtrl.dragEnterCounter = 0;
			this.cancelInserting();
		});
	}

	public clearSelection(): void {
		this.editorService.deselectComponent();
		this.editorService.setSidebarView(SidebarViewEnum.NEW_COMPONENT);
		this.setClipboardComponent(null);
	}

	public setClipboardComponent(component: IJsonComponent): void {
		this.clipboardComponent = component;
		this.clipboardComponentChangedEvent.raise(component);
	}

	public getClipboardComponent(): IJsonComponent {
		return this.clipboardComponent;
	}

	public getInsertComponent(): IJsonComponent {
		return this.insertComponent;
	}

	private setInsertComponent(component?: IJsonComponent): void {
		if (component) {
			// Workaround to reset the counter when inserting
			EditorComponentCtrl.dragEnterCounter = 0;
		}
		this.insertComponent = component;
	}

	public setInsertParent(newInsertParent?: IEditorFlexbox): void {
		const oldInsertParent = this.insertParent;
		if (oldInsertParent) {
			oldInsertParent.setIsInsertParent(false);
			this.insertHandlerOut(oldInsertParent);
		}
		if (newInsertParent) {
			newInsertParent.setIsInsertParent(true);
		} else {
			this.showCursorNoDrop(true);
		}
		this.insertParent = newInsertParent;
	}

	/**
	 * Sets canvas in INSERT-mode and returns a promise that,
	 * when resolved, returns the new parent for the inserted component
	 */
	public startInserting(
		component: IJsonComponent,
		insertType: string = InsertEventTypes.COPY
	): ng.IPromise<IEditorComponent> {
		if (this.finishedDeferred) {
			this.finishedDeferred.reject();
		}
		this.finishedDeferred = this.$q.defer<IEditorFlexbox>();
		this.insertType = insertType;
		this.setInsertComponent(component);
		this.editorService.setCanvasMode(CanvasModeEnum.INSERT);
		return this.finishedDeferred.promise;
	}

	public cancelInserting(): void {
		this.resetVariables();

		if (this.finishedDeferred) {
			this.finishedDeferred.reject();
		}
	}

	public finishInsert(
		event: IJQueryMouseEventObject | IJQueryDragEventObject,
		finalParent: IEditorFlexbox
	): void {
		if (this.canInsertAt(finalParent, event)) {
			this.cancelTimer();
			// Using original event to work with both drag and click
			const newMousePosition = { x: event.originalEvent.offsetX, y: event.originalEvent.offsetY };
			const foundIndex = this.currentIndex;
			const isFinalInsert = true;
			this.updateGhostPosition(finalParent, this.insertComponent, foundIndex, isFinalInsert);
			this.resetVariables();
			//finalParent.updateClassesAndStyle();
			this.finishedDeferred.resolve(finalParent);
		} else {
			this.cancelInserting();
		}
	}

	public insertIteration(
		event: IJQueryMouseEventObject | IJQueryDragEventObject,
		newParent: IEditorFlexbox
	): void {
		// Using original event to work with both drag and click
		const newMousePosition = {
			x: event.originalEvent.offsetX,
			y: event.originalEvent.offsetY
		};

		if (!this.canInsertAt(newParent, event)) {
			this.lastMousePosition = newMousePosition;
			return;
		}
		// console.log("insertIteartion");

		this.cancelTimer();

		const TIME_UNTIL_REGISTER_NEXT_MOUSE_POSITION = 16;
		this.timerPromise = this.$timeout(() => {
			// console.log("insidetimer");
			if (!this.movedFarEnough(newParent, newMousePosition)) {
				// To update cursor
				this.canInsertAt(newParent, event);
				return;
			}

			this.lastMousePosition = newMousePosition;
			const foundIndex = this.getInsertIndex(newParent, newMousePosition);
			const updateGhostPosition =
				this.currentParent !== newParent || foundIndex !== this.currentIndex;

			if (updateGhostPosition) {
				this.currentIndex = foundIndex;
				this.currentParent = newParent;

				this.updateGhostPosition(newParent, this.insertComponent, foundIndex);
			}
		}, TIME_UNTIL_REGISTER_NEXT_MOUSE_POSITION);
	}

	public canInsertAt(parentComponent: IEditorFlexbox, event: any): boolean {
		// Can insert when parent doesnt contain big component and
		let canInsert = !EditorHelper.containsBigComponent(parentComponent) &&
			// insertComponent is not big component or
			(!EditorHelper.isBigComponent(this.insertComponent) ||
				// parent doesn't have any children and insertComponent is big component or
				(!this.hasParentChildren(parentComponent) && EditorHelper.isBigComponent(this.insertComponent)) ||
				// except parent doesnt have child and insertComponent is layout component
				(!this.hasParentChildren(parentComponent) &&
					Defines.allComponents[this.insertComponent.typedef].isLayoutComponent));

		if (!canInsert) {
			canInsert = parentComponent.getDatabearer().children[0] === this.insertComponent;
		}

		this.showCursorNoDrop(!canInsert, event);

		return canInsert;
	}

	private showCursorNoDrop(enable: boolean, event?: IJQueryDragEventObject): void {
		const insideRulerElement = angular.element("body");

		const newCursorValue = (enable) ? CssConstantValues.CURSOR_NODROP : this.insertType;
		if (event && event.type === EventTypes.DRAGOVER) {
			// Changing cursor during drag is only supported in Safari and Edge
			// It creates flickering in IE11, it makes no difference in Chrome
			if (Helper.isSafari() || Helper.isEdge()) {
				// Existing components
				insideRulerElement.css(CssConstantKeys.CURSOR, newCursorValue);
				// New components
				$(".componentIconOverlay").css(CssConstantKeys.CURSOR, newCursorValue);
			}
			if (enable) {
				(event.originalEvent as any).dataTransfer.dropEffect = "none";
			} else {
				(event.originalEvent as any).dataTransfer.dropEffect = this.insertType;
				event.preventDefault();
			}
		} else { // for click-click insert
			insideRulerElement.css(CssConstantKeys.CURSOR, newCursorValue);
		}
	}

	private hasParentChildren(parentComponent: IEditorFlexbox): boolean {
		return parentComponent.getDatabearer().children.length > 0;
	}

	private movedFarEnough(newParent: IEditorFlexbox, newMousePosition: IMousePosition): boolean {
		const lastMousePosition = this.lastMousePosition;

		if (!lastMousePosition) {
			return true;
		}

		const availableInsertPoints = newParent.getInsertPoints();
		const orientation = availableInsertPoints ? availableInsertPoints.orientation : null;
		const notNewParent = this.currentParent !== newParent;
		const isVertical = orientation === OrientationEnum.VERTICAL;
		const axis = isVertical ? "y" : "x";
		const movedEnough = Math.abs(lastMousePosition[axis] - newMousePosition[axis]) > Numbers.TEN;

		return notNewParent || movedEnough;
	}

	private updateGhostPosition(
		parentComponent: IEditorFlexbox,
		newComponent: IJsonComponent,
		foundIndex: number,
		isFinalInsert?: boolean
	): void {
		if (this.undoFunction) {
			this.undoFunction();
			EditorHelper.updateParentComponentSizeClass(this.previousInsertParent);
		}
		if (newComponent) {
			this.undoFunction = parentComponent.insert(newComponent, foundIndex, isFinalInsert);
		} else {
			console.warn("[InsertComponentService] Tried to insert null component");
		}

		parentComponent.updateClassesAndStyle();
		parentComponent.updateIsFlexParent();

		EditorHelper.updateParentComponentSizeClass(parentComponent);
		this.previousInsertParent = parentComponent;
	}

	private getInsertIndex(newParent: IEditorFlexbox, mousePosition: IMousePosition): number {
		const availableInsertPoints = newParent.getInsertPoints();
		const isHorizontal = availableInsertPoints.orientation === OrientationEnum.HORIZONTAL;
		const isVertical = availableInsertPoints.orientation === OrientationEnum.VERTICAL;
		let foundIndex: number;

		if (isHorizontal || isVertical) {
			const positionToCompare = isVertical
				? mousePosition.y
				: mousePosition.x;

			for (let index = 0; index < availableInsertPoints.points.length; index++) {
				if (positionToCompare < availableInsertPoints.points[index]) {
					foundIndex = index;
					break;
				}
			}
		} else {
			console.warn("[InsertHandler] Unsupported orientation");
		}

		return foundIndex;
	}

	public insertHandlerOut(parentComponent: IEditorFlexbox): void {
		this.cancelTimer();
		if (this.undoFunction) {
			this.undoFunction();
			if (parentComponent) {
				EditorHelper.updateParentComponentSizeClass(parentComponent);
			}
			this.undoFunction = null;
		}
		this.currentParent = null;
	}

	private cancelTimer(): void {
		const promise = this.timerPromise;
		if (promise) {
			this.$timeout.cancel(promise);
			this.timerPromise = null;
		}
	}

	private resetVariables(): void {
		this.lastMousePosition = null;
		this.undoFunction = null;
		this.currentIndex = null;
		this.currentParent = null;
		this.setInsertComponent(null);
		if (this.insertParent) {
			this.insertParent.forEachChild((child: IEditorComponent) => {
				if (child.getScope().eirEditorComponent.isInsertChild) {
					child.setIsInsertChild(false);
				}
			}, false);
		}
		this.setInsertParent(null);
		angular.element("body").css(CssConstantKeys.CURSOR, "");
		angular.element(".componentIconOverlay").css(CssConstantKeys.CURSOR, CssConstantValues.CURSOR_POINTER);
	}

	public static add(ngModule: ng.IModule): void {
		ngModule.service(InjectNames.INSERT_COMPONENT_SERVICE, InsertComponentService);
	}
}
