import { ComponentClassNames } from "ng2/common/constants/common.constant";

import type { IJsonTabPanel } from "common/model/component/tabs.model";

import type { IEditorComponent } from "common/interface/ieditor-component";
import { TabPanel } from "components/tabs/tab-panel";
import type { IEditorComponentScope } from "editor/components/editor-component";
import { EditorFlexboxCtrl, IEditorFlexbox } from "editor/components/editor-flexbox";
import type { IEditorTabs } from "editor/components/editor-tabs";

export interface IEditorTabPanel extends IEditorFlexbox, TabPanel {
	children: IEditorComponent[];
	parent: IEditorTabs;
	// Overrides to make dual extend work
	getScope(): IEditorComponentScope;
	getDatabearer(): IJsonTabPanel;
}

export class EditorTabPanelCtrl extends EditorFlexboxCtrl {
	static add(m: ng.IModule): void {
		m.controller(
			ComponentClassNames.EDITOR_COMPONENT_PREFIX + TabPanel.name,
			EditorTabPanelCtrl
		);
	}

	protected component: IEditorTabPanel;

}
