import angular from "angular";

import {
	InteractionPairOperation, MaskProcess, SettingInputKeys,
	SettingKeys, SidebarTabTypes
} from "ng2/common/constants/common.constant";
import { BindingTypeEnum } from "ng2/common/enums/binding-type.enum";
import { SizeTypeEnum } from "ng2/common/enums/size-type.enum";
import { ValueTypeEnum } from "ng2/common/enums/value-type.enum";
import { IComponentSettingCategory } from "ng2/common/model/components/settings/component-setting-category.model";
import { IDataPair } from "ng2/common/model/data-pair.model";
import { IJsonSize } from "ng2/common/model/databearer/components/json-component.model";
import { IJSonMaskSettings, IJsonTextBox } from "ng2/common/model/databearer/components/json-textbox.model";
import { IJsonBindableField } from "ng2/common/model/databearer/json-binding.model";
import { ILayoutComponent } from "../../common/interface/ilayout-component";

import { IMaskOptions } from "../../common/model/sohoxi/sohoxi-mask.model";
import { Component } from "../component";

import { InteractionDefines } from "ng2/common/constants/interaction-defines.constant";

import { BackwardCompatible } from "ng2/common/project/backward-compatible";
import { FrameworkService } from "../services/framework-service";

export class TextBox extends Component {
	public static AVAILABLE_WIDTHS: IDataPair[] = [
		{ name: "Tiny", value: "input-xs" },
		{ name: "Small", value: "input-sm" },
		{ name: "Medium", value: "input-mm" },
		{ name: "Large", value: "input-default" }
	];
	public static DEFAULT_WIDTH: IJsonSize = { sizeType: SizeTypeEnum.AUTO };
	public static DEFAULT_HEIGHT: IJsonSize = { sizeType: SizeTypeEnum.AUTO };
	public static SOURCE_TRIGGERS: IDataPair[] = InteractionDefines.SOURCE_TRIGGERS;
	public static TARGET_ACTIONS: IDataPair[] = [
		InteractionPairOperation.GET,
		InteractionPairOperation.CLEAR
	];
	public static DEFAULT_ACTION: IDataPair = InteractionPairOperation.GET;
	public static DEFAULT_TRIGGER: IDataPair = InteractionPairOperation.ON_LOAD;
	public static SETTING_FIELDS: IComponentSettingCategory[] =
		(<IComponentSettingCategory[]>[{
			title: "Style",
			tab: SidebarTabTypes.STYLE,
			settings: [{
				title: "Show Label",
				boundarySetting: true,
				ref: SettingKeys.SHOW_LABEL,
				inputType: SettingInputKeys.CHECKBOX,
				defaultValue: true,
			}, {
				title: "Read Only",
				ref: SettingKeys.IS_READONLY,
				inputType: SettingInputKeys.CHECKBOX,
				defaultValue: false,
				button: {
					icon: "#icon-unlocked",
					tooltip: "Disable Component When...",
					dialog: true
				}
			}]
		}, {
			title: "Width",
			tab: SidebarTabTypes.STYLE,
			settings: [{
				boundarySetting: true,
				ref: SettingKeys.BOX_WIDTH,
				inputType: SettingInputKeys.RADIO,
				dataset: TextBox.AVAILABLE_WIDTHS,
				defaultValue: "input-default",
			}]
		}]).concat(Component.SETTING_FIELDS);

	public static BINDABLE_FIELDS_2: IJsonBindableField[] = [{
		name: "Text",
		ref: SettingKeys.TEXT,
		valueType: ValueTypeEnum.STRING,
		write: true,
		read: true,
		defaultValue: { Type: BindingTypeEnum.STATIC_VALUE, value: "" }
	}, {
		name: "Label",
		ref: SettingKeys.LABEL,
		valueType: ValueTypeEnum.LOCALIZED_STRING,
		write: true,
		read: false,
		defaultValue: { Type: BindingTypeEnum.STATIC_VALUE, value: "" }
	}, {
		name: "Placeholder",
		ref: SettingKeys.PLACEHOLDER,
		valueType: ValueTypeEnum.LOCALIZED_STRING,
		write: true,
		read: false,
		defaultValue: { Type: BindingTypeEnum.STATIC_VALUE, value: "" }
	}
	];

	public text: string;
	public label: string;
	public showLabel: boolean;
	public placeholder: string;
	public boxWidth: string;
	public editType: string;
	public lengthWarning: boolean;
	public maxLength = 2000;
	protected databearer: IJsonTextBox;

	constructor(
		obj: IJsonTextBox,
		$element: ng.IAugmentedJQuery,
		parent: ILayoutComponent,
		frameworkService: FrameworkService
	) {
		super(obj, $element, parent, frameworkService);
		this.showLabel = BackwardCompatible.getShowLabel(obj);
		this.databearer.mask = BackwardCompatible.tryGetMask(obj);
		this.lengthWarning = false;

		angular.element(() => {
			this.updateMaskOptions();
		});
	}

	public setBindableValue(bindableReference: string, value: any): void {
		super.setBindableValue(bindableReference, value);
		if (this.getMask().process !== MaskProcess.NONE) {
			this.frameworkService.getTimeout()(() => {
				this.updateMaskOptions();
			});
		}
	}

	public updateMaskOptions(maskOptions?: IJSonMaskSettings): void {
		this.editType = "text";
		if (maskOptions) {
			this.databearer.mask = maskOptions;
		}
		const mask = this.databearer.mask;
		const element = this.element.find("#" + this.getId());
		if (mask && element) { //old textboxes < 2020.05 do not have mask
			const maskApi = element.data("mask");
			if (maskApi) {
				maskApi.destroy(); //remove all the previous mask settings
				element.removeClass("is-number-mask"); //Remove this class so typing is on the left side
			}

			let maskOptions: IMaskOptions;
			switch (mask.process) {
				case MaskProcess.NUMBER:
					maskOptions = {
						process: mask.process,
						patternOptions: {
							allowDecimal: mask.decimalLimit > 0 ? true : false,
							allowNegative: mask.allowNegative,
							decimalLimit: mask.decimalLimit,
							integerLimit: mask.integerLimit,
							allowThousandsSeparator: mask.allowThousandsSeparator,
							prefix: mask.prefix
						}
					};
					break;
				case MaskProcess.DATE_TIME:
					maskOptions = {
						process: mask.process,
						patternOptions: {
							format: mask.format
						}
					};
					break;
				case MaskProcess.STRING:
					maskOptions = {
						pattern: mask.format
					};
					break;
				case MaskProcess.PASSWORD:
					this.editType = mask.process;
					break;
				case MaskProcess.NONE:
					// This is a fix for masking acc-tests.
					// For some reason < input > does not get a value in its type when masking option is set to 'none'
					element.attr("type", "text");
					break;
				default:
					// Mask not supported
					break;
			}

			if (maskOptions) {
				element.mask(maskOptions);
			} else {
				element.mask();
			}
		}
	}

	public newBoundaryEntered(boundaryId: string): void {
		super.newBoundaryEntered(boundaryId);
		this.setBoundarySettingIfChanged<boolean>(SettingKeys.SHOW_LABEL);

		let newBoxWidth = this.getBoundarySetting<string>(SettingKeys.BOX_WIDTH);
		newBoxWidth = BackwardCompatible.tryGetBoxWidth(newBoxWidth);
		if (this.boxWidth !== newBoxWidth) {
			this.boxWidth = newBoxWidth;
		}
	}

	public clear(): void {
		this.text = "";
	}

	public getMask(): IJSonMaskSettings {
		return this.databearer.mask;
	}

	public onUpdateValue(): void {
		super.onUpdateValue();
		this.lengthWarning = this.text.length === this.maxLength;
	}

	public static directive = [() => {
		return {
			template: require("./textbox.html"),
			replace: true
		};
	}];
}
