import angular from "angular";
import { AngularConstants } from "../../../common/angular-constants";

import { IDatasource } from "common/model/datasource/datasource.model";
import {
	CanvasModeEnum, InjectNames, InteractionActionEnum, InteractionPairOperation,
	SaveFlagsEnum, ValueObject
} from "ng2/common/constants/common.constant";
import { DatasourceTypeEnum } from "ng2/common/enums/datasource-type.enum";
import { IJsonDatasource } from "ng2/common/model/databearer/dataservice/json-datasource.model";
import { IBoundary } from "ng2/common/model/project/boundary.model";
import { Interaction } from "ng2/components/interaction/interaction";
import {
	DialogButtonType, DialogService, IDialogOptions, IDialogResult
} from "../../../common/services/dialog-service";
import { ComponentsHelper } from "../../../components/helper";
import type { IEditorComponent } from "common/interface/ieditor-component";
import { EditorService } from "../services/editor-service";
import { InteractionManagementService } from "../services/interaction-management-service";
import { SettingsBase } from "./settings-base";

export interface ISettingsScope extends ng.IScope {
	hideSidebar: Function;
}

export class SettingsCtrl extends SettingsBase {
	public selectedComponentClassName: string;
	public showOverlay: boolean;

	public currentBoundary: IBoundary;
	public availableDatasources: IJsonDatasource[];
	public settingDatasource: IJsonDatasource;

	static $inject = [
		AngularConstants.SCOPE,
		InjectNames.EDITOR_SERVICE,
		InjectNames.DIALOG_SERVICE,
		InjectNames.INTERACTION_MANAGEMENT_SERVICE,
	];

	constructor(
		protected $scope: ISettingsScope,
		editorService: EditorService,
		private dialogService: DialogService,
		private interactionManagementService: InteractionManagementService,
	) {
		super($scope, editorService);

		this.unsubscribers.push(editorService.currentBoundaryChangedEvent.on((newBoundary: IBoundary) => {
			this.currentBoundary = newBoundary;
		}));
		this.currentBoundary = editorService.getCurrentBoundary();

		this.unsubscribers.push(editorService.canvasModeChangedEvent.on((mode: CanvasModeEnum) => {
			this.showOverlay = mode === CanvasModeEnum.FINDER || mode === CanvasModeEnum.WIZARD;
		}));

		this.refreshAvailableDatasources();

		this.onComponentChanged(editorService.getSelectedComponent());
	}

	private refreshAvailableDatasources(): void {
		this.availableDatasources = this.editorService.getCurrentProject().getDatasourceDatabearers();
		this.availableDatasources.unshift({ displayName: ValueObject.TARGET_CONTEXT, id: undefined });
	}

	public manageDatasources(): void {
		const options: IDialogOptions<any> = {
			title: "Project Data Services",
			template: require("./manage-dialogs/datasources-dialog.html"),
			cssClass: "datasources-manage-dialog",
			style: "min-width: 1000px; max-width: 1125px;"
		};

		this.dialogService.show(options).then((result: IDialogResult<any>) => {
			if (result && result.button === DialogButtonType.Ok) {
				this.refreshAvailableDatasources();
			}
		});
	}

	public changeSidebarTab(item: string): void {
		this.editorService.setCurrentSidebarTab(item);
	}

	public hideSidebar(): void {
		this.editorService.setSidebarVisibility(false);
	}

	protected onComponentChanged(newComponent: IEditorComponent): void {

		this.selectedComponent = newComponent;

		if (this.selectedComponent) {
			this.selectedComponentClassName = ComponentsHelper.getComponentDisplayName(newComponent.getTypedef());
			this.settingDatasource = this.selectedComponent.getDatasource()
				? newComponent.getDatasource().getDatabearer()
				: null;
		} else {
			this.selectedComponentClassName = "";
		}
	}

	public onChangeDisplayName(): void {
		//Set the new width on the selectionbox.
		const extraDisplayNameWidth = 6;
		const extraWrapperWidth = 33;
		const newWidth = this.selectedComponent.getDisplayName().length * extraDisplayNameWidth;
		angular.element(".selectionbox-wrapper").css("width", newWidth + extraWrapperWidth);
		angular.element(".selBoxComponentNameInput").css("width", newWidth);
		this.editorService.saveCanvas();
	}

	public onChangeDatasource(
		previousDatasourceId: string
	): void {
		const newDatasource = this.settingDatasource.id ?
			this.editorService.getDatasourceObject(this.settingDatasource.id) :
			null;

		if (previousDatasourceId === "") {
			this.changeSelectDatasourceOnComponent(newDatasource);
		} else {
			this.promptUpdateOrRemoveInteractionsConfirmDialog(newDatasource, previousDatasourceId);
		}

	}

	private changeSelectDatasourceOnComponent(datasource: IDatasource): void {
		this.selectedComponent.setDatasource(datasource).then(() => {
			if (datasource) {
				this.runDefaultInteraction();
			}
			this.editorService.triggerDatasourceChanged(datasource);
			this.editorService.getTimeout()(() => {
				/* triggerDatasourceChanged will trigger value source changes which may call on
					async service. Thus race condition happens and this timeout is required to make
					sure all components has been probably updated before save */
				this.editorService.saveCanvas();
				this.editorService.saveCurrent(SaveFlagsEnum.INTERACTIONS);
				// Not sure why this reset is needed but Static lists context needs to remain.
				if (datasource === null || datasource.typedef !== DatasourceTypeEnum.INLINE_LIST) {
					this.selectedComponent.resetContext();
				}
			}, 1);
		});
	}

	private runDefaultInteraction(): void {
		const defaultAction = this.selectedComponent.getDefaultAction() === InteractionPairOperation.GET
			? InteractionActionEnum.GET
			: InteractionActionEnum.LIST;

		this.selectedComponent.loadInteraction(
			this.interactionManagementService.createNewOnTheFly(defaultAction));

	}

	private promptUpdateOrRemoveInteractionsConfirmDialog(
		newDatasource: IDatasource,
		prevDatasourceId: string
	): void {
		const isChangeDatasource = newDatasource !== null;
		const currentProject = this.editorService.getCurrentProject();
		const interactions = angular.copy(currentProject.getInteractions());
		const amountInteractions =
			interactions.filter(interaction => interaction.targetName === this.selectedComponent.getId()).length;

		if (newDatasource &&
			newDatasource.getDatabearer().id === prevDatasourceId
		) {
			return;
		}
		if (amountInteractions > 0) {
			this.dialogService.showYesNo(
				/* tslint:disable:max-line-length
					Long description */
				"Warning",
				`The data service on this component has ${amountInteractions} configured interaction${amountInteractions > 1 ? "s" : ""}.
				Do you want to ${isChangeDatasource ? "change" : "remove"} this data service from this component?`,
				"warning-text"
				/* tslint:enable:max-line-length */
			).then(dialogResult => {
				if (dialogResult.button === DialogButtonType.Yes) {
					this.removeComponentInteractions(interactions);
					this.changeSelectDatasourceOnComponent(newDatasource);
				} else { // No
					this.settingDatasource =
						this.availableDatasources.find(datasource => datasource.id === prevDatasourceId);
				}
			});
		} else {
			this.changeSelectDatasourceOnComponent(newDatasource);
		}
	}

	private removeComponentInteractions(interactions: Interaction[]): void {
		interactions.forEach((interaction, index) => {
			if (interaction.targetName === this.selectedComponent.getId()) {
				this.interactionManagementService.removeInteraction(interaction);
			}
			// if (index + 1 === interactions.length) {
			// 	this.changeSelectDatasourceOnComponent(null);
			// }
		});

		// this.editorService.saveCurrent(SaveFlagsEnum.INTERACTIONS);
	}
}

export class SettingsDirective implements ng.IDirective {
	template = require("./settings.html");
	scope = true;
	controller = SettingsCtrl;
	controllerAs = "eirSettingsCtrl";

	static add(m: ng.IModule): void {
		const directive = () => new SettingsDirective();
		m.directive("eirSettings", directive);
	}
}
