import { AngularConstants } from "../../../common/angular-constants";

import { InjectNames, SaveFlagsEnum } from "ng2/common/constants/common.constant";
import { IJsonBindableField } from "ng2/common/model/databearer/json-binding.model";

import { DialogService } from "../../../common/services/dialog-service";
import { Defines } from "../../../components/defines";
import type { IEditorComponent } from "common/interface/ieditor-component";
import { ISourceComponent, SourceComponent } from "../defines";
import { EditorService } from "../services/editor-service";
import { SettingsBase } from "./settings-base";

export class AttributeSettingsCtrl extends SettingsBase {

	public bindableFieldDefinitions: IJsonBindableField[];
	public availableSources: ISourceComponent[];

	public static $inject = [
		AngularConstants.SCOPE,
		AngularConstants.TIMEOUT,
		InjectNames.EDITOR_SERVICE,
		InjectNames.DIALOG_SERVICE
	];

	public constructor(
		protected $scope: ng.IScope,
		private $timeout: ng.ITimeoutService,
		editorService: EditorService,
		private dialogService: DialogService
	) {
		super($scope, editorService);

		this.onComponentChanged(editorService.getSelectedComponent());
	}

	protected onComponentChanged(newComponent: IEditorComponent): void {
		this.selectedComponent = newComponent;

		this.bindableFieldDefinitions = [];
		// Using timeout to re-initialize all valuesource-settings
		this.$timeout(() => {
			if (this.selectedComponent) {
				const availableSources = [
					SourceComponent.STATIC_VALUE_OBJECT,
					SourceComponent.TARGET_CONTEXT_OBJECT,
					SourceComponent.URL_VALUE_OBJECT
				];

				const components = this.editorService.getAllBindableComponents();
				components.forEach((component) => {
					availableSources.push({
						id: component.getId(),
						name: component.getDisplayName(),
						isComponent: true
					});
				});

				this.availableSources = availableSources;
				const bindableFieldDefinitions: IJsonBindableField[] =
					Defines.allComponents[this.selectedComponent.getTypedef()].class.BINDABLE_FIELDS_2;

				this.bindableFieldDefinitions =
					bindableFieldDefinitions.filter((bField) => {
						return bField.write;
					});
			}
		});
	}

	public save(saveTranslations: boolean): void {
		// dont save undo here
		this.editorService.saveCurrent(SaveFlagsEnum.CANVAS_COMPONENTS);
		if (saveTranslations) {
			this.editorService.getAllTranslatableComponents().forEach((component) => {
				component.initializeBindings();
			});
			this.editorService.projectSettingsChangedEvent.raise(
				this.editorService.getCurrentProject().getManifest()
			);
			this.editorService.saveCurrent(SaveFlagsEnum.TRANSLATIONS);
		}
	}
}

export class AttributeSettingsDirective {
	static add(m: ng.IModule): void {
		m.directive("eirAttributeSettings", () => {
			return {
				template: require("./attribute-settings.html"),
				scope: {},
				controller: AttributeSettingsCtrl,
				controllerAs: "eirAttributeSettingsCtrl"
			};
		});
	}
}
