import angular from "angular";
import { AngularConstants } from "../../angular-constants";

import { INewProject, InjectNames } from "ng2/common/constants/common.constant";
import { InforOwnedEnum } from "ng2/common/enums/infor-owned.enum";
import { IJsonManifest, IJsonRuntimeManifest } from "ng2/common/model/databearer/json-manifest.model";
import { IJsonTag } from "ng2/common/model/databearer/json-tag.model";
import { KeyCodeEnum } from "../../defines";
import { Helper } from "../../helper";
import { ISearchFieldOptions } from "../../model/sohoxi/sohoxi-searchfield.model";
import { TagService } from "../../services/tag-service";

export interface IProjectChangeObject {
	project: angular.IChangesObject<IJsonManifest | IJsonRuntimeManifest>;
}

export class TagDropdownComponent {

	public availableTags: IJsonTag[] = [];
	public availableTagNames: string[] = [];
	public currentTag: string;
	public errorMessage: string;
	public onSaveTags: Function;
	public project: IJsonManifest | IJsonRuntimeManifest | INewProject;
	public selectedTags: IJsonTag[] = [];

	public KeyCodeEnum = KeyCodeEnum;
	public searchFieldOptions: ISearchFieldOptions;

	public static $inject = [
		AngularConstants.SCOPE,
		AngularConstants.ELEMENT,
		InjectNames.TAG_SERVICE
	];

	public constructor(
		private $scope: ng.IScope,
		private $element: ng.IAugmentedJQuery,
		private tagService: TagService
	) {
		this.initAvailableTags();
		this.initSearchFieldEvent();
	}

	public $onChanges(changeObject: IProjectChangeObject): void {
		const project = changeObject.project;
		if (project && !project.isFirstChange() &&
			project.currentValue.id !== project.previousValue.id) {
			this.currentTag = undefined;
			this.errorMessage = undefined;
		}
	}

	public addTag(): void {
		const currentTag = this.currentTag;
		if (!Helper.isUndefinedEmpty(currentTag) &&
			this.isValid() && currentTag
		) {
			let tag: IJsonTag = this.getAvailableTag(currentTag);
			if (!tag) {
				tag = this.getNewTag();
			}
			this.selectedTags.push(tag);
			this.save();

			this.currentTag = undefined;
		}
	}

	public deleteTag(tag: IJsonTag): void {
		const selectedTags = angular.copy(this.selectedTags);
		this.selectedTags = selectedTags.filter((selectedTag) => {
			return tag.name !== selectedTag.name;
		});
		this.save();
	}

	public isValid(): boolean {
		const isValid = true;
		const currentTag = this.currentTag;

		/* For some reason when adding tags in create project view it will trigger addTag() twice
			when pressing enter, one time from keypress enter and one time from button click.
			This extra check on if tag is valid is to prevent the second call on addTag is evaluated. */
		if (Helper.isUndefinedEmpty(currentTag)) {
			this.errorMessage = undefined;
			return isValid;
		}

		const alreadyExists = this.selectedTags.some((tag) => {
			return tag.name === currentTag;
		});

		if (alreadyExists) {
			this.errorMessage = "Tag already added";
			return !isValid;
		}

		const isValidTag = /^[a-zA-Z0-9\s]{1,256}$/.test(currentTag);
		if (!isValidTag) {
			this.errorMessage = "Tag is not valid";
			return !isValid;
		}

		this.errorMessage = undefined;
		return isValid;
	}

	public onKeyPressed($event: JQueryKeyEventObject): void {
		if ($event.which === KeyCodeEnum.ENTER) {
			this.addTag();
		}
	}

	private getNewTag(): IJsonTag {
		return {
			id: Helper.generateUniqueId(),
			name: this.currentTag,
			inforOwned: InforOwnedEnum.FALSE
		};
	}

	private getAvailableTag(tagName: string): IJsonTag {
		return this.availableTags.find((availableTag) => {
			return availableTag.name === tagName;
		});
	}

	private save(): void {
		if ((this.project as INewProject).tags) {//new project
			(this.project as INewProject).tags = this.selectedTags;
		} else {
			this.onSaveTags({ $tags: this.selectedTags });
		}
	}

	private initAvailableTags(): void {
		this.tagService.getAvailableTags().then((tags) => {
			this.availableTags = tags;
			this.availableTagNames = Helper.getListNames(tags);
		});
	}

	private initSearchFieldEvent(): void {
		this.$element.on(
			"selected",
			(event: JQueryEventObject, selectedItem: ng.IAugmentedJQuery
			) => {
				this.currentTag = selectedItem.text().trim();
				this.isValid();
				this.$scope.$applyAsync();
			});
	}

	public static add(m: ng.IModule): void {
		m.component("eirTagDropdown", {
			bindings: {
				onSaveTags: "&",
				project: "<",
				selectedTags: "="
			},
			template: require("./tag-dropdown-component.html"),
			controller: TagDropdownComponent as any,
			controllerAs: "ctrl",
		});
	}
}
