import {
	AfterViewChecked,
	AfterViewInit,
	ChangeDetectorRef,
	ElementRef,
	OnDestroy,
	OnInit,
	Renderer2,
	ViewChild
} from "@angular/core";
import {
	SohoBusyIndicatorDirective,
	SohoMessageService,
} from "ids-enterprise-ng";
import get from "lodash-es/get";
import isNull from "lodash-es/isNull";
import isUndefined from "lodash-es/isUndefined";
import { forkJoin, Observable, of, Subscription } from "rxjs";
import { Subject } from "rxjs/internal/Subject";
import { take } from "rxjs/operators";

import {
	DEFAULT_BOUNDARY,
	InteractionActionEnum,
	InteractionTriggerEnum,
	Numbers,
	SettingKeys,
} from "ng2/common/constants/common.constant";

import { OrientationEnum } from "ng2/common/enums/orientation.enum";
import { SizeTypeEnum } from "ng2/common/enums/size-type.enum";

import type { IComponentStyleProperties } from "ng2/common/model/components/base/component-style.model";
import type { IComponent } from "ng2/common/model/components/base/component.model";
import type { IContext } from "ng2/common/model/components/base/context.model";
import type { ILayoutComponent } from "ng2/common/model/components/base/layout-component.model";
import type { IComponentSettings } from "ng2/common/model/components/settings/component-settings.model";
import type {
	IJsonComponent,
	IJsonComponentSettings,
	IJsonMargin,
	IJsonSize,
	TypeDef,
} from "ng2/common/model/databearer/components/json-component.model";
import type { IJsonBindableField } from "ng2/common/model/databearer/json-binding.model";
import type { IDataService } from "ng2/common/model/dataservice/data-service.model";
import type { IMap } from "ng2/common/model/imap.model";
import type { IInteraction } from "ng2/common/model/interaction/interaction.model";
import type { IBoundary } from "ng2/common/model/project/boundary.model";

import { ButtonMetadata } from "ng2/components/button/button.metadata";
import { ListMetadata } from "ng2/components/list/list.metadata";
import { TextboxMetadata } from "ng2/components/textbox/textbox.metadata";

import { DynamicComponentService } from "ng2/components/services/dynamic-component.service";
import { FrameworkService } from "ng2/components/services/framework.service";

export abstract class BaseComponent implements
	AfterViewChecked,
	AfterViewInit,
	IComponent,
	OnDestroy,
	OnInit {

	//#region component base properties
	typedef: TypeDef;
	id: string;
	generatedId?: string;
	displayName: string;
	boundarySettings: IMap<IJsonComponentSettings>;

	private _dataservice: IDataService;
	get datasource(): IDataService {

		const dataserviceId = this.databearer.datasource;
		if (!isUndefined(dataserviceId) &&
			isUndefined(this._dataservice)
		) {
			const dataserviceDatabearer = this.frameworkService.currentProject
				.getDatasources()[dataserviceId];

			this._dataservice = this.frameworkService.dataServiceBuilder
				.create(dataserviceDatabearer);
		}
		return this._dataservice;
	}

	element: any;
	parent: ILayoutComponent;
	context: IContext;
	databearer: IJsonComponent;
	//#endregion

	//#region component-boundary properties
	boundaryChange$ = new Subject<any>();
	//#endregion

	//#region component-status properties
	componentReady$ = new Subject<IComponent>();
	isLoading = false;

	isVisible = true;
	isReadonly = false;
	//#endregion

	//#region component-style properties
	cssHeight: string;
	cssWidth: string;
	//#endregion

	//#region component-interactio properties
	contextData$: Observable<any>;
	subscriptions: Subscription[] = [];
	//#endregion

	@ViewChild(SohoBusyIndicatorDirective, {
		static: true
	}) busyIndicator: SohoBusyIndicatorDirective;

	constructor(
		protected elementRef: ElementRef,
		protected renderer: Renderer2,
		protected changeDetectorRef: ChangeDetectorRef,
		protected frameworkService: FrameworkService,
		protected dynamicComponentService: DynamicComponentService,
		protected sohoMessageService: SohoMessageService,
	) { }

	ngOnInit(): void {
		this.parent.element = this.elementRef.nativeElement.parentElement;
		this.onInitComponent();
	}

	ngAfterViewInit(): void {
		this.setInitialSohoComponentSettings();
		console.info(`ID: ${this.displayName}, TYPEDEF: ${this.typedef}: ngAfterViewInit`);

		this.componentReady$.next(this);
	}

	ngAfterViewChecked(): void {
		// Need otherwise Homepages SDK throws exception
		this.changeDetectorRef.detectChanges();
	}

	ngOnDestroy(): void {

		console.info(`ID: ${this.displayName}, TYPEDEF: ${this.typedef}: ngOnDestroy`);

		this.componentReady$.unsubscribe();
		this.boundaryChange$.unsubscribe();

		this.subscriptions
			.forEach(subscription => subscription.unsubscribe());
	}

	onInitComponent(): void {
		console.info(`ID: ${this.displayName}, TYPEDEF: ${this.typedef}: onInitComponent`);
		this.updateClassesAndInlineStyles();
	}

	calcNewSizes(): void {
		this.calcNewWidth();
		this.calcNewHeight();
	}

	calcNewWidth(): void {
		const margin = this.getBoundarySetting<IJsonMargin>(SettingKeys.MARGIN);
		this.cssWidth = this.calcNewSize(
			this.getBoundarySetting<IJsonSize>(SettingKeys.WIDTH),
			OrientationEnum.HORIZONTAL, margin.left + margin.right
		);
	}

	calcNewHeight(): void {
		const margin = this.getBoundarySetting<IJsonMargin>(SettingKeys.MARGIN);
		this.cssHeight = this.calcNewSize(
			this.getBoundarySetting<IJsonSize>(SettingKeys.HEIGHT),
			OrientationEnum.VERTICAL, margin.top + margin.bottom
		);
	}

	protected calcNewSize(sizeObject: IJsonSize, orientation: OrientationEnum, margin = 0): string {
		let retSize = "";
		if (this.parent.hasIndividualChildSizes) {
			if (sizeObject.sizeType === SizeTypeEnum.PERCENT) {
				const pxPerPercentComponent = this.parent.getPxPerPercentComponent(orientation, sizeObject.value);
				retSize = `calc(${sizeObject.value || Numbers.HUNDRED}% - ${pxPerPercentComponent + margin}px)`;
			} else if (sizeObject.sizeType === SizeTypeEnum.PIXELS) {
				retSize = "calc(" + sizeObject.value + "px)";
			}
		}
		return retSize;
	}

	getInlineStyles(): IComponentStyleProperties {
		const margin = this.getBoundarySetting<IJsonMargin>(SettingKeys.MARGIN);
		return {
			width: this.cssWidth,
			height: this.cssHeight,
			margin: `${margin.top}px ${margin.right}px ${margin.bottom}px ${margin.left}px`,
		};
	}

	setStyles(styles: IComponentStyleProperties): void {
		for (const propertyName in styles) {
			if (styles.hasOwnProperty(propertyName)) {
				const propertyValue = styles[propertyName];
				this.renderer.setStyle(
					this.parent.element,
					propertyName,
					!isNull(propertyValue) ? propertyValue : "");
			}
		}
	}

	updateClassesAndInlineStyles(): void {

		console.info(`ID: ${this.displayName}, TYPEDEF: ${this.typedef}: updateClassesAndInlineStyles`);

		this.updateClassNames();
		this.updateInlineStyles();
	}

	updateClassNames(): void { }

	updateInlineStyles(): void {

		this.calcNewSizes();

		const componentStyleProperties = this.getInlineStyles();

		this.setStyles(componentStyleProperties);
	}

	getBoundarySetting<T>(
		settingId: string,
		boundaryId?: string,
		noTraverse?: boolean
	): T {
		const componentBoundarySettings = this.databearer.boundarySettings;

		if (!boundaryId) {
			boundaryId = this.frameworkService.currentBoundary.id;
		}
		if (noTraverse) {
			//Should return a copy, List needs some work
			return (componentBoundarySettings[boundaryId] as IMap<T>)[settingId];
		}

		let boundarySettings: IComponentSettings;
		while (!boundarySettings) {
			if (!componentBoundarySettings[boundaryId] ||
				(componentBoundarySettings[boundaryId] as IMap<T>)[settingId] === undefined ||
				(componentBoundarySettings[boundaryId] as IMap<T>)[settingId] === null
			) {
				boundaryId = this.frameworkService.getNextProjectBoundary(boundaryId).id;

				if (boundaryId === DEFAULT_BOUNDARY.id) {
					boundarySettings = componentBoundarySettings[DEFAULT_BOUNDARY.id];
					break;
				}

			} else {
				boundarySettings = componentBoundarySettings[boundaryId];
				break;
			}

		}
		return (boundarySettings as IMap<T>)[settingId];
	}

	getDatabearer(): IJsonComponent {
		return this.databearer;
	}

	loadInteraction(interaction: IInteraction): Observable<any> {

		const isDataserviceAction = (targetActionName: string) => {
			switch (targetActionName) {
				case InteractionActionEnum.CLEAR:
					return false;
				default:
					return true;
			}
		};
		if (isDataserviceAction(interaction.targetActionName)) {
			return this.runComponentDataService(interaction);
		} else {
			return this.runComponentAction(interaction);
		}
	}

	runComponentAction(interaction: IInteraction): Observable<any> {
		switch (interaction.targetActionName) {
			case InteractionActionEnum.CLEAR:
				this.clear();
				return of(true);
			default:
				return Observable.throw("Unknown action");
		}
	}

	runComponentDataService(interaction: IInteraction): Observable<any> {

		this.busyIndicator.activated = true;
		const parameters = interaction.getParameters();
		let newObservable: Observable<any>;

		switch (interaction.targetActionName) {
			case InteractionActionEnum.LIST:

				newObservable = this.datasource
					.getListData(parameters)
					.pipe(take(1));

				newObservable.subscribe(
					data => this.context.ListResult = data,
					(error) => this.dataServiceError(this, error),
					() => this.dataServiceCompleted(this)
				);

				break;

			case InteractionActionEnum.GET:
			default:

				newObservable = this.datasource
					.getListData(parameters)
					.pipe(take(1));

				newObservable.subscribe(
					data => this.context.DetailResult = data,
					(error) => this.dataServiceError(this, error),
					() => this.dataServiceCompleted(this)
				);

				break;
		}

		return newObservable;
	}

	dataServiceError(component: any, error: Error): void {
		this.busyIndicator.activated = false;

		this.showErrorDialog(error.message);
	}

	dataServiceCompleted(component: any): void {
		component.busyIndicator.activated = false;
		this.trigger(InteractionTriggerEnum.ON_LOAD);
	}

	// To be able to call forEachChild on arbitrary component
	forEachChild(fn: (child: IComponent) => void, doGenerated: boolean): void {
		fn(this);
	}

	initializeBinding(componentDatabearer: IJsonComponent): void {

	}

	updateBindings(): void {

		this.dynamicComponentService
			.componentRefs
			.forEach(componentRef => {

				const componentInstance = componentRef.instance;

				let componentMetaData: IJsonBindableField[];
				switch (componentInstance.typedef) {
					case "Button":
						componentMetaData = ButtonMetadata.BINDABLE_FIELDS_2;
						break;

					case "Dropdown":
						break;

					case "Flexbox":
						break;

					case "List":
						componentMetaData = ListMetadata.BINDABLE_FIELDS_2;
						break;

					case "Textbox":
					case "TextBox":
						componentMetaData = TextboxMetadata.BINDABLE_FIELDS_2;
						break;
					default:
						throw Error("Invalid component");
				}

				componentMetaData?.forEach(bindable => {
					const sourceKey =
						componentInstance.databearer[bindable.ref]?.sourceKey;

					if (sourceKey) {
						const value = get(this, sourceKey);

						componentInstance[bindable.ref] = value;
						console.info("sourceKey: " + sourceKey + ", value: " + value);
					}
				});

			});
	}

	setInitialSohoComponentSettings(): void {
		this.busyIndicator.displayDelay = 0;
	}

	clear(): void {

	}

	trigger(interactionTrigger: InteractionTriggerEnum): Observable<any> {

		const interactions = this.frameworkService
			.getInteractionByTriggerName(interactionTrigger)
			.filter(interaction => interaction.sourceName === this.id);

		const observables: Observable<any>[] = [];

		interactions.forEach(interaction => {
			observables.push(
				this.frameworkService.runInteraction(interaction));
		});

		return forkJoin(observables);
	}

	showErrorDialog(message: string): void {

		const buttons = [
			{
				text: "OK",
				click: (_: JQuery.TriggeredEvent, modal: SohoModalStatic) => modal.close(true),
				isDefault: true
			}
		];

		this.sohoMessageService
			.error()
			.title("Error")
			.buttons(buttons)
			.message(message)
			.open();
	}

	onBoundaryChange(boundary: IBoundary): Observable<void> {

		this.updateClassesAndInlineStyles();

		this.isVisible = this.getBoundarySetting<boolean>(
			SettingKeys.VISIBILITY,
			boundary.id
		);
		const componentELement = this.elementRef.nativeElement;

		!this.isVisible ?
			this.renderer.addClass(componentELement, "is-hidden") :
			this.renderer.removeClass(componentELement, "is-hidden");

		this.boundaryChange$.next(this);
		this.changeDetectorRef.detectChanges();

		// TODO: fix so it maybe returns a BoundaryResponse?
		return of();
	}
}
