import angular from "angular";
import { AngularConstants } from "common/angular-constants";

import { DEFAULT_BOUNDARY, InjectNames, ProjectLimit, SaveFlagsEnum } from "ng2/common/constants/common.constant";
import { STANDARD_BOUNDARIES } from "ng2/common/constants/standard-boundaries.constant";

import type { IBoundary } from "ng2/common/model/project/boundary.model";

import { DialogButtonType, DialogService, IDialogOptions, IDialogResult, IToastOptions } from "common/services/dialog-service";
import { BoundaryManagementService } from "editor/services/boundary-management-service";
import { EditorService } from "editor/services/editor-service";
import { IStandardBoundariesDialogParameter, StandardBoundariesCtrl } from "editor/canvas//standard-boundaries-dialog";

import "jquery-ui/ui/widgets/draggable";

export class CanvasSliderCtrl {
	standardBoundaries: StandardBoundariesCtrl;
	private insideRulerElement: JQuery;
	private unsubscribers: Array<Function> = [];

	static $inject = [
		AngularConstants.SCOPE,
		AngularConstants.ELEMENT,
		InjectNames.EDITOR_SERVICE,
		InjectNames.DIALOG_SERVICE,
		InjectNames.BOUNDARY_MANAGEMENT_SERVICE
	];

	constructor(
		private $scope: ng.IScope,
		private $element: ng.IAugmentedJQuery,
		private editorService: EditorService,
		private dialogService: DialogService,
		private boundaryManagementService: BoundaryManagementService
	) {

		this.insideRulerElement = $element.parent();

		this.initDraggable();

		this.unsubscribers.push(boundaryManagementService.newBoundaryAddedEvent.on((boundary: IBoundary) => {
			this.positionCanvasResizer(boundary);
		}));

		this.unsubscribers.push(boundaryManagementService.boundaryMovedEvent.on((boundary: IBoundary) => {
			this.positionCanvasResizer(boundary);
		}));

		this.positionCanvasResizer(editorService.getCurrentBoundary());

		$scope.$on(AngularConstants.DESTROY, () => {
			angular.forEach(this.unsubscribers, (unsubscribe) => {
				unsubscribe();
			});
		});
	}

	private initDraggable(): void {
		const dragCanvasWidth: JQueryUI.DraggableEvent = (event, ui) => {
			if (ui.position.left > 0) {
				const margin: number = 18;
				//Moving 18px so the slider is inside the canvas
				this.insideRulerElement.width(ui.position.left + margin);
				angular.element(window).trigger("resize");
			} else {
				ui.position.left = 0;
			}
		};

		this.$element.draggable({
			axis: "x",
			drag: dragCanvasWidth.bind(this),
			start: dragCanvasWidth.bind(this),
			containment: $("mashupCanvas"),
			scroll: false,
			snap: $(".hBoundaryMarker")
		});
	}

	addBoundaryMarker(): void {
		const offsetX: number = angular.element("#insideRuler").width();
		this.boundaryManagementService.addNewBoundaryMarker(offsetX);
	}

	showStandardBoundariesDialog(): void {
		const projectBoundaries = this.editorService.getCurrentProject().getBoundaries();
		const oldBoundaryIds: string[] = [];
		let newBoundaryIds: string[] = [];
		const addedboundaries: string[] = [];
		const notAddedBoundaries: string[] = [];
		let save: boolean = false;
		// Saving the old boundaries before the potential changed from the dialog
		STANDARD_BOUNDARIES.forEach((standardBoundary: IBoundary) => {
			const inProject = projectBoundaries.some((projectBoundary: IBoundary): boolean => {
				return projectBoundary.id === standardBoundary.id &&
					projectBoundary.displayName === standardBoundary.displayName;
			});
			if (inProject) {
				oldBoundaryIds.push(standardBoundary.id);
			}
		});

		const options: IDialogOptions<IStandardBoundariesDialogParameter> = {
			title: "Standard Boundaries",
			template: require("./standard-boundaries-dialog.html"),
			parameter: { standardBoundaries: STANDARD_BOUNDARIES, selectedStandardBoundaryIds: oldBoundaryIds }
		};

		this.dialogService.show<IStandardBoundariesDialogParameter, string[]>(options)
			.then((standardBoundariesDialogResult: IDialogResult<string[]>) => {
				if (standardBoundariesDialogResult.button === DialogButtonType.Ok) {
					newBoundaryIds = standardBoundariesDialogResult.value;

					STANDARD_BOUNDARIES.forEach((boundary) => {
						//Remove first to make place for eventually new ones
						if (oldBoundaryIds.indexOf(boundary.id) !== -1 && newBoundaryIds.indexOf(boundary.id) === -1) {
							this.boundaryManagementService.removeProjectBoundary(boundary);
							save = true;
						}
					});
				}
			}).then(() => {
				STANDARD_BOUNDARIES.forEach((boundary) => {
					if (oldBoundaryIds.indexOf(boundary.id) === -1 && newBoundaryIds.indexOf(boundary.id) !== -1) {

						const numberOfBoundaries = this.editorService
							.getCurrentProject()
							.getBoundaries()
							.length;

						if (numberOfBoundaries < ProjectLimit.MAX_BOUNDARIES) {
							this.boundaryManagementService.addProjectBoundary(boundary);
							addedboundaries.push(boundary.displayName);
							save = true;
						} else {
							notAddedBoundaries.push(boundary.displayName);
						}
					}
				});
				this.onMaxBoundariesReached(notAddedBoundaries);
			}).finally(() => {
				if (save) {
					this.editorService.saveCurrent(SaveFlagsEnum.MANIFEST).then(() => {
						if (addedboundaries.length > 0) {
							const options: IToastOptions = {
								title: "New Boundary",
								message: "Successfully added the " + addedboundaries.toString(),
								position: "top right",
								progressBar: true
							};
							this.dialogService.showToast(options);
						} else {
							this.onMaxBoundariesReached(notAddedBoundaries);
						}
					});
				}
			});
	}

	private onMaxBoundariesReached(boundaries: string[]): void {
		if (boundaries.length > 0) {
			const title = "Error";
			const message = `Unsuccessfully added ${boundaries.toString()}. ` +
				`Max boundaries are reached, try to delete one first.`;
			this.dialogService.showError(title, message);
		}
	}

	private positionCanvasResizer(boundary: IBoundary): void {
		if (boundary === DEFAULT_BOUNDARY) {
			// set back to default position. TODO: solv with ngClass?
			this.insideRulerElement.width("");
			this.$element.css({ left: "calc(100% - 18px)" });
		} else {
			const margin: number = 18;
			this.insideRulerElement.width(boundary.limitX);
			//Moving 18px so the slider is inside the canvas
			this.$element.css({ left: boundary.limitX - margin});
		}
		angular.element(window).trigger("resize");
	}
}

export class CanvasSliderDirective {
	static add(m: ng.IModule): void {
		m.directive("eirCanvasSlider", () => {
			return {
				scope: {},
				template: require("./canvas-slider.html"),
				controller: CanvasSliderCtrl,
				controllerAs: "eirCanvasSliderCtrl",
				replace: true
			};
		});
	}
}
