import type { ILayoutComponent } from "../../common/interface/ilayout-component";
import type { IJsonRadioButtons, IRadioButton } from "../../common/model/component/radiobutton.model";
import { Component } from "../component";
import { FrameworkService } from "../services/framework-service";

import {
	InteractionPairOperation, InteractionTriggerEnum, SettingInputKeys,
	SettingKeys, SidebarTabTypes
} from "ng2/common/constants/common.constant";
import { InteractionDefines } from "ng2/common/constants/interaction-defines.constant";

import { BindingTypeEnum } from "ng2/common/enums/binding-type.enum";
import { SizeTypeEnum } from "ng2/common/enums/size-type.enum";
import { ValueTypeEnum } from "ng2/common/enums/value-type.enum";

import type { IComponentSettingCategory } from "ng2/common/model/components/settings/component-setting-category.model";
import type { IDataPair } from "ng2/common/model/data-pair.model";
import type { IJsonSize } from "ng2/common/model/databearer/components/json-component.model";
import type { IJsonBindableField } from "ng2/common/model/databearer/json-binding.model";

import type { IRadioButtonComponent } from "common/interface/iradiobutton-component";
import { BackwardCompatible } from "ng2/common/project/backward-compatible";

export class RadioButton extends Component implements IRadioButtonComponent {
	static DEFAULT_WIDTH: IJsonSize = { sizeType: SizeTypeEnum.AUTO };
	static DEFAULT_HEIGHT: IJsonSize = { sizeType: SizeTypeEnum.AUTO };
	static SOURCE_TRIGGERS: IDataPair[] = InteractionDefines.SOURCE_TRIGGERS.concat(
		[InteractionPairOperation.ON_ITEM_CHANGED]
	);
	static TARGET_ACTIONS: IDataPair[] = InteractionDefines.TARGET_ACTIONS.concat([
		InteractionPairOperation.LIST,
		InteractionPairOperation.CLEAR
	]);
	static DEFAULT_TRIGGER: IDataPair = InteractionPairOperation.ON_ITEM_CHANGED;
	static DEFAULT_ACTION: IDataPair = InteractionPairOperation.GET;
	static SETTING_FIELDS: IComponentSettingCategory[] =
	([{
		title: "Style",
		tab: SidebarTabTypes.STYLE,
		settings: [{
			title: "Show Label",
			boundarySetting: true,
			ref: SettingKeys.SHOW_LABEL,
			inputType: SettingInputKeys.CHECKBOX,
			defaultValue: true,
		}, {
			title: "Disabled",
			ref: SettingKeys.IS_READONLY,
			inputType: SettingInputKeys.CHECKBOX,
			defaultValue: false,
			button: {
				icon: "#icon-unlocked",
				tooltip: "Disable Component When...",
				dialog: true
			}
		}]
	}] as IComponentSettingCategory[]).concat(Component.SETTING_FIELDS);
	static BINDABLE_FIELDS_2: IJsonBindableField[] = [{
		name: "Selected Item Value",
		ref: SettingKeys.CURRENT_ITEM_VALUE,
		valueType: ValueTypeEnum.STRING,
		write: false,
		read: true,
	}, {
		name: "Selected Item Name",
		ref: SettingKeys.CURRENT_ITEM_NAME,
		valueType: ValueTypeEnum.STRING,
		write: false,
		read: true,
	}, {
		name: "Label",
		ref: SettingKeys.LABEL,
		valueType: ValueTypeEnum.LOCALIZED_STRING,
		write: true,
		read: false,
		defaultValue: { Type: BindingTypeEnum.STATIC_VALUE, value: "" }
	}];

	radioButtons: IRadioButton[];
	selectedRadioButtonId: string;
	label: string;
	showLabel: boolean;
	currentItemValue: string;
	currentItemName: string;
	protected databearer: IJsonRadioButtons;

	constructor(
		obj: IJsonRadioButtons,
		$element: ng.IAugmentedJQuery,
		parent: ILayoutComponent,
		frameworkService: FrameworkService
	) {
		super(obj, $element, parent, frameworkService);
		this.radioButtons = this.databearer.componentRadioButtons;
		if (this.radioButtons.length ) {
			this.selectedRadioButtonId = this.radioButtons[0].id;
			this.currentItemName = this.radioButtons[0].name;
			this.currentItemValue = this.radioButtons[0].field;
		}
		this.showLabel = BackwardCompatible.getShowLabel(obj);
	}

	newBoundaryEntered(boundaryId: string): void {
		super.newBoundaryEntered(boundaryId);
		this.setBoundarySettingIfChanged<boolean>(SettingKeys.SHOW_LABEL);
	}

	clear(): void {
		this.selectedRadioButtonId = this.radioButtons[0].id;
		this.currentItemName = this.radioButtons[0].name;
		this.currentItemValue = this.radioButtons[0].field;
	}

	initializeBindings(): void {
		super.initializeBindings();
		this.updateValuesWithTranslations();
	}

	setCurrentItem(selected: IRadioButton): void {
		this.currentItemValue = selected.field;
		this.currentItemName = selected.name;
		this.trigger(InteractionTriggerEnum.CURRENT_ITEM_CHANGED);
		this.frameworkService.triggerExpressions();
	}

	updateValuesWithTranslations(): void {
		this.radioButtons.forEach((radioButton) => {
			const translatedTitle = this.frameworkService.getTranslatableStringByProperty(this.getId(), radioButton.id);

			if (translatedTitle) {
				const globalString = this.frameworkService.getGlobalValue(radioButton.name, this);
				radioButton.name = translatedTitle;

				if (globalString) {
					radioButton.value = globalString;
				}

				if (this.selectedRadioButtonId === radioButton.id) {
					this.currentItemName = translatedTitle;
				}
			}
		});
	}

	static directive = [() => {
		return {
			template: require("./radiobutton.html"),
			replace: true
		};
	}];
}
