import angular from "angular";
import { AngularConstants } from "../../../../../common/angular-constants";

import { IdoProperties, InjectNames } from "ng2/common/constants/common.constant";
import { DatasourceTypeEnum } from "ng2/common/enums/datasource-type.enum";
import { ParameterTypeEnum } from "ng2/common/enums/parameter-type.enum";
import { ValueTypeEnum } from "ng2/common/enums/value-type.enum";
import { IJsonDatasource, IJsonLocalIdoDatasource, IRecordCapOption } from "ng2/common/model/databearer/dataservice/json-datasource.model";
import { ILocalIdoParameterObject } from "ng2/common/model/dataservice/parameter-object.model";

import { HttpVerbEnum } from "../../../../../../app-builder/common/enums/http-verb.enum";
import { IDialogBaseScope } from "../../../../../common/defines";
import { ITestDataserviceParameters, TestDataserviceDialogType, TTestDataserviceDialogType } from "../../../../../common/dialogs/test-dataservice/test-dataservice-dialog";
import { IRowItem } from "../../../../../common/sohoxi/datagrid-helper";
import { IWizardStep, WizardCtrl } from "../../../../editor/wizard/wizard-ctrl";
import { SourceEnum } from "../../create-from-wizard/select-source-ctrl";
import { ILocalIdoParameter } from "../../search/local-ido/operation-details-ctrl";

import { Helper } from "../../../../../common/helper";
import { DialogService, IDialogOptions, IDialogResult } from "../../../../../common/services/dialog-service";
import { FeatureSwitchService } from "../../../../../common/services/feature-switch-service";
import { IdoCollection, IdoProperty, ILocalIDOTestDataService, LocalIdoService } from "../../../../../components/services/local-ido-service";

export interface ILocalIdoDataserviceWizardScope extends IDialogBaseScope<ILocalIdoParameter> {}

class IdoOperations {
	static LOAD = "Load Collection";
	static UPDATE = "Update Row";
	static INSERT = "Insert Row";
	static DELETE = "Delete Row";
}

export class LocalIdoDataserviceWizardCtrl extends WizardCtrl {
	public get idoCollection(): IdoCollection {
		return this.$scope.eirDialog.parameter.operation;
	}

	// tslint:disable:no-magic-numbers
	public get LocalIdoOperation(): string {
		switch (this.datasource.method) {
			case HttpVerbEnum.GET:
				return this.operationList[0];
			case HttpVerbEnum.PUT:
				return this.operationList[1];
			case HttpVerbEnum.POST:
				return this.operationList[2];
			default:
			case HttpVerbEnum.DELETE:
				return this.operationList[3];
		}
	}
	// tslint:enable

	public operationList: string[] = [
		IdoOperations.LOAD,
		IdoOperations.UPDATE,
		IdoOperations.INSERT,
		IdoOperations.DELETE
	];

	public recordCapOptions: IRecordCapOption[] = [
		{ name: "Use Default", id: "0", limit: 200 },
		{ name: "Retrieve All", id: "1", limit: -1 },
		{ name: "Use Specified Max", id: "2" },
	];
	public selectedRecordCapOption: string;

	private DEFAULT_ORDER: string = "Default Order";

	public $scope: ILocalIdoDataserviceWizardScope;

	public selectedOperation: string;

	// public itemIdChecked: boolean;
	public currentShownIdName: string;
	public idoProperties: IdoProperty[];
	public idoPropertyOptions: string[];

	public datasource: IJsonLocalIdoDatasource;

	private previousMethod: string;

	// TODO: MUPS-2682: Refactor wizard template URL so it uses require
	public steps: IWizardStep[] = [
		{
			id: "dataservice-wizard-operation.html",
			title: "Operation",
			userTip: "",
			template: require("./dataservice-wizard-operation.html")
		},
		{
			id: "dataservice-wizard-parameters.html",
			title: "Parameters",
			userTip: "",
			template: require("./dataservice-parameters/dataservice-wizard-parameters.html")
		},
		{
			id: "datasource-wizard-description.html",
			title: "Name &amp; Description",
			userTip: "",
			template: require("../datasource-wizard-description.html")
		},
		{
			id: "dataservice-wizard-summary.html",
			title: "Summary",
			userTip: "",
			template: require("./dataservice-summary/dataservice-wizard-summary.html")
		}
	];

	static $inject = [
		AngularConstants.SCOPE,
		AngularConstants.ELEMENT,
		AngularConstants.TEMPLATE_CACHE,
		InjectNames.FEATURE_SWITCH_SERVICE,
		InjectNames.LOCAL_IDO_SERVICE,
		InjectNames.DIALOG_SERVICE,
	];

	constructor(
		$scope: ILocalIdoDataserviceWizardScope,
		$element: ng.IAugmentedJQuery,
		$templateCache: ng.ITemplateCacheService,
		featureService: FeatureSwitchService,
		private localIdoService: LocalIdoService,
		private dialogService: DialogService,
	) {
		super(
			$scope,
			$element,
			$templateCache,
			featureService,
		);
		this.initWizard();
		this.nextDisabled = false;
		const eirDialog = $scope.eirDialog;

		if (eirDialog.parameter.datasource) {
			this.datasource = eirDialog.parameter.datasource;
			this.initEditDataServiceDialog();
		} else if (this.idoCollection) {
			// New from Wizard
			this.selectedOperation = this.operationList[0];
			this.selectedRecordCapOption = "0";

			this.datasource = {
				displayName: this.idoCollection.collectionName,
				method: HttpVerbEnum.GET,
				resource: this.idoCollection.collectionName,
				typedef: DatasourceTypeEnum.IDO,
				endpoint: "",
				suite: SourceEnum.LOCAL_IDO,
				version: 2,
				outputParameters: [],
				inputParameters: [],
				orderBy: this.DEFAULT_ORDER,
				recordCap: this.recordCapOptions[0]
			};

			this.loadIdoCollectionProperties();
		} else {
			// From scratch
			console.info("Create from scratch");
		}
	}

	private loadIdoCollectionProperties(): void {
		this.localIdoService
			.getIdoProperties(this.datasource.resource)
			.then(properties => {
				this.idoProperties = angular.copy(properties);
				return properties;
			})
			.then(properties => {
				this.idoPropertyOptions = properties.map(property => property.propertyName);
				this.idoPropertyOptions.unshift(this.DEFAULT_ORDER);
			})
			.then(() => {
				this.addItemID();
			});
	}

	private initEditDataServiceDialog(): void {
		// Edit existing datasource
		this.loadIdoCollectionProperties();

		const recordCap = this.datasource.recordCap;
		this.selectedRecordCapOption = !recordCap ? this.recordCapOptions[0].id : recordCap.id;

		this.setIdoOperation();
	}

	private setIdoOperation(): void {
		// tslint:disable:no-magic-numbers
		switch (this.datasource.method) {
			case HttpVerbEnum.GET:
				this.selectedOperation = this.operationList[0];
				break;
			case HttpVerbEnum.PUT:
				this.selectedOperation = this.operationList[1];
				break;
			case HttpVerbEnum.POST:
				this.selectedOperation = this.operationList[2];
				break;
			case HttpVerbEnum.DELETE:
			default:
				this.selectedOperation = this.operationList[3];
				break;
		}
		// tslint:enable
		this.previousMethod = this.datasource.method;
	}

	/**
	 * Adds ItemID if operation is LOAD, UPDATE, or DELETE.
	 * Otherwise remove ItemID if its in the list
	 */
	private addItemID(): void {
		const hasItemID = this.idoProperties.
			findIndex(property => property.propertyName === IdoProperties.ITEM_ID) !== -1;
		const hasRowPointer = this.idoProperties.
			findIndex(property => property.propertyName === IdoProperties.ROW_POINTER) !== -1;

		if (this.datasource.method !== HttpVerbEnum.POST && !hasItemID && hasRowPointer) {
			this.idoProperties.unshift({
				propertyName: IdoProperties.ITEM_ID,
				propertyType: "string",
				columnName: IdoProperties.ITEM_ID,
				isReadOnly: true,
				isRequired: true,
				dataType: "string",
				keySequence: 99
			});
		} else if (this.datasource.method === HttpVerbEnum.POST) {
			this.idoProperties =
				this.idoProperties.filter(property => property.propertyName !== IdoProperties.ITEM_ID);
		}
	}

	protected onChangeCurrentStep(): void {
		if (this.currentShownIdName) {
			this.onToggleParameterSetting(this.currentShownIdName);
		}
		if (!Helper.isUndefined(this.previousMethod) &&
		this.previousMethod !== this.datasource.method) {
			this.datasource.inputParameters.length = 0;
		}
		this.previousMethod = this.datasource.method;
	}

	protected getFinishParameter(): IJsonDatasource {
		const method = this.datasource.method;

		if (method === HttpVerbEnum.PUT) {
			this.datasource.outputParameters = this.datasource.inputParameters;
		}

		if (method === HttpVerbEnum.PUT ||
			method === HttpVerbEnum.POST) {
			this.addRequiredParameters();
		} else if (method === HttpVerbEnum.DELETE) {
			this.addPrimaryParameters();
		}

		return this.datasource;
	}

	private addRequiredParameters(dataservice?: IJsonLocalIdoDatasource): void {
		const localIdoDataservice = dataservice ?
			dataservice : this.datasource;
		this.idoProperties.forEach(property => {
			if (property.isRequired) {
				const datagridItem: IRowItem<IdoProperty> = {
					data: property
				} as any;
				localIdoDataservice.inputParameters.push(this.createParameterObject(datagridItem));
			}
		});
	}

	private addPrimaryParameters(dataservice?: IJsonLocalIdoDatasource): void {
		const localIdoDataservice = dataservice ?
			dataservice : this.datasource;
		this.idoProperties.forEach(property => {
			if (property.keySequence > 0) {
				const datagridItem: IRowItem<IdoProperty> = {
					data: property
				} as any;
				localIdoDataservice.inputParameters.push(this.createParameterObject(datagridItem));
			}
		});
	}

	public createParameterObject(item: IRowItem<IdoProperty>): ILocalIdoParameterObject {
		const currentParameters = this.datasource.method !== HttpVerbEnum.GET ?
			this.datasource.inputParameters : this.datasource.outputParameters;
		const existingParameter = currentParameters
			.find(param => param.key === item.data.propertyName);

		return ({
			id:  !existingParameter ? Helper.generateUniqueId() : existingParameter.id,
			key: item.data.propertyName,
			valueType: this.getValueType(item.data.dataType),
			dataType: item.data.dataType,
			parameterType: ParameterTypeEnum.QUERY,
			required: item.data.isRequired,
			alias: ""
		});
	}

	private getValueType(datatype: string): ValueTypeEnum {
		switch (datatype) {
			case "Date":
				return ValueTypeEnum.DATE;
			case "String":
			case "GUID":
				return ValueTypeEnum.STRING;
			case "Long Integer":
			case "Byte":
			default:
				return ValueTypeEnum.NUMBER;
		}
	}

	public onOperationChange(): void {
		switch (this.selectedOperation) {
			case IdoOperations.LOAD:
				this.datasource.method = HttpVerbEnum.GET;
				break;
			case IdoOperations.INSERT:
				this.datasource.method = HttpVerbEnum.POST;
				break;
			case IdoOperations.UPDATE:
				this.datasource.method = HttpVerbEnum.PUT;
				break;
			case IdoOperations.DELETE:
			default:
				this.datasource.method = HttpVerbEnum.DELETE;
				break;
		}
		this.addItemID();
	}

	public onRecordCapChange(): void {
		const optionId = parseInt(this.selectedRecordCapOption);
		this.datasource.recordCap = this.recordCapOptions[optionId];
	}

	public onToggleParameterSetting(idName: string): void {
		if (Helper.isUndefinedOrNull(this.currentShownIdName)) {
			this.toggleParameterSettings(idName);
			this.currentShownIdName = idName;
		} else if (this.currentShownIdName === idName) {
			this.toggleParameterSettings(idName);
			this.currentShownIdName = undefined;

		} else if (this.currentShownIdName !== idName) {
			this.toggleParameterSettings(this.currentShownIdName);
			this.toggleParameterSettings(idName);
			this.currentShownIdName = idName;
		}
	}

	private toggleParameterSettings(idName: string): void {
		let eirLocalIdoDataserviceElement =
			this.$element.find(`#${idName} > eir-local-ido-dataservice-parameters`);

		eirLocalIdoDataserviceElement = eirLocalIdoDataserviceElement.length ?
			eirLocalIdoDataserviceElement :
			this.$element.find(`#${idName} > #local-ido-settings`);

		eirLocalIdoDataserviceElement.slideToggle();

		const textElement = this.$element.find(`#${idName} a`);
		const text = textElement.text().trim();
		textElement.text(text === "Show more" ? "Show less" : "Show more");
	}

	public openTestDataserviceDialog(): ng.IPromise<IDialogResult<string>> {
		const preliminaryDataservice = angular.copy(this.datasource) as ILocalIDOTestDataService;
		if (this.selectedOperation !== IdoOperations.LOAD) {
			this.addRequiredParameters(preliminaryDataservice);
		}

		preliminaryDataservice.isTestIDO = true;

		const options: IDialogOptions<ITestDataserviceParameters> = {
			title: "Test Data Service",
			template: require("../../../../../common/dialogs/test-dataservice/test-dataservice-dialog.html"),
			parameter: {
				dataservice: preliminaryDataservice,
				type: this.getTestDialogType(),
			},
		};
		return this.dialogService.show<ITestDataserviceParameters, string>(options);
	}

	private getTestDialogType(): TTestDataserviceDialogType {
		const inputParamStep = 0;
		const outputParamStep = 1;
		let testDialogType: TTestDataserviceDialogType;

		switch (this.steps.indexOf(this.currentStep)) {
			case inputParamStep:
				testDialogType = TestDataserviceDialogType.UNFORMATTED;
				break;
			case outputParamStep:
				testDialogType = TestDataserviceDialogType.FORMATTED;
				break;
			default:
				testDialogType = TestDataserviceDialogType.UNFORMATTED;
				break;
		}

		testDialogType = TestDataserviceDialogType.UNFORMATTED;

		return testDialogType;
	}

	static add(module: ng.IModule): void {
		module.controller(
			"eirLocalIdoDataserviceWizardCtrl",
			LocalIdoDataserviceWizardCtrl
		);
	}
}
