import { AngularConstants } from "../common/angular-constants";

import { InjectNames } from "ng2/common/constants/common.constant";
import { IContext } from "ng2/common/model/components/base/context.model";
import { IJsonComponent } from "ng2/common/model/databearer/components/json-component.model";

import { IComponentScope } from "../common/interface/icomponent-scope";
import { ILayoutComponent } from "../common/interface/ilayout-component";

import { Debugger } from "../common/debugger";
import { Helper } from "../common/helper";
import { ApplicationParent } from "../components/application-parent";
import { FrameworkService } from "../components/services/framework-service";
import { RuntimeService } from "./runtime-service";

class RuntimeCanvasCtrl {

	public rootComponent: IJsonComponent;
	public parent: ApplicationParent;
	public context: IContext;

	static $inject = [
		AngularConstants.ROOT_SCOPE,
		AngularConstants.ELEMENT,
		InjectNames.FRAMEWORK_SERVICE,
		InjectNames.RUNTIME_SERVICE
	];

	constructor(
		private $rootScope: ng.IRootScopeService,
		private $element: ng.IAugmentedJQuery,
		private frameworkService: FrameworkService,
		private runtimeService: RuntimeService
	) {
		const project = runtimeService.getCurrentProject();
		this.loadApplication(project.rootComponent);
	}

	public onComponentReady(componentInstance: ILayoutComponent): void {
		Debugger.log(this, "[RuntimeDirective] Root component ready");

		this.parent.addChildInstance(componentInstance);
		this.runtimeService.setCurrentRootComponentInstance(componentInstance);

		this.runtimeService.runStartupInteractions();
		componentInstance.newBoundaryEntered(this.runtimeService.getCurrentBoundary().id);
		this.frameworkService.triggerExpressions();

		Helper.appIsReadyForSelenium();
	}

	private loadApplication(rootComponent: IJsonComponent): void {
		this.parent = new ApplicationParent(
			ApplicationParent.applicationParentDatabearer,
			ApplicationParent.createSpoofedElement(this.$rootScope.$new() as IComponentScope),
			null,
			this.frameworkService);
		this.context = {};
		// angular will compile when rootComponent is set
		this.rootComponent = rootComponent;
	}
}

export class RuntimeCanvasDirective implements ng.IDirective {

	public template = require("./runtime-canvas.html");
	public controller = RuntimeCanvasCtrl;
	public controllerAs = "eirRuntimeCanvasCtrl";

	static add(m: ng.IModule): void {
		const directive: ng.IDirectiveFactory = () => new RuntimeCanvasDirective();
		m.directive("eirRuntimeCanvas", directive);
	}
}
