import { AngularConstants } from "../../../../../common/angular-constants";

import type { IDialogBaseScope } from "../../../../../common/defines";

import { Helper } from "../../../../../common/helper";
import { DialogButtonType, IDialog, IDialogResult } from "../../../../../common/services/dialog-service";
import type { ISourceComponent } from "../../../defines";
import { EditorService } from "../../../services/editor-service";

import { InjectNames, SettingInputKeys } from "ng2/common/constants/common.constant";

import { QuestionDefinitionsEnum } from "ng2/common/model/questions/questions.enum";

import type { IDataPair } from "ng2/common/model/data-pair.model";
import type { IJsonQuestion } from "ng2/common/model/questions/questions.model";

export class UpsertQuestionDialogCtrl {
	selectedQuestion: IJsonQuestion;
	availableComponents: ISourceComponent[];
	availableTypes: IDataPair[] = [];
	availableComponentSettings: IDataPair[] = [];
	private dialog: IDialog<IJsonQuestion>;

	static $inject = [
		AngularConstants.SCOPE,
		InjectNames.EDITOR_SERVICE
	];

	constructor(
		private $scope: IDialogBaseScope<IJsonQuestion>,
		private editorService: EditorService
	) {
		const dialog: IDialog<IJsonQuestion> = $scope.eirDialog;
		this.dialog = dialog;

		this.selectedQuestion = {
			id: Helper.generateUniqueId(),
			componentId: "",
			type: QuestionDefinitionsEnum.COMPONENT_SETTINGS,
			settings: [{
				id: Helper.generateUniqueId(),
				text: "",
				key: ""
			}],
		};

		this.getAvailableComponents();
		this.availableTypes = [
			{
				name: "Setting",
				value: QuestionDefinitionsEnum.COMPONENT_SETTINGS
			}, {
				name: "Binding",
				value: QuestionDefinitionsEnum.SEEKS_BINDING
			}
		];
		this.getAvailableComponentSettings(this.availableComponents[0].id);
	}

	private getType(typeName: string): QuestionDefinitionsEnum {
		switch (typeName) {
			case "Setting":
				return QuestionDefinitionsEnum.COMPONENT_SETTINGS;
			case "Binding":
				return QuestionDefinitionsEnum.SEEKS_BINDING;
			default:
				// type do not exist
		}
	}

	save(): void {
		const result: IDialogResult<IJsonQuestion> = {
			button: DialogButtonType.Ok,
			value: this.selectedQuestion
		};
		this.dialog.close(result);
	}

	cancel(): void {
		const result: IDialogResult<void> = {
			button: DialogButtonType.Cancel
		};
		this.dialog.close(result);
	}

	getAvailableComponents(): void {
		const availableSources: ISourceComponent[] = [];
		const components = this.editorService.getAllComponents();
		components.forEach((component) => {
			availableSources.push({
				id: component.getId(),
				name: component.getDisplayName(),
				isComponent: true
			});
		});

		this.availableComponents = availableSources;
		this.selectedQuestion.componentId = this.availableComponents[0].id;
	}

	onComponentChange(): void {
		this.getAvailableComponentSettings(this.selectedQuestion.componentId);
	}

	onTypeChange(): void {
		if (this.selectedQuestion.type === QuestionDefinitionsEnum.SEEKS_BINDING) {
			this.selectedQuestion.settings = [];
		} else {
			this.selectedQuestion.settings = [
				{
					id: Helper.generateUniqueId(),
					text: "",
					key: ""
				}
			];
			this.getAvailableComponentSettings(this.selectedQuestion.componentId);
		}
	}

	getAvailableComponentSettings(componentId: string): void {
		const availableSettings: IDataPair[] = [];
		const component = this.editorService.getComponentInstanceById(componentId);
		const componentSettings = component.getFieldSettings();
		componentSettings.forEach((setting) => {
			setting.settings.forEach((data) => {
				// Dumb way to see if the setting is boolean
				if (data.inputType === SettingInputKeys.SWITCH || data.inputType === SettingInputKeys.CHECKBOX) {
					availableSettings.push({
						value: data.ref,
						name: this.prettyPrint(data.ref)
					});
				}
			});
		});
		this.availableComponentSettings = availableSettings;
		this.selectedQuestion.settings[0].key = this.availableComponentSettings[0].value;
	}

	private prettyPrint(settingReference: string): string {
		return settingReference.match(/^[a-z]+|[A-Z][a-z]*/g).map((word) => {
			return word[0].toUpperCase() + word.substr(1).toLowerCase();
		}).join(" ");
	}

	static add(m: ng.IModule): void {
		m.controller("eirUpsertQuestionDialogCtrl", UpsertQuestionDialogCtrl);
	}
}
