import angular from "angular";
import isNil from "lodash-es/isNil";
import isNumber from "lodash-es/isNumber";
import isUndefined from "lodash-es/isUndefined";

import {
	InteractionActionEnum,
	InteractionPairOperation,
	InteractionTriggerEnum,
	SettingInputKeys,
	SettingKeys,
	SidebarTabTypes,
} from "ng2/common/constants/common.constant";
import { InteractionDefines } from "ng2/common/constants/interaction-defines.constant";

import { BindingTypeEnum } from "ng2/common/enums/binding-type.enum";
import { OrientationEnum } from "ng2/common/enums/orientation.enum";
import { SizeTypeEnum } from "ng2/common/enums/size-type.enum";
import { ValueTypeEnum } from "ng2/common/enums/value-type.enum";

import type { IContext } from "ng2/common/model/components/base/context.model";
import type { IComponentSettingCategory } from "ng2/common/model/components/settings/component-setting-category.model";
import type { IDataPair } from "ng2/common/model/data-pair.model";
import type { IJsonComponent, IJsonSize } from "ng2/common/model/databearer/components/json-component.model";
import type { IJsonFlexbox } from "ng2/common/model/databearer/components/json-flexbox.model";
import type { IJsonLabel } from "ng2/common/model/databearer/components/json-label.model";
import type { IJsonList } from "ng2/common/model/databearer/components/json-list.model";
import type { IJsonBindableField } from "ng2/common/model/databearer/json-binding.model";
import type { IJsonLayoutComponent } from "ng2/common/model/databearer/json-layout-component.model";
import type { IListDataItem } from "ng2/common/model/dataservice/list-data-item.model";
import type { IMap } from "ng2/common/model/imap.model";
import type { IInteraction } from "ng2/common/model/interaction/interaction.model";

import type { IComponent } from "common/IComponent";
import type { ILayoutComponent } from "common/interface/ilayout-component";
import type { IListComponent } from "common/interface/ilist-component";
import { Component } from "components/component";
import { BackwardCompatible } from "ng2/common/project/backward-compatible";

import { ComponentsHelper } from "components/helper";

import { FrameworkService } from "components/services/framework-service";

export class List extends Component implements IListComponent {
	static DEFAULT_WIDTH: IJsonSize = { sizeType: SizeTypeEnum.PERCENT };
	static DEFAULT_HEIGHT: IJsonSize = { sizeType: SizeTypeEnum.PERCENT };
	static SOURCE_TRIGGERS: IDataPair[] = InteractionDefines.SOURCE_TRIGGERS.concat(
		[InteractionPairOperation.ON_ITEM_CHANGED]
	);
	static TARGET_ACTIONS: IDataPair[] = [InteractionPairOperation.LIST];
	static DEFAULT_TRIGGER: IDataPair = InteractionPairOperation.ON_ITEM_CHANGED;
	static DEFAULT_ACTION: IDataPair = InteractionPairOperation.LIST;
	static SETTING_FIELDS =
	([{
		title: "Style",
		tab: SidebarTabTypes.STYLE,
		settings: [{
			title: "Show Header",
			boundarySetting: true,
			ref: SettingKeys.SHOW_LABEL,
			inputType: SettingInputKeys.CHECKBOX,
			defaultValue: true,
		}]
	}, {
		title: "Options",
		tab: SidebarTabTypes.CONTENT,
		settings: [{
			title: "Keep Selection",
			tooltip: "Keep previous selected row on reload. (With ION APIs, an object ID is preferred if using this)",
			ref: SettingKeys.KEEP_SELECTED,
			inputType: SettingInputKeys.CHECKBOX,
			defaultValue: true
		},
		{
			title: "Search Field",
			ref: SettingKeys.SEARCH,
			inputType: SettingInputKeys.CHECKBOX,
			defaultValue: false
		}]
	}] as IComponentSettingCategory[]).concat(Component.SETTING_FIELDS);

	static BINDABLE_FIELDS_2: IJsonBindableField[] = [{
		name: "Selected Item",
		ref: SettingKeys.CURRENT_ITEM,
		valueType: ValueTypeEnum.OBJECT,
		write: false,
		read: true
	}, {
		name: "Header",
		ref: SettingKeys.LABEL,
		valueType: ValueTypeEnum.LOCALIZED_STRING,
		write: true,
		read: false,
		defaultValue: { Type: BindingTypeEnum.STATIC_VALUE, value: "" }
	}];
	static readonly SCROLLABLE_PAGE_FACTOR = 2;

	label: string;
	showLabel: boolean;
	currentItem: IListDataItem; // Used in binding framework
	selectedGeneratedId: string; // Used in template
	children: IMap<ILayoutComponent> = {};
	childrenDatabearers: IJsonComponent[] = [];
	currentTemplate: IJsonLayoutComponent;
	infiniteScrollEnabled: boolean;
	filterValue: string;
	protected databearer: IJsonList;
	private currentTemplateHeight: number;
	private childrenReadyDeferred: ng.IDeferred<void>;
	private keepSelected: boolean;
	private search: boolean;

	constructor(
		obj: IJsonList,
		$element: ng.IAugmentedJQuery,
		parent: ILayoutComponent,
		frameworkService: FrameworkService
	) {
		super(obj, $element, parent, frameworkService);
		if (obj) {
			this.keepSelected = obj.keepSelected;
			this.showLabel = BackwardCompatible.getShowLabel(obj);
			this.search = obj.search || false;
		}
		this.filterValue = "";
	}

	clearSearch(): void {
		this.filterValue = "";
		this.refresh();
	}

	getFilteredList(): IJsonComponent[] {
		if (this.getScope()) {
			return this.filterList(angular.copy(this.scope.context.ListResult));
			
		}
	}

	newBoundaryEntered(boundaryId: string): void {
		super.newBoundaryEntered(boundaryId);
		this.currentTemplate = this.getBoundarySetting<IJsonLayoutComponent>(SettingKeys.ITEM_TEMPLATE);
		this.setBoundarySettingIfChanged<boolean>(SettingKeys.SHOW_LABEL);
		this.refresh();
	}

	refresh(): void {
		// Some settings affect the height of the templates
		this.updateCurrentTemplateHeight();
		this.renderChildren();
	}

	setCurrentItem(generatedId: string): void {
		const realIndex = this.getRealIndexFromGeneratedId(generatedId);
		const listResults = this.getFilteredList();
		const currItem = listResults ? listResults[realIndex] : null;
		if (currItem !== null && currItem !== this.currentItem) {
			this.selectedGeneratedId = currItem? generatedId: null;
			this.currentItem = currItem;
			this.trigger(InteractionTriggerEnum.CURRENT_ITEM_CHANGED);
			this.frameworkService.triggerExpressions();
		}
	}

	forEachItemTemplate(fn: (view: IJsonComponent) => void): void {
		for (const boundaryId in this.databearer.boundarySettings) {
			if (this.databearer.boundarySettings[boundaryId].itemTemplate) {
				fn(this.databearer.boundarySettings[boundaryId].itemTemplate);
			}
		}
	}

	loadInteraction(interaction: IInteraction): ng.IPromise<any | any[]> {
		const interactionPromise = super.loadInteraction(interaction);
		if (interaction.targetActionName === InteractionActionEnum.LIST) {
			interactionPromise.then((data: any[]) => {
				if (data.length <= 0) {
					this.selectedGeneratedId = null;
					this.onLoad();
				}
			}).finally(() => {
				this.renderChildren().then(() => {
					this.onLoad();
				});
			});
		}
		return interactionPromise;
	}

	onComponentReady(childInstance: ILayoutComponent, generatedId: string): void {
		this.children[generatedId] = childInstance;
		childInstance.forEachChild((subChild) => {
			subChild.initializeBindings();
		}, true);
		if (Object.keys(this.children).length === this.childrenDatabearers.length) {
			this.updateCurrentTemplateHeight();
			this.loadFillOptimal();
			this.childrenReadyDeferred.resolve();
		}
	}

	getChildContext(generatedId: string): IContext {
		if (this.getFilteredList()) {
			const realIndex = this.getRealIndexFromGeneratedId(generatedId);
			return $.extend({}, this.getContext(), this.getFilteredList()[realIndex]);
		} else {
			// Dummy context
			return this.getContext();
		}
	}

	calcNewHeight(): void {
		this.calcNewSizes();
	}

	calcNewWidth(): void {
		this.calcNewSizes();
	}

	resizableChildren(direction: OrientationEnum): boolean {
		return false;
	}

	getPxPerPercentComponent(ori: OrientationEnum, percent: number): number {
		return 0;
	}

	hasIndividualChildSizes(ori: OrientationEnum): boolean {
		return true;
	}

	calcNewSizes(orientation?: OrientationEnum): void {
		super.calcNewWidth();
		super.calcNewHeight();
		// children are null on startup
		if (this.children) {
			Object.keys(this.children).forEach((childKey) => {
				const child = this.children[childKey];
				child.calcNewWidth();
				child.calcNewHeight();
			});
		}
	}

	forEachParent(fn: (parent: ILayoutComponent) => void): void {
		super.forEachParent(fn);
		fn(this as any);
	}

	forEachChild(fn: (child: IComponent) => void, doGenerated: boolean): void {
		super.forEachChild(fn, doGenerated);
		if (doGenerated) {
			for (const key in this.children) {
				this.children[key].forEachChild(fn, doGenerated);
			}
		}
	}

	setBindableValue(key: string, value: any): void {
		super.setBindableValue(key, value);
		if (key === SettingKeys.SHOW_LABEL) {
			this.updateClasses();
		}
	}

	/**
	 *	Method to tell the list to load more rows, either in the bottom or the top, depending on the "down" parameter.
		*
		* @param down True if scrolling down
		*/
	loadMore(down: boolean): void {
		if (!this.infiniteScrollEnabled || !this.childrenDatabearers.length) {
			return;
		}
		const pageFactor = 0.5;
		const listData = this.getFilteredList();
		let newRowCount = Math.floor(this.getRowsPerPage() * pageFactor);
		// make sure that newRowCount is atleast 1 for lists that have low height.
		newRowCount = newRowCount === 0 ? 1 : newRowCount;
		if (down) { // Scrolling down
			const realIndexMax = this.getRealIndexFromGeneratedId(
				this.childrenDatabearers[this.childrenDatabearers.length - 1].generatedId
			);
			const remainingSlots = listData.length - (realIndexMax + 1);
			if (remainingSlots < newRowCount) {
				newRowCount = remainingSlots;
			}
			this.childrenDatabearers.splice(0, newRowCount).forEach((deletedChild) => {
				delete this.children[deletedChild.generatedId];
			});
			for (let i = 0; i < newRowCount; i++) {
				this.childrenDatabearers.push(this.getNewGeneratedComponent(realIndexMax + 1 + i));
			}
		} else { // Scrolling up
			const realIndexMin = this.getRealIndexFromGeneratedId(
				this.childrenDatabearers[0].generatedId
			);
			const remainingSlots = realIndexMin;
			if (remainingSlots < newRowCount) {
				newRowCount = remainingSlots;
			}
			this.childrenDatabearers.splice(-newRowCount, newRowCount).forEach((deletedChild) => {
				delete this.children[deletedChild.generatedId];
			});
			for (let i = 0; i < newRowCount; i++) {
				this.childrenDatabearers.unshift(this.getNewGeneratedComponent(realIndexMin - 1 - i));
			}
		}
	}

	protected getClassList(): string[] {
		const classList = super.getClassList();
		if (!this.getBoundarySetting<boolean>(SettingKeys.SHOW_LABEL)) {
			classList.push("hide-header");
		}
		return classList;
	}

	private filterList(list: any[]): any[] {
		if(list) {
			const activeFields = this.getActiveFieldsFromTemplate();
			return list.filter((values) => {
				return this.checkObjectforFilterMatch(values, activeFields);
			});
		}
		return list;
	}

	private getActiveFieldsFromTemplate(): string[] {
		const activeIDs: string[] = [];
		const breakdown = (child: IJsonComponent): void => {
			let component;
			switch (child.typedef) {
				case "Flexbox":
					component = child as IJsonFlexbox;
					component.children.forEach((child: IJsonComponent) => {
						breakdown(child);
					});
					break;
				case "Label":
					component = child as IJsonLabel;
					if (component.content.Type === BindingTypeEnum.CONTEXT_BINDING) {
						if (activeIDs.indexOf(component.content.sourceKey) === -1) {
							activeIDs.push(component.content.sourceKey);
						}
					}
					if (component.toolTip.Type === BindingTypeEnum.CONTEXT_BINDING) {
						if (activeIDs.indexOf(component.toolTip.sourceKey) === -1) {
							activeIDs.push(component.toolTip.sourceKey);
						}
					}
					break;
				//Possible components to search
				case "Image":
				case "Hyperlink":
				default:
					break;
			}
		};
		this.currentTemplate.children.forEach(child => {
			breakdown(child);
		});

		return activeIDs;
	}

	private checkObjectforFilterMatch(objectValues: any, activeFields: string[]): boolean {
		if (isNil(this.filterValue)) {
			return true;
		}
		let match = false;

		Object.entries(objectValues).some(([key, value]) => {
			if (activeFields.indexOf(key) !== -1) {
				if ((value as string).toLowerCase().indexOf(this.filterValue.toLowerCase()) !== -1) {
					match = true;
					return match;
				}
			}
		});

		return match;
	}

	private updateCurrentTemplateHeight(): void {
		const firstLiElement = this.element.find("li").first();
		if (firstLiElement.length) {
			this.currentTemplateHeight = firstLiElement.children().first().height();
		} else {
			this.currentTemplateHeight = null;
		}
	}

	private onLoad(): void {
		this.trigger(InteractionTriggerEnum.ON_LOAD);
		this.setSelection();
	}

	private setSelection(): void {
		if (this.selectedGeneratedId && this.keepSelected) {
			this.moveIndex();
			this.goToPage();
			this.moveScroll();
			this.setCurrentItem(this.selectedGeneratedId);
		} else {
			this.setCurrentItem(Object.keys(this.children)[0]);
		}
	}

	private moveIndex(): void {
		const listresult = this.getFilteredList();
		const index = ComponentsHelper.findItemIndexById(listresult, this.currentItem.id);
		if (isUndefined(index)) {
			this.setErrorTooltip("Selection failed, Item ID is not unique.");
		} else {
			this.setErrorTooltip();
		}

		if (isNumber(index) && index !== -1) {
			this.selectedGeneratedId = this.getGeneratedPrefix() + index;
		} else if (index) { //if item id does not exists, try to find correct index
			const index = this.getRealIndexFromGeneratedId(this.selectedGeneratedId);
			this.selectedGeneratedId = this.getGeneratedPrefix() +
				ComponentsHelper.incrementIndex(
					index,
					this.getContext(),
					this.currentItem,
					this.getDatasource()
				);
		} else {
			this.setCurrentItem(Object.keys(this.children)[0]);
		}
	}

	private goToPage(): void {
		if (this.selectedGeneratedId) {
			const listData = this.getFilteredList();
			const offset = 5;
			let generatedIndex = this.getRealIndexFromGeneratedId(this.selectedGeneratedId) - offset;
			if (generatedIndex < 0) {
				generatedIndex = 0;
			}
			this.childrenDatabearers = [];
			let optimalRowCount = Math.floor(this.getRowsPerPage() * List.SCROLLABLE_PAGE_FACTOR);
			if (optimalRowCount > listData.length) {
				optimalRowCount = listData.length;
			}
			const remainingSlots = listData.length - (generatedIndex + 1);
			if (remainingSlots < optimalRowCount) {
				generatedIndex = listData.length - optimalRowCount;
			}
			for (let i = 0; i < optimalRowCount; i++) {
				this.childrenDatabearers.push(this.getNewGeneratedComponent(generatedIndex + i));
			}
		}
	}

	private moveScroll(): void {
		const rowCount = Math.floor(this.getRowsPerPage()) + 1;
		const realIndex = this.getFilteredList().length - this.getRealIndexFromGeneratedId(this.selectedGeneratedId);
		if (realIndex < rowCount) {
			const listElement = this.element.find(".listview")[0];
			listElement.scrollTop = listElement.scrollHeight;
		}
	}

	private loadFillOptimal(): void {
		const optimalRowCount = Math.floor(this.getRowsPerPage() * List.SCROLLABLE_PAGE_FACTOR);
		const dataLength = this.getFilteredList() ? this.getFilteredList().length : 0;
		const remainingTilOptimaleCount = optimalRowCount - this.childrenDatabearers.length;
		const remainingDataLength = dataLength - this.childrenDatabearers.length;
		if (remainingTilOptimaleCount > 0) {
			const newRowCount = Math.min(
				remainingTilOptimaleCount, // Remaining til optimal count
				remainingDataLength
			);
			if (this.childrenDatabearers.length > 0) {
				const realIndexMax = this.getRealIndexFromGeneratedId((
					this.childrenDatabearers[this.childrenDatabearers.length - 1]).generatedId);
				for (let i = 0; i < newRowCount; i++) {
					this.childrenDatabearers.push(this.getNewGeneratedComponent(realIndexMax + 1 + i));
				}
			}
			// Disable infinite scroll when not able to fill optimal space
		} else if (remainingTilOptimaleCount < 0) {
			// TODO: remove elements for optimization
		}
		if (optimalRowCount > this.childrenDatabearers.length) {
			this.infiniteScrollEnabled = false;
		} else {
			this.infiniteScrollEnabled = true;
		}
	}

	private getGeneratedPrefix(): string {
		return this.getId() + "_row";
	}

	private updateView(): void {
		this.currentTemplate = this.getBoundarySetting<IJsonLayoutComponent>(SettingKeys.ITEM_TEMPLATE);
		this.currentTemplateHeight = null;
	}

	private getRealIndexFromGeneratedId(generatedId: string): number {
		let returnNumber = -1;
		const reg = new RegExp(this.getGeneratedPrefix() + "(\\d+)");
		const match = reg.exec(generatedId);
		if (match) {
			returnNumber = parseInt(match[1]);
		}
		return returnNumber;
	}

	private getRowsPerPage(): number {
		const paddingTopAndBottom = 33;
		const eachHeight = this.currentTemplateHeight + paddingTopAndBottom;
		const availableSpace = this.element.find(".listview").height();
		return availableSpace / eachHeight;
	}

	private getNewGeneratedComponent(realIndex: number): IJsonComponent {
		const currentTemplate = this.currentTemplate;
		const generatedId = this.getGeneratedPrefix() + realIndex;
		return {
			...currentTemplate,
			generatedId
		};
	}

	private renderChildren(): ng.IPromise<void> {
		// Arbitrary timeout to wait for rendering of list
		const RENDER_TIMEOUT = 10;
		this.childrenReadyDeferred = this.frameworkService.getNewDeferred<void>();
		this.getScope().$evalAsync(() => {
			this.childrenDatabearers = [];
			this.children = {};
		});

		this.frameworkService.getTimeout()(() => {
			const listResults = this.getFilteredList();
			const hasListResults = !!listResults;
			const hasData = hasListResults && listResults.length !== 0;
			const noData = hasListResults && listResults.length === 0;

			// init all children with a child. Leave the loadFillOptimal to fill the list
			this.childrenDatabearers = [this.getNewGeneratedComponent(0)];

			// Empty dataset when there is no data in listresults
			// An uninitialized list will have hasListResults as undefined
			if (this.frameworkService.isDesigner() && noData && !hasListResults) {
				this.childrenDatabearers = [];
			}
			// Empty dataset when there is no data in listresults and listresults eq undefined
			if (this.frameworkService.isRuntimeMode() && !hasData) {
				this.childrenDatabearers = [];
			}
			// Needed if interaction exists within a list child
			this.loadFillOptimal();

		}, RENDER_TIMEOUT);

		return this.childrenReadyDeferred.promise;
	}

	// eslint-disable-next-line @typescript-eslint/explicit-module-boundary-types
	static directive = [() => {
		return {
			template: require("./list.html"),
			replace: true
		};
	}];
}
