export class DateHelper {
	private static MAX_TIMESTAMP = 8640000000000000;
	private static MIN_TIMESTAMP = -8640000000000000;

	static parse(value: string, date?: string, locale?: string): Date {
		let datetime: Date;

		if (/\d{8}\s{1}\d{2}:\d{2}:\d{2}.\d{3}/g.exec(value)) {
			// IDO DateTime pattern
			const datetimeParts = value.split(" ");
			const date = Soho.Locale.parseDate(datetimeParts[0], {
				pattern: "yyyyMMdd",
			});
			const time = Soho.Locale.parseDate(datetimeParts[1], {
				pattern: "HH:mm:ss.SSS",
			});
			datetime = new Date(
				date.getFullYear(),
				date.getMonth(),
				date.getDate(),
				time.getHours(),
				time.getMinutes(),
				time.getSeconds(),
				time.getMilliseconds()
			);
		} else if (/\/Date\(\d+\+?\d{4}\)\//.exec(value)) {
			// Mingle DateTime pattern
			const milliseconds = /\d+/.exec(value)[0];
			datetime = new Date(parseInt(milliseconds));
		} else if (/^[0-9]{8}$/.exec(value)) {
			// M3 DateTime pattern
			datetime = Soho.Locale.parseDate(value, { pattern: "yyyyMMdd" });
		} else if (/^\d+$/.exec(value)) {
			// possibly milliseconds
			datetime = new Date(parseFloat(value));
		} else {
			datetime = new Date(value);
		}

		return datetime;
	}

	static isValidMillisecondTimestamp(value: number): boolean {
		return value < this.MAX_TIMESTAMP && value > this.MIN_TIMESTAMP;
	}

	static isValidDate(date: Date): boolean {
		return !isNaN(date.getTime());
	}
}
