import { IExtendedDataGridColumn } from "ng2/common/model/databearer/components/json-datagrid.model";

import { AngularConstants } from "common/angular-constants";

import { Debugger } from "common/debugger";
import type { IAvailableSourceField } from "common/defines";
import { UpsertDatagridColumnContentSettings } from "editor/component-settings/upsert-datagrid-columns-content-settings";
import type { IEditorDatagrid } from "editor/components/editor-datagrid";

export interface IEditColumnOptions {
	column: IExtendedDataGridColumn;
	sourceFields: IAvailableSourceField[];
	component: IEditorDatagrid;
}

export interface IDatagridContentSettingsScope extends ng.IScope {
	eirDatagridContentSettingsCtrl: DatagridContentSettingsCtrl;
}

export class DatagridContentSettingsCtrl extends UpsertDatagridColumnContentSettings {
	protected selectedComponent: IEditorDatagrid;

	protected addTitle = "Add Datagrid Column";
	protected editTitle = "Edit Datagrid Column";
}

export class DatagridContentSettingsDirective implements ng.IDirective {
	template = require("./datagrid-content-settings.html");
	scope = true;
	controller = DatagridContentSettingsCtrl;
	controllerAs = "eirDatagridContentSettingsCtrl";

	link(scope: IDatagridContentSettingsScope): void {
		scope.$on(AngularConstants.DESTROY, () => {
			Debugger.log(this, "Destroy");
			scope.eirDatagridContentSettingsCtrl.unsubscribers.forEach(unsubscribe => {
				unsubscribe();
			});
		});
	}
}
