import { AngularConstants } from "../../common/angular-constants";

import { DatagridExportTypes } from "ng2/common/constants/common.constant";
import { IDialogBaseScope } from "../../common/defines";
import { DialogButtonType, IDialogResult } from "../../common/services/dialog-service";

export interface IDatagridExportResult {
	exportName: string;
	exportType: string;
}

export class DatagridExportDialogCtrl {
	public exportType: string;
	public exportName: string;

	static $inject = [
		AngularConstants.SCOPE,
	];

	constructor(
		private $scope: IDialogBaseScope<void>,
	) {
		this.exportName = "";
		this.exportType = DatagridExportTypes.EXCEL;
	}

	public export(): void {
		const result: IDialogResult<IDatagridExportResult> = {
			value: {
				exportName: this.exportName,
				exportType: this.exportType,
			},
			button: DialogButtonType.Ok
		};

		this.$scope.eirDialog.close(result);
	}

	public close(): void {
		this.$scope.eirDialog.close();
	}

	static add(m: ng.IModule): void {
		m.controller("eirDatagridExportDialogCtrl", DatagridExportDialogCtrl);
	}
}