import { IMap } from "ng2/common/model/imap.model";
import { InlineListDataserviceSummaryDatagridCtrl } from "./dataservice-wizard-summary-ctrl";

export class InlineListDataserviceSummaryDatagridComponent implements ng.IComponentOptions {

	public controller: any;
	public controllerAs: string;
	public template: string;
	public bindings: IMap<string>;

	constructor() {
		this.bindings = {
			headTitle: "@",
			gridOptions: "<",
			summaryDatagridOptions: "<?",
		};

		this.controller = InlineListDataserviceSummaryDatagridCtrl;
		this.controllerAs = "ctrl";
		this.template = require("./dataservice-summary-datagrid.html");
	}

	static add(m: ng.IModule): void {
		m.component("eirInlineListDataserviceSummaryDatagridComponent", new InlineListDataserviceSummaryDatagridComponent());
	}
}
