import { IDataPair } from "ng2/common/model/data-pair.model";
import { AngularConstants } from "../../angular-constants";

enum OrderEnum {
	ASCENDING = "",
	DESCENDING = "-"
}

export class SortPopupmenuCtrl {

	// component bindings
	public sortOptions: IDataPair[];
	public bindingSortValues: any[];
	public localPrefix: string;

	public get sortValues(): any[] {
		return this.bindingSortValues;
	}
	public set sortValues(value: any[]) {
		this.bindingSortValues = value;
		if (value && this.currentSelected && this.currentSelectedSortOrder) {
			this.doSort();
		}
	}

	public sortOrderTypes: IDataPair[] = [
		{ name: "Ascending", value: 0 },
		{ name: "Descending", value: 1 }
	];

	public currentSelected: IDataPair;
	public currentSelectedSortOrder: IDataPair;

	private readonly sortOn = "sort-on";
	private readonly sortOrder = "sort-order";

	$inject = [
		AngularConstants.FILTER
	];

	constructor(
		private $filter: ng.IFilterService
	) { }

	public $onInit(): void {
		const sortOn = JSON.parse(localStorage.getItem(this.localPrefix + this.sortOn));
		const sortOrder = JSON.parse(localStorage.getItem(this.localPrefix + this.sortOrder));

		if (!sortOn || !sortOrder) {
			const sortOption = this.sortOptions[0];
			const sortOrder = this.sortOrderTypes[1];

			localStorage.setItem(this.localPrefix + this.sortOn, JSON.stringify(sortOption));
			localStorage.setItem(this.localPrefix + this.sortOrder, JSON.stringify(sortOrder));
			this.currentSelected = sortOption;
			this.currentSelectedSortOrder = sortOrder;
		} else {
			this.currentSelected = sortOn;
			this.currentSelectedSortOrder = sortOrder;
		}
		this.doSort();
	}

	public onSort(sortOption: IDataPair): void {
		this.currentSelected = sortOption;
		localStorage.setItem(this.localPrefix + this.sortOn, JSON.stringify(sortOption));

		this.doSort();
	}

	public onSortOrder(sortOrder: IDataPair): void {
		this.currentSelectedSortOrder = sortOrder;
		localStorage.setItem(this.localPrefix + this.sortOrder, JSON.stringify(sortOrder));

		this.doSort();
	}

	private doSort(): void {
		const orderBy = this.currentSelectedSortOrder.value ?
			OrderEnum.DESCENDING : OrderEnum.ASCENDING;

		this.bindingSortValues = this.$filter("orderBy")(
			this.bindingSortValues, `${orderBy}(${this.currentSelected.value})`);
	}

}
