import { IMultiSelectOptions } from "../../model/sohoxi/sohoxi-multiselect.model";
import { Utils } from "../utils";

/**
 * Multiselect Directive
 *
 * Provides multiselection.
 * Note that the element must have an ID.
 */
export class MultiSelectDirective implements ng.IDirective {
	public restrict = "A";
	public scope = false;

	private static readonly DIRECTIVE_NAME = "xiMultiselect";

	constructor(private $timeout: ng.ITimeoutService) {}

	public link(
		scope: ng.IScope,
		element: ng.IAugmentedJQuery,
		attributes: ng.IAttributes
	): void {
		// ID is required
		if (!attributes.id) {
			return;
		}
		const options = attributes[MultiSelectDirective.DIRECTIVE_NAME];
		const unbindWatcher = scope.$watch(
			options,
			(newOptions: IMultiSelectOptions) => {
				// Set selected after binding
				this.$timeout(() => {
					if (newOptions) {
						element.multiselect(newOptions);
					} else {
						element.multiselect();
					}
				}, 100); // tslint:disable-line:no-magic-numbers
			}
		);
		// let modelWatcher;
		// const modelAttr = attributes["ngModel"];
		// if (modelAttr) {
		// 	modelWatcher = scope.$watch(modelAttr, () => {
		// 		if (api) {
		// 			this.$timeout(() => {
		// 				api.setValue();
		// 			}, 100);
		// 		}
		// 	});
		// }

		// let disabledWatcher;
		// const disabledAttr = attributes["ngDisabled"];
		// if (disabledAttr) {
		// 	disabledWatcher = scope.$watch(disabledAttr, newValue => {
		// 		if (api) {
		// 			if (newValue === false) {
		// 				api.enable();
		// 			} else if (newValue === true) {
		// 				api.disable();
		// 			}
		// 		}
		// 	});
		// }

		// Tear down
		element.on("$destroy", () => {
			const multiSelectApi = Utils.getData(element, "multiselect");
			if (multiSelectApi) {
				multiSelectApi.destroy();
			}
			unbindWatcher();
			// if (modelWatcher) {
			// 	modelWatcher();
			// }
			// if (disabledWatcher) {
			// 	disabledWatcher();
			// }
		});
	}

	public static add(m: ng.IModule): void {
		m.directive(MultiSelectDirective.DIRECTIVE_NAME, [
			"$timeout",
			timeout => {
				return new MultiSelectDirective(timeout);
			}
		]);
	}
}
