import { MaskProcess } from "ng2/common/constants/common.constant";
import { IDataPair } from "ng2/common/model/data-pair.model";
import { IJsonTextBox } from "ng2/common/model/databearer/components/json-textbox.model";

import { ISettingsClassInstance } from "../../defines";

import { TextBox } from "../../../../components/textbox/textbox";
import { ComponentSettingsCtrl } from "../component-settings";
import { TextboxStyleSettingsDirective } from "./textbox-style-settings";

export class TextboxSettings implements ISettingsClassInstance {

	private static readonly instance: TextboxSettings = new TextboxSettings();

	public static getInstance(): TextboxSettings {
		return this.instance;
	}

	public readonly availableMasks: IDataPair[] = [
		{ name: "None", value: MaskProcess.NONE },
		{ name: "Number", value: MaskProcess.NUMBER },
		{ name: "Password", value: MaskProcess.PASSWORD },
		{ name: "Date/Time", value: MaskProcess.DATE_TIME },
		{ name: "String", value: MaskProcess.STRING }
	];

	public styleSettingsDirective: ng.IDirective = new TextboxStyleSettingsDirective();

	public create(id: string): IJsonTextBox {
		return ComponentSettingsCtrl.create(id, TextBox) as IJsonTextBox;
	}

	public constructor() {
		if (TextboxSettings.instance) {
			throw new Error("Error: Instantiation failed: Use TextboxSettings.getInstance() instead of new.");
		}
	}
}
