// TODO: Fix the so that contextualactionpanel is in JQuery global
import angular from "angular";
import isUndefined from "lodash-es/isUndefined";

import { EventTypes, InjectNames } from "ng2/common/constants/common.constant";
import { AngularConstants } from "../angular-constants";
import { IDialogBaseScope, KeyCodeEnum } from "../defines";
import { Helper } from "../helper";

// Treating dialog-service as an asset
/* tslint:disable */
/**
* UI utility functions.
*/
export class Util {
	private static chars = "0123456789ABCDEFGHIJKLMNOPQRSTUVWXYZ";

	/**
	* Removes all popupmenus from the given element.
	* @param element Element from which to search for and remove xi-popupmenus
	*/
	public static removePopupMenus(element: JQuery): void {
		const popupMenus = element.find(".btn-menu[aria-haspopup]");
		for (let i = 0; i < popupMenus.length; i++) {
			const data = $(popupMenus[i]).data("popupmenu");
			if (data) {
				data.close();
				data.destroy();
			}
		}
	}

	/**
	 * Creates a string with random uppercase letters and numbers.
	 * The default length is 16 if the stringLength parameter is omitted.
	 */
	public static random(stringLength: number = 16): string {
		const chars = this.chars;
		let randomstring = "";
		for (let i = 0; i < stringLength; i++) {
			const rnum = Math.floor(Math.random() * chars.length);
			randomstring += chars.substring(rnum, rnum + 1);
		}
		return randomstring;
	}
}

/**
 * Represents options for a toast message.
 *
 * @since 1.0
 */
export interface IToastOptions {
	/**
	 * Gets or sets the toast title.
	 */
	title: string;

	/**
	 * Gets or sets the toast message.
	 */
	message: string;

	/**
	 * Sting that stated where the toast should be displayed
	 *
	 * Possible values: top right, top left, bottom left, bottom right
	 */
	position?: string;

	/**
	 * States whether the notification should be audible only.
	 */
	audibleOnly?: boolean;

	/**
	 * States whether or not to display a progress bar.
	 */
	progressBar?: boolean;

	/**
	 * States for how long the toast should be visible (milliseconds).
	 */
	timeout?: number;
}

export type MessageDialogStatus = "error" | "alert" | "success";
/**
 * Represents options for a message dialog.
 *
 * Use either the buttons or standardButtons property.
 *
 * Example options for a message with the standrad buttons Yes and No:
 *
 *		let options: lm.IMessageDialogOptions = {
 *			title: "Confirm Delete",
	*			message: "Are you sure that you want to delete the selected item?",
	*			standardButtons: s.StandardDialogButtons.YesNo
	*		}
	*
	* Example options for a message with custom buttons:
	*
	*		let options: lm.IMessageDialogOptions = {
	*			title: "Remove Widget",
	*			message: "Are you sure that you want to remove the widget?",
	*			buttons: [{
	*				text: "Remove it!",
	*				value: "Yes",
	*				isDefault: true
	*			}, {
	*					text: "Keep it!",
	*					value: "No",
	*					isDefault: false
	*				}]
	*		}
	*
	* @since 1.0
	*/
export interface IMessageDialogOptions {
	/**
	 * Gets or sets the dialog title.
	 */
	title: string;

	/**
	 * Gets or sets the dialog message.
	 */
	message: string;

	/**
	 * Gets or sets an optional array of dialog buttons.
	 *
	 * Use this property or the standardButtons property.
	 */
	buttons?: IDialogButton[];

	/**
	 * Gets or sets an optional stanard button configuration.
	 *
	 * Use this property or the buttons property.
	 */
	standardButtons?: StandardDialogButtons;

	/**
	 * Gets or sets an optional value that indicates if this is an error message.
	 *
	 * If this property is set to true the message dialog title will be shown with the error styling.
	 */
	isError?: boolean;
	/**
	 * Pass a status to style icon and title color ('error', 'alert', 'success')
	 */
	status?: MessageDialogStatus;
	/**
	 * String to include in message title that is strictly audible.
	 */
	audibleLabel?: string;

	/**
	 * Sets css class on modal.
	 */
	cssClass?: string;

	/**
	 * Specifies wether or not to automatically set primary button styling.
	 *
	 * True is default.
	 */
	hasPrimaryButton?: boolean;
}

/**
 * Defines standard button combinations for a message dialog.
 *
 * @since 1.0
 */
export enum StandardDialogButtons {
	/**
	 * Shows an OK button (1).
	 */
	Ok = 1,
	/**
	 * Shows an OK button and a Cancel button (2).
	 */
	OkCancel = 2,
	/**
	 * Shows a Yes and a No button (3).
	 */
	YesNo = 3,
	/**
	 * Shows a Yes, No and a Cancel button (4).
	 */
	YesNoCancel = 4,
}

/**
 * Represents options for a dialog.
 *
 * @since 1.0
 */
export interface IDialogOptions<ParameterType> {
	/**
	 * Gets or sets the dialog title.
	 */
	title: string;

	/**
	 * Gets or sets an optional parameter.
	 *
	 * If this property is set when showing a dialog it will be available on the[[IDialog]] instance.
	 */
	parameter?: ParameterType;

	/**
	 * Gets or sets an optional parent scope.
	 */
	scope?: ng.IScope;

	/**
	 * Gets or sets an optional AngularJS template.
	 */
	template?: string;

	/**
	 * Gets or sets an optional AngularJS template URL.
	 */
	templateUrl?: string;

	/**
	 * Gets or sets an optional array of dialog buttons.
	 *
	 * This property should not be set if custom buttons are added in the dialog template.
	 */
	buttons?: IDialogButton[];

	/**
	 * Gets or sets an optional style string that will be applied to the modal dialog instance.
	 */
	style?: string;

	/**
	 * Gets or sets an optional CSS class that will be applied to the modal dialog instance.
	 */
	cssClass?: string;

	/**
	 * Gets or sets an optional ID of the modal dialog instance.
	 */
	id?: string;

	//Hides the overlay when true. Used in wizard-interaction
	hideOverlay?: boolean;
	draggable?: true;
}

/**
 * Represents a dialog button.
 *
 * @since 1.0
 */
export interface IDialogButton {
	/**
	 * Gets or sets the text to display on the button.
	 */
	text: string;

	/**
	 * Gets or sets the type of button defined in [[DialogButtonType]].
	 */
	type?: any;

	/**
	 * Gets or sets an optional value object that will be returned in the [[IDialogResult]] if set.
	 */
	value?: any;

	/**
	 * Gets or sets an optional ID for the button.
	 */
	id?: string;

	/**
	 * Gets or sets an optional value that indicates if the button is a link.
	 */
	isLink?: boolean;

	/**
	 * Gets or sets an optional value that indicates if the button is the default button.
	 */
	isDefault?: boolean;

	/**
	 * Gets or sets an optional svg icon path for the button
	 */
	icon?: string;

	/**
	 * Gets or sets an optional css class for the dialog button
	 */
	cssClass?: string;

	/**
	 * FIXME: Do not use this attribute to set a function on your button. This does not work
	 * Rather, set a type on the button and switch on different types in the resolved
	 * promise.
	 */
	click?: Function;
}

/**
 * Represents the result from a closed dialog.
 *
 * @since 1.0
 */
export interface IDialogResult<ResultType> {
	/**
	 * Gets or sets the type of button clicked, defined in [[DialogButtonType]].
	 *
	 * This property might not be set depending on how the dialog was dismissed.
	 */
	button?: DialogButtonType;

	/**
	 * Gets or sets an optional result value.
	 *
	 * This property can be used for a result value object of any type in custom dialog scenarios.
	 */
	value?: ResultType;
}

/**
 * Defines the different types of buttons that can be used in dialogs.
 *
 * @since 1.0
 */
export enum DialogButtonType {
	/**
	 * No button is used or the button is unknown (1).
	 */
	None = 1,
	/**
	 * OK button (2).
	 */
	Ok = 2,
	/**
	 * Cancel button (3).
	 */
	Cancel = 3,
	/**
	 * Yes button (4).
	 */
	Yes = 4,
	/**
	 * No button (5).
	 */
	No = 5,
	/**
	 * Custom button (6).
	 */
	Custom = 6,
	/**
	 * Back button (7).
	 */
	Back = 7
}

export interface IContextualActionPanelOptions {
	title: string;
	template?: string;
	templateUrl?: string;
}

/**
* @internal
*/
interface IDialogButtonEx extends IDialogButton {
	click: Function;
}

export class DialogService {

	static $inject = [
		AngularConstants.ROOT_SCOPE,
		AngularConstants.COMPILE,
		AngularConstants.Q
	];

	private dialogOptions: IDialogOptions<any>;

	constructor(private $rootScope: ng.IScope, private $compile: ng.ICompileService, private $q: ng.IQService) { }

	public show<ParameterType, ResultType>(options: IDialogOptions<ParameterType>): ng.IPromise<IDialogResult<ResultType>> {
		const deferred = this.$q.defer();

		let title: any = null;
		if (options) {
			title = options.title;
			this.dialogOptions = options;
		}
		if (!title) {
			title = "";
		}

		const dialog = new Dialog(deferred);

		const modalScope = (options.scope || this.$rootScope).$new() as IDialogBaseScope<ParameterType>;
		if (options && options.parameter) {
			dialog.parameter = options.parameter;
		}
		modalScope.eirDialog = dialog;

		let contentTemplate = options.template;
		if (!contentTemplate) {
			const url = options.templateUrl;
			if (!url) {
				throw "One of the template and templateUrl properties must be set";
			}
			contentTemplate = "<div ng-include=\"'" + url + "'\"></div>";
		}

		// TODO: Add support for standard buttons
		// TODO: Default style

		const style = options.style || "";
		const cssClass = options.cssClass || "";
		const draggable = !!options.draggable;
		const id = options.id || Util.random(8);
		const template = `
		<div class="modal lm-scroll-no-y ${cssClass} ${options.draggable ? "draggable-dialog" : ""}" id="${id}" style="${style}">
			<div class="modal-content">
				<div class="modal-header"></div>
				<div class="modal-body">${contentTemplate}</div>`;

		const element = $(template);

		// Modify the template slightly depending if it's a regular modal or contextual action panel modal
		const headerElement = element.find(".modal-header");
		headerElement.append($(`<h1 class="modal-title">${ title }<h1>`));

		const compiledTemplate = this.$compile(element)(modalScope);
		if (draggable) { // Draggable dialog gets a position: relative added after compile. thus needs to be removed
			compiledTemplate.css("position", "");
		}

		$("body").append(compiledTemplate);

		if (this.hasHideOverlayOption()) {
			this.toggleOverlay();
		}

		let buttons: IDialogButtonEx[] = null;
		if (options.buttons) {
			buttons = this.setSpecificButtons(options.buttons, deferred, dialog);
		}
		/*
			TODO: fix for multiple ng-includes and no includes
		maybe like this: http://www.competa.com/blog/2013/12/how-to-detect-when-all-your-nested-ng-includes-have-loaded-their-content-in-angularjs/
		*/
		//			if (options.template && options.template.indexOf("ng-include") === -1) {
		//				this.createAndOpenModal(element, dialog, title, buttons, modalScope);
		//			} else { // If templateUrl, wait for included content
		//			}
		const unsubscribe = modalScope.$on("$includeContentLoaded", () => {
			this.createAndOpenModal(element, dialog, title, buttons, modalScope);
			unsubscribe();
		});
		if (isUndefined(options.templateUrl) && !isUndefined(options.template)) {
			this.createAndOpenModal(element, dialog, title, buttons, modalScope);
		}

		return deferred.promise;
	}

	private createAndOpenModal(element: JQuery, dialog: Dialog, title: string, buttons: IDialogButtonEx[], modalScope: ng.IScope): void {

		/* Fix for SVG icons in IE11 on Windows10
			related articles:
			http://stackoverflow.com/questions/33040911/svg-icons-dissapears-in-windows-10-ie-11-using-jquery-sortable
			http://jira/browse/SOHO-3069
		*/
		if (Helper.isIE()) {
			setTimeout(() => {
				element.find("use").each((idx: number, elem: Element) => {
					elem.setAttribute("xlink:href", elem.getAttribute("xlink:href"));
				});
			});
		}

		const modal = (<any>element).modal({
			title: title,
			buttons: buttons
		});
		dialog.init(modal);

		if (!buttons) { // To calculate width of included buttons
			modal.modal("addButtons");
		}

		// Destroy the modal scope when the modal is closing.
		modal.one("beforeclose", () => {
			modalScope.$destroy();
			Util.removePopupMenus(element);
			if (this.hasHideOverlayOption()) {
				this.toggleOverlay();
			}
		});

		modal.modal("open");
	}

	private hasHideOverlayOption<T>(options?: IDialogOptions<T>): boolean {
		const dialogOptions = this.dialogOptions;

		return dialogOptions && dialogOptions.hideOverlay ? true : false;
	}

	public showContextualActionPanel(options: IContextualActionPanelOptions): void {
		const template = options.template || `<div ng-include="'${options.templateUrl}'"></div>`;
		const element: any = $(template);

		($("body") as any).contextualactionpanel({
			title: options.title,
			content: this.$compile(element)(this.$rootScope.$new()),
			trigger: "immediate",
			buttons: [
				{
					text: "Close",
					cssClass: "btn",
					icon: "#icon-close",
				},
			],
		});
	}

	public showMessage(options: IMessageDialogOptions): ng.IPromise<IDialogResult<any>> {
		const deferred = this.$q.defer();
		const element: any = $("body");
		this.dialogOptions = options;

		if (!options.buttons && !options.standardButtons) {
			options.standardButtons = StandardDialogButtons.Ok;
		}

		if (options.standardButtons !== null) {
			options.buttons = this.setDefaultButtons(options.standardButtons, deferred, options.hasPrimaryButton === undefined || options.hasPrimaryButton);
		} else if (options.buttons) {
			options.buttons = this.setSpecificButtons(options.buttons, deferred, null);
		}

		element.message(options);

		const isChildDialog = this.hasHideOverlayOption();
		if (isChildDialog) {
			this.toggleOverlay();
		}
		return deferred.promise.finally(() => {
			if (isChildDialog) {
				this.toggleOverlay();
			}
		});
	}

	public showError(title: string, message: string): ng.IPromise<IDialogResult<void>> {
		const options: IMessageDialogOptions = {
			title: `<span>${title}</span>`,
			message: message,
			standardButtons: StandardDialogButtons.Ok,
			isError: true, //This will change to status: "error" in later SoHo Update to support status: "warning",
			status: "error",
			audibleLabel: "Error",
		};
		//if error dialog already exist do not show
		const errorDialogExists = angular.element(".is-error.modal-title");
		if (errorDialogExists.length === 0) {
			return this.showMessage(options);
		}
	}

	public showYesNo(
		title: string,
		message: string,
		cssClass?: string
	): ng.IPromise<IDialogResult<void>> {
		const options: IMessageDialogOptions = {
			title: title,
			message: message,
			standardButtons: StandardDialogButtons.YesNo,
			cssClass: cssClass
		};
		return this.showMessage(options);
	}

	private toggleOverlay(): void {
		$("body").toggleClass("no-overlay");
	}

	/**
	* Shows a toast notification with the specified options.
	*
	* Defaults position to "bottom right" and timeout to 3000 if not set.
	*/
	public showToast(options: IToastOptions): void {
		if (!options.position) {
			options.position = "bottom right";
		}
		if (!options.timeout) {
			options.timeout = 3000;
		}
		$("body")["toast"](options);
	}

	private setDefaultButtons(defaults: StandardDialogButtons, deferred: ng.IDeferred<any>, hasPrimaryButton: boolean): IDialogButtonEx[] {
		const buttons: IDialogButtonEx[] = [];
		if (defaults === StandardDialogButtons.OkCancel || defaults === StandardDialogButtons.YesNoCancel) {
			buttons.push({
				text: "Cancel",
				click: function () {
					(<any>$(this)).modal("close");
					deferred.resolve({ button: DialogButtonType.Cancel });
				},
				isDefault: false
			});
		}
		if (defaults === StandardDialogButtons.Ok || defaults === StandardDialogButtons.OkCancel) {
			buttons.push({
				text: "OK",
				click: function () {
					(<any>$(this)).modal("close");
					deferred.resolve({ button: DialogButtonType.Ok });
				},
				isDefault: hasPrimaryButton
			});
		} else if (defaults === StandardDialogButtons.YesNo || defaults === StandardDialogButtons.YesNoCancel) {
			buttons.push({
				text: "No",
				click: function () {
					(<any>$(this)).modal("close");
					deferred.resolve({ button: DialogButtonType.No });
				},
				isDefault: false
			});
			buttons.push({
				text: "Yes",
				click: function () {
					(<any>$(this)).modal("close");
					deferred.resolve({ button: DialogButtonType.Yes });
				},
				isDefault: hasPrimaryButton
			});
		}

		return buttons;
	}

	private setSpecificButtons(specificButtons: IDialogButton[], deferred: ng.IDeferred<IDialogResult<any>>, dialog: IDialog<any>): IDialogButtonEx[] {
		const buttons: IDialogButtonEx[] = [];
		specificButtons.forEach((button: IDialogButtonEx, index: any) => {
			button.click = function () {
				if (dialog) {
					dialog.close();
				} else {
					(<any>$(this)).modal("close");
				}
				let result: any;
				if (dialog) {
					result = dialog.result || {};
					result.button = button.type;
					deferred.resolve(result);
				} else {
					result = { button: button.type };
				}
				deferred.resolve(result);
			};
			buttons.push(button);
		});

		return buttons;
	}

	static add(ngModule: ng.IModule) {
		ngModule.service(InjectNames.DIALOG_SERVICE, DialogService);
	}
}

/**
 * Represents a dialog event.
 *
 * An instance of this type will be a parameter to the closing and closed events on the [[IDialog]].
 *
 * @since 1.0
 */
export interface IDialogEvent<T> {
	/**
	 * Gets or sets the dialog instance that is the source of the event.
	 */
	dialog: IDialog<T>;

	/**
	 * Gets or sets an optional value that indicates if the event should be cancelled.
	 *
	 * Note that only the closing event can currently be cancelled.
	 */
	cancel?: boolean;
}

/**
 * Represents a modal dialog instance.
 *
 * The dialog instance can be retrieved from the scope in a dialog controller using the name "lmDialog".
 *
 * TODO: Add <ResultType> generic type variable.
 *
 * @since 1.0
 */
export interface IDialog<ParameterType> {
	/**
	 * Closes the dialog.
	 *
	 * @param result Optional dialog result. The result property will be set if a value is provided.
	 */
	close(result?: IDialogResult<any>): void;

	/**
	 * Gets or sets a function that will be called when the dialog is closing.
	 *
	 * The closing of the dialog can be cancelled by setting the cancel property to true on the event parameter.
	 */
	closing: (e: IDialogEvent<any>) => void;

	/**
	 * Gets or sets a function that will be called when the dialog is closed.
	 */
	closed: (e: IDialogEvent<any>) => void;

	/**
	 * Gets or sets a function that will be called when the dialog is opened.
	 */
	opened: (e: IDialogEvent<any>) => void;

	/**
	 * Hides dialog in background without closing it.
	 */
	hide: () => void;

	/**
	 * Unhides dialog which previously was hidden.
	 */
	show: () => void;

	/**
	 * Get promise to hook up any interactions when result is given
	 * TODO: rm this solution when soho has fixed it.
	 * #MUPS-1124
	 */
	getPromise: () => ng.IPromise<any>;

	/**
	 * Gets or sets an optional parameter.
	 *
	 * This property will have the value set on the [[IDialogOptions]] instance when showing the dialog.
	 */
	parameter?: ParameterType;

	/**
	 * Gets or sets the dialog result.
	 */
	result?: IDialogResult<any>;
}

class Dialog implements IDialog<any> {
	public closing: (e: IDialogEvent<any>) => void;
	public closed: (e: IDialogEvent<any>) => void;
	public opened: (e: IDialogEvent<any>) => void;

	public parameter: any;
	public result: IDialogResult<any>;

	private modal: JQuery;
	private canClose = false;

	constructor(private deferred: ng.IDeferred<any>) { }

	private resolve(): void {
		const modal = this.modal;
		if (modal) {
			// SOHO FIX MUPS-1107
			// Removing excessive nodes: modal-wrapper & modal-page-container when a dialog is closed
			const modalParent = modal.parent();
			const modalGrandParent = modalParent.parent();
			if (modalParent.hasClass("modal-wrapper") && modalGrandParent.hasClass("modal-page-container")) {
				modalGrandParent.remove();
				modalParent.remove();
			}
			modal.remove();
			this.modal = null;
		}
		const result = this.result || {};
		this.deferred.resolve(result);
	}

	public init(modal: JQuery): void {
		this.modal = modal;

		// TODO: rm this solution when soho has fixed it.
		// #MUPS-1124
		const closeOnEscape = ($event: JQueryEventObject) => {
			if ($event.keyCode === KeyCodeEnum.ESCAPE) {
				const result: IDialogResult<KeyCodeEnum> = { value: KeyCodeEnum.ESCAPE };
				this.close(result);
			}
		};
		angular.element("body").on(EventTypes.KEYDOWN + ".dialog", closeOnEscape);

		modal.one("beforeclose", () => {
			angular.element("body").off(EventTypes.KEYDOWN + ".dialog", closeOnEscape);
			const closing = this.closing;
			if (closing) {
				const e = <IDialogEvent<any>>{ dialog: this };
				closing(e);
				return e.cancel ? false : true;
			} else {
				return this.canClose;
			}
		});

		modal.one("afterclose", () => {
			const closed = this.closed;
			if (closed) {
				const e = <IDialogEvent<any>>{ dialog: this };
				closed(e);
			}
			this.resolve();
		});

		modal.one("open", () => {
			const opened = this.opened;

			modal.attr("dialog-id", Helper.generateUniqueId());
			$(".overlay:not([id])").attr("id", modal.attr("dialog-id"));
			if (opened) {
				const e = <IDialogEvent<any>>{ dialog: this };
				opened(e);
			}
		});
	}

	// TODO: rm this solution when soho has fixed it.
	// #MUPS-1124
	public getPromise(): ng.IPromise<any> {
		return this.deferred.promise;
	}

	public close(result?: IDialogResult<any>): void {
		this.canClose = true;
		if (result) {
			this.result = result;
		}

		const modal = this.modal;
		if (modal) {
			(<any>modal).modal("close");
		}
	}

	public hide(): void {
		this.modal.hide();
		$(`div#${this.modal.attr("dialog-id")}.overlay`).hide();

	}

	public show(): void {
		this.modal.show();
		$(`div#${this.modal.attr("dialog-id")}.overlay`).show();
	}
}
