import angular from "angular";

import { AngularConstants } from "../../../../common/angular-constants";
import { EditorService } from "../../services/editor-service";
import type { IGenericSettingScope, ISettingController } from "../generic-settings";

import { SettingsBase } from "../settings-base";

import { BorderType, InjectNames } from "ng2/common/constants/common.constant";

import type { IJsonBorder } from "ng2/common/model/databearer/components/json-flexbox.model";
import type { IBoundary } from "ng2/common/model/project/boundary.model";

import type { IEditorComponent } from "common/interface/ieditor-component";

class BorderSettingsCtrl extends SettingsBase implements ISettingController {

	border: IJsonBorder;
	borderType: BorderType;
	borderAllValue: number;
	settingTitle: string;

	static $inject = [
		AngularConstants.SCOPE,
		InjectNames.EDITOR_SERVICE
	];

	constructor(
		protected $scope: IGenericSettingScope,
		editorService: EditorService
	) {
		super($scope, editorService);
		this.onComponentChanged(this.selectedComponent);
	}

	refresh(): void {
		this.settingTitle = "Border";
		const border = angular.copy(this.readSettingValue<IJsonBorder>(this.$scope.settingMeta));
		if (border) {
			this.border = border;
			this.borderType =
				border.top === border.right && border.right === border.bottom && border.bottom === border.left
					? BorderType.ALL
					: BorderType.INDIVIDUAL;
			this.borderAllValue = border.top || 0;
		}
	}

	protected onComponentChanged(newComponent: IEditorComponent): void {
		if (newComponent) {
			this.selectedComponent = newComponent;
			this.refresh();
		}
	}

	protected onBoundaryChanged(newBoundary: IBoundary): void {
		super.onBoundaryChanged(newBoundary);
		this.refresh();
	}

	onChangeBorderType(type: BorderType): void {
		this.borderType = type;
		this.onChangeBorder();
	}

	onChangeBorder(): void {
		this.checkForNull();

		if (this.borderType === BorderType.ALL) {
			this.setBorderValues();
		}
		this.onChangeSetting(this.$scope.settingMeta, this.border);
	}

	private checkForNull(): void {
		for (const key in this.border) {
			if (this.border[key] === null) {
				this.border[key] = 0;
			}
		}
	}

	private setBorderValues(): void {
		const value = this.borderAllValue || 0;
		this.border.top = value;
		this.border.right = value;
		this.border.bottom = value;
		this.border.left = value;
	}
}

export class BorderSettingsDirective implements ng.IDirective {
	template = require("./border-setting.html");
	scope = {
		settingMeta: "=",
		register: "&"
	};
	controller = BorderSettingsCtrl;
	controllerAs = "eirBorderSettingsCtrl";

	static add(m: ng.IModule): void {
		m.directive("eirBorderSetting", () => new BorderSettingsDirective());
	}
}
