import { ComponentClassNames, ContextKeys, SettingKeys } from "ng2/common/constants/common.constant";
import { BindingTypeEnum } from "ng2/common/enums/binding-type.enum";

import type { IDatasource } from "common/model/datasource/datasource.model";
import type { IJsonImage } from "common/model/component/image.model";
import type { IEditorComponent } from "common/interface/ieditor-component";
import type { IEditorFlexbox } from "editor/components/editor-flexbox";

import { Image } from "components/image/image";
import { EditorComponentCtrl, IEditorComponentScope } from "editor/components/editor-component";

export interface IEditorImage extends IEditorComponent, Image {
	parent: IEditorFlexbox;
	// Overrides to make dual extend work
	getScope(): IEditorComponentScope;
	getDatabearer(): IJsonImage;
}

export class EditorImageCtrl extends EditorComponentCtrl {
	public static add(m: ng.IModule): void {
		m.controller(
			ComponentClassNames.EDITOR_COMPONENT_PREFIX + Image.name,
			EditorImageCtrl
		);
	}

	protected component: IEditorImage;

	public setDatasource(newDatasource: IDatasource | null): ng.IPromise<void> {
		const promise = super.setDatasource(newDatasource);
		if (newDatasource) {
			const newValueSource = { Type: BindingTypeEnum.CONTEXT_BINDING, sourceKey: ContextKeys.DETAIL_RESULT };
			this.bindingManagementService.registerContextBinding(this.component, SettingKeys.SOURCE, newValueSource);
			this.component.getDatabearer().source = newValueSource;
		}
		return promise;
	}

	protected clearData(): void {
		super.clearData();
		this.bindingManagementService.unregisterContextBinding(this.component, ContextKeys.DETAIL_RESULT);
		this.component.getDatabearer().source = { Type: BindingTypeEnum.STATIC_VALUE, value: "" };
	}
}
