import angular from "angular";

import { IComponentRendered } from "ng2/common/constants/sohoxi.constant";

import { IListViewApi, IListViewOptions } from "../../model/sohoxi/sohoxi-listview.model";
import { Utils } from "../../sohoxi/utils";

/**
 * Listview Directive.
 *
 * Example:
 * scope["options"] = { dataset: items, selectable: "multiple" };
 * scope["items"] = ["first", "second", "third"];
 *
 * <div class="card">
 * 	<div class="card-header"></div>
 * 	<div class="card-content">
 * 		<div class="listview-toolbar toolbar is-hidden">
 * 			<div class="buttonset">
 * 				<button class="btn-tertiary" title="remove" type="button"
 * 					ng-click="ctrl.removeSelected()"
 * 				>
 * 					Remove
 * 				</button>
 * 			</div>
 * 		</div>
 * 		<div class="listview is-muliselect" id="multiselect-listview"
 * 			xi-listview="options"
 *			>
	* 			<ul>
	* 				<li ng-repeat="item in items track by $id($index)">
	* 					<p>{{item}}</p>
	* 				</li>
	* 			</ul>
	* 		</div>
	* 	</div>
	* </div>
	*/
export const LISTVIEW_RENDERED = "finishedListviewCreation";
export class ListviewDirective implements ng.IDirective {
	private static readonly DIRECTIVE_NAME = "xiListview";

	public restrict = "A";
	public scope = false;

	public link(
		scope: ng.IScope,
		element: ng.IAugmentedJQuery,
		attributes: ng.IAttributes
	): void {
		const options = attributes[ListviewDirective.DIRECTIVE_NAME];
		const toolbar = element
			.closest(".widget, .card")
			.find(".listview-toolbar");
		const unbindWatcher = scope.$watch(
			options,
			(newOptions: IListViewOptions) => {
				let api: IListViewApi;

				if (newOptions) {
					api =  Utils.getData(element, "listview");

					if (api) {
						api.updated(newOptions);
					} else {
						element.listview(newOptions);
						api =  Utils.getData(element, "listview");

						const toolbarElement: any = toolbar
							.removeClass("is-visible")
							.one("animateClosedComplete",
								event => {
									event.stopPropagation();
									toolbar.css("display", "none");
								});

						toolbarElement["animateClosed"](); // tslint:disable-line:no-string-literal

						angular.forEach(
							api.selectedItems,
							selected => selected.removeClass());
					}
				} else {
					api = element.listview();
				}

				if (newOptions?.id !== undefined) {
					const finishedResult: IComponentRendered = {
						id: newOptions.id,
						api,
					};
					element.trigger(LISTVIEW_RENDERED, finishedResult);
				}
			},
			true
		);
		// Tear down
		element.on("$destroy", () => {
			const listViewData = Utils.getData(element, "listview");
			if (listViewData) {
				listViewData.destroy();
			}
			unbindWatcher();
		});
	}

	public static add(m: ng.IModule): void {
		m.directive(ListviewDirective.DIRECTIVE_NAME, [
			() => {
				return new ListviewDirective();
			}
		]);
	}
}
