import {
	ChangeDetectorRef,
	Component,
	ElementRef,
	Renderer2,
	ViewChild,
} from "@angular/core";
import { SohoInputComponent, SohoMessageService } from "ids-enterprise-ng";

import { SubstitutionTags } from "ng2/common/constants/common.constant";

import type { IJsonTextBox } from "ng2/common/model/databearer/components/json-textbox.model";

import { TextboxMetadata } from "ng2/components/textbox/textbox.metadata";

import { BackwardCompatible } from "ng2/common/project/backward-compatible";
import { BaseComponent } from "ng2/components/base/component";

import { DynamicComponentService } from "ng2/components/services/dynamic-component.service";
import { FrameworkService } from "ng2/components/services/framework.service";

@Component({
	selector: "ng2-textbox",
	template: `
		<div class="field">
			<label [for]="id"
				*ngIf="showLabel">
				{{ label }}
			</label>
			<input [type]="editType" [id]="id" [name]="id" [class]="boxWidth"
				soho-input
				soho-busyindicator
				[(ngModel)]="text"
				[readOnly]="isReadonly"
			/>
		</div>
	`
})
export class TextboxComponent extends BaseComponent {

	text: string;
	label: string;
	showLabel: boolean;
	placeholder: string;
	boxWidth: string;
	editType: string;

	@ViewChild(SohoInputComponent) input: SohoInputComponent;

	databearer: IJsonTextBox;

	constructor(
		elementRef: ElementRef,
		renderer: Renderer2,
		changeDetectorRef: ChangeDetectorRef,
		frameworkService: FrameworkService,
		dynamicComponentService: DynamicComponentService,
		sohoMessageService: SohoMessageService,
	) {
		super(
			elementRef,
			renderer,
			changeDetectorRef,
			frameworkService,
			dynamicComponentService,
			sohoMessageService,

		);
	}

	onInitComponent(): void {
		super.onInitComponent();

		this.showLabel = BackwardCompatible.getShowLabel(this.databearer);
	}

	initializeBinding(componentDatabearer: IJsonTextBox): void {
		TextboxMetadata.BINDABLE_FIELDS_2.forEach(field => {
			const self: any = this;
			const bindingValue = componentDatabearer[field.ref].value;
			self[field.ref] = bindingValue === SubstitutionTags.VALUE ?
				"" : bindingValue;
		});
	}

	clear(): void {
		this.text = "";
		this.input.setValue(this.text);
	}

}
