import angular from "angular";
import { AngularConstants } from "../../../common/angular-constants";

import type { IBoundary } from "ng2/common/model/project/boundary.model";

import { DEFAULT_BOUNDARY, InjectNames, Numbers, SaveFlagsEnum } from "ng2/common/constants/common.constant";
import { DesignerMashup } from "ng2/common/project/designer-mashup";
import { IJQueryDragEventObject } from "common/defines";
import { STANDARD_BOUNDARIES, STANDARD_ID_PREFIX } from "ng2/common/constants/standard-boundaries.constant";

import {
	DialogButtonType, DialogService, IDialogOptions, IDialogResult,
	IMessageDialogOptions, StandardDialogButtons
} from "common/services/dialog-service";
import { BoundaryManagementService } from "editor/services/boundary-management-service";
import { EditorService } from "editor/services/editor-service";

export class RulerCtrl {
	currentBoundary: IBoundary;
	projectBoundaries: IBoundary[];
	private unsubscribers: Array<Function> = [];

	static $inject = [
		AngularConstants.SCOPE,
		AngularConstants.ELEMENT,
		AngularConstants.TIMEOUT,
		InjectNames.DIALOG_SERVICE,
		InjectNames.EDITOR_SERVICE,
		InjectNames.BOUNDARY_MANAGEMENT_SERVICE
	];

	constructor(
		private $scope: ng.IScope,
		private $element: ng.IAugmentedJQuery,
		private $timeout: ng.ITimeoutService,
		private dialogService: DialogService,
		private editorService: EditorService,
		private boundaryManagementService: BoundaryManagementService
	) {

		this.unsubscribers.push(editorService.currentBoundaryChangedEvent.on((boundary: IBoundary) => {
			this.currentBoundary = boundary;
		}));
		this.currentBoundary = editorService.getCurrentBoundary();

		this.unsubscribers.push(editorService.projectBoundariesChangedEvent.on((boundaries: IBoundary[]) => {
			this.projectBoundaries = boundaries;
			this.$timeout(() => {
				this.initBoundaryDraggable();
			}, Numbers.TEN);
		}));

		// Current project always exists before controller
		this.onCurrentProjectChanged(editorService.getCurrentProject());

		$timeout(() => {
			this.initBoundaryDraggable();
		}, Numbers.TWO_HUNDRED);

		$scope.$on(AngularConstants.DESTROY, () => {
			angular.forEach(this.unsubscribers, (unsubscribe) => {
				unsubscribe();
			});
		});
		this.convertOldStandardBoundariesToNormalBoundaries();
	}

	/**
	 * Backward compatiblity method. Turn previous standard boundaries to normal boundaries
	 */
	private convertOldStandardBoundariesToNormalBoundaries(): void {
		const projectBoundaries = this.boundaryManagementService.getProjectBoundaries();
		projectBoundaries.forEach((projectBoundary) => {
			if (projectBoundary.id.indexOf(STANDARD_ID_PREFIX) === 0 &&
				!STANDARD_BOUNDARIES.find((standardBoundary) => {
					return projectBoundary.displayName === standardBoundary.displayName;
				})) {
				projectBoundary.id = this.boundaryManagementService.generateBoundaryId();
			}
		});
	}

	private onCurrentProjectChanged(newProject: DesignerMashup): void {
		this.projectBoundaries = newProject.manifest.mashupBoundaries;
	}

	private isSingleClick: boolean;

	boundaryClick(boundary: IBoundary, $event: Event): void {
		this.isSingleClick = true;
		this.$timeout(() => {
			if (this.isSingleClick) {
				this.selectBoundary(boundary, $event);
			}
		}, Numbers.TWO_HUNDRED);
	}

	boundaryDblClick(boundary: IBoundary): void {
		this.isSingleClick = false; // To prevent single click trigger
		this.boundaryMarkerEditor(boundary);
	}

	private selectBoundary(boundary: IBoundary, $event?: Event): void {
		// check if boundary marker is being dragged to prevent click on drop
		if ($event && $event.target && $($event.target).data("dragStop") === true) {
			$($event.target).data("dragStop", false);
			return;
		}

		// if boundary already selected - unmark back to default.
		boundary = boundary === this.editorService.getCurrentBoundary() ? DEFAULT_BOUNDARY : boundary;
		// Select boundary
		this.boundaryManagementService.goToCurrentBoundaryMarker(boundary);
		this.editorService.setCurrentBoundary(boundary);
	}

	toggleDefaultBoundary(): void {
		this.selectBoundary(DEFAULT_BOUNDARY);
	}

	private initBoundaryDraggable(): void {
		this.projectBoundaries.forEach((boundary: IBoundary) => {
			if (!this.boundaryManagementService.isBoundaryReadOnly(boundary)) {
				this.$element.find("#" + boundary.id).draggable({
					axis: "x",
					containment: "parent",

					drag: (event: Event, ui: JQueryUI.DraggableEventUIParams) => {
						event.stopPropagation();
						const boundaryId = ui.helper.attr("id");
						const boundary = this.boundaryManagementService.getBoundary(boundaryId);
						let offsetX = ui.position.left;
						const oldOffsetX = boundary.limitX;

						offsetX = Math.round(offsetX / Numbers.TEN) * Numbers.TEN;
						boundary.limitX = offsetX;

						if (offsetX !== oldOffsetX) {
							this.boundaryManagementService.boundaryMovedEvent.raise(boundary);
						}
					},
					stop: (event: IJQueryDragEventObject, ui: JQueryUI.DraggableEventUIParams) => {
						$(event.originalEvent.target).data("dragStop", true);
						this.$scope.$evalAsync();
						this.editorService.saveCurrent(SaveFlagsEnum.MANIFEST);
					}
				});
			}
		});
	}

	private boundaryMarkerEditor(boundary: IBoundary): void {
		if (!this.boundaryManagementService.isBoundaryReadOnly(boundary)) {
			const options: IDialogOptions<IBoundary> = {
				title: "Edit Boundary Marker",
				template: require("./boundary-editor-dialog/boundary-editor-dialog.html"),
				parameter: angular.copy(boundary) // Copy to avoid setting value before Save is pressed
			};

			this.dialogService.show(options).then((result: IDialogResult<IBoundary>) => {
				if (result && result.button === DialogButtonType.Ok &&
					JSON.stringify(result.value) !== JSON.stringify(angular.copy(boundary))) {
					this.boundaryManagementService.updateBoundaryMarker(result.value);
					this.editorService.saveCurrent(SaveFlagsEnum.MANIFEST);
				}
			});
		}
	}

	setBoundaryMarker($event: JQueryEventObject): void {
		let offsetX = $event.offsetX;
		offsetX = Math.round(offsetX / Numbers.TEN) * Numbers.TEN;

		// Pressed scalenumber
		if ($event.target.classList.contains("scaleNumber")) {
			// Get the offset from the scale number.
			offsetX = Number($event.target.textContent);
		}

		this.boundaryManagementService.addNewBoundaryMarker(offsetX);
	}

	deleteBoundaryMarker(boundary: IBoundary): void {
		const options: IMessageDialogOptions = {
			title: "Are you sure?",
			message: "All component settings for this boundary limit will also be removed from the project. " +
			"Are you sure you want to continue?",
			standardButtons: StandardDialogButtons.YesNo
		};

		this.dialogService.showMessage(options).then((result: IDialogResult<void>) => {
			if (result.button === DialogButtonType.Yes) {
				this.boundaryManagementService.removeProjectBoundary(boundary);
				this.editorService.saveCurrent(SaveFlagsEnum.MANIFEST);
			}
		});
	}
}

export class RulerDirective {
	static add(m: ng.IModule): void {
		m.directive("eirRuler", () => {
			return {
				scope: {},
				template: require("./ruler.html"),
				controller: RulerCtrl,
				controllerAs: "eirRulerCtrl",
				link: ($scope, $element, $attrs) => {
					const step = 100;
					const moveLeft = 2;
					const moveTop = 4;
					const maxLength = Math.max($(document).outerWidth(), $(document).outerHeight());

					for (let i = 0; i < maxLength; i += step) {
						$("<div class='scaleNumber'></div>").appendTo(
							$element.find("#rulerHorizontal")).css("left", i + moveLeft).text(i);
						$("<div class='scaleNumber'></div>").appendTo(
							$element.find("#rulerVertical")).css("top", i + moveTop).text(i);
					}
				}
			};
		});
	}
}
