import { InjectNames, SaveFlagsEnum, ServerPaths } from "ng2/common/constants/common.constant";

import type { IJsonCompositeComponent } from "ng2/common/model/databearer/components/json-composite-component.model";
import type { IJsonDatasource } from "ng2/common/model/databearer/dataservice/json-datasource.model";

import { AngularConstants } from "common/angular-constants";
import type { IExportDialogParams } from "common/dialogs/export/export-dialog";
import { ErrorResponse } from "common/error-response";
import { CollectionData } from "designer/repository/ido/collection-data";
import { DesignerMashup } from "ng2/common/project/designer-mashup";

import { Helper } from "common/helper";

import { CommonService } from "common/services/common-service";
import { DialogButtonType, DialogService, IDialogOptions, IDialogResult } from "common/services/dialog-service";
import { HttpService } from "common/services/http-service";
import { IdoCollection, IdoProperty, LocalIdoService } from "components/services/local-ido-service";

export class RepositoryService {
	cacheData: any[] = [];
	cacheDatasources: IJsonDatasource[] = [];
	cacheComposites: IJsonCompositeComponent[] = [];

	static $inject = [
		AngularConstants.Q,
		AngularConstants.WINDOW,
		InjectNames.HTTP_SERVICE,
		InjectNames.COMMON_SERVICE,
		InjectNames.DIALOG_SERVICE,
		InjectNames.LOCAL_IDO_SERVICE,
	];

	constructor(
		private $q: ng.IQService,
		private $window: ng.IWindowService,
		private httpService: HttpService,
		private commonService: CommonService,
		private dialogService: DialogService,
		private localIdoService: LocalIdoService, // todo move this service out from 'cp' scope
	) {
	}

	//#region Data Service Methods
	getDatasources(): ng.IPromise<IJsonDatasource[]> {
		return this.httpService.executeGet(ServerPaths.REPOSITORY + ServerPaths.DATASOURCES)
			.then((responseData: IJsonDatasource[]) => {
				this.cacheDatasources = [];
				this.cacheDatasources = responseData;
				return this.cacheDatasources;
			});
	}

	addDatasources(datasources: IJsonDatasource[]): ng.IPromise<IJsonDatasource[]> {

		const path = ServerPaths.REPOSITORY + ServerPaths.DATASOURCES;

		if (DEV_SERVER) {
			const devDatasources: any[] = [];
			datasources.forEach(item => {
				item.id = Helper.generateUniqueId();
				devDatasources.push({ path: item.id, data: item });
			});

			return this.httpService.executePost(path, devDatasources);
		}

		return this.httpService.executePost(path, datasources);
	}

	updateDatasource(datasource: IJsonDatasource): ng.IPromise<IJsonDatasource> {
		return this.httpService.executePut(ServerPaths.REPOSITORY + ServerPaths.DATASOURCES, datasource);
	}

	deleteDatasources(ids: string[]): ng.IPromise<void> {
		return this.httpService.executeDelete(
			ServerPaths.REPOSITORY + ServerPaths.DATASOURCES + "?" + Helper.createQueryParameters(ids, "id"));
	}

	exportDatasources(ids: string[]): void {
		const defaultFilename = Helper.getFormattedDate();
		const options: IDialogOptions<IExportDialogParams> = {
			title: "Export Data Services",
			template: require("common/dialogs/export/export-dialog.html"),
			parameter: {
				type: "dataservice",
				filename: defaultFilename,
			}
		};
		this.dialogService.show(options).then((result: IDialogResult<string>) => {
			if (result.button === DialogButtonType.Ok) {
				this.commonService.exportProjectContent(
					ids, result.value, SaveFlagsEnum.DATASOURCES);
			}
		});
	}

	//#endregion

	//#region Composite Components Methods
	loadCompositeComponents(): ng.IPromise<IJsonCompositeComponent[]> {
		return this.httpService.executeGet<IJsonCompositeComponent[]>(
			ServerPaths.REPOSITORY + ServerPaths.COMPOSITE_COMPONENTS);
	}

	addCompositeComponents(
		compositeComponents: IJsonCompositeComponent[]): ng.IPromise<IJsonCompositeComponent[]> {
		return this.httpService.executePost<IJsonCompositeComponent[]>(
			ServerPaths.REPOSITORY + ServerPaths.COMPOSITE_COMPONENTS, compositeComponents);
	}

	deleteCompositeComponents(ids: string[]): ng.IPromise<void> {
		return this.httpService.executeDelete(
			ServerPaths.REPOSITORY + ServerPaths.COMPOSITE_COMPONENTS + "?" + Helper.createQueryParameters(ids, "id"));
	}

	updateCompositeComponents(compositeComponents: IJsonCompositeComponent[]): ng.IPromise<void> {
		return this.httpService.executePut<void>(
			ServerPaths.REPOSITORY + ServerPaths.COMPOSITE_COMPONENTS, compositeComponents);
	}

	exportCompositeComponents(ids: string[]): void {
		const exportDefaultName = (new Date()).toISOString().split("T")[0];
		const options: IDialogOptions<IExportDialogParams> = {
			title: "Export Composite Components",
			template: require("common/dialogs/export/export-dialog.html"),
			parameter: {
				type: "composite component",
				filename: exportDefaultName,
			}
		};
		this.dialogService.show(options).then((result: IDialogResult<string>) => {
			if (result.button === DialogButtonType.Ok) {
				this.commonService.exportProjectContent(
					ids, result.value, SaveFlagsEnum.COMPOSITE_COMPONENTS);
			}
		});
	}

	//#endregion

	//#region IDO Methods
	getIDOs(): ng.IPromise<IdoCollection[]> {
		return this.localIdoService.getIdoCollectionList();
	}

	getIdoProperties(collectionName: string): ng.IPromise<IdoProperty[]> {
		return this.localIdoService.getIdoProperties(collectionName);
	}

	createIDO(newCollection: CollectionData): ng.IPromise<boolean> {
		return this.httpService.executePost(
			`${ServerPaths.REPOSITORY}${ServerPaths.IDO}create`,
			newCollection
		);
	}

	deleteIDOs(idoNames: string[]): ng.IPromise<boolean> {
		return this.httpService.executePost(
			`${ServerPaths.REPOSITORY}${ServerPaths.IDO}delete`,
			idoNames
		);
	}

	//#endregion

	//#region General Methods
	readJsonFile<T>(file: File): ng.IPromise<T> {
		const deferredFile = this.$q.defer<T>();
		const fileReader = new FileReader();
		fileReader.readAsText(file);
		fileReader.onloadend = () => {
			try {
				const parsedObject: T = JSON.parse(fileReader.result as string);
				deferredFile.resolve(parsedObject);
			} catch (e) {
				deferredFile.reject(new ErrorResponse(e.message));
			}
		};

		fileReader.onerror = (ev: Event) => {
			deferredFile.reject(new ErrorResponse("Error reading file" + ev.type));
		};

		return deferredFile.promise;
	}

	onAllPromises(promises: ng.IPromise<any>[]): ng.IPromise<any[]> {
		return this.$q.all(promises);
	}

	newDeferred<T>(): ng.IDeferred<T> {
		return this.$q.defer<T>();
	}

	getDataByName<T>(serverPath: string): ng.IPromise<T[]> {
		return this.httpService.executeGet(ServerPaths.REPOSITORY + serverPath).then((responseData: T[]) => {
			this.cacheData = [];
			this.cacheData = responseData;
			return this.cacheData;
		});
	}

	getCurrentProject(): DesignerMashup {
		return this.commonService.getCurrentProject() as DesignerMashup;
	}

	getLanguage(): string {
		return this.commonService.getLanguage();
	}

	//#endregion

	static add(ngModule: ng.IModule): void {
		ngModule.service(InjectNames.REPOSITORY_SERVICE, RepositoryService);
	}
}