import type { ISourceItem } from "common/model/component/hyperlink.model";
import { IJsonPersistentComponent } from "ng2/common/model/databearer/components/base/json-persistent-components.model";
import type { IJsonComponent, IJsonComponentSettings } from "ng2/common/model/databearer/components/json-component.model";
import type { IJsonValueSource } from "ng2/common/model/databearer/json-binding.model";
import type { IMap } from "ng2/common/model/imap.model";
import { IDataGridColumn, ISortOrderSetting } from "ng2/common/model/sohoxi/sohoxi-datagrid.model";

export interface IPreviousAttributes {
	row: number;
	activePage: number;
	sortOrder: ISortOrderSetting;
}

//#region saved data
export interface IJsonDatagrid extends IJsonComponent, IJsonPersistentComponent {
	isList: boolean;
	usePaging: boolean;
	pageSize?: number;
	columns: IExtendedDataGridColumn[];
	boundarySettings: IMap<IJsonDatagridSettings>;
	showLabel: boolean; // Deprecated
	label: IJsonValueSource;
	rowCount: boolean;
	filterBar: boolean;
	enableExport: boolean;
	multiSelect: boolean;
}

export interface IJsonDatagridSettings extends IJsonComponentSettings {
	rowHeight: string;
	showLabel: boolean;
}

export interface IExtendedDataGridColumn extends IDataGridColumn {
	editable?: boolean;
	formatterName?: string;
	listOptions?: IColumnListOptions;
	hyperlinkOptions?: IColumnHyperlinkOptions;
	summarize?: boolean;
	summaryText?: string;
	summaryTextPlacement?: string;
	substitute?: boolean;
	substituteOptions?: IColumnSubstituteOptions;
	align?: string;
	value?: string; //TODO move value MUPS-3016
}

export interface IColumnListOptions {
	sourceId: string;
	parameterId: string;
}

export interface IColumnSubstituteOptions {
	source: string;
	from: string;
	to: string;
}

export interface IColumnHyperlinkOptions {
	value: string;
	isDrillback: boolean;
	sources?: ISourceItem[];
}

//#endregion
