import {
	forkJoin,
	Observable,
} from "rxjs";

import { OrientationEnum } from "ng2/common/enums/orientation.enum";

import type { IComponent } from "ng2/common/model/components/base/component.model";
import type { ILayoutComponent } from "ng2/common/model/components/base/layout-component.model";
import type { IBoundary } from "ng2/common/model/project/boundary.model";

import { BaseComponent } from "ng2/components/base/component";

export abstract class LayoutComponent
	extends BaseComponent
	implements ILayoutComponent {

	abstract hasIndividualChildSizes: boolean;
	children: ILayoutComponent[] = [];

	onChildrenReady(): void {
		console.info(`ID: ${this.displayName}, TYPEDEF: ${this.typedef}: Children Ready`);
		this.componentReady$.next(this);
	}

	abstract getPxPerPercentComponent(ori: OrientationEnum, percent: number): number;

	forEachChild(fn: (child: IComponent) => void, doGenerated: boolean): void {
		super.forEachChild(fn, doGenerated);

		this.children.forEach(child => {
			child.forEachChild(fn, doGenerated);
		});
	}

	onBoundaryChange(boundary: IBoundary): Observable<any> {
		const observers = [super.onBoundaryChange(boundary)];

		this.children.forEach(child => observers.push(child.onBoundaryChange(boundary)));

		// TODO: fix so it maybe returns a BoundaryResponse?
		return forkJoin(observers);
	}
}
