import type { ILayoutComponent } from "../../common/interface/ilayout-component";

import { Component } from "../component";
import { FrameworkService } from "../services/framework-service";

import { colors } from "ng2/common/constants/colors.constant";
import {
	InteractionPairOperation,
	SettingInputKeys,
	SettingKeys,
	SidebarTabTypes,
} from "ng2/common/constants/common.constant";
import { InteractionDefines } from "ng2/common/constants/interaction-defines.constant";

import { BindingTypeEnum } from "ng2/common/enums/binding-type.enum";
import { SizeTypeEnum } from "ng2/common/enums/size-type.enum";
import { ValueTypeEnum } from "ng2/common/enums/value-type.enum";

import type { IComponentSettingCategory } from "ng2/common/model/components/settings/component-setting-category.model";
import type { IDataPair } from "ng2/common/model/data-pair.model";
import type { IJsonSize } from "ng2/common/model/databearer/components/json-component.model";
import type { IJsonLabel } from "ng2/common/model/databearer/components/json-label.model";
import type { IJsonBindableField } from "ng2/common/model/databearer/json-binding.model";

export class Label extends Component {
	static availableStyles = [
		{ name: "Default", value: "default" },
		{ name: "Paragraph", value: "paragraph" },
		{ name: "Small Heading", value: "small-heading" },
		{ name: "Medium Heading", value: "medium-heading" },
		{ name: "Large Heading", value: "large-heading" },
		{ name: "Size 10 - Uppercase", value: "font-size-px-10" },
		{ name: "Size 12 - Uppercase", value: "font-size-px-12" },
		{ name: "Size 14", value: "font-size-px-14" },
		{ name: "Size 16", value: "font-size-px-16" },
		{ name: "Size 20", value: "font-size-px-20" },
		{ name: "Size 24", value: "font-size-px-24" },
		{ name: "Size 28", value: "font-size-px-28" },
		{ name: "Size 32", value: "font-size-px-32" },
		{ name: "Size 40", value: "font-size-px-40" },
		{ name: "Size 48", value: "font-size-px-48" },
		{ name: "Size 60", value: "font-size-px-60" },
		{ name: "Size 72", value: "font-size-px-72" },
	];

	static availableCharacteristics = [
		{ name: "Default", value: "" },
		{ name: "Descriptive", value: "text-descriptive" },
		{ name: "Link", value: "text-link" },
		{ name: "Muted", value: "text-muted" },
		{ name: "Alert", value: "text-alert"},
		{ name: "Emphasis", value: "text-emphasis" },
		{ name: "Strong", value: "text-strong"}
	];

	static availableColors = [
		{ name: "Default", value: "default" },
		{ name: "Light White", value: "rgba(255,255,255,0.5)" },
		{ name: "Medium White", value: "rgba(255,255,255,0.75)" },
		{ name: "Strong White", value: "#fff" },
		{ name: "Light Gray", value: "rgba(26,26,26,0.5)" },
		{ name: "Medium Gray", value: "rgba(26,26,26,0.75)" },
		{ name: "Strong Gray", value: colors.graphite10 }
	];
	static DEFAULT_WIDTH: IJsonSize = { sizeType: SizeTypeEnum.AUTO };
	static DEFAULT_HEIGHT: IJsonSize = { sizeType: SizeTypeEnum.AUTO };
	static SOURCE_TRIGGERS: IDataPair[] = InteractionDefines.SOURCE_TRIGGERS;
	static TARGET_ACTIONS: IDataPair[] = InteractionDefines.TARGET_ACTIONS;
	static DEFAULT_ACTION: IDataPair = InteractionPairOperation.GET;
	static DEFAULT_TRIGGER: IDataPair = InteractionPairOperation.ON_LOAD;
	static SETTING_FIELDS: IComponentSettingCategory[] =
	([
		{
			title: "Typography",
			tab: SidebarTabTypes.STYLE,
			settings: [{
				ref: SettingKeys.FONT_STYLE,
				title: "Font Style",
				inputType: SettingInputKeys.DROPDOWN,
				dataset: Label.availableStyles,
				defaultValue: Label.availableStyles[0].value,
			}, {
				ref: SettingKeys.FONT_CHAR,
				title: "Font Characteristic",
				inputType: SettingInputKeys.DROPDOWN,
				dataset: Label.availableCharacteristics,
				defaultValue: Label.availableCharacteristics[0].value,
				tooltip: "Note that you can only change the text color if the text has the following characteristics: Default, Strong or Emphasis."
			}]
		}, {
			title: "Color",
			tab: SidebarTabTypes.STYLE,
			settings: [{
				ref: SettingKeys.COLOR,
				inputType: SettingInputKeys.DROPDOWN,
				dataset: Label.availableColors,
				defaultValue: Label.availableColors[0].value,
			}]
		}] as IComponentSettingCategory[]).concat(Component.SETTING_FIELDS);

	static BINDABLE_FIELDS_2: IJsonBindableField[] =
	[{
		name: "Label",
		ref: SettingKeys.CONTENT,
		valueType: ValueTypeEnum.LOCALIZED_STRING,
		write: true,
		read: true,
		defaultValue: { Type: BindingTypeEnum.STATIC_VALUE, value: "" }
	}, {
		name: "Tooltip",
		ref: SettingKeys.TOOLTIP,
		valueType: ValueTypeEnum.LOCALIZED_STRING,
		write: true,
		read: false,
		defaultValue: { Type: BindingTypeEnum.STATIC_VALUE, value: "" }
	}];

	content: string;
	toolTip: string;
	fontStyle: string;
	fontChar: string;
	color: string;

	constructor(
		obj: IJsonLabel,
		$element: ng.IAugmentedJQuery,
		parent: ILayoutComponent,
		frameworkService: FrameworkService
	) {
		super(obj, $element, parent, frameworkService);

		if (obj) {
			this.fontStyle = obj.fontStyle;
			this.fontChar = obj.fontChar ? obj.fontChar : "";
			this.color = obj.color;
		}
	}

	static directive = [() => {
		return {
			template: require("./label.html"),
			replace: true
		};
	}];
}
