import { AngularConstants } from "../../../../../common/angular-constants";

import { InjectNames } from "ng2/common/constants/common.constant";
import { IDataGridColumn, IDataGridOptions } from "ng2/common/model/sohoxi/sohoxi-datagrid.model";
import { IDocument, IParameterObject } from "ng2/common/model/swagger.model";

import { IDialogBaseScope } from "../../../../../common/defines";
import { IOperation } from "../../../../../common/model/ion-metadata.model";
import { CustomFormatters } from "../../../../../common/sohoxi/custom-formatters";
import { RepositoryHelper } from "../../../repository-helper";
import { IResponseDatagridData } from "./ion-api-search-wizard-ctrl";

//tslint:disable-next-line:interface-name
export interface IonApiOperationDetailsParameter {
	operation: IOperation;
	swaggerDocument: IDocument;
}

export class IonApiOperationDetails {
	static $inject = [
		AngularConstants.SCOPE,
		AngularConstants.ELEMENT,
		InjectNames.DIALOG_SERVICE
	];

	public parameterDatagridOption: IDataGridOptions<IParameterObject>;
	public responseDatagridOption: IDataGridOptions<IResponseDatagridData>;

	public get swaggerDocument(): IDocument {
		return this.$scope.eirDialog.parameter.swaggerDocument;
	}

	public get operation(): IOperation {
		return this.$scope.eirDialog.parameter.operation;
	}

	constructor(
		private $scope: IDialogBaseScope<IonApiOperationDetailsParameter>
	) {
		this.initParameterDatagrid();
		this.initResponseDatagrid();
		this.initDialogData();
	}

	private initDialogData(): void {
		const swaggerOp = RepositoryHelper.getSwaggerOperation(
			this.operation,
			this.swaggerDocument
		);
		this.parameterDatagridOption.dataset = swaggerOp.parameters;

		const responseDataset: IResponseDatagridData[] = [];
		for (const responseCode in swaggerOp.responses) {
			const response = swaggerOp.responses[responseCode];
			responseDataset.push(
				RepositoryHelper.mapSchemaToGridData(
					response.schema,
					response.description || responseCode
				)
			);
		}
		this.responseDatagridOption.dataset = responseDataset;
	}

	private initParameterDatagrid(): void {
		const columns: IDataGridColumn[] = [
			{
				id: "name",
				name: "Name",
				field: "name",
				sortable: true,
				width: "20%",
				formatter: Soho.Formatters.Ellipsis
			},
			{
				id: "description",
				name: "Description",
				field: "description",
				width: "20%",
				sortable: false
			},
			{
				id: "type",
				name: "Type",
				field: "type",
				sortable: false,
				width: "20%"
			},
			{ id: "in", name: "In", field: "in", sortable: false, width: "20%" },
			{
				id: "required",
				name: "Required",
				field: "required",
				sortable: true,
				width: "20%",
				formatter: CustomFormatters.RequiredFormatter("required")
			}
		];
		this.parameterDatagridOption = {
			columns: columns,
			dataset: [],
			selectable: false,
			isList: true,
			saveColumns: false
		};
	}

	private initResponseDatagrid(): void {
		this.responseDatagridOption = {
			columns: [
				{
					id: "name",
					name: "Name",
					field: "name",
					formatter: Soho.Formatters.Tree,
					width: "33%"
				},
				{
					id: "description",
					name: "Description",
					field: "description",
					width: "33%"
				},
				{ id: "type", name: "Type", field: "type", width: "33%" }
			],
			dataset: [],
			selectable: false,
			treeGrid: true,
			isList: true,
			saveColumns: false
		};
	}

	static add(module: ng.IModule): void {
		module.controller("eirIonApiOperationDetails", IonApiOperationDetails);
	}
}
