import { AngularConstants } from "../../../../common/angular-constants";

import { InjectNames, } from "ng2/common/constants/common.constant";
import { IJsonChart, IJsonChartDataset } from "ng2/common/model/databearer/components/json-chart.model";

import { IAvailableSourceField } from "../../../../common/defines";
import { DialogButtonType, DialogService, IDialogOptions, IDialogResult } from "../../../../common/services/dialog-service";
import { Chart } from "../../../../components/chart/chart";
import { IEditorChart } from "../../components/editor-chart";
import { EditorService } from "../../services/editor-service";
import { ComponentSettingsCtrl } from "../component-settings";
import { SettingsBase } from "../settings-base";
import { ChartDialogActionEnum } from "./chart-settings";
import { IChartDialogOptions, IChartDialogResult } from "./chart-settings-dialog";

export class ChartContentSettingsCtrl extends SettingsBase {
	protected selectedComponent: IEditorChart;

	static $inject = [
		AngularConstants.SCOPE,
		InjectNames.EDITOR_SERVICE,
		InjectNames.DIALOG_SERVICE
	];

	constructor(
		$scope: ng.IScope,
		editorService: EditorService,
		private dialogService: DialogService
	) {
		super($scope, editorService);
		this.onComponentChanged(editorService.getSelectedComponent());
	}

	/**
	 * Used in view to enable/disable dataset settings
	 */
	hasDatasource(): boolean {
		return this.selectedComponent.datasource !== undefined;
	}

	/**
	 *	Triggered from view when click on add dataset
		*
		* @param {ChartContentTypeEnum} chartContentType
		*
		* @memberOf ChartSettingsCtrl
		*/

	onAddDialog(): void {
		this.openDialog(ChartDialogActionEnum.ADD);
	}

	/**
		*	Triggered from view when edit dataset.
		*
		* @param {IJsonChartDataset} chartContent
		* @param {ChartContentTypeEnum} chartContentType
		*
		* @memberOf ChartSettingsCtrl
	*/
	onEditDialog(chartContent: IJsonChartDataset): void {
		this.openDialog(ChartDialogActionEnum.EDIT, chartContent);
	}

	private openDialog(
		dialogAction: ChartDialogActionEnum,
		chartContent?: IJsonChartDataset // undefined if onAddDialog, reference to databearer content if edit
	): void {
		const emptyContent: IJsonChartDataset = {
			name: "",
			categories: "",
			series: "",
		};

		this.selectedComponent.getLocalContextMetaData()
			.then((sourceFields: IAvailableSourceField[]) => {
				const title = dialogAction.toString();
				const parameter: IChartDialogOptions = {
					content: {
						chartContent: chartContent ? chartContent : emptyContent,
						sourceFields: sourceFields
					},
					type: dialogAction,
				};

				const dialogOptions: IDialogOptions<any> = {
					title: title,
					parameter: parameter,
					template: require("./chart-content-dialog.html"),
				};

				this.dialogService.show(dialogOptions)
					.then((dialogResult: IDialogResult<IChartDialogResult>) => {
						if (dialogResult.button === DialogButtonType.Ok) {
							this.onDialogResult(dialogResult);
							this.selectedComponent.updateChart();
							this.editorService.saveCanvas();
						}
					});
			});
	}

	private onDialogResult(dialogResult: IDialogResult<IChartDialogResult>): void {
		if (dialogResult) {
			switch (dialogResult.value.dialogAction) {
				case ChartDialogActionEnum.ADD:
					this.add(dialogResult.value.content);
					break;
				case ChartDialogActionEnum.EDIT:
					break;
				default:
					console.error("Unknown option");
			}
		}
	}

	private add(content: IJsonChartDataset): void {
		const selectedDatabearer = this.selectedComponent.getDatabearer();
		selectedDatabearer.datasets.push(content);
	}

	delete(content: IJsonChartDataset): void {
		const contents: IJsonChartDataset[] = this.selectedComponent.getDatabearer().datasets;

		if (contents) {
			const index = contents.findIndex(dataset => dataset.name === content.name);

			if (index !== -1) {
				contents.splice(index, 1);
			}
		}
		this.selectedComponent.updateChart();
		this.editorService.saveCanvas();
	}

	getDatasets(): IJsonChartDataset[] {
		return this.selectedComponent.getDatabearer().datasets;
	}

	static create(id: string): IJsonChart {
		const newChart = ComponentSettingsCtrl.create(id, Chart) as IJsonChart;
		return newChart;
	}

}

export class ChartContentSettingsDirective implements ng.IDirective {
	template = require("./chart-content-settings.html");
	scope = true;
	controller = ChartContentSettingsCtrl;
	controllerAs = "eirChartContentSettingsCtrl";
}
