import isNil from "lodash-es/isNil";
import isUndefined from "lodash-es/isUndefined";

import { AppState } from "../../app.state";

import { Config, DeveloperConstants, InjectNames } from "ng2/common/constants/common.constant";

import { AngularConstants } from "common/angular-constants";
import { Debugger } from "common/debugger";
import { ViewEnum } from "common/defines";

import { CommonService } from "common/services/common-service";
import { DialogService } from "common/services/dialog-service";
import { RepositoryService } from "designer/repository/repository-service";
import { DesignerService } from "designer/services/designer-service";
import { ProjectService } from "designer/services/project-service";

export class DeveloperToolCtrl {

	static $inject = [
		AngularConstants.Q,
		AngularConstants.ELEMENT,
		AngularConstants.STATE_SERVICE,
		InjectNames.COMMON_SERVICE,
		InjectNames.DIALOG_SERVICE,
		InjectNames.PROJECT_SERVICE,
		InjectNames.REPOSITORY_SERVICE,
		InjectNames.DESIGNER_SERVICE,
	];

	get isDeveloper(): boolean {
		return Debugger.isEnabled() && Config.IS_DEV;
	}
	get mgConfigName(): string {
		return Config.MG_CONFIG_NAME;
	}
	set mgConfigName(value: string) {
		Config.MG_CONFIG_NAME = value;
	}
	get ionApiToken(): string {
		const token = localStorage.getItem(DeveloperConstants.ION_TOKEN);
		return token;
	}
	set ionApiToken(value: string) {
		this.commonService.httpService
			.executePut(
				"/settings/token",
				{ path: "token", data: value}
			)
			.then(response => localStorage
				.setItem(DeveloperConstants.ION_TOKEN, response as string));
	}

	currentProject: string;

	isIonProxyActive: boolean;
	isLaunchWithAngular: boolean;
	isProcessing: boolean;
	canNavigateBack: boolean;

	private acceptanceTestProjectIdList: string[];
	private acceptanceTestDataserviceIdList: string[];
	private progressApi: any;

	private fromEditor: boolean;

	constructor(
		private $q: ng.IQService,
		private $element: ng.IAugmentedJQuery,
		private $state: ng.ui.IStateService,
		private commonService: CommonService,
		private dialogService: DialogService,
		private projectService: ProjectService,
		private repositoryService: RepositoryService,
		private designerService: DesignerService,
	) { }

	$onInit(): void {
		this.setCanNavigateBack();

		if (this.isDeveloper) {
			let value = localStorage.getItem(DeveloperConstants.ION_PROXY);
			this.isIonProxyActive = !isNil(value) && value === "true";

			value = localStorage.getItem(DeveloperConstants.LAUNCH_WITH_ANGULAR);
			this.isLaunchWithAngular = !isNil(value) && value === "true";

		} else {
			localStorage.removeItem(DeveloperConstants.ION_PROXY);
			localStorage.removeItem(DeveloperConstants.LAUNCH_WITH_ANGULAR);
			this.$state.go(AppState.HOME.name);
		}

		const filterAcceptanceTestItems = (list: { id: string, name: string}[]): string[] => {
			return list
				.filter(item => item.name.indexOf("ACCTEST") === 0)
				.map(item => item.id);
		};

		this.commonService.loadProjects()
			.then(projects => {
				return projects.map(project => {
					return {
						id: project.manifest.id,
						name: project.manifest.displayName,
					};
				});
			})
			.then(filterAcceptanceTestItems)
			.then(result => this.acceptanceTestProjectIdList = result);

		this.repositoryService.getDatasources()
			.then(dataservices => {
				return dataservices.map(dataservice => {
					return {
						id: dataservice.id,
						name: dataservice.displayName,
					};
				});
			})
			.then(filterAcceptanceTestItems)
			.then(result => this.acceptanceTestDataserviceIdList = result);

		this.progressApi = this.$element.find("#progress-bar").progress().data("progress");

		// load the current project textarea field
		this.loadCurrentProjectDatabearer();
	}

	private loadCurrentProjectDatabearer(): void {

		const projectId = this.commonService.getCurrentProject()?.getId();

		if (!isUndefined(projectId)) {
			this.commonService.loadProject(projectId)
				.then(project => this.currentProject = JSON.stringify(project));
		} else {
			this.currentProject = "No project loaded";
		}
	}

	static resolve(): void {
		Debugger.initialize();
	}

	deleteAllAcceptanceTestProjects(): void {
		const amount = this.acceptanceTestProjectIdList.length;
		const promises: ng.IPromise<void>[] = [];
		this.isProcessing = true;

		this.acceptanceTestProjectIdList.forEach((projectId, index) => {
			promises.push(this.projectService.deleteById(projectId)
				.then(() => this.dialogService.showToast({
					title: "Deleted",
					message: `Deleted ${projectId}, still ${amount - index - 1} of ${amount} to process`,

				}))
				.then(() => {
					// tslint:disable-next-line:no-magic-numbers
					const processed =  100 - (100 / (amount / (amount - index - 1.0)));
					this.progressApi.update(processed.toString());
				}));
		});

		this.$q.all(promises)
			.then(() => this.dialogService.showToast({
				title: "Result",
				message: "All acceptance test projects deleted",
			}))
			.finally(() => {
				this.acceptanceTestProjectIdList.length = 0;
				this.isProcessing = false;
			});
	}

	deleteAllAcceptanceTestDataservices(): void {
		this.repositoryService.deleteDatasources(this.acceptanceTestDataserviceIdList)
			.finally(() => this.acceptanceTestDataserviceIdList.length = 0);
	}

	onEnableIonProxy(value: boolean): void {
		this.isIonProxyActive = value;
		if (value) {
			localStorage.setItem(DeveloperConstants.ION_PROXY, "true");
		} else {
			localStorage.removeItem(DeveloperConstants.ION_PROXY);
		}
	}

	onLaunchAppWithAngular(value: boolean): void {
		this.isLaunchWithAngular = value;
		if (value) {
			localStorage.setItem(DeveloperConstants.LAUNCH_WITH_ANGULAR, "true");
		} else {
			localStorage.removeItem(DeveloperConstants.LAUNCH_WITH_ANGULAR);
		}
	}

	navigateBack(): void {
		if (this.canNavigateBack) {
			if (this.fromEditor) {
				this.$state.go(AppState.EDITOR.name, {
					id: this.designerService.getCurrentProject().getId(),
					reloadProject: false,
				});
			} else {
				this.$state.go(AppState.HOME.name);
			}
		}
	}

	private setCanNavigateBack(): void {
		const currentView = this.designerService.getView();

		this.canNavigateBack = currentView === ViewEnum.DASHBOARD || currentView === ViewEnum.EDITOR;
		if (this.canNavigateBack) {
			this.fromEditor = currentView === ViewEnum.EDITOR;
		}
	}
}
