import { SizeTypeEnum } from "ng2/common/enums/size-type.enum";
import { IMap } from "ng2/common/model/imap.model";

export interface IJsonComponent extends IMap<any> {
	typedef: TypeDef;
	id: string;
	generatedId?: string; // ID for component with generated child components e.g. List
	displayName: string;
	isReadonly: boolean;
	datasource?: string;
	boundarySettings: IMap<IJsonComponentSettings>;
}

export interface IJsonComponentSettings {
	width?: IJsonSize;
	height?: IJsonSize;
	visibility?: boolean;
	margin?: IJsonMargin;
	showError?: boolean;
}

export interface IJsonSize {
	sizeType: SizeTypeEnum;
	value?: number;
}

export interface IJsonMargin extends IMap<number> {
	top: number;
	right: number;
	bottom: number;
	left: number;
}

export type TypeDef =
	"ApplicationParent" |
	"Button" |
	"Chart" |
	"Checkbox" |
	"Datagrid" |
	"Datepicker" |
	"Dropdown" |
	"Flexbox" |
	"Hyperlink" |
	"Image" |
	"Label" |
	"List" |
	"Lookup" |
	"RadioButton" |
	"Tabs" |
	"TabPanel" |
	"Textarea" |
	"Textbox" |
	"TextBox";
