import { InjectNames } from "ng2/common/constants/common.constant";
import { IComponentFieldSetting } from "ng2/common/model/components/settings/component-field-setting.model";
import { IComponentSettingCategory } from "ng2/common/model/components/settings/component-setting-category.model";
import { IMap } from "ng2/common/model/imap.model";
import { AngularConstants } from "../../../common/angular-constants";
import type { IEditorComponent } from "common/interface/ieditor-component";
import { EditorService } from "../services/editor-service";
import { SettingsBase } from "./settings-base";

export interface IGenericSettingsScope extends ng.IScope {
	settingsTab: string;
}

export interface ISettingController {
	refresh: () => void;
}

export interface IGenericSettingScope extends ng.IScope {
	settingMeta: IComponentFieldSetting;
	register: (obj: {settingCtrl: ISettingController, settingRef: string}) => void;
}

export class GenericSettingsCtrl extends SettingsBase {
	public relevantSettings: IComponentSettingCategory[];

	private settingInstances: IMap<ISettingController> = {};

	public static $inject = [
		AngularConstants.SCOPE,
		AngularConstants.TIMEOUT,
		InjectNames.EDITOR_SERVICE
	];

	public constructor(
		protected $scope: IGenericSettingsScope,
		private $timeout: ng.ITimeoutService,
		editorService: EditorService
	) {
		super($scope, editorService);
		this.onComponentChanged(editorService.getSelectedComponent());
		this.unsubscribers.push(this.editorService.refreshSettingEvent.on((settingRef) => {
			if (this.settingInstances[settingRef]) {
				this.settingInstances[settingRef].refresh();
			}
		}));
	}

	public registerSetting(settingCtrl: ISettingController, settingRef: string): void {
		this.settingInstances[settingRef] = settingCtrl;
	}

	protected onComponentChanged(newComponent: IEditorComponent): void {
		this.selectedComponent = newComponent;

		if (this.selectedComponent) {
			const settingFields = newComponent.getSettingCategories();
			this.relevantSettings = settingFields.filter(setting => {
				const hasRelevantTab = setting.tab === this.$scope.settingsTab;
				let conditionPassed = true;
				if (hasRelevantTab && setting.condition) {
					conditionPassed = this.$scope.$eval(`eirGenericSettingsCtrl.selectedComponent.${setting.condition}`) === true;
				}
				return hasRelevantTab && conditionPassed;
			});
		}
	}
}

export class GenericSettingsDirective {
	public static add(m: ng.IModule): void {
		m.directive("eirGenericSettings", () => {
			return {
				template: require("./generic-settings.html"),
				scope: {
					settingsTab: "="
				},
				controller: GenericSettingsCtrl,
				controllerAs: "eirGenericSettingsCtrl"
			};
		});
	}
}
