import { IComponentRendered } from "ng2/common/constants/sohoxi.constant";
import { IDatagridApi, IDataGridOptions } from "ng2/common/model/sohoxi/sohoxi-datagrid.model";

import { CacheableCtrl } from "../../common/shared-components/base/cacheable-ctrl";
import { DATAGRID_RENDERED } from "../../common/sohoxi/component/datagrid-directive";
import { IRowItem } from "../../common/sohoxi/datagrid-helper";
import { RepositoryService } from "./repository-service";

export interface IRepositoryTab extends ng.IComponentController {
	create?(optional?: any): void;
	edit?(): void;
	delete(): void;
	import(): void;
	export?(): void;
}

export abstract class RepositoryTab extends CacheableCtrl implements IRepositoryTab {
	public loading: boolean = false;

	protected abstract datagridId: string;
	protected abstract datagridOptions: IDataGridOptions<any>;
	protected datagridApi: IDatagridApi;

	protected abstract anySelectedDataItem: boolean;
	protected abstract selectedDataItems: any[];

	constructor(
		protected $scope: ng.IScope,
		protected $element: ng.IAugmentedJQuery,
		$cacheFactory: ng.ICacheFactoryService,
		protected repositoryService: RepositoryService,
	) {
		super($cacheFactory);
	}

	public $onInit(): void {
		this.initDatagridOptions();
		this.datagridOptions.id = this.datagridId;

		this.$element
			.on(DATAGRID_RENDERED, (event: JQueryEventObject, result: IComponentRendered) => {
				if (result.id === this.datagridId) {
					this.datagridApi = result.api;
					this.initDatagridEvents();
				}
			});
	}

	abstract delete(): void;

	abstract import(): void;

	protected clearSelected(): void {
		this.anySelectedDataItem = false;
		this.selectedDataItems = [];
		this.datagridOptions.updateDatagrid = true;
		if (this.datagridApi) {
			this.datagridApi.updated(this.datagridOptions);
		}
	}

	protected initDatagridEvents(): void {
		this.datagridApi?.element
			.on("selected", () => {
				const selectedRows = this.datagridApi.selectedRows();
				this.$scope.$evalAsync(() => {
					if (selectedRows.length > 0) {
						this.anySelectedDataItem = true;
						this.selectedDataItems = selectedRows
							.map((rowObject: IRowItem<any>) => { return rowObject.data; });
					} else {
						this.selectedDataItems = [];
						this.anySelectedDataItem = false;
					}
				});
			});
	}

	protected abstract initDatagridOptions(): void;
}
