import angular from "angular";

import { KeyCodeEnum } from "../../../common/defines";
import { Flexbox } from "../../../components/flexbox/flexbox";
import type { IEditorFlexbox } from "../components/editor-flexbox";

import { EditorHelper } from "../helper";

import { EditorService } from "./editor-service";
import { InsertComponentService } from "./insert-component-service";
import { UndoService } from "./undo-service";

import {
	CanvasModeEnum, CssConstantKeys, CssConstantValues, EventTypes, InjectNames,
	InsertEventTypes, SaveFlagsEnum
} from "ng2/common/constants/common.constant";

import type { IJsonComponent } from "ng2/common/model/databearer/components/json-component.model";
import type { IMap } from "ng2/common/model/imap.model";
import type { IJsonLanguageComponent } from "ng2/common/model/language.model";

export class HotkeyService {

	static $inject = [
		InjectNames.EDITOR_SERVICE,
		InjectNames.UNDO_SERVICE,
		InjectNames.INSERT_COMPONENT_SERVICE
	];

	private originalTranslations: IMap<IJsonLanguageComponent[]> = {};
	private oldToNewIdMap: IMap<string>;

	constructor(
		private editorService: EditorService,
		private undoService: UndoService,
		private insertComponentService: InsertComponentService,
	) {
		this.bindEvents();
	}

	private bindEvents(): void {
		// Put it on body to avoid sohos deletion of key-events on document
		angular.element("body").on(EventTypes.KEYDOWN, this.onKeyDown.bind(this));
		angular.element("body").on(EventTypes.KEYUP, this.onKeyUp.bind(this));
	}

	removeClipboardComponentByUndoStack(): void {
		const component = this.insertComponentService.getClipboardComponent();
		if (component) {
			const currentUndoComponent = this.undoService.getCurrentUndo().stateRootComponent;
			this.editorService.forEachComponent(currentUndoComponent, (subComponent) => {
				if (subComponent.id === component.id) {
					this.insertComponentService.setClipboardComponent(null);
					return;
				}
			});
		}
	}

	private getSelectedText(): string {
		return window.getSelection().toString();
	}

	// Add Canvaswide keylisteners for special functions
	private onKeyDown($event: JQueryEventObject): void {
		const keyCode = $event.keyCode;
		const altKey = $event.altKey;
		const ctrlKey = $event.ctrlKey && !altKey;
		const cmdKey = $event.metaKey;
		const modifierKey = ctrlKey || cmdKey;

		if (keyCode === KeyCodeEnum.CTRL) {
			return;
		}

		// Take care of ESCAPE only if the editor is in finder mode.
		// Also raise an event for listeners to act upon.
		if (keyCode === KeyCodeEnum.ESCAPE) {
			switch (this.editorService.getCanvasMode()) {
				case CanvasModeEnum.FINDER:
					this.editorService.setCanvasMode(CanvasModeEnum.SELECTION);
					return;
				case CanvasModeEnum.INSERT:
					this.insertComponentService.cancelInserting();
					break;
				default:
				// Other canvasmodes
			}
		}
		const previewMode = keyCode === KeyCodeEnum.LESS_THAN && modifierKey;
		const saveProject = keyCode === KeyCodeEnum.KEY_S && modifierKey;
		if (previewMode) {
			// Toggle preview mode
			if (this.editorService.getCanvasMode() === CanvasModeEnum.RUNTIME) {
				this.editorService.setCanvasMode(CanvasModeEnum.SELECTION);
			} else {
				this.editorService.setCanvasMode(CanvasModeEnum.RUNTIME);
			}
		} else if (saveProject) {
			this.editorService.saveCurrent(SaveFlagsEnum.PROJECT);
			$event.preventDefault();
		} else if (
			this.getSelectedText() === ""
			&& $($event.target).prop("nodeName") !== "INPUT"
			&& !this.isAnyModalOpen()
		) {
			// Clipboard overrides shall only run when no text is selected in the UI and no modal dialog are open
			if (this.editorService.isMovable()) {
				//check if current component is movable
				switch (keyCode) {
					case KeyCodeEnum.KEY_X: // cut
						if (modifierKey && this.editorService.canCut()) {
							this.cutComponent($event);
						}
						break;
					case KeyCodeEnum.KEY_C: // copy
						if (modifierKey && this.editorService.canCopy()) {
							this.copyComponent($event);
						}
						break;
					case KeyCodeEnum.DELETE:
						if (this.editorService.getSelectedComponent().canDelete()) {
							this.deleteComponent();
						}
						break;
					default:
						//
				}
			}
			if (modifierKey) {
				switch (keyCode) {
					case KeyCodeEnum.KEY_Z: // Undo
						this.removeClipboardComponentByUndoStack();
						this.undoService.undoAction();
						break;
					case KeyCodeEnum.KEY_Y: //Redo
						this.undoService.redoAction();
						break;
					case KeyCodeEnum.KEY_V: // Paste
						this.pasteComponent();
						break;
					default:
						//
				}
			}
		}
	}

	private onKeyUp($event: JQueryEventObject): void {
		if ($event.keyCode === KeyCodeEnum.KEY_C) {
			this.editorService.multiInsert = false;
		}
	}

	private isAnyModalOpen(): boolean {
		return $(".modal").length > 0;
	}

	copyComponent($event?: JQueryEventObject): void {
		const currentComponent = this.editorService.getSelectedComponent();
		if (currentComponent) {
			if ($event) {
				// If it was triggered through shortcut
				$event.preventDefault();
			}
			this.oldToNewIdMap = {};
			const component = this.editorService.copy(currentComponent.getDatabearer(), this.oldToNewIdMap);
			this.originalTranslations = this.editorService.getTranslationsFromComponentTree(currentComponent.getDatabearer());
			this.insertComponentService.setClipboardComponent(component);
		}
	}

	cutComponent($event?: JQueryEventObject): void {
		const currentComponent = this.editorService.getSelectedComponent();
		if (currentComponent) {
			if ($event) {
				// If it was triggered through shortcut
				$event.preventDefault();
			}

			this.editorService.deselectComponent();
			const component = currentComponent.detach();
			EditorHelper.updateDOMTreeElement(component.id);

			this.originalTranslations =
				angular.copy(this.editorService.getTranslationsFromComponentTree(currentComponent.getDatabearer()));
			const project = this.editorService.getCurrentProject();
			currentComponent.forEachChild((childComponentInstance) => {
				const childComponentId = childComponentInstance.getId();
				project.removeTranslation(childComponentId);
			}, false);

			this.insertComponentService.setClipboardComponent(component);
			this.editorService.addUndoStack(project);
			this.editorService.saveCurrent(SaveFlagsEnum.PROJECT);
		}
	}

	pasteComponent($event?: JQueryEventObject): void {
		const component = this.insertComponentService.getClipboardComponent();
		if (component) {
			if ($event) {
				// If it was triggered through shortcut
				$event.preventDefault();
			}
			const bodyElement = angular.element("body");
			const designerElement = angular.element("#designer");

			bodyElement.css(CssConstantKeys.CURSOR, CssConstantValues.CURSOR_NODROP);
			const bodyClickListener = () => {
				this.insertComponentService.cancelInserting();
			};
			designerElement.on(EventTypes.CLICK, bodyClickListener);
			this.insertComponentService.startInserting(component, InsertEventTypes.COPY).then((newParent: IEditorFlexbox) => {
				newParent.newBoundaryEntered(this.editorService.getCurrentBoundary().id);
				this.finishInsertComponent(component);
			}).finally(() => {
				designerElement.off(EventTypes.CLICK, bodyClickListener);
				bodyElement.css(CssConstantKeys.CURSOR, "");
				this.editorService.setCanvasMode(CanvasModeEnum.SELECTION);

				this.oldToNewIdMap = {};
				this.originalTranslations = this.editorService.getTranslationsFromComponentTree(component);
				const nextCopy = this.editorService.copy(component, this.oldToNewIdMap);
				this.insertComponentService.setClipboardComponent(nextCopy);
			});
		}
	}

	private finishInsertComponent(component: IJsonComponent): void {
		this.editorService.copyTranslationsToCurrentProject(this.originalTranslations, this.oldToNewIdMap, component);
		this.editorService.saveCanvas();
		this.editorService.saveCurrent(SaveFlagsEnum.TRANSLATIONS);
	}

	deleteComponent(): void {
		const selectedComponent = this.editorService.getSelectedComponent();
		const parent = selectedComponent.parent;
		if (selectedComponent && selectedComponent.canDelete()) {
			// Clean Interactions and translations related to component about to be deleted
			const project = this.editorService.getCurrentProject();
			selectedComponent.forEachChild((childComponentInstance) => {
				const childComponentId = childComponentInstance.getId();
				project.interactions = project.getInteractions().filter((interaction) => {
					return interaction.targetName !== childComponentId && interaction.sourceName !== childComponentId;
				});
				project.removeTranslation(childComponentId);
				project.removeExpression(childComponentId);
			}, false);

			parent.removeChild(selectedComponent.getId());
			this.editorService.deselectComponent();
			EditorHelper.updateParentComponentSizeClass(parent);
			if (parent instanceof Flexbox) {
				parent.updateChildrenClasses();
			}
			this.editorService.addUndoStack(project);
			this.editorService.saveCurrent(SaveFlagsEnum.PROJECT);
		}
	}

	static add(ngModule: ng.IModule): void {
		ngModule.service(InjectNames.HOTKEY_SERVICE, HotkeyService);
	}
}
