import {
	Component,
	Injector,
	HostListener,
	OnDestroy,
	OnInit,
} from "@angular/core";
import {
	from,
	Observable,
	Subject
} from "rxjs";
import { takeUntil } from "rxjs/operators";

import {
	AppTypeEnum,
	InjectNames
} from "ng2/common/constants/common.constant";

import { ErrorResponse } from "common/error-response";
import { AppLauncherBase } from "ng2/app-launcher/app-launcher.base";
import { RuntimeApp } from "ng2/common/project/runtime-app";

import { FrameworkService } from "components/services/framework-service";
import { RuntimeService as ng2RuntimeService } from "ng2/app-launcher/runtime-canvas/runtime.service";
import { FrameworkService as ng2FrameworkService } from "ng2/components/services/framework.service";
import { RuntimeService } from "runtime/runtime-service";

@Component({
	selector: "ng2-app-launcher",
	template: `
		<ng2-runtime-canvas
			*ngIf="showMashup"
			[app]="runtimeApp"
		></ng2-runtime-canvas>
	`,
})
export class AppLauncherHybridComponent
	extends AppLauncherBase
	implements OnInit, OnDestroy {

	private ng1RuntimeService: RuntimeService;
	private ng1FrameworkService: FrameworkService;
	private unsubscribe: Subject<RuntimeApp> = new Subject<RuntimeApp>();

	constructor(
		protected frameworkService: ng2FrameworkService,
		private injector: Injector,
	) {
		super(frameworkService);
	}

	ngOnInit(): void {

		if (HYBRID) {

			this.setNg1Services();
			this.loadNg1Project();
		} else {
			// Not HYBRID
			this.runtimeApp = this.frameworkService.loadProject(null);
		}
	}

	private setNg1Services(): void {
		this.ng1RuntimeService =
			this.injector.get(InjectNames.RUNTIME_SERVICE);

		this.ng1FrameworkService =
			this.injector.get(InjectNames.FRAMEWORK_SERVICE);
	}

	private loadNg1Project(): void {
		const self = this;
		if (this.appType && this.projectId) {
			this.loadProject(self.projectId)
				.pipe(takeUntil(this.unsubscribe))
				.subscribe({
					next(value: RuntimeApp): void {
						self.runtimeApp = value;
						self.setNg2FrameworkServiceProperties();

						self.showMashup = true;
						// this.initProjectUrlVariables(project);
					},

					error(error: ErrorResponse): void {
						console.error(
							`[AppLauncherComponent] Failed to load project with id: ${self.projectId}`);
						// this.dialogService.showError("Load Project Error", error.message);
					}
				});
		}
	}

	/**
	 * @summary Sets Properties from NG1 to NG2 FrameworkService.
	 * @description
	 * Method is used in Hybrid Mode to set properties to the Framework Service.
	 * After App Launcher point only NG2 services are/should be used.
	 *
	 * @private
	 * @memberof AppLauncherComponent
	 */
	private setNg2FrameworkServiceProperties(): void {
		this.frameworkService.currentBoundary =
			this.ng1FrameworkService.getCurrentBoundary();

		this.frameworkService.currentProject = this.runtimeApp;
	}

	ngOnDestroy(): void {
		this.unsubscribe.next();
		this.unsubscribe.complete();
	}

	private loadProject(projectId: string): Observable<RuntimeApp> {

		let promise: Promise<RuntimeApp>;

		const observable = new Observable<RuntimeApp>(observer => {
			switch (this.appType) {
				case AppTypeEnum.ADMIN_PREVIEW:
					promise = this.ng1RuntimeService
						.loadPublishedProjectNoConstraint(projectId, null) as Promise<RuntimeApp>;
					break;
				case AppTypeEnum.RUNTIME:
				case AppTypeEnum.APPHUB:
					observer.next( null );
					promise = this.ng1RuntimeService
						.loadPublishedProject(projectId, null) as Promise<RuntimeApp>;
					break;
				case AppTypeEnum.DESIGNER_PREVIEW:
					promise = this.ng1RuntimeService
						.loadRuntimeProject(projectId, null) as Promise<RuntimeApp>;

					from(promise).subscribe({
						next(value: RuntimeApp): void {
							observer.next(value);
						}
					});
					break;
				default:
					observer.error({
						message: "Error",
						detail: "Invalid appType: " + this.appType
					});
					break;
			}
		});

		return observable;
	}

	@HostListener("window:resize", ["$event"])
	onResize(event: Event): void {

		const width = (event.target as Window).innerWidth;

		this.frameworkService.updateCurrentBoundary(width);
	}
}
