import angular from "angular";

import { Config, InjectNames, ThemeEnum } from "ng2/common/constants/common.constant";

import { HttpVerbEnum } from "ng2/common/enums/http-verb.enum";

import type { IMap } from "ng2/common/model/imap.model";

import { AngularConstants } from "common/angular-constants";

import { HashService, HashSourceTypes } from "common/services/hash-service";
import { HttpService } from "common/services/http-service";

export class ThemeService {
	private mainTheme = ThemeEnum.LIGHT;
	private canvasTheme: ThemeEnum;

	private changeStylesheetPromises: IMap<ng.IPromise<void>> = {};

	static $inject = [
		AngularConstants.Q,
		InjectNames.HTTP_SERVICE,
		InjectNames.HASH_SERVICE
	];

	constructor(
		private $q: ng.IQService,
		private httpService: HttpService,
		private hashService: HashService
	) {

	}

	/**
	 * Change the href value of the <link> element.
	 *
	 * @param newTheme The theme to be set
	 * @param updateCanvasCss Update the css of the canvas or the designer, always true for runtime
	 */
	changeThemeCss(newTheme: ThemeEnum, updateCanvasCss: boolean): ng.IPromise<void> {

		const deferred = this.$q.defer<void>();

		// Make sure that the canvas stylesheet exist, otherwise update main theme
		// #sohoxi-stylesheet-canvas is 0 on runtime
		updateCanvasCss = updateCanvasCss && angular.element("#sohoxi-stylesheet-canvas").length > 0;
		if (updateCanvasCss) {
			if (this.canvasTheme === newTheme) {
				deferred.reject();

				return deferred.promise;
			}
			this.canvasTheme = newTheme;
		} else {
			if (this.mainTheme === newTheme) {
				deferred.reject();

				return deferred.promise;
			}
			this.mainTheme = newTheme;
		}

		const isDesigner = APP_BUILDER;
		const appRoot = isDesigner ? Config.APP_ROOT : Config.APP_RUNTIME_ROOT;
		let cssUrl = appRoot + "assets/xi/";

		let linkId = "sohoxi-stylesheet";
		if (updateCanvasCss) {
			cssUrl += "canvas-";
			linkId += "-canvas";
		}
		cssUrl += this.getCssTheme(newTheme, updateCanvasCss);
		const srcHashData = angular.element("#sohoxi-stylesheet-canvas").length > 0
			? HashSourceTypes.DESIGNER
			: HashSourceTypes.RUNTIME;
		this.hashService.getHashFromMapOfHashes(cssUrl, srcHashData).then((hashedString: string) => {
			// Solution for transparent themes
			const bodyElement = $("body");
			const transparentClass = "transparent";
			if (newTheme === ThemeEnum.LIGHT_TRANSPARENT || newTheme === ThemeEnum.DARK_TRANSPARENT) {
				bodyElement.addClass(transparentClass);
			} else {
				bodyElement.removeClass(transparentClass);
			}

			const linkElement = angular.element("#" + linkId);
			this.changeStylesheet(linkElement, hashedString).then(() => {
				deferred.resolve();
			}).catch(() => {
				deferred.reject();
			});
		});

		return deferred.promise;
	}

	private getCssTheme(newTheme: ThemeEnum, updateCanvasCss: Boolean): String {
		switch (newTheme) {
			case ThemeEnum.DEFAULT:
			case ThemeEnum.LIGHT:
			case ThemeEnum.LIGHT_TRANSPARENT:
				return "light-theme.css";
			case ThemeEnum.DARK:
			case ThemeEnum.DARK_TRANSPARENT:
				return "dark-theme.css";
			case ThemeEnum.HIGH_CONTRAST:
				return "high-contrast-theme.css";
			default:
				console.warn("[MashupService] Invalid theme: " + newTheme);
				return "";
		}
	}

	private changeStylesheet(linkElement: ng.IAugmentedJQuery, url: string): ng.IPromise<void> {
		if (!this.changeStylesheetPromises[url]) {
			const deferred: ng.IDeferred<void> = this.$q.defer<void>();

			// We make a request to avoid FOUC
			this.httpService.executeRequest({ method: HttpVerbEnum.GET, url: url }).then(() => {
				deferred.resolve();
			}).catch(() => {
				deferred.reject();
			});

			// Cache
			this.changeStylesheetPromises[url] = deferred.promise;
		}
		return this.changeStylesheetPromises[url].then(() => {
			linkElement.attr("href", url);
		});
	}

	static add(ngModule: ng.IModule): void {
		ngModule.service(InjectNames.THEME_SERVICE, ThemeService);
	}
}
