import { InjectNames } from "ng2/common/constants/common.constant";

import type { IJsonTag } from "ng2/common/model/databearer/json-tag.model";

import { InstanceEvent } from "common/instance-event";
import { IProjectInfo } from "ng2/common/project/project-info";

import { HttpService } from "common/services/http-service";

export class TagService {

	projectInfoChangedEvent = new InstanceEvent<IProjectInfo[]>();

	static $inject = [
		InjectNames.HTTP_SERVICE
	];

	constructor(
		protected httpService: HttpService,
	) {
	}

	//TODO: extend this class for custom tags as well

	getAvailableTags(): ng.IPromise<IJsonTag[]> {
		return this.httpService.executeGet("tags/");
	}

	static add(ngModule: ng.IModule): void {
		ngModule.service(InjectNames.TAG_SERVICE, TagService);
	}
}
