import angular from "angular";

import { AngularConstants } from "../../../../common/angular-constants";

import { IDialogBaseScope } from "../../../../common/defines";
import { FeatureSwitchService } from "../../../../common/services/feature-switch-service";
import { Defines } from "../../../../components/defines";
import { Flexbox } from "../../../../components/flexbox/flexbox";
import { FrameworkService } from "../../../../components/services/framework-service";
import { EditorService } from "../../services/editor-service";
import { IWizardStep, WizardCtrl } from "../../wizard/wizard-ctrl";

import { DEFAULT_BOUNDARY, InjectNames } from "ng2/common/constants/common.constant";

import { BindingTypeEnum } from "ng2/common/enums/binding-type.enum";

import type { IJsonComponent } from "ng2/common/model/databearer/components/json-component.model";
import type {
	IJsonCompositeComponent,
	IJsonCompositeComponentManifest
} from "ng2/common/model/databearer/components/json-composite-component.model";
import type { IJsonDatasource } from "ng2/common/model/databearer/dataservice/json-datasource.model";
import type { IJsonValueSource } from "ng2/common/model/databearer/json-binding.model";
import type { IJsonExpression } from "ng2/common/model/databearer/json-expression.model";
import type { IJsonInteraction } from "ng2/common/model/databearer/json-interaction.model";
import type { IMap } from "ng2/common/model/imap.model";
import type { IJsonLanguageComponent } from "ng2/common/model/language.model";

export interface ICompositeComponentWizardScope extends IDialogBaseScope<IJsonComponent> {
}

export class CompositeComponentWizardCtrl extends WizardCtrl {

	steps: IWizardStep[] = [
		{
			id: "composite-component-wizard-name.html",
			title: "Name",
			userTip: "",
			template: require("./composite-component-wizard-name.html")
		}, {
			id: "composite-component-wizard-include.html",
			title: "Include",
			userTip: "Which parts do you want included?",
			template: require("./composite-component-wizard-include.html")
		}, {
			id: "composite-component-wizard-summary.html",
			title: "Summary",
			userTip: "Does this seem correct to you?",
			template: require("./composite-component-wizard-summary.html")
		}
	];

	selectedComponent: IJsonComponent;
	settingCompositeName: string;
	settingIncludeDatasources = true;
	settingIncludeInteractions = true;
	settingIncludeTranslations = true;
	settingIncludeExpressions = true;
	settingIncludeBoundaries = true;
	compositeComponent: IJsonCompositeComponent;

	static $inject = [
		AngularConstants.SCOPE,
		AngularConstants.ELEMENT,
		AngularConstants.TEMPLATE_CACHE,
		InjectNames.EDITOR_SERVICE,
		InjectNames.FRAMEWORK_SERVICE,
		InjectNames.FEATURE_SWITCH_SERVICE,
	];

	constructor(
		$scope: ICompositeComponentWizardScope,
		$element: ng.IAugmentedJQuery,
		$templateCache: ng.ITemplateCacheService,
		private editorService: EditorService,
		private frameworkService: FrameworkService,
		featureService: FeatureSwitchService,
	) {
		super(
			$scope,
			$element,
			$templateCache,
			featureService
		);
		this.initWizard();

		this.onComponentSelected(this.dialog.parameter);

		this.settingIncludeTranslations = this.isFeatureTranslationEnabled();
	}

	onChangeCompositeName(): void {
		if (this.settingCompositeName) {
			this.nextDisabled = false;
		} else {
			this.nextDisabled = true;
		}
	}

	private onComponentSelected(component: IJsonComponent): void {
		this.selectedComponent = component;
		this.settingCompositeName = component.displayName;
		this.onChangeCompositeName();
	}

	private getDatasources(): IJsonDatasource[] {
		const datasourceIds: IMap<string> = {};
		this.frameworkService.forEachComponent(this.selectedComponent, (component) => {
			if (component.datasource) {
				datasourceIds[component.datasource] = "";
			}
		});
		return Object.keys(datasourceIds).map((datasourceId) => {
			return this.frameworkService.getDatasource(datasourceId);
		});
	}

	private getInteractions(): IJsonInteraction[] {
		const interactions: IJsonInteraction[] = [];
		const currentProject = this.editorService.getCurrentProject();

		currentProject.getInteractionDatabearers().forEach((interaction: IJsonInteraction) => {
			const newInteraction = angular.copy(interaction);
			const source = this.getComponentIdBySourceId(newInteraction.sourceName);
			const target = this.getComponentIdBySourceId(newInteraction.targetName);

			if ((source || interaction.sourceName === "Application") && (target || interaction.targetName === "Application")) {
				if (interactions.indexOf(newInteraction) === -1) {
					interactions.push(newInteraction);
				}
			}
		});
		return interactions;
	}

	private getTranslations(): IMap<IJsonLanguageComponent[]> {
		const translations: IMap<IJsonLanguageComponent[]> = {};
		const currentProject = this.editorService.getCurrentProject();

		this.frameworkService.forEachComponent(this.selectedComponent, component => {
			// Flexbox should never have translations except AppPanel
			// AppPanel has app header, do not include that.
			if (component.typedef !== Flexbox.name) {
				const translation = currentProject.getTranslation(component.id);
				if (translation) {
					translations[component.id] = translation;
				}
			}
		});

		return angular.copy(translations);
	}

	private getExpressions(): IJsonExpression[] {
		const expressions: IJsonExpression[] = [];
		const currentProject = this.editorService.getCurrentProject();

		this.frameworkService.forEachComponent(this.selectedComponent, component => {
			if (component.typedef !== Flexbox.name) {
				const componentExpressions = currentProject.getExpressionsByComponentId(component.id);
				if (componentExpressions.length > 0) {
					expressions.push(...componentExpressions);
				}
			}
		});

		return angular.copy(expressions);
	}

	private discardOutOfReachBindings(): void {
		let binding: IJsonValueSource;

		this.frameworkService.forEachComponent(this.selectedComponent, (component: any) => {
			Defines.allComponents[component.typedef].class.BINDABLE_FIELDS_2.forEach((field: any) => {
				binding = component[field.ref];
				if (binding && binding.Type === BindingTypeEnum.BINDING) {
					const source = this.getComponentIdBySourceId(binding.sourceName);
					if (!source) {
						binding.sourceName = null;
						binding.sourceKey = null;
					}
				}
			});
		});
	}

	private getComponentIdBySourceId(valueName: string): string {
		let componentId: string;
		this.frameworkService.forEachComponent(this.selectedComponent, (component: IJsonComponent) => {
			if (valueName === component.id) {
				componentId = component.id;
			}
		});
		return componentId;
	}

	protected onChangeCurrentStep(): void {
		if (this.currentStep === this.steps[this.steps.length - 1]) {
			this.compositeComponent = this.getFinishParameter();
		}
	}

	protected getFinishParameter(): IJsonCompositeComponent {

		const manifest: IJsonCompositeComponentManifest = {
			id: null,
			displayName: this.settingCompositeName,
			author: null,
			creationDate: null
		};
		this.discardOutOfReachBindings();
		const newCompositeComponent: IJsonCompositeComponent = {
			manifest: manifest,
			component: this.selectedComponent,
		};
		if (this.settingIncludeDatasources) {
			newCompositeComponent.datasources = this.getDatasources();
		}
		if (this.settingIncludeInteractions) {
			newCompositeComponent.interactions = this.getInteractions();
		}
		if (this.settingIncludeTranslations) {
			newCompositeComponent.translations = this.getTranslations();
		}
		if (this.settingIncludeExpressions) {
			newCompositeComponent.expressions = this.getExpressions();
		}
		if (!this.settingIncludeBoundaries) {
			this.editorService.forEachComponent(newCompositeComponent.component, (component) => {
				const defaultBoundarySettings = component.boundarySettings[DEFAULT_BOUNDARY.id];
				component.boundarySettings = {};
				component.boundarySettings[DEFAULT_BOUNDARY.id] = defaultBoundarySettings;
			});
		}

		return newCompositeComponent;
	}

	static add(m: ng.IModule): void {
		m.controller("eirCompositeComponentWizardCtrl", CompositeComponentWizardCtrl);
	}
}
