import {
	ComponentFactoryResolver,
	ComponentRef,
	Inject,
	Injectable,
	ViewContainerRef
} from "@angular/core";

@Injectable()
export class DynamicComponentService {

	readonly componentRefs: ComponentRef<any>[] = [];

	private viewContainerRef: ViewContainerRef;

	constructor(
		@Inject(ComponentFactoryResolver)
		private componentFactoryResolver: ComponentFactoryResolver
	) { }

	setRootViewContainerRef(viewContainerRef: ViewContainerRef): void {

		this.viewContainerRef = viewContainerRef;
	}

	addComponent(component: any): ComponentRef<any> {

		const componentFactory = this.componentFactoryResolver
			.resolveComponentFactory(component);

		const newComponentRef = this.viewContainerRef.createComponent(componentFactory);

		this.componentRefs.push(newComponentRef);

		return newComponentRef;
	}

	destroyComponent(): void {

		this.componentRefs
			.forEach(componentRef => {
				componentRef.instance.ngOnDestroy();
				componentRef.destroy();
			});
		this.componentRefs.length = 0;
	}
}
