import {
	AfterViewChecked,
	ChangeDetectorRef,
	Component,
	Input,
	OnInit,
} from "@angular/core";
import type { IWidgetSize } from "lime"; // eslint-disable-line import/no-unresolved
import { take } from "rxjs/operators";

import type { IContext } from "ng2/common/model/components/base/context.model";
import type { ILayoutComponent } from "ng2/common/model/components/base/layout-component.model";
import type { IJsonComponent } from "ng2/common/model/databearer/components/json-component.model";

import { RuntimeApp } from "ng2/common/project/runtime-app";
import { ApplicationParent } from "ng2/components/base/application-parent";

import { FrameworkService } from "ng2/components/services/framework.service";

@Component({
	selector: "ng2-runtime-canvas",
	template: `
		<ng2-titlebar remove-host
			[manifest]="app.getManifest()"
		></ng2-titlebar>
		<section class="page-container mashupRootContainer runtimeMode">
			<component-container
				*ngIf="rootComponent"
				[component]="rootComponent"
				[context]="context"
				[parent]="parent"
				(componentReady)="onComponentReady($event)"
			></component-container>
		</section>
	`
})
export class RuntimeCanvasComponent
implements OnInit, AfterViewChecked {

	private _app: RuntimeApp;
	@Input() set app(value: RuntimeApp) {
		this._app = value;
		this.loadApplication();
	}

	get app(): RuntimeApp {
		return this._app;
	}

	@Input() widgetSize?: IWidgetSize;

	constructor(
		private frameworkService: FrameworkService,
		private changeDetectorRef: ChangeDetectorRef,
	) { }
	context: IContext;
	parent: ApplicationParent;
	rootComponent: IJsonComponent;

	ngOnInit(): void {

		this.loadApplication();
	}

	ngAfterViewChecked(): void {
		// Needed otherwise Homepages SDK throws exception
		this.changeDetectorRef.detectChanges();
	}

	onComponentReady(componentInstance: ILayoutComponent): void {
		console.info("[RuntimeDirective] Root component ready");

		this.parent.addChildInstance(componentInstance);

		this.frameworkService.rootComponentInstance = componentInstance;

		this.frameworkService
			.runStartupInteractions()
			.pipe(take(1))
			.subscribe({
				complete: () => {

					if (this.widgetSize) {
						const cols = this.widgetSize.cols;
						this.frameworkService.currentBoundary =
							this.frameworkService.getBoundaryByWidgetSize(cols);
					} else {
						const currentBrowserWidth = window.innerWidth;
						this.frameworkService.currentBoundary =
							this.frameworkService.getBoundaryByWidth(currentBrowserWidth);
					}

					componentInstance
						.onBoundaryChange(this.frameworkService.currentBoundary)
						.pipe(take(1))
						.subscribe({
							next: (value) => console.info(value),
							complete: () => this.changeDetectorRef.detectChanges()
						});
				}
			});

		// this.frameworkService.triggerExpressions();

		// Helper.appIsReadyForSelenium();
	}

	private loadApplication(): void {

		const manifest = this.app.manifest;

		this.parent = new ApplicationParent(
			ApplicationParent.APPLICATION_PARENT_DATABEARER,
			ApplicationParent.createSpoofElement(),
			null,
			manifest
		);

		this.context = {};

		this.rootComponent = this.app.rootComponent;

	}
}
