import angular from "angular";

import { ComponentClassNames, SettingKeys } from "ng2/common/constants/common.constant";

import type { IJsonTabComponent, IJsonTabPanel, IJsonTabs } from "common/model/component/tabs.model";
import type { ITab, ITabContent } from "common/model/sohoxi/sohoxi-tabs.model";
import type { IArrangeEvent } from "common/model/sohoxi/sohoxi.model";
import type { IComponentFieldSetting } from "ng2/common/model/components/settings/component-field-setting.model";

import type { IChildScope } from "common/interface/ichild-scope";
import type { IEditorComponent } from "common/interface/ieditor-component";
import { TabPanel } from "components/tabs/tab-panel";
import { Tabs } from "components/tabs/tabs";
import { IEditorTabPanel, TabPanelSettings } from "editor/component-settings/tabs/tab-panel-settings";
import type { IEditorComponentScope } from "editor/components/editor-component";
import type { IEditorFlexbox } from "editor/components/editor-flexbox";
import { EditorLayoutComponentCtrl, IEditorLayoutComponent } from "editor/components/editor-layout-component";

import { Helper } from "common/helper";
import { ComponentsHelper } from "components/helper";
import { EditorHelper } from "editor/helper";

export interface IEditorTabs extends IEditorLayoutComponent, Tabs {
	children: IEditorTabPanel[];
	parent: IEditorFlexbox;
	addTab(): IJsonTabComponent;
	deleteTab(tab: IJsonTabComponent): void;
	updateTab(tab: IJsonTabComponent, updatedTab: IJsonTabComponent): void;
	switchFocusTab(tab: IJsonTabComponent): void;
	updateTabPosition(status: any): void;

	// Overrides to make dual extend work
	getScope(): IEditorComponentScope;
	getDatabearer(): IJsonTabs;
}

export class EditorTabsCtrl extends EditorLayoutComponentCtrl {
	static add(m: ng.IModule): void {
		m.controller(
			ComponentClassNames.EDITOR_COMPONENT_PREFIX + Tabs.name,
			EditorTabsCtrl
		);
	}

	protected component: IEditorTabs;

	exposesLocalizedBindables(): boolean {
		return true; //tabs
	}

	init(component: IEditorTabs): void {
		super.init(component);

		component.addTab = this.addTab.bind(this);
		component.deleteTab = this.deleteTab.bind(this);
		component.updateTab = this.updateTab.bind(this);
		component.switchFocusTab = this.switchFocusTab.bind(this);
		component.updateTabPosition = this.updateTabPosition.bind(this);
	}

	onComponentInserted(childInstance: IEditorComponent, index: number): void {
		super.onComponentInserted(childInstance, index);
	}

	getTabApi(): ITab {
		return this.component.element
			.find(".tab-container")
			.data("tabs");
	}

	addTab(): IJsonTabComponent {
		const databearer = this.component.getDatabearer();

		const newTabComponent = this.createTabComponent();
		databearer.componentTabs.push(newTabComponent);

		const newTabPanel = TabPanelSettings.getInstance().create(newTabComponent.id);
		databearer.children.push(newTabPanel);

		this.appendTabComponentToTemplate(newTabComponent, newTabPanel);

		return newTabComponent;
	}

	deleteTab(tab: IJsonTabComponent): void {
		const databearer = this.component.getDatabearer();

		const listOfTabs = databearer.componentTabs;
		const index = listOfTabs.indexOf(tab);
		Helper.removeItemFromListByIndex(listOfTabs, index);
		Helper.removeItemFromListByIndex(databearer.children, index);

		this.translationManagementService.removeTranslation(databearer.id, tab.id);
		this.getTabApi().remove(tab.id);

		// switch to adjencent tab
		const adjencentTabIndex = index - 1 > 0 ? index - 1 : 0;
		this.switchFocusTab(listOfTabs[adjencentTabIndex]);
	}

	updateTab(tab: IJsonTabComponent, updatedTab: IJsonTabComponent): void {
		const componentTabs = this.component.componentTabs;
		const index = componentTabs.indexOf(tab);

		if (index !== -1) {
			// Temporary until there's a proper method in SohoXI Tab API
			const tabElement = angular.element("a[href$='" + tab.id + "']");

			// Use global value if availale
			const value = updatedTab.value ? updatedTab.value : updatedTab.name;

			tabElement.text(value);
			this.switchFocusTab(this.component.currentTab);
		}
	}

	switchFocusTab(tab: IJsonTabComponent): void {
		this.getTabApi().select("#" + tab.id, false);
		this.component.currentTab = tab;
		this.component.updatePanels();
	}

	updateTabPosition(event: IArrangeEvent): void {
		const databearer = this.component.getDatabearer();
		const componentTabs = databearer.componentTabs;
		const children = databearer.children;

		if (event.startIndex > event.endIndex) {
			//moving up
			this.getTabElement(event.endIndex).before(this.getTabElement(event.startIndex));
		} else if (event.endIndex > event.startIndex) {
			//moving down
			this.getTabElement(event.endIndex).after(this.getTabElement(event.startIndex));
		}

		EditorHelper.moveByIndex(componentTabs, event.startIndex, event.endIndex);
		EditorHelper.moveByIndex(children, event.startIndex, event.endIndex);

		this.switchFocusTab(componentTabs[event.endIndex]);
	}

	setSetting(settingMeta: IComponentFieldSetting, newSettingValue: number): void {
		super.setSetting(settingMeta, newSettingValue);
		if (settingMeta.ref === SettingKeys.ORIENTATION) {
			this.component.onOrientationChanged();
		}
	}

	private getTabElement(index: number): JQuery {
		const componentDatabearer = this.component.getDatabearer();
		const elementId = componentDatabearer.componentTabs[index].id;
		const elementSelector = "a[href$='" + elementId + "']";

		return angular.element(elementSelector).parent();
	}

	private createTabComponent(): IJsonTabComponent {
		const databearer = this.component.getDatabearer();

		const newTabId = ComponentsHelper.nextNumber(
			databearer.componentTabs.map((tab) => {
				return tab.id;
			}));
		const newId: string = this.component.getId() + "Panel" + newTabId;
		const newName: string = "Tab " + newTabId;

		return {
			id: newId,
			name: newName
		};
	}

	private appendTabComponentToTemplate(
		tabComponent: IJsonTabComponent,
		tabPanel: IJsonTabPanel
	): void {
		const elementName = TabPanel.name;
		const tabPanelTemplate =
			EditorLayoutComponentCtrl.createTemplate(elementName);

		const templateLinkingFunction = this.$compile(tabPanelTemplate);

		const childScope = this.$scope.$new(true) as IChildScope;
		childScope.component = tabPanel;
		childScope.parent = this.component;
		childScope.context = this.$scope.context;

		templateLinkingFunction(childScope, (clone) => {
			const tabElement = this.component.element;
			const tabOption: ITabContent = { name: tabComponent.name };

			this.getTabApi().add(tabComponent.id, tabOption);
			const newTabPanel = tabElement.find(`#${tabComponent.id}`);
			if (newTabPanel.length) {
				newTabPanel.append(clone);
				this.switchFocusTab(tabComponent);
			} else {
				console.error("Invalid tab component id");
			}
		});
	}

}
