import type { ITimePickerOptions } from "../../model/sohoxi/sohoxi-timepicker.model";
import { Utils } from "../utils";

/**
 * Timepicker Directive.
 *
 * Example:
 * <input xi-timepicker="options" class="timepicker">
*/
export class TimepickerDirective implements ng.IDirective {
	private static readonly DIRECTIVE_NAME = "xiTimepicker";
	restrict = "A";
	scope = false;

	link(
		scope: ng.IScope,
		element: ng.IAugmentedJQuery,
		attributes: ng.IAttributes
	): void {
		const options = attributes[TimepickerDirective.DIRECTIVE_NAME];
		const unbindWatcher = scope.$watch(
			options,
			(newOptions: ITimePickerOptions) => {
				if (newOptions) {
					// Destory old settings
					element.data("timepicker").destroy();
					element.timepicker(newOptions);
				} else {
					element.timepicker();
				}
			}
		);
		// Tear down
		element.on("$destroy", () => {
			const timePickerData = Utils.getData(element, "timepicker");
			if (timePickerData) {
				timePickerData.destroy();
			}
			unbindWatcher();
		});
	}

	static add(m: ng.IModule): void {
		m.directive(TimepickerDirective.DIRECTIVE_NAME, () => {
			return new TimepickerDirective();
		});
	}
}
