import { IValidationResult } from "../../../../../common/shared-components/base/field-validation";
import { CollectionProperty } from "../../collection-data";
import { IdoDataType } from "ng2/common/constants/common.constant";

import { IdoValidator } from "../ido-wizard-ctrl";
import { Helper } from "../../../../../common/helper";

export class IdoPropertyCtrl implements ng.IController {
	public dataTypes = [
		IdoDataType.boolean,
		IdoDataType.date,
		IdoDataType.decimal,
		IdoDataType.file,
		IdoDataType.integer,
		IdoDataType.string
	];

	// Component Bindings
	public value: CollectionProperty;
	public errorList: any[] = [];
	public showHelp: () => void;

	public isValidPropertyName(
		value: string
	): IValidationResult {
		return IdoValidator.isValidName(value);
	}

	public isValidNumber(
		value: string
	): IValidationResult {
		return value.indexOf("0") !== 0 ?
			{ status: true } :
			{ status: false, errorMessage: "Cannot start with 0" };
	}

	public isValidPropertyLength(
		value: string
	): IValidationResult {
		const parsedValue = parseInt(value);
		const validMaxLengthForPropertyLength = 4000;

		return parsedValue <= validMaxLengthForPropertyLength ||
			Helper.isUndefinedEmpty(value) ||
			isNaN(parsedValue) ?
			{ status: true } :
			{ status: false, errorMessage: "Cannot be larger than 4000" };
	}

	private tempDecimalLength: string;

	public onChangeDataType(): void {
		this.clearOptions();

		if (this.value.decimalLength) {
			this.tempDecimalLength = this.value.decimalLength;
		}

		if (this.value.dataType === IdoDataType.decimal &&
			this.tempDecimalLength
		) {
			this.value.decimalLength = this.tempDecimalLength;
		}

		if (this.value.dataType !== IdoDataType.decimal) {
			delete this.value.decimalLength;
		}

		if (this.value.dataType === IdoDataType.file) {
			this.value.propertyLength = "-1";
			this.clearPropertyError("Property Length");
		}

		if (this.value.dataType === IdoDataType.boolean) {
			this.value.propertyLength = "1";
		}

		this.setSqlDataType(this.value);
	}

	public onPrimaryKeyChange(): void {
		if (this.value.primaryKey) {
			this.value.required = true;
		}
	}

	private clearOptions(): void {
		this.value.propertyLength = "";
		this.value.primaryKey = false;
		this.value.required = false;
		this.value.readonly = false;
		this.value.decimalLength = "";
	}

	private clearPropertyError(propertyName: string): void {
		for (let i = 0; i < this.errorList.length; i++) {
			if (this.errorList[i].field === propertyName) {
				this.errorList.splice(i, 1);
			}
		}
	}

	private setSqlDataType(value: CollectionProperty): void {
		switch (value.dataType) {
			case IdoDataType.file:
				value.sqlDataType = "varbinary";
				break;
			case IdoDataType.boolean:
				value.sqlDataType = "tinyint";
				break;
			case IdoDataType.date:
				value.sqlDataType = "datetime";
				break;
			case IdoDataType.decimal:
				value.sqlDataType = "decimal";
				break;
			case IdoDataType.integer:
				value.sqlDataType = "int";
				break;
			case IdoDataType.string:
			default:
				value.sqlDataType = "nvarchar";
				break;
		}
	}
}

export class IdoPropertyComponent implements ng.IComponentOptions {
	public controller = IdoPropertyCtrl;
	public controllerAs = "$ctrl";
	public template = require("./ido-property-template.html");

	public bindings = {
		value: "<",
		errorList: "=",
		showHelp: "<"
	};

	public static add(m: ng.IModule): void {
		m.component("eirIdoProperty", new IdoPropertyComponent());
	}
}
