import { IMap } from "ng2/common/model/imap.model";
import { FieldValidation } from "../base/field-validation";
import { ISharedStyling } from "../base/shared-styling";

export class InputFieldCtrl
	extends FieldValidation
	implements ISharedStyling {
	public readonly: boolean = false;
}

export class InputFieldComponent implements ng.IComponentOptions {
	public controller: any;
	public controllerAs: string;
	public template: string;
	public bindings: IMap<string>;

	constructor() {
		this.bindings = {
			id: "@?",
			labelName: "@?",
			fieldValue: "=",
			fieldName: "@?",
			validation: "<?",
			required: "@?",	// mark field as required
			readonly: "<?",	// readonly field

			onComponentChange: "&?",
			parentAnnounce: "<?"
		};

		this.controller = InputFieldCtrl;
		this.controllerAs = "ctrl";
		this.template = require("./input-field-template.html");
	}

	static add(m: ng.IModule): void {
		m.component("eirInputFieldComponent", new InputFieldComponent());
	}
}
