import angular from "angular";

import { InjectNames, ThemeEnum } from "ng2/common/constants/common.constant";
import { DesignerManifest } from "ng2/common/project/manifest/designer-manifest";
import { AngularConstants } from "../../common/angular-constants";
import { FeatureSwitchService } from "../../common/services/feature-switch-service";
import { FeatureSwitchCtrl } from "../../common/shared-components/base/feature-switch-ctrl";
import { FrameworkService } from "../services/framework-service";

export interface IThemes {
	data: string;
	displayName: string;
}

export class TitlebarCtrl extends FeatureSwitchCtrl {

	public showTitlebar: boolean;
	public showThemeSwitch: boolean;
	public title: string;
	public currentTheme: ThemeEnum;

	public themes: IThemes[] =
		[
			{
				data: "light-theme",
				displayName: "Light"
			},
			{
				data: "dark-theme",
				displayName: "Dark"
			},
			{
				data: "high-contrast-theme",
				displayName: "High Contrast"
			},
			{
				data: "light-transparent-theme",
				displayName: "Light Transparent"
			},
			{
				data: "dark-transparent-theme",
				displayName: "Dark Transparent"
			}
		];

	private unsubscribers: Array<Function> = [];

	static $inject = [
		AngularConstants.SCOPE,
		InjectNames.FRAMEWORK_SERVICE,
		InjectNames.FEATURE_SWITCH_SERVICE,
	];

	constructor(
		$scope: ng.IScope,
		private frameworkService: FrameworkService,
		featureSwitchService: FeatureSwitchService,
	) {
		super(featureSwitchService);

		this.updateSettings(frameworkService.getCurrentProject().manifest);

		// Event used in editor when editing project settings
		this.unsubscribers.push(frameworkService.projectSettingsChangedEvent.on((manifest) => {
			this.updateSettings(manifest);
		}));

		this.unsubscribers.push(frameworkService.currentCanvasThemeChangedEvent.on((theme: ThemeEnum) => {
			this.currentTheme = theme;
		}));

		$scope.$on(AngularConstants.DESTROY, () => {
			angular.forEach(this.unsubscribers, (unsubscribe) => {
				unsubscribe();
			});
		});
	}

	public updateSettings(manifest: DesignerManifest): void {
		this.currentTheme = manifest.defaultTheme;
		this.showTitlebar = manifest.showTitleBar;
		this.showThemeSwitch = manifest.themeSwitchEnabled;
		this.title = this.isFeatureTranslationEnabled() ?
			this.frameworkService.getTranslatableHeaderString() :
			manifest.title;
	}

	public changeCanvasTheme(theme: ThemeEnum): void {
		this.frameworkService.setCanvasTheme(theme);
	}
}

export class TitlebarDirective implements ng.IDirective {
	controller = TitlebarCtrl;
	controllerAs = "eirTitlebarCtrl";
	template = require("./titlebar.html");
	replace = true; // Important for css-rule

	static add(m: ng.IModule): void {
		const directive = () => new TitlebarDirective();
		m.directive("eirTitlebar", directive);
	}
}
