if (JENKINS_JOB) {
	console.debug(process.env.NODE_ENV);

	// Specifically so Jenkins' acc-test job can find jQuery
	window.$ = jQuery;
}

export const appBuilder = angular.module("app-builder",
	[
		uiRouter,
		upgradeModule.name,
		idsAssetsModule.name,
		designerModule.name,
		repositoryModule.name,
		runtimeModule.name,
	]);

appBuilder.run([
	AngularConstants.STATE_SERVICE,
	AngularConstants.INTERVAL, (
		$state: StateService,
		$interval: ng.IIntervalService,
	) => {
		WSUserControl.onReady(() => {
			const stop = $interval(() => {
				parent.MG.App.doUnloadPrompt = false;
				WSForm.getVarValue("InitURL", (initUrl: string) => {

					const prefix = "run/";
					const isLaunchApp = initUrl !== "" && initUrl.indexOf(prefix) === 0;
					Config.IS_FIRST_LOAD = false;

					if (Config.IS_CONFIG_LOADED) {

						if (isLaunchApp) {
							const isLaunchWithAngular = Config.IS_DEV &&
								localStorage.getItem(DeveloperConstants.LAUNCH_WITH_ANGULAR) === "true";
							const id = initUrl.slice(prefix.length);

							const stateName = isLaunchWithAngular ?
								appLauncherState.name : AppState.APP_LAUNCHER.name;
							const params = isLaunchWithAngular ?
								{ projectId: id } : { id };

							$state.go(stateName, params);
						}
					}
				});
				$interval.cancel(stop);
			}, Numbers.HUNDRED);
		});
	}
]);

appBuilder.config([
	"$qProvider", "$compileProvider",
	(
		$qProvider: ng.IQProvider,
		$compileProvider: ng.ICompileProvider
	) => {
		$qProvider.errorOnUnhandledRejections(false);
		$compileProvider.debugInfoEnabled(Debugger.isEnabled());
		$compileProvider.commentDirectivesEnabled(false);
		$compileProvider.cssClassDirectivesEnabled(false);
	}
]);

