import { BindingTypeEnum } from "ng2/common/enums/binding-type.enum";
import { ValueTypeEnum } from "ng2/common/enums/value-type.enum";
import { IMap } from "ng2/common/model/imap.model";

export interface IJsonValueSource extends IMap<any>  {
	//Should be lower case
	Type: BindingTypeEnum;
	value?: any;
	sourceName?: string;
	sourceKey?: string;
	visibleName?: string;
}

export interface IJsonBindableField {
	name: string;
	ref: string;
	valueType: ValueTypeEnum;
	write: boolean;
	read: boolean;
	defaultValue?: IJsonValueSource;
	condition?: string;
}
