import { IColorPickerApi, IColorPickerOptions } from "../../model/sohoxi/sohoxi-colorpicker.model";
import { Utils } from "../utils";

/**
 * Colorpicker Directive.
 *
 * Provides a color picker consisting of colors specified by the IColorpickerOptions option,
 * or default colors if not specified. Use the value attribute to set a default color.
 *
 * Example:
 * scope["options"] = {
 * 	colors: [
 * 		{label: "white", value: "ffffff" },
 * 		{label: "black", value: "000000"}
 * 	]
 * }
 * <input value="#ffffff" xi-colorpicker="colors" />
 */
export class ColorPickerDirective implements ng.IDirective {
	public restrict = "A";
	public scope = false;

	public require = "?ngModel";

	private static readonly DIRECTIVE_NAME = "xiColorPicker";

	constructor(private $parse: ng.IParseService) {}

	public link(
		scope: ng.IScope,
		element: ng.IAugmentedJQuery,
		attributes: ng.IAttributes,
		ngModel: ng.INgModelController
	): void {
		// Save selected/default color then trigger change so control updates
		let selectedColor = element.val() as string;
		const options = attributes[ColorPickerDirective.DIRECTIVE_NAME];
		let api: IColorPickerApi;

		const unbindWatcher = scope.$watch(
			options,
			(newOptions: IColorPickerOptions) => {
				if (newOptions) {
					element.colorpicker(newOptions);
				} else {
					element.colorpicker();
				}
				api = Utils.getData(element, "colorpicker");
				if (ngModel) {
					// Watch ng-model and update element value on change
					scope.$watch(attributes.ngModel, (color: string) => {
						if (color && color.length > 0 && color !== selectedColor) {
							element.val(color);
							if (api) {
								api.setColor(color);
							}
							selectedColor = color;
						}
					});
					element.on("selected.colorpicker", (e, item) => {
						scope.$apply(() => {
							this.$parse(attributes.ngModel).assign(
								scope,
								"#" + item.data("value")
							);
						});
					});
				}
			}
		);
		// Tear down
		element.on("$destroy", () => {
			if (api) {
				element.off("change");
				element.off("selected.colorpicker");
				api.destroy();
			}
			unbindWatcher();
		});
	}

	public static add(m: ng.IModule): void {
		m.directive(ColorPickerDirective.DIRECTIVE_NAME, [
			"$parse",
			parse => {
				return new ColorPickerDirective(parse);
			}
		]);
	}
}
