import { StateParams } from "@uirouter/angular";
import { NgHybridStateDeclaration } from "@uirouter/angular-hybrid";

import { AppTypeEnum } from "ng2/common/constants/common.constant";
import { StateNames } from "ng2/common/constants/state-names.constant";

import { AppLauncherHybridComponent } from "ng2/app-launcher/app-launcher-hybrid.component";

export const appLauncherState: NgHybridStateDeclaration = {
	url: "/launch/:projectId",
	name: StateNames.APP_LAUNCHER,
	component: AppLauncherHybridComponent,
	resolve: {
		appType: (): AppTypeEnum => AppTypeEnum.DESIGNER_PREVIEW,
		projectId: ["$stateParams", (
			$stateParams: StateParams
		): any => {
			return $stateParams.projectId;
		}]
	}
};

export const appStates = [appLauncherState];
