import { IAutoCompleteOptions } from "../../model/sohoxi/sohoxi-autocomplete.model";
import { Utils } from "../../sohoxi/utils";

/**
 * Autocomplete Directive.
 *
 * Suggests values in given data (source) that matches the input.
 * Instead of providing av options object, the following attributes can be used as an alternative:
 * data-autocomplete (string url value only, instead of options.source)
 * data-tmpl (instead of options.template)
 *
 * Example:
 * scope["options"]: IXiAutoCompleteOptions = {
 * 	source: ["Alpha", "Bravo"],
 * 	template: "#template", filterMode: "contains"
 * }
 * <input xi-autocomplete="options" />
 */
export class AutocompleteDirective implements ng.IDirective {
	private static readonly DIRECTIVE_NAME = "xiAutocomplete";
	public restrict = "A";
	public scope = false;

	public link(
		scope: ng.IScope,
		element: ng.IAugmentedJQuery,
		attributes: ng.IAttributes
	): void {
		const options = attributes[AutocompleteDirective.DIRECTIVE_NAME];
		const unbindWatcher = scope.$watchCollection(
			options,
			(newOptions: IAutoCompleteOptions) => {
				if (newOptions) {
					// Set the data-autocomplete attribute to source (external in options object)
					element.attr("data-autocomplete", "source");
					element.autocomplete(newOptions);
				} else {
					element.autocomplete();
				}
			}
		);
		// Tear down
		element.on("$destroy", () => {
			const autoCompleteData = Utils.getData(element, "autocomplete");
			if (autoCompleteData) {
				autoCompleteData.destroy();
			}
			unbindWatcher();
		});
	}

	public static add(m: ng.IModule): void {
		m.directive(AutocompleteDirective.DIRECTIVE_NAME, () => {
			return new AutocompleteDirective();
		});
	}
}
