import { IBusyOptions } from "../../model/sohoxi/sohoxi-busy.model";
import { Utils } from "../../sohoxi/utils";

/**
 * Busy Directive.
 *
 * Shows a busy indicator, covering the area spanned by the element
 *
 * Example:
 * scope["isAppBusy"]: boolean = true
 * <div xi-busy="isAppBusy">
 * 	This area is busy until variable isAppBusy is set to false. Indicator shown after 3000 ms when triggered
 * </div>
 *
 * Default delay until the loading indicator shows up, after triggered, is 3000 ms.
 * To set another delay, use the attribute busy-delay
 *
 * Example:
 * scope["isAnotherAppBusy"]: boolean = true
 * <div xi-busy="isAnotherAppBusy" busy-delay="0">
 * 	This area is busy until variable isAnotherAppBusy is set to false. Indicator shown immediately when triggered
 * </div>
 *
 */
export class BusyDirective implements ng.IDirective {
	private static readonly DIRECTIVE_NAME = "xiBusy";
	public restrict = "A";
	public scope = false;

	public link(
		scope: ng.IScope,
		element: ng.IAugmentedJQuery,
		attributes: ng.IAttributes
	): void {
		element.addClass("busy");
		const options: IBusyOptions = {};
		const delay = attributes.busyDelay;
		const transparentOverlay = attributes.transparentOverlay;

		if ($.isNumeric(delay)) {
			options.displayDelay = parseInt(delay);
		}
		if (transparentOverlay === "true") { // default is false
			options.transparentOverlay = true;
		}

		element.busyindicator(options);

		const expression = attributes[BusyDirective.DIRECTIVE_NAME];
		let isRunning = false;

		const unbindWatcher = scope.$watch(
			expression,
			(newValue: boolean, _) => {
				if (newValue && !isRunning) {
					isRunning = true;
					element.trigger("start.busyindicator");
				} else if (!newValue && isRunning) {
					isRunning = false;
					element.trigger("complete.busyindicator");
				}
			}
		);
		// Tear down
		element.on("$destroy", () => {
			const busyData = Utils.getData(element, "busyindicator");
			if (busyData) {
				busyData.destroy();
			}
			unbindWatcher();
		});
	}

	public static add(m: ng.IModule): void {
		m.directive(BusyDirective.DIRECTIVE_NAME, () => {
			return new BusyDirective();
		});
	}
}
