import { NgModule } from "@angular/core";
import { BrowserModule } from "@angular/platform-browser";
import { UpgradeModule } from "@angular/upgrade/static";
import { UIRouterUpgradeModule } from "@uirouter/angular-hybrid";

import { appStates } from "./app.states";

import { appBuilder as ng1RootModule } from "../appbuilder/app";

import { AppLauncherModule } from "ng2/app-launcher/app-launcher.module";

import "./index.less";

// The main NgModule for the Angular portion of the hybrid app
@NgModule({
	imports: [
		BrowserModule,
		// Provide angular upgrade capabilities
		UpgradeModule,
		// Provides the @uirouter/angular directives and registers
		// the future state for the lazy loaded contacts module
		UIRouterUpgradeModule.forRoot({ states: appStates }),
		AppLauncherModule,
	],
	declarations: [],
	entryComponents: [],
})
export class RootModule {
	constructor(
		private upgrade: UpgradeModule
	) { }

	ngDoBootstrap(): void {
		this.upgrade.bootstrap(document.body, [ng1RootModule.name]);
	}
}
