import angular from "angular";

import { InjectNames, MarginType } from "ng2/common/constants/common.constant";
import { IJsonMargin } from "ng2/common/model/databearer/components/json-component.model";
import { IBoundary } from "ng2/common/model/project/boundary.model";
import { AngularConstants } from "../../../../common/angular-constants";
import type { IEditorComponent } from "common/interface/ieditor-component";
import { EditorService } from "../../services/editor-service";
import { IGenericSettingScope, ISettingController } from "../generic-settings";
import { SettingsBase } from "../settings-base";

class MarginSettingsCtrl extends SettingsBase implements ISettingController {

	public margin: IJsonMargin;
	public marginType: MarginType;
	public marginAllValue: number;
	public settingTitle: string;

	public static $inject = [
		AngularConstants.SCOPE,
		InjectNames.EDITOR_SERVICE
	];

	public constructor(
		protected $scope: IGenericSettingScope,
		editorService: EditorService
	) {
		super($scope, editorService);
		this.onComponentChanged(this.selectedComponent);
	}

	public refresh(): void {
		this.settingTitle = this.selectedComponent.isLayoutComponent() ? "Padding" : "Margin";

		const margin = angular.copy(this.readSettingValue<IJsonMargin>(this.$scope.settingMeta));
		this.margin = margin;
		this.marginType =
			margin.top === margin.right && margin.right === margin.bottom && margin.bottom === margin.left
				? MarginType.ALL
				: MarginType.INDIVIDUAL;
		this.marginAllValue = margin.top || 0;
	}

	protected onComponentChanged(newComponent: IEditorComponent): void {
		if (newComponent) {
			this.selectedComponent = newComponent;
			this.refresh();
		}
	}

	protected onBoundaryChanged(newBoundary: IBoundary): void {
		super.onBoundaryChanged(newBoundary);
		this.refresh();
	}

	public onChangeMarginType(type: MarginType): void {
		this.marginType = type;
		this.onChangeMargin();
	}

	public onChangeMargin(): void {
		this.checkForNull();

		if (this.marginType === MarginType.ALL) {
			this.setMarginValues();
		}
		this.onChangeSetting(this.$scope.settingMeta, this.margin);
	}

	private checkForNull(): void {
		for (const key in this.margin) {
			if (this.margin[key] === null) {
				this.margin[key] = 0;
			}
		}
	}

	private setMarginValues(): void {
		const value = this.marginAllValue || 0;
		this.margin.top = value;
		this.margin.right = value;
		this.margin.bottom = value;
		this.margin.left = value;
	}
}

export class MarginSettingsDirective implements ng.IDirective {
	template = require("./margin-setting.html");
	scope = {
		settingMeta: "=",
		register: "&"
	};
	controller = MarginSettingsCtrl;
	controllerAs = "eirMarginSettingsCtrl";

	public static add(m: ng.IModule): void {
		m.directive("eirMarginSetting", () => new MarginSettingsDirective());
	}
}
