import { AngularConstants } from "../../../../common/angular-constants";

import { SelectableEnum } from "ng2/common/constants/sohoxi.constant";
import { IMetaDataField } from "ng2/common/model/dataservice/meta-data-field.model";
import { IDataGridColumn, IDataGridOptions } from "ng2/common/model/sohoxi/sohoxi-datagrid.model";

import { IDialogBaseScope } from "../../../../common/defines";
import { DialogButtonType, IDialog, IDialogResult } from "../../../../common/services/dialog-service";
import { IRowItem } from "../../../../common/sohoxi/datagrid-helper";

export interface IAutoAddColumnResponse {
	selectedColumns: IMetaDataField[];
	autoFormatColumns: boolean;
}

export class AutoAddColumnCtrl {

	public datagridOptions: IDataGridOptions<any>;
	public autoFormatColumns = false;

	private dialog: IDialog<IMetaDataField[]>;
	private metadata: IMetaDataField[];
	private selectedFields: IMetaDataField[];

	public static $inject = [
		AngularConstants.SCOPE,
		AngularConstants.ELEMENT
	];

	public constructor(
		private scope: IDialogBaseScope<IMetaDataField[]>,
		private element: ng.IAugmentedJQuery
	) {
		const dialog: IDialog<IMetaDataField[]> = this.scope.eirDialog;
		this.dialog = dialog;
		this.metadata = dialog.parameter;
		this.datagridOptions = this.initDatagridOptions();
		this.initSelectEvent();
	}

	public cancel(): void {
		this.dialog.close();
	}

	public ok(): void {
		const result: IDialogResult<IAutoAddColumnResponse> = {
			button: DialogButtonType.Ok,
			value: {
				selectedColumns: this.selectedFields,
				autoFormatColumns: this.autoFormatColumns
			}
		};

		this.dialog.close(result);
	}

	private initDatagridOptions(): IDataGridOptions<IMetaDataField> {
		const columns: IDataGridColumn[] = [
			{
				id: "selectionCheckbox",
				sortable: false,
				resizable: false,
				formatter: Soho.Formatters.SelectionCheckbox,
				align: "center"
			},
			{
				id: "flnm",
				name: "Name",
				field: "FLNM",
				resizable: true,
				sortable: true,
				width: "50%",
				formatter: Soho.Formatters.Ellipsis,
			},
			{
				id: "flds",
				name: "Description",
				field: "FLDS",
				resizable: true,
				sortable: true,
				width: "50%",
				formatter: Soho.Formatters.Ellipsis
			}
		];
		return {
			columns,
			dataset: this.metadata,
			rowHeight: "short",
			selectable: SelectableEnum.Multiple
		};
	}

	private initSelectEvent(): void {
		this.element.find("#metadata-grid").on("selected", (event, rowItems: IRowItem<IMetaDataField>[]) => {
			if (rowItems.length > 0) {
				this.selectedFields = rowItems.map(rowObject => rowObject.data);
			} else {
				this.selectedFields = [];
			}
		});
	}

	public static add(m: ng.IModule): void {
		m.controller("eirAutoAddColumnCtrl", AutoAddColumnCtrl);
	}
}
