import angular from "angular";
import { AngularConstants } from "common/angular-constants";

import type { IBoundary } from "ng2/common/model/project/boundary.model";

import { InjectNames } from "ng2/common/constants/common.constant";
import { IDialogBaseScope } from "common/defines";

import { DialogButtonType, IDialog, IDialogResult } from "common/services/dialog-service";
import { BoundaryManagementService } from "editor/services/boundary-management-service";
import { EditorService } from "editor/services/editor-service";

export interface IStandardBoundariesDialogParameter {
	selectedStandardBoundaryIds: string[];
	standardBoundaries: IBoundary[];
}

export class StandardBoundariesCtrl {
	standardBoundaries: IBoundary[];
	private dialog: IDialog<IStandardBoundariesDialogParameter>;

	static $inject = [
		AngularConstants.SCOPE,
		InjectNames.EDITOR_SERVICE,
		InjectNames.BOUNDARY_MANAGEMENT_SERVICE
	];

	constructor(
		private scope: IDialogBaseScope<IStandardBoundariesDialogParameter>,
		private editorService: EditorService,
		private boundaryManagementService: BoundaryManagementService
	) {

		const dialog: IDialog<IStandardBoundariesDialogParameter> = this.scope.eirDialog;
		this.dialog = dialog;
		this.standardBoundaries = angular.copy(dialog.parameter.standardBoundaries);

		this.standardBoundaries.forEach((boundary: IBoundary) => {

			if (dialog.parameter.selectedStandardBoundaryIds.indexOf(boundary.id) >= 0) {
				boundary.selected = true;
			}
		});
	}

	ok(): void {
		const newBoundaryIds: string[] = [];
		this.standardBoundaries.forEach((boundary: IBoundary) => {
			if (boundary.selected === true) {
				newBoundaryIds.push(boundary.id);
			}
		});

		const result: IDialogResult<string[]> = {
			value: newBoundaryIds,
			button: DialogButtonType.Ok
		};
		this.dialog.close(result);
	}

	static add(m: ng.IModule): void {
		m.controller("eirStandardBoundariesCtrl", StandardBoundariesCtrl);
	}
}
