import { Config, DeveloperConstants, InjectNames } from "ng2/common/constants/common.constant";

import { HttpVerbEnum } from "ng2/common/enums/http-verb.enum";

import type { IJsonM3CloudData } from "ng2/common/model/databearer/dataservice/json-datasource.model";
import type {
	IIonApiContext,
	IIonApiOptions,
	IIonApiRequestOptions,
} from "ng2/common/model/ion-api/ion-api.model";

import { AngularConstants } from "common/angular-constants";
import { HttpUtil } from "common/services/http-utils";

import { HttpService } from "common/services/http-service";

export class IonApiContext implements IIonApiContext {
	constructor(
		private url: string,
		private tenantId: string,
		private token: string
	) { }

	getUrl(setTenant = true): string {
		if (setTenant) {
			return this.url + "/" + this.tenantId;
		} else {
			return this.url + Config.ION_API_ROOT;
		}
	}

	getToken(): string {
		return this.token;
	}

	setToken(token: string): void {
		this.token = token;
	}

	setUrl(url: string): void {
		this.url = url;
	}

	getHeaderName(): string {
		return "Authorization";
	}

	getHeaderValue(): string {
		return "Bearer " + this.token;
	}
}

export class IonApiService {

	private get isDeveloperModeActive(): boolean {
		return Config.IS_DEV && localStorage.getItem(DeveloperConstants.ION_PROXY) === "true";
	}

	private ionApiContext: IonApiContext;

	static $inject = [
		AngularConstants.Q,
		InjectNames.HTTP_SERVICE
	];

	constructor(
		private $q: ng.IQService,
		private httpService: HttpService
	) {
	}

	getIonApiContextAsync(options?: IIonApiOptions): ng.IPromise<IIonApiContext> {
		const deferred = this.$q.defer<IIonApiContext>();
		const refresh = options && options.refresh;

		if (Config.ION_API_URL) {
			const hasToken = this.ionApiContext && this.ionApiContext.getToken();
			const ionBaseURL = this.getIonBaseUrl();

			if (!hasToken || refresh) {
				const tokenURL =
					this.isDeveloperModeActive ?
						`${Config.REST_ROOT}/settings/token` :
						"/grid/rest/security/sessions/oauth?forceRefresh=true";

				this.httpService.executeRequest<string>({ url: tokenURL, method: HttpVerbEnum.GET, cache: false })
					.then((response) => {

						const token = DEV_SERVER ?
							(response.data as any).data :
							response.data;

						const context = new IonApiContext(ionBaseURL, Config.TENANT_ID, token);
						this.ionApiContext = context;
						deferred.resolve(context);
					}).catch((error) => {
						deferred.reject(error);
					});
			} else {
				if (this.ionApiContext.getUrl() !== ionBaseURL) {
					this.ionApiContext.setUrl(ionBaseURL);
				}
				deferred.resolve(this.ionApiContext);
			}
		} else {
			deferred.reject();
		}

		return deferred.promise;
	}

	private getIonBaseUrl(): string {
		const proxyURL = `http://localhost:${Config.ION_API_PROXY_PORT}`;

		// Possible alternative to the proxy running in our own server, see grunt job ion-api-proxy
		//const proxyURL = "https://localhost:8000";
		return this.isDeveloperModeActive ?
			proxyURL :
			Config.ION_API_URL;

	}

	executeIonApiAsync<T>(options: IIonApiRequestOptions): ng.IPromise<ng.IHttpPromiseCallbackArg<T>> {
		const deferred = this.$q.defer<ng.IHttpPromiseCallbackArg<T>>();
		this.getIonApiContextAsync().then((context: IIonApiContext) => {
			this.executeIonApi(context, options, deferred, false);
		}).catch(r => {
			deferred.reject(r);
		});
		return deferred.promise;
	}

	/**
	 * NOTE: Solution copied from lime (May 2017, HEAD at 2203077d3a3):
	 *	http://git.infor.com/projects/ITECH/repos/homepages/browse/
		* Client/Framework/Framework/scripts/lime/app/container.ts#1034
		*/
	private executeIonApi<T>(
		context: IIonApiContext,
		options: IIonApiRequestOptions,
		deferred: ng.IDeferred<any>,
		isRetry: boolean
	): void {
		const url = options.url;
		const contextUrl = context.getUrl(options.setTenant);
		if (url && url.indexOf("https://") !== 0 && url.indexOf(contextUrl) !== 0) {
			// Prepend the ION API base URL to the relative URL
			options.url = HttpUtil.combine(contextUrl, url);
		}

		// Add the authorization header for ION API including the OAuth token.
		const headers = options.headers || {};
		headers[context.getHeaderName()] = context.getHeaderValue();
		options.headers = headers;

		this.httpService.executeRequest(options).then((httpResponse: ng.IHttpResponse<any>) => {
			//M3 returns failures as 200 in some cases. Currently only covering M3 Cloud data.
			const m3Data = httpResponse.data as unknown as IJsonM3CloudData;
			if (m3Data && m3Data.nrOfFailedTransactions && m3Data.nrOfFailedTransactions > 0) {
				const modifiedHttpResponse = {
					...httpResponse,
				};
				modifiedHttpResponse.data.error = {
					message: m3Data.results[0].errorMessage,
				};

				deferred.reject(modifiedHttpResponse);
			}

			// Resolve successful request.
			deferred.resolve(httpResponse);
		}).catch((error: ng.IHttpResponse<any>) => {
			const unauthorizedError = 401;
			const isUnauthorized = error.status === unauthorizedError;
			// Check if the failed request should be retried with a new ION API context
			if (!isRetry && isUnauthorized && options.ionApiRetry !== false) {
				this.getIonApiContextAsync({ refresh: true }).then((retryContext: IIonApiContext) => {
					this.executeIonApi(retryContext, options, deferred, true);
				}).catch(retryError => {
					// Reject when a new context cannot be retrieved
					deferred.reject(retryError);
				});
			} else {
				// Reject failed retry attemtps or when retry is disabled
				deferred.reject(error);
			}
		});
	}

	static add(ngModule: ng.IModule): void {
		ngModule.service(InjectNames.ION_API_SERVICE, IonApiService);
	}
}
