import { ITextAreaOptions } from "../../model/sohoxi/sohoxi-textarea.model";
import { Utils } from "../utils";

/**
 * Textarea directive.
 *
 * Example:
 * <textarea xi-textarea type="text" maxlength="100"></textarea>
 */
export class TextareaDirective implements ng.IDirective {
	private static readonly DIRECTIVE_NAME = "xiTextarea";
	public restrict = "A";
	public scope = false;

	public link(
		scope: ng.IScope,
		element: ng.IAugmentedJQuery,
		attributes: ng.IAttributes
	): void {
		const options = attributes[TextareaDirective.DIRECTIVE_NAME];
		const unbindWatcher = scope.$watch(
			options,
			(newOptions: ITextAreaOptions) => {
				const textareaApi = Utils.getData(element, "textarea");
				if (newOptions && textareaApi === undefined) {
					element.textarea(newOptions);
				} else if (textareaApi) {
					textareaApi.updated(newOptions);
				} else {
					element.textarea();
				}
			}
		);
		// Tear down
		element.on("$destroy", () => {
			const textAreaData = Utils.getData(element, "textarea");
			if (textAreaData) {
				textAreaData.destroy();
			}
			unbindWatcher();
		});
	}

	public static add(m: ng.IModule): void {
		m.directive(TextareaDirective.DIRECTIVE_NAME, () => {
			return new TextareaDirective();
		});
	}
}
