import { IJsonTabComponent, IJsonTabs } from "../../../../common/model/component/tabs.model";
import { Tabs } from "../../../../components/tabs/tabs";
import { IEditorTabs } from "../../components/editor-tabs";
import { ISettingsClassInstance } from "../../defines";
import { ComponentSettingsCtrl } from "../component-settings";
import { TabPanelSettings } from "./tab-panel-settings";
import { TabsContentSettingsDirective } from "./tabs-content-settings";

export class TabsSettings implements ISettingsClassInstance {
	static getInstance(): TabsSettings {
		return TabsSettings.instance;
	}
	private static instance: TabsSettings = new TabsSettings();
	contentSettingsDirective: ng.IDirective = new TabsContentSettingsDirective();
	protected selectedComponent: IEditorTabs;

	constructor() {
		if (TabsSettings.instance) {
			throw new Error("Error: Instantiation failed: Use TabsSettings.getInstance() instead of new.");
		}
	}

	create(id: string): IJsonTabs {
		const newTabs = ComponentSettingsCtrl.create(id, Tabs) as IJsonTabs;
		const numberOfTabs = 4;
		//Create each tab and a flexbox for every tab
		newTabs.children = [];
		newTabs.componentTabs = [];
		for (let i = 1; i <= numberOfTabs; i++) {
			const newId: string = id + "Panel" + i;
			const componentTab: IJsonTabComponent = {
				id: newId,
				name: "Tab " + i
			};
			newTabs.componentTabs.push(componentTab);
			newTabs.children.push(TabPanelSettings.getInstance().create(newId, i));
		}

		return newTabs;
	}
}
