import angular from "angular";
import { AngularConstants } from "../../../../common/angular-constants";

import { DateFormat, InjectNames, MaskProcess } from "ng2/common/constants/common.constant";
import { IDataPair } from "ng2/common/model/data-pair.model";
import { IJSonMaskSettings } from "ng2/common/model/databearer/components/json-textbox.model";

import { DialogButtonType, DialogService, IDialogResult } from "../../../../common/services/dialog-service";
import type { IEditorComponent } from "common/interface/ieditor-component";
import { IEditorTextbox } from "../../components/editor-textbox";
import { EditorService } from "../../services/editor-service";
import { SettingsBase } from "../settings-base";
import { TextboxSettings } from "./textbox-settings";

export class TextboxStyleSettingsCtrl extends SettingsBase {

	public get availableMasks(): IDataPair[] {
		return TextboxSettings.getInstance().availableMasks;
	}

	public settingMask: IDataPair;
	protected selectedComponent: IEditorTextbox;
	private mask: IJSonMaskSettings;

	public static $inject = [
		AngularConstants.SCOPE,
		InjectNames.EDITOR_SERVICE,
		InjectNames.DIALOG_SERVICE
	];

	public constructor(
		$scope: ng.IScope,
		editorService: EditorService,
		private dialogService: DialogService
	) {
		super($scope, editorService);

		this.onComponentChanged(editorService.getSelectedComponent());
		this.initiationMaskProcess();
	}

	protected onComponentChanged(newComponent: IEditorComponent): void {
		super.onComponentChanged(newComponent);
		if (this.selectedComponent) {
			this.initiationMaskProcess();
		}
	}

	public openMaskSettings(): void {
		const options = {
			title: "Masking Options",
			parameter: angular.copy(this.selectedComponent.getMask()),
			template: require("./mask-setting-dialog.html")
		};
		this.dialogService.show(options).then((result: IDialogResult<IJSonMaskSettings>) => {
			if (result.button === DialogButtonType.Ok) {
				this.selectedComponent.updateMaskOptions(result.value);
				this.editorService.saveCanvas();
			}
		});
	}

	public onChangeProcess(): void {
		//Give mask a default value
		switch (this.settingMask.value) {
			case MaskProcess.NUMBER:
				const limit = 10;
				this.mask = {
					process: this.settingMask.value,
					allowNegative: false,
					integerLimit: limit,
					decimalLimit: 0,
					allowThousandsSeparator: false,
					prefix: ""
				};
				break;
			case MaskProcess.DATE_TIME:
				this.mask = {
					process: this.settingMask.value,
					format: DateFormat.FORMAT_1
				};
				break;
			case MaskProcess.STRING:
				this.mask = {
					process: this.settingMask.value,
					format: "****-****-****"
				};
				break;
			default:
				// Password & None
				this.mask = { process: this.settingMask.value };
				break;
		}
		this.selectedComponent.updateMaskOptions(this.mask);
		this.editorService.saveCanvas();
	}

	private initiationMaskProcess(): void {
		const mask = this.selectedComponent.getMask();
		const availableMasks = this.availableMasks;
		for (let i = 0; i < availableMasks.length; i++) {
			if (mask.process === availableMasks[i].value) {
				this.settingMask = availableMasks[i];
				break;
			}
		}
	}
}

export class TextboxStyleSettingsDirective implements ng.IDirective {
	template = require("./textbox-style-settings.html");
	scope = true;
	controller = TextboxStyleSettingsCtrl;
	controllerAs = "eirTextboxStyleSettingsCtrl";
}
