import { AngularConstants } from "../common/angular-constants";

import { CanvasModeEnum, InjectNames } from "ng2/common/constants/common.constant";
import { EditorService } from "./editor/services/editor-service";

export interface IComponentValueScope extends ng.IScope {
	content: any;
}

export class ComponentValueController {
	static $inject = [
		AngularConstants.SCOPE,
		InjectNames.EDITOR_SERVICE
	];

	public defaultValue: string  = "<Empty>";

	public showLabelEmpty: boolean;

	constructor(
		private scope: IComponentValueScope,
		private editorService: EditorService
	) { }

	public watchChange(): void {
		this.scope.$watch("content",
			() => {
				const isRuntimeMode = this.editorService.getCanvasMode();
				this.setLabel(isRuntimeMode);
			}
		);

		this.scope.$watch(
			() => {
				return this.editorService.getCanvasMode();
			},
			(isRuntimeMode: CanvasModeEnum) => {
				this.setLabel(isRuntimeMode);
			}
		);
	}

	private setLabel(isRuntimeMode: CanvasModeEnum): void {
		const rawJson = JSON.stringify(this.scope.content);
		if (rawJson) {
			if ((isRuntimeMode === CanvasModeEnum.SELECTION ||
				isRuntimeMode === CanvasModeEnum.INSERT) &&
				rawJson.match(/""/i)) {
				this.showLabelEmpty = true;
			} else if (isRuntimeMode === CanvasModeEnum.RUNTIME
			) {
				this.showLabelEmpty = false;
			}
		}
	}
}

export class ComponentValueDirective implements ng.IDirective {
	scope = {
		content: "=content"
	};

	template = "{{!content && vm.showLabelEmpty ? vm.defaultValue : content }}";

	controller = ComponentValueController;
	controllerAs = "vm";

	link = (
		scope: IComponentValueScope,
		element: ng.IAugmentedJQuery,
		attribute: ng.IAttributes,
		controller: ComponentValueController
	) => {
		controller.watchChange();
	}

	static add(m: ng.IModule): void {
		const directive: ng.IDirectiveFactory = () => new ComponentValueDirective();

		m.directive("eirComponentValue", directive);
	}
}
