import { FieldValidation } from "../base/field-validation";
import { ISharedStyling } from "../base/shared-styling";

import * as xi from "../../model/sohoxi/sohoxi-textarea.model";

export class TextareaFieldCtrl
	extends FieldValidation
	implements ISharedStyling {
	public readonly: boolean = false;

	public textareaOptions: xi.ITextAreaOptions;

	public $onInit(): void {
		super.$onInit();

		this.textareaOptions = {
			autoGrow: true,
			autoGrowAnimate: true
		};
	}
}
