import type { IComponentController, IOnInit } from "angular";

import { InjectNames } from "ng2/common/constants/common.constant";

import type { IAboutOptions } from "common/model/sohoxi/sohoxi-about.model";

import { AngularConstants } from "common/angular-constants";

import { CommonService } from "common/services/common-service";

export class LicensesButton implements IComponentController, IOnInit{
	static $inject: string[] = [
		AngularConstants.Q,
		InjectNames.COMMON_SERVICE
	];

	appName: string;
	aboutOptions: IAboutOptions;

	constructor(
		private $q: ng.IQService,
		private commonService: CommonService
	) { }

	$onInit(): void {
		const promises: ng.IPromise<string>[] = [
			this.commonService.loadApplicationVersion(),
			this.commonService.loadDBVersions(),
		];

		this.$q
			.all(promises)
			.then(response => {
				const version = response[0];
				const dbVersion = response[1];
				const productName = this.appName.replace("Infor ", "");
				const content = `<p>Form definitions database version ${dbVersion}</p>`;

				this.aboutOptions = {
					appName: this.appName,
					productName,
					version,
					useDefaultCopyright: true,
					content
				};
			});

	}

	static add(m: ng.IModule): void {
		m.component("eirLicensesButton", {
			bindings: {
				appName: "@"
			},
			controller: LicensesButton,
			controllerAs: "eirLicenseButtonCtrl",
			template: `
			<button class="btn about" id="about-dialog"
				xi-about="eirLicenseButtonCtrl.aboutOptions"
			>
				About App Builder
			</button>
			`
		});
	}
}
