import { AngularConstants } from "../../../../common/angular-constants";

import { IJsonChartDataset } from "ng2/common/model/databearer/components/json-chart.model";

import { IAvailableSourceField, IDialogBaseScope } from "../../../../common/defines";
import { DialogButtonType, IDialog, IDialogResult } from "../../../../common/services/dialog-service";
import { ChartDialogActionEnum } from "./chart-settings";

export interface IChartDialogOptions {
	content: IChartDialogContent;
	type: ChartDialogActionEnum;
}

export interface IChartDialogContent {
	chartContent?: IJsonChartDataset;
	sourceFields: IAvailableSourceField[];
}

export interface IChartDialogResult {
	content?: IJsonChartDataset;
	dialogAction: ChartDialogActionEnum;
}

export class ChartSettingsDialogCtrl {
	type: ChartDialogActionEnum;
	chartActionEnum = ChartDialogActionEnum;
	private dialog: IDialog<IChartDialogOptions>;
	private content: IChartDialogContent;
	private selectedCategory: IAvailableSourceField;
	private selectedSeries: IAvailableSourceField;
	static $inject = [AngularConstants.SCOPE];

	constructor(private scope: IDialogBaseScope<IChartDialogOptions>) {
		const dialog: IDialog<IChartDialogOptions> = scope.eirDialog;
		this.dialog = dialog;
		this.type = dialog.parameter.type;
		this.content = dialog.parameter.content;
		this.setSelectedSourceField();
	}

	private setSelectedSourceField(): void {
		const sourceFields = this.content.sourceFields;
		const chartContent = this.content.chartContent;
		for (let i = 0; i < sourceFields.length; i++) {
			if (sourceFields[i].sourceKeyShort === chartContent.categories) {
				this.selectedCategory = sourceFields[i];
			}
			if (sourceFields[i].sourceKeyShort === chartContent.series) {
				this.selectedSeries = sourceFields[i];
			}
		}
	}

	cancel(): void {
		const result: IDialogResult<void> = {
			button: DialogButtonType.Cancel
		};
		this.dialog.close(result);
	}

	/**
	 * Triggered in view from the edit dataset dialog.
	 *
	 * @param {boolean} [isNewContent=false]
	 *
	 * @memberOf EditSerieCtrl
	 */
	save(): void {
		this.content.chartContent.categories = this.selectedCategory ? this.selectedCategory.sourceKeyShort : null;
		this.content.chartContent.series = this.selectedSeries ? this.selectedSeries.sourceKeyShort : null;
		const result: IDialogResult<IChartDialogResult> = {
			value: {
				dialogAction: this.type,
				content: this.content.chartContent
			},
			button: DialogButtonType.Ok
		};
		this.dialog.close(result);
	}

	static add(m: ng.IModule): void {
		m.controller("eirChartSettingsDialogCtrl", ChartSettingsDialogCtrl);
	}
}
