import angular from "angular";
import { AngularConstants } from "common/angular-constants";

import type { IEditorComponent } from "common/interface/ieditor-component";
import { CanvasModeEnum, InjectNames } from "ng2/common/constants/common.constant";
import { IJQueryMouseEventObject } from "common/defines";

import { IErrorResponse } from "common/error-response";
import { EditorApplicationParent } from "editor/components/editor-application-parent";

import { EditorService } from "editor/services/editor-service";
import { SaveService } from "editor/services/save-service";
class StatusbarCtrl {
	private components: IEditorComponent[];
	private applicationParent: EditorApplicationParent;
	private selectedComponent: IEditorComponent;
	private unsubscribers: Array<Function> = [];

	public saveUpdate: string;

	static $inject = [
		AngularConstants.SCOPE,
		InjectNames.EDITOR_SERVICE,
		InjectNames.SAVE_SERVICE
	];

	constructor(
		scope: ng.IScope,
		private editorService: EditorService,
		private saveService: SaveService
	) {
		this.unsubscribers.push(editorService.selectedComponentChangedEvent.on((component: IEditorComponent) => {
			this.selectedComponent = component;
			this.updateStatusbar(component);
		}));

		this.unsubscribers.push(saveService.saveStateEvent.on((value: string | IErrorResponse) => {
			this.updateSave(value);
		}));

		scope.$on(AngularConstants.DESTROY, () => {
			angular.forEach(this.unsubscribers, (unsubscribe) => {
				unsubscribe();
			});
		});
	}

	private updateStatusbar(component: IEditorComponent): void {
		const components: IEditorComponent[] = [];
		if (!component) {
			components.push(this.getApplicationParent());
		} else if (this.isApplicationComponent(component)) {
			components.push(component);
		} else {
			while (component) {
				components.unshift(component);
				component = component.parent;
			}
		}
		this.components = components;
	}

	private getApplicationParent(): EditorApplicationParent {
		if (!this.applicationParent) {
			this.applicationParent = this.editorService.getApplicationParent();
		}
		return this.applicationParent;
	}

	private isApplicationComponent(component: IEditorComponent): boolean {
		return component.getId() === this.getApplicationParent().getId();
	}

	public updateSave(value: string | IErrorResponse): void {
		let outValue: string = "";

		if (typeof (value) === "string") {
			outValue = value;
			if (value === this.saveService.saveStates.saved) {
				const cutString = 8;
				let currentTime = new Date().toTimeString();
				currentTime = currentTime.substring(0, cutString);
				outValue = value + currentTime;
			}
		} else {
			outValue = value.message;
		}

		this.saveUpdate = outValue;
	}

	// Public since used in view
	public selectComponent(component: IEditorComponent, event: IJQueryMouseEventObject): void {
		const isApplication = this.isApplicationComponent(component);
		const canvasMode = this.editorService.getCanvasMode();
		if (!isApplication) {
			// TODO Allow context to be set to ApplicationComponent?
			component.onClick(event);
		} else {
			if (canvasMode === CanvasModeEnum.SELECTION) {
				this.editorService.deselectComponent();
			} else if (canvasMode === CanvasModeEnum.FINDER) {
				this.editorService.selectFinderComponent(component);
			}
		}
	}

	public onMouseEnter(component: IEditorComponent, $event: IJQueryMouseEventObject): void {
		if (!this.isApplicationComponent(component)) {
			component.onMouseOver($event);
		}
	}

	public onMouseLeave(component: IEditorComponent, $event: IJQueryMouseEventObject): void {
		if (!this.isApplicationComponent(component)) {
			component.onMouseLeave($event);
		}
	}

	/*private isSelectedComponent(component: IEditorComponent): boolean {
		return this.selectedComponent && component.getId() === this.selectedComponent.getId();
	}*/
}

export class StatusbarDirective {
	static add(m: ng.IModule): void {
		m.directive("eirStatusbar", () => {
			return {
				controller: StatusbarCtrl,
				controllerAs: "eirStatusbarCtrl",
				template: require("./statusbar.html")
			};
		});
	}
}
