import { Injectable } from "@angular/core";
import isEmpty from "lodash-es/isEmpty";
import isNil from "lodash-es/isNil";
import isUndefined from "lodash-es/isUndefined";
import orderBy from "lodash-es/orderBy";
import {
	forkJoin,
	Observable,
	throwError
} from "rxjs";

import { DEFAULT_BOUNDARY, InteractionTriggerEnum } from "ng2/common/constants/common.constant";
import { WIDGET_BOUNDARIES } from "ng2/common/constants/standard-boundaries.constant";

import type { IComponent } from "ng2/common/model/components/base/component.model";
import type { ILayoutComponent } from "ng2/common/model/components/base/layout-component.model";
import type { IJsonRuntimeMashup } from "ng2/common/model/databearer/json-mashup.model";
import type { IInteraction } from "ng2/common/model/interaction/interaction.model";
import type { IBoundary } from "ng2/common/model/project/boundary.model";
import type { IMashup } from "ng2/common/model/project/mashup/mashup.model";

import { RuntimeApp } from "ng2/common/project/runtime-app";

import { ComponentService } from "ng2/components/services/component.service";
import { DataServiceBuilder } from "ng2/components/services/data-service.service";

@Injectable()
export class FrameworkService {
	constructor(
		public componentService: ComponentService,
		public dataServiceBuilder: DataServiceBuilder,
	) {}

	//#region Boundary
	updateCurrentBoundary(width: number): void {

		this.currentBoundary = this.getBoundaryByWidth(width);
		this.rootComponentInstance.onBoundaryChange(this.currentBoundary);
	}

	private _currentBoundary: IBoundary;

	get currentBoundary(): IBoundary {
		return this._currentBoundary;
	}

	set currentBoundary(value: IBoundary) {
		this._currentBoundary = value;
	}

	/**
	 * Returns boundary for widget size if exist otherwise returns DEFAULT_BOUNDARY
	 *
	 * @param {number} cols
	 * @returns {IBoundary}
	 * @memberof FrameworkService
	 */
	getBoundaryByWidgetSize(cols: number): IBoundary {

		const boundaryId = WIDGET_BOUNDARIES[cols - 1].id;
		const boundary = this.currentProject.manifest.mashupBoundaries
			.find(boundary => boundary.id === boundaryId);

		return boundary ? boundary : DEFAULT_BOUNDARY;
	}

	/**
	 * Returns boundary for width if specified otherwise returns DEFAULT_BOUNDARY
	 *
	 * @returns {IBoundary}
	 * @memberof FrameworkService
	 */
	getBoundaryByWidth(width?: number): IBoundary {

		if (isNil(width)) {
			return DEFAULT_BOUNDARY;
		}

		const boundaries = orderBy(
			this.currentProject.manifest.mashupBoundaries,
			["limitX"])
			.filter(boundary => !isNil(boundary.limitX));

		const boundary = boundaries
			.find(boundary => width <= boundary.limitX);

		return isNil(boundary) ?
			DEFAULT_BOUNDARY :
			boundary;
	}

	getNextProjectBoundary(boundaryId: string): IBoundary {
		if (boundaryId === DEFAULT_BOUNDARY.id) {
			return DEFAULT_BOUNDARY;
		}
		const projectBoundaries = orderBy(
			this.currentProject.manifest.mashupBoundaries,
			["limitX"]
		);

		for (let i = 0; i < projectBoundaries.length; i++) {
			if (projectBoundaries[i].id === boundaryId) {
				return projectBoundaries[i + 1];
			}
		}

		return DEFAULT_BOUNDARY;
	}

	//#endregion

	private _currentProject: IMashup;

	get currentProject(): IMashup {
		return this._currentProject;
	}

	set currentProject(value: IMashup) {
		this._currentProject = value;
	}

	private _componentInstance: ILayoutComponent;

	get rootComponentInstance(): ILayoutComponent {
		return this._componentInstance;
	}

	set rootComponentInstance(value: ILayoutComponent) {
		this._componentInstance = value;
	}

	runStartupInteractions(): Observable<any> {
		const startupInteraction =
			this.getInteractionByTriggerName(InteractionTriggerEnum.STARTUP);

		const observables: Observable<any>[] = [];
		startupInteraction.forEach(interaction => {
			observables.push(this.runInteraction(interaction));
		});

		return forkJoin(observables);
	}

	runInteraction(interaction: IInteraction): Observable<any> {
		if (interaction.targetName && interaction.targetActionName) {

			const observables: Observable<any>[] = [];

			this.getComponentOnInteractionTargetName(interaction.targetName)
				.forEach(component =>
					observables.push(
						component.loadInteraction(interaction))
				);

			return forkJoin(observables);

		} else {
			return throwError(
				`[FrameworkService] Interaction: ${interaction.getId()} did not run, not all prerequisites were present`
			);
		}
	}

	getInteractionByTriggerName(triggerName: InteractionTriggerEnum): IInteraction[] {
		return this.currentProject
			.getInteractions()
			.filter(interaction => {
				return interaction.sourceTriggerName === triggerName;
			});
	}

	private getComponentOnInteractionTargetName(targetName: string): IComponent[] {
		const components: IComponent[] = [];
		const attrName = "databearer.id";
		const attrValue = "";

		this.rootComponentInstance.forEachChild((child) => {
			if (targetName === child.id) {
				components.push(child);
			}
		}, true);

		return components;
	}

	/**
	 * Mock stuff
	 */
	loadProject(metadata?: string): RuntimeApp {

		let runtimeApp;
		if (isUndefined(metadata) || isEmpty(metadata)) {
			runtimeApp = RuntimeApp.fromObj(this.jsonRuntimeApp);
		} else {
			runtimeApp = RuntimeApp.fromObj(JSON.parse(metadata));
		}

		this.currentBoundary = DEFAULT_BOUNDARY;
		// this.currentBoundary = runtimeApp.manifest.mashupBoundaries[0];

		this.currentProject = runtimeApp;

		return runtimeApp;
	}

	//tslint:disable
	jsonRuntimeApp: IJsonRuntimeMashup = {"rootComponent":{"children":[],"typedef":"Flexbox","id":"AppPanel","displayName":"AppPanel","boundarySettings":{"Default":{"orientation":2,"size":1,"sizeType":2,"horizontalAlignment":1,"verticalAlignment":1,"overflow":1,"backgroundColor":null,"padding":null,"width":{"sizeType":2,"number":0},"height":{"sizeType":3,"number":0},"visibility":true,"margin":{"top":0,"right":0,"bottom":0,"left":0},"showError":true}},"TargetAction":"Get"},"datasources":[],"interactions":[],"translations":[{"id":"1595510938502760163","componentId":"AppPanel","property":"applicationHeader","translations":[{"locale":"en-US","value":"No Project Loaded"}]}],"compositeComponents":[],"expressions":[],"manifest":{"createdBy":"Leung","createdTime":1595510938607,"description":"","displayName":"No Project Loaded","fileMetaData":{},"id":"85db9130-9510-4e40-9719-f9593cfd77f8","lastChangedBy":"Leung","lastChangedTime":1595510938920,"mashupBoundaries":[{"id":"Default","displayName":"Default"}],"version":"1.0","previewImg":"","createdByDisplayName":"Leung Wong (site developer)","lastChangedByDisplayName":"Leung Wong (site developer)","title":"No Project Loaded","showTitleBar":true,"themeSwitchEnabled":false,"defaultTheme":1,"urlVariables":[]},"tags":[]} as any;
	//tslint:enable
}
