import { ComponentClassNames } from "ng2/common/constants/common.constant";

import { IJsonLabel } from "ng2/common/model/databearer/components/json-label.model";

import type { IEditorComponent } from "common/interface/ieditor-component";
import { Label } from "components/label/label";
import { EditorComponentCtrl, IEditorComponentScope } from "editor/components/editor-component";
import type { IEditorFlexbox } from "editor/components/editor-flexbox";

export interface IEditorLabel extends IEditorComponent, Label {
	parent: IEditorFlexbox;
	// Overrides to make dual extend work
	getScope(): IEditorComponentScope;
	getDatabearer(): IJsonLabel;
}

export class EditorLabelCtrl extends EditorComponentCtrl {
	static add(m: ng.IModule): void {
		m.controller(
			ComponentClassNames.EDITOR_COMPONENT_PREFIX + Label.name,
			EditorLabelCtrl
		);
	}

	protected component: IEditorLabel;

}
