import angular from "angular";
import { AngularConstants } from "../../../../../common/angular-constants";

import { BindingTypes, InjectNames, ValueObject } from "ng2/common/constants/common.constant";
import { IDataPair } from "ng2/common/model/data-pair.model";
import { IAvailableSourceField } from "../../../../../common/defines";
import { ISourceItem } from "../../../../../common/model/component/hyperlink.model";

import { FrameworkService } from "../../../../../components/services/framework-service";
import { EditorService } from "../../../services/editor-service";

export interface IUrlSourceScope extends ng.IScope {
	source: ISourceItem;
	availableSources: IDataPair[];
	buildLink(): () => {};
	changeSourceAlias(item: INewOldAlias): () => {};
	removeSource(item: ISourceItem): () => {};
	index: string;
}

export interface INewOldAlias {
	oldAlias: string;
	newAlias: string;
}

export class UrlSourceFieldCtrl {
	public source: ISourceItem;
	public availableSources: IDataPair[] = [];
	public availableBindings: IAvailableSourceField[] = [];
	public currentSourceAlias: string;
	public componentName: string;
	public index: string;
	private unsubscribers: Function[] = [];

	static $inject = [
		AngularConstants.SCOPE,
		AngularConstants.ELEMENT,
		InjectNames.EDITOR_SERVICE,
		InjectNames.FRAMEWORK_SERVICE
	];

	constructor(
		private $scope: IUrlSourceScope,
		private $element: ng.IAugmentedJQuery,
		private editorService: EditorService,
		private frameworkService: FrameworkService
	) {
		this.source = $scope.source;
		this.index = $scope.index;
		this.availableSources = $scope.availableSources;
		this.getAvailableBindings(true);
		this.currentSourceAlias = angular.copy(this.source.alias);
		this.setSourceName();
		this.unsubscribers.push(editorService.hyperlinkAliasChangedEvent.on((alias) => {
			// Handle error (review this on SoHo 4.15)
			if (alias.index === this.index) {
				const aliasElement = this.$element.find("#alias-id-" + this.index);
				if (alias.new === "") {
					aliasElement.addError({ message: "Alias can not be empty." });
				} else if (alias.old === this.source.alias) {
					aliasElement.addError({ message: "Alias already exists." });
				} else {
					this.source.alias = this.currentSourceAlias;
					aliasElement.removeError();
					this.$element.find(".icon-error").remove();
				}
			}
		}));

		$scope.$on(AngularConstants.DESTROY, () => {
			angular.forEach(this.unsubscribers, (unsubscribe) => {
				unsubscribe();
			});
		});
	}

	public onChangeAlias(): void {
		if (this.currentSourceAlias !== this.source.alias) {
			const aliases: INewOldAlias = {
				oldAlias: this.source.alias,
				newAlias: this.currentSourceAlias
			};
			this.$scope.changeSourceAlias(aliases);
		}
	}

	public onBuildLink(): void {
		this.$scope.buildLink();
	}

	public onRemoveSource(): void {
		this.$scope.removeSource(this.source);
	}

	public getAvailableBindings(init: boolean = false): void {
		const component = this.editorService.getComponentInstanceById(this.source.type);
		if (component) {
			component.getBindingMetaData().then((availableBindings) => {
				this.addSourceValue(availableBindings, init);
			});
		} else if (this.source.type === BindingTypes.CONTEXT) {
			const selectedComponent = this.editorService.getSelectedComponent();
			selectedComponent.getContextMetaData().then((availableBindings) => {
				this.addSourceValue(availableBindings, init);
			});
			this.frameworkService.registerContextBinding(selectedComponent, "linkvalue", this.source.value, "{$value}");
		} else {
			this.onRemoveSource();
		}
	}

	private addSourceValue(availableBindings: IAvailableSourceField[], init: boolean = false): void {
		this.availableBindings = availableBindings;
		if (availableBindings.length > 0) {
			if (!init || this.source.value === "") {
				this.source.value = availableBindings[0].sourceKey;
			}
			this.onBuildLink();
		}
	}

	private setSourceName(): void {
		//Component name is used in UI
		if (this.source.type === BindingTypes.CONTEXT) {
			this.componentName = ValueObject.TARGET_CONTEXT;
		} else {
			this.componentName = this.editorService.getComponentInstanceById(this.source.type).getDisplayName();
		}
	}
}

export class UrlSourceFieldDirective implements ng.IDirective {
	template = require("./url-source-field.html");
	scope = {
		source: "=",
		availableSources: "<",
		buildLink: "&",
		removeSource: "&",
		changeSourceAlias: "&",
		index: "<"
	};
	controller = UrlSourceFieldCtrl;
	controllerAs = "eirUrlSourceFieldCtrl";

	static add(m: ng.IModule): void {
		m.directive("eirUrlSourceField", () => new UrlSourceFieldDirective());
	}
}
