import angular from "angular";

import { Config, MGPlatform, ProjectLimit, SaveFlagsEnum, ServerPaths } from "ng2/common/constants/common.constant";
import { SelectableEnum } from "ng2/common/constants/sohoxi.constant";
import { DatasourceTypeEnum } from "ng2/common/enums/datasource-type.enum";
import {
	IJsonDatasource,
	IJsonGenericDatasource2,
} from "ng2/common/model/databearer/dataservice/json-datasource.model";
import { IDataGridColumn, IDataGridOptions, IDataGridRow } from "ng2/common/model/sohoxi/sohoxi-datagrid.model";

import { IExportDialogParams } from "../../../../common/dialogs/export/export-dialog";
import { ITestDataserviceParameters, TestDataserviceDialogType } from "../../../../common/dialogs/test-dataservice/test-dataservice-dialog";
import { IComponent } from "../../../../common/IComponent";
import { DialogButtonType, IDialogOptions, IDialogResult, StandardDialogButtons } from "../../../../common/services/dialog-service";
import { CustomFormatters } from "../../../../common/sohoxi/custom-formatters";
import { IDatasourceWizardParameter } from "../../../repository/datasource/create-wizard/datasource-wizard-ctrl";
import { IUpsertDialogParameter } from "../../../repository/datasource/datasource-tab-ctrl";
import type { IEditorComponent } from "common/interface/ieditor-component";
import { IItemSpecificData, ManageDialogBase } from "./manage-dialog-base";

import { Helper } from "../../../../common/helper";

export class ManageDatasourcesCtrl extends ManageDialogBase<IJsonDatasource> {

	protected getItemSpecificData(): IItemSpecificData {
		return {
			dataName: "Data Service",
			conflictKey: "displayName",
			urlName: ServerPaths.DATASOURCES,
			maxAllowed: ProjectLimit.MAX_DATASOURCES,
			maxErrorMessage: ProjectLimit.DATASOURCE_ERROR_MESSAGE
		};
	}

	protected getDataList(): IJsonDatasource[] {
		return this.editorService.getCurrentProject().getDatasourceDatabearers();
	}

	protected addItemsToProject(datasources: IJsonDatasource[]): void {
		datasources.forEach((datasource) => {
			this.editorService.getCurrentProject().addDatasource(datasource);
		});
	}

	protected initGrid(): void {
		const columns: IDataGridColumn[] = [];
		columns.push({
			id: "selectionCheckbox",
			width: 50,
			sortable: false,
			resizable: false,
			formatter: Soho.Formatters.SelectionCheckbox,
			align: "center"
		});
		columns.push({
			id: "displayName",
			name: "Name",
			field: "displayName",
			width: 200,
			sortable: true,
			resizable: false,
			formatter: Soho.Formatters.Ellipsis
		});
		columns.push({
			id: "author",
			name: "Author",
			field: "authorDisplayName",
			width: 150,
			sortable: true,
			resizable: false,
			formatter: Soho.Formatters.Ellipsis
		});
		columns.push({
			id: "date",
			name: "Created",
			field: "creationDate",
			width: 150,
			resizable: false,
			sortable: true,
			formatter: Soho.Formatters.Date
		});
		columns.push({
			id: "description",
			name: "Description",
			field: "description",
			width: 200,
			sortable: true,
			resizable: false,
			formatter: CustomFormatters.Multi(CustomFormatters.DataSourceDescription(), Soho.Formatters.Ellipsis)
		});
		columns.push({
			id: "suite",
			name: "Type",
			field: "suite",
			width: 100,
			sortable: true,
			resizable: false,
			formatter: CustomFormatters.DatasourceSuiteFormatter()
		});
		columns.push({
			id: "action",
			name: "Action",
			field: "",
			width: 210,
			sortable: true,
			resizable: false,
			align: "center",
			formatter: Soho.Formatters.Button,
			click: (_: JQueryEventObject , rowsSelected: IDataGridRow<any>[]) => {
				const item: IJsonGenericDatasource2 = rowsSelected[0].item;
				this.repositoryService.getDatasources()
					.then((datasources) => this.openTestDataserviceDialog(item))
					.catch((error: Error) => this.dialogService.showError("Could not test data service", error.message));
			},
			text: "Test Data Service",
		});

		const datagridOptions: IDataGridOptions<IJsonDatasource> = {
			columns: columns,
			dataset: [],
			selectable: SelectableEnum.Multiple,
			enableTooltips: true,
			toolbar: {
				title: "Data Services",
				results: true,
				dateFilter: false,
				keywordFilter: true,
				advancedFilter: true,
				actions: true,
				views: true,
				rowHeight: true
			}
		};
		this.datagridOptions = datagridOptions;
		const currentProject = this.editorService.getCurrentProject();
		this.datagridOptions.dataset = currentProject.getDatasourceDatabearers();
	}

	public openMapper(): void {
		this.openDataserviceWizard();
	}

	protected deleteItems(dataserviceIds: string[]): ng.IPromise<IJsonDatasource[]> {
		const selectedComponent = this.editorService.getSelectedComponent();
		const currentProject = this.editorService.getCurrentProject();

		return this.getDataserviceIdsToRemove(dataserviceIds).then(dataserviceToRemove => {
			this.removeInteractionsConnectedWithDataservice(dataserviceToRemove);

			const removedDatasources: IJsonDatasource[] = [];
			dataserviceToRemove.forEach((idToDelete: string) => {
				this.editorService.getCurrentRootComponentInstance().forEachChild((component: IEditorComponent) => {
					if (component.datasource && component.datasource.id === idToDelete) {
						component.removeDatasource();
					}
				}, false);
				removedDatasources.push(currentProject.removeDatasource(idToDelete));
			});
			this.datagridOptions.dataset = currentProject.getDatasourceDatabearers();
			if (selectedComponent.isGenerated) {
				// Quickfix to solve that the generated are regenerated
				this.editorService.deselectComponent();
			} else {
				// To update UI
				this.editorService.setSelectedComponent(selectedComponent);
			}
			return removedDatasources;
		});

	}

	private removeInteractionsConnectedWithDataservice(selectedDataserviceIds: string[]): void {
		const interactions = this.editorService.getCurrentProject().getInteractions();

		selectedDataserviceIds.forEach(dataserviceId => {
			const components = this.getComponentsUsingDataservice(dataserviceId);
			components.forEach(component => {
				interactions.forEach(interaction => {
					if (interaction.targetName === component.getId()) {
						this.interactionManagementService.removeInteraction(interaction);
					}
				});
			});
		});
	}

	private getDataserviceIdsToRemove(selectedDataserviceIds: string[]): ng.IPromise<string[]> {
		const idsToRemove: string[] = [];
		const promises: ng.IPromise<void>[] = [];

		for (let index = 0; selectedDataserviceIds.length > index; index++) {
			const dataserviceId = selectedDataserviceIds[index];
			const componentsUsingDataservice = this.getComponentsUsingDataservice(dataserviceId);

			if (componentsUsingDataservice.length > 0) {
				const dataservice = this.editorService
					.getCurrentProject()
					.getDatasources()[dataserviceId];
				promises.push(this.promptConfirmWhenDataserviceUsed(
					dataservice,
					componentsUsingDataservice.length)
					.then(dialogResult => {
						if (dialogResult.button === DialogButtonType.Yes) {
							idsToRemove.push(dataserviceId);
						}
					}));
			} else {
				idsToRemove.push(dataserviceId);
			}
		}

		// const dataserviceUsed = idsToDelete.find(dataserviceId => selectedComponent.getDatasource().id === dataserviceId);
		return this.editorService.onAllPromises(promises)
			.then(() => idsToRemove);
	}

	private getComponentsUsingDataservice(dataserviceId: string): IComponent[] {
		return this.editorService
			.getAllComponents()
			.filter(component => component.getDatasource() &&
				component.getDatasource().id === dataserviceId);
	}

	private promptConfirmWhenDataserviceUsed(
		dataservice: IJsonDatasource,
		numberOfComponentsUsedIn: number
	): ng.IPromise<IDialogResult<void>> {
		return this.dialogService.showYesNo(
			"Warning",
			`The data service "${dataservice.displayName}"
			is used by ${numberOfComponentsUsedIn}
			component${numberOfComponentsUsedIn > 1 ? "s" : ""}.
			Do you want to remove it?`,
			"warning-text");
	}

	private openTestDataserviceDialog(
		dataserviceToTest: IJsonDatasource
	): ng.IPromise<IDialogResult<string>> {
		const options: IDialogOptions<ITestDataserviceParameters> = {
			title: "Test Data Service",
			template: require("../../../../common/dialogs/test-dataservice/test-dataservice-dialog.html"),
			parameter: {
				dataservice: dataserviceToTest as IJsonGenericDatasource2,
				type: TestDataserviceDialogType.UNFORMATTED,
			},
		};
		return this.dialogService.show<ITestDataserviceParameters, string>(options);
	}

	// TODO: make shared functon, same functionality in datasource-tab-ctrl.ts
	private openDataserviceWizard(
		datasource?: IJsonGenericDatasource2): ng.IPromise<IDialogResult<IJsonDatasource>> {
		const title = (datasource ? "Edit" : "Create") + " Data Service";
		let template: string = "<eir-wizard ng-controller='eirDatasourceWizardCtrl as wizardCtrl'></eir-wizard>";
		if (datasource) {
			switch (datasource.typedef) {
				case DatasourceTypeEnum.IDO:
					template = "<eir-wizard ng-controller='eirLocalIdoDataserviceWizardCtrl as wizardCtrl'></eir-wizard>";
					break;
				case DatasourceTypeEnum.INLINE_LIST:
					template = "<eir-wizard ng-controller='eirInlineListDataserviceWizardCtrl as wizardCtrl'></eir-wizard>";
					break;
				default:
					break;
			}
		}

		const options: IDialogOptions<IDatasourceWizardParameter> = {
			title: title,
			parameter: {
				datasource: angular.copy(datasource)
			},
			template,
			cssClass: "wizard-dialog large",
			id: "datasource-wizard"
		};

		return this.dialogService.show<IDatasourceWizardParameter, IJsonDatasource>(options).then((result) => {
			if (result.button === DialogButtonType.Ok) {
				if (datasource) {
					this.updateDatasource(result.value);
				} else {
					this.addDatasource(result.value);
				}
			}
			return result;
		});
	}

	public createLocalDatasource(isSearch?: boolean): void {
		const actionTitle = "Create from Wizard";
		const project = this.editorService.getCurrentProject();
		const options: IDialogOptions<IUpsertDialogParameter> = {
			title: actionTitle,
			parameter: {
				datasources: project.getDatasourceDatabearers(),
				datasource: null,
				isEdit: false
			},
			cssClass: "select-source-modal",
			id: "dataservice-search-wizard"
		};
		if (Helper.exceedAllowed(ProjectLimit.MAX_DATASOURCES, project.getDatasourceDatabearers())) {
			this.dialogService.showError(actionTitle, ProjectLimit.DATASOURCE_ERROR_MESSAGE);
		} else {
			if (isSearch) {
				options.template = require("../../../repository/datasource/create-from-wizard/select-source.html"),
				this.dialogService.show(options)
					.then((result: IDialogResult<IJsonDatasource>) => {
						if (result.button === DialogButtonType.Ok && result.value) {
							this.addDatasource(result.value);
						}
					});
			}
		}
	}

	private populateUserInformation(datasource: IJsonDatasource, isNew?: boolean): void {
		if (isNew) {
			datasource.id = Helper.generateUniqueId();
			datasource.author = Config.USER.name;
			datasource.authorDisplayName = Config.USER.displayName;
			datasource.creationDate = Date.now();
		}
		datasource.changedBy = Config.USER.name;
		datasource.changedByDisplayName = Config.USER.displayName;
		datasource.changedDate = Date.now();
	}

	// Add from wizard or BI-create
	private addDatasource(datasource: IJsonDatasource): void {
		this.populateUserInformation(datasource, true);

		this.addItemsToProject([datasource]);
		this.datagridOptions.dataset.push(datasource);

		this.save();
	}

	private updateDatasource(datasource: IJsonDatasource): void {
		//The updated datasource should be the selected
		this.selectedItems[0] = datasource;
		this.populateUserInformation(datasource);
		// addDatasource will overwrite old one with same id
		this.addItemsToProject([datasource]);
		this.editorService.updateDatasourceOnComponents(datasource);

		const selectedComponent = this.editorService.getSelectedComponent();
		if (selectedComponent.isGenerated) {
			// Quickfix to solve that the generated are regenerated
			this.editorService.deselectComponent();
		} else {
			// To update UI
			this.editorService.setSelectedComponent(selectedComponent);
		}

		this.save()
			.then(() =>
				// updates affected entries in datagrid
				this.datagridOptions.dataset = this.editorService.getCurrentProject().getDatasourceDatabearers());
	}

	public export(): void {
		const ids: string[] = this.selectedItems.map((item) => {
			return item.id;
		});
		const projectId = this.editorService.getCurrentProject().getId();
		const exportDefaultName = Helper.getFormattedDate();

		if (Config.MG_PLATFORM === MGPlatform.WEB) {
			const options: IDialogOptions<IExportDialogParams> = {
				title: "Export Data Services",
				template: require("../../../../common/dialogs/export/export-dialog.html"),
				parameter: {
					type: "dataservice",
					filename: exportDefaultName,
				}
			};

			this.dialogService.show(options).then((result: IDialogResult<string>) => {
				if (result.button === DialogButtonType.Ok) {
					const fileName = result.value;
					// Saving first because the current dataset may not be present on server
					this.save().then(() => {
						this.projectService.exportProjectContent(
							projectId, ids, fileName, SaveFlagsEnum.DATASOURCES);
					});
				}
			});
		}
	}

	protected save(): ng.IPromise<void> {
		return this.editorService.saveCurrent(
			SaveFlagsEnum.DATASOURCES + SaveFlagsEnum.MANIFEST + SaveFlagsEnum.INTERACTIONS);
	}

	public editDatasource(): void {
		const selectedDatasource: IJsonDatasource = this.selectedItems[0];
		const project = this.editorService.getCurrentProject();
		// Only accept generic version 2
		const version: number = 2;
		const isCorrectVersion = selectedDatasource.version === version;
		const typedef = selectedDatasource.typedef;

		if (isCorrectVersion &&
			(typedef === DatasourceTypeEnum.generic ||
				typedef === DatasourceTypeEnum.IDO ||
				typedef === DatasourceTypeEnum.MONGOOSE ||
				typedef === DatasourceTypeEnum.INLINE_LIST)
		) {
			this.openDataserviceWizard(selectedDatasource as IJsonGenericDatasource2);
		} else { // Fallback code, should not get here
			this.dialogService.showMessage({
				title: "Edit unavailable",
				message:
					"Edit of old Data Service of this type is not available." +
					"Please recreate your data service in new Data Service wizard.",
				standardButtons: StandardDialogButtons.Ok
			});
		}
	}

	protected getItemDisplayName(item: IJsonDatasource): string {
		return item.displayName;
	}

	protected getItemId(item: IJsonDatasource): string {
		return item.id;
	}

	protected setItemId(item: IJsonDatasource, newId: string): void {
		item.id = newId;
	}

	public static add(m: ng.IModule): void {
		m.controller("eirManageDatasourcesCtrl", ManageDatasourcesCtrl);
	}
}
