import { CommonModule } from "@angular/common";
import { HttpClientModule } from "@angular/common/http";
import { NgModule } from "@angular/core";
import { FormsModule } from "@angular/forms";
import { SohoComponentsModule } from "ids-enterprise-ng";

import { RemoveHostDirective } from "ng2/common/directives/remove-host.directive";

import { ButtonComponent } from "ng2/components/button/button.component";
import { ChildrenComponentContainer } from "ng2/components/containers/children-component-container.component";
import { ComponentContainer } from "ng2/components/containers/component-container.component";
import { DropdownComponent } from "ng2/components/dropdown/dropdown.component";
import { FlexboxComponent } from "ng2/components/layout-components/flexbox/flexbox.component";
import { ListComponent } from "ng2/components/list/list.component";
import { LookupComponent } from "ng2/components/lookup/lookup.component";
import { TextboxComponent } from "ng2/components/textbox/textbox.component";
import { TitlebarComponent } from "ng2/components/titlebar/titlebar.component";

import { ComponentService } from "ng2/components/services/component.service";
import { DataServiceBuilder } from "ng2/components/services/data-service.service";
import { DynamicComponentService } from "ng2/components/services/dynamic-component.service";
import { FrameworkService } from "ng2/components/services/framework.service";

@NgModule({
	imports: [
		CommonModule,
		HttpClientModule,
		FormsModule,
		SohoComponentsModule,
	],

	exports: [
		ChildrenComponentContainer,
		ComponentContainer,
		ButtonComponent,
		DropdownComponent,
		FlexboxComponent,
		ListComponent,
		LookupComponent,
		TitlebarComponent,

		RemoveHostDirective,
	],

	declarations: [
		ChildrenComponentContainer,
		ComponentContainer,
		ButtonComponent,
		DropdownComponent,
		FlexboxComponent,
		ListComponent,
		LookupComponent,
		TextboxComponent,
		TitlebarComponent,

		RemoveHostDirective,
	],

	/* Needed for the dynamic load of component a.k.a. appending to DOM in Runtime
		Can be removed if Ivy is activated */
	entryComponents: [
		ButtonComponent,
		DropdownComponent,
		FlexboxComponent,
		ListComponent,
		LookupComponent,
		TextboxComponent,
	],

	providers: [
		ComponentService,
		DataServiceBuilder,
		DynamicComponentService,
		FrameworkService,
	]
})
export class ComponentsModule {

}