import { ILookupOptions } from "../../model/sohoxi/sohoxi-lookup.model";
import { Utils } from "../../sohoxi/utils";

//tslint:disable
/**
 * Lookup Directive.
 *
 * Example:
 * scope["options"]: ILookupOptions = {
 * 	field: "productId",
 * 	options: {
 * 		columns: [
 * 			{ id: 'productId', name: 'Product Id', field: 'productId', width: 140 },
 * 			{ id: 'productName', name: 'Product Name', sortable: false, field: 'productName', width: 250, formatter: (<any>window).Formatters.Hyperlink },
 * 			{ id: 'activity', hidden: true, name: 'Activity', field: 'activity', width: 125 }
 * 		],
 * 		dataset: [
 * 			{ id: 1, productId: 2142201, productName: 'Compressor', activity: 'Assemble Paint' },
 * 			{ id: 2, productId: 2241202, productName: 'Different Compressor', activity: 'Inspect and Repair' },
 * 			{ id: 3, productId: 2342203, productName: 'Third Compressor', activity: 'Inspect and Repair' }
 * 		],
 * 		selectable: 'single'
 * 	}
 * }
 * <input id="lookup-example" data-init="false" class="lookup" type="text" xi-lookup="options"/>
 */

//tslint:enable
export class LookupDirective implements ng.IDirective {
	private static readonly DIRECTIVE_NAME = "xiLookup";
	public restrict = "A";
	public scope = false;

	public link(
		scope: ng.IScope,
		element: ng.IAugmentedJQuery,
		attributes: ng.IAttributes
	): void {
		const options = attributes[LookupDirective.DIRECTIVE_NAME];

		const unbindWatcher = scope.$watch(
			options,
			(newOptions: ILookupOptions) => {
				if (newOptions) {
					element.lookup(newOptions);
				} else {
					element.lookup();
				}
			},
			true
		);

		element.on("$destroy", () => {
			const lookupData = Utils.getData(element, "lookup");
			if (lookupData) {
				lookupData.destroy();
			}
			$("body").off("open");
			unbindWatcher();
		});
	}

	public static add(m: ng.IModule): void {
		m.directive(LookupDirective.DIRECTIVE_NAME, () => {
			return new LookupDirective();
		});
	}
}
