import { OrientationEnum } from "ng2/common/enums/orientation.enum";
import { IJsonFlexbox } from "ng2/common/model/databearer/components/json-flexbox.model";
import { Flexbox } from "../../../../components/flexbox/flexbox";
import { ComponentSettingsCtrl } from "../component-settings";
import { FlexboxContentSettingsDirective } from "./flexbox-content-settings";

export class FlexboxSettings {

	private static instance: FlexboxSettings = new FlexboxSettings();

	constructor() {
		if (FlexboxSettings.instance) {
			throw new Error("Error: Instantiation failed: Use FlexboxSettings.getInstance() instead of new.");
		}
	}

	public static getInstance(): FlexboxSettings {
		return FlexboxSettings.instance;
	}

	public contentSettingsDirective: ng.IDirective = new FlexboxContentSettingsDirective();

	public create(id: string): IJsonFlexbox {
		const newFlexbox = ComponentSettingsCtrl.create(id, Flexbox) as IJsonFlexbox;
		newFlexbox.children = [];
		return newFlexbox;
	}

	/**
	 * For creating a flexbox without any padding or margin values
	 *
	 * @param {string} id
	 * @returns {IJsonFlexbox}
	 *
	 * @memberof FlexboxSettings
	 */
	public createWithoutPadding(id: string, orientation: number = OrientationEnum.VERTICAL): IJsonFlexbox {
		const defaultFlexbox = this.create(id);
		const modifiedFlexbox: IJsonFlexbox = defaultFlexbox;
		const boundarySettingsToChangeFromFlexbox = {
			margin: { bottom: 0, left: 0, right: 0, top: 0 }
		};

		modifiedFlexbox.boundarySettings.Default = {
			...defaultFlexbox.boundarySettings.Default,
			...boundarySettingsToChangeFromFlexbox,
			orientation
		};
		return modifiedFlexbox;
	}
}
