import angular from "angular";

import { IdoProperties, InjectNames, ServerPaths } from "ng2/common/constants/common.constant";
import { ParameterTypeEnum } from "ng2/common/enums/parameter-type.enum";
import { ValueTypeEnum } from "ng2/common/enums/value-type.enum";
import {
	IJsonGenericDatasource2,
	IJsonLocalIdoDatasource,
} from "ng2/common/model/databearer/dataservice/json-datasource.model";
import {
	ILocalIdoParameterObject,
	IParameterObject
} from "ng2/common/model/dataservice/parameter-object.model";
import { IMap } from "ng2/common/model/imap.model";
import { AngularConstants } from "../../common/angular-constants";
import { Helper } from "../../common/helper";
import { HttpService } from "../../common/services/http-service";

export interface ILocalIDOTestDataService extends IJsonLocalIdoDatasource {
	isTestIDO: boolean;
}

// tslint:disable-next-line:interface-name
export interface IdoProject {
	serverName: string;
	serverDesc: string;
	accessAs: string;
}

// tslint:disable-next-line:interface-name
export interface IdoCollection {
	collectionName: string;
	collectionDesc: string;
	serverName: string;
	createdBy: string;
	createDate: string;
}

// tslint:disable-next-line:interface-name
export interface IdoProperty {
	propertyName: string;
	propertyType: string;

	columnName: string;

	isReadOnly: boolean;
	isRequired: boolean;

	dataLength?: number;
	dataType: string;

	keySequence: number;
}

export class LocalIdoService {
	static $inject = [
		AngularConstants.Q,
		InjectNames.HTTP_SERVICE,
	];

	constructor(
		private $q: ng.IQService,
		private httpService: HttpService,
	) { }

	/**
	 * Returns a list of IDO projects
	 */
	public getIdoProjectList(): ng.IPromise<IdoProject[]> {
		const path = ServerPaths.LOCAL_IDO + "projects";

		return this.httpService.executeGet<IdoProject[]>(path);
	}

	/**
	 * Returns list of IDO collections
	 * @param projectName
	 * @param query
	 * @param pageNo
	 */
	public getIdoCollectionList(
		projectName?: string,
		query?: string,
		pageNo?: number
	): ng.IPromise<IdoCollection[]> {
		const path = ServerPaths.LOCAL_IDO;
		const parameters = {
			projectName,
			query,
			pageNo
		};

		return this.httpService.executeGet<IdoCollection[]>(path, parameters);
	}

	/**
	 * Returns list of properties of an IDO
	 * @param collectionName
	 */
	public getIdoProperties(collectionName: string): ng.IPromise<IdoProperty[]> {
		const path = ServerPaths.LOCAL_IDO + "properties/?collectionName=" +  collectionName;

		return this.httpService.executeGet(path);
	}

	/**
	 * Returns data from an IDO collection
	 * @param dataservice
	 */
	public getIdoCollection(
		dataservice: IJsonLocalIdoDatasource,
		inputParameterValues?: IMap<string>
	): ng.IPromise<any[]> {
		const collectionName = dataservice.resource;
		const recordCap = dataservice.recordCap.limit;
		const orderBy = dataservice.orderBy === "Default Order" ? "" : dataservice.orderBy;
		const filterQuery = [];
		const parsedParameters = this.parseParameters(inputParameterValues, dataservice.inputParameters);
		let parameters = angular.copy(dataservice.outputParameters);

		if (!(dataservice as ILocalIDOTestDataService).isTestIDO) {
			parameters = this.initParameters(parameters);
		}

		for (const key in parsedParameters) {
			filterQuery.push(`${key} = '${parsedParameters[key]}'`);
		}

		let path = `${ServerPaths.LOCAL_IDO}collection/?
			collectionName=${collectionName}
			${filterQuery.length === 0 ? "" : `&filter=${encodeURIComponent(filterQuery.join(" AND "))}`}
			${Helper.isUndefinedOrNull(recordCap) ? "" : `&recordCap=${recordCap}`}
			${Helper.isUndefinedOrNull(orderBy) ? "" : `&orderBy=${orderBy}`}`;

		path = Helper.cleanTemplateLiteral(path);
		return this.httpService.executeGet(path, parameters);
	}

	/**
	 * Updates an IDO Collection entry
	 * @param dataservice
	 * @param inputParameterValues
	 */
	public updateIdoCollectionEntry(
		dataservice: IJsonGenericDatasource2,
		inputParameterValues?: IMap<string>
	): ng.IPromise<any[]> {
		const parsedParameters =
			this.parseParameters(inputParameterValues, dataservice.inputParameters);

		return this.isValidUpsertRequest(parsedParameters)
			.then(() => {
				const collectionName = dataservice.resource;

				let path = `${ServerPaths.LOCAL_IDO}collection/?
					collectionName=${collectionName}`;
				path = Helper.cleanTemplateLiteral(path);

				return this.httpService.executePut(path, JSON.stringify(parsedParameters));
			});
	}

	/**
	 * Inserts an IDO Collection entry
	 * @param dataservice Inserts an entry to an IDO Collection
	 * @param inputParameterValues
	 */
	public insertIdoCollectionEntry(
		dataservice: IJsonGenericDatasource2,
		inputParameterValues?: IMap<string>
	): ng.IPromise<any[]> {
		const parsedParameters =
			this.parseParameters(inputParameterValues, dataservice.inputParameters);
		const collectionName = dataservice.resource;

		let path =
			`${ServerPaths.LOCAL_IDO}collection/?collectionName=${collectionName}`;
		path = Helper.cleanTemplateLiteral(path);

		return this.httpService.executePost(path, JSON.stringify(parsedParameters));
	}

	public deleteIdoCollectionEntry(
		dataservice: IJsonGenericDatasource2,
		inputParameterValues?: IMap<string>
	): ng.IPromise<void> {
		const parsedParameters =
			this.parseParameters(inputParameterValues, dataservice.inputParameters);
		const collectionName = dataservice.resource;

		return this.isValidUpsertRequest(parsedParameters)
			.then(() => {
				let path =
					`${ServerPaths.LOCAL_IDO}collection/?collectionName=${collectionName}`;
				path = Helper.cleanTemplateLiteral(path);

				return this.httpService.executeDelete(path, JSON.stringify(parsedParameters));
			});
	}

	/**
	 * Re-maps property names to readable
	 * @param parameterValues
	 * @param parameterNames
	 */
	private parseParameters(
		parameterValues: IMap<string>,
		parameterNames: IParameterObject[]
	): IMap<string> {
		const result = {} as IMap<string>;
		for (const key in parameterValues) {
			const targetInput = parameterNames
				.find(inputParameter => inputParameter.id === key);

			if (targetInput &&
				!Helper.isUndefinedOrNull(parameterValues[key])
			) {
				result[targetInput.key] = parameterValues[key];
			}
		}
		return result;
	}

	/**
	 * Add rowpointer temporary if not added as output-parameter
	 * used as a data id for keep selection
	 * @param parameters
	 */
	private initParameters(parameters: ILocalIdoParameterObject[]): ILocalIdoParameterObject[] {
		if (!parameters.some(item => item.key === IdoProperties.ROW_POINTER)) {
			const rowPointer = {
				id: "id",
				key: IdoProperties.ROW_POINTER,
				valueType: ValueTypeEnum.STRING,
				parameterType: ParameterTypeEnum.QUERY,
				required: false,
				alias: ""
			};
			parameters.push(rowPointer);
		}
		return parameters;
	}

	private isValidUpsertRequest(parsedParameters: IMap<string>): ng.IPromise<void> {
		const deferred = this.$q.defer<void>();
		const isUndefinedEmpty = Helper.isUndefinedEmpty;

		if (isUndefinedEmpty(parsedParameters[IdoProperties.ITEM_ID]) &&
			(isUndefinedEmpty(parsedParameters[IdoProperties.RECORD_DATE]) &&
				isUndefinedEmpty(parsedParameters[IdoProperties.ROW_POINTER]))) {
			deferred.reject(new Error(`
			Update request must include the ItemId property, or the RecordDate and RowPointer properties.`));
		} else {
			deferred.resolve();
		}

		return deferred.promise;
	}

	static add(module: ng.IModule): void {
		module.service(InjectNames.LOCAL_IDO_SERVICE, LocalIdoService);
	}
}