import { AngularConstants } from "common/angular-constants";

import type { IBoundary } from "ng2/common/model/project/boundary.model";
import type { IEditorComponent } from "common/interface/ieditor-component";

import { InjectNames, Numbers, ProjectLimit, SaveFlagsEnum } from "ng2/common/constants/common.constant";
import { STANDARD_ID_PREFIX } from "ng2/common/constants/standard-boundaries.constant";
import { InstanceEvent } from "common/instance-event";

import { DialogService, IToastOptions } from "common/services/dialog-service";
import { EditorHelper } from "editor/helper";
import { EditorService } from "editor/services/editor-service";

export class BoundaryManagementService {
	private boundaryIdCounter = 0;
	newBoundaryAddedEvent = new InstanceEvent<IBoundary>();
	boundaryMovedEvent = new InstanceEvent<IBoundary>();

	static $inject = [
		AngularConstants.TIMEOUT,
		InjectNames.EDITOR_SERVICE,
		InjectNames.DIALOG_SERVICE
	];

	constructor(
		private $timeout: ng.ITimeoutService,
		private editorService: EditorService,
		private dialogService: DialogService
	) {

	}

	getBoundary(boundaryId: string): IBoundary {
		return this.getProjectBoundaries().find((boundary) => {
			return boundary.id === boundaryId;
		});
	}

	getProjectBoundaries(): IBoundary[] {
		return this.editorService.getCurrentProject().getBoundaries();
	}

	generateBoundaryId(): string {
		const newId = "boundary" + this.boundaryIdCounter++;
		const boundaries = this.getProjectBoundaries();
		for (const key in boundaries) {
			if (boundaries[key].id === newId) {
				return this.generateBoundaryId();
			}
		}

		return newId;
	}

	goToCurrentBoundaryMarker(boundary: IBoundary): void {
		this.newBoundaryAddedEvent.raise(boundary);
	}

	private alreadyExistsHere(position: number): boolean {
		const boundaries = this.getProjectBoundaries();
		return boundaries.some((boundary) => {
			return boundary.limitX === position;
		});
	}

	private showError(title: string, message: string): void {
		this.dialogService.showError(title, message);
	}

	addNewBoundaryMarker(offsetX: number, displayName?: string, description?: string): void {
		const boundaries = this.getProjectBoundaries();
		offsetX = Math.floor(offsetX); //IE needs this
		if (boundaries.length >= ProjectLimit.MAX_BOUNDARIES) {
			this.showError("Error", "Maximum number of boundaries reached, try to delete one first.");
		} else if (this.alreadyExistsHere(offsetX)) {
			this.showError("Error", "A Boundary already has this value, try to add one at a different place.");
		} else { //Can add a boundary
			const newBoundaryId = this.generateBoundaryId();
			const boundary: IBoundary = ({
				id: newBoundaryId,
				displayName: displayName ? displayName : "",
				description: description ? description : "",
				limitX: offsetX,
			});

			this.addProjectBoundary(boundary);

			this.$timeout(() => {
				this.editorService.setCurrentBoundary(boundary);
				this.newBoundaryAddedEvent.raise(boundary);
				this.editorService.saveCurrent(SaveFlagsEnum.MANIFEST).then(() => {
					const options: IToastOptions = {
						title: "New Boundary",
						message: "The boundary at position " + boundary.limitX + " successfully added",
						position: "top right",
						progressBar: true
					};
					this.dialogService.showToast(options);
				});
			}, Numbers.TEN);
		}
	}

	isBoundaryReadOnly(boundary: IBoundary): boolean {
		return boundary.id.substring(0, STANDARD_ID_PREFIX.length) === STANDARD_ID_PREFIX;
	}

	private removeBoundarySettingsFromComponents(boundaryId: string): void {
		const allComponents: IEditorComponent[] = this.editorService.getAllComponents();
		allComponents.forEach((component: IEditorComponent) => {
			component.getDatabearer().boundarySettings[boundaryId] = null;
		});
	}

	removeProjectBoundary(boundary: IBoundary): void {
		this.removeBoundarySettingsFromComponents(boundary.id);
		this.removeBoundaryMarker(boundary.id);
		this.editorService.projectBoundariesChangedEvent.raise(this.getProjectBoundaries());
	}

	private removeBoundaryMarker(boundaryId: string): void {
		const allBoundaries = this.getProjectBoundaries();
		const index: number = EditorHelper.findIndex(allBoundaries, boundaryId);
		if (index !== -1) {
			allBoundaries.splice(index, 1);
		}
	}

	addProjectBoundary(boundary: IBoundary): void {
		this.getProjectBoundaries().push(boundary);
		this.editorService.projectBoundariesChangedEvent.raise(this.getProjectBoundaries());
	}

	updateBoundaryMarker(boundary: IBoundary): void {
		const allBoundaries = this.getProjectBoundaries();
		for (let i = 0; i < allBoundaries.length; i++) {
			if (allBoundaries[i].id === boundary.id) {
				allBoundaries[i] = boundary;
				this.editorService.projectBoundariesChangedEvent.raise(this.getProjectBoundaries());
				return;
			}
		}
	}

	static add(ngModule: ng.IModule): void {
		ngModule.service(InjectNames.BOUNDARY_MANAGEMENT_SERVICE, BoundaryManagementService);
	}
}
