import { IMap } from "ng2/common/model/imap.model";
import { IDocument } from "ng2/common/model/swagger.model";

/**
 * Various utility functions, exposed as static methods.
 */
export class ComponentHelper {

	/**
	 * Transform a Swagger document such that every $ref is replaced by the object it is referencing.
	 *	TODO: Should use a proper parser/validator for JSON-schema.
		*/
	public static dereferenceSwagger(doc: IDocument): IDocument {
		// Clone the document to avoid side-effects. (SLOW! Wait for Object.assign support?)
		const copy: IDocument = JSON.parse(JSON.stringify(doc));

		ComponentHelper.mapObject(copy, (key, value) => {
			if (key === "$ref") {
				const {prop, propName} = ComponentHelper.lookupRef(value, copy);
				if (prop) {
					return { key: propName, value: prop };
				} else {
					console.warn(`Could not dereference ${value}`);
					return null;
				}
			} else {
				return null;
			}
		});

		return copy;
	}

	/**
	 * Transform an object by calling the mapFunc on every property.
	 *
	 *	NOTE: Will modify the given object.
		* @param obj
		* @param mapFunc
		*/
	public static mapObject(obj: IMap<any>, mapFunc: (k: string, value: any) => any): void {
		for (const propName in obj) {
			if (obj.hasOwnProperty(propName)) {
				if (typeof obj[propName] === "object") {
					ComponentHelper.mapObject(obj[propName], mapFunc);
				}
				const newProp = mapFunc(propName, obj[propName]);
				if (newProp !== null) {
					// TODO: Temporary workaround for swagger schema. REMOVEME
					for (const k in newProp.value) {
						obj[k] = newProp.value[k];
					}
					// obj[newProp.key] = newProp.value
					delete (obj[propName]);
				}
			}
		}
	}

	/**
	 * Look up a JSON-schema reference ('$ref') and return the object it is referencing.
	 * @param path A JSON-schema reference, e.g '#/objectroot/deep/property'
	 * @param obj The object to search for the reference.
	 * @param upperPropName Name of the returned parent object.
	 */
	public static lookupRef(path: string, obj: IMap<any>, upperPropName?: string): { prop: any, propName: string } {
		path = path.replace(/^#/, "");	// Remove #/
		const regex = /\/?[^\/]+\/?/;		// Match e.g [/herp/]derp
		const matches = path.match(regex);
		if (matches) {
			const firstMatch = matches[0];	// /herp/
			const restOfPath = path.slice(firstMatch.length);	// 'derp'
			const propName = firstMatch.replace(/\//g, "");	// 'herp'

			return ComponentHelper.lookupRef(restOfPath, obj[propName], propName);
		} else {
			return { prop: obj, propName: upperPropName };
		}
	}
}
