import { ChartTypeEnum } from "ng2/common/constants/common.constant";
import { IJsonComponent, IJsonComponentSettings } from "ng2/common/model/databearer/components/json-component.model";
import { IJsonValueSource } from "ng2/common/model/databearer/json-binding.model";
import { IMap } from "ng2/common/model/imap.model";

export interface IJsonChart extends IJsonComponent {
	title: IJsonValueSource;
	showLabel: boolean; // Deprecated
	axisCategoryTitle: IJsonValueSource;
	axisValuesTitle: IJsonValueSource;
	datasets: IJsonChartDataset[];
	content: IJsonValueSource;
	boundarySettings: IMap<IJsonChartBoundarySettings>;
	categories: IJsonChartCategories[];  // backwards compatibility, legacy format
	series: IJsonChartSerie[];  // backwards compatibility, legacy format
}

export interface IJsonChartDataset {
	name: string;
	categories: string;
	series: string;
}

export interface IJsonChartBoundarySettings extends IJsonComponentSettings {
	chartType: ChartTypeEnum;
	legendDisplay: boolean;
	startupAnimationEnable: boolean;
	showLabel: boolean;
}

//#region backwards compatibility, legacy formats
export interface IJsonChartContent {
	name: string;
	field: string;
}

export interface IJsonChartCategories extends IJsonChartContent {
}

export interface IJsonChartSerie extends IJsonChartContent {
	chartType: ChartTypeEnum;
}
//#endregion
