/**
 * Tooltip Directive
 */

import has from "lodash-es/has";

import * as xi from "../../model/sohoxi/sohoxi-tooltip.model";
import { Utils } from "../../sohoxi/utils";

export class TooltipDirective implements ng.IDirective {
	private static readonly DIRECTIVE_NAME = "xiTooltip";
	public restrict = "A";
	public scope = false;

	public link(
		scope: ng.IScope,
		element: ng.IAugmentedJQuery,
		attributes: ng.IAttributes
	): void {
		let isInstantiated = false;
		if (has(attributes, "ngDisabled")) {
			const content = attributes.xiTooltip.replace(/^\'+|\'+$/g, "");
			element.append(
				`<div class="disabled-tooltip" title="${content}"></div>`
			);
		}

		const options = attributes[TooltipDirective.DIRECTIVE_NAME];
		const unbindWatcher = scope.$watch(
			options,
			newValue => {
				if (typeof newValue === "string") {
					if (newValue.length === 0) {
						// Empty tooltips should not be shown so destroy the plugin
						if (isInstantiated) {
							Utils.getData(element, "tooltip").destroy();
							isInstantiated = false;
						}
						return;
					} else {
						if (!isInstantiated) {
							/*
								Timeout is required because when xi-tooltip is used together with xi-toolbar
								and if tooltip is initialized before toolbar then toolbar overrides tooltip.
							*/
							setTimeout(() => {
								element.tooltip({
									content: newValue
								});
								//tslint:disable-next-line
							}, 100);

							const disabledTooltip = element.find("div[title]");
							if (disabledTooltip.length !== 0) {
								disabledTooltip.tooltip();
							}
							isInstantiated = true;
						} else {
							const newOptions = {
								content: newValue
							};

							const api = Utils.getData(element, "tooltip");
							api.updated(newOptions);

							const disabledTooltip =
								element.find("div.disabled-tooltip");
							if (disabledTooltip.length === 1) {
								disabledTooltip
									.data("tooltip")
									.updated(newOptions);
							}
						}
					}
				} else if (typeof newValue === "undefined" || newValue === null) {
					// Empty tooltips should not be shown so destroy the plugin
					if (isInstantiated) {
						Utils.getData(element, "tooltip").destroy();
						isInstantiated = false;
					}
					return;
				} else {
					// Instantiate plugin with tooltip value
					element.tooltip(newValue);
					isInstantiated = true;
				}
			}
		);
		// Tear down
		element.on("$destroy", () => {
			const tooltipData = Utils.getData(element, "tooltip") as xi.ITooltipApi;
			if (isInstantiated && tooltipData) {
				tooltipData.destroy();
			}
			unbindWatcher();
		});
	}

	public static add(m: ng.IModule): void {
		m.directive(
			TooltipDirective.DIRECTIVE_NAME,
			() => {
				return new TooltipDirective();
			});
	}
}
