import angular from "angular";
import isArray from "lodash-es/isArray";

import {
	ABFormScriptMethod,
	Config,
	InjectNames,
	Subcodes,
} from "ng2/common/constants/common.constant";

import { HttpVerbEnum } from "ng2/common/enums/http-verb.enum";

import type { IMap } from "ng2/common/model/imap.model";

import { AngularConstants } from "common/angular-constants";
import { Debugger } from "common/debugger";
import { ErrorResponse, IErrorResponse } from "common/error-response";
import { WSForm } from "etc-assets/user-control-es";

import { Helper } from "common/helper";

export interface IMashupRestResponse<DataType> {
	data?: DataType;
	success: boolean;
	error?: IErrorResponse;
}

export class HttpService {
	private overrideUrl: string;

	static $inject = [
		AngularConstants.HTTP,
		AngularConstants.Q
	];

	constructor(
		private http: ng.IHttpService,
		private q: ng.IQService
	) { }

	getUrl(path?: string): string {
		if (path) {
			return this.getBaseUrl() + Config.REST_ROOT + path;
		}
		return this.getBaseUrl() + Config.REST_ROOT;
	}

	setOverrideUrl(url: string): void {
		this.overrideUrl = url;
	}

	/**
	 * Execute a GET request
	 * @param resource URL resource path
	 * @param params optional parameters to be sent in the request
	 * @param external pass true if GET request not goes to Mongoose internally
	 */
	executeGet<T>(resource: string, params?: any, external?: boolean): ng.IPromise<T> {
		return this.execute<T>(resource, HttpVerbEnum.GET, null, params, external);
	}

	executePost<T>(resource: string, data?: any, params?: any): ng.IPromise<T> {
		return this.execute<T>(resource, HttpVerbEnum.POST, data, params);
	}

	executePut<T>(resource: string, data: any, params?: any): ng.IPromise<T> {
		return this.execute<T>(resource, HttpVerbEnum.PUT, data, params);
	}

	executeDelete(resource: string, params?: any): ng.IPromise<void> {
		return this.execute<void>(resource, HttpVerbEnum.DELETE, null, params);
	}

	executeRequest<T>(requestConfig: ng.IRequestConfig): ng.IPromise<ng.IHttpPromiseCallbackArg<T>> {
		this.debug("Executing " + requestConfig.method + " " + requestConfig.url);
		const deferred: ng.IDeferred<ng.IHttpPromiseCallbackArg<T>> = this.q.defer();
		// Special case for the ION API Token
		if (!DEV_SERVER &&
			(requestConfig.url.indexOf(Config.REST_ROOT + "dev/token") === 0 ||
				requestConfig.url.indexOf("/grid/rest/security/sessions/oauth") === 0)) {
			WSForm.call({
				name: ABFormScriptMethod.TOKEN,
				parameters: [JSON.stringify(requestConfig)],
				onCompleted: (response) => {
					//const response: T = JSON.parse(responseJson);
					//deferred.resolve(response.data);
					const httpResponse: ng.IHttpPromiseCallbackArg<T> = {
						data: response
					} as any;
					if (!Helper.isUndefinedEmpty(response)) {
						deferred.resolve(httpResponse);
					} else {
						const error = new ErrorResponse(
							`Could not retrieve ION token. ${Subcodes.CONTACT_SYSTEM_ADMIN}`,
							Subcodes.GENERIC_ERROR);
						deferred.reject(error);
					}
				}
			});

			return deferred.promise;
		}

		return this.executeHttp<T>(requestConfig);
	}

	upload<T>(
		resource: string,
		files: File[],
		formFields?: IMap<string>): ng.IPromise<ng.IHttpPromiseCallbackArg<IMashupRestResponse<T>>> {

		const deferred = this.q.defer<ng.IHttpPromiseCallbackArg<IMashupRestResponse<T>>>();
		const url = this.getBaseUrl() + Config.REST_ROOT + resource;
		const formData = new FormData();

		for (let i = 0; i < files.length; i++) {
			formData.append("file", files[i]);
		}

		if (formFields) {
			for (const key in formFields) {
				// Dont send null since it will be interpreted as the string "null" by the server
				if (formFields[key] !== null) {
					formData.append(key, formFields[key]);
				}
			}
		}

		const params = {
			// Let the browser decide Content-Type
			headers: ({ "Content-Type": undefined } as any),
			transformRequest: angular.identity
		};

		this.http.post<IMashupRestResponse<T>>(encodeURI(url), formData, params).then((httpResponse) => {
			this.debug("[HttpService] Completed ", httpResponse);
			deferred.resolve(httpResponse);
		}).catch((httpResponse: ng.IHttpPromiseCallbackArg<IMashupRestResponse<void> | any>) => {
			let errorPayload: IMashupRestResponse<void> = httpResponse.data;
			if (!(errorPayload && errorPayload.error)) {
				errorPayload = {
					success: false,
					error: {
						code: httpResponse.status,
						message: httpResponse.statusText,
						detail: httpResponse.statusText,
					}
				};
			}
			deferred.reject(errorPayload.error);
		});

		return deferred.promise;
	}

	/**
	 * Sequentially resolves a list of promises.
	 * @param promises
	 */
	reduce(promises: ng.IPromise<any>[]): ng.IPromise<any> {
		return this.q.all(promises);
	}

	private execute<T>(
		resource: string, method: HttpVerbEnum, data?: any, params?: any, outside?: boolean
	): ng.IPromise<T> {
		const deferred: ng.IDeferred<T> = this.q.defer();
		const url = DEV_SERVER ?
			`${ Config.REST_ROOT }/${ resource }` :
			`${ Config.REST_ROOT }${ resource }`;
		this.debug("Executing " + method + " " + url);

		const requestConfig: angular.IRequestConfig = {
			method,
			url,
			data,
			params,
			cache: false
		};

		if (DEV_SERVER) {
			this.executeHttp<T>(requestConfig).then((result) => {
				if (result.data && isArray(result.data)) {
					deferred.resolve(result.data);
				} else {
					const response = (result.data as any).data;
					deferred.resolve(response);
				}
			});
		} else {
			if (!outside) {
				WSForm.call({
					name: ABFormScriptMethod.APPBUILDER_REST,
					parameters: [JSON.stringify(requestConfig)],
					onCompleted: (rawJson) => {
						if (rawJson !== "") {
							const response: IMashupRestResponse<T> = JSON.parse(rawJson);
							if (response.success) {
								deferred.resolve(response.data);
							} else {
								deferred.reject(response.error);
							}
						} else {
							deferred.reject(`Response from '${requestConfig.url}', contains invalid JSON`);
						}
					}
				});
			} else if (outside) {
				this.executeHttp<T>(requestConfig).then((result) => {
					if (result.data) {
						deferred.resolve(result.data);
					}
				});
			} else {
				deferred.reject("Failed due to: WSForm not available.");
			}
		}

		return deferred.promise;
	}

	private executeHttp<T>(requestConfig: ng.IRequestConfig): ng.IPromise<ng.IHttpPromiseCallbackArg<T>> {
		const deferred: ng.IDeferred<ng.IHttpPromiseCallbackArg<T>> = this.q.defer();
		this.http<T>(requestConfig).then((httpResponse: ng.IHttpPromiseCallbackArg<any>) => {
			deferred.resolve(httpResponse);
		}).catch((httpResponse: ng.IHttpPromiseCallbackArg<any>) => {
			const modifiedHttpResponse = {
				...httpResponse
			};

			//Sanitize error message for veracode scan
			//https://stackoverflow.com/questions/44949254/how-to-fix-veracode-cwe-117-improper-output-neutralization-for-logs
			const logMessage = `
				[HttpService] Failed to ${requestConfig.method} ${requestConfig.url},
				Response: ${angular.toJson(modifiedHttpResponse)}
			`;
			logMessage.replace("\n", "_").replace("\r", "_");
			console.error(logMessage);
			/*
			* Check if the response status is -1. Found no documentation on this but through emperical research
			* we found that most of the times, the reason is:
			* - You're Offline
			* - The http request timed out
			* - CORS options http request failed for some reason
			*/
			if (modifiedHttpResponse.status === -1) {
				const possibleReasons = [
					"You're Offline",
					"The http request timed out",
					"CORS options http request failed for some reason"
				];
				console.error(`Status -1 was returned from angular httpService, this could mean multiple things:
					${possibleReasons.map(reason => `\n- ${reason}`).join()}
				`);
				const newStatusText = `
					This could mean multiple things:
					${possibleReasons.map(reason => `<br>- ${reason}`).join()}
				`;
				modifiedHttpResponse.statusText = modifiedHttpResponse.statusText
					? modifiedHttpResponse.statusText
					: newStatusText;
			}

			deferred.reject(modifiedHttpResponse);
		});
		return deferred.promise;
	}

	private getBaseUrl(): string {
		return (this.overrideUrl ? this.overrideUrl : "");
	}

	private debug(message: string, data?: any): void {
		if (Config.IS_DEV && Debugger.isEnabled()) {
			data ? console.debug("[HttpService] " + message, data) : console.debug("[HttpService] " + message);
		}
	}

	static add(ngModule: ng.IModule): void {
		ngModule.service(InjectNames.HTTP_SERVICE, HttpService);
	}
}
