import { AngularConstants } from "../../angular-constants";

import { InjectNames } from "ng2/common/constants/common.constant";
import { IMongooseString } from "ng2/common/model/language.model";
import { IDialogBaseScope } from "../../defines";
import { DialogButtonType, DialogService, IDialog, IDialogResult } from "../../services/dialog-service";
import { LanguageService } from "../../services/language-service";

export class AddStringCtrl {
	public stringName: string;
	public stringValue: string;
	private dialog: IDialog<string>;

	static $inject = [
		AngularConstants.SCOPE,
		InjectNames.LANGUAGE_SERVICE,
		InjectNames.DIALOG_SERVICE
	];

	constructor(
		private scope: IDialogBaseScope<string>,
		private languageService: LanguageService,
		private dialogService: DialogService
	) {
		const dialog = scope.eirDialog;
		this.dialog = dialog;
		this.stringName = this.dialog.parameter;
		this.stringValue = null;
	}

	public close(): void {
		this.dialog.close();
	}

	public add(): void {
		if (this.stringValue) {
			if (!this.languageService.getGlobalString(this.stringName)) {
				const result: IDialogResult<IMongooseString> = {
					button: DialogButtonType.Ok,
					value: {
						Name: this.stringName,
						Value: this.stringValue
					}
				};
				this.dialog.close(result);
			} else {
				this.dialogService.showError("Add String", "String Key already exists");
			}
		} else {
			this.dialogService.showError("Add String", "Must have a string Value");
		}
	}

	static add(m: ng.IModule): void {
		m.controller("eirAddStringCtrl", AddStringCtrl);
	}
}
