import angular from "angular";
import { AngularConstants } from "../../../../../common/angular-constants";

import { InjectNames, Operators } from "ng2/common/constants/common.constant";
import { IJsonExpression } from "ng2/common/model/databearer/json-expression.model";
import { IAvailableSourceField, IDialogBaseScope } from "../../../../../common/defines";
import { DialogButtonType, IDialog, IDialogResult } from "../../../../../common/services/dialog-service";
import { ISourceComponent, SourceComponent } from "../../../defines";
import { EditorService } from "../../../services/editor-service";
import { IConditionData } from "../expression-dialog";

import { Helper } from "../../../../../common/helper";

export interface IOperator {
	value: string;
	name: string;
}

export class EditExpressionDialogCtrl {
	selectedExpression: IJsonExpression;
	availableSourceFields: IAvailableSourceField[];
	availableSources: ISourceComponent[];
	availableTargets: ISourceComponent[];
	availableTargetFields: IAvailableSourceField[];
	operators: IOperator[];
	private availableComponents: ISourceComponent[];
	private dialog: IDialog<IConditionData>;

	static $inject = [
		AngularConstants.SCOPE,
		InjectNames.EDITOR_SERVICE
	];

	constructor(
		private $scope: IDialogBaseScope<IConditionData>,
		private editorService: EditorService
	) {
		const dialog: IDialog<IConditionData> = $scope.eirDialog;
		this.dialog = dialog;
		this.availableComponents = this.getAvailableComponents();
		this.setOperators();

		if (dialog.parameter.expression) {
			this.selectedExpression = angular.copy(dialog.parameter.expression);
		} else {
			// Default values for new
			this.selectedExpression = {
				id: Helper.generateUniqueId(),
				clauseOperator: Operators.EQUALS,
				source: this.availableComponents[1].id, //Application
				target: this.availableComponents[0].id, //Static
				targetProperty: "",
				usedBy: this.editorService.getSelectedComponent().getId(),
				conditionType: dialog.parameter.expressionType
			};
		}
		this.setAvailableSourceFields();
		this.setAvailableTargetFields();
	}

	save(): void {
		// TODO: stop save if static is empty
		const result: IDialogResult<IJsonExpression> = {
			button: DialogButtonType.Ok,
			value: this.selectedExpression
		};
		this.dialog.close(result);
	}

	cancel(): void {
		const result: IDialogResult<void> = {
			button: DialogButtonType.Cancel
		};
		this.dialog.close(result);
	}

	setOperators(): void {
		this.operators = [
			{
				name: "Equals",
				value: Operators.EQUALS
			}, {
				name: "Not Equals",
				value: Operators.NOT_EQUALS
			}, {
				name: "Greater than",
				value: Operators.GREATER
			}, {
				name: "Equals or Greater",
				value: Operators.SAME_GREATER
			}, {
				name: "Lower than",
				value: Operators.LOWER
			}, {
				name: "Equals or Lower",
				value: Operators.SAME_LOWER
			}, {
				name: "Contains In",
				value: Operators.INCLUDES
			}
		];
	}

	getAvailableComponents(): ISourceComponent[] {
		const availableSources = [
			SourceComponent.STATIC_VALUE_OBJECT
		];

		const components = this.editorService.getAllBindableComponents();
		components.forEach((component) => {
			availableSources.push({
				id: component.getId(),
				name: component.getDisplayName(),
				isComponent: true
			});
		});

		return availableSources;
	}

	onSourceFieldChanged(): void {
		this.setAvailableSourceFields();
		if (this.selectedExpression.source === "<static>") {
			this.selectedExpression.sourceProperty = "";
		}
	}

	onTargetFieldChanged(): void {
		this.setAvailableTargetFields();
		if (this.selectedExpression.target === "<static>") {
			this.selectedExpression.targetProperty = "";
		}
	}

	private setAvailableSourceFields(): void {
		if (this.selectedExpression.source !== "<static>") {
			const sourceComponent = this.editorService.getComponentInstanceById(this.selectedExpression.source);
			sourceComponent.getBindingMetaData().then((bindings) => {
				this.availableSourceFields = bindings;
				if (this.selectedExpression) {
					this.selectedExpression.sourceProperty =
					this.setCurrentBinding(bindings, this.selectedExpression.sourceProperty);
				}
			});
		}

		this.availableTargets = this.filterSources(this.selectedExpression.source);
	}

	private setAvailableTargetFields(): void {
		if (this.selectedExpression.target !== "<static>") {
			const targetComponent = this.editorService.getComponentInstanceById(this.selectedExpression.target);
			targetComponent.getBindingMetaData().then((bindings) => {
				this.availableTargetFields = bindings;
				if (this.selectedExpression) {
					this.selectedExpression.targetProperty =
					this.setCurrentBinding(bindings, this.selectedExpression.targetProperty);
				}
			});
		}

		this.availableSources = this.filterSources(this.selectedExpression.target);
	}

	private setCurrentBinding(bindings: IAvailableSourceField[], property: string): string {
		const foundItem = bindings.find((item) => {
			return item.sourceKey === property;
		});

		return foundItem ? foundItem.sourceKey : bindings[0].sourceKey;
	}

	private filterSources(filter: string): ISourceComponent[] {
		return angular.copy(this.availableComponents.filter((component) => {
			return component.id !== filter;
		}));
	}

	static add(m: ng.IModule): void {
		m.controller("eirEditExpressionDialogCtrl", EditExpressionDialogCtrl);
	}
}
