import { AngularConstants } from "../../angular-constants";

import { IDialogBaseScope } from "../../defines";
import { DialogButtonType, IDialog, IDialogResult } from "../../services/dialog-service";

export class ImportProjectCtrl {
	public chosenFile: FileList;

	private dialog: IDialog<FileList>;

	static $inject = [
		AngularConstants.SCOPE,
	];

	constructor(private scope: IDialogBaseScope<FileList>) {
		const dialog = scope.eirDialog;
		this.dialog = dialog;
		this.chosenFile = dialog.parameter;
	}

	public close(): void {
		this.dialog.close();
	}

	public import(): void {
		const result: IDialogResult<FileList> = {
			value: this.chosenFile,
			button: DialogButtonType.Ok
		};
		this.dialog.close(result);
	}

	static add(m: ng.IModule): void {
		m.controller("eirImportProjectCtrl", ImportProjectCtrl);
	}
}
