/**
 * Standard Control Directive.
 *
 * Used to call a standard initialize method for the specified SohoXi Control.
 * Does not support initialization with control options
 */

import { Utils } from "../../sohoxi/utils";

export class ControlDirective implements ng.IDirective {
	private static readonly DIRECTIVE_NAME = "xiControl";

	static $inject = ["$log"];

	public restrict = "A";
	public scope = false;

	constructor(
		private log: ng.ILogService,
		private timeout: ng.ITimeoutService
	) {}

	public link(
		scope: ng.IScope,
		element: ng.IAugmentedJQuery,
		attributes: ng.IAttributes
	): void {
		const functionName = attributes[ControlDirective.DIRECTIVE_NAME];
		if (!functionName) {
			return;
		}
		try {
			this.timeout(() => {
				const data = Utils.getData(element, functionName);
				if (!data) {
					switch (functionName) {
						case "expandablearea":
							element.expandablearea();
							break;
						case "toolbar":
							element.toolbar();
							break;
						case "accordion":
							element.accordion();
							break;
						case "wizard":
						default:
							element.wizard();
							break;
					}
				}
			}, 1);
		} catch (ex) {
			this.log.error(name + " Failed to call " + functionName, ex);
		}
		scope.$on("$destroy", () => {
			const data = Utils.getData(element, functionName);
			if (data) {
				try {
					data.destroy();
				} catch (exc) {
					this.log.error(name + " Failed to destroy " + functionName, exc);
				}
			}
		});
	}

	public static add(m: ng.IModule): void {
		m.directive(ControlDirective.DIRECTIVE_NAME, [
			"$log",
			"$timeout",
			(log, timeout) => {
				return new ControlDirective(log, timeout);
			}
		]);
	}
}
