import angular from "angular";
import { WSForm } from "etc-assets/user-control-es";

import {
	ABFormScriptMethod, ComponentConstants, Config, DashboardStates,
	DashboardViewStates, ElementDataKeys, InjectNames, Numbers, ReplaceEnum, Subcodes
} from "ng2/common/constants/common.constant";
import { IJsonCompositeComponent } from "ng2/common/model/databearer/components/json-composite-component.model";
import { IJsonDatasource } from "ng2/common/model/databearer/dataservice/json-datasource.model";
import { IJsonManifest } from "ng2/common/model/databearer/json-manifest.model";
import { IJsonDesignerMashup } from "ng2/common/model/databearer/json-mashup.model";
import { DesignerMashup } from "ng2/common/project/designer-mashup";
import { AngularConstants } from "../../../../common/angular-constants";
import { Debugger } from "../../../../common/debugger";
import { IErrorResponse } from "../../../../common/error-response";
import { Helper } from "../../../../common/helper";
import { DialogButtonType, DialogService, IDialogOptions, IDialogResult, IToastOptions } from "../../../../common/services/dialog-service";
import { IConflictChoice, IConflictDialogParameter } from "../../import-dialog/id-conflict-dialog";

import { CommonService } from "../../../../common/services/common-service";
import { ConflictResolutonService } from "../../../../common/services/conflict-resolution-service";
import { ProjectService } from "../../../services/project-service";

export class AllProjectsToolbarComponent {
	public state: number;
	public currentProject: DesignerMashup;
	public isProjectChanged: boolean;
	public searchString: string;
	public onLoadProjects: Function;
	public viewState: DashboardViewStates;

	public get isDeveloper(): boolean {
		return Debugger.isEnabled() && Config.IS_DEV;
	}

	private importTitle = "Import Project";

	public static $inject = [
		AngularConstants.ELEMENT,
		InjectNames.COMMON_SERVICE,
		InjectNames.PROJECT_SERVICE,
		InjectNames.DIALOG_SERVICE,
		InjectNames.CONFLICT_RESOLUTION_SERVICE,
	];

	public constructor(
		private $element: ng.IAugmentedJQuery,
		private commonService: CommonService,
		private projectService: ProjectService,
		private dialogService: DialogService,
		private conflictResolutionService: ConflictResolutonService,
	) {
		this.currentProject = commonService.getCurrentProject() as DesignerMashup;
		this.$element.data(ElementDataKeys.allProjectsToolbar, this);
	}

	public setState(): void {
		// Triggers dropdown and popover to close (soho xi)
		angular.element("body").click();
		this.state = DashboardStates.CREATE;
	}

	public importProject(): void {
		WSForm.call({
			name: ABFormScriptMethod.SHOW_OPEN_FILE_DIALOG,
			parameters: [
				"ABProject.iap", // Filename
				"Import Project",  // Caption
				"AppBuilder Project (*.iap)|*.iap" // Filter
			],
			onCompleted: (response) => response !== "-1" ?
				this.importProjectFile(response) : response
		});
	}

	private importProjectFile(
		filePath: string,
		replaceOption: ReplaceEnum = ReplaceEnum.DONOTHING,
		newDisplayName: string = null
	): void {
		this.projectService
			.importProject(filePath, replaceOption, newDisplayName)
			.then(importedProject => {
				this.onLoadProjects();
				this.validateData(importedProject);

				const options: IToastOptions = {
					title: "Imported",
					message: "Successfully imported project",
					position: "top right",
					progressBar: true,
					timeout: 3000,
				};
				this.dialogService.showToast(options);
			})
			.catch((errorResponse: IErrorResponse) => {
				this.handleImportError(
					errorResponse,
					filePath);
			});
	}

	private importIdConflict(
		conflictingManifest: IJsonManifest,
		filePath: string,
		errorResponse: IErrorResponse
	): void {
		this.commonService.loadProjects().then((projects) => {
			const importDisplayName = conflictingManifest.displayName;
			const options: IDialogOptions<IConflictDialogParameter> = {
				title: this.importTitle,
				template: require("../../import-dialog/id-conflict-dialog.html"),
				parameter: {
					message: errorResponse.message,
					oldDisplayName: importDisplayName,
					newDisplayName: Helper.getProposedNewDisplayName(
						importDisplayName + ComponentConstants.COPY_SUFFIX,
						projects
					),
					newDisplayNameOverwrite: Helper.getProposedNewDisplayName(importDisplayName, projects, conflictingManifest.id)
				}
			};

			this.dialogService
				.show(options)
				.then((result: IDialogResult<IConflictChoice>) => {
					if (result.button === DialogButtonType.Ok) {
						const option: ReplaceEnum = result.value.option;
						const newDisplayName = result.value.newDisplayName;
						this.importProjectFile(filePath, option, newDisplayName);
					}
				});
		}).catch((error: IErrorResponse) => {
			this.dialogService.showError("Did not load projects", error.message);
		});
	}

	private importDisplayNameConflict(
		conflictedManifest: IJsonManifest,
		filePath: string,
		errorResponse: IErrorResponse
	): void {
		this.commonService.loadProjects().then((projects) => {
			const options: IDialogOptions<IConflictDialogParameter> = {
				title: this.importTitle,
				template: require("../../import-dialog/displayname-conflict-dialog.html"),
				parameter: {
					message: errorResponse.message,
					oldDisplayName: conflictedManifest.displayName,
					newDisplayName: Helper.getProposedNewDisplayName(
						conflictedManifest.displayName + ComponentConstants.COPY_SUFFIX,
						projects
					)
				},
			};

			this.dialogService
				.show(options)
				.then((result: IDialogResult<string>) => {
					if (result.button === DialogButtonType.Ok) {
						const newDisplayName = result.value;
						this.importProjectFile(filePath, ReplaceEnum.COPYNEW, newDisplayName);
					}
				});
		}).catch((error: IErrorResponse) => {
			this.dialogService.showError("Did not load projects", error.message);
		});
	}

	/**
	 * Small Easter egg summer 2020
	 */
	public onSearch(): void {
		if (this.searchString.toLowerCase() === "imperial march") {
			this.searchString = "";
			const projectCards = $("eir-project-card");
			projectCards.each((_index, element) => {
				const plusOrMinus = Math.random() < Numbers.HALF ? -1 : 1;
				const pixel = plusOrMinus * Math.floor(Math.random() * Numbers.ONE_THOUSAND) + "px";
				$(element).animate({ left: pixel, top: pixel });
			});
		}
	}

	private handleImportError(errorResponse: IErrorResponse, filePath: string): void {
		if (errorResponse.subcode === Subcodes.PROJECT_ID_CONFLICT) {
			const conflictedManifest: IJsonManifest = JSON.parse(errorResponse.detail);
			this.importIdConflict(conflictedManifest, filePath, errorResponse);
		} else if (errorResponse.subcode === Subcodes.PROJECT_DISPLAYNAME_CONFLICT) {
			const conflictedManifest: IJsonManifest = JSON.parse(errorResponse.detail);
			this.importDisplayNameConflict(conflictedManifest, filePath, errorResponse);
		} else {
			this.showImportProjectErrorMessage(errorResponse.message);
		}
	}

	private validateData(project: IJsonDesignerMashup): void {
		this.conflictResolutionService.checkIfInvalidImportedItems<IJsonDatasource>(
			"data service(s)",
			project.datasources,
		).catch((errorDialogOptions) => this.dialogService.showMessage(errorDialogOptions));
		this.conflictResolutionService.checkIfInvalidImportedItems<IJsonCompositeComponent>(
			"composite(s)",
			project.compositeComponents,
		).catch((errorDialogOptions) => this.dialogService.showMessage(errorDialogOptions));
	}

	private showImportProjectErrorMessage(reasonMessage: string): void {
		const message = `Failed to import project: ${reasonMessage}`;
		this.dialogService.showError(this.importTitle, message);
	}

	public static add(m: ng.IModule): void {
		m.component("eirAllProjectsToolbar", {
			bindings: {
				state: "=",
				onLoadProjects: "&",
				searchString: "=",
				isProjectChanged: "=",
				viewState: "<"
			},
			template: require("./all-projects-toolbar.html"),
			controller: AllProjectsToolbarComponent,
			controllerAs: "ctrl",
		});
	}
}
