import angular from "angular";
import { AngularConstants } from "../../../common/angular-constants";

import { InjectNames } from "ng2/common/constants/common.constant";
import { IJsonUrlVariable } from "ng2/common/model/databearer/json-url-variable.model";
import { IDialogBaseScope } from "../../../common/defines";
import { DialogButtonType, IDialog, IDialogResult } from "../../../common/services/dialog-service";
import { IValidatorError } from "../../../common/shared-components/base/field-validation";
import { IOnComponentChangeNotification, NotifiableComponent } from "../../../common/shared-components/base/notifiable-component";

import { Helper } from "../../../common/helper";
import { EditorService } from "../services/editor-service";

export class UrlVariableDialogCtrl extends NotifiableComponent {
	public dialog: IDialog<void>;
	public variables: IJsonUrlVariable[];
	public newVariable: string = "";
	public newDescription: string = "";
	public saveDisabled: boolean;
	public hasError: boolean;
	public errorList: IValidatorError[] = [];
	public get ErrorList(): IValidatorError[] {
		// TODO: MUPS-Refactor-1:
		// Remove this part also when refactoring done
		this.hasError = this.errorList
			.filter(item => typeof item !== "boolean").length > 0;
		this.updateSaveDisabled();
		return this.errorList;
	}
	private oldVariables: IJsonUrlVariable[];

	static $inject = [
		AngularConstants.SCOPE,
		InjectNames.EDITOR_SERVICE,
		AngularConstants.TIMEOUT,
	];

	constructor(
		scope: IDialogBaseScope<void>,
		editorService: EditorService,
		private $timeout: ng.ITimeoutService
	) {
		super();

		const dialog: IDialog<void> = scope.eirDialog;
		this.dialog = dialog;
		this.variables = angular.copy(editorService.getCurrentProject().getUrlVariables());

		this.errorList = [];
		this.saveDisabled = true;
		this.hasError = false;
		this.oldVariables = angular.copy(this.variables);
	}

	public updateSaveDisabled(): void {
		const hasChange = Helper.isDifferent(this.variables, this.oldVariables);
		const value = !hasChange || this.hasError;
		if (value !== this.saveDisabled) {
			this.$timeout(() => {
				this.saveDisabled = value;
			});
		}
	}

	public addVariable(): void {
		this.variables.push({
			id: Helper.generateUniqueId(),
			name: this.newVariable,
			description: this.newDescription
		});
		this.clearInput();
		this.updateSaveDisabled();
	}

	public removeVariable(variable: IJsonUrlVariable): void {
		this.variables = this.variables.filter(oldVariable => oldVariable.id !== variable.id);
		this.updateSaveDisabled();
	}

	public saveVariables(): void {
		const result: IDialogResult<IJsonUrlVariable[]> = {
			value: this.variables,
			button: DialogButtonType.Ok
		};

		this.dialog.close(result);
	}

	public close(): void {
		this.dialog.close();
	}

	public isNameUnique(newVariable: IJsonUrlVariable): boolean {
		const isUnique = !this.variables.find(variable => {
			return variable.name === newVariable.name &&
				variable.id !== newVariable.id;
		});
		return isUnique;
	}

	public validate($data: { validator: string, id: string, value: string, callback: boolean}): boolean {
		return this.isNameUnique({id: $data.id, name: $data.value});
	}

	public getValidation($data: IOnComponentChangeNotification): boolean {
		let result: boolean;
		if ($data.validator === "validate") {
			result = this.validate($data);
		}

		return result;
	}

	private clearInput(): void {
		this.newVariable = "";
		this.newDescription = "";
	}

	static add(m: ng.IModule): void {
		m.controller("eirUrlVariableDialogCtrl", UrlVariableDialogCtrl);
	}
}
