export interface IErrorResponse {
	message: string;
	detail?: string;
	code?: number;
	subcode?: number;
	successfulItems?: string[];
	failedItems?: string[];
}

export class ErrorResponse implements IErrorResponse {
	message: string;
	detail?: string;
	code: number;

	constructor(message: string, code: number = -1, stack: string = "No-stacktrace/details") {
		this.message = message;
		this.detail = stack;
		this.code = code;
	}
}
