import cloneDeep from "lodash-es/cloneDeep";
import isEqual from "lodash-es/isEqual";

import { IComponentRendered } from "ng2/common/constants/sohoxi.constant";
import { IDataGridOptions } from "ng2/common/model/sohoxi/sohoxi-datagrid.model";

import { DatagridHelper } from "../../sohoxi/datagrid-helper";
import { Utils } from "../../sohoxi/utils";

export const DATAGRID_RENDERED = "finishedDatagridCreation";
/**
 * Data Grid Directive
 *
 * Example:
 *
 * scope["options"] = {
 * 	dataset: [
 * 		{ name: "Name #1", age: 12, phone: "12345" },
 * 		{ name: "Name #2", age: 22, phone: "1234567" },
 * 		{ name: "Name #3", age: 32, phone: "1234589" }
 * 	]
 * }
 *
 * <div xi-datagrid="ctrl.options"></div>
 */
export class DatagridDirective implements ng.IDirective {
	private static readonly DIRECTIVE_NAME = "xiDatagrid";
	public restrict = "A";
	public scope = false;

	public link(
		scope: ng.IScope,
		element: ng.IAugmentedJQuery,
		attributes: ng.IAttributes
	): void {
		const options: IDataGridOptions<any> =
			scope.$eval(attributes[DatagridDirective.DIRECTIVE_NAME]);

		if (options.dataset) {
			if (!options.columns) {
				// Generate "simple" default columns based on dataset properties, if no columns provided
				const columns = [];
				// ReSharper disable once MissingHasOwnPropertyInForeach
				for (const propertyName in options.dataset[0]) {
					const columnName =
						propertyName[0].toUpperCase() + propertyName.substring(1);
					const column = {
						id: propertyName,
						name: columnName,
						field: propertyName,
						width: 150
					};
					columns.push(column);
				}
				options.columns = columns;
			}

			element.datagrid(options);

			const api = Utils.getData(element, "datagrid");

			if (options.id !== undefined) {
				const finishedResult: IComponentRendered = {
					id: options.id,
					api
				};
				element.trigger(DATAGRID_RENDERED, finishedResult);
			}

			const isDatasetEqual = (a: any, b: any): boolean => {
				// temp fix on equality check
				const newDataset = cloneDeep(a);
				const oldDataset = cloneDeep(b);
				newDataset.forEach(DatagridHelper.clearSohoProperties);
				oldDataset.forEach(DatagridHelper.clearSohoProperties);

				return isEqual(newDataset, oldDataset);
			};

			const unbindWatcher = scope.$watch(
				attributes[DatagridDirective.DIRECTIVE_NAME],
				(
					newOptions: IDataGridOptions<any>,
					oldOptions: IDataGridOptions<any>
				) => {
					if (!isDatasetEqual(newOptions.dataset, oldOptions.dataset)) {
						api.loadData(newOptions.dataset, {
							preserveSelected: true // kept so selected is not fired
						});
					}

					if (newOptions.updateDatagrid) {
						delete newOptions.updateDatagrid;
						api.updated(newOptions);
					}
				},
				true
			);

			// Tear down
			element.on("$destroy", () => {
				unbindWatcher();
			});
		}
	}

	public static add(m: ng.IModule): void {
		m.directive(DatagridDirective.DIRECTIVE_NAME, () => {
			return new DatagridDirective();
		});
	}
}
