import { AngularConstants } from "../../../../../common/angular-constants";

import { InjectNames, Subcodes } from "ng2/common/constants/common.constant";
import { IJsonDatasource } from "ng2/common/model/databearer/dataservice/json-datasource.model";
import { ErrorResponse, IErrorResponse } from "../../../../../common/error-response";
import { InstanceEvent } from "../../../../../common/instance-event";
import { IApiSuite, IOperation } from "../../../../../common/model/ion-metadata.model";
import { ILookupOptions } from "../../../../../common/model/sohoxi/sohoxi-lookup.model";
import { IDatasourceWizardParameter } from "../../create-wizard/datasource-wizard-ctrl";
import { BaseSearchCtrl, ISearchDialogFieldScope } from "../base-search-ctrl";

import { IDocument } from "ng2/common/model/swagger.model";
import {
	DialogButtonType, DialogService, IDialog, IDialogOptions
} from "../../../../../common/services/dialog-service";
import { IonMetadataService } from "../../../../../components/services/ion-metadata-service";
import { IonApiOperationDetailsParameter } from "./ion-api-operation-details-ctrl";

export interface IIonApiDialogFieldScope extends ISearchDialogFieldScope {
	methodsFilter: (op: IOperation) => boolean;
}

export interface IResponseDatagridData {
	name: string;
	description: string;
	type: string;
	children: IResponseDatagridData[];
	depth: number;
	expanded?: boolean;
}

export class IonApiSearchCtrl extends BaseSearchCtrl {
	public $scope: IIonApiDialogFieldScope;

	public genericInstanceEvent: InstanceEvent<any>;

	public filterOptions: { method: string, displayText: string, checked: boolean }[];
	public methodOptions: ILookupOptions;

	public dropdownOptions: IApiSuite[];
	public operations: IOperation[];
	public metadataService: IonMetadataService;
	public focusedOperation: IOperation;

	static $inject = [
		AngularConstants.SCOPE,
		AngularConstants.ELEMENT,
		AngularConstants.CACHE_FACTORY,
		InjectNames.DIALOG_SERVICE,
		InjectNames.ION_METADATA_SERVICE
	];

	constructor(
		$scope: IIonApiDialogFieldScope,
		$element: ng.IAugmentedJQuery,
		$cacheFactory: ng.ICacheFactoryService,
		dialogService: DialogService,
		ionMetadataService: IonMetadataService
	) {
		super($scope, $element, $cacheFactory, dialogService, ionMetadataService);

		this.setFilterOptions();
	}

	private setFilterOptions(): void {
		this.filterOptions = [
			{ method: "get", displayText: "Get", checked: false },
			{ method: "put", displayText: "Put", checked: false },
			{ method: "post", displayText: "Post", checked: false },
			{ method: "delete", displayText: "Delete", checked: false },
			{ method: "patch", displayText: "Patch", checked: false },
			{ method: "head", displayText: "Head", checked: false },
		];
		this.$scope.methodsFilter = (operation: IOperation): boolean => {
			// TODO: Hacky way to run the filter function with correct scope...
			const allUnchecked = this.filterOptions.filter(f => f.checked).length === 0;
			let operationShouldPass = false;
			if (allUnchecked) {
				// Apply no filters
				operationShouldPass = true;
			} else {
				this.filterOptions.forEach(opt => {
					if (opt.method === operation.method && opt.checked) {
						operationShouldPass = true;
					}
				});
			}
			return operationShouldPass;
		};
	}

	public setDropdownOptions(): void {
		this.isBusy = true;
		this.metadataService.getApiSuites()
			.then(suites => {
				this.dropdownOptions = suites;
				this.dropdownOptions.unshift({ description: "All", name: "All", suiteContext: undefined, type: "" });
				this.$scope.selectedOption = this.dropdownOptions[0];
			})
			.catch(() => this.showError(new ErrorResponse(
				`Could not get suite list. ${Subcodes.CONTACT_SYSTEM_ADMIN}`, Subcodes.GENERIC_ERROR
			)))
			.finally(() => {
				this.isBusy = false;
			});
	}

	public getListItems(selectedOption: IApiSuite, query: string): ng.IPromise<any> {
		const suiteContext = selectedOption && selectedOption.suiteContext ?
			selectedOption.suiteContext : undefined;

		return this.metadataService.searchOperations(suiteContext, query, this.pageNo);
	}

	public handleRequestError(errorResponse: ng.IHttpPromiseCallbackArg<any>): void {
		let error: ErrorResponse;
		if (!(errorResponse instanceof ErrorResponse)) {
			error = new ErrorResponse(errorResponse.statusText, errorResponse.status);
		} else {
			this.showError(new ErrorResponse(
				`Could not perform search request. ${Subcodes.CONTACT_SYSTEM_ADMIN}`,
				errorResponse.code,
				errorResponse.message));
		}
		this.showError(error);
	}

	public showOperationDetails(operation: IOperation): void {
		const openDialog = (swaggerDocument: IDocument) => {
			const options: IDialogOptions<IonApiOperationDetailsParameter> = {
				title: `Operation details - ${operation.operationSummary}`,
				template: require("./ion-api-operation-details.html"),
				cssClass: "operation-details",
				parameter: {
					operation,
					swaggerDocument
				},
				buttons: [{
					text: "Close",
					click: (event: Event, modal: IDialog<void>) => {
						modal.close();
					}
				}],
				id: "dataservice-search-wizard"
			};
			this.$scope.eirDialog.hide();
			this.dialogService.show(options).then(() => {
				this.$scope.eirDialog.show();
			});
		};

		this.showSwaggerDocumentation(operation, openDialog);
	}

	private getSwaggerDocumentFromCache(operation: IOperation): IDocument {
		let cacheKey = "";
		switch (operation.suiteContext) {
			case "M3":
				cacheKey = operation.swaggerJsonFilePath;
				break;
			case "IDM":
			case "Mingle":
			default:
				cacheKey = operation.swaggerOperationId;
		}
		return this.cacheObject.get(cacheKey);
	}

	private addSwaggerDocumentToCache(
		operation: IOperation,
		swaggerDocument: IDocument
	): void {
		let cacheKey = "";
		switch (operation.suiteContext) {
			case "M3":
				cacheKey = operation.swaggerJsonFilePath;
				break;
			case "IDM":
			case "Mingle":
			default:
				cacheKey = operation.swaggerOperationId;
		}
		this.cacheObject.put(cacheKey, swaggerDocument);
	}

	private isValidSwaggerDocument(item: any): boolean {
		// IDM returns 200 OK even though it is an error.
		// More checks might needed in future
		if (typeof item === "string") {
			return item.toLowerCase().indexOf("error") !== 0;
		}

		return typeof item === "object" && item !== null;
	}

	public runCreateNewWizard(operation: IOperation): void {
		const openDialog = (swaggerDocument: IDocument) => {
			const options: IDialogOptions<IDatasourceWizardParameter> = {
				title: "Create Data Service",
				parameter: {
					swaggerDocument,
					focusedOperation: operation
				},
				template: "<eir-wizard ng-controller='eirDatasourceWizardCtrl as wizardCtrl'></eir-wizard>",
				cssClass: "wizard-dialog large",
				id: "datasource-wizard"
			};

			this.$scope.eirDialog.hide();
			this.dialogService
				.show<IDatasourceWizardParameter, IJsonDatasource>(options)
				.then(result => {
					if (result.button === DialogButtonType.Ok) {
						this.$scope.eirDialog.close(result);
					} else { // cancel
						this.$scope.eirDialog.show();
					}
				});
		};

		this.showSwaggerDocumentation(operation, openDialog);
	}

	private showSwaggerDocumentation(operation: IOperation, openDialog: Function): void {
		const swaggerDocument = this.getSwaggerDocumentFromCache(operation);
		if (swaggerDocument) {
			openDialog(swaggerDocument);
		} else {
			this.isBusy = true;
			this.metadataService.getSwaggerDoc(operation.swaggerJsonFilePath)
				.then(swaggerDocument => {
					if (this.isValidSwaggerDocument(swaggerDocument)) {
						this.addSwaggerDocumentToCache(operation, swaggerDocument);
						openDialog(swaggerDocument);
					} else if (typeof swaggerDocument === "string") {
						this.showError(swaggerDocument);
					}
				}).catch((errorResponse: IErrorResponse) => {
					this.showError(errorResponse);
				}).finally(() => {
					this.isBusy = false;
				});

		}
	}

	public showError(errorResponse: IErrorResponse | string): void {
		if (typeof errorResponse === "object" && errorResponse !== null) {
			const errorStatus = errorResponse.code;
			const errorMessage = `Status Code: ${errorStatus} <br><br>${this.getErrorMessage(errorResponse)}`;
			this.dialogService.showError("Error", errorMessage);
		} else if (typeof errorResponse === "string") {
			this.dialogService.showError("Error",  errorResponse);
		}
	}

	private getErrorMessage(errorResponse: IErrorResponse): string {
		const errorStatus = errorResponse.code;
		// tslint:disable-next-line
		const is500To599 = 500 <= errorStatus && errorStatus <= 599;
		if (is500To599) {
			return `
				Unable to retrieve available ION API suits from ION API,
				if the issue persists please contact your system administrator.
			`;
		}
		return errorResponse.message;
	}

	static add(m: ng.IModule): void {
		m.controller("eirIonApiSearchCtrl", IonApiSearchCtrl);
	}
}
