import angular from "angular";

import { EditorHelper } from "../helper";

import {
	ComponentNames,
	InjectNames,
	Numbers,
	SaveFlagsEnum,
} from "ng2/common/constants/common.constant";
import { FormatterEnum } from "ng2/common/constants/sohoxi.constant";

import { DatasourceTypeEnum } from "ng2/common/enums/datasource-type.enum";

import type { IDatasource } from "common/model/datasource/datasource.model";
import type { IArrangeEvent } from "common/model/sohoxi/sohoxi.model";
import type {
	IExtendedDataGridColumn,
	IJsonDatagrid,
	IPreviousAttributes
} from "ng2/common/model/databearer/components/json-datagrid.model";
import type { IJsonLookup } from "ng2/common/model/databearer/components/json-lookup.model";
import type { IMetaDataField } from "ng2/common/model/dataservice/meta-data-field.model";
import type { ILanguage } from "ng2/common/model/language.model";
import type { IDataGridOptions } from "ng2/common/model/sohoxi/sohoxi-datagrid.model";

import { AngularConstants } from "common/angular-constants";
import type { IAvailableSourceField } from "common/defines";
import type { IEditorComponent } from "common/interface/ieditor-component";
import type { IAutoAddColumnResponse } from "editor/component-settings/dialog/auto-add-columns-dialog";
import { SettingsBase } from "editor/component-settings/settings-base";
import type { IEditorComponentScope } from "editor/components/editor-component";
import type { IEditorFlexbox } from "editor/components/editor-flexbox";

import { Helper } from "common/helper";
import { DatagridHelper } from "common/sohoxi/datagrid-helper";
import { ComponentsHelper } from "components/helper";

import {
	DialogButtonType,
	DialogService,
	IDialogOptions,
	IDialogResult,
} from "common/services/dialog-service";
import { TranslationManagementService } from "components/services/translation-management-service";
import { EditorService } from "editor/services/editor-service";

export interface IEditorComponentWithUpsertDatagridColumnSettings extends IEditorComponent {
	parent: IEditorFlexbox;

	// Overrides to make dual extend work
	getScope(): IEditorComponentScope;
	getDatabearer(): IJsonLookup | IJsonDatagrid;
	loadOptions(column: IExtendedDataGridColumn): void;
	updateValuesWithTranslations(): void;
	refreshDatagrid(): void;
	updateColumns(): void;
	addClickEvent(column: IExtendedDataGridColumn): void;
	addHyperlinkEvent(column: IExtendedDataGridColumn): void;
	toggleSelected(): void;
	toggleMultiSelection(): void;
	previousAttributes: IPreviousAttributes;
	datagridOptions: IDataGridOptions<any>;
}

export interface IUpsertOption {
	sourceFields: IAvailableSourceField[];
	isAdd?: boolean;
	column?: IExtendedDataGridColumn;
	component: IEditorComponentWithUpsertDatagridColumnSettings;
}

/**
 * Extend this class if component has upsert properties to data grid columns in its content settings
 * E.g. Lookup, and Data Grid component
 */
export abstract class UpsertDatagridColumnContentSettings extends SettingsBase {
	static $inject = [
		AngularConstants.SCOPE,
		InjectNames.EDITOR_SERVICE,
		InjectNames.DIALOG_SERVICE,
		InjectNames.TRANSLATION_MANAGEMENT_SERVICE
	];

	protected htmlDecode = Helper.htmlDecode;

	protected selectedComponent: IEditorComponentWithUpsertDatagridColumnSettings;

	protected abstract addTitle: string;
	protected abstract editTitle: string;

	unsubscribers: Function[] = [];
	get Columns(): IExtendedDataGridColumn[] {
		return this.selectedComponent.getDatabearer().columns;
	}

	constructor(
		$scope: ng.IScope,
		editorService: EditorService,
		protected dialogService: DialogService,
		protected translationManagementService: TranslationManagementService
	) {
		super($scope, editorService);

		this.unsubscribers.push(
			editorService.selectedDatasourceChangedEvent.on(datasource => {
				this.refreshMetaSettings(datasource);
			}),
			this.translationManagementService.onTranslationsChangedEvent.on(
				() => {
					if (this.selectedComponent) {
						this.selectedComponent.updateValuesWithTranslations();
					}
				})
		);
	}

	onArrange(event: IArrangeEvent): void {
		const databearer = this.selectedComponent.getDatabearer();
		EditorHelper.moveByIndex(databearer.columns, event.startIndex, event.endIndex);
		this.refreshComponent();
		this.save();
	}

	onAddColumn(): void {
		const column: IExtendedDataGridColumn = {
			id: "",
			name: "",
			sortable: true,
			resizable: true,
			editable: false,
			summarize: false,
			textOverflow: "ellipsis",
		} as IExtendedDataGridColumn;
		this.addColumn(column);

		if (this.selectedComponent.getDatasource()) {
			this.openUpsertDialog(column, true).then(
				(dialogResult: IDialogResult<IExtendedDataGridColumn>) => {
					if (dialogResult.button === DialogButtonType.Ok) {
						this.editColumn(dialogResult.value);
						this.save();
					} else {
						this.deleteColumn(column, false);
					}
				}
			);
		}
	}

	onEditColumn(column: IExtendedDataGridColumn): void {
		this.openUpsertDialog(column).then(
			(dialogResult: IDialogResult<IExtendedDataGridColumn>) => {
				if (dialogResult.button) {
					switch (dialogResult.button) {
						case DialogButtonType.Ok:
							this.editColumn(dialogResult.value);
							break;
						default:
							console.warn("Invalid option");
					}
					this.save();
				}
			}
		);
	}

	deleteColumn(column: IExtendedDataGridColumn, save = true): void {
		const databearer = this.selectedComponent.getDatabearer();
		const columns: IExtendedDataGridColumn[] = databearer.columns;
		const index = columns.findIndex(item => item.id === column.id);
		if (index > -1) {
			columns.splice(index, 1);
		}
		this.refreshComponent();
		this.translationManagementService.removeTranslation(
			databearer.id,
			column.id
		);

		if (column.text) {
			this.translationManagementService.removeTranslation(databearer.id, column.id + "CellText");
		}

		if (save) {
			this.save();
		}
	}

	refreshMetaSettings(dataservice: IDatasource): ng.IPromise<void> {
		const newDeferred = this.editorService.getNewDeferred<void>();

		if (dataservice) {
			const resultPromise = dataservice.getMetaData().then(metadata => {
				this.addColumns(metadata.outputFields, true, dataservice.typedef);
			});
			newDeferred.resolve(resultPromise);
		}

		return newDeferred.promise;
	}

	protected deleteAllColumns(): void {
		const columns = angular.copy(
			this.selectedComponent.getDatabearer().columns
		);
		if (columns.length) {
			columns.forEach(column => this.deleteColumn(column));
		}
	}

	protected addColumns(
		outputFields: IMetaDataField[],
		showAskDialog: boolean,
		typedef?: DatasourceTypeEnum
	): void {
		if (showAskDialog) {
			this.showAddPredefinedColumnsDialog(outputFields)
				.then((dialogResponse: IDialogResult<IAutoAddColumnResponse>) => {

					if (dialogResponse.button === DialogButtonType.Ok) {
						this.deleteAllColumns();

						const values = dialogResponse.value;
						this.autoAddColumns(values.selectedColumns, values.autoFormatColumns, typedef);
					}
					this.save();
				});
		} else {
			this.autoAddColumns(outputFields);
			this.refreshComponent();
		}
	}

	protected refreshComponent(): void {
		this.selectedComponent.updateValuesWithTranslations();
		if (this.selectedComponent.getTypedef() === ComponentNames.Datagrid) {
			this.selectedComponent.refreshDatagrid();
		}
	}

	protected save(): void {
		this.editorService.saveCurrent(SaveFlagsEnum.TRANSLATIONS);
		this.editorService.saveCanvas();
		//Columns are updated
		this.editorService.setSourceTriggerEvent.raise();
	}

	private autoAddColumns(
		outputFields: IMetaDataField[],
		autoFormatColumns?: boolean,
		typedef?: DatasourceTypeEnum,
	): void {
		outputFields.forEach(item => {
			const newColumn: IExtendedDataGridColumn = {
				id: "",
				name: item.FLDS.trim(),
				sortable: true,
				resizable: true,
				summarize: false,
				textOverflow: "ellipsis",
				field: item.FLNM,
				formatterName: autoFormatColumns ?
					this.getFormatterName(item.type, typedef) : undefined
			};

			if (autoFormatColumns) {
				DatagridHelper.setColumnSettings(
					newColumn,
					this.translationManagementService.getSelectedLocale());
			}

			this.addColumn(newColumn);
		});

		if (this.selectedComponent.getTypedef() === ComponentNames.Datagrid) {
			this.selectedComponent.toggleMultiSelection();
		}
	}

	private getFormatterName(type: string, typedef: DatasourceTypeEnum): string {
		switch (typedef) {
			case DatasourceTypeEnum.IDO:
			case DatasourceTypeEnum.generic:
				switch (type) {
					case "Boolean":
					case "Byte":
						return FormatterEnum.Checkbox;
					case "Date":
						return FormatterEnum.Date;
					case "Date Time":
						return FormatterEnum.Datetime;
					case "Email":
						return FormatterEnum.Hyperlink;
					case "Short Integer":
					case "Long Integer":
					case "Number":
						return FormatterEnum.Integer;
					case "Decimal":
						return FormatterEnum.Decimal;
					case "GUID":
					case "String":
					default:
						return undefined;
				}
			case DatasourceTypeEnum.IDM:
			case DatasourceTypeEnum.M3:
			default:
				// implement other type of data types here
				return undefined;
		}
	}

	private addColumn(newColumn: IExtendedDataGridColumn): void {
		const databearer = this.selectedComponent.getDatabearer();

		const columnIds: string[] = databearer.columns.map((column) => {
			return column.id;
		});

		const index: number = ComponentsHelper.nextNumber(columnIds);
		const newId: string = this.selectedComponent.getId() + "Column" + index;

		newColumn.id = newId;

		databearer.columns.push(newColumn);
		this.adjustColumnSizes();
		this.addToLanguageComponents(newColumn, index);
	}

	private editColumn(column: IExtendedDataGridColumn): void {
		const databearer = this.selectedComponent.getDatabearer();
		databearer.columns.forEach((ignoredColumn, index) => {
			if (column.id === ignoredColumn.id) {
				databearer.columns[index] = column;
			}
		});

		const hasTextTranslation = this.translationManagementService.hasLanguageComponent(
			databearer.id,
			column.id + "CellText"
		);

		if (!hasTextTranslation && !Helper.isUndefinedOrNull(column.text)) { //if column text is added
			this.translationManagementService.createGeneralLanguageComponent(
				databearer.id,
				column.id + "CellText",
				column.text,
				false
			);
		} else if (hasTextTranslation && !Helper.isUndefinedOrNull(column.text)) { //if column text is changed
			this.translationManagementService.setTranslatableStringByProperty(
				databearer.id,
				column.id + "CellText",
				{
					locale: this.translationManagementService.getSelectedLocale(),
					value: column.text
				} as ILanguage,
				false
			);
		} else if (hasTextTranslation && Helper.isUndefinedOrNull(column.text)) { //if column text is removed
			this.translationManagementService.removeTranslation(
				databearer.id,
				column.id + "CellText",
				false
			);
		}

		this.translationManagementService.setTranslatableStringByProperty(
			databearer.id,
			column.id,
			{
				locale: this.translationManagementService.getSelectedLocale(),
				value: column.name
			} as ILanguage
		);
		if (column.formatterName === FormatterEnum.Button) {
			this.selectedComponent.addClickEvent(column);
		} else if (column.formatterName === FormatterEnum.Hyperlink) {
			this.selectedComponent.addHyperlinkEvent(column);
		}

		// if summarize is disabled, remove the associated properties
		if (!column.summarize) {
			column.summaryText = null;
			column.summaryTextPlacement = null;
		}

		if (!column.substitute) {
			column.substituteOptions = null;
		}

		return this.refreshComponent();
	}

	private adjustColumnSizes(): void {
		const columns = this.selectedComponent.getDatabearer().columns;

		const numberOfColumns = columns.length;
		const width = (Numbers.HUNDRED / numberOfColumns).toString() + "%";
		columns.forEach(column => column.width = width);
	}

	private showAddPredefinedColumnsDialog(
		outputFields: IMetaDataField[]
	): ng.IPromise<IDialogResult<IAutoAddColumnResponse>
		> {
		const options: IDialogOptions<IMetaDataField[]> = {
			title: "Auto Add Columns",
			template: require("./dialog/auto-add-columns-dialog.html"),
			parameter: outputFields
		};

		return this.dialogService.show(options);
	}

	private openUpsertDialog(
		column?: IExtendedDataGridColumn,
		isAdd?: boolean
	): ng.IPromise<IDialogResult<IExtendedDataGridColumn | {}>> {
		const options: IDialogOptions<IUpsertOption> = {
			title: isAdd ? this.addTitle : this.editTitle,
			parameter: {
				sourceFields: [],
				column: angular.copy(column),
				isAdd,
				component: this.selectedComponent
			},
			template: require("./dialog/upsert-column.html")
		};
		if (this.selectedComponent.datasource) {
			return this.getAvailableSourceFields().then(
				(sourceFields: IAvailableSourceField[]) => {
					options.parameter.sourceFields = sourceFields;
					return this.dialogService.show(options);
				}
			);
		} else {
			return this.dialogService.show(options);
		}
	}

	private getAvailableSourceFields(): ng.IPromise<IAvailableSourceField[]> {
		return this.selectedComponent.datasource.getMetaData().then(metadata => {
			const availableSourceField: IAvailableSourceField[] = [];
			metadata.outputFields.map((outputField: IMetaDataField) => {
				availableSourceField.push({
					visibleName: outputField.FLDS.trim(),
					sourceKey: outputField.FLNM,
					sourceKeyShort: outputField.FLNM
				});
			});
			return availableSourceField;
		});
	}

	private addToLanguageComponents(column: IExtendedDataGridColumn, index: number): void {
		const databearer = this.selectedComponent.getDatabearer();
		const newId: string = column.id;
		const newName: string = column.name ? column.name : "Column " + index;
		this.translationManagementService.createGeneralLanguageComponent(
			databearer.id,
			newId,
			newName
		);

		return this.translationManagementService.setTranslatableStringByProperty(
			databearer.id,
			column.id,
			{
				locale: this.translationManagementService.getSelectedLocale(),
				value: column.name
			} as ILanguage
		);
	}
}
