import { AngularConstants } from "../common/angular-constants";
import { IChildScope } from "../common/interface/ichild-scope";
import { IChildrenScope } from "../common/interface/ichildren-scope";

import { IComponent } from "../common/IComponent";

export class ChildrenDirective implements ng.IDirective {
	public restrict = "E";
	public scope = {
		parent: "=",
		childrenReady: "&",
	};

	constructor(
		private $compileService: ng.ICompileService
	) { }

	public link($scope: IChildrenScope,
		element: ng.IAugmentedJQuery,
		attrs: ng.IAttributes
	): void {
		let nrChildrenReady = 0;
		const parentInstance = $scope.parent;
		$scope.context = parentInstance.getContext();
		$scope.onChildReady = (childInstance: IComponent, index: number) => {
			parentInstance.children[index] = childInstance;
			nrChildrenReady++;
			if (
				nrChildrenReady === parentInstance.getDatabearer().children.length
			) {
				$scope.childrenReady();
			}
		};
		$scope.$applyAsync(() => {
			if (parentInstance.getDatabearer().children.length === 0) {
				$scope.childrenReady();
			} else {
				parentInstance.getDatabearer().children.forEach((child, index) => {
					const childScope = $scope.$new() as IChildScope;
					childScope.component = child;
					childScope.parent = parentInstance;
					childScope.context = $scope.context;
					childScope.index = index;
					this.$compileService(ChildrenDirective.createTemplate(child.typedef))(
						childScope,
						(clone) => {
							element.append(clone);
						}
					);
				});
			}
		});
	}

	public static createTemplate(typedef: string): JQuery {
		const componentTemplate = $(
			"<eir-component-directive></eir-component-directive>"
		);
		componentTemplate.attr("component", "component");
		componentTemplate.attr("context", "context");
		componentTemplate.attr("parent", "parent");
		componentTemplate.attr(
			"component-ready",
			"onChildReady(componentInstance, index);"
		);
		componentTemplate.attr("generated-id", "parent.generateId(index)");
		componentTemplate.attr("typedef", typedef);
		return componentTemplate;
	}

	public static add(m: ng.IModule): void {
		const directive: ng.IDirectiveFactory = (
			$compileService: ng.ICompileService
		) => new ChildrenDirective($compileService);
		directive.$inject = [AngularConstants.COMPILE];
		m.directive("eirChildren", directive);
	}
}
