import angular from "angular";

import { DEFAULT_BOUNDARY } from "ng2/common/constants/common.constant";
import { IComponentFieldSetting } from "ng2/common/model/components/settings/component-field-setting.model";
import { IComponentSettingCategory } from "ng2/common/model/components/settings/component-setting-category.model";
import { IJsonComponent, IJsonComponentSettings, TypeDef } from "ng2/common/model/databearer/components/json-component.model";
import { IMap } from "ng2/common/model/imap.model";

import { IJsonBindableField } from "ng2/common/model/databearer/json-binding.model";
import { Helper } from "../../../common/helper";
import { IStaticComponent } from "../../../components/defines";

export class ComponentSettingsCtrl {
	public static create<T extends IJsonComponent>(id: string, componentClass: IStaticComponent): T {
		const boundarySettings: IMap<IJsonComponentSettings> = {};
		boundarySettings[DEFAULT_BOUNDARY.id] = {
			width: componentClass.DEFAULT_WIDTH,
			height: componentClass.DEFAULT_HEIGHT
		};
		const component: IJsonComponent = {
			typedef: componentClass.name as TypeDef,
			id: id,
			displayName: id,
			isReadonly: false,
			boundarySettings: boundarySettings
		};
		componentClass.BINDABLE_FIELDS_2.forEach((field: IJsonBindableField) => {
			(component as any)[field.ref] = angular.copy(field.defaultValue);
		});
		this.initiateDefaultValues(component, componentClass);

		return component as T;
	}

	private static initiateDefaultValues(component: IJsonComponent, componentClass: IStaticComponent): void {
		componentClass.SETTING_FIELDS.forEach((settingCategory: IComponentSettingCategory) => {
			settingCategory.settings.forEach((setting: IComponentFieldSetting) => {
				if (!Helper.isUndefinedOrNull(setting.defaultValue)) {
					if (setting.boundarySetting) {
						(component.boundarySettings[DEFAULT_BOUNDARY.id] as IMap<any>)[setting.ref]
							= angular.copy(setting.defaultValue);
					} else {
						(component as IMap<any>)[setting.ref] = angular.copy(setting.defaultValue);
					}
				}
			});

		});
	}
}
