import {
	ChangeDetectorRef,
	Component,
	ElementRef,
	Renderer2
} from "@angular/core";
import { SohoMessageService } from "ids-enterprise-ng";

import { AlignmentEnum, FlexSizeTypeEnum, SettingKeys } from "ng2/common/constants/common.constant";

import { OrientationEnum } from "ng2/common/enums/orientation.enum";
import { SizeTypeEnum } from "ng2/common/enums/size-type.enum";

import type { IComponentStyleProperties, ICssProperties } from "ng2/common/model/components/base/component-style.model";
import type { IComponent } from "ng2/common/model/components/base/component.model";
import type { IJsonMargin, IJsonSize } from "ng2/common/model/databearer/components/json-component.model";
import type { IJsonFlexbox } from "ng2/common/model/databearer/components/json-flexbox.model";

import { FlexboxMetadata } from "ng2/components/layout-components/flexbox/flexbox.metadata";

import { LayoutComponent } from "ng2/components/base/layout-component";

import { DynamicComponentService } from "ng2/components/services/dynamic-component.service";
import { FrameworkService } from "ng2/components/services/framework.service";

@Component({
	selector: "ng2-flexbox",
	template: `
		<div
			id="child-cage-{{ id }}"
			class="child-cage"
			[ngStyle]="inlineStyles"
		>
			<children-component-container
				[ngClass]="{ 'vertical': orientation === 2 }"
				[parent]="this"
				(onChildrenReady)="onChildrenReady($event)"
			></children-component-container>
		</div>
	`
})
export class FlexboxComponent extends LayoutComponent {

	// Style Settings
	orientation: OrientationEnum;
	sizeType: FlexSizeTypeEnum;
	size: number;

	classNames: string;
	inlineStyles: ICssProperties; // .child-cage

	hasIndividualChildSizes = true;

	databearer: IJsonFlexbox;

	constructor(
		elementRef: ElementRef,
		renderer: Renderer2,
		changeDetectorRef: ChangeDetectorRef,
		frameworkService: FrameworkService,
		dynamicComponentService: DynamicComponentService,
		sohoMessageService: SohoMessageService,
	) {
		super(
			elementRef,
			renderer,
			changeDetectorRef,
			frameworkService,
			dynamicComponentService,
			sohoMessageService,
		);
	}

	ngAfterViewInit(): void {
		// super.ngAfterViewInit();
		// console.info("ngAfterViewInit", this.children);
	}

	private getOrientationSettings(id: string): OrientationEnum {

		let orientation = this.getBoundarySetting<OrientationEnum>(SettingKeys.ORIENTATION, id);

		if (!orientation) {
			orientation = this.frameworkService.componentService
				.getDefaultComponentSettingValue<OrientationEnum>(
					FlexboxMetadata.SETTING_FIELDS,
					SettingKeys.ORIENTATION, "Orientation"
				);
		}
		return orientation;
	}

	private getAlignment(): ICssProperties {
		const horizAlignment = this.getBoundarySetting<AlignmentEnum>(SettingKeys.ALIGNMENT_HORIZONTAL);
		const verticAlignment = this.getBoundarySetting<AlignmentEnum>(SettingKeys.ALIGNMENT_VERTICAL);

		return {
			"justify-content": this.getAlignmentProperty(
				this.orientation === OrientationEnum.HORIZONTAL ? horizAlignment : verticAlignment),
			"align-items": this.getAlignmentProperty(
				this.orientation === OrientationEnum.HORIZONTAL ? verticAlignment : horizAlignment)
		};
	}

	private getAlignmentProperty(alignment: AlignmentEnum): string {
		let alignmentProperty: string;
		switch (alignment) {
			case AlignmentEnum.FLEX_START:
				alignmentProperty = "flex-start";
				break;
			case AlignmentEnum.CENTER:
				alignmentProperty = "center";
				break;
			case AlignmentEnum.FLEX_END:
				alignmentProperty = "flex-end";
				break;
			default:
				console.warn("[Flexbox] Invalid alignment:" + alignment);
		}
		return alignmentProperty;
	}

	private getPadding(): ICssProperties {
		const margin = this.getBoundarySetting<IJsonMargin>(SettingKeys.MARGIN);
		const padding: ICssProperties = {
			padding: margin.top + "px " + margin.right + "px " + margin.bottom + "px " + margin.left + "px"
		};
		return padding;
	}

	getPxPerPercentComponent(ori: OrientationEnum, percent: number): number {
		let pixels = 0;
		// Only calculate for vertical (height) since flexbox doesn't allow content to be placed horizontally yet.
		if (ori === OrientationEnum.VERTICAL) {
			this.children.forEach((child: IComponent) => {
				if (child.getBoundarySetting<boolean>(SettingKeys.VISIBILITY) &&
					child.getBoundarySetting<IJsonSize>(SettingKeys.HEIGHT).sizeType === SizeTypeEnum.AUTO) {
					pixels += child.element.outerHeight(true);
				}
			}, false);
		}
		return pixels;
	}

	updateClassNames(): void {
		this.classNames = "something";
	}

	updateInlineStyles(): void {
		this.calcNewSizes();
		this.updateChildCageInlineStyles();
		this.updateParentInlineStyles();
	}

	private updateChildCageInlineStyles(): void {

		const boundary = this.frameworkService.currentBoundary;

		this.orientation = this.getOrientationSettings(boundary.id);
		this.sizeType = this.getBoundarySetting<FlexSizeTypeEnum>(
			SettingKeys.SIZE_TYPE, boundary.id);
		this.size = this.getBoundarySetting<number>(SettingKeys.SIZE, boundary.id);

		this.inlineStyles =
			Object.assign({}, this.getAlignment(), this.getPadding());
	}

	private updateParentInlineStyles(): void {
		const generalComponentStyle = this.getInlineStyles();

		const backgroundColor =
			this.getBoundarySetting<string>(SettingKeys.BACKGROUND_COLOR);

		const componentStyleProperties: IComponentStyleProperties = Object.assign(
			{
				backgroundColor: backgroundColor === undefined ? "" : backgroundColor,
				padding: null,
				// Width and height have default values in flexbox.less
				width: "",
				height: "",
			},
			generalComponentStyle
		);

		// TODO: Padding SettingKeys are for some reason called MARGIN which is wrong fix this
		delete componentStyleProperties.margin;

		componentStyleProperties.width = "";

		if (this.sizeType === FlexSizeTypeEnum.PIXELS) {
			const value = this.size + "px";
			componentStyleProperties.flexGrow = 0;
			componentStyleProperties.flexShrink = 0;
			componentStyleProperties.flexBasis = value;
		} else {
			componentStyleProperties.flexShrink = 1;
			componentStyleProperties.flexBasis = 0;
			componentStyleProperties.flexGrow = this.size;
		}

		this.setStyles(componentStyleProperties);
	}
}
