	/**
	 * Get the Swagger operation with the same path and method as the given operation.
	 */
	public static getSwaggerOperation(operation: IOperation, swagger: IDocument): IOperationObject {
		const pathItemObj = swagger.paths[operation.restPath];
		switch (operation.method.toLowerCase()) {
			case "get":
				return pathItemObj.get;
			case "put":
				return pathItemObj.put;
			case "post":
				return pathItemObj.post;
			case "delete":
				return pathItemObj.delete;
			case "options":
				return pathItemObj.options;
			case "head":
				return pathItemObj.head;
			default:
				console.warn(`Operation has unknown method '${operation.method}', using default 'get'`);
				return pathItemObj.get;
		}
	}

	/**
	 * Extract the suite, endpoint and resource of an operation such that it can be combined to a valid path, e.g:
	 *
	 * Given an operation with {
	 * 	suiteContext: "mysuite",
	 *		proxyPath: "mytenant/mysuite/endpoint/",
		*		restPath: "/my_resource"
		* }
		* this will produce:
		* {
		*		suite: "/mysuite",
		*		endpoint: "/endpoint/",
		*		resource: "my_resource"
		* }
		* Such that it can be concatenated to an ION API request path:
		* /mysuite/endpoint/my_resource
		*/
	public static deconstructPath(operation: IOperation): {
		suite: string,
		endpoint: string,
		resource: string,
		version: string
	} {
		const customerApi = "/CustomerApi";
		// SuiteContext is always: "/suite"
		let suite = `/${operation.suiteContext}`;
		const tenant = Config.TENANT_ID || "";

		// ProxyPath is always: "tenant/suite/some_endpoint_path" or just "tenant/suite/"
		// manually added APIs looks like this "tenant/CustomerApi/suite"
		if (operation.proxyPath.indexOf(customerApi) !== -1) {
			operation.proxyPath = operation.proxyPath.replace(customerApi, "");
			suite = customerApi + suite;
		}

		const caseInsensitive = new RegExp(`${tenant}/${operation.suiteContext}`, "i");
		let endpoint = operation.proxyPath.replace(caseInsensitive, "");

		if (!endpoint.endsWith("/")) {
			endpoint = `${endpoint}/`;
		}
		// RestPath is a swagger path key, so "/some/path"
		const resource = operation.restPath.replace(/^\//, "");

		// determine version, currently we only check M3, all else is v1
		let version = "v1";
		if (endpoint.includes("/m3api-rest/v2/")) {
			version = "v2";
		}

		return { suite, endpoint, resource, version };
	}

	/**
	 * Transform a Swagger schema (e.g response) to something that can be used in a Responses data grid
	 */
	public static mapSchemaToGridData(schema: ISchemaObject, name: string, depth: number = 1): IResponseDatagridData {
		if (!schema) {
			console.warn("mapSchemaToGridData was called with an undefined schema");
			schema = {
				description: "Schema not available",
			};
		}
		const data: IResponseDatagridData = {
			name,
			description: schema.description,
			depth,
			children: [],
			type: schema.type as string,
			expanded: false,
		};
		if (schema.type === "object" || schema.properties) {
			for (const childName in schema.properties) {
				const childSchema = schema.properties[childName];
				const child = RepositoryHelper.mapSchemaToGridData(childSchema, childName, depth + 1);
				data.children.push(child);
			}
		} else if (schema.type === "array" || schema.items) {
			const childSchema = schema.items as ISchemaObject;
			const childName = "items";
			data.children = [RepositoryHelper.mapSchemaToGridData(childSchema, childName, depth + 1)];
		}

		return data;
	}

	/**
	 * Add query parameter with a specified ID to a datasource.resource.
	 * NOTE: Will modify the resource field of the datasource.
	 *
	 *	TODO: Move to helper module.
		* @param id id of parameter to add, e.g /api?id={id}
		* @param datasource datasource to modify
		*/
	public static addQueryParameter(id: string, datasource: IJsonGenericDatasource): void {
		if (!id || !id.trim()) {
			console.warn("addQueryParameter called with an empty ID");
			return;
		}
		id = id.trim();
		const hasNoQueryParameters: boolean = datasource.resource.match(/^[\w\-\.\_\{\}]+(\/[\w\-\.\_\{\}]+)*$/g) !== null;
		if (hasNoQueryParameters) {
			datasource.resource += "?";
		}
		const hasSomeQueryParameters: boolean =
			datasource.resource.match(/\?.*$/) !== null && !datasource.resource.endsWith("?");
		if (hasSomeQueryParameters) {
			datasource.resource += "&";
		}
		datasource.resource += `${id}={${id}}`;
	}

	/**
	 * Converts a Mongoose DataType to App Builder data type.
	 *
	 * @static
	 * @param dataType DataType to be converted
	 * @return The App Builder data type.
	 */
	public static getAppbuilderDataType(dataType: string): string {
		let newDataType = dataType;
		switch (dataType) {
			case "Byte":
				newDataType = IdoDataType.boolean;
				break;
			case "Binary":
				newDataType = IdoDataType.file;
				break;
			case "Long Integer":
				newDataType = IdoDataType.integer;
				break;
			default:
				break;
		}
		return newDataType;
	}

