import { ProjectTypeEnum, ThemeEnum } from "ng2/common/constants/common.constant";

import type { IJsonUrlVariable } from "ng2/common/model/databearer/json-url-variable.model";
import type { IMap } from "ng2/common/model/imap.model";
import type { IBoundary } from "ng2/common/model/project/boundary.model";

export interface IJsonManifest {
	createdBy: string;
	createdByDisplayName: string;
	createdTime: number;
	description: string;
	displayName: string;
	id: string;
	lastChangedBy: string;
	lastChangedByDisplayName: string;
	lastChangedTime: number;
	mashupBoundaries: IBoundary[];
	urlVariables: IJsonUrlVariable[];
	title: string;
	version: string;
	previewImg: string;
	showTitleBar: boolean;
	themeSwitchEnabled: boolean;
	defaultTheme: ThemeEnum;
}

export interface IJsonRuntimeManifest extends IJsonManifest {
	urlName: string;
	status: number;
}

export interface IJsonDesignerManifest extends IJsonManifest {
	fileMetaData: IMap<IJsonFileMetaData>;
	typeDef: ProjectTypeEnum;
}

export interface IJsonFileMetaData {
	mimeType: string;
	visibleName: string;
}
