import { IJsonChart } from "ng2/common/model/databearer/components/json-chart.model";

import { Chart } from "../../../../components/chart/chart";
import { ISettingsClassInstance } from "../../defines";
import { ComponentSettingsCtrl } from "../component-settings";
import { ChartContentSettingsDirective } from "./chart-content-settings";
import { ChartStyleSettingsDirective } from "./chart-style-settings";

export enum ChartDialogActionEnum {
	ADD = ("Add Data Set") as any,
	EDIT = ("Edit Data Set") as any
}

export class ChartSettingsCtrl implements ISettingsClassInstance {
	private static instance: ChartSettingsCtrl = new ChartSettingsCtrl();

	public static getInstance(): ChartSettingsCtrl {
		return ChartSettingsCtrl.instance;
	}

	public contentSettingsDirective: ng.IDirective = new ChartContentSettingsDirective();
	public styleSettingsDirective: ng.IDirective = new ChartStyleSettingsDirective();

	public constructor() {
		if (ChartSettingsCtrl.instance) {
			throw new Error("Error: Instantiation failed: Use ChartSettingsCtrl.getInstance() instead of new.");
		}
	}

	public create(id: string): IJsonChart {
		const newChart = ComponentSettingsCtrl.create(id, Chart) as IJsonChart;

		return newChart;
	}
}
