import {
	InteractionPairOperation, SettingInputKeys, SettingKeys,
	SidebarTabTypes
} from "ng2/common/constants/common.constant";
import { InteractionDefines } from "ng2/common/constants/interaction-defines.constant";
import { BindingTypeEnum } from "ng2/common/enums/binding-type.enum";
import { SizeTypeEnum } from "ng2/common/enums/size-type.enum";
import { ValueTypeEnum } from "ng2/common/enums/value-type.enum";
import { IComponentSettingCategory } from "ng2/common/model/components/settings/component-setting-category.model";
import { IDataPair } from "ng2/common/model/data-pair.model";
import { IJsonSize } from "ng2/common/model/databearer/components/json-component.model";
import { IJsonBindableField } from "ng2/common/model/databearer/json-binding.model";
import { ILayoutComponent } from "../../common/interface/ilayout-component";
import { IJsonTextArea } from "../../common/model/component/textarea.model";
import { ITextAreaOptions } from "../../common/model/sohoxi/sohoxi-textarea.model";
import { Component } from "../component";

import { BackwardCompatible } from "ng2/common/project/backward-compatible";
import { FrameworkService } from "../services/framework-service";

export class TextArea extends Component {
	public static readonly DEFAULT_MAX_LENGTH: number = 2000;
	public static DEFAULT_WIDTH: IJsonSize = { sizeType: SizeTypeEnum.AUTO };
	public static DEFAULT_HEIGHT: IJsonSize = { sizeType: SizeTypeEnum.AUTO };
	public static SOURCE_TRIGGERS: IDataPair[] = InteractionDefines.SOURCE_TRIGGERS;
	public static TARGET_ACTIONS: IDataPair[] = [
		InteractionPairOperation.GET,
		InteractionPairOperation.CLEAR
	];
	public static DEFAULT_ACTION: IDataPair = InteractionPairOperation.GET;
	public static DEFAULT_TRIGGER: IDataPair = InteractionPairOperation.ON_LOAD;
	public static SETTING_FIELDS: IComponentSettingCategory[] =
	([
		{
			title: "Character Limit",
			tab: SidebarTabTypes.CONTENT,
			settings: [
				{
					title: "Value",
					ref: SettingKeys.MAX_LENGTH,
					inputType: SettingInputKeys.NUMBER,
				}]
		},
		{
			title: "Style",
			tab: SidebarTabTypes.STYLE,
			settings: [{
				title: "Show Label",
				boundarySetting: true,
				ref: SettingKeys.SHOW_LABEL,
				inputType: SettingInputKeys.CHECKBOX,
				defaultValue: true,
			}, {
				title: "Read Only",
				ref: SettingKeys.IS_READONLY,
				inputType: SettingInputKeys.CHECKBOX,
				defaultValue: false,
				button: {
					icon: "#icon-unlocked",
					tooltip: "Disable Component When...",
					dialog: true
				},
			},
			{
				title: "Resizable",
				ref: SettingKeys.IS_RESIZABLE,
				inputType: SettingInputKeys.CHECKBOX,
				defaultValue: false
			}]
		}
	] as IComponentSettingCategory[]).concat(Component.SETTING_FIELDS);

	public static BINDABLE_FIELDS_2: IJsonBindableField[] = [{
		name: "Text",
		ref: SettingKeys.TEXT,
		valueType: ValueTypeEnum.STRING,
		write: true,
		read: true,
		defaultValue: { Type: BindingTypeEnum.STATIC_VALUE, value: "" }
	}, {
		name: "Label",
		ref: SettingKeys.LABEL,
		valueType: ValueTypeEnum.LOCALIZED_STRING,
		write: true,
		read: false,
		defaultValue: { Type: BindingTypeEnum.STATIC_VALUE, value: "" }
	}, {
		name: "Placeholder",
		ref: SettingKeys.PLACEHOLDER,
		valueType: ValueTypeEnum.LOCALIZED_STRING,
		write: true,
		read: false,
		defaultValue: { Type: BindingTypeEnum.STATIC_VALUE, value: "" }
	}];

	public text: string;
	public label: string;
	public placeholder: string;
	public showLabel: boolean;
	public resizable: boolean;
	public maxLength: number;
	public textareaOptions: ITextAreaOptions;
	protected databearer: IJsonTextArea;

	constructor(
		obj: IJsonTextArea,
		$element: ng.IAugmentedJQuery,
		parent: ILayoutComponent,
		frameworkService: FrameworkService
	) {
		super(obj, $element, parent, frameworkService);
		this.showLabel = BackwardCompatible.getShowLabel(obj);
		this.resizable = obj.resizable;
		this.maxLength = obj.maxLength;
		this.updateMaxLength();
	}

	public newBoundaryEntered(boundaryId: string): void {
		super.newBoundaryEntered(boundaryId);
		this.setBoundarySettingIfChanged<boolean>(SettingKeys.SHOW_LABEL);
	}

	public clear(): void {
		this.text = "";
	}

	public updateMaxLength(): void {
		this.textareaOptions = {
			characterCounter: this.maxLength ? true : false,
			maxLength: this.maxLength ? this.maxLength : TextArea.DEFAULT_MAX_LENGTH,
			charRemainingText: "Characters left: {0}"
		};
	}

	public static directive = [() => {
		return {
			template: require("./textarea.html"),
			replace: true
		};
	}];
}
