import { AngularConstants } from "../../../../../common/angular-constants";

import { InjectNames } from "ng2/common/constants/common.constant";
import { IComponentRendered, SelectableEnum } from "ng2/common/constants/sohoxi.constant";
import { DatasourceTypeEnum } from "ng2/common/enums/datasource-type.enum";
import { IExtendedDataGridColumn } from "ng2/common/model/databearer/components/json-datagrid.model";
import { IJsonDatasource, IJsonInlineListDatasource, IJsonInlineListParameter } from "ng2/common/model/databearer/dataservice/json-datasource.model";
import { IListDataItem } from "ng2/common/model/dataservice/list-data-item.model";
import { IDatagridApi, IDatagridColumnArgs, IDataGridOptions } from "ng2/common/model/sohoxi/sohoxi-datagrid.model";

import { IDialogBaseScope } from "../../../../../common/defines";
import { DialogButtonType, DialogService, IDialogOptions, IDialogResult } from "../../../../../common/services/dialog-service";
import { DATAGRID_RENDERED } from "../../../../../common/sohoxi/component/datagrid-directive";
import { IWizardStep, WizardCtrl } from "../../../../editor/wizard/wizard-ctrl";
import { SourceEnum } from "../../create-from-wizard/select-source-ctrl";
import { IDatasourceWizardParameter } from "../datasource-wizard-ctrl";

import { Helper } from "../../../../../common/helper";
import { FeatureSwitchService } from "../../../../../common/services/feature-switch-service";
import { DesignerService } from "../../../../services/designer-service";

export interface IInlineListDataserviceWizardScope extends IDialogBaseScope<IDatasourceWizardParameter> { }

export class InlineListDataserviceWizardCtrl extends WizardCtrl {
	$scope: IDialogBaseScope<void>;
	datagridOptions: IDataGridOptions<any>;
	private columns: IExtendedDataGridColumn[] = [];
	private dataset: IListDataItem[] = [];
	selectedItem: any;
	datasource: IJsonDatasource;
	private gridApi: IDatagridApi;
	steps: IWizardStep[] = [
		{
			id: "inline-list-properties.html",
			title: "Properties",
			userTip: "",
			template: require("./inline-list-properties.html")
		},
		{
			id: "datasource-wizard-description.html",
			title: "Name &amp; Description",
			userTip: "",
			template: require("../datasource-wizard-description.html")
		},
		{
			id: "inline-dataservice-wizard-summary.html",
			title: "Summary",
			userTip: "",
			template: require("./summary-page/dataservice-wizard-summary.html")
		}
	];
	static $inject = [
		AngularConstants.SCOPE,
		AngularConstants.ELEMENT,
		AngularConstants.TEMPLATE_CACHE,
		InjectNames.FEATURE_SWITCH_SERVICE,
		InjectNames.DIALOG_SERVICE,
		InjectNames.DESIGNER_SERVICE
	];

	constructor(
		$scope: IInlineListDataserviceWizardScope,
		$element: ng.IAugmentedJQuery,
		$templateCache: ng.ITemplateCacheService,
		featureService: FeatureSwitchService,
		private dialogService: DialogService,
		private designerService: DesignerService
	) {
		super(
			$scope,
			$element,
			$templateCache,
			featureService
		);
		this.initWizard();
		this.nextDisabled = true;
		this.backDisabled = false;
		const eirDialog = $scope.eirDialog;
		if (eirDialog.parameter) {
			this.datasource = eirDialog.parameter.datasource;
		}
	}

	$onInit(): void {
		this.initDatagrid();
		this.$element.on(DATAGRID_RENDERED, (_event: JQueryEventObject, result: IComponentRendered) => {
			this.gridApi = result.api;
			this.initDatagridEvents();
		});
	}

	addRow(): void {
		this.dataset.push({
			id: Helper.generateUniqueId()
		});
		this.datagridOptions.dataset = this.dataset;
		this.gridApi.updated(this.datagridOptions);
		this.updateNextDisabled();
	}

	deleteRow(): void {
		this.dataset = this.datagridOptions.dataset.filter(item => item.id !== this.selectedItem.id);
		this.datagridOptions.dataset = this.dataset;
		this.gridApi.deSelectAllRows();
		this.gridApi.syncSelectedRowsIdx();
		this.gridApi.updated(this.datagridOptions);
		this.updateNextDisabled();
	}

	addColumn(): void {
		const columnNames: string[] = [];
		this.columns.forEach((column) => {
			columnNames.push(column.name);
		});
		const options: IDialogOptions<string[]> = {
			title: "Add New Column",
			parameter: columnNames,
			template: require("./add-new-column/add-new-column.html")
		};
		this.dialogService.show(options).then((result: IDialogResult<string>) => {
			if (result.button === DialogButtonType.Ok) {
				const newColumn = {
					id: Helper.generateUniqueId(),
					name: result.value,
					field: "",
					width: "100%",
					editor: Soho.Editors.Input
				};
				this.columns.push(newColumn);
				this.updateColumns();
			}
		});
	}

	deleteColumn(): void {
		const options: IDialogOptions<IExtendedDataGridColumn[]> = {
			title: "Delete Column",
			parameter: this.columns,
			template: require("./delete-column/delete-column.html")
		};
		this.dialogService.show(options).then((result: IDialogResult<IExtendedDataGridColumn[]>) => {
			if (result.button === DialogButtonType.Ok) {
				this.columns = result.value;
				this.updateColumns();
			}
		});
	}

	back(): void {
		if (this.currentStep === this.steps[0]) {
			this.$scope.eirDialog.close({
				button: DialogButtonType.Back
			});
		} else {
			super.back();
		}
	}

	updateBackDisabled(): void {
		this.backDisabled = false;
	}

	protected getFinishParameter(): IJsonDatasource {
		const displayName = this.datasource.displayName;
		const description = this.datasource.description;
		const outputParams: IJsonInlineListParameter[] = [];
		this.datagridOptions.columns.forEach((column) => {
			outputParams.push({ id: column.id, key: column.name, field: column.field });
		});
		const datasource = {
			...this.datasource,
			typedef: DatasourceTypeEnum.INLINE_LIST,
			suite: SourceEnum.INLINE_LIST,
			dataset: this.datagridOptions.dataset,
			displayName: displayName,
			version: 2,
			outputParameters: outputParams,
			description: description,
		} as IJsonInlineListDatasource;
		return datasource;
	}

	protected onChangeCurrentStep(): void {
		if (this.currentStep === this.steps[1]) {
			if (!this.datasource) {
				this.datasource = { displayName: "My Static Source" };
			}
		}
	}

	protected initDatagridEvents(): void {
		this.gridApi.element.on("selected", () => {
			const selectedRow = this.gridApi.selectedRows();
			this.$scope.$evalAsync(() => {
				if (selectedRow.length > 0) {
					this.selectedItem = selectedRow[0].data;
				} else {
					this.selectedItem = null;
				}
			});
		});
		this.gridApi.element.on("cellchange", (_event: any, args: IDatagridColumnArgs<any>) => {
			this.$scope.$evalAsync(() => {
				if (args.value !== args.oldValue) {
					const selectedRow = this.datagridOptions.dataset.find(row => row.id === args.rowData.id);
					const selectedColumn = this.columns.find(column => column.id === args.column.id);
					selectedRow[args.column.id] = args.value;
					selectedColumn.field = args.column.id;
					this.gridApi.updated(this.datagridOptions);
				}
			});
		});
	}

	private updateColumns(): void {
		if (this.columns.length > 0) {
			const totalWidth = 100;
			const newWidth = totalWidth / this.columns.length;
			for (let i = 0; i < this.columns.length; i++) {
				this.columns[i].width = newWidth + "%";
			}
		}
		this.datagridOptions.columns = this.columns;
		this.gridApi.updateColumns(this.columns);
		this.updateNextDisabled();
	}

	private initDatagrid(): void {
		this.initOnEdit();
		this.datagridOptions = {
			id: "inline-list-id",
			columns: this.columns,
			dataset: this.dataset,
			isList: false,
			editable: true,
			saveColumns: false,
			selectable: SelectableEnum.Single,
			rowHeight: "short",
			frozenColumns: { // Could possible be removed on newer IDS Version
				left: [],
				right: []
			}
		};
	}

	private initOnEdit(): void {
		if (this.datasource) {
			const inlineDatasource = (this.datasource as IJsonInlineListDatasource);
			inlineDatasource.outputParameters.forEach((parameter) => {
				const totalWidth = 100;
				const width = totalWidth / inlineDatasource.outputParameters.length;
				const newColumn = {
					id: parameter.id,
					name: parameter.key,
					field: parameter.field,
					width: width + "%",
					editor: Soho.Editors.Input
				};
				this.columns.push(newColumn);
			});
			this.dataset = inlineDatasource.dataset;
			this.updateNextDisabled();
		}
	}

	private updateNextDisabled(): void {
		if (this.columns.length === 0 || this.dataset.length === 0) {
			this.nextDisabled = true;
		} else {
			this.nextDisabled = false;
		}
	}

	static add(module: ng.IModule): void {
		module.controller(
			"eirInlineListDataserviceWizardCtrl",
			InlineListDataserviceWizardCtrl
		);
	}
}