import angular from "angular";

import { AngularConstants } from "../../../../../../common/angular-constants";

import { Helper } from "../../../../../../common/helper";
import type { IValidationResult } from "../../../../../../common/shared-components/base/field-validation";
import { IOnComponentChangeNotification, NotifiableComponent } from "../../../../../../common/shared-components/base/notifiable-component";
import { CustomFormatters } from "../../../../../../common/sohoxi/custom-formatters";
import type { IRowItem } from "../../../../../../common/sohoxi/datagrid-helper";
import type { IdoProperty } from "../../../../../../components/services/local-ido-service";
import type { ILocalIdoDataserviceWizardScope } from "../dataservice-wizard-ctrl";

import { IdoProperties, Numbers } from "ng2/common/constants/common.constant";
import { SelectableEnum } from "ng2/common/constants/sohoxi.constant";

import type { ILocalIdoParameterObject , IParameterObject } from "ng2/common/model/dataservice/parameter-object.model";
import type { IDatagridApi, IDataGridColumn, IDataGridOptions } from "ng2/common/model/sohoxi/sohoxi-datagrid.model";

export class LocalIdoDataserviceParametersCtrl extends NotifiableComponent {

	// component bindings
	headTitle: string;
	idoProperties: IdoProperty[];
	dataserviceParameters: IParameterObject[];
	wizardNextDisabled: boolean;
	errorList: any[] = [];
	setRequiredParameters: boolean;
	setPrimaryParameters: boolean;
	createParameterObject: (item: any) => ILocalIdoParameterObject;

	private datagridApi: IDatagridApi;
	datagridOptions: IDataGridOptions<IdoProperty>;

	static $inject = [
		AngularConstants.SCOPE,
		AngularConstants.ELEMENT,
		AngularConstants.TIMEOUT,
	];

	constructor(
		private $scope: ILocalIdoDataserviceWizardScope,
		private $element: ng.IAugmentedJQuery,
		private $timeout: ng.ITimeoutService,
	) {
		super();
	}

	$onInit(): void {
		this.initParameterDatagrid();
		this.initDatagridEvent();
	}

	private initDatagridEvent(): void {
		this.$element.on(
			"rendered",
			(event: any, element: ng.IAugmentedJQuery) => {
				this.initDatagridSelectedEvent(element);
				this.$timeout(() => {
					// needs because Soho Datagrid API isn't initialized right after datagrid triggers rendered
					this.datagridApi = element.data("datagrid") as IDatagridApi;

					if (this.dataserviceParameters.length > 0) {
						this.setSelectedRows();
					}
				}, Numbers.HUNDRED);
			}
		);
	}

	private setSelectedRows(): void {
		let rowsToSelect: number[] = [];
		if (this.datagridApi) {
			this.dataserviceParameters.forEach(parameter => {
				rowsToSelect = this.addToSelect(rowsToSelect, parameter.key);
			});
			this.datagridApi.selectRows(rowsToSelect);
		}
	}

	private addToSelect(list: number[], propertyName: string): number[] {
		return list.concat(this.datagridApi.findRowsByValue("propertyName", propertyName));
	}

	private initDatagridSelectedEvent(element: ng.IAugmentedJQuery): void {
		element.on(
			"selected",
			(event: any, selectedDatagridItems: IRowItem<IdoProperty>[]) => {
				this.$scope.$applyAsync(() => {
					this.onSelectedEvent(selectedDatagridItems);
				});
			}
		);
	}

	private onSelectedEvent(selectedDatagridItems: IRowItem<IdoProperty>[]): void {
		const newDataserviceParameters =
			this.getSelectedDataserviceParameters(selectedDatagridItems);

		const addedDataserviceParameters = this.dataserviceParameters;

		newDataserviceParameters.forEach((newDataserviceParameter, index, array) => {
			const parameter = addedDataserviceParameters
				.find(addedOutputParameter => addedOutputParameter.key === newDataserviceParameter.key);
			if (parameter) {
				array[index].alias = parameter.alias;
			}
		});
		this.dataserviceParameters = newDataserviceParameters;
	}

	isAliasUnique(alias: string, id: string): boolean {
		if (Helper.isUndefinedEmpty(alias)) {
			// alias is empty string skip checking
			this.wizardNextDisabled = false;
			return true;
		}

		const parameters = this.dataserviceParameters;
		// alias check comes from same id skip checking
		const checkAlias = parameters.filter(
			parameter => parameter.alias === alias
		);
		if (checkAlias.length > 1) {
			this.wizardNextDisabled = true;
			return false;
		} else if (checkAlias.length === 1 && checkAlias[0].id === id) {
			this.wizardNextDisabled = false;
			return true;
		} else if (checkAlias.length === 1 && checkAlias[0].id !== id) {
			this.wizardNextDisabled = true;
			return false;
		}

		this.wizardNextDisabled = false;
		return true;
	}

	private getSelectedDataserviceParameters(
		selectedDatagridItems: IRowItem<IdoProperty>[]
	): ILocalIdoParameterObject[] {
		const dataserviceParameters: ILocalIdoParameterObject[] = [];

		selectedDatagridItems.forEach(item => {
			dataserviceParameters.push(this.createParameterObject({ $item: item }));
		});
		return dataserviceParameters;
	}

	private initParameterDatagrid(): void {
		let columns: IDataGridColumn[] = [
			{
				id: "selectionCheckbox",
				sortable: false,
				resizable: false,
				width: 50,
				formatter: Soho.Formatters.SelectionCheckbox,
				align: "center",
			},
			{
				id: "propertyName",
				name: "Property Name",
				field: "propertyName",
				sortable: true,
				width: 200,
				formatter: Soho.Formatters.Ellipsis
			},
			{
				id: "dataType",
				name: "Data Type",
				field: "dataType",
				width: 200,
				sortable: true
			},
			{
				id: "dataLength",
				name: "Length",
				field: "dataLength",
				sortable: true,
				width: 200
			}
		];

		if (this.setRequiredParameters) {
			columns = columns.concat([{
				id: "isReadOnly",
				name: "Read Only",
				field: "isReadOnly",
				sortable: true,
				width: "21%",
				formatter: CustomFormatters.RequiredFormatter("isReadOnly")
			},
			{
				id: "isRequired",
				name: "Required",
				field: "isRequired",
				sortable: true,
				width: "21%",
				formatter: CustomFormatters.RequiredFormatter("isRequired")
			}]);
		}

		if (this.setPrimaryParameters) {
			columns = columns.concat([{
				id: "keySequence",
				name: "Primary Key",
				field: "keySequence",
				sortable: true,
				width: "21%",
				formatter: CustomFormatters.RequiredFormatter("keySequence", true)
			}]);
		}

		//ItemID is nothing you can filter with
		if (this.headTitle === "Filter Properties") {
			this.idoProperties = this.idoProperties.filter((idoProperty) => {
				return idoProperty.propertyName !== IdoProperties.ITEM_ID;
			});
		}

		let requiredFirst;
		if (this.setPrimaryParameters) {
			requiredFirst = angular.copy(this.idoProperties).sort((x, y) => {
				return x.keySequence > 0 ? -1 : y.keySequence > 0 ? 1 : 0;
			});
		} else {
			requiredFirst = Helper.sortObjectByProperty(
				angular.copy(this.idoProperties), "isRequired", true);
		}

		this.datagridOptions = {
			columns,
			dataset: requiredFirst,
			indeterminate: true,
			isList: false,
			saveColumns: false,
			enableTooltips: true,
			selectable: this.setPrimaryParameters ? false : SelectableEnum.Multiple,
			rowHeight: "short",
			isRowDisabled: (idx, item: any) => {
				if (this.setRequiredParameters && item.isRequired) {
					item._selected = true;
				} else if (this.setPrimaryParameters && item.keySequence > 0) {
					item._selected = true;
				} else {
					item._selected = false;
				}
				return item._selected;
			}
		};
	}

	getValidation($data: IOnComponentChangeNotification): boolean | IValidationResult {
		let result: boolean;
		if ($data.validator === "isAliasUnique") {
			result = this.isAliasUnique($data.value, $data.id);
		} else if ($data.validator === "isFirstCharLetter") {
			result = $data.value ? Helper.isFirstCharLetter($data.value) : true;
		}

		return result;
	}
}
