import angular from "angular";
import { AngularConstants } from "../../../../common/angular-constants";

import { InjectNames } from "ng2/common/constants/common.constant";
import { ValueTypeEnum } from "ng2/common/enums/value-type.enum";
import { ICustomComponentDefinition } from "ng2/common/model/components/custom-component-definition.model";
import { IJsonBindableField } from "ng2/common/model/databearer/json-binding.model";

import { IModalDialog, IModalOptions } from "../../../../common/model/sohoxi/sohoxi.model";
import { EditorService } from "../../services/editor-service";
import { ISettingsScope } from "../settings";

export interface ICustomComponentCtrlScope extends ISettingsScope {
	newCustom: Function;
	editCustom: Function;
	deleteCustom: Function;
	customComponent: ICustomComponentDefinition;
	newBindable: Function;
	updateUI: Function;

	splitCSV: (value: string) => string[];
	getCustomComponents: () => void;
}

export class CustomComponentCtrl {
	public idCounter = 0;

	static $inject = [
		AngularConstants.SCOPE,
		AngularConstants.TEMPLATE_CACHE,
		AngularConstants.COMPILE,
		AngularConstants.ELEMENT,
		AngularConstants.TIMEOUT,
		InjectNames.EDITOR_SERVICE
	];

	constructor(
		private $scope: ICustomComponentCtrlScope,
		private $templateCache: ng.ITemplateCacheService,
		private $compile: ng.ICompileService,
		private $element: ng.IAugmentedJQuery,
		private $timeout: ng.ITimeoutService,
		private editorService: EditorService
	) {
		$scope.newCustom = () => { this.newCustom(); };
		$scope.editCustom = (customComponentId: string) => { this.editCustom(customComponentId); };
		$scope.deleteCustom = (customComponentId: string) => { this.deleteCustom(customComponentId); };
		$scope.updateUI = () => { this.updateDialogUi(); };

		$scope.getCustomComponents = () => {
			return this.editorService.getCurrentProject().getCustomComponentDefinitions();
		};

		$scope.newBindable = () => {
			const bindableField: IJsonBindableField = {
				name: "",
				ref: "",
				valueType: ValueTypeEnum.STRING,
				write: true,
				read: true
			};

			if (!$scope.customComponent.bindableFields) {
				$scope.customComponent.bindableFields = [];
			}

			$scope.customComponent.bindableFields.push(bindableField);
		};

		$scope.splitCSV = (value: string) => {
			if (value) {
				return value.split(",");
			} else {
				return null;
			}
		};
	}

	private newCustom(): void {
		const customComponents = this.editorService.getCurrentProject().getCustomComponentDefinitions();
		const generatedId = this.generateId("custom_component", Object.keys(customComponents));
		const customComponent: ICustomComponentDefinition = {
			id: generatedId,
			displayName: generatedId
		};

		customComponent.htmlTemplate = "<span>TJENA</span>";
		customComponent.init = 'console.info("[CustomComponentCtrl] ID: " + this.Id);';
		customComponent.bindableFields = [];

		this.editorService.getCurrentProject().addCustomComponentDefinition(customComponent);
		this.editCustom(customComponent.id);
	}

	private editCustom(customComponentId: string): void {
		const template = this.$templateCache.get<string>("designer/editor/component-settings/custom/new-custom-dialog.html");
		if (template) {
			this.$scope.customComponent =
				this.editorService.getCurrentProject().getCustomComponentDefinitions()[customComponentId];

			if (this.$scope.customComponent) {
				if (!this.$scope.customComponent.sourceTriggers) {
					this.$scope.customComponent.sourceTriggers = [];
				}
				if (!this.$scope.customComponent.targetActions) {
					this.$scope.customComponent.targetActions = [];
				}

				const ctemplate = this.$compile(template)(this.$scope);
				const settings: IModalOptions = {
					title: "Edit Custom Component",
					content: ctemplate,
					buttons: [
						{
							text: "OK",
							click: (event: JQueryEventObject, modal: IModalDialog) => {
								modal.close();
							}
						},
						{
							text: "Cancel",
							isDefault: true,
							click: (event: JQueryEventObject, modal: IModalDialog) => {
								modal.close();
							}
						}]
				};
				angular.element("body").modal(settings);
				angular.element("customComponentDialog").find(".tab-panel").bind("updated", (e: JQueryEventObject) => {
					e.stopPropagation();
				});
				this.updateDialogUi();
			} else {
				console.warn("[CustomComponentCtrl] Invalid custom component ID");
			}
		} else {
			console.error("[CustomComponentCtrl] TEMPLATE error");
		}
	}

	private deleteCustom(customComponentId: string): void {
		//var template = this.$templateCache.get("designer/editor/component-settings/custom/new-custom-dialog.html");
		this.$scope.customComponent =
			this.editorService.getCurrentProject().getCustomComponentDefinitions()[customComponentId];

		if (this.$scope.customComponent) {
			//var ctemplate = this.$compile(template)(this.$scope);
			const settings: IModalOptions = {
				title: "Confirm delete",
				content: "Really delete Custom component: " + this.$scope.customComponent.displayName,
				buttons: [
					{
						text: "Delete",
						click: (event: JQueryEventObject, modal: IModalDialog) => {
							console.warn("[CustomComponentCtrl] TODO implement delete custom component");
							modal.close();
						}
					},
					{
						text: "Cancel",
						isDefault: true,
						click: (event: JQueryEventObject, modal: IModalDialog) => {
							modal.close();
						}
					}]
			};
			$("body").modal(settings);

			this.updateDialogUi();
		} else {
			console.error("[CustomComponentCtrl] TEMPLATE error");
		}
	}

	private generateId(key: string, scDefsIds: string[]): string {
		const newId = key + this.idCounter++;
		for (const i in scDefsIds) {
			if (scDefsIds[i] === newId) {
				return this.generateId(key, scDefsIds);
			}
		}

		return newId;
	}

	private updateDialogUi(): void {
		this.$timeout(() => {
			$("customComponentDialog").find("select").trigger("updated");
		}, 1);
	}

	static add(m: ng.IModule): void {
		m.controller("eirCustomComponentCtrl", CustomComponentCtrl);
	}
}
