import angular from "angular";

import {
	AlignmentEnum,
	ComponentSizeTypes,
	DEFAULT_BOUNDARY,
	FlexOverflowEnum,
	FlexSizeTypeEnum,
	InteractionPairOperation,
	SettingInputKeys,
	SettingKeys,
	SidebarTabTypes,
} from "ng2/common/constants/common.constant";
import { InteractionDefines } from "ng2/common/constants/interaction-defines.constant";

import { OrientationEnum } from "ng2/common/enums/orientation.enum";
import { SizeTypeEnum } from "ng2/common/enums/size-type.enum";

import type { IComponentSettingCategory } from "ng2/common/model/components/settings/component-setting-category.model";
import type { IDataPair } from "ng2/common/model/data-pair.model";
import type { IJsonSize } from "ng2/common/model/databearer/components/json-component.model";
import type { IJsonBorder, IJsonFlexbox } from "ng2/common/model/databearer/components/json-flexbox.model";
import type { IJsonBindableField } from "ng2/common/model/databearer/json-binding.model";
import type { IMap } from "ng2/common/model/imap.model";

import type { IFlexboxStyle } from "common/defines";
import type { IComponent } from "common/IComponent";
import type { IFlexboxComponent } from "common/interface/iflexbox-component";
import type { ILayoutComponent } from "common/interface/ilayout-component";

import { Component } from "components/component";
import { LayoutComponent } from "components/layout-component";

import { ComponentsHelper } from "components/helper";

import { FrameworkService } from "components/services/framework-service";

export class Flexbox extends LayoutComponent implements IFlexboxComponent {
	static availableOrientations = [
		{ name: "Vertical", value: OrientationEnum.VERTICAL },
		{ name: "Horizontal", value: OrientationEnum.HORIZONTAL }
	];
	static availableHorizontalAlignments: IDataPair[] = [
		{ name: "Left", value: AlignmentEnum.FLEX_START },
		{ name: "Center", value: AlignmentEnum.CENTER },
		{ name: "Right", value: AlignmentEnum.FLEX_END }
	];
	static availableVerticalAlignments: IDataPair[] = [
		{ name: "Top", value: AlignmentEnum.FLEX_START },
		{ name: "Middle", value: AlignmentEnum.CENTER },
		{ name: "Bottom", value: AlignmentEnum.FLEX_END }
	];
	static availableFlexSizeTypes: IDataPair[] = [
		{ name: "Relative", value: FlexSizeTypeEnum.FLEX },
		{ name: "Pixel", value: FlexSizeTypeEnum.PIXELS }
	];
	static availableOverflows: IDataPair[] = [
		{ name: "Hidden", value: FlexOverflowEnum.HIDDEN },
		{ name: "Wrap", value: FlexOverflowEnum.WRAP },
		{ name: "Scroll", value: FlexOverflowEnum.SCROLL }
	];
	static DEFAULT_WIDTH: IJsonSize = { sizeType: SizeTypeEnum.PERCENT };
	static DEFAULT_HEIGHT: IJsonSize = { sizeType: SizeTypeEnum.AUTO };
	static SOURCE_TRIGGERS: IDataPair[] = InteractionDefines.SOURCE_TRIGGERS;
	static TARGET_ACTIONS: IDataPair[] = [
		InteractionPairOperation.GET,
		InteractionPairOperation.CLEAR
	];
	static DEFAULT_TRIGGER: IDataPair = InteractionPairOperation.ON_LOAD;
	static DEFAULT_ACTION: IDataPair = InteractionPairOperation.GET;
	static BINDABLE_FIELDS_2: IJsonBindableField[] = [];
	static SETTING_FIELDS: IComponentSettingCategory[] =
	([
		{
			title: "Orientation",
			tab: SidebarTabTypes.STYLE,
			settings: [{
				ref: SettingKeys.ORIENTATION,
				boundarySetting: true,
				inputType: SettingInputKeys.RADIO,
				dataset: Flexbox.availableOrientations,
				defaultValue: OrientationEnum.VERTICAL
			}]
		}, {
			title: "Width",
			tab: SidebarTabTypes.STYLE,
			condition: "parent.orientation === " + OrientationEnum.HORIZONTAL,
			settings: [{
				ref: SettingKeys.SIZE_TYPE,
				boundarySetting: true,
				inputType: SettingInputKeys.RADIO,
				dataset: Flexbox.availableFlexSizeTypes,
				defaultValue: FlexSizeTypeEnum.FLEX
			}, {
				title: "Value",
				ref: SettingKeys.SIZE,
				boundarySetting: true,
				inputType: SettingInputKeys.TEXT,
				defaultValue: 1
			}]
		}, {
			title: "Height",
			tab: SidebarTabTypes.STYLE,
			condition: "parent.orientation === " + OrientationEnum.VERTICAL,
			settings: [{
				ref: SettingKeys.SIZE_TYPE,
				boundarySetting: true,
				inputType: SettingInputKeys.RADIO,
				dataset: Flexbox.availableFlexSizeTypes,
				defaultValue: FlexSizeTypeEnum.FLEX
			}, {
				title: "Value",
				ref: SettingKeys.SIZE,
				boundarySetting: true,
				inputType: SettingInputKeys.TEXT,
				defaultValue: 1
			}]
		}, {
			title: "Overflow",
			tab: SidebarTabTypes.STYLE,
			settings: [{
				ref: SettingKeys.OVERFLOW,
				boundarySetting: true,
				inputType: SettingInputKeys.RADIO,
				dataset: Flexbox.availableOverflows,
				defaultValue: FlexOverflowEnum.HIDDEN
			}]
		}, {
			title: "Background Color",
			tab: SidebarTabTypes.STYLE,
			settings: [{
				ref: SettingKeys.BACKGROUND_COLOR,
				boundarySetting: true,
				inputType: SettingInputKeys.COLOR_PICKER,
				defaultValue: undefined
			}]
		}, {
			title: "Alignment",
			tab: SidebarTabTypes.STYLE,
			settings: [
				{
					title: "Horizontal",
					ref: SettingKeys.ALIGNMENT_HORIZONTAL,
					boundarySetting: true,
					dataset: Flexbox.availableHorizontalAlignments,
					inputType: SettingInputKeys.DROPDOWN,
					defaultValue: AlignmentEnum.FLEX_START
				},
				{
					title: "Vertical",
					ref: SettingKeys.ALIGNMENT_VERTICAL,
					boundarySetting: true,
					dataset: Flexbox.availableVerticalAlignments,
					inputType: SettingInputKeys.DROPDOWN,
					defaultValue: AlignmentEnum.FLEX_START
				}
			]
		}, {
			title: "Border",
			tab: SidebarTabTypes.STYLE,
			settings: [
				{
					ref: SettingKeys.BORDER,
					inputType: SettingInputKeys.BORDER,
					defaultValue: { left: 0, top: 0, right: 0, bottom: 0 }
				}, {
					title: "Color",
					ref: SettingKeys.BORDER_COLOR,
					inputType: SettingInputKeys.COLOR_PICKER,
					defaultValue: undefined
				}
			]
		}, {
			title: "Padding",
			tab: SidebarTabTypes.STYLE,
			settings: [{
				title: "Padding",
				ref: SettingKeys.MARGIN,
				boundarySetting: true,
				inputType: SettingInputKeys.MARGIN,
				defaultValue: { left: 10, top: 10, right: 10, bottom: 10 }
			}]
		}] as IComponentSettingCategory[]).concat(Component.SETTING_FIELDS.filter((settingCategory) => {
		// Removing settings "margin" from Component settings
		// It is replaced by the "padding" setting on flexbox
		if (settingCategory.settings.some((setting) => {
			return setting.ref === SettingKeys.MARGIN;
		})) {
			return false;
		}
		return true;
	}));

	private sizeType: FlexSizeTypeEnum;
	private size: number;
	protected backgroundColor: string;
	border: IJsonBorder;
	borderColor: string;
	orientation: OrientationEnum;
	styleOptions: IMap<string>;
	isFlexParent = false;
	childrenClasses: string;

	protected databearer: IJsonFlexbox;

	constructor(
		obj: IJsonFlexbox,
		$element: ng.IAugmentedJQuery,
		parent: ILayoutComponent,
		frameworkService: FrameworkService
	) {
		super(obj, $element, parent, frameworkService);
		if (obj) {
			this.border = obj.border ? obj.border : { left: 0, top: 0, right: 0, bottom: 0 };
			this.borderColor = obj.borderColor;
		}
		this.updateIsFlexParent();
	}

	getDatabearer(): IJsonFlexbox {
		return this.databearer;
	}

	updateIsFlexParent(): void {
		this.isFlexParent = this.databearer.children.some((child) => {
			return child.typedef === Flexbox.name;
		});
	}

	isParent(): boolean {
		return this.isFlexParent;
	}

	clear(): void {
		this.children.forEach((component) => {
			component.clear();
		});
	}

	private getAlignment(): IMap<string> {
		const horizAlignment = this.getBoundarySetting<AlignmentEnum>(SettingKeys.ALIGNMENT_HORIZONTAL);
		const verticAlignment = this.getBoundarySetting<AlignmentEnum>(SettingKeys.ALIGNMENT_VERTICAL);

		return {
			"justify-content": this.getAlignmentProperty(
				this.orientation === OrientationEnum.HORIZONTAL ? horizAlignment : verticAlignment),
			"align-items": this.getAlignmentProperty(
				this.orientation === OrientationEnum.HORIZONTAL ? verticAlignment : horizAlignment)
		};
	}

	private getAlignmentProperty(alignment: AlignmentEnum): string {
		let alignmentProperty: string;
		switch (alignment) {
			case AlignmentEnum.FLEX_START:
				alignmentProperty = "flex-start";
				break;
			case AlignmentEnum.CENTER:
				alignmentProperty = "center";
				break;
			case AlignmentEnum.FLEX_END:
				alignmentProperty = "flex-end";
				break;
			default:
				console.warn("[Flexbox] Invalid alignment:" + alignment);
		}
		return alignmentProperty;
	}

	protected getInlineStyle(): IFlexboxStyle {
		const generalComponentStyle = super.getInlineStyle();
		// Try to get backgroundcolor from the new boundary settings. If it doesnt exist,
		// try getting it from the databearer
		const backgroundColor =
			this.getBoundarySetting<string>(SettingKeys.BACKGROUND_COLOR)
			|| this.getDatabearer().backgroundColor;

		const style: IFlexboxStyle = {
			...generalComponentStyle,
			backgroundColor: backgroundColor === undefined ? "" : backgroundColor,
			padding: null,
			// Width and height have default values in flexbox.less
			width: "",
			height: "",
		};

		if (this.sizeType === FlexSizeTypeEnum.PIXELS) {
			const value = this.size + "px";
			style.flexGrow = 0;
			style.flexShrink = 0;
			style.flexBasis = value;
		} else {
			style.flexShrink = 1;
			style.flexBasis = "auto";
			style.flexGrow = this.size;
		}

		return style;
	}

	protected getClassList(): string[] {
		const wrapClass = "overflowWrap";
		const scrollClass = "overflowScroll";
		const classes = super.getClassList();
		const overflow = this.getBoundarySetting<FlexOverflowEnum>(SettingKeys.OVERFLOW);

		switch (overflow) {
			case FlexOverflowEnum.WRAP:
				classes.push(wrapClass);
				break;
			case FlexOverflowEnum.SCROLL:
				classes.push(scrollClass);
				break;
			default:
				// No class needed
		}
		return classes;
	}

	updateChildrenClasses(): string {
		const childrenHasPercentSizes = this.databearer.children.some((child) => {
			return child.boundarySettings[DEFAULT_BOUNDARY.id].width.sizeType === SizeTypeEnum.PERCENT;
		});
		this.childrenClasses = childrenHasPercentSizes ? ComponentSizeTypes.LARGE : ComponentSizeTypes.SMALL;
		return this.childrenClasses;
	}

	resizableChildren(_ori: OrientationEnum): boolean { return false; }

	getPxPerPercentComponent(ori: OrientationEnum, _percent: number): number {
		let pixels = 0;
		// Only calculate for vertical (height) since flexbox doesn't allow content to be placed horizontally yet.
		if (ori === OrientationEnum.VERTICAL) {
			this.children.forEach((child: IComponent) => {
				if (child.getBoundarySetting<boolean>(SettingKeys.VISIBILITY) &&
					child.getBoundarySetting<IJsonSize>(SettingKeys.HEIGHT).sizeType === SizeTypeEnum.AUTO) {
					pixels += child.element.outerHeight(true);
				}
			}, false);
		}
		return pixels;
	}

	hasIndividualChildSizes(_ori: OrientationEnum): boolean { return true; }

	updateClassesAndStyle(): void {
		const boundaryId = this.frameworkService.getCurrentBoundary().id;
		this.orientation = this.getOrientationSetting(boundaryId);

		this.sizeType = this.getBoundarySetting<FlexSizeTypeEnum>(SettingKeys.SIZE_TYPE, boundaryId);
		this.size = this.getBoundarySetting<number>(SettingKeys.SIZE, boundaryId);

		this.styleOptions = angular.extend({}, this.getAlignment(), this.getPadding(), this.getBorder());
		this.updateChildrenClasses();

		super.updateClassesAndStyle();
	}

	private getBorder(): IMap<string> {
		const borderColor = this.borderColor ? this.borderColor : "transparent";
		const border: IMap<string> = {
			"border-top": this.border?.top + "px solid " + borderColor,
			"border-bottom": this.border?.bottom + "px solid " + borderColor,
			"border-right": this.border?.right + "px solid " + borderColor,
			"border-left": this.border?.left + "px solid " + borderColor
		};
		return border;
	}

	private getOrientationSetting(boundaryId: string): OrientationEnum {
		let orientation = this.getBoundarySetting<OrientationEnum>(SettingKeys.ORIENTATION, boundaryId);
		if (!orientation) {
			orientation = ComponentsHelper.getDefaultComponentSettingValue<OrientationEnum>(
				Flexbox.SETTING_FIELDS, SettingKeys.ORIENTATION, "Orientation");
		}
		return orientation;
	}

	static directive = [() => {
		return {
			template: require("./flexbox.html"),
			replace: true
		};
	}];
}
