import { IMap } from "ng2/common/model/imap.model";
import { LocalIdoDataserviceSummaryDatagridCtrl } from "./dataservice-summary-datagrid-ctrl";

export class LocalIdoDataserviceSummaryDatagridComponent implements ng.IComponentOptions {

	public controller: any;
	public controllerAs: string;
	public template: string;
	public bindings: IMap<string>;

	constructor() {
		this.bindings = {
			headTitle: "@",
			dataset: "<",
			datagridOptions: "<?"
		};

		this.controller = LocalIdoDataserviceSummaryDatagridCtrl;
		this.controllerAs = "ctrl";
		this.template = require("./dataservice-summary-datagrid.html");
	}

	static add(m: ng.IModule): void {
		m.component("eirLocalIdoDataserviceSummaryDatagridComponent", new LocalIdoDataserviceSummaryDatagridComponent());
	}
}
