import angular from "angular";
import { AngularConstants } from "../../../../common/angular-constants";

import { IChanged, InjectNames, SubstitutionTags } from "ng2/common/constants/common.constant";
import { SelectableEnum } from "ng2/common/constants/sohoxi.constant";
import { ValueTypeEnum } from "ng2/common/enums/value-type.enum";
import { IMap } from "ng2/common/model/imap.model";
import { IDataGridColumn } from "ng2/common/model/sohoxi/sohoxi-datagrid.model";

import { IDialogBaseScope } from "../../../../common/defines";
import { ILookupOptions } from "../../../../common/model/sohoxi/sohoxi-lookup.model";
import { DialogButtonType, IDialog, IDialogResult } from "../../../../common/services/dialog-service";
import { FeatureSwitchService } from "../../../../common/services/feature-switch-service";
import { FeatureSwitchCtrl } from "../../../../common/shared-components/base/feature-switch-ctrl";
import { TranslationManagementService } from "../../../../components/services/translation-management-service";
import { EditorService } from "../../services/editor-service";

export interface IEditBindingDialogParameter {
	bindingString: string;
	valueType: ValueTypeEnum;
	property: string;
}

export class EditBindingCtrl extends FeatureSwitchCtrl {
	public bindingString: string;
	public invalidString = false;
	public lookupOptions: ILookupOptions;
	public isLocalized: boolean;
	private property: string;
	private dialog: IDialog<IEditBindingDialogParameter>;
	private substitutionTag: string;

	static $inject = [
		AngularConstants.SCOPE,
		InjectNames.EDITOR_SERVICE,
		InjectNames.TRANSLATION_MANAGEMENT_SERVICE,
		InjectNames.FEATURE_SWITCH_SERVICE
	];

	constructor(
		private scope: IDialogBaseScope<IEditBindingDialogParameter>,
		private editorService: EditorService,
		private translationManagementService: TranslationManagementService,
		featureSwitchService: FeatureSwitchService
	) {
		super(featureSwitchService);
		const dialog = scope.eirDialog;
		this.dialog = dialog;
		this.substitutionTag = SubstitutionTags.VALUE;
		const valueType = dialog.parameter.valueType;
		if (!dialog.parameter.bindingString) {
			this.bindingString = this.substitutionTag;
		} else {
			this.bindingString = dialog.parameter.bindingString;
		}
		this.isLocalized = valueType === ValueTypeEnum.LOCALIZED_STRING;
		this.property = dialog.parameter.property;
		this.onChangeBindingString();
		this.initLookupOptions();
	}

	private initLookupOptions(): void {
		const columns: IDataGridColumn[] = [];
		columns.push({
			id: "Name",
			name: "Name",
			field: "Name",
			width: 250,
			resizable: false,
			sortable: true,
		});
		columns.push({
			id: "Value",
			name: "Value",
			field: "Value",
			width: 250,
			resizable: false,
			sortable: true,
			formatter: Soho.Formatters.Ellipsis
		});

		const dataset = this.translationManagementService.getCurrentStringsList();

		this.lookupOptions = {
			title: "Select Item",
			field: (selectedRow: IMap<string>) => {
				const propertyName = "Name";
				return this.substitutionTag + "{" + selectedRow[propertyName] + "}";
			},
			match: () => {
				// fake method
			},
			options: {
				paging: true,
				pagesize: 10,
				columns: columns,
				dataset: dataset,
				selectable: SelectableEnum.Single,
				toolbar: {
					keywordFilter: true,
				}
			},
		};
	}

	public onChangeBindingString(): void {
		if (this.bindingString === "") {
			this.bindingString = this.substitutionTag;
			this.invalidString = false;
		} else {
			const index = this.bindingString.indexOf(this.substitutionTag);
			if (index === -1) {
				this.invalidString = true;
			} else {
				this.invalidString = false;
			}
		}
	}

	public close(): void {
		this.dialog.close({
			button: DialogButtonType.Cancel
		});
	}

	public save(): void {
		const result: IDialogResult<string> = {
			value: this.bindingString,
			button: DialogButtonType.Ok
		};
		this.dialog.close(result);
	}

	public openTranslations(): void {
		const selectedComponent = this.editorService.getSelectedComponent();
		this.translationManagementService.openTranslations(selectedComponent, this.property).then((result) => {
			if (result.button === DialogButtonType.Ok) {
				const data = result.value as IChanged;
				if (data.savedComponentStrings.length > 0) {
					const selectedChanged = data.savedComponentStrings.filter((translation) => {
						return translation.component === selectedComponent.getId();
					});
					if (selectedChanged) {
						selectedChanged.forEach((updatedTranslation) => {
							if (updatedTranslation.reference === this.property) {
								this.bindingString = updatedTranslation.newValue;
							}
							this.editorService.translationChangedEvent.raise(updatedTranslation);
						});
						// if selected component changed add undo
						this.editorService.addUndoStack(this.editorService.getCurrentProject());
					}
					this.editorService.updateTranslatables();
				}
			}
			//If global translations is changed
			if (result.button === DialogButtonType.Ok && !angular.equals({}, result.value.savedGlobalStrings)) {
				this.translationManagementService.saveGlobalTranslations(result.value.savedGlobalStrings).then(() => {
					this.editorService.updateTranslatables();
				});
			}
			//If global translations is deleted
			if (result.button === DialogButtonType.Ok && !angular.equals({}, result.value.deletedGlobalStrings)) {
				this.translationManagementService.deleteGlobalTranslations(result.value.deletedGlobalStrings).then(() => {
					this.editorService.updateTranslatables();
				});
			}
		});
	}

	static add(m: ng.IModule): void {
		m.controller("eirEditBindingCtrl", EditBindingCtrl);
	}
}
