import { AngularConstants } from "../../../../common/angular-constants";

import { IDialogBaseScope } from "../../../../common/defines";
import { DialogButtonType, IDialog, IDialogResult } from "../../../../common/services/dialog-service";
import { IValidatorError } from "../../../../common/shared-components/base/field-validation";

export class CopyFormDialogController {
	public dialog: IDialog<string>;
	public formName: string = "";
	public errorList: IValidatorError[] = [];
	public get isNameInvalid(): boolean {
		return this.errorList.length !== 0;
	}

	static $inject = [
		AngularConstants.SCOPE
	];

	constructor(
		scope: IDialogBaseScope<string>
	) {
		const dialog: IDialog<string> = scope.eirDialog;
		this.dialog = dialog;
		this.errorList = [];
		this.formName = this.dialog.parameter;
	}

	public saveForm(): void {
		const result: IDialogResult<string> = {
			button: DialogButtonType.Ok,
			value: this.formName
		};

		this.dialog.close(result);
	}

	public close(): void {
		this.dialog.close();
	}

	static add(m: ng.IModule): void {
		m.controller("eirCopyFormDialogController", CopyFormDialogController);
	}
}
