import { Helper } from "../helper";

import { CustomFormatters } from "./custom-formatters";
import { LocaleHelper } from "./locale-helper";

import { GridAggregators } from "ng2/common/constants/common.constant";
import { DateFormatEnum, FormatterEnum } from "ng2/common/constants/sohoxi.constant";

import type { IDatasource } from "common/model/datasource/datasource.model";
import type { IExtendedDataGridColumn } from "ng2/common/model/databearer/components/json-datagrid.model";
import type { IJsonGenericDatasource2 } from "ng2/common/model/databearer/dataservice/json-datasource.model";
import type { IMap } from "ng2/common/model/imap.model";
import type {
	IColumnOptions,
	IDataGridColumn,
	IDatagridColumnArgs,
	IDataItem,
} from "ng2/common/model/sohoxi/sohoxi-datagrid.model";
import type { IFilterExpression, IPageRequest } from "ng2/common/model/sohoxi/sohoxi-pager.model";

import { DateHelper } from "ng2/common/helpers/date.helper";

export interface IExpandedRowItem<T> {
	item: T;
}
export interface IRowItem<T> {
	data: T;
	elem: any; // JQuery Element
	idx: number;
}

export class DatagridHelper {
	static getPage<T>(dataset: T[], pageRequest: IPageRequest): T[] {
		pageRequest.total = dataset.length;
		if (dataset.length > pageRequest.pagesize) {
			return dataset.slice(
				(pageRequest.activePage - 1) * pageRequest.pagesize, pageRequest.activePage * pageRequest.pagesize);
		} else {
			pageRequest.total = dataset.length;
			return dataset;
		}
	}

	private static normalizeValue(value: any): string {
		if (value) {
			return value.toString().toLowerCase();
		}
		return value;
	}

	static sortDatasetByField<T>(dataset: T[], sortField: string, sortAsc: boolean): T[] {
		dataset = dataset.sort((itemA: T, itemB: T) => {
			const a = this.normalizeValue(Helper.getDeepValue(itemA, sortField));
			const b = this.normalizeValue(Helper.getDeepValue(itemB, sortField));
			if (a < b) {
				return -1;
			}
			if (a > b) {
				return 1;
			}
			return 0;
		});
		return sortAsc ? dataset : dataset.reverse();
	}

	/**
	 * Filters dataset on path
	 * @param dataset
	 * @param filterExpression
	 * @param paths
	 */
	static getSearchResultOnPaths<T>(
		dataset: T[],
		filterExpression: IFilterExpression,
		paths: string
	): T[] {

		dataset.forEach(DatagridHelper.clearSohoProperties);

		return dataset.filter((item: T) => {
			const deepValue = Helper.getDeepValue(item, paths);
			if (deepValue) {
				return deepValue.toLowerCase().indexOf(filterExpression.value) !== -1;
			}
			return false;
		});
	}

	/**
	 * Filters dataset on keyword
	 * @param dataset
	 * @param filterExpression
	 */
	static getSearchResultOnKeyword<T>(
		dataset: T[],
		filterExpression: IFilterExpression
	): T[] {

		const result = dataset.filter((item: T) => {
			for (const key in item) {
				const value = item[key];
				if (typeof value === "string" &&
					value.toLowerCase().indexOf(filterExpression.value) !== -1
				) {
					return true;
				}
			}
			return false;
		});

		return result;
	}

	/**
	 * Sometime Soho adds properties to row items which should not tag along.
	 * This method will remove those properties.
	 * @param item item
	 */
	static clearSohoProperties(item: IDataItem): void {
		delete item.isFiltered;
		delete item._selected;
		delete item._isFilteredOut;
	}

	static setEditableOnColumn(
		column: IExtendedDataGridColumn,
		locale?: string
	): void {
		if (column.editable) {
			delete column.editorOptions;

			switch (column.formatterName) {
				case FormatterEnum.Checkbox:
					column.editor = Soho.Editors.Checkbox;
					break;
				case FormatterEnum.Date:
					column.editor = Soho.Editors.Date;
					break;
				case FormatterEnum.Time:
					column.editor = Soho.Editors.Time;
					column.editorOptions = {
						timeFormat: Soho.Locale.cultures[locale].calendars[0].dateFormat.timestamp,
						minuteInterval: 5,
						secondInterval: 5,
					};
					break;
				case FormatterEnum.Datetime:
					column.editor = Soho.Editors.Date;
					column.editorOptions = {
						timeFormat: column.timeFormat,
						dateFormat: column.dateFormat,
						showTime: true,
					} as any;
					break;
				case FormatterEnum.Hyperlink:
					column.editor = Soho.Editors.Input;
					break;
				case FormatterEnum.Textarea:
					column.editor = Soho.Editors.Textarea;
					break;
				case FormatterEnum.Button:
					column.editor = Soho.Editors.Button;
					break;
				case FormatterEnum.Dropdown:
					column.editor = Soho.Editors.Dropdown;
					break;
				case FormatterEnum.Autocomplete:
					column.editor = Soho.Editors.Autocomplete;
					break;
				case FormatterEnum.Lookup:
					column.editor = Soho.Editors.Lookup;
					break;
				default:
					column.editor = Soho.Editors.Input;
					break;
			}
		} else {
			column.editor = null;
		}
		column.isEditable = () => {
			return column.editable;
		};
	}

	static setColumnSettings(
		column: IExtendedDataGridColumn,
		locale: string,
	): void {
		// reset column
		delete column.dateFormat;
		delete column.href;
		delete column.click;
		delete column.editorOptions;
		delete column.options;
		column.textOverflow = FormatterEnum.Ellipsis;

		this.setFormatterOnColumns(column, locale);
		this.setEditableOnColumn(column, locale);
	}

	static setFormatterOnColumns(
		column: IExtendedDataGridColumn,
		locale: string): void {
		if (column.formatterName) {
			switch (column.formatterName) {
				case FormatterEnum.Checkbox:
					column.formatter = Soho.Formatters.Checkbox;
					column.formatterName = FormatterEnum.Checkbox;
					column.isChecked = (value: string | number | boolean) => {
						if (jQuery.isNumeric(value) && typeof value === "string") {
							return parseInt(value, 10) === 1;
						} else if (typeof value === "boolean") {
							return value;
						} else if (typeof value === "string") {
							return value.toLowerCase() === "true";
						}
						return false;
					};
					break;
				case FormatterEnum.Date:
					column.formatter = CustomFormatters.Date(locale);
					column.formatterName = FormatterEnum.Date;
					column.dateFormat = LocaleHelper.getDateFormat(DateFormatEnum.Short, locale);
					break;
				case FormatterEnum.Hyperlink:
					column.formatter = Soho.Formatters.Hyperlink;
					column.formatterName = FormatterEnum.Hyperlink;
					break;
				case FormatterEnum.Integer:
					column.formatter = Soho.Formatters.Integer;
					column.formatterName = FormatterEnum.Integer;
					break;
				case FormatterEnum.Decimal:
					column.formatter = Soho.Formatters.Decimal;
					column.formatterName = FormatterEnum.Decimal;
					break;
				case FormatterEnum.Textarea:
					column.formatter = Soho.Formatters.Textarea;
					column.formatterName = FormatterEnum.Textarea;
					break;
				case FormatterEnum.Datetime:
					column.formatter = CustomFormatters.Datetime(locale);
					column.formatterName = FormatterEnum.Datetime;

					column.dateFormat = LocaleHelper.getDateFormat(DateFormatEnum.Short, locale);
					column.timeFormat = LocaleHelper.getDateFormat(DateFormatEnum.Timeformat, locale);
					break;
				case FormatterEnum.Time:
					column.formatter = CustomFormatters.Time(locale);
					column.formatterName = FormatterEnum.Time;
					column.dateFormat = LocaleHelper.getDateFormat(DateFormatEnum.Timestamp, locale);
					break;
				case FormatterEnum.Button:
					column.formatter = Soho.Formatters.Button;
					column.formatterName = FormatterEnum.Button;
					break;
				case FormatterEnum.Dropdown:
					column.formatter = Soho.Formatters.Dropdown;
					column.formatterName = FormatterEnum.Dropdown;
					delete column.textOverflow;
					break;
				case FormatterEnum.Autocomplete:
					column.formatter = Soho.Formatters.Autocomplete;
					column.formatterName = FormatterEnum.Autocomplete;
					delete column.textOverflow;
					break;
				case FormatterEnum.Lookup:
					column.formatter = Soho.Formatters.Lookup;
					column.formatterName = FormatterEnum.Lookup;
					delete column.textOverflow;
					break;
				case FormatterEnum.SelectionCheckbox: //used for multi selection
					column.formatter = Soho.Formatters.SelectionCheckbox;
					break;
				case FormatterEnum.AbsoluteValue:
					column.formatter = CustomFormatters.AbsoluteValue();
					column.formatterName = FormatterEnum.AbsoluteValue;
					break;
				default:
					console.error("Error: Formatter does not exist.");
					break;
			}
		}
	}

	static ToggleSummarizeOnColumn(column: IExtendedDataGridColumn): void {
		if (!column.summarize) {
			column.summaryRowFormatter = Soho.Formatters.SummaryRow;
			column.aggregator = GridAggregators.SUM;
		} else {
			column.summaryRowFormatter = null;
			column.aggregator = null;
		}
	}

	static convertDatetimeToTicks(selectedRow: IDatagridColumnArgs<any>, locale: string): number {
		const column = selectedRow.column as IExtendedDataGridColumn;
		const calendar = Soho.Locale.cultures[locale].calendars[0];
		const formatterName = column.formatterName;

		const oldDate = DateHelper.parse(selectedRow.oldValue, formatterName, locale);
		const parseOption: IFormateDateOptions = {};
		let date: Date;

		switch (formatterName) {
			case FormatterEnum.Date: {
				const short = "short";
				parseOption.pattern = calendar.dateFormat[short];
				date = Soho.Locale.parseDate(selectedRow.value, parseOption);
				date.setHours(oldDate.getHours());
				date.setMinutes(oldDate.getMinutes());
				date.setSeconds(oldDate.getSeconds());
				break;
			}
			case FormatterEnum.Time: {
				const timestamp = "timestamp";
				parseOption.pattern = calendar.dateFormat[timestamp];
				date = Soho.Locale.parseDate(selectedRow.value, parseOption);
				date.setFullYear(oldDate.getFullYear());
				date.setMonth(oldDate.getMonth());
				date.setDate(oldDate.getDate());
				break;
			}
			case FormatterEnum.Datetime:
				parseOption.pattern = calendar.dateFormat[formatterName];
				date = Soho.Locale
					.parseDate(selectedRow.value, parseOption);
				break;
			default:
		}

		return date.getTime();
	}

	static addColumnData(column: IExtendedDataGridColumn, listResult: any[], dataservice: IDatasource): string[] {
		const autocompleteSource: string[] = [];
		switch (column.formatterName) {
			case FormatterEnum.Dropdown: {
				const dropdownOptions: IColumnOptions[] = [];
				listResult.forEach((listItem, index) => {
					dropdownOptions.push({
						id: "optionID" + index,
						value: listItem[column.listOptions.parameterId],
						label: listItem[column.listOptions.parameterId]
					});
				});
				column.options = dropdownOptions;
				break;
			}

			case FormatterEnum.Autocomplete:
				listResult.forEach((listItem) => {
					autocompleteSource.push(listItem[column.listOptions.parameterId]);
				});
				break;

			case FormatterEnum.Lookup: {
				const lookupData = listResult;
				const lookupColumns: IDataGridColumn[] = [];

				const outputParams = (dataservice.getDatabearer() as IJsonGenericDatasource2).outputParameters;
				outputParams.forEach((outputParam) => {
					lookupColumns.push({
						id: outputParam.id,
						name: outputParam.key,
						field: outputParam.id
					});
				});
				const lookupOptions = {
					field: (row: IMap<string>) => {
						const match = row[column.listOptions.parameterId];
						if (!match) {
							console.warn("Warning: No current match to dataset.");
						}
						return match;
					},
					match: () => {
						// fake method
					},
					options: {
						columns: lookupColumns,
						dataset: lookupData,
						selectable: "single"
					}
				};
				column.editorOptions = lookupOptions;
				break;
			}

			default:
				// formatter not exists, should not happen
				break;
		}
		return autocompleteSource;
	}
}
