import angular from "angular";

import { ComponentSizeTypes, CssConstantKeys, DEFAULT_BOUNDARY } from "ng2/common/constants/common.constant";
import { SizeTypeEnum } from "ng2/common/enums/size-type.enum";
import type{ IJsonComponent } from "ng2/common/model/databearer/components/json-component.model";

import { IJQueryDragEventObject, IJQueryMouseEventObject } from "common/defines";
import type { IComponent } from "common/IComponent";
import type { ILayoutComponent } from "common/interface/ilayout-component";
import type { IEditorComponent } from "common/interface/ieditor-component";

import { Defines, ISettingsClassInstance } from "editor/defines";
import { EditorService } from "editor/services/editor-service";

export class EditorHelper {
	static getComponentSettingsClass(componentName: string): ISettingsClassInstance {
		let settingsClass: ISettingsClassInstance = null;
		if (Defines.allSettingsComponents[componentName]) {
			settingsClass = Defines.allSettingsComponents[componentName].settingsClass.getInstance();
		}
		return settingsClass;
	}

	static updateParentComponentSizeClass(parentComponent: ILayoutComponent): void {
		const parentComponentEirChildren: JQuery =
			angular.element(parentComponent.element).find("eir-children").filter(() => {
				return $(parentComponent.element).find(".layoutComponent").length === 0;
			});
		if (!this.containsBigComponent(parentComponent)) {
			parentComponentEirChildren.attr("class", ComponentSizeTypes.SMALL);
		} else {
			parentComponentEirChildren.attr("class", ComponentSizeTypes.LARGE);
		}
	}

	static containsBigComponent(parentComponent: ILayoutComponent): boolean {
		return parentComponent.getDatabearer().children.some((child: IJsonComponent) => {
			return this.isBigComponent(child);
		});
	}

	static isBigComponent(jsonComponent: IJsonComponent): boolean {
		const width = jsonComponent.boundarySettings[DEFAULT_BOUNDARY.id].width;
		return width.sizeType === SizeTypeEnum.PERCENT;
	}

	/**
	 * Will get the underlying component of an DOM-component if there is any
	 * @param {component} IEditorComponent
	 * @param {event} IJQueryMouseEventObject | IJQueryDragEventObject
	 * @return {IComponent} IComponent
	 */
	static getUnderlyingComponent(
		component: IEditorComponent,
		event: IJQueryMouseEventObject | IJQueryDragEventObject,
		editorService: EditorService
	): IComponent {
		//Get the underlying component by point.
		angular.element(".id-" + component.getId()).css(CssConstantKeys.DISPLAY, "none");
		const componentElement = $(document.elementFromPoint(event.originalEvent.clientX, event.originalEvent.clientY));
		angular.element(".id-" + component.getId()).css(CssConstantKeys.DISPLAY, "");
		const id = componentElement.attr("id");
		if (id) {
			return editorService.getComponentInstanceById(id);
		} else {
			return component;
		}
	}

	/**
	 * Will swap two items in an array
	 * any because any kind of array can be used here.
	 * @param {array} any
	 * @param {firstIndex} number
	 * @param {secondIndex} number
	 */
	static swap(array: any[], firstIndex: number, secondIndex: number): void {
		const temporaryItem = array[firstIndex];
		array[firstIndex] = array[secondIndex];
		array[secondIndex] = temporaryItem;
	}

	/**
	 * Will move a array object from startIndex to endIndex
	 * any kind of array can be used here.
	 * @param {array} any
	 * @param {startIndex} number
	 * @param {endIndex} number
	 */
	static moveByIndex(array: any[], startIndex: number, endIndex: number): void {
		const tempItem = array[startIndex];
		array.splice(startIndex, 1);
		array.splice(endIndex, 0, tempItem);
	}

	/**
	 * Will return index if item is found
	 * @param {array} any
	 * @param {itemId} string
	 * @return {boolean} true/false
	 */
	static findIndex(array: any[], itemId: string): number {
		for (let index = 0; index < array.length; index++) {
			if (array[index].id === itemId) {
				return index;
			}
		}
		return -1;
	}

	static updateDOMTreeElement(componentId: string): void {
		//Trigger resize to redraw element
		const element = $("#" + componentId);
		element.trigger("resize");
	}

	/**
	 * Returns true if element is overflowed
	 *
	 * @static
	 * @param {*} element
	 * @returns {boolean}
	 *
	 * @memberOf Helper
	 */
	// Method not used?
	static isOverflowed(element: any): boolean {
		return element.scrollHeight > element.clientHeight || element.scrollWidth > element.clientWidth;
	}

	/**
	* Returns true if item is unique, looks first id at base then in manifest.
	*
	* @static
	* @param {targetItem} any
	* @returns {boolean}
	*
	*/
	static itemUnique(targetItem: any, items: any[]): boolean {
		return !items.some(
			(element: any, index: number, array: any[]): boolean => {
				if (element.id) {
					return (element.id.toLowerCase() === targetItem.id.toLowerCase());
				} else {
					return (element.manifest.id.toLowerCase() === targetItem.manifest.id.toLowerCase());
				}
			});
	}
}
