import { AngularConstants } from "../../angular-constants";

import { IDialogBaseScope } from "../../defines";
import { ConflictResolutionEnum, IConflictObject, IConflictResult } from "../../services/conflict-resolution-service";
import { DialogButtonType, IDialog, IDialogResult } from "../../services/dialog-service";

import { Helper } from "../../helper";

export class ImportConflictCtrl {
	public objectName: string;
	public remainingCount: number;
	public newName: string;
	public resolutionOption: string;
	public sameForAll: boolean = false;
	public currentActionName: string;
	public maxItems: number;
	public occupiedStrings: string[];

	public get ConflictResolutionEnum(): any {
		return ConflictResolutionEnum;
	}

	private dialog: IDialog<IConflictObject>;

	static $inject = [AngularConstants.SCOPE];

	constructor(private scope: IDialogBaseScope<IConflictObject>) {
		const dialog: IDialog<IConflictObject> = scope.eirDialog;
		this.dialog = dialog;
		const parameter = dialog.parameter;

		this.objectName = parameter.objectName;
		this.remainingCount = parameter.remainingCount;
		this.maxItems = parameter.maxItems;
		this.occupiedStrings = parameter.occupiedStrings;
		if (this.maxItems > this.occupiedStrings.length) {
			this.resolutionOption = parameter.lastAction
				? parameter.lastAction.toString()
				: ConflictResolutionEnum.REPLACE.toString();
		} else {
			this.resolutionOption = ConflictResolutionEnum.REPLACE.toString();
		}
		this.onChangeOption();
		this.newName = Helper.generateNewDisplayName(parameter.conflictedString, parameter.occupiedStrings);
	}

	public close(): void {
		this.dialog.close();
	}

	public resolve(): void {
		const result: IDialogResult<IConflictResult> = {
			value: {
				action: parseInt(this.resolutionOption),
				sameForRemaining: this.sameForAll,
				newName: this.newName
			},
			button: DialogButtonType.Ok
		};
		this.dialog.close(result);
	}

	public onChangeOption(): void {
		switch (parseInt(this.resolutionOption)) {
			case ConflictResolutionEnum.REPLACE:
				this.currentActionName = "Replace";
				break;
			case ConflictResolutionEnum.COPY:
				this.currentActionName = "Copy";
				break;
			case ConflictResolutionEnum.SKIP:
				this.currentActionName = "Skip";
				break;
			default:
				console.warn("[ImportConflictCtrl] Invalid option");
				break;
		}
	}

	static add(m: ng.IModule): void {
		m.controller("eirImportConflictCtrl", ImportConflictCtrl);
	}
}
