import {
	ChangeDetectorRef,
	Component,
	ElementRef,
	Renderer2,
} from "@angular/core";
import { SohoMessageService } from "ids-enterprise-ng";
import { take } from "rxjs/operators";

import { InteractionTriggerEnum, SubstitutionTags } from "ng2/common/constants/common.constant";

import type { IJsonButton } from "ng2/common/model/databearer/components/json-button.model";

import { ButtonMetadata } from "ng2/components/button/button.metadata";

import { BaseComponent } from "ng2/components/base/component";

import { DynamicComponentService } from "ng2/components/services/dynamic-component.service";
import { FrameworkService } from "ng2/components/services/framework.service";

@Component({
	selector: "ng2-button",
	template: `
		<button
			[soho-button]="style"
			soho-busyindicator
			[disabled]="busyIndicator.activated"
			(click)="onClick()"
		>
			{{ content }}
		</button>
	`
})
export class ButtonComponent extends BaseComponent {

	content: string;
	style: string;

	databearer: IJsonButton;

	constructor(
		elementRef: ElementRef,
		renderer: Renderer2,
		changeDetectorRef: ChangeDetectorRef,
		frameworkService: FrameworkService,
		dynamicComponentService: DynamicComponentService,
		sohoMessageService: SohoMessageService,
	) {
		super(
			elementRef,
			renderer,
			changeDetectorRef,
			frameworkService,
			dynamicComponentService,
			sohoMessageService,
		);
	}

	initializeBinding(componentDatabearer: IJsonButton): void {
		ButtonMetadata.BINDABLE_FIELDS_2.forEach(field => {
			const self: any = this;
			const bindingValue = componentDatabearer[field.ref].value;
			self[field.ref] = bindingValue === SubstitutionTags.VALUE ?
				"" : bindingValue;
		});
	}

	onClick(): void {
		this.trigger(InteractionTriggerEnum.CLICK)
			.pipe(take(1));
	}

}
