import { ComponentClassNames } from "ng2/common/constants/common.constant";
import type { IJsonTextBox } from "ng2/common/model/databearer/components/json-textbox.model";

import type { IEditorComponent } from "common/interface/ieditor-component";
import type { IEditorFlexbox } from "editor/components/editor-flexbox";

import { TextBox } from "components/textbox/textbox";
import { EditorComponentCtrl, IEditorComponentScope } from "editor/components/editor-component";

export interface IEditorTextbox extends IEditorComponent, TextBox {
	parent: IEditorFlexbox;
	// Overrides to make dual extend work
	getScope(): IEditorComponentScope;
	getDatabearer(): IJsonTextBox;
}

export class EditorTextboxCtrl extends EditorComponentCtrl {
	public static add(m: ng.IModule): void {
		m.controller(
			ComponentClassNames.EDITOR_COMPONENT_PREFIX + TextBox.name,
			EditorTextboxCtrl
		);
	}

	protected component: IEditorTextbox;

}
