import { Component } from "../component";

import { FrameworkService } from "../services/framework-service";

import { DEFAULT_BOUNDARY, HourMode, InteractionPairOperation, InteractionTriggerEnum, RegexStrings, SettingInputKeys, SettingKeys, SidebarTabTypes } from "ng2/common/constants/common.constant";

import { InteractionDefines } from "ng2/common/constants/interaction-defines.constant";

import { BindingTypeEnum } from "ng2/common/enums/binding-type.enum";
import { SizeTypeEnum } from "ng2/common/enums/size-type.enum";

import { ValueTypeEnum } from "ng2/common/enums/value-type.enum";

import type { IJsonTimepicker } from "common/model/component/timepicker.model";

import type { ITimepickerApi, ITimePickerOptions } from "common/model/sohoxi/sohoxi-timepicker.model";
import type { IComponentSettingCategory } from "ng2/common/model/components/settings/component-setting-category.model";
import type { IDataPair } from "ng2/common/model/data-pair.model";
import type { IJsonSize } from "ng2/common/model/databearer/components/json-component.model";

import type { IJsonBindableField } from "ng2/common/model/databearer/json-binding.model";

import type { IComponentScope } from "common/interface/icomponent-scope";
import type { ILayoutComponent } from "common/interface/ilayout-component";

export class Timepicker extends Component {
	static minuteInterval = 1;
	static secondInterval = 1;
	static availableHourMode: IDataPair[] = [
		{ name: "12-hour", value: HourMode.TWELVE },
		{ name: "24-hour", value: HourMode.TWENTYFOUR }
	];
	static DEFAULT_WIDTH: IJsonSize = { sizeType: SizeTypeEnum.AUTO };
	static DEFAULT_HEIGHT: IJsonSize = { sizeType: SizeTypeEnum.AUTO };
	static SOURCE_TRIGGERS = InteractionDefines.SOURCE_TRIGGERS.concat([InteractionPairOperation.ON_CHANGED]);
	static TARGET_ACTIONS: IDataPair[] = [
		InteractionPairOperation.GET,
		InteractionPairOperation.CLEAR
	];
	static DEFAULT_TRIGGER = InteractionPairOperation.ON_CHANGED;
	static DEFAULT_ACTION = InteractionPairOperation.GET;
	static SETTING_FIELDS: IComponentSettingCategory[] =
		[
			{
				title: "Style",
				tab: SidebarTabTypes.STYLE,
				settings: [
					{
						title: "Show Label",
						boundarySetting: true,
						ref: SettingKeys.SHOW_LABEL,
						inputType: SettingInputKeys.CHECKBOX,
						defaultValue: true,
					},
					{
						title: "Read Only",
						ref: SettingKeys.IS_READONLY,
						inputType: SettingInputKeys.CHECKBOX,
						defaultValue: false,
						button: {
							icon: "#icon-unlocked",
							tooltip: "Disable Component When...",
							dialog: true
						}
					},
				]
			},
			{
				title: "Format",
				tab: SidebarTabTypes.STYLE,
				settings: [
					{
						title: "Time Format",
						ref: SettingKeys.TIME_FORMAT,
						inputType: SettingInputKeys.DROPDOWN,
						dataset: Timepicker.availableHourMode,
						defaultValue: HourMode.TWENTYFOUR,
					},
					{
						title: "Enable Seconds",
						ref: SettingKeys.ENABLE_SECONDS,
						inputType: SettingInputKeys.CHECKBOX,
						defaultValue: false,
					},
				]
			},
			...Component.SETTING_FIELDS
		];

	static BINDABLE_FIELDS_2: IJsonBindableField[] = [
		{
			name: "Label",
			ref: SettingKeys.LABEL,
			valueType: ValueTypeEnum.LOCALIZED_STRING,
			write: true,
			read: false,
			defaultValue: { Type: BindingTypeEnum.STATIC_VALUE, value: "" }
		},
		{
			name: "Time",
			ref: SettingKeys.TIME_VALUE,
			valueType: ValueTypeEnum.STRING,
			write: true,
			read: true,
			defaultValue: { Type: BindingTypeEnum.STATIC_VALUE, value: "" }
		}
	];

	label: string;
	showLabel: boolean;
	timeFormat: string;
	enableSeconds: boolean;
	isReadonly: boolean;

	timeValue: string;

	formatString: string;
	timepickerOptions: ITimePickerOptions;
	timepickerApi: ITimepickerApi;

	constructor(
		obj: IJsonTimepicker,
		$element: ng.IAugmentedJQuery,
		parent: ILayoutComponent,
		frameworkService: FrameworkService
	) {
		super(obj, $element, parent, frameworkService);

		this.enableSeconds = obj.enableSeconds;
		this.isReadonly = obj.isReadonly;
		this.timeFormat = obj.timeFormat;
		this.formatString = this.timeFormat;
		this.showLabel = obj.boundarySettings[DEFAULT_BOUNDARY.id].showLabel;
	}

	init(): void {
		this.frameworkService.getTimeout()(() => {
			this.updateReadonly();
			this.setSeconds();
			const timepickerApi = this.getSohoXiApi();
			timepickerApi.element.on("change", () => {
				this.timeValue = timepickerApi.value(false);
				this.frameworkService.trigger(this, InteractionTriggerEnum.CHANGED);
			});
			this.setTimepickerOptions();
		});
	}

	setBindableValue(bindableReference: string, value: any): void {
		if (bindableReference === SettingKeys.TIME_VALUE) {
			this.setErrorTooltip();
			if (!value) {
				this.clear();
				return;
			}
			const upperCaseTime = (value as string).toUpperCase();
			if (this.isValidTime(upperCaseTime)) {
				this.setTime(upperCaseTime);
			} else {
				this.setErrorTooltip(`Invalid time: '${value}'`);
				this.clear();
			}
		} else {
			super.setBindableValue(bindableReference, value);
		}
	}

	newBoundaryEntered(boundaryId: string): void {
		super.newBoundaryEntered(boundaryId);
		this.setBoundarySettingIfChanged<boolean>(SettingKeys.SHOW_LABEL);
	}

	getSohoXiApi(): ITimepickerApi {
		if (!this.timepickerApi) {
			this.timepickerApi = this.element.find(`#time-field-${this.getId()}`).data("timepicker");
		}
		return this.timepickerApi;
	}

	refreshTimepicker(): void {
		this.updateReadonly();
		this.setSeconds();
		this.setTimepickerOptions();
		this.setTime($(`#time-field-${this.getId()}`).val() as string);
	}

	clear(): void {
		this.setTime("");
	}

	private setTimepickerOptions(): void {
		this.timepickerOptions = {
			timeFormat: this.formatString,
			minuteInterval: Timepicker.minuteInterval,
			secondInterval: Timepicker.secondInterval,
		};
	}

	private setTime(time: string): void {
		const field = $(`#time-field-${this.getId()}`);
		let finalTime: string;

		if (time) {
			// Extract time stamp from dummy date
			finalTime = Soho.Locale.formatDate(new Date(`2021-01-01 ${time}`), { pattern: this.formatString });
		} else {
			finalTime = "";
		}

		field.val(finalTime);
		this.timeValue = finalTime;
	}

	private setSeconds(): void {
		if (this.enableSeconds) {
			this.formatString = this.timeFormat.replace(":mm", ":mm:ss");
		} else {
			this.formatString = this.timeFormat;
		}
	}

	private updateReadonly(): void {
		const timepickerApi = this.getSohoXiApi();
		if (this.isReadonly) {
			timepickerApi.readonly();
		} else {
			timepickerApi.enable();
		}
	}

	private isValidTime(time: string): boolean {
		return RegexStrings.TIME.test(time);
	}

	static directive = [() => {
		return {
			template: require("./timepicker.html"),
			replace: true,
			link: ($scope: IComponentScope) => {
				const componentInstance = $scope.componentInstance as Timepicker;
				componentInstance.init();
				$scope.componentReady({ componentInstance: $scope.componentInstance });
			}
		};
	}];
}
