import { DEFAULT_BOUNDARY, RowHeight } from "ng2/common/constants/common.constant";
import { IJsonLookup } from "ng2/common/model/databearer/components/json-lookup.model";
import { ISettingsClassInstance } from "../../defines";
import { ComponentSettingsCtrl } from "../component-settings";

import { Lookup } from "../../../../components/lookup/lookup";
import { LookupContentSettingsDirective } from "./lookup-content-settings";

export class LookupSettings implements ISettingsClassInstance {
	private static instance = new LookupSettings();

	public static getInstance(): LookupSettings {
		return LookupSettings.instance;
	}

	public constructor() {
		if (LookupSettings.instance) {
			throw new Error("Error: Instantiation failed: Use LookupSettings.getInstance() instead of new.");
		}
	}

	public contentSettingsDirective: ng.IDirective = new LookupContentSettingsDirective();

	public create(id: string): IJsonLookup {
		const lookupSettings = ComponentSettingsCtrl.create(id, Lookup) as IJsonLookup;
		lookupSettings.boundarySettings[DEFAULT_BOUNDARY.id].rowHeight = RowHeight.MEDIUM;
		lookupSettings.columns = [];

		return lookupSettings;
	}
}
