import {
	InteractionPairOperation,
	SettingInputKeys,
	SettingKeys,
	SidebarTabTypes
} from "ng2/common/constants/common.constant";
import { InteractionDefines } from "ng2/common/constants/interaction-defines.constant";

import { BindingTypeEnum } from "ng2/common/enums/binding-type.enum";
import { SizeTypeEnum } from "ng2/common/enums/size-type.enum";
import { ValueTypeEnum } from "ng2/common/enums/value-type.enum";

import type { IComponentSettingCategory } from "ng2/common/model/components/settings/component-setting-category.model";
import type { IDataPair } from "ng2/common/model/data-pair.model";
import type { IJsonSize } from "ng2/common/model/databearer/components/json-component.model";
import type { IJsonBindableField } from "ng2/common/model/databearer/json-binding.model";

import { BaseComponentMetadata } from "ng2/components/base/component.metadata";

export class DropdownMetadata {
	static DEFAULT_WIDTH: IJsonSize = { sizeType: SizeTypeEnum.AUTO };
	static DEFAULT_HEIGHT: IJsonSize = { sizeType: SizeTypeEnum.AUTO };
	static SOURCE_TRIGGERS: IDataPair[] = InteractionDefines.SOURCE_TRIGGERS.concat(
		[InteractionPairOperation.ON_ITEM_CHANGED]
	);
	static TARGET_ACTIONS: IDataPair[] = [
		InteractionPairOperation.LIST,
		InteractionPairOperation.SET_CURRENT_ITEM,
		InteractionPairOperation.CLEAR
	];
	static DEFAULT_TRIGGER: IDataPair = InteractionPairOperation.ON_ITEM_CHANGED;
	static DEFAULT_ACTION: IDataPair = InteractionPairOperation.LIST;
	static SETTING_FIELDS =
		([
			{
				title: "Options",
				tab: SidebarTabTypes.CONTENT,
				settings: [{
					title: "Keep Selection",
					tooltip: "Keep previous selected item on reload. (With ION APIs, an object ID is preferred if using this)",
					ref: SettingKeys.KEEP_SELECTED,
					inputType: SettingInputKeys.CHECKBOX,
					defaultValue: true
				}]
			},
			{
				title: "Style",
				tab: SidebarTabTypes.STYLE,
				settings: [{
					title: "Show Label",
					boundarySetting: true,
					ref: SettingKeys.SHOW_LABEL,
					inputType: SettingInputKeys.CHECKBOX,
					defaultValue: true,
				}, {
					title: "Disabled",
					ref: SettingKeys.IS_READONLY,
					inputType: SettingInputKeys.CHECKBOX,
					defaultValue: false,
					button: {
						icon: "#icon-unlocked",
						tooltip: "Disable Component When...",
						dialog: true
					}
				}]
			}
		] as IComponentSettingCategory[]).concat(BaseComponentMetadata.SETTING_FIELDS);

	static BINDABLE_FIELDS_2: IJsonBindableField[] = [{
		name: "Selected Item",
		ref: SettingKeys.CURRENT_ITEM,
		valueType: ValueTypeEnum.OBJECT,
		write: false,
		read: true
	}, {
		name: "Label",
		ref: SettingKeys.LABEL,
		valueType: ValueTypeEnum.LOCALIZED_STRING,
		write: true,
		read: false,
		defaultValue: { Type: BindingTypeEnum.STATIC_VALUE, value: "" }
	}, {
		name: "Display Value",
		ref: SettingKeys.DISPLAY_VALUE,
		valueType: ValueTypeEnum.STRING,
		write: true,
		read: false,
		defaultValue: { Type: BindingTypeEnum.STATIC_VALUE, value: "" }
	}
	];
}
