import isEmpty from "lodash-es/isEmpty";
import isNil from "lodash-es/isNil";
import isUndefined from "lodash-es/isUndefined";

import {
	ButtonStyle,
	ChartTypeEnum,
	DEFAULT_BOUNDARY,
	ValueObject,
} from "ng2/common/constants/common.constant";

import type { IJsonButton } from "ng2/common/model/databearer/components/json-button.model";
import type { IJsonChart, IJsonChartDataset } from "ng2/common/model/databearer/components/json-chart.model";
import type { IExtendedDataGridColumn } from "ng2/common/model/databearer/components/json-datagrid.model";
import type { IJSonMaskSettings, IJsonTextBox } from "ng2/common/model/databearer/components/json-textbox.model";
import type { IHttpHeader } from "ng2/common/model/databearer/dataservice/json-datasource.model";
import type { IJsonRuntimeManifest } from "ng2/common/model/databearer/json-manifest.model";

import { HttpAcceptEnum } from "ng2/common/enums/http-accept-enum";

export class BackwardCompatible {
	static setDataServiceHeader(headers: IHttpHeader): IHttpHeader {
		return !isUndefined(headers) && !isEmpty(headers) ? headers : {
			Accept: HttpAcceptEnum.APPLICATION_JSON,
		};
	}

	static tryUpdateUrlName(manifest: IJsonRuntimeManifest): boolean {
		if (manifest.urlName === "url") { // backward compatbility
			const newUrlName = `(SETVARVALUES(InitURL=run/${manifest.id}))`;
			manifest.urlName = newUrlName;

			return true;
		}

		return false;
	}

	static tryGetMask(obj: IJsonTextBox): IJSonMaskSettings {
		if (obj.mask) { // backward compatbility
			return obj.mask;
		}

		return {
			process: "none"
		};
	}

	/**
	 * Check if showlabel is legacy format or not.
	 * Parameter is any beacuse it can be any component
	 * @param obj
	 */

	static getShowLabel(obj: any): boolean {
		const defaultBoundary = obj.boundarySettings[DEFAULT_BOUNDARY.id];
		if (!isNil(defaultBoundary.showLabel)) {
			return defaultBoundary.showLabel;
		} else {
			if (obj.showLabel) {
				obj.boundarySettings[DEFAULT_BOUNDARY.id].showLabel = obj.showLabel.value;
			} else {
				obj.boundarySettings[DEFAULT_BOUNDARY.id].showLabel = true;
			}
			return obj.boundarySettings[DEFAULT_BOUNDARY.id].showLabel;
		}
	}

	static tryGetBoxWidth(width: string): string {
		if (width) {
			return width;
		} else {
			return "input-default";
		}
	}

	static tryUpdateButtonStyle(obj: IJsonButton): string {
		let style: string;

		if (!obj.style) {
			if (obj.isDefault) {
				style = ButtonStyle.PRIMARY;
			} else {
				style = ButtonStyle.SECONDARY;
			}
			console.warn("Deprecated property found in Button component");
		} else {
			style = obj.style;
		}
		return style;
	}

	static getHref(rowData: any, column: IExtendedDataGridColumn): string {
		let hrefString = "";
		if (column.hyperlinkOptions) {
			if (column.field === ValueObject.STATIC_VALUE) {
				hrefString = column.hyperlinkOptions.value.replace("{$value}", column.text);
			} else {
				hrefString = column.hyperlinkOptions.value.replace("{$value}", rowData[column.field]);
			}
		} else {
			if (column.field === ValueObject.STATIC_VALUE) {
				hrefString = column.text;
			} else {
				hrefString = rowData[column.field];
			}
		}
		return hrefString;
	}

	static tryConvertLegacyChart(databearer: IJsonChart, obj: IJsonChart): void {
		const defaultBoundary = obj.boundarySettings[DEFAULT_BOUNDARY.id];
		if (this.isLegacyChart(databearer)) {
			if (this.isLegacyChartType(defaultBoundary.chartType) || !defaultBoundary.chartType) {
				let newType: ChartTypeEnum;
				switch (defaultBoundary.chartType) {
					case ChartTypeEnum.StackedColumn:
						newType = ChartTypeEnum.ColumnStacked;
						break;
					case ChartTypeEnum.Profile:
					case ChartTypeEnum.StackedLine:
						newType = ChartTypeEnum.Line;
						break;
					default: // unknown legacy chartType
						newType = ChartTypeEnum.Bar;
						break;
				}
				obj.boundarySettings[DEFAULT_BOUNDARY.id].chartType = newType;
			}

			if (this.hasLegacyChartData(databearer)) {
				this.convertToNewChartFormat(databearer);
			}
		}
		//Add default values if missing
		if (isNil(defaultBoundary.legendDisplay)) {
			obj.boundarySettings[DEFAULT_BOUNDARY.id].legendDisplay = true;
		}
		if (isNil(defaultBoundary.startupAnimationEnable)) {
			obj.boundarySettings[DEFAULT_BOUNDARY.id].startupAnimationEnable = true;
		}
	}

	private static isLegacyChart(databearer: IJsonChart): boolean {
		return this.hasLegacyChartData(databearer) ||
			this.isLegacyChartType(databearer.boundarySettings[DEFAULT_BOUNDARY.id].chartType) ||
			!databearer.boundarySettings[DEFAULT_BOUNDARY.id].chartType;
	}

	private static hasLegacyChartData(databearer: IJsonChart): boolean {
		return databearer.categories && databearer.categories.length > 0;
	}

	private static isLegacyChartType(chartType: ChartTypeEnum): boolean {
		return [ChartTypeEnum.Profile, ChartTypeEnum.StackedColumn, ChartTypeEnum.StackedLine].includes(chartType);
	}

	private static convertToNewChartFormat(databearer: IJsonChart): void {
		databearer.categories.forEach((category, i) => {
			let newDataset: IJsonChartDataset;
			if (databearer.series[i]) {
				newDataset = {
					name: category.name,
					categories: category.field,
					series: databearer.series[i].field,
				};
				databearer.datasets.push(newDataset); // push new format
			}
		});
		databearer.series = null; // remove legacy format
		databearer.categories = null; // remove legacy format
	}
}
