/**
 * Popup Menu Directive.
 */

import { Utils } from "../../sohoxi/utils";

export class PopupmenuDirective implements ng.IDirective {
	private static readonly DIRECTIVE_NAME = "xiPopupmenu";

	public restrict = "A";
	public scope = false;

	constructor(private timeout: ng.ITimeoutService) {}

	public link(
		scope: ng.IScope,
		element: ng.IAugmentedJQuery,
		attributes: ng.IAttributes
	): void {
		const unbindWatcher = scope.$watch(
			attributes[PopupmenuDirective.DIRECTIVE_NAME],
			newOptions => {
				// Init popup menu plugin. The timeout allows angular bindings to be evaluated
				this.timeout(() => {
					if (newOptions) {
						element.popupmenu(newOptions);
					} else {
						element.popupmenu();
					}
				}, 1);
			}
		);
		// Tear down
		element.on("$destroy", () => {
			const popupMenuData = Utils.getData(element, "popupmenu");
			if (popupMenuData) {
				popupMenuData.destroy();
			}
			unbindWatcher();
		});
	}

	public static add(m: ng.IModule): void {
		m.directive(PopupmenuDirective.DIRECTIVE_NAME, [
			"$timeout",
			timeout => {
				return new PopupmenuDirective(timeout);
			}
		]);
	}
}
