import { IAboutOptions } from "../../model/sohoxi/sohoxi-about.model";
import { Utils } from "../../sohoxi/utils";

/**
 * About Directive.
 *
 * Shows a modal dialog which contains information typical for about dialogs
 *
 * Example:
 * scope["options"]: IXiAboutOptions = {
 * 	appName: "SoHo Xi Test",
 * 	productName: "Test page for directives", version: "0.0.7",
 * 	useDefaultCopyright: true,
 * 	content: "<div><p>Additional content goes here</p></div>"
 * }
 *
 * <button class="btn about" id="about-dialog" xi-about="options">Open about modal</button>
 */
export class AboutDirective implements ng.IDirective {
	restrict = "A";
	scope = false;

	private static readonly DIRECTIVE_NAME = "xiAbout";

	link(
		scope: ng.IScope,
		element: ng.IAugmentedJQuery,
		attributes: ng.IAttributes
	): void {
		const options = attributes[AboutDirective.DIRECTIVE_NAME];
		const unbindWatcher = scope.$watch(
			options,
			(newOptions: IAboutOptions) => {
				const aboutComponent = Utils.getData(element, "about");
				if (aboutComponent) {
					aboutComponent.destroy();
					aboutComponent.modal.remove();
				}
				if (newOptions) {
					element.about(newOptions);
					// Replace the option specified additional content with a compiled angular version
					// if (newOptions.content) {
					// 	const content: any = this.$compile(newOptions.content)(scope);
					// 	Utils.getData(element, "about").modal.one("open", () => {
					// 		$("body")
					// 			.find("#about-modal" + id)
					// 			.find(".additional-content")
					// 			.html(content);
					// 		$("body")
					// 			.find("#about-modal" + id)
					// 			.find(".modal-body")
					// 			.css({
					// 				"max-height": "350px",
					// 				"overflow-y": "auto",
					// 			});
					// 	});
					// }
				} else {
					element.about();
				}
				// Support more than one about modal on the same page
				// Utils.getData(element, "about").modal.attr("id", "about-modal" + id);
				// element.attr("data-modal", "about-modal" + id);
			}
		);

		// Tear down
		element.on("$destroy", () => {
			const modalData = Utils.getData(element, "about");
			if (modalData) {
				modalData.destroy();

				const aboutSelector = "#about-modal";
				do {
					$(aboutSelector).remove();
				} while ($(aboutSelector).length > 0);
			}
			unbindWatcher();
		});
	}

	static add(m: ng.IModule): void {
		m.directive(AboutDirective.DIRECTIVE_NAME, () => new AboutDirective());
	}
}
