export class RepeatDoneDirective {
	public static add(m: ng.IModule): void {
		m.directive("repeatDone", () => {
			return (
				scope: ng.IRepeatScope,
				element: ng.IAugmentedJQuery,
				attrs: any
			) => {
				if (scope.$last) {
					scope.$eval(attrs.repeatDone);
				}
			};
		});
	}
}
