import {
	Component,
	Input,
	OnChanges,
	OnInit,
	SimpleChanges,
} from "@angular/core";
import type { IWidgetSize } from "lime"; // eslint-disable-line import/no-unresolved
import isEmpty from "lodash-es/isEmpty";

import { AppLauncherBase } from "ng2/app-launcher/app-launcher.base";

import { FrameworkService } from "ng2/components/services/framework.service";

@Component({
	selector: "ng2-app-launcher",
	template: `
		<ng2-runtime-canvas
			*ngIf="showMashup"
			[app]="runtimeApp"
			[widgetSize]="widgetSize"
		></ng2-runtime-canvas>
	`,
})
export class AppLauncherComponent
	extends AppLauncherBase
	implements OnInit, OnChanges {

	@Input() widgetSize: IWidgetSize;

	constructor(
		frameworkService: FrameworkService,
	) {
		super(frameworkService);
	}

	ngOnInit(): void {

	}

	ngOnChanges(changes: SimpleChanges): void {

		const projectIdChanges = changes.projectId;

		if (projectIdChanges.previousValue !== projectIdChanges.currentValue &&
			!isEmpty(projectIdChanges.currentValue)
		) {
			this.runtimeApp = this.frameworkService
				.loadProject(projectIdChanges.currentValue);

			this.showMashup = true;
		}
	}
}
