import {
	DEFAULT_BOUNDARY,
	ImagePositions,
	ListTemplateIdEnum,
} from "ng2/common/constants/common.constant";
import { OrientationEnum } from "ng2/common/enums/orientation.enum";
import { SizeTypeEnum } from "ng2/common/enums/size-type.enum";

import type { IJsonLabel } from "ng2/common/model/databearer/components/json-label.model";
import type { IJsonList } from "ng2/common/model/databearer/components/json-list.model";
import type { IJsonLayoutComponent } from "ng2/common/model/databearer/json-layout-component.model";
import type { IJsonImage } from "common/model/component/image.model";
import type { ISettingsClassInstance } from "editor/defines";

import { ComponentSettingsCtrl } from "editor/component-settings/component-settings";
import { FlexboxSettings } from "editor/component-settings/flexbox/flexbox-settings";
import { ListStyleSettingsDirective } from "editor/component-settings/list/list-style-settings";

import { Hyperlink } from "components/hyperlink/hyperlink";
import { Image } from "components/image/image";
import { Label } from "components/label/label";
import { List } from "components/list/list";

export class ListSettings implements ISettingsClassInstance {

	private static readonly instance: ListSettings = new ListSettings();

	public static getInstance(): ListSettings {
		return this.instance;
	}

	public readonly availableTemplates: { id: string, displayName: string }[] = [
		{ id: ListTemplateIdEnum.OneLabelFlexbox, displayName: "One Label" },
		{ id: ListTemplateIdEnum.TwoVerticalLabels, displayName: "Two Labels Vertical" },
		{ id: ListTemplateIdEnum.ImageAndLabels, displayName: "Image & Labels" },
		{ id: ListTemplateIdEnum.OneHyperlink, displayName: "One Hyperlink" },
		{ id: ListTemplateIdEnum.OneHyperlinkAndLabel, displayName: "Label & Hyperlink" },
		{ id: ListTemplateIdEnum.ImageLabelAndHyperlink, displayName: "Image, Label and Hyperlink"}
	];

	public styleSettingsDirective: ng.IDirective = new ListStyleSettingsDirective();

	public constructor() {
		if (ListSettings.instance) {
			throw new Error("Error: Instantiation failed: Use ListSettings.getInstance() instead of new.");
		}
	}

	public create(id: string): IJsonList {
		const newList = ComponentSettingsCtrl.create<IJsonList>(id, List);
		newList.boundarySettings[DEFAULT_BOUNDARY.id].itemTemplate =
			this.getTemplate(this.availableTemplates[0].id, id);
		return newList;
	}

	public getTemplate(id: string, listId: string): IJsonLayoutComponent {
		let templateComponent: IJsonLayoutComponent;
		switch (id) {
			case ListTemplateIdEnum.OneLabelFlexbox:
				const labelFlexbox = FlexboxSettings
					.getInstance()
					.createWithoutPadding(ListTemplateIdEnum.OneLabelFlexbox);

				labelFlexbox.children.push(ComponentSettingsCtrl.create("OneLabel" + listId, Label));
				templateComponent = labelFlexbox;
				break;
			case ListTemplateIdEnum.TwoVerticalLabels:
				const flexboxVertical = FlexboxSettings
					.getInstance()
					.createWithoutPadding(ListTemplateIdEnum.TwoVerticalLabels);

				const topLabel = this.createLabel(listId, "TopLabel");
				const bottomLabel = ComponentSettingsCtrl.create<IJsonLabel>("BottomLabel" + listId, Label);

				flexboxVertical.children.push(topLabel);
				flexboxVertical.children.push(bottomLabel);

				templateComponent = flexboxVertical;
				break;
			case ListTemplateIdEnum.ImageAndLabels:
				const imageFlexbox = FlexboxSettings
					.getInstance()
					.createWithoutPadding(ListTemplateIdEnum.ImageAndLabels, OrientationEnum.HORIZONTAL);

				const image = this.createImage(listId);
				const innerFlexbox = FlexboxSettings.getInstance().createWithoutPadding("InnerFlexbox");
				const label1 = this.createLabel(listId, "Label1");
				const label2 = ComponentSettingsCtrl.create<IJsonLabel>("Label2" + listId, Label);

				innerFlexbox.children.push(label1);
				innerFlexbox.children.push(label2);

				imageFlexbox.children.push(image);
				imageFlexbox.children.push(innerFlexbox);
				templateComponent = imageFlexbox;
				break;
			case ListTemplateIdEnum.OneHyperlink:
				const hyperlinkFlexbox = FlexboxSettings
					.getInstance()
					.createWithoutPadding(ListTemplateIdEnum.OneHyperlink);

				hyperlinkFlexbox.children.push(ComponentSettingsCtrl.create("Hyperlink" + listId, Hyperlink));
				templateComponent = hyperlinkFlexbox;
				break;
			case ListTemplateIdEnum.OneHyperlinkAndLabel:
				const flexboxHyperlinkVertical = FlexboxSettings
					.getInstance()
					.createWithoutPadding(ListTemplateIdEnum.OneHyperlinkAndLabel);

				const label = this.createLabel(listId, "Label");
				const hyperlink = ComponentSettingsCtrl.create("Hyperlink" + listId, Hyperlink);

				flexboxHyperlinkVertical.children.push(label);
				flexboxHyperlinkVertical.children.push(hyperlink);

				templateComponent = flexboxHyperlinkVertical;
				break;
			case ListTemplateIdEnum.ImageLabelAndHyperlink:
				const imageHyperlinkFlexbox = FlexboxSettings
					.getInstance()
					.createWithoutPadding(ListTemplateIdEnum.ImageLabelAndHyperlink, OrientationEnum.HORIZONTAL);

				const imagePiece = this.createImage(listId);
				const innerFlexboxPiece = FlexboxSettings.getInstance().createWithoutPadding("InnerFlexbox");
				const labelPiece = this.createLabel(listId, "Label");
				const hyperlinkPiece = ComponentSettingsCtrl.create("Hyperlink" + listId, Hyperlink);

				innerFlexboxPiece.children.push(labelPiece);
				innerFlexboxPiece.children.push(hyperlinkPiece);

				imageHyperlinkFlexbox.children.push(imagePiece);
				imageHyperlinkFlexbox.children.push(innerFlexboxPiece);
				templateComponent = imageHyperlinkFlexbox;
				break;
			default:
				console.warn("[DataListpanelSettingsCtrl] Invalid template choice");
		}

		return templateComponent;
	}

	private createLabel(listId: string, name: string): IJsonLabel {
		const label = ComponentSettingsCtrl.create<IJsonLabel>(name + listId, Label);

		label.fontStyle = "medium-heading";
		label.boundarySettings[DEFAULT_BOUNDARY.id].margin = {
			top: 2,
			right: 0,
			bottom: 10,
			left: 0
		};

		return label;
	}

	private createImage(listId: string): IJsonImage {
		const image = ComponentSettingsCtrl.create<IJsonImage>("Image" + listId, Image);
		image.boundarySettings[DEFAULT_BOUNDARY.id].width = { sizeType: SizeTypeEnum.PIXELS, value: 40 };
		image.boundarySettings[DEFAULT_BOUNDARY.id].height = { sizeType: SizeTypeEnum.PIXELS, value: 40 };
		image.boundarySettings[DEFAULT_BOUNDARY.id].margin = {
			top: -1,
			right: 15,
			bottom: 2,
			left: 0
		};
		image.position = ImagePositions.FIT;

		return image;
	}
}
