import { IExtendedDataGridColumn } from "ng2/common/model/databearer/components/json-datagrid.model";
import { IMap } from "ng2/common/model/imap.model";

import { FormatterEnum, IComponentRendered } from "ng2/common/constants/sohoxi.constant";
import {
	IDatagridApi,
	IDatagridColumnArgs,
	IDataGridOptions,
	IDataGridRow,
} from "ng2/common/model/sohoxi/sohoxi-datagrid.model";
import { IDialogBaseScope } from "../../../../../../common/defines";
import { Helper } from "../../../../../../common/helper";

import { DialogButtonType, IDialog, IDialogResult } from "../../../../../../common/services/dialog-service";
import { IOnComponentChangeNotification, NotifiableComponent } from "../../../../../../common/shared-components/base/notifiable-component";
import { DATAGRID_RENDERED } from "../../../../../../common/sohoxi/component/datagrid-directive";
import { DatagridHelper } from "../../../../../../common/sohoxi/datagrid-helper";

export interface IRequestHeaderItem {
	key: string;
	value: string;
}
export class RequestEditorDialogCtrl extends NotifiableComponent {

	public dialog: IDialog<IMap<string>>;

	public errorList: [] = [];
	public isInitialValid: boolean = false;
	public newParameter: IRequestHeaderItem;
	public dataset: IRequestHeaderItem[] = [];
	public datagridOptions: IDataGridOptions<IRequestHeaderItem>;
	public readonly datagridId = "requestEditorDatagrid";
	public datagridApi: IDatagridApi;
	public hasError: boolean = false;
	public editMode: boolean = false;

	public constructor(
		$scope: IDialogBaseScope<IMap<string>>,
	) {
		super();

		this.dialog = $scope.eirDialog;
		this.initDatagridOptions();

		$("#request-header-datagrid")
			.on(DATAGRID_RENDERED, (_, result: IComponentRendered) => {
				this.datagridApi = result.api;
			})
			.on("entereditmode", () => {
				$scope.$applyAsync(() => {
					this.editMode = true;
				});
			})
			.on("exiteditmode", (_, args: IDatagridColumnArgs<IRequestHeaderItem>) => {
				$scope.$applyAsync(() => {
					this.hasError = args.target.hasClass("error");
					this.editMode = false;
				});
				if (args.value !== args.oldValue) {
					this.datagridApi.validateCell(args.row, args.cell);
				}
			});
	}

	public add(): void {
		this.dataset.push(this.newParameter);
		delete this.newParameter;
	}

	public close(): void {
		this.dialog.close();
	}

	public save(): void {
		const result = this.dataset
			.map((item: { key: string, value: string }) => {
				return `${item.key}:${item.value}`;
			})
			.join("\n");
		const dialogResult: IDialogResult<string> = {
			button: DialogButtonType.Ok,
			value: result
		};

		this.dialog.close(dialogResult);
	}

	private initDatagridOptions(): void {
		const createDatagridColumns = (): IExtendedDataGridColumn[] => {
			const columns = [
				{
					id: "key",
					name: "Key",
					field: "key",
					width: "40%",
					editable: true,
					formatterName: FormatterEnum.Text,
					validate: "required"
				},
				{
					id: "value",
					name: "Value",
					field: "value",
					width: "40%",
					editable: true,
					formatterName: FormatterEnum.Text,
					validate: "required"
				},
				{
					id: "action",
					name: "Action",
					field: "",
					width: "20%",
					align: "center",
					formatter: Soho.Formatters.Button,
					text: "Remove",
					click: (_: JQueryEventObject, rowsSelected: IDataGridRow<IRequestHeaderItem>[]) => {
						if (rowsSelected.length === 1) { // only one should have been selected
							Helper.removeItemFromListByIndex(this.dataset, rowsSelected[0].row);
							this.datagridApi.updateDataset(this.dataset, null);
						}
					}
				}
			];

			columns.forEach(
				column => DatagridHelper.setEditableOnColumn(column));

			return columns;
		};

		const getDataset = (): IRequestHeaderItem[] => {
			const headerObject = this.dialog.parameter;
			const dataset: IRequestHeaderItem[] = [];
			for (const key in headerObject) {
				dataset.push({
					key,
					value: headerObject[key],
				});
			}

			return dataset;
		};

		const columns = createDatagridColumns();
		this.dataset = getDataset();

		this.datagridOptions = {
			columns,
			dataset: this.dataset,
			editable: true,
			id: this.datagridId,
		};
	}

	protected notify(
		$data: IOnComponentChangeNotification
	): boolean {
		if ($data.id.indexOf("key") !== -1) {
			this.isInitialValid =
				!Helper.isUndefinedEmpty($data.value) &&
				!Helper.isUndefinedEmpty(this.newParameter.value);
		} else if ($data.id.indexOf("value") !== -1) {
			this.isInitialValid =
				!Helper.isUndefinedEmpty(this.newParameter.key) &&
				!Helper.isUndefinedEmpty($data.value);
		}
		return this.isInitialValid;
	}

	public static add(m: ng.IModule): void {
		m.controller("eirRequestEditorDialogCtrl", RequestEditorDialogCtrl);
	}
}
