import angular from "angular";
import { AngularConstants } from "../../../../common/angular-constants";

import { DateFormat, InjectNames, MaskProcess } from "ng2/common/constants/common.constant";
import { IDataPair } from "ng2/common/model/data-pair.model";
import { IJSonMaskSettings } from "ng2/common/model/databearer/components/json-textbox.model";

import { IDialogBaseScope } from "../../../../common/defines";
import { DialogButtonType, DialogService, IDialog, IDialogResult } from "../../../../common/services/dialog-service";
import { IValidatorError } from "../../../../common/shared-components/base/field-validation";
import { NotifiableComponent } from "../../../../common/shared-components/base/notifiable-component";

export class MaskSettingsDialogCtrl extends NotifiableComponent {

	public AVAILABLE_FORMATS: IDataPair[] = [
		{ name: `${DateFormat.FORMAT_1}`, value: DateFormat.FORMAT_1 },
		{ name: `${DateFormat.FORMAT_2}`, value: DateFormat.FORMAT_2 },
		{ name: `${DateFormat.FORMAT_3}`, value: DateFormat.FORMAT_3 },
		{ name: `${DateFormat.FORMAT_4}`, value: DateFormat.FORMAT_4 },
		{ name: `ISO8601: ${DateFormat.ISO8601}`, value: DateFormat.ISO8601 },
		//{ name: `M3: ${DateFormat.M3}`, value: DateFormat.M3 } TODO: not supported currently in IDS
	];

	public maskSetting: string;
	public mask: IJSonMaskSettings;
	public errorList: IValidatorError[] = [];
	public get isPrefixInvalid(): boolean {
		return this.errorList.length !== 0;
	}
	private dialog: IDialog<IJSonMaskSettings>;

	static $inject = [
		AngularConstants.SCOPE,
		InjectNames.DIALOG_SERVICE
	];

	constructor(
		scope: IDialogBaseScope<IJSonMaskSettings>,
		private dialogService: DialogService
	) {
		super();
		const dialog: IDialog<IJSonMaskSettings> = scope.eirDialog;
		this.dialog = dialog;
		this.maskSetting = dialog.parameter.process;
		this.mask = dialog.parameter;
	}

	public onChangeMask(): void {
		if (this.maskSetting === MaskProcess.NUMBER) {
			this.mask = {
				process: this.maskSetting,
				allowNegative: this.mask.allowNegative,
				integerLimit: this.mask.integerLimit,
				decimalLimit: this.mask.decimalLimit,
				allowThousandsSeparator: this.mask.allowThousandsSeparator,
				prefix: this.mask.prefix
			};
		} else if (this.maskSetting === MaskProcess.DATE_TIME || this.maskSetting === MaskProcess.STRING) {
			this.mask = {
				process: this.maskSetting,
				format: this.mask.format
			};
		}
	}

	public openMaskHelp(): void {
		let message: string;
		if (this.maskSetting === MaskProcess.STRING) {
			// tslint:disable:max-line-length
			message =
				"Use these characters to create your string masking patterns:<br><br>" +
				"<b>#</b> - (Pound or number symbol) This character allows only numerals, 0 through 9.<br><br>" +
				"<b>x</b> - (Lower case ‘x’) This character allows only alphabetic letters, both upper case and lower case.<br><br>" +
				"<b>*</b> - (Asterisk) This character allows both alphabetic and numeric characters.<br><br>" +
				"<b>a</b> - (Lower case ‘a’) This character allows only ‘a’, ‘A’, ‘p’, or ‘P’. It is used to represent the first letter in a time period designation (AM or PM). <br><br>" +
				"<b>m</b> - (Lower case ‘m’) This character allows only ‘m’ or ‘M’. It is used to represent the second letter in a time period designation (AM or PM). ";
		} else if (this.maskSetting === MaskProcess.DATE_TIME) {
			message =
				"A date format can be constructed by assembling the desired date and time format characters;<br> for example: yyyy-MM-DD HH:mm.<br><br>" +
				"When creating a date-time format, these characters can be used as placeholders for input:<br><br>" +
				"<b>MMMM</b> - Displays the full name of the month; for example, “August”.<br>" +
				"<b>MMM</b> - Displays the abbreviated name of the month; for example, “Aug”.<br>" +
				"<b>MM</b> - Displays the number of the month, in 2-digit format; for example, “08” (for August).<br>" +
				"<b>M</b> - Displays the number of the month unpadded.<br><br>" +
				"<b>dd</b> - Displays the day/date padded with zeroes.<br>" +
				"<b>d</b> - Displays the day/date unpadded.<br><br>" +
				"<b>yyyy</b> - Displays the year using a 4-digit format. You should always use this format for the year.<br>" +
				"<b>yy</b> - Accepts input for the year using a 2-digit format, but if used, this is converted to the 4-digit format.<br><br>" +
				"<b>hh</b> - Displays the hour using a 12-hour format, with padding.<br>" +
				"<b>h</b> - Displays the hour using a 12-hour format, without padding.<br>" +
				"<b>HH</b> - Displays the hour using a 24-hour format, with padding.<br>" +
				"<b>H</b> - Displays the hour using a 24-hour format, without padding.<br><br>" +
				"<b>a</b> - Displays the am/pm part of the time with the 12-hour format.<br>" +
				"<b>mm</b> - Displays the minute digits.<br><br>" +
				"<b>ss</b> - Displays the seconds digits.<br>" +
				"<b>SSS</b> - Displays the milliseconds digits.<br><br>" +
				"<b>zz</b> - Displays the time zone name in short form, for example, “EST”, using the language of the current locale.<br>" +
				"<b>zzzz</b> - Displays the time zone name in long form; for example, “Eastern Standard Time”, using the language of the current locale.<br><br>" +
				"<b>EEEE</b> - Displays the full name of the day; for example: “Monday”";
		}
		// tslint:enable:max-line-length

		const showOptions = {
			message: message,
			title: "Masking Help",
			cssClass: "mask-options-help"
		};
		this.dialogService.showMessage(showOptions);
	}

	public close(): void {
		this.dialog.close();
	}

	public save(): void {
		const result: IDialogResult<IJSonMaskSettings> = {
			button: DialogButtonType.Ok,
			value: this.mask
		};
		this.dialog.close(result);
	}

	static add(m: ng.IModule): void {
		m.controller("eirMaskSettingsDialogCtrl", MaskSettingsDialogCtrl);
	}
}
