/**
 * Contains various utilities functions (as static methods).
 */
import jsonpath from "jsonpath";

import { Helper } from "../../../../common/helper";

import { RegexStrings } from "ng2/common/constants/common.constant";

import { DatasourceTypeEnum } from "ng2/common/enums/datasource-type.enum";
import { ParameterTypeEnum } from "ng2/common/enums/parameter-type.enum";
import { ValueTypeEnum } from "ng2/common/enums/value-type.enum";

import { IJsonGenericDatasource2 } from "ng2/common/model/databearer/dataservice/json-datasource.model";
import { IParameterObject } from "ng2/common/model/dataservice/parameter-object.model";

import { DateHelper } from "ng2/common/helpers/date.helper";

export type jsonValueTypes = string | number | Date | boolean;
export class DatasourceWizardHelper {
	static isSupportedIonApiDataservice(suite: string): boolean {
		const value = suite.toUpperCase();
		const result =
			value.indexOf(DatasourceTypeEnum.IDM) !== -1 ||
			value.indexOf(DatasourceTypeEnum.IDO) !== -1 ||
			value.indexOf(DatasourceTypeEnum.M3) !== -1 ||
			value.indexOf(DatasourceTypeEnum.MINGLE) !== -1;

		return result;
	}

	static generateParameters(
		datasource: IJsonGenericDatasource2,
		isInput = false
	): IParameterObject[] {
		const root = isInput ? datasource.inputRootElement : datasource.outputRootElement;
		const parameters: IParameterObject[] = [];
		const jsonData = isInput ? datasource.bodyTemplate : datasource.outputJson;
		const result = jsonpath.query(JSON.parse(jsonData), root);

		// if results exist, use them to find the parameters
		if (result.length === 1) {
			this.findParameters(result[0], parameters, "", datasource.suite);
			this.renameDuplicates(parameters);
		}

		return parameters;
	}

	private static renameDuplicates(parameters: IParameterObject[]): void {
		parameters.forEach((parameter, index, parameters) => {
			const alias = parameter.alias.toLowerCase();
			const id = parameter.id;
			let count = 1;
			parameters.forEach((parameter2) => {
				if (alias && alias === parameter2.alias.toLowerCase() && id !== parameter2.id) {
					count++;
					parameter2.alias = parameter2.alias + count;
				}
			});
		});
	}

	private static findParameters(
		object: Object,
		parameters: IParameterObject[],
		path = "",
		suite: string
	): void {
		Object.entries(object).forEach(([key, value]: any) => {
			if (typeof value === "object" && !Array.isArray(value)) { // value is a object
				this.findParameters(value, parameters, path + "." + key, suite);
			} else if (Array.isArray(value)) { // is array (assume all content is same type)
				if (typeof value[0] === "object") { // is array of objects
					// scenarios
					// 1. template object, e.g [{ "prop1": "string", "prop2": "int" ...}]
					// 3. M3 metadata [{"@name": "value" ...}, ...]
					// 2. named key-value pair (M3), e.g [{ "Key": "name", "Value": "val"}, ...]

					if (value.length === 1 && suite !== "/M3") { // template
						this.findParameters(value[0], parameters, path + "." + key + "[*]", suite);
					} else if (Object.entries(value[0])[0][0].includes("@")) { // metadata (M3)
						value.forEach((element, index) => {
							this.findParameters(element, parameters, path + "." + key + "[" + index + "]", suite);
						});
					} else {
						let propertyName = "";
						value.forEach((element, index, list) => {
							const entries = Object.entries<string>(element);
							let propertyValue: jsonValueTypes = "";

							// tslint:disable-next-line:no-magic-numbers
							if (entries.length === 2 && entries[1].length === 2) {
								propertyValue = entries[1][1];
								propertyName = entries[1][0];

								if (suite === "/M3") {
									propertyValue = this.getm3OnpremValue(propertyValue);
								}
							} else if (Helper.isUndefinedEmpty(propertyName)) {
								/*
									if entries[1][1] happens to be undefined then the loop will
									look forward to see if any other entries infront has entry which is defined
									and set the propertyName accordingly.
								*/
								let doIndex = index;
								do {
									const entries = Object.entries<string>(list[doIndex]);
									// tslint:disable-next-line:no-magic-numbers
									if (entries.length === 2 && entries[1].length === 2) {
										propertyName = entries[1][1];
									}
									doIndex++;

								} while (Helper.isUndefinedEmpty(propertyName) ||
									doIndex > value.length);
							}

							parameters.push(
								DatasourceWizardHelper.createParameterObject(
									`${path}.${key}[?(@.${entries[0][0]}=='${entries[0][1]}')].${propertyName}`,
									entries[0][1],
									propertyValue
								)
							);
						});
					}
				} else { // array of values (string / int)
					parameters.push(DatasourceWizardHelper.createParameterObject(path + "." + key, key));
				}
			} else { // property in object
				const newPath = key.includes("@") ? path + "['" + key + "']" : path + "." + key;
				parameters.push(DatasourceWizardHelper.createParameterObject(newPath, key, value, suite));
			}
		});
	}

	private static getm3OnpremValue(sample: string): jsonValueTypes {
		switch (sample) {  // M3 (named key-value pair)
			case "date":
				return "1985-04-12";
			case "date-time":
				return "1985-04-12T23:20:50.52Z";
			case "number":
				return 1;
			case "string":
			default:
				return "text";
		}
	}

	private static createParameterObject(
		key: string,
		alias: string,
		value?: any,
		dataserviceType?: string,
	): IParameterObject {
		const dataType = this.getParameterType(value, dataserviceType);
		const finalAlias = RegexStrings.FIRST_CHAR_LETTER.test(alias) ? alias : "";

		return {
			id: Helper.generateUniqueId(),
			key,
			valueType: ValueTypeEnum.STRING,
			parameterType: ParameterTypeEnum.OUTPUT,
			required: false,
			alias: finalAlias,
			dataType
		};
	}

	static getParameterType(value: any, dataserviceType: string): string {
		const type = typeof value;
		let dataType = "";
		switch (type) {
			case "number":
				dataType = this.getNumericType(value);
				break;
			case "string":
				dataType = this.getStringType(value, dataserviceType);
				break;
			case "boolean":
				dataType = "Boolean";
				break;
			default:
				dataType = "String";
				break;
		}

		return dataType;
	}

	static getStringType(
		value: string,
		dataserviceType = ""): string {

		let date = new Date(value);
		if (DatasourceWizardHelper.isSupportedIonApiDataservice(dataserviceType)) {
			date = DateHelper.parse(value);
		}

		if (date.toString() !== "Invalid Date") {
			if (date.getHours() !== 0) {
				const dateSplit = value.split(":");

				if (dateSplit.length > 1) {
					return "Date Time";
				}
				return "Date";
			}
		}
		if (value.toLowerCase() === "true" || value.toLowerCase() === "false") {
			return "Boolean";
		}

		if (jQuery.isNumeric(value)) {
			return this.getNumericType(value);
		}

		if (RegexStrings.EMAIL.test(value)) {
			return "Email";
		}

		return "String";
	}

	static getNumericType(value: string): string {
		const MIN_LONG_INTEGER = -9223372036854775808;
		const MAX_LONG_INTEGER = 9223372036854775808;
		const MIN_SHORT_INTEGER = -32768;
		const MAX_SHORT_INTEGER = 32768;
		const numericValue = Number(value);
		if (Helper.isFloat(numericValue)) {
			return "Decimal";
		} else {
			if (numericValue >= MIN_SHORT_INTEGER && numericValue <= MAX_SHORT_INTEGER) {
				return "Short Integer";
			}
			if (numericValue >= MIN_LONG_INTEGER && numericValue <= MAX_LONG_INTEGER) {
				return "Long Integer";
			}
		}

		return "Integer";
	}
}
