
import type { IJsonRuntimeMashup } from "ng2/common/model/databearer/json-mashup.model";
import type { IRuntimeManifest } from "ng2/common/model/project/mashup/runtime-manifest.model";

import { RuntimeManifest } from "ng2/common/project/manifest/runtime-manifest";
import { Mashup } from "ng2/common/project/mashup";

export class RuntimeApp extends Mashup {
	manifest: IRuntimeManifest;

	getManifest(): IRuntimeManifest {
		return this.manifest;
	}

	static fromObj(obj: IJsonRuntimeMashup): RuntimeApp {
		const runtimeApp = Mashup.create(RuntimeApp, RuntimeManifest, obj);

		return runtimeApp;
	}
}
