import { ComponentClassNames, SettingKeys } from "ng2/common/constants/common.constant";
import { IComponentFieldSetting } from "ng2/common/model/components/settings/component-field-setting.model";

import type { IJsonTextArea } from "common/model/component/textarea.model";
import type { IEditorComponent } from "common/interface/ieditor-component";

import { TextArea } from "components/textarea/textarea";
import { EditorComponentCtrl, IEditorComponentScope } from "editor/components/editor-component";
import { IEditorFlexbox } from "editor/components/editor-flexbox";
export interface IEditorTextArea extends IEditorComponent, TextArea {
	parent: IEditorFlexbox;
	// Overrides to make dual extend work
	getScope(): IEditorComponentScope;
	getDatabearer(): IJsonTextArea;
}

export class EditorTextAreaCtrl extends EditorComponentCtrl {
	public static add(m: ng.IModule): void {
		m.controller(
			ComponentClassNames.EDITOR_COMPONENT_PREFIX + TextArea.name,
			EditorTextAreaCtrl
		);
	}
	protected component: IEditorTextArea;

	public setSetting(settingMeta: IComponentFieldSetting, newSettingValue: number): void {
		super.setSetting(settingMeta, newSettingValue);
		switch (settingMeta.ref) {
			case SettingKeys.MAX_LENGTH:
				this.component.updateMaxLength();
				break;
			default:
				// Other settings
		}
	}
}
