import angular from "angular";
import { AngularConstants } from "../../../common/angular-constants";

import { InjectNames, SaveFlagsEnum, SettingKeys, ThemeEnum } from "ng2/common/constants/common.constant";
import { IJsonTag } from "ng2/common/model/databearer/json-tag.model";
import { ILanguage } from "ng2/common/model/language.model";
import { IDesignerMashup } from "ng2/common/model/project/mashup/designer-mashup.model";
import { DesignerManifest } from "ng2/common/project/manifest/designer-manifest";
import { IDialogBaseScope } from "../../../common/defines";
import { FeatureSwitchCtrl } from "../../../common/shared-components/base/feature-switch-ctrl";

import { DialogButtonType, DialogService, IDialog, IDialogResult } from "../../../common/services/dialog-service";
import { FeatureSwitchService } from "../../../common/services/feature-switch-service";
import { TranslationManagementService } from "../../../components/services/translation-management-service";
import { EditorService } from "../services/editor-service";
import { SaveService } from "../services/save-service";

export class ProjectSettingsCtrl extends FeatureSwitchCtrl {
	public manifest: DesignerManifest;
	public defaultTheme: any;
	public currentProject: IDesignerMashup;
	private originalManifest: DesignerManifest;
	// The object is created in this way to actually help with transformation from string to ThemeEnum
	public themes = {
		[ThemeEnum.LIGHT]: { id: ThemeEnum.LIGHT, name: "LIGHT" },
		[ThemeEnum.DARK]: { id: ThemeEnum.DARK, name: "DARK" },
		[ThemeEnum.HIGH_CONTRAST]: { id: ThemeEnum.HIGH_CONTRAST, name: "High Contrast" },
		[ThemeEnum.LIGHT_TRANSPARENT]: { id: ThemeEnum.LIGHT_TRANSPARENT, name: "LIGHT Transparent" },
		[ThemeEnum.DARK_TRANSPARENT]: { id: ThemeEnum.DARK_TRANSPARENT, name: "DARK Transparent" }
	} as any;
	private dialog: IDialog<DesignerManifest>;
	private errorList: string[] = [];
	private tagsChanged: boolean = false;
	private newTags: IJsonTag[] = [];

	public get isManifestInvalid(): boolean {
		return this.errorList.length !== 0;
	}

	static $inject = [
		AngularConstants.SCOPE,
		InjectNames.EDITOR_SERVICE,
		InjectNames.DIALOG_SERVICE,
		InjectNames.SAVE_SERVICE,
		InjectNames.TRANSLATION_MANAGEMENT_SERVICE,
		InjectNames.FEATURE_SWITCH_SERVICE
	];

	constructor(
		private scope: IDialogBaseScope<DesignerManifest>,
		private editorService: EditorService,
		private dialogService: DialogService,
		private saveService: SaveService,
		private translationManagementService: TranslationManagementService,
		featureService: FeatureSwitchService,
	) {
		super(featureService);

		const dialog: IDialog<DesignerManifest> = scope.eirDialog;
		this.dialog = dialog;
		this.originalManifest = dialog.parameter;
		this.manifest = angular.copy(this.originalManifest);
		this.defaultTheme = this.themes[this.manifest.defaultTheme];
		this.currentProject = this.editorService.getCurrentProject();
	}

	public close(): void {
		this.dialog.close();
	}

	public saveTags(tags: IJsonTag[]): void {
		this.tagsChanged = true;
		this.newTags = tags;
	}

	public save(): void {
		if (this.tagsChanged) {
			this.currentProject.setTags(this.newTags);
		}
		if (this.manifest.title !== this.originalManifest.title) {
			const rootId = this.editorService.getCurrentRootComponentInstance().getId();
			const updatedValue: ILanguage = {
				locale: this.translationManagementService.getSelectedLocale(),
				value: this.manifest.title
			};
			this.translationManagementService.setTranslatableStringByProperty(
				rootId,
				SettingKeys.APPLICATION_HEADER, updatedValue
			);
		}
		if (this.manifest.displayName !== this.originalManifest.displayName) {
			this.editorService.renameProject(this.manifest.id, this.manifest.displayName).then(() => {
				this.saveManifest();
			}).catch((exception) => {
				this.errorOnSave(exception);
			});
		} else {
			this.saveManifest();
		}
	}

	private errorOnSave(exception: any): void {
		this.dialogService.showError("Server Error", exception.message);
		// Reset manifest and savestate when save fails
		this.editorService.setManifest(this.originalManifest);
		this.saveService.saveStateEvent.raise(this.saveService.saveStates.saved);
	}

	private saveManifest(): void {
		this.manifest.defaultTheme = this.defaultTheme.id;
		this.editorService.setManifest(this.manifest);
		//tslint:disable:no-bitwise
		/* Manifest needs to be saved first before translation is saved
		due to save translation also updates lastChanged property in manifest */
		this.editorService.saveCurrent(SaveFlagsEnum.MANIFEST).then(() => {
			this.editorService.saveCurrent(SaveFlagsEnum.TRANSLATIONS | SaveFlagsEnum.TAGS).then(() => {
				const result: IDialogResult<void> = { button: DialogButtonType.Ok };
				this.dialog.close(result);
			});
		}).catch((exception: any) => {
			this.errorOnSave(exception);
		});
		//tslint:enable:no-bitwise
	}

	static add(m: ng.IModule): void {
		m.controller("eirProjectSettingsCtrl", ProjectSettingsCtrl);
	}
}
