import { DateFormatEnum } from "ng2/common/constants/sohoxi.constant";
import { DatasourceTypeEnum } from "ng2/common/enums/datasource-type.enum";
import { ParameterTypeEnum } from "ng2/common/enums/parameter-type.enum";
import { IDataPair } from "ng2/common/model/data-pair.model";
import { IJsonDatasource, IJsonM3Datasource } from "ng2/common/model/databearer/dataservice/json-datasource.model";
import { IParameterObject } from "ng2/common/model/dataservice/parameter-object.model";
import { IDataGridColumn, IFormatterFunction } from "ng2/common/model/sohoxi/sohoxi-datagrid.model";

import { DateHelper } from "ng2/common/helpers/date.helper";
import { Helper } from "../helper";

export class CustomFormatters {
	static DataSourceDescription(): IFormatterFunction {
		return (row: number, cell: number, value: string, col: IDataGridColumn, item: IJsonDatasource, datagrid: any) => {
			if (item.typedef === DatasourceTypeEnum.M3) {
				const m3Datasource = item as IJsonM3Datasource;
				if (m3Datasource.operations[0]) {
					const descriptionValue = m3Datasource.operations[0].dataParameters;
					value = (descriptionValue["MI.Program"] + "." + descriptionValue["MI.Transaction"]);
				} else {
					value = "";
				}
			}
			return value;
		};
	}

	static DataSourceParameterType(): IFormatterFunction {
		return (
			row: number,
			cell: number,
			value: string,
			col: IDataGridColumn,
			item: IParameterObject,
			datagrid: any
		): string => {
			switch (item.parameterType) {
				case ParameterTypeEnum.BODY_PART:
					return "Body(jsonpath)";
				case ParameterTypeEnum.MATRIX:
					return "Matrix";
				case ParameterTypeEnum.PATH:
					return "Path";
				case ParameterTypeEnum.QUERY:
					return "Query";
				default:
					return "Output";
			}
		};
	}

	static Multi(...formatters: IFormatterFunction[]): IFormatterFunction {
		return (row: number, cell: number, value: string, col: IDataGridColumn, item: any, datagrid: any) => {
			const result = formatters.reduce((value, formatter) => {
				return formatter(row, cell, value, col, item, datagrid);
			}, value);
			return result;
		};
	}

	static DataPairPresenter(dataPairList: IDataPair[]): IFormatterFunction {
		return (row: number, cell: number, value: string, col: IDataGridColumn, item: any, datagrid: any) => {
			const foundItem = dataPairList.find((datapair: IDataPair) => {
				return value === datapair.value;
			});
			return foundItem ? foundItem.name : "";
		};
	}

	static DatasourceSuiteFormatter(): IFormatterFunction {
		return (row: number, cell: number, value: string, col: IDataGridColumn, item: IJsonDatasource, datagrid: any) => {
			return value.replace("/", "");
		};
	}

	static RequiredFormatter(propertyName: string, checkValue: boolean = false): IFormatterFunction {
		return (row, cell, value, col, item) => {
			const empty = "";
			const checked = `
				<svg class="icon">
					<use xlink:href="#icon-check"></use>
				</svg>
			`;
			if ((checkValue && value >= 1) || !checkValue) {
				return item.hasOwnProperty(propertyName) && item[propertyName] ? checked : empty;
			}
			return empty;
		};
	}

	static Date(locale: string): IFormatterFunction {
		return (row: number, cell: number, value: string, col: IDataGridColumn, item: any, datagrid: any): string => {
			return CustomFormatters.Datetime(locale, DateFormatEnum.Short)(row, cell, value, col, item, datagrid);
		};
	}

	static Time(locale: string): IFormatterFunction {
		return (row: number, cell: number, value: string, col: IDataGridColumn, item: any, datagrid: any): string => {
			return CustomFormatters.Datetime(locale, DateFormatEnum.Timestamp)(row, cell, value, col, item, datagrid);
		};
	}

	static Datetime(locale: string, date: string = DateFormatEnum.Datetime): IFormatterFunction {
		return (row: number, cell: number, value: string, col: IDataGridColumn, item: any, datagrid: any): string => {
			if (Helper.isUndefinedEmpty(value)) {
				return value;
			}
			const options: IFormateDateOptions = {
				date,
				locale
			};
			const trimedValue = value.trim();
			const datetime = DateHelper.parse(trimedValue, date, locale);
			const result = Soho.Locale.formatDate(
				datetime.toString() !== "Invalid Date" ? datetime : trimedValue,
				options);

			return result.indexOf("NaN") === -1 ? result : trimedValue;
		};
	}

	static AbsoluteValue(): IFormatterFunction {
		return (row: number, cell: number, value: string, col: IDataGridColumn, item: any, datagrid: any) => {
			let parsedValue = parseInt(value, 10);

			if (Helper.isUndefinedEmpty(value) || Number.isNaN(parsedValue)) {
				return value;
			}

			if (+value%1 !== 0) {
				parsedValue = parseFloat(value);
			}

			return Math.abs(parsedValue).toString();
			
		};
	}
}
