import { ParameterTypeEnum } from "ng2/common/enums/parameter-type.enum";

import type { IParameterObject } from "ng2/common/model/dataservice/parameter-object.model";

export class DataServiceHelper {
	static readonly urlComponentRegex = "[^{}/=?&;]+";
	static readonly urlComponentRegexBracket = "{" + DataServiceHelper.urlComponentRegex + "}";

	private static id: number;

	private static getNextId(): string {
		const id = DataServiceHelper.id++;
		return id.toString();
	}

	static parse(resourceUrl: string): IParameterObject[] {
		DataServiceHelper.id = 0;
		const [path, matrix, query] = DataServiceHelper.deconstructPath(resourceUrl);

		const pathParameters = DataServiceHelper.getPathParameters(path);
		const matrixParameters = DataServiceHelper.getSpecificParameters(matrix, ParameterTypeEnum.MATRIX);
		const queryParameters = DataServiceHelper.getSpecificParameters(query, ParameterTypeEnum.QUERY);
		return pathParameters.concat(matrixParameters, queryParameters);
	}

	static deconstructPath(resourceUrl: string): string[] {
		const pathMatch = resourceUrl.match(/^\/?[^;?]+/);
		const matrixMatch = resourceUrl.match(/;[^?]+/);
		const queryMatch = resourceUrl.match(/\?[^#]+/);
		const hashMatch = resourceUrl.match(/#.+/);

		const restPath = pathMatch ? pathMatch[0] : "";
		const matrixPath = matrixMatch ? matrixMatch[0] : "";
		const queryPath = queryMatch ? queryMatch[0] : "";
		const hashTag = hashMatch ? hashMatch[0] : "";

		return [restPath, matrixPath, queryPath, hashTag];
	}

	/**
	 *	Method to get substitution tag for use in urls
		*
		* @param key used as name for MATRIX and QUERY, and just the tag for PATH. It will be URL encoded.
		* @param paramType (optional)
		*/
	static getURLVariable(key: string, paramType?: ParameterTypeEnum): string {
		let value: string;
		switch (paramType) {
			case undefined:
			case ParameterTypeEnum.PATH:
				value = "{" + encodeURIComponent(key) + "}";
				break;
			case ParameterTypeEnum.MATRIX:
			case ParameterTypeEnum.QUERY:
				value = encodeURIComponent(key) + "={" + encodeURIComponent(key) + "}";
				break;
			default:
				value = key;
		}
		return value;
	}

	private static getPathParameters(resourceUrl: string): IParameterObject[] {
		const regexp = new RegExp("/" + this.urlComponentRegexBracket, "g");
		const result = resourceUrl.match(regexp);
		if (!result) {
			return [];
		}

		const output = result.map<IParameterObject>((item) => {
			return {
				id: DataServiceHelper.getNextId(),
				key: decodeURIComponent(item.match(new RegExp(this.urlComponentRegex))[0]),
				valueType: null,
				parameterType: ParameterTypeEnum.PATH,
				required: true,
				alias: ""
			} as any;
		});
		return output;
	}

	private static getSpecificParameters(queryPath: string, type: ParameterTypeEnum): IParameterObject[] {
		const regexp = new RegExp(this.urlComponentRegex + "=" + this.urlComponentRegexBracket, "g");
		const result = queryPath.match(regexp);
		let output: IParameterObject[] = [];
		if (result) {
			output = result.map<IParameterObject>((item) => {
				return {
					id: DataServiceHelper.getNextId(),
					key: decodeURIComponent(item.match(new RegExp(this.urlComponentRegex))[0]),
					valueType: null,
					parameterType: type,
					required: false,
					alias: ""
				} as any;
			});
		}
		return output;
	}

	/**
	 * Validate if resource URL conforms to correct syntax
	 * @param resourceUrl
	 */
	static validate(resourceUrl: string): boolean {
		let result = true;
		const [path, matrix, query, hashTag] = DataServiceHelper.deconstructPath(resourceUrl);

		if (path.length > 0) {
			result = DataServiceHelper.validatePath(path);
		}
		if (matrix.length > 0) {
			result = result && DataServiceHelper.validateUrlPart(matrix, ParameterTypeEnum.MATRIX);
		}
		if (query.length > 0) {
			result = result && DataServiceHelper.validateUrlPart(query, ParameterTypeEnum.QUERY);
		}

		return result;
	}

	private static validatePath(path: string): boolean {
		const pathElements = path.split("/").filter(DataServiceHelper.emptyItem);
		const errorsFound = pathElements.filter(item => {
			const pathRegex = new RegExp("^" + this.urlComponentRegexBracket + "$|^" + this.urlComponentRegex + "$");
			return item.match(pathRegex) === null;
		});

		return errorsFound.length === 0;
	}

	private static validateUrlPart(query: string, type: ParameterTypeEnum): boolean {
		const twoElements = 2;
		const deliminator: any =
			type === ParameterTypeEnum.MATRIX ? ";" : new RegExp(/[?&]/);
		const queryElements = query.split(deliminator).filter(DataServiceHelper.emptyItem);
		const result = queryElements.filter(item => {
			if (item.indexOf("=") === -1) {
				return true;
			}
			const elem = item.split("=").filter(DataServiceHelper.emptyItem);

			// verify it is two elements
			if (elem.length !== twoElements) {
				return true;
			}
			// verify Name
			let nameMatch = elem[0].match("^" + this.urlComponentRegex + "$");
			if (!nameMatch) {
				return true;
			}
			// Verify Value
			nameMatch = elem[1].match("^" + this.urlComponentRegexBracket + "$");
			if (!nameMatch) { // Static value
				if (elem[1].match("^" + this.urlComponentRegex + "$") === null) {
					return true;
				}
				return false;
			} else { // Variable value
				const pairMatch = item.match(new RegExp("^(" + this.urlComponentRegex + ")={\\1}$"));
				if (!pairMatch) {
					return true;
				}
			}

			return false;
		});
		return result.length === 0; // equals zero means no errors found
	}

	static emptyItem(item: string): boolean {
		return item.length > 0;
	}
}
