import angular from "angular";

import { AngularConstants } from "../../../../common/angular-constants";

import type { IAvailableSourceField, IDialogBaseScope } from "../../../../common/defines";
import { Helper } from "../../../../common/helper";
import type { IAccordionApi } from "../../../../common/model/sohoxi/sohoxi-accordion.model";
import { DialogButtonType, DialogService, IDialog, IDialogOptions, IDialogResult } from "../../../../common/services/dialog-service";
import { FeatureSwitchService } from "../../../../common/services/feature-switch-service";
import { FeatureSwitchCtrl } from "../../../../common/shared-components/base/feature-switch-ctrl";
import { DatagridHelper } from "../../../../common/sohoxi/datagrid-helper";
import { Datagrid } from "../../../../components/datagrid/datagrid";
import { TranslationManagementService } from "../../../../components/services/translation-management-service";
import { EditorService } from "../../services/editor-service";
import type { IBuildLinkParameter, IBuildLinkResponse } from "../build-link-dialog/build-link-dialog";
import type { IEditorComponentWithUpsertDatagridColumnSettings, IUpsertOption } from "../upsert-datagrid-columns-content-settings";

import { ColumnFormatterType, IChanged, InjectNames, SummaryTextPlacement, TextAlignment, ValueObject } from "ng2/common/constants/common.constant";
import { ColumnOptionEnum, FormatterEnum } from "ng2/common/constants/sohoxi.constant";

import type { ISourceItem } from "common/model/component/hyperlink.model";
import type { IAutoCompleteOptions } from "common/model/sohoxi/sohoxi-autocomplete.model";
import type { ILookupOptions } from "common/model/sohoxi/sohoxi-lookup.model";
import type { IDataPair } from "ng2/common/model/data-pair.model";
import type { IExtendedDataGridColumn } from "ng2/common/model/databearer/components/json-datagrid.model";
import type { IJsonGenericDatasource2 } from "ng2/common/model/databearer/dataservice/json-datasource.model";
import type { IParameterObject } from "ng2/common/model/dataservice/parameter-object.model";
import type { IMap } from "ng2/common/model/imap.model";

export interface IColumnFormatter {
	name: string;
	value: string;
	type: number;
}

export class UpsertColumnCtrl extends FeatureSwitchCtrl {
	availableDataservices: IJsonGenericDatasource2[];
	isAdd: boolean;
	selectedSourceField: IAvailableSourceField;
	selectedFormatter: IColumnFormatter;
	selectedListSource: IJsonGenericDatasource2;
	selectedSubstituteSource: IJsonGenericDatasource2;
	selectedListOutput: IParameterObject;
	selectedToSubstituteOutput: IParameterObject;
	selectedFromSubstituteOutput: IParameterObject;
	listSourceOutputParameters: IParameterObject[];
	substituteSourceOutputParameters: IParameterObject[];
	isDataGrid: boolean;
	lookupOptions: ILookupOptions;
	autocompleteOptions: IAutoCompleteOptions;

	availableTextPlacement: IDataPair[] = [
		{ name: "Before", value: SummaryTextPlacement.BEFORE },
		{ name: "After", value: SummaryTextPlacement.AFTER },
	];

	availableAlignment: IDataPair[] = [
		{ name: "Left", value: TextAlignment.LEFT },
		{ name: "Right", value: TextAlignment.RIGHT },
		{ name: "Center", value: TextAlignment.CENTER }
	]

	columnFormatters: IColumnFormatter[] = [
		{
			name: "None",
			value: "none",
			type: ColumnFormatterType.BASIC
		}, {
			name: "Checkbox",
			value: FormatterEnum.Checkbox,
			type: ColumnFormatterType.BASIC
		}, {
			name: "Date Picker",
			value: FormatterEnum.Date,
			type: ColumnFormatterType.BASIC
		}, {
			name: "Date/Time Picker",
			value: FormatterEnum.Datetime,
			type: ColumnFormatterType.BASIC
		}, {
			name: "Decimal",
			value: FormatterEnum.Decimal,
			type: ColumnFormatterType.BASIC
		}, {
			name: "Hyperlink",
			value: FormatterEnum.Hyperlink,
			type: ColumnFormatterType.HYPERLINK
		}, {
			name: "Integer",
			value: FormatterEnum.Integer,
			type: ColumnFormatterType.BASIC
		}, {
			name: "Text Area",
			value: FormatterEnum.Textarea,
			type: ColumnFormatterType.BASIC
		}, {
			name: "Time Picker",
			value: FormatterEnum.Time,
			type: ColumnFormatterType.BASIC
		}, {
			name: "Button",
			value: FormatterEnum.Button,
			type: ColumnFormatterType.BASIC
		}, {
			name: "Dropdown",
			value: FormatterEnum.Dropdown,
			type: ColumnFormatterType.LIST
		}, {
			name: "Auto-complete",
			value: FormatterEnum.Autocomplete,
			type: ColumnFormatterType.LIST
		}, {
			name: "Lookup",
			value: FormatterEnum.Lookup,
			type: ColumnFormatterType.LIST
		}, {
			name: "Absolute Value",
			value: FormatterEnum.AbsoluteValue,
			type: ColumnFormatterType.BASIC
		}
	];

	private component: IEditorComponentWithUpsertDatagridColumnSettings;
	private column: IExtendedDataGridColumn;
	private sourceFields: IAvailableSourceField[];
	private dialog: IDialog<IUpsertOption>;

	private accordionApiList: IMap<IAccordionApi> = {
		"accordion-column-field-source": undefined,
		"accordion-column-options": undefined,
		"accordion-column-formatters": undefined,
		"accordion-column-summary-row": undefined,
		"accordion-column-substitute": undefined,
	};

	static $inject = [
		AngularConstants.SCOPE,
		AngularConstants.ELEMENT,
		InjectNames.TRANSLATION_MANAGEMENT_SERVICE,
		InjectNames.EDITOR_SERVICE,
		InjectNames.FEATURE_SWITCH_SERVICE,
		InjectNames.DIALOG_SERVICE
	];

	constructor(
		private $scope: IDialogBaseScope<IUpsertOption>,
		$element: ng.IAugmentedJQuery,
		private translationManagementService: TranslationManagementService,
		private editorService: EditorService,
		featureSwitchService: FeatureSwitchService,
		protected dialogService: DialogService
	) {
		super(featureSwitchService);
		const dialog: IDialog<IUpsertOption> = $scope.eirDialog;
		this.dialog = dialog;
		const parameter: IUpsertOption = dialog.parameter;
		this.setAvailableDataservices();
		this.initSourceFields(parameter);
		this.isAdd = parameter.isAdd;

		this.setColumn(parameter.column);
		this.setSelectedSourceField(this.column.field);
		this.initSelectedListSource();
		this.initSelectedSubstitutionSource();

		this.component = parameter.component;
		this.isDataGrid = parameter.component.getTypedef() === Datagrid.name ? true : false;
		this.column.name = this.column.value ? this.column.value : Helper.htmlDecode(this.column.name);

		//Init xi-components
		this.lookupOptions = this.translationManagementService.getLookupOptions();
		this.autocompleteOptions = this.translationManagementService.getAutoOptions();

		this.initAccordionElements($element);
	}

	private initSelectedSubstitutionSource(): void {
		if (this.column.substituteOptions) {
			if (this.column.substituteOptions.source) {
				const selectedSource = this.availableDataservices.find((dataservice) => {
					return dataservice.id === this.column.substituteOptions.source;
				});
				// Data service could be deleted
				if (selectedSource) {
					this.selectedSubstituteSource = selectedSource;
					this.substituteSourceOutputParameters = selectedSource.outputParameters;

					if (this.column.substituteOptions.from) {
						const selectedParameter = this.selectedSubstituteSource.outputParameters.find((parameter) => {
							return parameter.id === this.column.substituteOptions.from;
						});
						this.selectedFromSubstituteOutput = selectedParameter;
					}

					if (this.column.substituteOptions.to) {
						const selectedParameter = this.selectedSubstituteSource.outputParameters.find((parameter) => {
							return parameter.id === this.column.substituteOptions.to;
						});
						this.selectedToSubstituteOutput = selectedParameter;
					}
				}
			}
		} else {
			this.selectedSubstituteSource = this.availableDataservices[0];
			this.setSelectedSubstitutionSource();
		}
	}

	private initSourceFields(parameter: IUpsertOption): void {
		this.sourceFields = [
			{
				visibleName: "Static Value",
				sourceKey: ValueObject.STATIC_VALUE,
				sourceKeyShort: ValueObject.STATIC_VALUE,
				type: ValueObject.STATIC_VALUE,
				currentValue: parameter.column.text
			}
		];
		this.sourceFields.push(...parameter.sourceFields);
	}

	private setColumn(column: IExtendedDataGridColumn): void {
		this.column = column;

		// backward compatbility
		if (Helper.isUndefinedOrNull(this.column.editable)) {
			this.column.editable = false;
		}

		// backward compatbility
		if (Helper.isUndefinedOrNull(this.column.summaryTextPlacement)) {
			this.column.summaryTextPlacement = SummaryTextPlacement.AFTER;
		}

		// backward compatbility
		if (Helper.isUndefinedOrNull(this.column.align)) {
			this.column.align = TextAlignment.LEFT;
		}

		// set column formatters
		const selectedFormatter = this.columnFormatters.find((formatter) => {
			return formatter.value === this.column.formatterName;
		});
		this.selectedFormatter = selectedFormatter ? selectedFormatter : this.columnFormatters[0];
	}

	private setSelectedSourceField(field: string): void {
		const result = this.sourceFields.find((sourceField) => {
			return sourceField.sourceKeyShort === field;
		});
		if (result) {
			this.selectedSourceField = result;
		} else if (this.sourceFields.length === 1) {
			this.selectedSourceField = this.sourceFields[0];
			this.column.field = this.sourceFields[0].sourceKeyShort;
		} else if (this.sourceFields.length > 1) {
			this.selectedSourceField = this.sourceFields[1];
			this.column.name = this.sourceFields[1].visibleName;
			this.column.field = this.sourceFields[1].sourceKeyShort;
		}
	}

	private initSelectedListSource(): void {
		if (this.column.listOptions) {
			if (this.column.listOptions.sourceId) {
				const selectedSource = this.availableDataservices.find((dataservice) => {
					return dataservice.id === this.column.listOptions.sourceId;
				});
				// Data service could be deleted
				if (selectedSource) {
					this.selectedListSource = selectedSource;
					this.listSourceOutputParameters = selectedSource.outputParameters;

					if (this.column.listOptions.parameterId) {
						const selectedParameter = this.selectedListSource.outputParameters.find((parameter) => {
							return parameter.id === this.column.listOptions.parameterId;
						});
						this.selectedListOutput = selectedParameter;
					}
				}
			}
		}
	}

	setSelectedSubstitutionSource(): void {
		if (this.selectedSubstituteSource) {
			this.substituteSourceOutputParameters = this.selectedSubstituteSource.outputParameters;
			this.selectedFromSubstituteOutput = this.substituteSourceOutputParameters[0];
			this.selectedToSubstituteOutput = this.substituteSourceOutputParameters[0];
			this.updateSubstitute();
		}
	}

	updateSubstitute(): void {
		this.column.substituteOptions = {
			source: this.selectedSubstituteSource.id,
			from: this.selectedFromSubstituteOutput.id,
			to: this.selectedToSubstituteOutput.id
		};
	}

	toggleSubstitution(): void {
		if (!this.column.substitute) {
			this.selectedFormatter = this.columnFormatters[0];
			this.onSelectColumnFormatter();
			this.column.substitute = true;
			this.setSelectedSubstitutionSource();
		} else if (this.column.formatterName !== FormatterEnum.Dropdown) {
			this.column.options = null;
		}
	}

	setAvailableDataservices(): void {
		const dataservices =
			this.editorService.getCurrentProject().getDatasourceDatabearers() as IJsonGenericDatasource2[];
		this.availableDataservices = dataservices.filter((dataservice) => {
			if (dataservice.inputParameters) {
				return !dataservice.inputParameters.find((inputParameter) => {
					return inputParameter.required;
				});
			}
			return true;
		});
	}

	setSelectedListOutput(): void {
		this.selectedListOutput =
			this.selectedListOutput ? this.selectedListOutput : this.listSourceOutputParameters[0];
		this.column.listOptions = {
			sourceId: this.selectedListSource.id,
			parameterId: this.selectedListOutput.id
		};
		this.component.loadOptions(this.column);
	}

	setSelectedListSource(): void {
		if (this.selectedListSource) {
			this.listSourceOutputParameters = this.selectedListSource.outputParameters;
			this.column.listOptions = {
				sourceId: this.selectedListSource.id,
				parameterId: ""
			};
		}
	}

	openLinkDialog(): void {
		const options: IDialogOptions<IBuildLinkParameter> = {
			title: "Hyperlink - Build link",
			parameter: {
				link: this.column.hyperlinkOptions.value,
				sources: this.column.hyperlinkOptions.sources,
				component: this.component
			},
			template: require("../../../editor/component-settings/build-link-dialog/build-link-dialog.html")
		};

		this.dialogService.show(options).then((result: IDialogResult<IBuildLinkResponse>) => {
			if (result.button === DialogButtonType.Ok) {
				this.column.hyperlinkOptions.value = result.value.link;
				this.column.hyperlinkOptions.sources = result.value.sources;
			}
		});
	}

	openTranslations(): void {
		this.translationManagementService
			.openTranslations(this.component, this.column.id)
			.then((result: IDialogResult<IChanged>) => {
				if (result.button === DialogButtonType.Ok) {
					const changes = result.value;
					if (changes.savedComponentStrings.length > 0) {
						const selectedChanged = changes.savedComponentStrings.find((translation) => {
							return translation.component === this.component.getId() &&
								translation.reference === this.column.id;
						});
						if (selectedChanged) {
							this.column.name = selectedChanged.newValue;
						}
						this.editorService.updateTranslatables();
					}
				}
				//If global translations is changed
				if (result.button === DialogButtonType.Ok && !angular.equals({}, result.value.savedGlobalStrings)) {
					this.translationManagementService.saveGlobalTranslations(result.value.savedGlobalStrings)
						.then(() => {
							this.editorService.updateTranslatables();
						});
				}
				//If global translations is deleted
				if (result.button === DialogButtonType.Ok && !angular.equals({}, result.value.deletedGlobalStrings)) {
					this.translationManagementService.deleteGlobalTranslations(result.value.deletedGlobalStrings)
						.then(() => {
							this.editorService.updateTranslatables();
						});
				}
			});
	}

	private cleanColumn(column: IExtendedDataGridColumn): void {
		delete column.formatter;
		delete column.formatterName;
		delete column.readonly;
	}

	toggleSummaryRow(): void {
		DatagridHelper.ToggleSummarizeOnColumn(this.column);
	}

	onSelectColumnFormatter(): void {
		const formatter = this.selectedFormatter;
		const column = this.column;
		this.cleanColumn(column);

		if (formatter.value !== "none") {
			column.formatterName = Object.keys(FormatterEnum)
				.find(item => item.toLowerCase() === formatter.value)
				.toLowerCase();

			const locale = this.translationManagementService.getCurrentLanguage();
			DatagridHelper.setColumnSettings(column, locale);
		}

		if (formatter.type === ColumnFormatterType.LIST) {
			this.selectedListSource = this.availableDataservices[0];
			this.setSelectedListSource();
			this.setSelectedListOutput();
		} else {
			// Clear list specific properties
			this.selectedListOutput = null;
			this.selectedListSource = null;
			if (this.column.listOptions) {
				this.column.listOptions.parameterId = null;
				this.column.listOptions.sourceId = null;
			}
		}
		if (formatter.type === ColumnFormatterType.HYPERLINK) {
			const sources: ISourceItem[] = [];
			if (this.column.field && this.column.field !== ValueObject.STATIC_VALUE) {
				sources.push(
					{
						alias: "link",
						value: this.column.field,
						type: "<target>"
					}
				);
			}
			this.column.hyperlinkOptions = {
				value: sources.length > 0 ? "{link}" : "https://",
				isDrillback: false,
				sources
			};
		} else {
			//Clear hyperlink options
			if (this.column.hyperlinkOptions) {
				this.column.hyperlinkOptions.value = null;
				this.column.hyperlinkOptions.isDrillback = false;
				this.column.hyperlinkOptions.sources = [];
			}
		}
	}

	onSelectColumnOption(option: string): void {
		switch (option.toLowerCase()) {
			case ColumnOptionEnum.Editable:
				const locale = this.translationManagementService.getSelectedLocale();
				DatagridHelper.setEditableOnColumn(this.column, locale);
				break;
			case ColumnOptionEnum.Resizable:
			case ColumnOptionEnum.Sortable:
			default:
				break;
		}
	}

	getColumnOptions(): IMap<boolean> {
		const supportedOption = (option: string) => {
			switch (option) {
				case ColumnOptionEnum.Editable:
				case ColumnOptionEnum.Resizable:
				case ColumnOptionEnum.Sortable:
					return true;
				default:
					return false;
			}
		};
		const result: IMap<boolean> = {};

		angular.forEach(this.column, (value, key) => {
			if (supportedOption(key)) {
				if (this.isDataGrid) {
					result[key] = value;
				} else if (key !== ColumnOptionEnum.Editable) {
					result[key] = value;
				}
			}
		});

		return Helper.sortObjectKeys(result);
	}

	onFieldSelected(): void {
		const selectedSourceField = this.selectedSourceField;

		if (selectedSourceField.type === ValueObject.STATIC_VALUE) {
			this.column.text = selectedSourceField.currentValue;
		} else {
			this.column.text = null;
			this.column.name = selectedSourceField ?
				selectedSourceField.visibleName.replace("(" + selectedSourceField.sourceKeyShort + ")", "") : "";
		}
		this.column.field = selectedSourceField ? selectedSourceField.sourceKeyShort : null;
	}

	cancel(): void {
		this.dialog.close();
	}

	save(): void {
		//clear possible global string
		delete this.column.value;

		const result: IDialogResult<IExtendedDataGridColumn> = {
			value: this.column,
			button: DialogButtonType.Ok
		};

		this.dialog.close(result);
		this.component.initializeBindings();
	}

	validForm(): boolean {
		return !Helper.isUndefinedEmpty(this.column.name);
	}

	private initAccordionElements($element: ng.IAugmentedJQuery): void {
		this.$scope.$applyAsync(() => {
			const closeDropdown = () => {
				$element
					.find("#upsert-column-dialog-source-field")
					.data("dropdown")
					.close();
			};

			angular.forEach(this.accordionApiList, (value, key, obj) => {
				obj[key] = $element
					.find(`#${key}`)
					.accordion()
					.on("expand collapse", closeDropdown) as any;
			});

			$(".modal").on("beforeclose", () => {
				angular.forEach(this.accordionApiList, (api => {
					api.destroy();
				}));
			});
		});
	}

	static add(m: ng.IModule): void {
		m.controller("eirUpsertColumn", UpsertColumnCtrl);
	}
}
