import { AngularConstants } from "../../angular-constants";

import type { IDialogBaseScope } from "../../defines";
import { Helper } from "../../helper";
import { DialogButtonType, IDialog, IDialogResult } from "../../services/dialog-service";

import { FileExtensions, RegexStrings } from "ng2/common/constants/common.constant";

import type { IValidation } from "common/shared-components/base/field-validation";

export interface IExportDialogParams {
	type: "project" | "composite component" | "dataservice";
	filename: string;
}

const getFormattedFilename = (params: IExportDialogParams): string => {
	const { type, filename } = params;
	let formattedFilename = filename
		.split(" ")
		.join("_");
	formattedFilename = formattedFilename.replace(RegexStrings.NO_SPECIAL, "");
	switch (params.type) {
		case "project":
			return `${formattedFilename}_${Helper.getFormattedDate("yyyy-mm-dd")}`;
		default:
			return `${Helper.getFormattedDate("yyyy-mm-dd")}`;
	}
};

const getFileExtension = (params: IExportDialogParams): string => {
	const { type } = params;
	switch (type) {
		case "project":
			return FileExtensions.PROJECT;
		case "composite component":
			return FileExtensions.COMPOSITE_COMPONENT;
		case "dataservice":
			return FileExtensions.DATA_SERVICE;
		default:
			return ".somethingIsWrong";
	}
};

export class ExportProjectCtrl {
	exportFileName: string;
	fileExtension: string;
	defaultFilename: string;
	errorList: IValidation[] = [];
	get isInvalid(): boolean {
		return this.errorList.length !== 0;
	}

	private dialog: IDialog<IExportDialogParams>;

	static $inject = [AngularConstants.SCOPE];

	constructor(
		private scope: IDialogBaseScope<IExportDialogParams>
	) {
		const dialog = scope.eirDialog;
		this.dialog = dialog;
		this.exportFileName = getFormattedFilename(dialog.parameter);
		this.fileExtension = getFileExtension(dialog.parameter);
		this.defaultFilename = getFormattedFilename(dialog.parameter);
	}

	validate(value: string): boolean {
		return !RegexStrings.NO_SPECIAL.test(value);
	}

	close(): void {
		const result: IDialogResult<string> = {
			button: DialogButtonType.Cancel
		};
		this.dialog.close(result);
	}

	export(): void {
		const filename: string = Helper.isUndefinedEmpty(this.exportFileName) ?
			this.defaultFilename : this.exportFileName;

		const result: IDialogResult<string> = {
			value: `${filename}.${this.fileExtension}`,
			button: DialogButtonType.Ok
		};
		this.dialog.close(result);
	}

	static add(m: ng.IModule): void {
		m.controller("eirExportProjectCtrl", ExportProjectCtrl);
	}

}
