import angular from "angular";

import {
	CanvasModeEnum,
	ConditionType,
	ComponentClassNames,
	ComponentNames,
	ContextKeys,
	CssConstantKeys,
	CssConstantValues,
	EventTypes,
	InjectNames,
	InsertEventTypes,
	InteractionActionEnum,
	Numbers,
	SettingKeys,
	WizardFocusEnum,
} from "ng2/common/constants/common.constant";

import { DatasourceTypeEnum } from "ng2/common/enums/datasource-type.enum";
import { OrientationEnum } from "ng2/common/enums/orientation.enum";
import { SizeTypeEnum } from "ng2/common/enums/size-type.enum";
import { ValueTypeEnum } from "ng2/common/enums/value-type.enum";

import type { IDatasource } from "common/model/datasource/datasource.model";
import type { IComponentFieldSetting } from "ng2/common/model/components/settings/component-field-setting.model";
import type { IComponentSettingCategory } from "ng2/common/model/components/settings/component-setting-category.model";
import type { IDataPair } from "ng2/common/model/data-pair.model";
import type { IJsonComponent, IJsonSize } from "ng2/common/model/databearer/components/json-component.model";
import type { IJsonValueSource } from "ng2/common/model/databearer/json-binding.model";
import type { IMetaDataField } from "ng2/common/model/dataservice/meta-data-field.model";
import type { IMetaData } from "ng2/common/model/dataservice/meta-data.model";
import type { IMap } from "ng2/common/model/imap.model";
import type { IAvailableTargetField } from "ng2/common/model/interaction/available-target-field.model";

import { AngularConstants } from "common/angular-constants";
import type {
	IAvailableSourceField,
	IJQueryDragEventObject,
	IJQueryMouseEventObject
} from "common/defines";
import type { IComponent } from "common/IComponent";
import type { IComponentScope } from "common/interface/icomponent-scope";
import type { IEditorComponent } from "common/interface/ieditor-component";
import type { IListComponent } from "common/interface/ilist-component";
import { Defines } from "components/defines";
import type { IEditorList } from "editor/component-settings/list/list-style-settings";
import type { IEditorFlexbox } from "editor/components/editor-flexbox";
import type { IEditorLayoutComponent } from "editor/components/editor-layout-component";

import { ComponentsHelper } from "components/helper";
import { EditorHelper } from "editor/helper";

import type { DialogService, IDialogOptions } from "common/services/dialog-service";
import type { TranslationManagementService } from "components/services/translation-management-service";
import type { BindingManagementService } from "editor/services/binding-management-service";
import type { EditorService } from "editor/services/editor-service";
import type { InsertComponentService } from "editor/services/insert-component-service";
import type { InteractionManagementService } from "editor/services/interaction-management-service";

export interface IEditorComponentScope extends IComponentScope {
	componentInstance: IEditorComponent;
	eirEditorComponent: EditorComponentCtrl;
}

export interface IExpressionData {
	component: IEditorComponent;
	option: string;
}

export class EditorComponentCtrl {
	protected component: IEditorComponent;

	// State variables, used by ng-class
	isHovered: boolean;
	isFinder: boolean;
	isInsertChild: boolean;
	isInsertParent: boolean;
	isSelected: boolean;
	wizardFocusType: WizardFocusEnum;
	isDraggable = true;

	private dragstartTimer: ng.IPromise<void>;

	// Keeping the dragEnterCounter here as a reminder on how it works
	// http://stackoverflow.com/questions/7110353/html5-dragleave-fired-when-hovering-a-child-element
	static dragEnterCounter = 0;

	static add(m: ng.IModule): void {
		m.controller(ComponentClassNames.EDITOR_COMPONENT_PREFIX + ComponentClassNames.COMPONENT, EditorComponentCtrl);
	}
	static $inject = [
		AngularConstants.SCOPE,
		AngularConstants.ELEMENT,
		AngularConstants.COMPILE,
		AngularConstants.TIMEOUT,
		InjectNames.EDITOR_SERVICE,
		InjectNames.INTERACTION_MANAGEMENT_SERVICE,
		InjectNames.BINDING_MANAGEMENT_SERVICE,
		InjectNames.INSERT_COMPONENT_SERVICE,
		InjectNames.TRANSLATION_MANAGEMENT_SERVICE,
		InjectNames.DIALOG_SERVICE
	];

	constructor(
		protected $scope: IEditorComponentScope,
		protected $element: ng.IAugmentedJQuery,
		protected $compile: ng.ICompileService,
		protected $timeout: ng.ITimeoutService,
		protected editorService: EditorService,
		protected interactionManagementService: InteractionManagementService,
		protected bindingManagementService: BindingManagementService,
		protected insertComponentService: InsertComponentService,
		protected translationManagementService: TranslationManagementService,
		protected dialogService: DialogService
	) {
		const unsubscribe = $scope.$watch("componentInstance", (component: IEditorComponent) => {
			const selectionBox = this.$compile(
				"<eir-selection-box data-html2canvas-ignore ng-if='eirEditorComponent.isSelected'></eir-selection-box>"
			)($scope);
			this.init(component);
			this.$element.prepend(selectionBox);
			component.updateClasses();
			unsubscribe();
		});
	}

	exposesReadableBindables(): boolean {
		const component = this.component;
		let exposesBindables: boolean;

		exposesBindables = Defines
			.allComponents[component.getTypedef()]
			.class
			.BINDABLE_FIELDS_2
			.some((bindableField) => {
				return bindableField.read;
			});
		if (component.getDatasource()) { // Assuming a datasource always have output fields
			exposesBindables = true;
		}
		return exposesBindables;
	}

	exposesLocalizedBindables(): boolean {
		const component = this.component;
		const translatableBindables: boolean =
			Defines
				.allComponents[component.getTypedef()]
				.class
				.BINDABLE_FIELDS_2
				.some((bindableField) => {
					return bindableField.valueType === ValueTypeEnum.LOCALIZED_STRING;
				});

		return translatableBindables;
	}

	protected init(component: IEditorComponent): void {
		//Helper methods
		component.exposesLocalizedBindables = this.exposesLocalizedBindables.bind(this);
		component.exposesReadableBindables = this.exposesReadableBindables.bind(this);

		// View methods
		component.onClick = this.onClick.bind(this);
		component.onDragenter = (e) => { EditorComponentCtrl.dragEnterCounter++; this.onMouseOver(e); };
		component.onDragleave = (e) => { EditorComponentCtrl.dragEnterCounter--; this.onMouseLeave(e); };
		component.onDragover = this.onMouseMove.bind(this);
		component.onDragstart = this.onDragstart.bind(this);
		component.onDrop = this.onClick.bind(this);
		component.onMouseLeave = this.onMouseLeave.bind(this);
		component.onMouseMove = this.onMouseMove.bind(this);
		component.onMouseOver = (e) => { this.onMouseOver(e); };
		component.resizableHeight = this.resizableHeight.bind(this);
		component.resizableWidth = this.resizableWidth.bind(this);
		component.setIsFinder = this.setIsFinder.bind(this);
		component.setIsHovered = this.setIsHovered.bind(this);
		component.setIsInsertChild = this.setIsInsertChild.bind(this);
		component.setIsInsertParent = this.setIsInsertParent.bind(this);
		component.setIsSelected = this.setIsSelected.bind(this);
		component.setIsWizardFocus = this.setIsWizardFocus.bind(this);
		component.wizardFocusType = WizardFocusEnum.UNFOCUSED;

		// Metadata
		component.getBindingMetaData = this.getBindingMetaData.bind(this);
		component.getContextMetaData = this.getContextMetaData.bind(this);
		component.getLocalContextMetaData = this.getLocalContextMetaData.bind(this);
		component.getParameterMetadata = this.getParameterMetadata.bind(this);
		component.getFieldSettings = this.getFieldSettings.bind(this);
		component.getSourceTriggers = this.getSourceTriggers.bind(this);
		component.getTargetActions = this.getTargetActions.bind(this);
		component.getDefaultAction = this.getDefaultAction.bind(this);
		component.getDefaultTrigger = this.getDefaultTrigger.bind(this);

		// Data manipulative methods
		component.detach = this.detach.bind(this);
		component.setProperty = this.setProperty.bind(this);
		component.setDatasource = this.setDatasource.bind(this);
		component.removeDatasource = this.removeDatasource.bind(this);

		//Settings
		component.getBindableField = this.getBindableField.bind(this);
		component.setSetting = this.setSetting.bind(this);
		component.setBoundarySetting = this.setBoundarySetting.bind(this);
		component.getSettingCategories = this.getCurrentComponentFieldSettings.bind(this);
		component.refreshMetaSettings = this.refreshMetaSettings.bind(this);
		component.openDialog = this.openDialog.bind(this);

		this.component = component;
		if (this.insertComponentService.getInsertComponent() === component.getDatabearer()) {
			this.isInsertChild = true;
		}

		if (this.editorService.getCanvasMode() === CanvasModeEnum.SELECTION) {
			this.isDraggable = true;
		}
		this.editorService.canvasModeChangedEvent.on((canvasMode: CanvasModeEnum) => {
			if (canvasMode === CanvasModeEnum.SELECTION) {
				this.isDraggable = true;
			} else {
				this.isDraggable = false;
			}
		});
	}

	openDialog(settingKey: string): ng.IPromise<void> {
		const deferred = this.editorService.getNewDeferred<void>();
		let options: IDialogOptions<IExpressionData>;
		const style = "width: 780px;";

		if (settingKey === SettingKeys.IS_READONLY) {
			options = {
				style,
				title: "Component Expression - Disable When",
				parameter: {
					component: this.component,
					option: ConditionType.DISABLE
				},
				template: require("../component-settings/expression-dialog/expression-dialog.html")
			};
		} else if (settingKey === SettingKeys.VISIBILITY) {
			options = {
				style,
				title: "Component Expression - Hide When",
				parameter: {
					component: this.component,
					option: ConditionType.HIDE
				},
				template: require("../component-settings/expression-dialog/expression-dialog.html")
			};
		}
		if (options) {
			this.dialogService.show(options);
		}
		return deferred.promise;
	}

	setSetting(settingMeta: IComponentFieldSetting, newSettingValue: any): void {
		const component = this.component;
		const settingReference = settingMeta.ref;

		if (component.isGenerated) {
			// Update original template
			const generatingParent = component.getGeneratingParent();
			if (generatingParent.getTypedef() === ComponentNames.List) {
				const generatingList: IEditorList = generatingParent as any;
				this.editorService.forEachComponent(
					(generatingList as any).currentTemplate, // TODO: MUPS-2682 Remove this any
					(subComponent) => {
						if (subComponent.id === component.getId()) {
							if (settingMeta.boundarySetting) {
								const currentBoundaryId = this.editorService.getCurrentBoundary().id;
								this.setBoundarySettingOnDatabearer(subComponent, settingReference, newSettingValue, currentBoundaryId);
							} else {
								(subComponent as IMap<any>)[settingReference] = newSettingValue;
							}
						}
					}
				);
				generatingList.refresh();
			} else {
				// We only have List as generating parent so far
			}
		} else {
			if (settingMeta.boundarySetting) {
				this.setBoundarySetting<any>(settingReference, newSettingValue);
			} else {
				this.setProperty(settingReference, newSettingValue);
			}
		}
		if (settingReference === SettingKeys.VISIBILITY) {
			component.updateVisibility();
		} else if (settingReference === SettingKeys.SHOW_ERROR) {
			component.updateShowError();
		}
	}

	detach(): IJsonComponent {
		if (this.component.parent.isLayoutComponent()) {
			return this.component.parent.detachChild(this.component.getId());
		} else {
			return null;
		}
	}

	setBoundarySetting<T>(settingId: string, settingValue: T, boundaryId?: string): void {
		const componentBoundarySettings = this.component.getDatabearer().boundarySettings;
		const currentBoundaryId = this.editorService.getCurrentBoundary().id;

		if (!boundaryId) {
			boundaryId = currentBoundaryId;
		}
		this.setBoundarySettingOnDatabearer(this.component.getDatabearer(), settingId, settingValue, boundaryId);
		if (boundaryId === currentBoundaryId) {
			this.component.newBoundaryEntered(boundaryId);
		}
	}

	private setBoundarySettingOnDatabearer<T>(
		databearer: IJsonComponent,
		settingId: string,
		settingValue: T,
		boundaryId: string): void {
		const componentBoundarySettings = databearer.boundarySettings;
		if (!componentBoundarySettings[boundaryId]) {
			componentBoundarySettings[boundaryId] = {};
		}
		(componentBoundarySettings[boundaryId] as IMap<T>)[settingId] = settingValue;
	}

	private onClick(event: IJQueryMouseEventObject | IJQueryDragEventObject): void {
		const editorService = this.editorService;
		const canvasMode = editorService.getCanvasMode();
		let component = this.component;
		switch (canvasMode) {
			case CanvasModeEnum.SELECTION:
				// Toggle component selection
				if (this.isSelected) {
					editorService.deselectComponent();
				} else {
					editorService.setSelectedComponent(component);
				}
				event.stopPropagation();
				break;
			case CanvasModeEnum.FINDER:
				if (component.wizardFocusType !== WizardFocusEnum.UNFOCUSED) {
					//Must include editorService
					component = EditorHelper.getUnderlyingComponent(component, event, editorService) as IEditorComponent;
				}
				this.editorService.selectFinderComponent(component);
				event.stopPropagation();
				break;
			case CanvasModeEnum.INSERT:
				this.$timeout.cancel(this.dragstartTimer);
				if (component.isLayoutComponent() && !component.isGenerated) {
					this.insertComponentService.finishInsert(event, (component as IEditorFlexbox));
					event.stopPropagation();
				} else {
					// Propagate for regular components
				}
				break;
			default:
			// Do nothing
		}
		// Triggers dropdown and popover to close (soho xi)
		angular.element("body").click();
	}

	private onMouseLeave(e: IJQueryDragEventObject | IJQueryMouseEventObject): void {
		const editorService = this.editorService;
		const canvasMode = editorService.getCanvasMode();
		switch (canvasMode) {
			case CanvasModeEnum.SELECTION:
				editorService.setHoveredComponent();
				e.stopPropagation();
				break;
			case CanvasModeEnum.FINDER:
				editorService.setFinderComponent();
				e.stopPropagation();
				break;
			case CanvasModeEnum.INSERT:
				if (this.component.isLayoutComponent() && !this.component.isGenerated) {
					if (e.type === EventTypes.DRAGLEAVE) {
						// When leaving the canvas
						if (EditorComponentCtrl.dragEnterCounter === 0) {
							this.insertComponentService.setInsertParent(null);
						}
					} else {
						this.insertComponentService.setInsertParent(null);
					}
					e.stopPropagation();

				}
				break;
			default:
			// Do nothing
		}
	}

	private onDragstart(event: IJQueryDragEventObject): void {
		event.stopPropagation();
		const canvasMode = this.editorService.getCanvasMode();
		switch (canvasMode) {
			case CanvasModeEnum.SELECTION:
				const component = this.component;
				const editorService = this.editorService;
				this.$timeout.cancel(this.dragstartTimer);
				if (!component.isLayoutComponent() && !component.isGenerated) {
					// Deselect text
					window.getSelection().removeAllRanges();

					event.originalEvent.dataTransfer.effectAllowed = "move";

					// Using timeout because Chrome cancels drag-event on some dom manipulations
					this.dragstartTimer = this.$timeout(() => {
						component.detach();
					});

					// Setting pointer-events: auto, to override insertMode {pointe-events: none; }
					component.element.css(CssConstantKeys.POINTER_EVENTS, CssConstantValues.AUTO);
					const originIndex = component.parent.getDatabearer().children.indexOf(component.getDatabearer());

					this.insertComponentService.startInserting(component.getDatabearer(), InsertEventTypes.MOVE)
						.then((newParent: IEditorFlexbox) => {
							editorService.saveCanvas();
						}).catch(() => {
							component.parent.insert(component.getDatabearer(), originIndex);
						}).finally(() => {
							editorService.setCanvasMode(CanvasModeEnum.SELECTION);
						});
				}
				break;
			case CanvasModeEnum.FINDER:
			case CanvasModeEnum.INSERT:
				break;
			default:
			// Do nothing
		}
	}

	private onMouseMove(event: IJQueryMouseEventObject | IJQueryDragEventObject): void {
		const canvasMode = this.editorService.getCanvasMode();
		switch (canvasMode) {
			case CanvasModeEnum.SELECTION:
			case CanvasModeEnum.FINDER:
				break;
			case CanvasModeEnum.INSERT:
				const insertComponent = this.insertComponentService.getInsertComponent();
				if (
					insertComponent
					&& this.component.isLayoutComponent()
					&& !this.component.isGenerated
				) {
					this.insertComponentService.insertIteration(event, (this.component as IEditorFlexbox));
					event.stopPropagation();
				}
				break;
			default:
			// Do nothing
		}
	}

	private onMouseOver(e: IJQueryDragEventObject | IJQueryMouseEventObject): void {
		const component = this.component;
		const editorService = this.editorService;
		const canvasMode = editorService.getCanvasMode();
		switch (canvasMode) {
			case CanvasModeEnum.SELECTION:
				editorService.setHoveredComponent(this.isSelected ? null : component);
				e.stopPropagation();
				break;
			case CanvasModeEnum.FINDER:
				editorService.setFinderComponent(component);
				e.stopPropagation();
				break;
			case CanvasModeEnum.INSERT:
				if (component.isLayoutComponent() && !component.isGenerated) {
					this.insertComponentService.setInsertParent(component as IEditorFlexbox);
					e.stopPropagation();
				}
				break;
			default:
			// Do nothing
		}
	}

	resizableHeight(): boolean {
		const component = this.component;
		const height = component.getBoundarySetting<IJsonSize>(SettingKeys.HEIGHT);
		if (!height) {
			return false;
		}
		const heightType = height.sizeType;
		if ((heightType === SizeTypeEnum.PIXELS || heightType === SizeTypeEnum.PERCENT) &&
			component.parent.resizableChildren(OrientationEnum.VERTICAL) &&
			!component.isGenerated) {
			return true;
		}

		return false;
	}

	resizableWidth(): boolean {
		const component = this.component;
		const width = component.getBoundarySetting<IJsonSize>(SettingKeys.WIDTH);
		if (!width) {
			return false;
		}
		const widthType = width.sizeType;
		if ((widthType === SizeTypeEnum.PIXELS || widthType === SizeTypeEnum.PERCENT) &&
			component.parent.resizableChildren(OrientationEnum.HORIZONTAL) &&
			!component.isGenerated) {
			return true;
		}

		return false;
	}

	private setIsFinder(isFinder: boolean): void {
		this.isFinder = isFinder;
	}

	private setIsHovered(isHovered: boolean): void {
		this.isHovered = isHovered;
	}

	private setIsInsertChild(isInsertChild: boolean): void {
		this.isInsertChild = isInsertChild;
	}

	private setIsInsertParent(isInsertParent: boolean): void {
		this.isInsertParent = isInsertParent;
	}

	private setIsSelected(isSelected: boolean): void {
		this.isSelected = isSelected;
	}

	private setIsWizardFocus(isWizardFocusEnum: WizardFocusEnum): void {
		this.wizardFocusType = isWizardFocusEnum;
		this.component.wizardFocusType = isWizardFocusEnum;
	}

	private setProperty(propertyName: string, newValue: any): void {
		(this.component as any)[propertyName] = newValue;
		(this.component.getDatabearer() as any)[propertyName] = newValue;
		// TODO: Maybe this is too general. Could run updateClasses, updateInlineStyle depending on the need
		this.component.updateClassesAndStyle();
	}

	private getCurrentComponentFieldSettings(): IComponentSettingCategory[] {
		return this.component.getFieldSettings();
	}

	setDatasource(newDatasource: IDatasource | null): ng.IPromise<void> {
		const component = this.component;
		const deferred = this.editorService.getNewDeferred<void>();

		component.resetLayout();

		if (component.datasource) {
			component.datasource.usedBy.forEach((usedByComponent, index, list) => {
				if (usedByComponent === component) {
					list.splice(index, 1);
				}
			});
		}
		if (newDatasource) {
			component.datasource = newDatasource;
			component.getDatabearer().datasource = newDatasource.id;
			newDatasource.usedBy.push(component);

			if (component.isGenerated) {
				this.refreshGeneratedComponents(component);
			}

			newDatasource.getMetaData()
				.catch(() => {
					console.error("[EdtiorComponent] Problem getting datasourceMetadata");
					this.clearData();
					deferred.reject();
				})
				.finally(() => {
					deferred.resolve();
				});
		} else {
			this.removeDatasource();
			// Finished unsetting
			deferred.resolve();
		}
		return deferred.promise;
	}

	private refreshGeneratedComponents(component: IEditorComponent): void {
		const parent = component.getGeneratingParent() as IComponent;
		if (parent.getTypedef() === ComponentNames.List) {
			(parent as IListComponent).refresh();
		}
	}

	protected clearData(): void {
		const component = this.component;
		component.resetContext();
		if (component.isGenerated) {
			const generatingParent = component.getGeneratingParent();
			(generatingParent as any as IListComponent).refresh();
		}
	}

	refreshMetaSettings(showDialog?: boolean): void {
		// Override this method
	}

	removeDatasource(): void {
		const component = this.component;
		this.clearData();

		// Reset datasource
		component.datasource = null;
		component.getDatabearer().datasource = null;
		this.refreshMetaSettings();

		// Reset the layout to remove any extra error tooltips etc
		component.resetLayout();
	}

	/**
	 * Get metadata about available parameters for a given Action
	 * Most cases are reliant on datasource metadata, and therefore returns a promise
	 *
	 * @param action{InteractionActionEnum}
	 */
	getParameterMetadata(action: InteractionActionEnum): ng.IPromise<IAvailableTargetField[]> {
		const component = this.component;
		const deferred = this.editorService.getNewDeferred<IAvailableTargetField[]>();
		if (component.datasource) {
			return component.datasource.getMetaData().then((metaData) => {
				return metaData.inputFields.map((inputField) => {
					const isMandatory = inputField.MAND === "1";
					const availableTargetField: IAvailableTargetField = {
						visibleName: inputField.FLDS + " (" + (isMandatory ? "Required" : "Optional") + ")",
						targetKey: inputField.FLNM,
						isMandatory: isMandatory,
						Meta: {
							name: inputField.FLDS,
							ref: inputField.FLNM,
							valueType: ValueTypeEnum.STRING,
							write: true,
							read: true
						}
					};
					return availableTargetField;
				});
			});
		} else {
			deferred.reject();
		}
		return deferred.promise;
	}

	getLocalContextMetaData(): ng.IPromise<IAvailableSourceField[]> {
		const deferred = this.editorService.getNewDeferred<IAvailableSourceField[]>();
		const meta: IAvailableSourceField[] = [];
		const datasource = this.component.getDatasource();
		if (datasource) {
			// Only add the ListData and SingleData on generic version 1
			if (datasource.typedef === DatasourceTypeEnum.generic && datasource.version === 1) {
				meta.push({
					visibleName: datasource.displayName + "(ListData)",
					sourceKey: ContextKeys.LIST_RESULT,
					sourceKeyShort: ContextKeys.LIST_RESULT,
					currentValue: ""
				});

				meta.push({
					visibleName: datasource.displayName + "(SingleData)",
					sourceKey: ContextKeys.DETAIL_RESULT,
					sourceKeyShort: ContextKeys.DETAIL_RESULT,
					currentValue: ""
				});
			}

			datasource.getMetaData()
				.then((metaData: IMetaData) => {
					metaData.outputFields.map((outputField: IMetaDataField) => {
						meta.push({
							visibleName: outputField.FLDS.trim(),
							sourceKey: outputField.FLNM,
							sourceKeyShort: outputField.FLNM,
						});
					});
					deferred.resolve(meta);
				});

		} else {
			deferred.resolve([]);
		}
		return deferred.promise;
	}

	getContextMetaData(): ng.IPromise<IAvailableSourceField[]> {
		const deferred = this.editorService.getNewDeferred<IAvailableSourceField[]>();
		const promiseList: ng.IPromise<IAvailableSourceField[]>[] = [];
		let availableSourceField: IAvailableSourceField[] = [];

		this.component.forEachParent((parent: IEditorLayoutComponent) => {
			const promise = parent.getLocalContextMetaData();
			promise.then((parentMeta) => {
				availableSourceField = availableSourceField.concat(parentMeta);
			});
			promiseList.push(promise);
		});

		const promise = this.getLocalContextMetaData();
		promiseList.push(promise);
		promise.then((metadata) => {
			availableSourceField = availableSourceField.concat(metadata);
		});

		this.editorService.onAllPromises(promiseList).then(() => {

			// Removing duplicates. http://stackoverflow.com/a/32238830/304894
			const values: any = {};
			availableSourceField = availableSourceField.filter((item: any) => {
				const key = "sourceKey";
				const val = item[key];
				const exists = values[val];
				values[val] = true;
				return !exists;
			});

			availableSourceField.forEach((contextField) => {
				const context = this.component.getContext();
				const maxLength = Numbers.TWENTY;
				const key = contextField.sourceKeyShort;
				if (context[key]) {
					if (key === ContextKeys.DETAIL_RESULT) {
						contextField.currentValue = "size: " + context[key].length;
					} else if (key === ContextKeys.LIST_RESULT) {
						contextField.currentValue = "length: " + context[key].length;
					} else {
						contextField.currentValue = ComponentsHelper.truncate(context[key], maxLength);
					}
				}
			});
			deferred.resolve(availableSourceField);
		});

		return deferred.promise;
	}

	getBindingMetaData(): ng.IPromise<IAvailableSourceField[]> {
		const promises: ng.IPromise<void>[] = [];
		const component = this.component;
		let availableBindings: IAvailableSourceField[] = [];

		if (component.getDatasource() && !this.hasBindableFields()) {
			const deferred = this.editorService.getNewDeferred<void>();
			this.getContextMetaData().then((availableSourceFields: IAvailableSourceField[]) => {
				availableBindings = availableSourceFields;
				deferred.resolve();
			});
			promises.push(deferred.promise);
		} else {
			this.component.forEachBindable((bindableFieldDefinition, currentValueSource: IJsonValueSource) => {
				const deferred = this.editorService.getNewDeferred<void>();
				if (bindableFieldDefinition.valueType === ValueTypeEnum.OBJECT) {
					this.getMetaData(bindableFieldDefinition.ref, (objectMetaData: IMetaDataField[]) => {
						objectMetaData.forEach((currentItemMetaData) => {
							const currentValue: string = currentValueSource ?
								currentValueSource[currentItemMetaData.FLNM] : null;
							const fldsName = currentItemMetaData.FLDS ? " [" + currentItemMetaData.FLDS + "]" : "";
							const currentVisibleName =
								bindableFieldDefinition.name + fldsName;

							availableBindings.push({
								visibleName: currentVisibleName,
								sourceKey: bindableFieldDefinition.ref + "." + currentItemMetaData.FLNM,
								sourceKeyShort: currentItemMetaData.FLNM,
								currentValue: currentValue
							});
						});
						deferred.resolve();
					});
					promises.push(deferred.promise);
				} else {
					if (bindableFieldDefinition.read) {
						availableBindings.push({
							visibleName: bindableFieldDefinition.name,
							sourceKey: bindableFieldDefinition.ref,
							sourceKeyShort: bindableFieldDefinition.ref,
							currentValue: (this as any)[bindableFieldDefinition.ref]
						});
					}
				}
			});
		}
		const deferred = this.editorService.getNewDeferred<IAvailableSourceField[]>();
		this.editorService.onAllPromises(promises).then(() => {
			deferred.resolve(availableBindings);
		});

		return deferred.promise;
	}

	// Method for components with bindable value type "Object"
	getMetaData(fieldRef: string, onSuccess: Function): void {
		console.warn("[Component] Unimplemented method getMetaData()");
	}

	getBindableField(bindableReference: string): IJsonValueSource {
		return (this.component.getDatabearer() as any)[bindableReference];
	}

	getFieldSettings(): IComponentSettingCategory[] {
		return Defines.allComponents[this.component.getTypedef()].class.SETTING_FIELDS;
	}

	getSourceTriggers(): IDataPair[] {
		return Defines.allComponents[this.component.getTypedef()].class.SOURCE_TRIGGERS;
	}

	getTargetActions(): IDataPair[] {
		return Defines.allComponents[this.component.getTypedef()].class.TARGET_ACTIONS;
	}

	getDefaultAction(): IDataPair {
		return Defines.allComponents[this.component.getTypedef()].class.DEFAULT_ACTION;
	}

	getDefaultTrigger(): IDataPair {
		return Defines.allComponents[this.component.getTypedef()].class.DEFAULT_TRIGGER;
	}

	private hasBindableFields(): boolean {
		const bindableFields = Defines.allComponents[this.component.getDatabearer().typedef].class.BINDABLE_FIELDS_2;
		return bindableFields.length > 0;
	}
}

export class EditorComponentDirective {
	static add(m: ng.IModule): void {
		m.directive("eirEditorComponent", () => {
			return {
				restrict: "A",
				controller: "@",
				controllerAs: "eirEditorComponent"
			};
		});
	}
}
