import { Utils } from "../utils";

/**
 * Spinbox Directive.
 *
 * Example:
 * <input xi-spinbox class="spinbox" min="-99" max="99" value="0" step="3" />
 *
 * Attributes: min, max, value (default), step
 * Supports xi-track-dirty
 */
export class SpinboxDirective implements ng.IDirective {
	private static readonly DIRECTIVE_NAME = "xiSpinbox";
	public restrict = "A";
	public scope = false;

	public link(
		scope: ng.IScope,
		element: ng.IAugmentedJQuery,
		attributes: ng.IAttributes
	): void {
		element.spinbox();
		// Tear down
		element.on("$destroy", () => {
			const spinBoxData = Utils.getData(element, "spinbox");
			if (spinBoxData) {
				spinBoxData.destroy();
			}
		});
	}

	public static add(m: ng.IModule): void {
		m.directive(SpinboxDirective.DIRECTIVE_NAME, () => {
			return new SpinboxDirective();
		});
	}
}
