import angular from "angular";
import { AngularConstants } from "./angular-constants";

export class DragDirective {
	static add(m: ng.IModule): void {
		// http://stackoverflow.com/a/38753647/304894
		const ngDragEventDirectives: any = {};
		angular.forEach(
			"drag dragend dragenter dragexit dragleave dragover dragstart drop".split(
				" "
			),
			(eventName) => {
				const directiveName: string =
					"ng" + eventName.charAt(0).toUpperCase() + eventName.slice(1);

				ngDragEventDirectives[directiveName] = [
					"$parse",
					AngularConstants.ROOT_SCOPE,
					($parse: ng.IParseService, $rootScope: ng.IRootScopeService) => {
						return {
							restrict: "A",
							compile: (
								$element: ng.IRootElementService,
								attr: ng.IAttributes
							) => {
								const fn = $parse(attr[directiveName]);

								return function ngDragEventHandler(
									scope: ng.IScope,
									element: ng.IAugmentedJQuery
								): void {
									element.on(eventName, (event: JQueryEventObject) => {
										fn(scope, { $event: event });
										scope.$digest(); // do digest directly on scope since apply does digest on rootScope
									});
								};
							},
						};
					},
				];
			}
		);

		m.directive(ngDragEventDirectives);
	}
}
