import { AngularConstants } from "../../../../common/angular-constants";

import { ConditionType, InjectNames, SaveFlagsEnum } from "ng2/common/constants/common.constant";
import { SelectableEnum } from "ng2/common/constants/sohoxi.constant";
import { IJsonExpression } from "ng2/common/model/databearer/json-expression.model";
import { IMap } from "ng2/common/model/imap.model";
import { Mashup } from "ng2/common/project/mashup";
import type { IDatagridApi, IDataGridColumn, IDataGridOptions } from "ng2/common/model/sohoxi/sohoxi-datagrid.model";

import { IDialogBaseScope } from "../../../../common/defines";
import { IComponent } from "../../../../common/IComponent";
import { DialogButtonType, DialogService, IDialog, IDialogOptions, IDialogResult } from "../../../../common/services/dialog-service";
import { IRowItem } from "../../../../common/sohoxi/datagrid-helper";
import { FrameworkService } from "../../../../components/services/framework-service";
import { IExpressionData } from "../../components/editor-component";
import { EditorService } from "../../services/editor-service";

interface IExpressionView {
	id: string;
	sourceName: string;
	targetName: string;
	sourcePropertyName: string;
	targetPropertyName: string;
	operatorName: string;
	usedBy: string;
}

interface IExpressionTreeView {
	componentId: string;
	children: IExpressionView[];
	expanded: boolean;
}

export interface IConditionData {
	expression: IJsonExpression;
	expressionType: string;
}

export class ExpressionDialogCtrl {
	datagridOptions: IDataGridOptions<IExpressionView | IExpressionTreeView>;
	itemSelected = false;
	private selectedExpression: IJsonExpression;
	private expressionType: string;
	private dataset: IExpressionView[] | IExpressionTreeView[] = [];
	private selectedComponent: IComponent;
	private dialog: IDialog<IExpressionData>;
	private operatorDictionary: IMap<string> = {
		"=": "Equals",
		"<>": "Not Equals",
		">": "Greater than",
		">=": "Equals or Greater",
		"<": "Lower than",
		"<=": "Equals or Lower",
		"*": "Contains In"
	};

	private typeDictionary: IMap<string> = {
		"disable": "Disable",
		"hide": "Hide"
	};

	private currentProject: Mashup;

	static $inject = [
		AngularConstants.SCOPE,
		AngularConstants.ELEMENT,
		AngularConstants.TIMEOUT,
		InjectNames.DIALOG_SERVICE,
		InjectNames.EDITOR_SERVICE,
		InjectNames.FRAMEWORK_SERVICE
	];

	constructor(
		private $scope: IDialogBaseScope<IExpressionData>,
		private $element: ng.IAugmentedJQuery,
		private $timeout: ng.ITimeoutService,
		private dialogService: DialogService,
		private editorService: EditorService,
		private frameworkService: FrameworkService
	) {
		const dialog: IDialog<IExpressionData> = $scope.eirDialog;
		this.dialog = dialog;
		this.selectedComponent = dialog.parameter.component;
		this.expressionType = dialog.parameter.option;
		this.currentProject = this.editorService.getCurrentProject();

		this.refreshDataset();
		this.initGridOptions();
	}

	addExpression(): void {
		this.editExpression(true);
	}

	editExpression(newExpression: boolean = false): void {
		if (newExpression) {
			this.selectedExpression = null;
		}
		const dialogOptions: IDialogOptions<IConditionData> = {
			template: require("./edit-expression-dialog/edit-expression-dialog.html"),
			title: newExpression ? "New Condition" : "Edit Condition",
			parameter: {
				expression: this.selectedExpression,
				expressionType: this.expressionType
			}
		};
		this.dialogService.show(dialogOptions).then((results: IDialogResult<IJsonExpression>) => {
			const button = results.button;
			if (button === DialogButtonType.Ok) {
				if (newExpression) {
					this.currentProject.addExpression(results.value);
				} else {
					this.currentProject.saveExpression(results.value);
				}
				this.refreshDataset();
				this.save();
			}
		});
	}

	deleteExpression(): void {
		this.dialogService.showYesNo("Delete Condition", "Do you want to delete this condition?").then((result) => {
			if (result.button === DialogButtonType.Yes) {
				this.currentProject.deleteExpression(this.selectedExpression);
				this.refreshDataset();
				this.save();
			}
		});
	}

	close(): void {
		const result: IDialogResult<void> = {
			button: DialogButtonType.Ok
		};
		this.frameworkService.triggerExpressions();
		this.dialog.close(result);
	}

	private save(): void {
		this.editorService.saveCurrent(SaveFlagsEnum.EXPRESSIONS + SaveFlagsEnum.MANIFEST);
	}

	private updateDisplayNames(): void {
		this.updatePropertyNames().then(() => {
			// Timeout needed to view correct displayname
			this.$timeout(() => {
				this.datagridOptions.dataset = this.dataset;
			});
		});
	}

	private updatePropertyNames(): ng.IPromise<void> {
		const deferred = this.frameworkService.getNewDeferred<void>();
		this.dataset.forEach((item: IExpressionTreeView | IExpressionView) => {
			if (this.expressionType === ConditionType.ALL) {
				(item as IExpressionTreeView).children.forEach((child) => {
					this.setSourcePropertyName(child).then(() => {
						this.setTargetPropertyName(child).then(() => {
							deferred.resolve();
						});
					});
				});
			} else {
				this.setSourcePropertyName(item as IExpressionView).then(() => {
					this.setTargetPropertyName(item as IExpressionView).then(() => {
						deferred.resolve();
					});
				});
			}
		});
		return deferred.promise;
	}

	private getComponentExpression(): IJsonExpression[] {
		const allExpressions = this.currentProject.getExpressions();
		if (this.expressionType !== ConditionType.ALL) {
			return allExpressions.filter((expression) => {
				return expression.usedBy === this.selectedComponent.getId()
					&& expression.conditionType === this.expressionType;
			});
		} else {
			return allExpressions;
		}
	}

	private setTargetPropertyName(item: IExpressionView): ng.IPromise<void> {
		return this.getPropertyName(item.targetName, item.targetPropertyName).then((propertyName) => {
			item.targetPropertyName = propertyName;
			item.targetName = this.getName(item.targetName);
		});
	}

	private setSourcePropertyName(item: IExpressionView): ng.IPromise<void> {
		return this.getPropertyName(item.sourceName, item.sourcePropertyName).then((propertyName) => {
			item.sourcePropertyName = propertyName;
			item.sourceName = this.getName(item.sourceName);
		});
	}

	private getPropertyName(componentName: string, propertyName: string): ng.IPromise<string> {
		const deferred = this.frameworkService.getNewDeferred<string>();
		let propertyDisplayName;
		if (componentName === "<static>") {
			propertyDisplayName = propertyName;
			deferred.resolve(propertyDisplayName);
		} else {
			const component = this.editorService.getComponentInstanceById(componentName);
			if (component) {
				component.getBindingMetaData().then((bindings) => {
					if (bindings.length > 0) {
						propertyDisplayName = bindings.find((binding) => {
							return binding.sourceKey === propertyName;
						}).visibleName;
						deferred.resolve(propertyDisplayName);
					}
				});
			}
		}
		return deferred.promise;
	}

	private getName(componentName: string): string {
		let componentDisplayName;
		if (componentName === "<static>") {
			componentDisplayName = "Static Value";
		} else {
			const component = this.editorService.getComponentInstanceById(componentName);
			componentDisplayName = component.getDisplayName();
		}
		return componentDisplayName;
	}

	private buildDataset(): IExpressionView[] | IExpressionTreeView[] {
		const expressionViewData: IExpressionView[] | IExpressionTreeView[] = [];
		this.getComponentExpression().forEach((condition) => {
			const displayItem = {
				id: condition.id,
				sourceName: condition.source,
				targetName: condition.target,
				sourcePropertyName: condition.sourceProperty,
				targetPropertyName: condition.targetProperty,
				operatorName: this.operatorDictionary[condition.clauseOperator],
				usedBy: this.getName(condition.usedBy),
				conditionalType: this.typeDictionary[condition.conditionType]
			};
			if (this.expressionType === ConditionType.ALL) { // Manage Dialog
				const existingComponent = (expressionViewData as IExpressionTreeView[]).find((item) => {
					return item.componentId === condition.usedBy;
				});
				if (existingComponent) {
					existingComponent.children.push(displayItem);
				} else {
					const manageDisplayItem = {
						componentId: condition.usedBy,
						expanded: false,
						children: [displayItem]
					};
					(expressionViewData as IExpressionTreeView[]).push(manageDisplayItem);
				}
			} else {
				(expressionViewData as IExpressionView[]).push(displayItem);
			}
		});
		return expressionViewData;
	}

	private initGridOptions(): void {
		const columns: IDataGridColumn[] = [];
		if (this.expressionType === ConditionType.ALL) {
			const manageColumns = [
				{
					id: "component",
					name: "Component",
					field: "componentId",
					sortable: true,
					formatter: Soho.Formatters.Tree,
					width: "21%"
				}, {
					id: "conditionalType",
					name: "Type",
					field: "conditionalType",
					sortable: true,
					formatter: Soho.Formatters.Ellipsis,
					width: "21%"
				}
			];
			columns.push(...manageColumns);
		}
		const gridcolumns = [{
			id: "source",
			name: "Source",
			field: "sourceName",
			sortable: true,
			formatter: Soho.Formatters.Ellipsis,
			width: "21%"
		}, {
			id: "sourceProperty",
			name: "Property/Value",
			field: "sourcePropertyName",
			sortable: true,
			formatter: Soho.Formatters.Ellipsis,
			width: "21%"
		}, {
			id: "operator",
			name: "Operator",
			field: "operatorName",
			sortable: true,
			formatter: Soho.Formatters.Ellipsis,
			width: "16%"
		}, {
			id: "target",
			name: "Target",
			field: "targetName",
			sortable: true,
			formatter: Soho.Formatters.Ellipsis,
			width: "21%"
		}, {
			id: "targetProperty",
			name: "Property/Value",
			field: "targetPropertyName",
			sortable: true,
			formatter: Soho.Formatters.Ellipsis,
			width: "21%"
		}
		];
		columns.push(...gridcolumns);

		const options: IDataGridOptions<IExpressionTreeView | IExpressionView> = {
			dataset: [],
			columns: columns,
			treeGrid: true,
			isList: true,
			selectable: SelectableEnum.Single,
			toolbar: {
				title: "Expression",
				results: true,
				dateFilter: false,
				keywordFilter: true,
				advancedFilter: true,
				actions: true,
				views: true,
				rowHeight: true
			}
		};

		this.datagridOptions = options;
		this.$element.find("#datagrid-expressions")
			.on("selected", (e: any, args: IRowItem<IExpressionView | IExpressionTreeView>[]) => {
			// When datagrid get unselected args[0] is undefined
				this.$scope.$evalAsync(() => {
					if (args.length > 0) {
						if (!(args[0].data as IExpressionTreeView).children) {
							this.itemSelected = true;
							this.selectedExpression = this.getComponentExpression().find((condition) => {
								return (args[0].data as IExpressionView).id === condition.id;
							});
						}
					} else {
						this.selectedExpression = null;
						this.itemSelected = false;
					}
				});
			});
	}

	private refreshDataset(): void {
		this.dataset = this.buildDataset();
		if (this.dataset.length > 0) {
			this.updateDisplayNames();
		} else {
			this.$timeout(() => {
				this.datagridOptions.dataset = this.dataset;
			});
		}
		this.clearSelected();
	}

	private clearSelected(): void {
		this.selectedExpression = null;
		this.itemSelected = false;
		const api = this.$element.find("#datagrid-expressions").data("datagrid") as IDatagridApi;

		if (api) {
			api.deSelectAllRows();
		}
	}

	static add(m: ng.IModule): void {
		m.controller("eirExpressionDialogCtrl", ExpressionDialogCtrl);
	}
}
